<template>
    <div ref="wrapper" class="ecom__element element__video ecom-element">
        <div class="ecom-element_video-iframe ecom-element-overlay">
            <a v-if="data?.settings?.autoplay && linkExtra && linkExtra.href != ''" class="ecom-video_link-extra" v-bind="linkExtra"></a>
            <button
                v-if="video?.image_overlay"
                class="ecom-video_overlay-icon"
                :data-ec-tracking-id="settings?.tracking_id"
                :class="video?.image_overlay ? 'ecom-overlay' : ''"
                v-bind="popupAttr"
            >
                <span
                    v-if="video?.image_overlay"
                    class="ecom-video-icon-play"
                    v-html="video.play_icon?.value"
                />
            </button>
            <video
                :poster="posterImage??''"
                style="display: flex !important"
                :id="popupId"
                @loadeddata="handleLoadVideo"
                v-if="['hosted','uploaded'].includes(video?.source)"
                class="ecom_video"
                v-bind="attrVideo"
                ref="video"
            >
                <source :src="linkSelfHosted" />
            </video>
            <iframe
                :id="popupId"
                v-else
                class="ecom_iframe"
                frameborder="0"
                allowfullscreen="1"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                style="border: 0; width: 100%; display: flex !important"
                :src="video?.source == 'vimeo' ? linkVimeo : linkYoutube"
            />
        </div>
    </div>
  </template>

  <script>
  import { Element, Javascript, Liquid } from "@/mixins";
  export default {
    name: "Video",

    mixins: [Element, Javascript, Liquid],
    vendors: ["modal_js", "modal_css"],
    props: {
        data: {
            type: Object,
            default() {
                return {
                };
            }
        }
    },
    data() {
        return {
            jsreactives: [
                "link",
                "player_controls",
                "end_time",
                "start_time",
                "image_overlay",
                "play_on_lightbox",
                "aspect_ratio",
                'autoplay',
                "loop",
                'upload',
                "poster_image"
            ]
        };
    },
    mounted() {
        if(this.$refs.video && (this.data?.settings?.autoplay || this.data?.settings?.mute)) {
            this.$refs.video.setAttribute('muted', '');
        }
    },
    computed: {
        linkExtra() {
            return this.data?.settings?.link_extra;
        },
        videoData() {
            return this.data;
        },
        popupAttr() {
            let popup = {};
            if (this.video.play_on_lightbox && this.video.image_overlay) {
                popup = {
                    "ecom-modal-source": "#ecom-elements__popup-video-" + this.data.id,
                    "ecom-modal": "inline"
                };
            }
            return popup;
        },
        popupId() {
            return "ecom-elements__popup-video-" + this.data.id;
        },
        posterImage() {
            return this.data.settings?.poster_image?.value;
        },
        settings() {
            return [
                {
                    params: [
                        {
                            type: "popup",
                            label: "Source",
                            name: "source",
                            options: {
                                type: "dropdown",
                                values: {
                                    youtube: "Youtube",
                                    vimeo: "Vimeo",
                                    hosted: "External URL",
                                    uploaded: 'Upload'
                                },
                                preview: 'title',
                                default: false
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            type: 'picker',
                            label: 'Poster image',
                            name: 'poster_image',
                            options: {
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data["source"] === "hosted" || data["source"] === "uploaded";
                                    }
                                },
                                responsive: false,
                                type: 'image',
                                editAlt: false
                            }
                        },
                        {
                            name: "link",
                            label: "External URL",
                            description: "Use link video share driver, .mp4, .oog,...",
                            type: "text",
                            options: {
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data["source"] === "hosted";
                                    }
                                }
                            }
                        },
                        {
                            type:'picker',
                            name: 'upload',
                            label: "Choose or upload a video\n <i>(maximum 10Mb)</i>",
                            options: {
                                type:'video',
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data["source"] === "uploaded";
                                    }
                                }
                            }
                        },
                        {
                            name: "srcYoutube",
                            label: "Youtube link",
                            type: "text",
                            options: {
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data["source"] === "youtube";
                                    }
                                }
                            }
                        },
                        {
                            name: "srcVimeo",
                            label: "Vimeo link",
                            type: "text",
                            options: {
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data["source"] === "vimeo";
                                    }
                                }
                            }
                        },
                          {
                            name: "aspect_ratio",
                            label: "Aspect Ratio",
                            type: "popup",
                            options: {
                                type: "dropdown",
                                preview:'title',
                                values: {
                                    "1/1": "1:1",
                                    "3/2": "3:2",
                                    "4/3": "4:3",
                                    "16/9": "16:9",
                                    "21/9": "21:9",
                                    "9/16": "9:16"
                                },
                                 responsive: true
                            },
                            css: {
                                selector: ' .ecom-element_video-iframe iframe, .ecom-element_video-iframe video',
                                properties: {
                                    'aspect-ratio': '',
                                    'max-width':'100%'
                                }
                            }
                        },
                        {
                            name: "start_time",
                            label: "Start time (s)",
                            type: "number",
                            options: {
                                half: true,
                                slider: false,
                                min: 0,
                                max: 1000,
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data["source"] !== "uploaded";
                                    }
                                }
                            }
                        },
                        {
                            name: "end_time",
                            label: "End time",
                            type: "number",
                            options: {
                                slider: false,
                                min: 0,
                                max: 1000,
                                half: true,
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return ['hosted', 'youtube'].includes(data["source"]);
                                    }
                                }
                            }
                        },
                        {
                            name: "mute",
                            label: "Mute",
                            type: "toggle",
                            options: {
                                values: {
                                    on: {
                                        label: "Yes",
                                        value: true
                                    },
                                    off: {
                                        label: "No",
                                        value: false
                                    }
                                },
                                oneline: true
                            }
                        },
                        {
                            name: "autoplay",
                            label: "Auto play",
                            type: "toggle",
                            description: "To autoplay you have to enable muted option, Auto play also not work on safari mobile",
                            options: {
                                values: {
                                    on: {
                                        label: "Yes",
                                        value: true
                                    },
                                    off: {
                                        label: "No",
                                        value: false
                                    }
                                },
                                oneline: true
                            }
                        },
                        {
                            type: "link",
                            label: "Link",
                            name: "link_extra",
                            options: {
                                reset: true,
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data["autoplay"] == true;
                                    }
                                }
                            }
                        },
                        {
                            name: "loop",
                            label: "Loop",
                            type: "toggle",
                            options: {
                                values: {
                                    on: {
                                        label: "Yes",
                                        value: true
                                    },
                                    off: {
                                        label: "No",
                                        value: false
                                    }
                                },
                                oneline: true
                            }
                        },
                        {
                            name: "player_controls",
                            label: "Enable player controls",
                            type: "toggle",
                            options: {
                                values: {
                                    on: {
                                        label: "Yes",
                                        value: true
                                    },
                                    off: {
                                        label: "No",
                                        value: false
                                    }
                                },
                                oneline: true
                            }
                        },
                        {
                            name: "image_overlay",
                            label: "Enable overlay",
                            type: "toggle",
                            options: {
                                values: {
                                    on: {
                                        label: "Yes",
                                        value: true
                                    },
                                    off: {
                                        label: "No",
                                        value: false
                                    }
                                },
                                oneline: true
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            name: "play_icon",
                            label: "Play icon",
                            type: "picker",
                            oneline: true,
                            options: {
                                type: "icon",
                                multiple: false,
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data["image_overlay"] === true;
                                    }
                                }
                            }
                        },
                        {
                            name: "play_on_lightbox",
                            label: "Open in lightbox",
                            type: "toggle",
                            oneline: true,
                            options: {
                                values: {
                                    on: {
                                        label: "Yes",
                                        value: true
                                    },
                                    off: {
                                        label: "No",
                                        value: false
                                    }
                                },
                                oneline: true,
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data["image_overlay"] === true;
                                    }
                                }
                            }
                        }
                    ]
                },
                {
                    
                    group_title: 'Tracking',
                    params: [
                     
                        {
                            type: 'text',
                            name: 'tracking_id',
                            label: 'Tracking ID',
                            options: {
                                placeholder: 'Enter tracking ID'
                            }
                        },
                        {
                            type: 'paragraph',
                            content: 'You can use this field to add a tracking ID to your button. This ID will be sent to your EC Analytics when the button is clicked.'
                        },
                    ]
                }
            ];
        },

        javascript() {
            return function () {
                if (!this.settings || !this.$el) {
                    return;
                }
                var $button = this.$el.querySelector(".ecom-video_overlay-icon");
                if ($button) {
                    $button.addEventListener("click", () => {
                            
                            if (this.settings.source == "hosted" || this.settings.source == "uploaded") {
                                let video = this.$el.querySelector(".ecom_video");
                                video.autoplay = true;
                                video.load();
                                this.$el
                                    .querySelector(".ecom-video_overlay-icon")
                                    .classList.remove("ecom-overlay");
                                this.$el.querySelector(".ecom-video-icon-play").style.display =
                                    "none";
                            } else {
                                let iframe = this.$el.querySelector(".ecom_iframe");
                                let src = iframe.getAttribute("src") + "&autoplay=1";
                                iframe.setAttribute("src", src);
                                this.$el
                                    .querySelector(".ecom-video_overlay-icon")
                                    .classList.remove("ecom-overlay");
                                this.$el.querySelector(".ecom-video-icon-play").style.display =
                                    "none";
                            }
                            
                        });
                        if($button.dataset.ecTrackingId)
                        {
                            $button.addEventListener('click', function(){
                                if(window.Shopify.analytics)
                                {
                                    Shopify.analytics.publish("ec_custom_events",{
                                        "button_id": $button.id,
                                        "tracking_id": $button.dataset.ecTrackingId
                                    })
                                }
                            }, { once: true});
                        }
                    }
                //to do
                const popup = this.$el.querySelector('[ecom-modal]');
                if (popup && this.settings.image_overlay && this.settings.play_on_lightbox && window.EComModal) {
                    var id = this.id
                    new window.EComModal(popup, {
                        cssClass: [id, 'ecom-core', 'ecom-core-simulator'],
                        onOpen: function() {
                            var modal = document.querySelector('.ecom-modal.' + id);

                            if(modal) {
                                modal.querySelector('.ecom-modal-box__content').classList.add('ecom-element_video-iframe')
                            }

                        }
                    });
                }
            };
        },
        css() {
            return `

            .ecom-element_video-popup{
                display:none;
            }

            .ecom-video_link-extra {
                position: absolute;
                inset: 0;
                z-index: 5;
            }

                .element__video.ecom-element{
                    display: flex;
                    width: 100%;
                }
                .ecom-element_video-iframe{
                    display:inline-flex;
                    width:100%;
                    position:relative;
                    overflow:hidden;
                    justify-content: center;
                }
                .ecom-element_video-iframe iframe,
                .ecom-element_video-iframe video{
                    background:#000;
                }
                .ecom-element_video-iframe .ecom-video_overlay-icon{
                    position:absolute;
                    display:flex;
                    top:0;
                    bottom:0;
                    left:0;
                    right:0;
                    align-items:center;
                    justify-content:center;
                    border:none;
                    outline:none;
                    width: 100%;
                    height: 100%;
                    cursor:pointer;
                    background-color:#3336;
                    z-index:-1;
                    opacity:0;
                    border-radius: 0;

                }
                .ecom-element_video-iframe .ecom-video_overlay-icon:hover {
                    background-color:#3336;
                }
                .ecom-element_video-iframe .ecom-overlay{
                    z-index:1;
                    opacity:1;
                }

                .ecom-element_video-iframe video {
                    width: 100%;
                    /*height: intrinsic;*/
                }

                .ecom-video-icon-play{
                    position:absolute;
                    top:50%;
                    left:50%;
                    transform:translate(-50%,-50%);
                    color:white;
                    fill: currentColor;
                    display: flex;
                    z-index: 2;
                }
                .ecom-video-icon-play svg{
                    width: 50px;
                    height: auto;
                }
                .ecom-element_video-lightbox{
                    position: fixed;
                    top: 0;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    z-index: 100;
                    display: none;
                    opacity:0;
                }
                .ecom-light-box-modal{
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    background-color: #333333d1;
                    display:block !important;
                }
                .ecom-element_video-lightbox.fadeIn{
                    display:block;
                    opacity:1;
                    -webkit-transition: opacity 3s;
                    -moz-transition: opacity 3s;
                    transition: opacity 3s;
                }
                .ecom-video-wrapper {
                    position: absolute;
                    top: 50%;
                    left: 10%;
                    right:10%;
                    z-index: 10;
                    height:700px;
                    -webkit-transform: translateY(-50%);
                    -moz-transform: translateY(-50%);
                    -ms-transform: translateY(-50%);
                    -o-transform: translateY(-50%);
                    transform: translateY(-50%);
                    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.1);
                }

                .ecom-video-close-btn {
                    width:30px;
                    height:30px;
                    color: grey;
                    font-size: 25px;
                    position: fixed;
                    top: 3%;
                    right: 3%;
                    z-index: 2;
                    -webkit-transform: scale(1, 1);
                    -moz-transform: scale(1, 1);
                    -ms-transform: scale(1, 1);
                    -o-transform: scale(1, 1);
                    transform: scale(1, 1);
                    -webkit-transition: transform .5s ease, color .5s ease;
                    -moz-transition: transform .5s ease, color .5s ease;
                    -ms-transition: transform .5s ease, color .5s ease;
                    -o-transition: transform .5s ease, color .5s ease;
                    transition: transform .5s ease, color .5s ease;
                }

                .ecom-video-close-btn:hover {
                    color: white;
                    cursor: pointer;
                    -webkit-transform: scale(1.2, 1.2);
                    -moz-transform: scale(1.2, 1.2);
                    -ms-transform: scale(1.2, 1.2);
                    -o-transform: scale(1.2, 1.2);
                    transform: scale(1.2, 1.2);
                    -webkit-transition: transform .5s ease, color .5s ease;
                    -moz-transition: transform .5s ease, color .5s ease;
                    -ms-transition: transform .5s ease, color .5s ease;
                    -o-transition: transform .5s ease, color .5s ease;
                    transition: transform .5s ease, color .5s ease;
                }
                @media screen and (max-width : 786px) {
                    .ecom-video-wrapper {
                    height:350px;
                    }
                }
                @media screen and (max-width : 480px) {
                    .ecom-video-wrapper {
                    height:180px;
                    }
                }
            `;
        },
        video() {
            return this.data.settings;
        },

        linkYoutube() {
            if (this.video?.srcYoutube) {
                const regExpYt = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|&v=)([^#&?]*).*/;
                let val = this.video.srcYoutube.match(regExpYt);
                let videoIdYt = val && val[2].length === 11 ? val[2] : null;
                return `${location.protocol}//www.youtube.com/embed/${videoIdYt}?version=3?${
                    this.video.start_time ? "&start=" + this.video.start_time : ""
                }${this.video.end_time ? "&end=" + this.video.end_time : ""}&controls=${
                    this.video.player_controls ? 1 : 0
                }&mute=${
                    this.video.mute ? 1 : 0
                }&showinfo=0&iv_load_policy=3&playlist=${videoIdYt}&autoplay=${this.video?.autoplay ? 1 : 0}&loop=${this.video?.loop ? 1 : 0}`;
            } else {
                return "";
            }
        },
        linkVimeo() {
            if (this.video?.srcVimeo) {
                const regExpVm = /(?:http?s?:\/\/)?(?:www\.)?(?:vimeo\.com)\/?(.+)/;
                let vals = this.video.srcVimeo.match(regExpVm);
                let videoIdVm = vals ? vals[1] : null;
                return `${
                    location.protocol
                }//player.vimeo.com/video/${videoIdVm}?rel=0?version=3&controls=${
                    this.video.player_controls ? 1 : 0
                }&muted=${this.video.mute ? 1 : 0}${
                    this.video.start_time ? "&byline=1#t=00h00m" + this.video.start_time + "ss" : ""
                }&showinfo=0&autoplay=${this.video?.autoplay ? 1 : 0}&loop=${this.video?.loop ? 1 : 0}`;
            } else {
                return "";
            }
        },
        linkSelfHosted() {
            if(this.video.source === 'hosted')
            {
                if (this.video?.link) {
                    let check = this.video.link.match(
                        /(http(s)?:\/\/.)?(www\.)?[-a-zA-Z0-9@:%._+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_+.~#?&=]*)/
                    );
                    if (check && check[0] == "https://drive.google.com") {
                        const regex = /\/file\/d\/(.+)\/(.+)/;
                        let val = this.video.link.match(regex);
                        let videoId = val ? val[1] : null;
                        return `${
                            location.protocol
                        }//drive.google.com/uc?export=download&id=${videoId}#t=${
                            this.video.start_time ? this.video.start_time : ""
                        },${this.video.end_time ? this.video.end_time : ""}`;
                    } else {
                        return (
                            this.video.link +
                            `#t=${this.video.start_time ? this.video.start_time : ""},${
                                this.video.end_time ? this.video.end_time : ""
                            }`
                        );
                    }
                } else {
                    return "";
                }
            }
            else if(this.video?.source === 'uploaded')
            {
                return this.video?.upload?.value
            }
            return '';

        },
        attrVideo() {
            return {
                controls: this.video.player_controls,
                autoplay: this.video.autoplay,
                loop: this.video.loop,
                playsinline:this.video.autoplay,
                preload: this.video.autoplay ? true: false
            };
        },
        default() {
            return {
                settings: {
                    link: "https://cdn.shopify.com/s/files/1/0629/4392/0358/files/flower.webm?v=1649754044",
                    srcVimeo: "https://vimeo.com/210603721",
                    srcYoutube: "https://www.youtube.com/watch?v=2djJhgfl91s",
                    source: "youtube",
                    player_controls: true
                }
            };
        }
    },

    watch: {
        'data.settings.mute': {
            deep: true,
            handler: function(newVal) {
                // console.log(this.$refs.video.muted)
                if(this.$refs.video) {
                    this.$refs.video.muted = newVal;
                    if(newVal) {
                        this.$refs.video.setAttribute('muted','');
                    } else {
                        this.$refs.video.removeAttribute('muted');
                    }
                }
            }
        }
    },
    methods: {
        handleLoadVideo() {
            if(this.$refs.video) {
                if(this.data?.settings?.mute == true) {
                    this.$refs.video.muted = true;
                    this.$refs.video.setAttribute('muted', '');
                } else {
                    this.$refs.video.muted = false;
                    this.$refs.video.removeAttribute('muted');
                }
            }
        },
        style() {
            let style = [];
            if (this.data.settings?.play_icon) {
                style.push({
                    group_alias: "icon:hover",
                    options: {
                        group_name: "videoPlayIcon",
                        group_title: "Play icon",
                        selector: " .element__video .ecom-video-icon-play"
                    },
                    modify: {
                        params: {
                            position: -1,
                            fields: [
                                {
                                    type: "line"
                                },
                                {
                                    alias: "spacing"
                                }
                            ]
                        }
                    }
                });
            }
            if (this.data.settings?.image_overlay) {
                style.push({
                    group_alias: "background:overlay",
                    options: {
                        group_name: "overlay",
                        group_title: "Overlay",
                        selector: " .ecom-element_video-iframe"
                    }
                });

            }
            style.push({
                group_title: 'Popup content',
                group_name: 'popup_content',
                selector: ' .ecom-element_video-iframe',
                params: [
                    {
                        type:'number',
                        label:'Width',
                        name:'popup_width',
                        options:{
                            units:{
                                '%': {
                                    min: 0,
                                    max: 100
                                },
                                px: {
                                    min: 0,
                                    max: 2000
                                },
                                vw: {
                                    min: 0,
                                    max: 100
                                }
                            },
                            reset:false,
                            responsive:true
                        },
                        css:{
                            properties:{
                                width:''
                            },
                        }
                    },
                    {
                        type:'number',
                        label:'Height',
                        name:'popup_height',
                        options:{
                            units:{
                                    px: {
                                        min: 0,
                                        max: 2000
                                    },
                                    vw: {
                                        min: 0,
                                        max: 100
                                    }
                            },
                            reset:false,
                            responsive:true
                        },
                        css:{
                            properties:{
                                height:''
                            },
                        }
                    },
                    {
                        alias: 'justify-content',
                        options: {
                            label: "Alignment",
                            css: {
                                selector: "root .element__video.ecom-element "
                            }
                        }
                    },
                    {
                        alias: 'spacing'
                    }
                ]
            })
            return style;
        }
    }
  };
  </script>
