<template>
    <div class="ecom-element ecom-base-elements ecom-element__countdown">
        <div class="ecom-element__countdown-wrapper ecom-flex">
            <div class="ecom-element__countdown-container">
                <div
                    class="ecom-element__countdown--time"
                    :data-countdown-to="countdown_to"
                    :data-countdown-from="countdown_from"
                    :data-countdown-restart="data.settings?.countdown_restart"
                    :data-countdown-type="data.settings?.typeCountdown"
                    :data-trans-week="lang(data.settings?.text_week, 'text_week')"
                    :data-trans-day="lang(data.settings?.text_day, 'text_day')"
                    :data-trans-hour="lang(data.settings?.text_hour, 'text_hour')"
                    :data-trans-minute="lang(data.settings?.text_minute, 'text_minute')"
                    :data-trans-second="lang(data.settings?.text_second, 'text_second')"
                    :data-show-fields="shows"
                />
                <div v-if="data.settings.enable_progress_bar" class="ecom-countdown-progress-bar">
                    <div class="ecom-countdown-progress-bar--timer" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Element, Javascript } from "@/mixins";
export default {
    name: "Countdown",
    presets:true,
    docs: 'https://help.ecomposer.io/docs/elements/basic-elements/countdown/',
    mixins: [Element, Javascript],
    vendors: ["countdown_js"],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    data() {
        return {
            jsreactives: ["week", "day", "hour", "minute", "second"]
                .map((show) => "show_" + show)
                .concat(["week", "day", "hour", "minute", "second"].map((show) => "text_" + show))
                .concat(['countdown','countdown_from', "typeCountdown", "countdown_restart"])
        };
    },
    computed: {
        css() {
            return `
                .ecom-element__countdown--time{
                    display:flex;
                    flex-wrap:wrap;
                    justify-content:inherit;
                }
                [class*="ecom-element__countdown-item--"]{
                    display:flex;
                }
                .ecom-element__countdown-container{
                    display:inline-flex;
                    justify-content:inherit;
                    flex-direction: column;
                    align-items:inherit
                }
                .ecom-countdown-progress-bar{
                    display:block;
                    width:100%;
                    background:green;
                    border-radius:5px;
                    overflow:hidden;
                }
                .ecom-countdown-progress-bar .ecom-countdown-progress-bar--timer{
                    width:100%;
                    height:8px;
                    background-color:red;
                    display:block;
                    border-radius:5px;
                }

                .ecom-countdown-progress-bar--timer {
                        background-image: -webkit-linear-gradient(45deg,rgba(255,255,255,.15) 25%,transparent 25%,transparent 50%,rgba(255,255,255,.15) 50%,rgba(255,255,255,.15) 75%,transparent 75%,transparent);
                        background-image: linear-gradient(45deg,rgba(255,255,255,.15) 25%,rgba(0,0,0,0) 25%,rgba(0,0,0,0) 50%,rgba(255,255,255,.15) 50%,rgba(255,255,255,.15) 75%,rgba(0,0,0,0) 75%,rgba(0,0,0,0));
                        -webkit-animation: 2s linear infinite ecom_progress_bar;
                        animation: 2s linear infinite ecom_progress_bar;
                        background-size: 60px 60px;
                        transition: width 1s;
                        -webkit-transition: width 1s;
                }

                @-webkit-keyframes ecom_progress_bar {
                    from {
                        background-position: 0 0
                    }

                    to {
                        background-position: 40px 0
                    }
                }

                @keyframes ecom_progress_bar {
                    from {
                        background-position: 0 0
                    }

                    to {
                        background-position: 40px 0
                    }
                }
            `;
        },

        javascript() {
            return function () {
                const $el = this.$el;
                if (window.EComCountdown) {
                    const $dom = $el.querySelector(".ecom-element__countdown--time");
                    let $progressbar = $el.querySelector('.ecom-countdown-progress-bar'),
                        $timer =  $el.querySelector('.ecom-countdown-progress-bar--timer'),
                        from = $dom ? $dom.dataset.countdownFrom : 0,
                        type = $dom ? ($dom.dataset.countdownType ? $dom.dataset.countdownType : 'visit') : 'visit',
                        restart = $dom ? ($dom.dataset.countdownRestart ? $dom.dataset.countdownRestart : false) : false;
                    if ($dom && $dom.dataset.countdownTo) {
                        const fields = $dom.dataset.showFields ? $dom.dataset.showFields : "";
                        const params = $dom.dataset;
                        var format = "";
                        const regex = /\[([^\]]+)\]/gm;

                        if (fields.includes("week") && params.transWeek.length > 0) {

                            let number = '';
                            let text = params.transWeek.replace(regex,(...m)=>{
                                  number =  m[1];
                                  return '';
                            });
                            format += `
                                        <div class="ecom-element__countdown-item--weeks">
                                            <span class="ecom-element__countdown-number">${number}</span>
                                            <span class="ecom-element__countdown-text">
                                                 ${text}
                                            </span>
                                        </div>`;
                        }
                        if (fields.includes("day") && params.transDay.length > 0) {
                            let number = '';
                            let text = params.transDay.replace(regex,(...m)=>{
                                  number =  m[1];
                                  return '';
                            });
                            format += `
                                        <div class="ecom-element__countdown-item--days">
                                            <span class="ecom-element__countdown-number">
                                                ${number}
                                            </span>
                                            <span class="ecom-element__countdown-text">
                                                ${text}
                                            </span>
                                        </div>`;
                        }
                        if (fields.includes("hour") && params.transHour.length > 0) {
                            let number = '';
                            let text = params.transHour.replace(regex,(...m)=>{
                                  number =  m[1];
                                  return '';
                            });
                            format += `
                                        <div class="ecom-element__countdown-item--hours">
                                            <span class="ecom-element__countdown-number">
                                                 ${number}
                                            </span>
                                            <span class="ecom-element__countdown-text">
                                                ${text}
                                            </span>
                                        </div>`;
                        }
                        if (fields.includes("minute") && params.transMinute.length > 0) {
                            let number = '';
                            let text = params.transMinute.replace(regex,(...m)=>{
                                  number =  m[1];
                                  return '';
                            });
                            format += `
                                        <div class="ecom-element__countdown-item--minutes">
                                            <span class="ecom-element__countdown-number">
                                                ${number}
                                            </span>
                                            <span class="ecom-element__countdown-text">
                                                ${text}
                                            </span>
                                        </div>`;
                        }
                        if (fields.includes("second") && params.transSecond.length > 0) {
                            let number = '';
                            let text = params.transSecond.replace(regex,(...m)=>{
                                  number =  m[1];
                                  return '';
                            });
                            format += `
                                        <div class="ecom-element__countdown-item--seconds">
                                            <span class="ecom-element__countdown-number">
                                                ${
                                                    number
                                                }
                                            </span>
                                            <span class="ecom-element__countdown-text">
                                                ${
                                                    text
                                                }
                                            </span>
                                    </div>`;
                        }
                        function countdownCallback(event) {
                            this.innerHTML = event.strftime(format);
                            if ($progressbar && from) {
                                let now_time = new Date().getTime(),
                                    start = new Date(from),
                                    start_time = start.getTime(),
                                    end_time = event.finalDate.getTime();
                                if (start_time < now_time && end_time > start_time) {
                                    $progressbar.style.display = 'inherit';;
                                    let total = end_time - start_time;
                                    let elapse = end_time - now_time;
                                    let result = Math.round((elapse * 100) / total);
                                    $timer.style.width = result + '%';
                                } else {
                                    $progressbar.style.display = 'none';
                                }
                            }
                        }

                        if (from && new Date().getTime() < new Date(from).getTime() && type == 'time') {
                            // console.info('Countdown will run on: ',new Date(parseInt(from)));
                        } else {
                            let date = new Date($dom.dataset.countdownTo)

                            if( window.EComCountdown )
                            {
                                let date_diff =  new Date($dom.dataset.countdownTo).getTime() - new Date($dom.dataset.countdownFrom).getTime(),
                                    new_date = new Date($dom.dataset.countdownTo).getTime() + date_diff;
                                if(date_diff == 0) return;
                                while(new_date < new Date().getTime()){
                                    new_date += date_diff;
                                }
                                window
                                .EComCountdown($dom,date, {});
                                $dom.addEventListener('update.ecom.countdown', countdownCallback);
                                $dom.addEventListener('finish.ecom.countdown', function(event) {
                                    if(restart == 'true' && type == 'time') {
                                        window.EComCountdown($dom,new Date(new_date), countdownCallback)
                                    }
                                });

                            }
                        }

                    }
                }
            };
        },
        countdown_to() {
            return this.data && this.data.settings && "countdown" in this.data.settings
                ? this.data.settings.countdown
                : "";
        },
        countdown_from() {
            return this.data && this.data.settings && "countdown_from" in this.data.settings
                ? this.data.settings.countdown_from
                : "";
        },

        shows() {
            const shows = ["week", "day", "hour", "minute", "second"];
            return shows.filter((show) => this.data.settings["show_" + show]);
        },

        default() {
            let date = new Date();
            let cur_month = date.getMonth()+1;
            let cur_year = date.getFullYear();

            return {
                settings: {
                    title: "Hurry up! The sale will end on",
                    countdown_from: `${cur_year}/${cur_month-1}/29 12:30 GMT+0700`,
                    countdown: `${cur_year}/${cur_month}/20 12:30 GMT+0700`,
                    show_week: true,
                    show_day: true,
                    show_hour: true,
                    show_minute: true,
                    show_second: true,
                    text_week: "[%-w] week%!w",
                    text_day: "[%-d] day%!D",
                    text_hour: "[%-H] hour%!H",
                    text_minute: "[%-M] minute%!M",
                    text_second: "[%-S] second%!S",
                    styleCountdown: "column",
                    typeCountdown: "time",
                },
                "style": {
                    "progess_bar": {
                        "tab": "normal",
                        "maxWidth": "96%",
                        "background": {
                            "classic": {
                                "background-color": "#deb4b4"
                            }
                        },
                        "height": "14px",
                        "borderRadius": {
                            "top": "8px",
                            "left": "8px",
                            "bottom": "8px",
                            "right": "8px"
                        }
                    },
                    "general": {
                        "justify-content": "center"
                    },
                    "items": {
                        "spacing": {
                            "padding": {
                                "left": "10px",
                                "top": "5px",
                                "bottom": "5px",
                                "right": "10px"
                            },
                            "margin": {
                                "top": "5px",
                                "left": "10px",
                                "bottom": "5px",
                                "right": "10px"
                            }
                        },
                        "boxBackground": "rgba(255, 255, 255, 0.05)",
                        "spacing__mobile": {
                            "padding": {
                                "left": "10px",
                                "top": "10px",
                                "bottom": "10px",
                                "right": "10px"
                            },
                            "margin": {}
                        },
                        "boxBorder": {
                            "border-style": "solid",
                            "border-color": "#f0f0f0",
                            "border-width": {
                                "bottom": "1px",
                                "top": "1px",
                                "left": "1px",
                                "right": "1px"
                            }
                        },
                        "boxBorderRadius": {
                            "top": "3px",
                            "left": "3px",
                            "bottom": "3px",
                            "right": "3px"
                        },
                        "boxShadow": {
                            "box-shadow": {
                                "color": "rgba(0, 0, 0, 0.06)",
                                "horizontal": "1px",
                                "vertical": "1px",
                                "blur": "5px",
                                "spread": "1px"
                            }
                        }
                    },
                    "countdown_number": {
                        "buttonColor": "#333",
                        "spacing": {
                            "margin": {
                                "right": "8px"
                            }
                        },
                        "spacing__mobile": {
                            "margin": {
                                "bottom": "0px",
                                "top": "0px",
                                "left": "0px",
                                "right": "0px"
                            },
                            "padding": {
                                "bottom": "0px",
                                "top": "0px",
                                "left": "0px",
                                "right": "0px"
                            }
                        },
                        "buttonTypography": {
                            "font-family": {
                                "name": "Jost",
                                "value": "https://fonts.googleapis.com/css?family=Jost:100,200,300,400,500,600,700,800,900",
                                "thumbnail": "Jost"
                            },
                            "font-size": "26px",
                            "font-weight": "700",
                            "line-height": "42px"
                        },
                        "text-align": "center",
                        "buttonWidth": "52px"
                    },
                    "countdown_label": {
                        "spacing__mobile": {
                            "margin": {
                                "bottom": "0px",
                                "top": "0px",
                                "left": "0px",
                                "right": "0px"
                            },
                            "padding": {
                                "bottom": "0px",
                                "top": "0px",
                                "left": "0px",
                                "right": "0px"
                            }
                        },
                        "buttonTypography": {
                            "global-typography": "m5lJMKLv"
                        },
                        "text-align": "center"
                    }
                }
            };
        }
    },
    methods:{
        settings() {
            return [
                {
                    group_title: "General",
                    params: [
                        {
                            name: "styleCountdown",
                            label: "Layout",
                            type: "popup",
                            options: {
                                type: "dropdown",
                                default: false,
                                preview: 'title',
                                values: {
                                    column: "Vertical",
                                    row: "Horizontal"
                                }
                            },
                            css: {
                                selector: ' .ecom-element__countdown--time > div',
                                properties: {
                                    "flex-direction": ""
                                }
                            }
                        },
                        {
                            name: "typeCountdown",
                            label: "Countdown Start",
                            type: "popup",
                            options: {
                                type: "dropdown",
                                default: false,
                                preview: 'title',
                                values: {
                                    visit: "On the visit page",
                                    time: "At specific time"
                                }
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            name: "countdown_from",
                            type: "datetime",
                            placeholder: "YYYY/MM/DD hh:mm",
                            label: "Countdown start from",
                            options: {
                                type: "datetime",
                                max: this.data?.settings?.countdown || '',
                                format: "YYYY/MM/DD hh:mmZ",
                                visible: (data) => {
                                    return data.typeCountdown == 'time'
                                }
                            }
                        },
                        {
                            name: "countdown",
                            type: "datetime",
                            placeholder: "YYYY/MM/DD hh:mmZ",
                            label: "Countdown end at",
                            options: {
                                toolbar: false,
                                type: "datetime",
                                min: this.data?.settings?.countdown_from || '',
                                format: "YYYY/MM/DD hh:mmZ"
                            }
                        },
                        {
                            name: "countdown_restart",
                            label: "Restart on finish",
                            type: "toggle",
                            options: {
                                values: {
                                    on: {
                                        label: "Yes",
                                        value: true
                                    },
                                    off: {
                                        label: "No",
                                        value: false
                                    }
                                },
                                visible: (data) => {
                                    return data.typeCountdown == 'time'
                                }
                            }
                        },
                        {
                            name: "enable_progress_bar",
                            label: "Show progress bar",
                            value: true,
                            type: "toggle",
                            options: {
                                values: {
                                    on: {
                                        label: "Yes",
                                        value: true
                                    },
                                    off: {
                                        label: "No",
                                        value: false
                                    }
                                }
                            }
                        },
                        ...["week", "day", "hour", "minute", "second"].map((value) => {
                            return {
                                name: "show_" + value,
                                label: "Show " + value,
                                value: true,
                                type: "toggle",
                                options: {
                                    values: {
                                        on: {
                                            value: true
                                        },
                                        off: {
                                            value: false
                                        }
                                    }
                                }
                            };
                        })
                    ]
                },
                {
                    group_title: "Change text",
                    options: {
                        visible: (data) => {
                            return (
                                data.show_week ||
                                data.show_day ||
                                data.show_hour ||
                                data.show_minute ||
                                data.show_second
                            );
                        }
                    },
                    params: [
                        {
                            type: "text",
                            name: "text_week",
                            value: "[%-w] week%!w",
                            description: "Example: [%-w] week%!w",
                            label: "Week",
                            options: {
                                visible: (data) => data.show_week
                            }
                        },
                        {
                            type: "text",
                            name: "text_day",
                            value: "[%-d] day%!D",
                            description: "Example: [%-d] day%!d",
                            label: "Days",
                            options: {
                                visible: (data) => data.show_day
                            }
                        },
                        {
                            type: "text",
                            name: "text_hour",
                            value: "[%-H] hour%!H",
                            description: "Example: [%-H] hour%!H",
                            label: "Hours",
                            options: {
                                visible: (data) => data.show_hour
                            }
                        },
                        {
                            type: "text",
                            name: "text_minute",
                            value: "[%-M] minute%!M",
                            description: "Example: [%-M] minute%!M",
                            label: "Minutes",
                            options: {
                                visible: (data) => data.show_minute
                            }
                        },
                        {
                            type: "text",
                            name: "text_second",
                            value: "[%-S] second%!S",
                            description: "Example: [%-S] second%!S",
                            label: "Seconds",
                            options: {
                                visible: (data) => data.show_second
                            }
                        }
                    ]
                }
            ];
        },
        style() {
            let modify = {}
            if(this.data.settings.styleCountdown == 'column')
            {
                modify = {
                    params:{
                        alias:'text-align',
                        options:{
                            label:'Alignment'
                        }
                    }
                }
            }
            let style = [
                {
                    group_alias: "box",
                    options: {
                        group_title: "General",
                        group_name: "general",
                        selector: " .ecom-element__countdown-container"
                    },
                    modify: {
                        params: [
                            {
                                position: 0,
                                fields: {
                                    alias: "justify-content",
                                    options: {
                                        label: "Alignment",
                                        css: {
                                            selector: "root .ecom-element__countdown-wrapper",
                                            properties:{
                                                'justify-content':'',
                                                'align-items':''
                                            }
                                        }
                                    }
                                }
                            },
                            {
                                position: 10,
                                fields: {
                                    alias: "spacing"
                                }
                            }
                            //todo
                        ]
                    }
                },
                {
                    group_alias: "box",
                    options: {
                        group_title: "Items",
                        group_name: "items",
                        selector: ' .ecom-element__countdown--time > div'
                    },
                    modify: {
                        params: [
                            {
                                position:0,
                                fields:[
                                    {
                                        type:'number',
                                        label:'Width',
                                        name:'width',
                                        options:{
                                            units:{
                                                px:{
                                                    min:20,
                                                    max:200
                                                }
                                            },
                                            reset:false,
                                            responsive:true
                                        },
                                        css:{
                                            properties:{
                                                width:''
                                            }
                                        }
                                    },
                                    {
                                        type:'number',
                                        label:'Height',
                                        name:'height',
                                        options:{
                                            units:{
                                                px:{
                                                    min:20,
                                                    max:200
                                                }
                                            },
                                            reset:false,
                                            responsive:true
                                        },
                                        css:{
                                            properties:{
                                                height:''
                                            }
                                        }
                                    }
                                ]
                            },
                            {
                                position: 10,
                                fields: {
                                    alias: "spacing"
                                }
                            }
                        ]
                    }
                },
                {
                    group_alias: "button:label",
                    options: {
                        group_name: "countdown_number",
                        group_title: "Number",
                        selector: " .ecom-element__countdown-number"
                    },
                    modify
                },
                {
                    group_alias: "button:label",
                    options: {
                        group_name: "countdown_label",
                        group_title: "Label",
                        selector: " .ecom-element__countdown-text"
                    },
                    modify
                }
            ];
            if (this.data.settings.enable_progress_bar) {
                style.push({
                    group_name: 'progess_bar',
                    group_title: 'Progress bar',
                    selector: ' .ecom-countdown-progress-bar',
                    params: [
                        {
                            type: 'number',
                            name: 'maxWidth',
                            label: 'Width',
                            options: {
                                responsive: true,
                                reset: true,
                                units: {
                                    '%': {
                                        min: 0,
                                        max: 100
                                    },
                                    px: {
                                        min: 0,
                                        max: 1000
                                    },
                                    vw: {
                                        min: 0,
                                        max: 100
                                    }
                                }
                            },
                            css:{
                                properties:{
                                    'max-width':''
                                }
                            }
                        },
                        {
                            type: 'number',
                            name: 'height',
                            label: 'Height',
                            options: {
                                responsive: true,
                                reset: true,
                                units: {
                                    px: {
                                        min: 0,
                                        max: 1000
                                    },
                                    vh: {
                                        min: 0,
                                        max: 100
                                    }
                                }
                            },
                            css:{
                                selector: ' .ecom-countdown-progress-bar--timer',
                                properties:{
                                    'height':'',
                                    '--ecom-countdown-max-height':''
                                }
                            }
                        },
                        {
                            name: 'tab',
                            type: 'tab',
                            value: 'normal',
                            options: {
                                tabs: [{
                                        name: 'normal',
                                        title: 'Normal'
                                    },
                                    {
                                        name: 'active',
                                        title: 'Active'
                                    }
                                ]
                            },
                            css: {
                                isCss: false,
                            }
                        },
                        {
                            type: 'background',
                            label: 'Background',
                            name: 'background',
                            options: {
                                oneline: true,
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data['tab'] === 'normal';
                                    }
                                }
                            },
                            css:{
                                properties:{
                                    'background':''
                                }
                            }
                        },
                        {
                            type: 'dimension',
                            name: 'borderRadius',
                            label: 'Border radius',
                            options: {
                                type: 'radius',
                                units: 'default',
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data['tab'] === 'normal';
                                    }
                                }
                            },
                            css:{
                                properties:{
                                    'border-radius':''
                                }
                            }
                        },
                        {
                            type: 'background',
                            label: 'Background',
                            name: 'backgroundActive',
                            options: {
                                oneline: true,
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data['tab'] === 'active';
                                    }
                                }
                            },
                            css:{
                                selector: ' .ecom-countdown-progress-bar--timer',
                                properties:{
                                    'background':''
                                }
                            }
                        },

                        {
                            type: 'dimension',
                            name: 'borderRadiusActive',
                            label: 'Border radius',
                            options: {
                                type: 'radius',
                                units: 'default',
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data['tab'] === 'active';
                                    }
                                }
                            },
                            css:{
                                selector: ' .ecom-countdown-progress-bar--timer',
                                properties:{
                                    'border-radius':''
                                }
                            }
                        },
                        {
                            alias:'spacing'
                        }
                    ]
                });
            }
            return style;
        }
    }
};
</script>

