<template>
    <div
        class="ecom-element ecom-product-single ecom-product-single__product-sold"
    >
        <div class="ecom-product-single__product-sold-wrapper">
            <div class="ecom-product-single__product-sold-container">
                <div
                    v-if="data.settings.icon"
                    class="ecom-product-single__product-sold-icon ecom-in_flex"
                    v-html="data.settings.icon"
                    />
                <div
                    class="ecom-product-single__product-sold-content"
                    :data-text="lang(text,'product_sold_text')"
                />
            </div>
        </div>
    </div>
</template>

<script>
import {  Javascript,Element } from '@/mixins';
export default {
    name:'ProductSold',
    mixins: [ Javascript, Element],
    props: {
        data: {
            type: Object,
            default () {
                return {};
            }
        }
    },
    data(){
        return {
            jsreactives: [
                'animation_loop_time',
                'animation',
                'min',
                'max',
                'min_time',
                'max_time',
                'text'
            ]
        }
    },
    computed: {
        settings() {
            return [{
                params: [
                {
                    type: 'paragraph',
                    content: 'Display a fake the number of product sold on your product page.'
                },
                {
                    type: 'number',
                    label: 'Minimum number of product sold',
                    name: 'min',
                    value: 1,
                    placeholder: 1,
                    options: {
                        min: 1,
                        max: 200
                    },
                    description: 'Minimum product sold number must be less than maximum product sold'
                },
                {
                    type: 'number',
                    label: 'Maximum number of product sold',
                    name: 'max',
                    value: 100,
                    placeholder: 100,
                    options: {
                        min: 1,
                        max: 200
                    },
                    decription: 'Maximum product sold number must be greater than minimum product sold'
                },
                {
                    type: 'number',
                    label: 'Minimum number of time',
                    name: 'min_time',
                    value: 1,
                    placeholder: 1,
                    options: {
                        min: 1,
                        max: 200
                    },
                    description: 'Minimum time number must be less than maximum time'
                },
                {
                    type: 'number',
                    label: 'Maximum number of time',
                    name: 'max_time',
                    value: 5,
                    placeholder: 5,
                    options: {
                        min: 1,
                        max: 24
                    },
                    decription: 'Maximum time number must be greater than minimum time'
                },
                {
                    type: 'textarea',
                    label: 'Text',
                    name:'text',
                    options: {
                        toolbar: true
                    },
                    value: '',
                    description: 'The text shows the number of product sold. Example: {sold} sold in last {hour} hours'
                },
                {
                    type: 'picker',
                    name:'icon',
                    label: 'Icon',
                    options: {
                        oneline: true,
                        type: 'icon',
                        layout: 'grid',
                        output: 'value',
                        multiple: false
                    }
                },
                {
                  type: 'toggle',
                  name: 'animation',
                  label: 'Icon animation',
                  options: {
                      visible: function (data) {
                          return data.icon != '';
                      },
                      values: {
                          on: {
                              label: 'Enable',
                              value: true
                          },
                          off: {
                              label: 'Disable',
                              value: false
                          }
                      }
                  },
                },
                {
                  type: "number",
                  label: 'Loop time <span class="lowercase">(s)</span>',
                  name: "animation_loop_time",
                  options: {
                      min: 2,
                      default: 6,
                      max: 10,
                      visible: function (data) {
                          return data.icon != '';
                      }
                  }
                },
                ]
            }];
        },

        text(){
            return this.data && this.data.settings && this.data.settings.text ? this.data.settings.text : '';
        },
        javascript(){
            return function(){
                const $el = this.$el;
                if(!$el) return;

                let $content = $el.querySelector('.ecom-product-single__product-sold-content');
                function getRandomIntMinMax(min,max) {
                    return Math.floor(Math.random() *  (max - min)) + min ;
                }
                if($content && $content.dataset.text)
                {
                    let $max = parseInt(this.settings.max),
                        $min = parseInt(this.settings.min),
                        $max_time = parseInt(this.settings.max_time),
                        $min_time = parseInt(this.settings.min_time);
                    $max = isNaN($max) ? 30 : $max;
                    $min = isNaN($min) ? 2 : $min;
                    $min = $min > $max ? $max : $min;
                    $max_time = isNaN($max_time) ? 24 : $max_time;
                    $min_time = isNaN($min_time) ? 2 : $min_time;
                    $min_time = $min_time > $max_time ? $max_time : $min_time;

                    const fakeProductSold = function(){
                        let product_sold = getRandomIntMinMax($min,$max);
                        let time = getRandomIntMinMax($min_time,$max_time);
                        let text = $content.dataset.text.replace(/{\s*sold\s*}/g,product_sold);
                        text = text.replace(/{\s*hour\s*}/g, time);
                        $content.innerHTML = text;
                    };
                    fakeProductSold();

                    const handleAnimation = function(_this) {
                      if (!$el) return;

                      const $icon = $el.querySelector('.ecom-product-single__product-sold-icon');
                      if (!$icon) return;

                      let intervalTime = parseInt(_this.settings.animation_loop_time) * 1000 || 6000,
                          animTime = 1000;

                      setInterval(function() {
                          $icon.classList.add('animated');
                          setTimeout(function(){
                            $icon.classList.remove('animated');
                          }, animTime);
                      }, intervalTime);
                    }

                    if (this.settings.animation) {
                      handleAnimation(this);
                    }

                }
            };
        },
        css(){
            return `
                .ecom-product-single__product-sold-container {
                    display: flex;
                    align-items:center;
                    column-gap: 10px;

                }
            `;
        },
        default() {
            return {
                "settings": {
                    "min": 1,
                    "max": 100,
                    "min_time": 1,
                    "max_time": 6,
                    "text": "{sold} sold in last {hour} hours",
                    "icon": "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 32 32\" fill=\"currentColor\"><path d=\"M 14.375 3 L 14.1875 3.78125 C 13.886719 5.128906 11.953125 7.347656 9.96875 10.0625 C 7.984375 12.777344 6 16.160156 6 20.40625 C 6 22.410156 6.613281 24.550781 8.125 26.21875 C 9.636719 27.886719 12.046875 29 15.3125 29 C 15.457031 29 15.578125 28.972656 15.71875 28.96875 C 15.8125 28.972656 15.902344 29 16 29 C 16.304688 29 16.589844 28.964844 16.875 28.9375 C 16.960938 28.929688 17.039063 28.914063 17.125 28.90625 C 20.472656 28.628906 22.695313 27.597656 24.0625 26.0625 C 25.667969 24.261719 26 21.976563 26 20 C 26 15.535156 23.429688 11.382813 20.9375 8.3125 C 18.445313 5.242188 15.9375 3.21875 15.9375 3.21875 L 15.65625 3 Z M 15.5 5.5 C 16.308594 6.210938 17.671875 7.496094 19.375 9.59375 C 21.726563 12.492188 24 16.332031 24 20 C 24 21.753906 23.691406 23.453125 22.5625 24.71875 C 22.320313 24.992188 22.027344 25.257813 21.6875 25.5 C 22.226563 23.941406 22.109375 22.167969 21.75 20.40625 C 21.339844 18.394531 20.5625 16.375 19.8125 14.75 C 19.0625 13.125 18.421875 11.976563 17.875 11.34375 L 17.59375 11 L 15.65625 11 L 16.03125 12.28125 C 17.078125 15.683594 16.527344 18 15.9375 18.6875 C 15.644531 19.03125 15.515625 19.0625 15.21875 18.96875 C 14.921875 18.875 14.386719 18.488281 13.875 17.53125 L 13.125 16.125 L 12.1875 17.40625 C 10.316406 19.925781 9.597656 22.6875 10.21875 25 C 10.273438 25.203125 10.359375 25.402344 10.4375 25.59375 C 10.140625 25.367188 9.863281 25.136719 9.625 24.875 C 8.507813 23.644531 8 22 8 20.40625 C 8 16.785156 9.703125 13.835938 11.59375 11.25 C 13.132813 9.144531 14.632813 7.367188 15.5 5.5 Z M 18.5625 17.21875 C 19.027344 18.394531 19.53125 19.582031 19.78125 20.8125 C 20.144531 22.601563 20.136719 24.238281 19.625 25.28125 C 19.242188 26.0625 18.648438 26.636719 17.4375 26.875 C 17.304688 26.902344 17.148438 26.917969 17 26.9375 C 16.6875 26.964844 16.34375 26.988281 16 27 C 15.910156 27 15.835938 27.003906 15.75 27 C 13.585938 26.929688 12.546875 25.964844 12.15625 24.5 C 11.832031 23.292969 12.253906 21.609375 13.15625 19.90625 C 13.601563 20.367188 14.074219 20.703125 14.625 20.875 C 15.644531 21.195313 16.78125 20.804688 17.46875 20 C 18.058594 19.308594 18.40625 18.351563 18.5625 17.21875 Z\"></path></svg>",
                    "animation": true,
                    "textTextAlign": "flex-start",
                    "animation_loop_time": 2
                },
                "style": {
                    "icon_animation": {
                        "animation": "heart-beat"
                    },
                    "icon": {
                        "spacing": {
                            "margin": {
                                "right": "-5px"
                            }
                        },
                        "iconFontSize": "24px"
                    }
                }
            }
        }
    },
    methods:
    {
         style(){
             let style = [
                {
                    group_title: 'General',
                    group_name: 'general',
                    selector: ' .ecom-product-single__product-sold-container',
                    params: [
                        {
                            alias:'justify-content',
                        },
                        {
                            alias: 'spacing'
                        },
                    ]
                },
               {
                  group_alias: 'text',
                  options:{
                      group_title: 'Text',
                      group_name: 'text',
                      selector: ' .ecom-product-single__product-sold-content',
                  },
                  modify: {
                    remove:{
                        index:0,
                        length:1
                    }
                  }
               }

            ];
            if(this.data.settings.icon){
                style.push(
                    {
                      group_alias: 'icon',
                      options:{
                          group_title: 'Icon',
                          group_name: 'icon',
                          selector: ' .ecom-product-single__product-sold-icon',
                      },
                      modify:{
                        params:[
                            {
                                position:15,
                                fields:[
                                    {
                                        type:'line'
                                    },
                                    {
                                        alias:'spacing'
                                    }
                                ]
                            }
                        ]
                      }
                    },
                    {
                      group_name: 'icon_animation',
                      group_title: 'Animation effect',
                      selector: ' .ecom-product-single__product-sold-icon',
                      params: [
                          {
                            name: "animation",
                            label: "Animation",
                            type: "popup",
                            options: {
                                type: "dropdown",
                                values: "entrance-animation",
                                size: "small",
                            },
                            css: {
                                selector: ".animated",
                                properties: {
                                    'entrance-animation': ""
                                }
                            }
                        },
                      ]
                    }
                );
            }
            return style;
        }
    }
};
</script>
