                            }
                        },
                        "iconBorderRadiusnormalmode": {
                            "top": "63%",
                            "left": "63%",
                            "bottom": "63%",
                            "right": "63%"
                        }
                    },
                    "iconListGeneral": {
                        "listJustifyContent": "flex-start"
                    },
                    "iconListText": {
                        "textTypography": {
                            "global-typography": "AbgUXj49"
                        },
                        "textTextAlign": "center",
                        "spacing": {
                            "margin": {
                                "top": "10px"
                            }
                        }
                    },
                    "iconListDescription": {
                        "textTypography": {
                            "font-family": {
                                "name": "Jost",
                                "value": "https://fonts.googleapis.com/css?family=Jost:100,200,300,400,500,600,700,800,900",
                                "thumbnail": "Jost"
                            },
                            "font-size": "16px",
                            "line-height": "23px"
                        },
                        "textTextAlign": "center",
                        "textColor": "#696969"
                    }
                }
            };
        }
    },
    methods: {
        hasTitle() {
            return this.data.settings.items?.some((x) => x.label);
        },
        hasDesc() {
            return this.data.settings.items?.some((x) => x.desc);
        },

        style() {
            let style = [];
            if (this.data.settings?.items) {
                style.push(
                    {
                        group_name: "iconListGeneral",
                        group_title: "General",
                        selector: " .ecom-base-iconlist .ecom-iconlist--list",
                        params: [
                            this.data.settings.layout == "inline"
                                ? {
                                      type: "choose",
                                      label: "Alignment",
                                      name: "listJustifyContent",
                                      options: {
                                          oneline: true,
                                          type: "text-align",
                                          values: ["flex-start", "center", "flex-end"]
                                      },
                                      css: {
                                          properties: {
                                              display: "flex",
                                              "justify-content": ""
                                          }
                                      }
                                  }
                                : {
                                      type: "choose",
                                      label: "Alignment",
                                      name: "listJustifyContent",
                                      options: {
                                          oneline: true,
                                          type: "text-align",
                                          values: ["flex-start", "center", "flex-end"]
                                      },
                                      css: {
                                          selector: " .ecom-iconlist--item",
                                          properties: {
                                              display: "flex",
                                              "align-items": ""
                                          }
                                      }
                                  },
                            {
                                alias: 'width',
                                options: {
                                    css: {
                                        selector: 'root .ecom-iconlist--item'
                                    }
                                }
                            },
                            {
                                type: "background",
                                label: "Background Type",
                                name: "background",
                                options: {
                                    responsive: true,
                                    types: ["classic", "gradient"]
                                },
                                css: {
                                    selector: " .ecom-iconlist--item",
                                    properties: {
                                        background: ""
                                    }
                                }
                            },
                            {
                                type: 'popup',
                                label: 'Border',
                                name: 'boxBorder',
                                options: {
                                    type: 'border',
                                    oneline: true
                                },
                                css:{
                                    selector : " .ecom-iconlist--item",
                                    properties : {
                                        border : ""
                                    }
                                },
                            },
                            {
                                name: 'boxBorderRadius',
                                label: 'Border Radius',
                                type: 'dimension',
                                options: {
                                    responsive: true,
                                    type: 'radius',
                                    units: {
                                        px: {
                                            min: 0,
                                            max: 1000
                                        },
                                        '%': {
                                            min: 0,
                                            max: 100
                                        }
                                    }
                                },
                                css:{
                                    selector : " .ecom-iconlist--item",
                                    properties:{
                                        overflow:'hidden',
                                        'border-radius': ''
                                    }
                                }
                            },
                            {
                                alias: "spacing",
                                options: {
                                    css: {
                                        selector: " .ecom-iconlist--item"
                                    }
                                }
                            }
                        ]
                    },
                    {
                        group_title: "Icon",
                        group_name: "iconListIcon",
                        selector: " .ecom-base-iconlist .ecom-iconlist--icon",
                        params: [
                            {
                                type: "choose",
                                label: "Alignment",
                                name: "itemAlignSelf",
                                options: {
                                    oneline: true,
                                    type: ['row', 'row-reverse'].includes(this.data?.settings?.flexDirection) ? 'align-y-full' : 'align-x-full',
                                    values: ["flex-start", "center", "flex-end"]
                                },
                                css: {
                                    properties: {
                                        "align-self": ""
                                    }
                                }
                            },
                            ...this.$helpers.copy(
                                iconHover("root .ecom-iconlist--container:hover .ecom-iconlist--icon").params
                            ),
                            {
                                type: "line"
                            },
                            {
                                alias: "spacing"
                            }
                        ]
                    }
                );
            }
            if (this.hasTitle()) {
                style.push({
                    group_alias: "text:spacing",
                    options: {
                        group_title: "Label",
                        group_name: "iconListText",
                        selector: " .ecom-base-iconlist .ecom-iconlist--title"
                    }
                });
            }
            if (this.hasDesc()) {
                style.push({
                    group_alias: "text:spacing",
                    options: {
                        group_title: "Description",
                        group_name: "iconListDescription",
                        selector: " .ecom-base-iconlist .ecom-iconlist--des"
                    }
                });
            }
            return style;
        }
    }
};
</script>
