<template>
    <div class="ecom-element ecom-base-elements ecom-element__lightbox">
        <div class="ecom-element__lightbox-wrapper">
            <div
                class="ecom-element__lightbox-container ecom-flex"
                v-if="data.template === 'product'"
                :class="!exporting ? 'ecom-placeholder-on-builder-mode' : ''"
                :data-ecom-placeholder="exporting ? '' : 'This product not match with your condition'"
                v-html="liquid('product')"
            />
            <div
                class="ecom-element__lightbox-container ecom-flex"
                v-else
            >
                <button v-if="data.settings.general !== 'image'" ecom-modal="inline"
                        class="ecom__element--button ecom-elements__lightbox-button ecom-flex ecom-al_center ecom-fl_center"
                        :ecom-modal-source="popupSrc"
                >
                    <span  class="ecom-elements__lightbox-button-text" v-html="lang(data.settings.button_title,'lightbox_button_title')" />
                    <span class="ecom-button-icon ecom-elements__lightbox-button-icon ecom-flex" v-html="data.settings.icon" />
                </button>

                <button v-if="data.settings.general == 'image' && imageHandled?.value" ecom-modal="inline"
                        class="ecom-elements__lightbox-button-image"
                        :ecom-modal-source="popupSrc"
                >
                    <div class="ecom-image-default">
                        <picture class="ecom-lightbox-picture" v-html="$helpers.renImageResponsive(imageHandled, screens)" />
                    </div>
                </button>
                <div v-if="data.settings.source_type !== 'page'" style="display:none" >
                    <div v-if="data.settings.source_type === 'custom'" class="ecom-html" :id="'ecom-elements__lightbox-content-' + element_id">
                        <div class="ecom-elements__lightbox-content-heading" v-html="lang(data.settings.custom_heading_content)" />
                        <div class="ecom-elements__lightbox-content-body" v-html="lang(data.settings.custom_content)" />
                    </div>
                    <div v-if="data.settings.source_type === 'video'" :id="'ecom-elements__lightbox-content-' + element_id" style="height:100%; width: 100%">
                        <video
                            style="display: flex !important;width: 100%; height:100%;"
                            v-if="data.settings?.source == 'self hosted'"
                            class="ecom_video"
                            controls
                            muted
                        >
                            <source :src="linkSelfHosted" />
                        </video>
                        <iframe
                            v-else
                            class="ecom_iframe"
                            frameborder="0"
                            allowfullscreen="1"
                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                            style="border: 0; width: 100%; height:100%; display: flex !important"
                            :src="data.settings?.source == 'vimeo' ? linkVimeo : linkYoutube"
                        >
                        </iframe>
                    </div>
                    <div v-if="data.settings.source_type == 'image'" :id="'ecom-elements__lightbox-content-' + element_id" style="height:100%; width: 100%; display:flex;">
                        <img :src="data.settings?.image_source?.value" style="object-fit: cover; width: 100%;">
                    </div>
                </div>
                <div v-else v-html="liquid('content')" />
            </div>
        </div>
    </div>
</template>

<script>
import { Liquid, Element, Javascript } from '@/mixins';
import {hover} from "@/plugins/fields/core/alias/icon";
export default {
    name: 'lightbox',
    docs: 'https://help.ecomposer.io/docs/elements/basic-elements/light-box/',
    vendors: ['modal_js', 'modal_css'],
    mixins: [Liquid, Element, Javascript],
    props: {
        data: {
            type: Object,
            default() {
                return {

                };
            }
        }
    },
    data(){
        return {
            jsreactives:['source_type','general']
        }
    },
    computed: {
        screens() {
            return this.$store.getters["builder/screens"];
        },

        imageHandled() {
            if (!this.data?.settings) {
                return;
            }
            let image = { ...this.data.settings.image_popup };
            return {
                alt: image.name,
                ...image
            };
        },
        linkSelfHosted() {
            if (this.data.settings?.link) {
                let check = this.data.settings.link.match(
                    /(http(s)?:\/\/.)?(www\.)?[-a-zA-Z0-9@:%._+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_+.~#?&=]*)/
                );
                if (check[0] == "https://drive.google.com") {
                    const regex = /\/file\/d\/(.+)\/(.+)/;
                    let val = this.video.link.match(regex);
                    let videoId = val ? val[1] : null;
                    return `${location.protocol}//drive.google.com/uc?export=download&id=${videoId}`;
                } else {
                    return (
                        this.data.settings.link
                    );
                }
            } else {
                return;
            }
        },

        linkYoutube() {
            if (this.data.settings?.srcYoutube) {
                const regExpYt = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|&v=)([^#&?]*).*/;
                let val = this.data.settings.srcYoutube.match(regExpYt);
                let videoIdYt = val && val[2].length === 11 ? val[2] : null;
                return `${location.protocol}//www.youtube.com/embed/${videoIdYt}?version=3&autoplay=1&mute=1`;
            } else {
                return "";
            }
        },
        linkVimeo() {
            if (this.data.settings?.srcVimeo) {
                const regExpVm = /(?:http?s?:\/\/)?(?:www\.)?(?:vimeo\.com)\/?(.+)/;
                let vals = this.data.settings.srcVimeo.match(regExpVm);
                let videoIdVm = vals ? vals[1] : null;
                return `${
                    location.protocol
                }//player.vimeo.com/video/${videoIdVm}?rel=0?version=3&autoplay=1&mute=1`;
            } else {
                return "";
            }
        },

        element_id() {
            return this.data.id;
        },
        popupSrc() {
            let src = '#ecom-elements__lightbox-content-' + this.element_id
            return src;
        },
        liquids() {
            if(this.data.template !== 'product')
            {
                return {
                    content: {
                        code: `
                            ${this.data.settings && this.data.settings.source_type === 'page' ? `
                                <div class="ecom-html ecom-elements__lightbox-content" id="ecom-elements__lightbox-content-${this.element_id}" style="display:none">
                                    <div>
                                        {%- capture page_handle -%}${this.data.settings.page ? this.data.settings.page.value: ''}{%- endcapture-%}
                                        {% assign p = pages[page_handle] %}
                                        {% if p %}{{p.content}}{% endif %}
                                    </div>
                                </div>

                            `: ''}
                        `,
                        preview: ''
                    }
                }
            }
            return {
                product: {
                    code: `
                            {%- capture show_when -%}${this.data.settings.show_when}{%- endcapture -%}
                            {%- capture value -%}${this.data.settings.value}{%- endcapture -%}
                            {%- assign check = false -%}
                            {%- case show_when-%}
                                {%- when 'all' -%}
                                    {% assign check = true %}
                                {%- when 'option'- %}
                                    {% if value and  product.options contains value %}
                                        {% assign check = true %}
                                    {% endif %}
                                {%- when 'tag'-%}
                                    {%- if value and  product.tags contains value -%}
                                        {% assign check = true %}
                                    {%- endif  -%}
                                {% when 'type' %}
                                    {%- if value and  product.type contains value -%}
                                        {% assign check = true %}
                                    {%- endif  -%}
                                {% when 'vendor' %}
                                    {%- if value and  product.vendor contains value -%}
                                        {% assign check = true %}
                                    {%- endif  -%}
                            {%- endcase -%}
                            {%- if check -%}
                                {%- capture type -%}${this.data.settings.source_type}{%- endcapture -%}
                                    ${
                                        this.data.settings.general !== 'image' ? `
                                        <button ecom-modal="inline" class="ecom__element--button ecom-elements__lightbox-button ecom-flex ecom-fl_cener ecom-al_center" ecom-modal-source="#ecom-elements__lightbox-content-${this.element_id}">
                                            <span class="ecom-elements__lightbox-button-text" >
                                                ${this.lang(this.data.settings.button_title,'lightbox_button_title')}
                                            </span>
                                            <span class="ecom-button-icon ecom-elements__lightbox-button-icon">
                                                ${this.data.settings.icon ? this.data.settings.icon : ''}
                                            </span>
                                        </button>
                                        ` : ''
                                    }
                                    ${
                                        this.data.settings.general == 'image' && this.imageHandled?.value ? `
                                            <button ecom-modal="inline"
                                                    class="ecom-elements__lightbox-button-image"
                                                    ecom-modal-source="#ecom-elements__lightbox-content-${this.element_id}"
                                            >
                                                <div class="ecom-image-default">
                                                    <picture class="ecom-lightbox-picture">
                                                        ${this.$helpers.renImageResponsive(this.imageHandled, this.screens)}
                                                    </picture>
                                                </div>
                                            </button>
                                        ` : ''
                                    }

                                    {%- if type != 'page' -%}
                                        <div style="display:none" >
                                            {%- if type == 'custom' -%}
                                                <div class="ecom-html" id="${'ecom-elements__lightbox-content-' + this.element_id}">
                                                    <div class="ecom-elements__lightbox-content-heading">${this.lang(this.data.settings.custom_heading_content)}</div>
                                                    <div class="ecom-elements__lightbox-content-body">${this.lang(this.data.settings.custom_content)}</div>
                                                </div>
                                            {%- elsif type == 'video' -%}

                                                <div id="${'ecom-elements__lightbox-content-' + this.element_id}" style="height:100%">
                                                    ${
                                                        this.data.settings?.source == 'self hosted' ? `
                                                            <video
                                                                style="display: flex !important;width: 100%; height:100%;"
                                                                class="ecom_video"
                                                                controls
                                                            >
                                                                <source src="${this.linkSelfHosted}" />
                                                            </video>
                                                        ` : `
                                                            <iframe
                                                                class="ecom_iframe"
                                                                frameborder="0"
                                                                allowfullscreen="1"
                                                                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                                                style="border: 0; width: 100%; height:100%; display: flex !important"
                                                                src="${this.data.settings?.source == 'vimeo' ? this.linkVimeo : this.linkYoutube}"
                                                            >
                                                            </iframe>
                                                        `
                                                    }


                                                </div>
                                            {%- else -%}
                                                <div id="${'ecom-elements__lightbox-content-' + this.element_id}" style="height:100%; width: 100%; display:flex;">
                                                    <img src="${this.data.settings?.image_source?.value}" alt="" style="object-fit: cover; width: 100%;">
                                                </div>
                                            {%- endif -%}
                                        </div>
                                    {%- else -%}
                                        <div class="ecom-elements__lightbox-content ecom-html" id="ecom-elements__lightbox-content-${this.element_id}" style="display:none">
                                            <div>
                                                {%- capture page_handle -%}
                                                    ${this.data.settings.page ? this.data.settings.page.value: ''}
                                                {%- endcapture-%}
                                                {% assign p = pages[page_handle] %}
                                                {% if p %}{{p.content}}{% endif %}
                                            </div>
                                        </div>
                                    {%- endif -%}
                            {%- endif -%}
                        `,
                    preview: `
                            <div style="display: block;position: relative;width: 100%;">
                                <div class="ecom-skeleton-item">
                                    <div class="ecom-skeleton-col-12">
                                        <div class="ecom-skeleton-picture ecom-skeleton-big"></div>
                                        <div class="ecom-skeleton-row">
                                            <div class="ecom-skeleton-col-9 ecom-skeleton-big"></div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        `
                }
            };
        },
        javascript() {
            return function () {
                let $el = this.$el;
                var $el_id = this.id;
                if($el && window.EComModal) {
                    var popup_selector = $el.querySelector('[ecom-modal]');
                    new window.EComModal(popup_selector, {
                        cssClass: ['ecom-container-lightbox-' + this.id, 'ecom-html', 'ecom-modal' ,'ecommodal-slide'],
                        onOpen: function() {
                            const popup = document.querySelectorAll('.ecom-container-lightbox-' + $el_id);

                                if(popup.length) {
                                    popup.forEach(function(p) {
                                        const video = p.querySelector('video');
                                        if(video != null){
                                            video.play();
                                        }
                                    })
                                }
                        },
                        onClose: function() {
                                const popup = document.querySelectorAll('.ecom-container-lightbox-' + $el_id);

                                if(popup.length) {
                                    popup.forEach(function(p) {
                                        const popup_iframe = p.querySelector('iframe');
                                        if(popup_iframe != null){
                                            popup_iframe.src = popup_iframe.src;
                                        }

                                        const video = p.querySelector('video');
                                        if(video != null){
                                            video.pause();
                                        }
                                    })
                                }
                            }
                    });
                }

            };
        },
        settings() {
            const settings =  [
                {
                    params: [
                        {
                            type: 'popup',
                            label: 'General',
                            name: 'general',
                            options: {
                                type: 'dropdown',
                                default: false,
                                values: {
                                    image: 'Image',
                                    button: 'Button',
                                }
                            }
                        },
                        {
                            type: 'picker',
                            name: 'image_popup',
                            label: 'Select image',
                            options: {
                                type: 'image',
                                responsive: true,
                                multiple: false,
                                visible: function (data) {
                                    return data && data['general'] === 'image';
                                }
                            }
                        },
                        {
                            type: 'text',
                            label: 'Button label',
                            name: 'button_title',
                            options: {
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data["general"] === "button";
                                    }
                                }
                            }
                        },
                        {
                            type: 'picker',
                            label: 'Icon',
                            name: 'icon',
                            options: {
                                type: 'icon',
                                layout: 'grid',
                                output: 'value',
                                multiple: false,
                                simple: false,
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data["general"] === "button";
                                    }
                                }
                            }
                        },
                        {
                            type: 'choose',
                            label: 'Icon position',
                            name: 'icon_position',
                            options: {
                                type: 'align-x',
                                values: [-1, 1],
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['icon'] && data["general"] === "button";
                                    }
                                }
                            },
                            css:{
                                selector:' .ecom-button-icon',
                                properties:{
                                    order:''
                                }
                            }
                        },
                        {
                            type: 'number',
                            label: 'Icon spacing',
                            name: 'spacing',
                            options: {
                                units: {
                                    px: {
                                        min: 0,
                                        max: 200
                                    }
                                },
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['icon'];
                                    }
                                }
                            },
                            css:{
                                selector:' .ecom__element--button',
                                properties:{
                                    gap:''
                                }
                            }
                        },
                        {
                            type: 'popup',
                            label: 'Content source',
                            name: 'source_type',
                            options: {
                                type: 'dropdown',
                                default: false,
                                preview: 'title',
                                values: {
                                    page: 'Page',
                                    video: 'Video',
                                    image: 'Image',
                                    custom:'Custom content',
                                }
                            }
                        },
                        {
                            type: 'picker',
                            label: 'Choose Image',
                            name: 'image_source',
                            options: {
                                responsive: true,
                                type: 'image',
                                editAlt: false,
                                visible: function (data) {
                                    return data && data['source_type'] === 'image';
                                }
                            }
                        },
                        {
                            type: 'textarea',
                            label: 'Heading content',
                            name: 'custom_heading_content',
                            options: {
                                height: 120,
                                visible: function (data) {
                                    return data && data['source_type'] === 'custom';
                                }
                            }
                        },
                        {
                            type: 'textarea',
                            label: 'Content',
                            name: 'custom_content',
                            options: {
                                toolbar: 'full',
                                height: 250,
                                visible: function (data) {
                                    return data && data['source_type'] === 'custom';
                                }
                            }
                        },
                        {
                            type: 'picker',
                            name: 'page',
                            label: 'Select page',
                            description: 'This page content will appear.',
                            options: {
                                type: 'page',
                                layout: 'list',
                                multiple: false,
                                visible: function (data) {
                                    return data && data['source_type'] === 'page';
                                }
                            }
                        },
                        {
                            type: "popup",
                            label: "Source",
                            name: "source",
                            options: {
                                type: "dropdown",
                                preview: "title",
                                values: {
                                    youtube: "Youtube",
                                    vimeo: "Vimeo",
                                    "self hosted": "External URL"
                                },
                                default: false,
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data["source_type"] === "video";
                                    }
                                }
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            name: "link",
                            label: "External URL",
                            description: "Use link video share driver, .mp4, .oog,...",
                            type: "text",
                            options: {
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data["source"] === "self hosted" && data["source_type"] === "video";
                                    }
                                }
                            }
                        },
                        {
                            name: "srcYoutube",
                            label: "Youtube link",
                            type: "text",
                            options: {
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data["source"] === "youtube" && data["source_type"] === "video";
                                    }
                                }
                            }
                        },
                        {
                            name: "srcVimeo",
                            label: "Vimeo link",
                            type: "text",
                            options: {
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data["source"] === "vimeo" && data["source_type"] === "video";
                                    }
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            name: 'custom_content_popup',
                            label: 'Custom content popup',
                            options: {
                                values: {
                                    on: {
                                        label: 'YES',
                                        value: true
                                    },
                                    off: {
                                        label: 'NO',
                                        value: false
                                    }
                                }
                            }
                        },
                        {
                            type: 'number',
                            label: 'Width',
                            name: 'width',
                            placeholder: '300',
                            options: {
                                responsive:true,
                                units: {
                                    px: {
                                        min: 100,
                                        max: 1500
                                    }
                                },
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['custom_content_popup'];
                                    }
                                }
                            },
                            css: {
                                selector: ' .ecom-container-lightbox-' + this.data.id +' .ecom-modal-box__content',
                                wrapper:'body'
                            }
                        },
                        {
                            type: 'number',
                            label: 'Height',
                            name: 'height',
                            placeholder: '300',
                            options: {
                                responsive:true,
                                units: {
                                    px: {
                                        min: 100,
                                        max: 1500
                                    }
                                },
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['custom_content_popup']
                                    }
                                }
                            },
                            css: {
                                selector: ' .ecom-container-lightbox-' + this.data.id +' .ecom-modal-box__content',
                                wrapper:'body'
                            }
                        },
                    ]
                }
            ];
            if(this.data.template === 'product')
                {
                    settings[0].params.splice(1,0,{
                            type: 'popup',
                            label: 'Show when match condition',
                            name: 'show_when',
                            options: {
                                default:false,
                                type: 'dropdown',
                                values: {
                                    all: 'All product',
                                    tag: 'Product tag contains',
                                    type: 'Product type contains',
                                    vendor: 'Product vendor contains',
                                    option: 'Product option contains'
                                }
                            }
                        },
                        {
                            type: 'text',
                            label: 'Contains value',
                            name: 'value',
                            placeholder: 'Size',
                            description:
                                'Enter the product option name you want to have a size guide for',
                            options: {
                                visible: function (data) {
                                    return data && data['show_when'] && data['show_when'] !== 'all';
                                }
                            }
                        })
                }

            return settings;
        },

        css() {
            return `
                    .ecom-elements__lightbox-content {
                        background: #fff;
                        padding: 30px;
                        min-height: 100px;
                    }
                    .ecom-modal-box__content {
                        overflow-y: auto;
                    }
                    .ecom-element__lightbox-container > button {
                        cursor: pointer;
                    }
                    .ecom-modal--visible .ecom-elements__lightbox-content {
                        display: block !important;
                    }
                `;
        },

        requestShopifyType() {
            if(this.data.template === 'product')
            {
                return {
                    shopify_type: 'product'
                };
            }
            else
            {
                return {}
            }
        },

        default() {
            return {
                "settings": {
                    "button_title": "Click here",
                    "type": "image",
                    "source_type": "custom",
                    "general": "button",
                    "custom_content": "<h2 style=\"margin: 0px 0px 10px; padding: 0px; font-weight: 400; font-family: DauphinPlain; font-size: 24px; line-height: 24px; color: rgb(0, 0, 0); background-color: rgb(255, 255, 255);\"><strong style=\"text-align: justify; font-family: system-ui, -apple-system, \"Segoe UI\", Roboto, Helvetica, Arial, sans-serif, \"Apple Color Emoji\", \"Segoe UI Emoji\"; font-size: 14px; margin: 0px; padding: 0px;\">Lorem Ipsum</strong><span style=\"text-align: justify; font-family: system-ui, -apple-system, \"Segoe UI\", Roboto, Helvetica, Arial, sans-serif, \"Apple Color Emoji\", \"Segoe UI Emoji\"; font-size: 14px;\"> is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum</span><br></h2>",
                    "custom_content_popup": true,
                    "width": "838px",
                    "height": "458px",
                    "link": "https://cdn.shopify.com/s/files/1/0629/4392/0358/files/flower.webm?v=1649754044",
                    "srcVimeo": "https://vimeo.com/582518956",
                    "source": "youtube",
                    "show_when": "all",
                    "custom_heading_content": "What is Lorem Ipsum?"
                },
                "style": {
                    "box": {
                        "boxBackground": "#fcfcfc",
                        "spacing": {
                            "padding": {
                                "left": "13px",
                                "top": "13px",
                                "bottom": "13px",
                                "right": "13px"
                            },
                            "margin": {}
                        },
                        "boxBorderRadius": {
                            "top": "11px",
                            "left": "11px",
                            "bottom": "11px",
                            "right": "11px"
                        }
                    },
                    "button": {
                        "tab": "normal",
                        "buttonBackgroundnormalmode": {
                            "classic": {
                                "background-color": "#0691b1"
                            }
                        },
