<template>
    <div class="ecom-element  ecom__progress ecom-html">
        <div class="element__progress-wrapper">
            <div v-if="progressStyle == 'circle'" 
                class="el-progress__pie-circle">
                <div class="el-progress__percent-wrapper"
                    :data-percent="percent"
                >
                    <div class="el-progress__percent-chart">
                        <div class="el-progress__percent-outner" 
                        :style="{
                            backgroundColor:track
                        }">
                            <div class="el-progress__percent-inner">
                                <div v-if="percent" class="el-progress__pie-label">
                                    {{ percent }}
                                </div>
                                <div v-else class="el-progress__pie-label">
                                    0%
                                </div>
                            </div>
                        </div>
                        <svg>
                            <circle class="el-progress__circle" 
                            cx="50%" cy="50%" r="50%"
                            :style="{
                                '--stroke':progressColor
                                }"
                            >

                            </circle>
                        </svg>
                    </div>
                </div>
            </div>
            <div v-else class="el-progress__line">
                <div class="el-porgress__line-wrapper"
                    :style="{backgroundColor:track}"
                >   
                    <div class="el-progress__line-inner"
                        :style="{width:percent,'--progress-line':progressColor, '--progress-line-width': percent}"
                        :data-width-line="percent"
                    >
                        <div class="el-porgress__percent-line "
                            :class="[striped ? 'el-porgress__percent-line--striped': '', animated ? 'el-porgress__percent-line--animated' : '' ]"
                        >
                            <div v-if="percent" class="el-progress__pie-label">
                                {{ percent }}
                            </div>
                            <div v-else class="el-progress__pie-label">
                                0%
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {Element, Javascript } from '@/mixins';
export default {
    name: 'Progress',
    presets:true,
    mixins: [Element, Javascript],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    data() {
        return {
            jsreactives:[
                
            ]
        };
    },
    methods: {
        refresh() {
            this.progress.refresh = this.$helpers.randid();
        }
    },
    computed: {

        striped(){
            return this.data.settings?.striped;
        },
        animated(){
            return this.data.settings.animated;
        },
        track() {
            return this.data.settings?.track;
        },
        progressColor() {
            return this.data.settings?.progress;
        },
        percent(){
            return this.data.settings?.percent;
        },
        progressStyle(){
            return this.data.settings?.progress_style;
        },
        getThicknessLine(){
            return this.data.settings?.thickness_line;
        },
        progress() {
            return this.data;
        },
        settings() {
            return [
                {
                    params: [
                        {
                            type: 'popup',
                            name: 'progress_style',
                            label: 'Progress style',
                            options: {
                                type: 'dropdown',
                                default: false,
                                values: {
                                    circle: 'Circle',
                                    line: 'Line'
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            label: 'Show striped',
                            name: 'striped',
                            value: true,
                            options: {
                                values:{
                                    on:{
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                },
                                visible: function (data) {
                                    return (
                                        data &&
                                        data['progress_style'] === 'line'
                                    );
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            label: 'Animation striped',
                            name: 'animated',
                            value: true,
                            options: {
                                values:{
                                    on:{
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                },
                                visible: function (data) {
                                    return (
                                        data &&
                                        data['progress_style'] === 'line' &&
                                        data['striped'] === true
                                    );
                                }
                            }
                        },
                        {
                            type: "number",
                            name: "percent",
                            label: "Percent",
                            "options": {
                                "responsive": false,
                                "units": {
                                    "%": {
                                    "min": 0,
                                    "max": 100
                                    }
                                }
                            },
                            "slider": true,
                            "input": true,
                            "min": 0,
                            "max": 100
                        },
                        {
                            type: "color",
                            name: "progress",
                            label: "Progress",
                            options: {
                                global: {
                                    type: "colors"
                                },
                                oneline: true,
                            }
                        },
                        {
                            type: "color",
                            name: "track",
                            label: "Track",
                            options: {
                                global: {
                                    type: "colors"
                                },
                                oneline: true,
                            }
                        },
                        {
                            type: "number",
                            name: "thickness",
                            label: "Thickness",
                            "options": {
                                visible: function (data) {
                                    return (
                                        data &&
                                        data['progress_style'] === 'circle'
                                    );
                                },
                                "responsive": true,
                                "units": {
                                    "px": {
                                        "min": 5,
                                        "max": 50
                                    }
                                }
                            },
                            "slider": true,
                            "input": true,
                            "min": 5,
                            "max": 50,
                            css: {
                                selector: "root .el-progress__percent-chart svg circle, root .el-progress__percent-outner",
                                properties: {
                                    "stroke-width": "%value%",
                                    "padding": "%value%",
                                    "r": "calc(50% - %value%/2)"
                                }
                            }
                        },
                        {
                            type: "number",
                            name: "thickness_line",
                            label: "Thickness",
                            "options": {
                                visible: function (data) {
                                    return (
                                        data &&
                                        data['progress_style'] === 'line'
                                    );
                                },
                                "responsive": true,
                                "units": {
                                    "px": {
                                        "min": 5,
                                        "max": 50
                                    }
                                }
                            },
                            "slider": true,
                            "input": true,
                            "min": 5,
                            "max": 50,
                            css: {
                                selector: "root .el-porgress__line-wrapper",
                                properties: {
                                    "height": "%value%"
                                }
                            }
                        }
                        
                    ]
                }
            ];
        },
        style() {
            return [
                {
                    group_alias: "text",
                    options: {
                        group_name: "progress_percent",
                        group_title: "Percent text",
                        selector: "root .el-progress__pie-label"
                    }
                }
            ];
        },
        css() {
            return `
                    .el-progress__percent-chart svg{
                        transform: rotate(-90deg);
                        width: 100%;
                        height: 100%;
                        position: absolute;
                    }
                    .el-progress__percent-chart svg circle{
                        fill:none;
                        stroke-width: 10px;
                        cx: 50%;
                        cy: 50%;
                        r: calc(50% - 5px);
                        stroke-linecap: round;
                        box-sizing: border-box;
                        stroke-dashoffset: var(--stroke-dasharray);
                        stroke-dasharray: var(--stroke-dasharray);   
                    }
                    .element__progress--percent-circle-animated{
                        -webkit-animation: progress-circle-anim ease 1s forwards;
                        animation: progress-circle-anim ease 1s forwards;
                    }
                    @keyframes progress-circle-anim{
                        0%{
                            stroke: var(--stroke);
                        }
                        100%{
                            stroke: var(--stroke);
                            stroke-dashoffset: var(--stroke-dashoffset);
                            stroke-dasharray: var(--stroke-dasharray);
                        }
                    }
                    @-webkit-keyframes progress-circle-anim{
                        0%{
                            stroke-dashoffset: var(--stroke-dasharray);

                        }
                        100%{
                            stroke-dashoffset: var(--stroke-dashoffset);
                        }
                    }
                    .el-progress__percent-chart{
                        width: 100%;
                        position: relative;
                        height: 100%;
                        padding-bottom: 100%;
                    }
                    .el-progress__percent-outner{
                        background-color: #f2f2f2;
                        position: absolute;
                        width: 100%;
                        height: 100%;
                        border-radius: 50%;
                        padding: 10px;
                    }
                    .el-progress__percent-inner{
                        background-color: white;
                        width:100%;
                        height: 100%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        border-radius: 50%;
                    }
                    .el-progress__pie-label{
                        width: 100%;
                    }
                    .el-porgress__line-wrapper{
                        background-color: rgba(0 0 0 /.1);
                        width: 100%;
                        height: 20px;
                        border-top-left-radius: 10px;
                        border-top-right-radius: 4px;
                        border-bottom-right-radius: 4px;
                        border-bottom-left-radius: 10px;
                    }
                    .el-progress__line-inner{
                        width: 0%;
                        transition: width ease 1s;
                        height: 100%;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        overflow: hidden;
                        color: #fff;
                        text-align: center;
                        white-space: nowrap;
                        border-top-left-radius: 4px;
                        border-top-right-radius: 4px;
                        border-bottom-right-radius: 4px;
                        border-bottom-left-radius: 4px;
                    }
                    .el-porgress__percent-line{
                        width:100%;
                        height: 100%;
                        display: flex;
                        align-items: center;
                    }
                    .el-progress__percent-line-animation{
                        animation: progress-percent-line ease 1s forwards;
                    }
                    @keyframes progress-percent-line{
                        0%{
                            background-color: var(--progress-line);
                            width: 0%;
                        }
                        100%{
                            background-color: var(--progress-line);
                            width: var(--progress-line-width);
                        }
                    }
                    .el-porgress__percent-line--striped{
                        background-image: linear-gradient(
                            -45deg, 
                            rgba(255, 255, 255, .2) 25%, 
                            transparent 25%, 
                            transparent 50%, 
                            rgba(255, 255, 255, .2) 50%, 
                            rgba(255, 255, 255, .2) 75%, 
                            transparent 75%, 
                            transparent
                        );
                        background-size: 20px 20px;
                    }
                    .el-porgress__percent-line--animated{
                        animation: progress-pie-move 2s linear infinite;
                    }
                    .ecom-animation.ecom-visibility--visible{
                        opacity: 1;
                    }
                    @keyframes progress-pie-move {
                        0% {
                            background-position: 0 0;
                        }
                        100% {
                            background-position: 50px 50px;
                        }
                    }
                `;
        },
        default() {
            return {
                    "settings": {
                        "progress_style": "circle",
                        "thickness": "20px",
                        "thickness__tablet": "15px",
                        "thickness__mobile": "10px",
                        "thickness_line": "30px",
                        "progress": "#ff2400",
                        "percent": "34%",
                        "track": "#f2f2f2",
                        "striped": true,
                        "animated": true
                        },
                        "style": {
                            "progress_percent": {
                            "textTextAlign": "center",
                            "textTypography": {
                                "font-size": "30px",
                                "font-weight": "600"
                            },
                            "textColor": "#403c3c"
                            }
                        }
                    }
        },

        javascript(){
            return function(){
                if(!this.$el)
                {
                    return false;
                }
                const $el = this.$el;
                function renderLine(){
                    const element_percent_wrapper = $el.querySelector('.el-porgress__line-wrapper');
                    const element_percent_line = $el.querySelector('.el-progress__line-inner');
                    if(!element_percent_line) return false;
                    element_percent_line.classList.remove("el-progress__percent-line-animation");
                    let element_label = element_percent_line.querySelector('.el-progress__pie-label');
                    element_label.innerHTML = '';
                    createObserver(element_percent_line);
                    function createObserver(element){
                        let options = {
                            root: null,
                            rootMargin: "0px",
                            threshold: 0.5
                        };
                        let observer = new IntersectionObserver(handleIntersect, options);
                        observer.observe(element);
                    }
                    function handleIntersect(entries, observer){
                        entries.forEach(function(entry){
                            if(entry.intersectionRatio > 0.0) {
                                // console.log(entry.target);
                                function countUp(element) {
                                    let x = 0;
                                    let y = parseInt(element_percent_line.dataset.widthLine)||0; 
                                    if(y==0) return;
                                    function addNum() {
                                        element.innerHTML = `${x}%`;
                                        x += 1;
                                        if (x > y) {
                                        clearInterval(timer);
                                        }
                                    }
                                    let timer = window.setInterval(addNum, 1000/parseInt(element_percent_line.dataset.widthLine));
                                }
                                element_percent_line.classList.add('el-progress__percent-line-animation');
                                if(element_percent_line.dataset.widthLine=='100%'){
                                    element_percent_wrapper.style.borderTopRightRadius = '10px';
                                    element_percent_wrapper.style.borderBottomRightRadius = '10px';
                                }
                                countUp(element_label);
                                observer.unobserve(element_percent_line);
                            }
                        });
                    }
                }
                function renderCircle() {
                    const elements_progress = $el.querySelectorAll(".el-progress__percent-wrapper");
                    const elements_circle = $el.querySelectorAll(".el-progress__circle");
                    if(!elements_progress || !elements_circle) return false;
                    elements_circle.forEach(function(element){
                        element.classList.remove("element__progress--percent-circle-animated");
                    });
                    elements_progress.forEach(function(element){
                        createObserver(element);
                    });
                    function createObserver(element) {
                        let options = {
                            root: null,
                            rootMargin: "0px",
                            threshold: 0.5
                        };

                        
                        let observer = new IntersectionObserver(handleIntersect, options);
                        observer.observe(element);
                    }
                   
                    function handleIntersect(entries, observer) {
                        entries.forEach((entry) => {
                            if (entry.intersectionRatio > 0.0) {
                                function countUp(element) {
                                    let x = 0;
                                    let y = parseInt(entry.target.dataset.percent)||0; 
                                    if(y==0) return;
                                    function addNum() {
                                        element.innerHTML = `${x}%`;
                                        x += 1;
                                        if (x > y) {
                                        clearInterval(timer);
                                        }
                                    }
                                    let timer = window.setInterval(addNum, 1000/parseInt(entry.target.dataset.percent));
                                }
                                let element_circle = entry.target.querySelector(".el-progress__circle");
                                let elements_label = entry.target.querySelector(".el-progress__pie-label")
                                element_circle.classList.add("element__progress--percent-circle-animated");
                                countUp(elements_label);
                                observer.unobserve(entry.target);
                            } 
                        });
                    }
                    let items = [];
                    elements_progress.forEach((element) => {
                        if(element.dataset.percent){
                            items.push(parseInt(element.dataset.percent));
                        }
                        else{
                            items.push(0);
                        }
                    });
                    items.forEach((item,index) => {
                        let width = parseFloat(elements_circle[index].getBoundingClientRect().width);
                        let dasharray = (width)*Math.PI;
                        let dashoffset = dasharray;
                        if(item){
                            dashoffset = (width)*Math.PI - (width)*Math.PI*item/100;
                        }
                        elements_circle[index].style.setProperty('--stroke-dasharray',dasharray);
                        elements_circle[index].style.setProperty('--stroke-dashoffset',dashoffset);
                    });
                }
                let ro = new ResizeObserver(function(entries) {
                    entries.forEach(function(entry){
                        if(entry.contentBoxSize){
                            setTimeout(renderCircle,150);
                            setTimeout(renderLine);
                        }
                    });
                });
                
                // Observe one or multiple elements
                ro.observe($el);
                
                
            }
        }
    },
    watch:{
        'data.settings': {
            deep: true,
            handler: function (newVal) {
                
                this.refresh();
            }
        }
        
        
    },
    mounted(){
    }
};
</script>

