<template>
    <Liquid :data="liquidBefore" />
    <form
        v-if="(exporting === false || exporting === 'preview') && ['product', 'cart'].indexOf(shopify_type) > -1"
        method="post"
        :action="shopify_type == 'product' ? '/cart/add' : '/cart/update'"
        :class="shopify_type == 'product' ? 'ecom-product-form ecom-product-form--single' : 'ecom-cart-form'"
        enctype="multipart/form-data"
        @submit.prevent="false"
    >
        <Blocks
            :data="$parent.gblocks(data)"
            :deep="deep"
            :index="index"
            :control="control"
            :section_id="section_id"
        />
    </form>
    <Blocks
        v-else
        :data="$parent.gblocks(data)"
        :deep="deep"
        :index="index"
        :control="control"
        :section_id="section_id"
    />
    <Liquid :data="liquidAfter" />
</template>

<script>
    import { ProcessLiquid } from '@/mixins';

    export default {
        name: 'GroupItem',
        mixins: [ProcessLiquid],
        inject: ['exporting'],
        props: {
            data: {
                type: Object,
                default () {
                    return {};
                }
            },
            index: {
                type: Number,
                default: 0
            },
            deep: {
                type: Number,
                default: 0
            },
            control: {
                type: Boolean,
                default: true
            },
            'section_id': {
                type: String,
                default: ''
            },
        },

        computed: {}

    };

</script>