<template>
    <div class="ecom-element ecom-blog ecom-shopify__blog--title">
        <div class="ecom-shopify__blog--title-text-wrapper">
            <component
                :is="heading"
                class="ecom-shopify__blog--title-text"
                :class="classes"
                :href="liquid('url')"
                v-html="liquid('title')"
            />
        </div>
    </div>
</template>

<script>
import { Liquid, Element } from '@/mixins';
export default {
    name: 'BlogTitle',
    
    mixins: [Liquid, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        classes() {
            return this.data && this.data.settings && this.data.settings.classes
                ? this.data.settings.classes
                : '';
        },

        heading() {
            return this.data && this.data.settings && this.data.settings.tag
                ? this.data.settings.tag
                : 'h3';
        },

        liquids() {
            return {
                title: {
                    code: `
                        {%- if  blog != blank -%}
                            {{blog.title}}
                        {%- else -%}
                            ${this.exporting ? '<div>No blog yet!</div>' : '<div>Please select the blog in settings</div>'}
                        {%- endif -%}
                    `,
                    preview: `
                        <div class="ecom-skeleton-item">
                            <div class="ecom-skeleton-col-12">
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-9 ecom-skeleton-big"></div>
                                </div>
                            </div>
                        </div>
                    `
                },
                url: {
                    code: '{{ blog.url }}',
                    preview: '#'
                }
            };
        },

        settings() {
            return [
                {
                    group_alias:'title:settings',
                    options:{
                        group_name:'',
                        group_title:''
                    }
                }
            ];
        },

        requestShopifyType() {
            return {
                shopify_type: 'blog'
            };
        },

        style() {
            let style = [
                {
                    group_alias: 'text:spacing',
                    options:{
                        group_title: '',
                        selector: ' .ecom-shopify__blog--title-text'
                    }
                }
            ];
            return style;
        },

        default() {
            return {
                settings: {
                    tag: "h2",
                    classes: ""
                },
                style: {
                    textSpacing: {
                        textTextAlign: "left"
                    }
                }
            }
        }
    }
};
</script>
