<template>
    <div
        class="ecom-element ecom-product-single ecom-product-single__inventory"
    >
        <div class="ecom-product-single__inventory-wrapper">
            <div class="ecom-product-single__inventory-container" v-html="liquid('inventory')" />
            <div v-if="data.settings?.enable_progress_bar" class="ecom-product-single__inventory-progress-bar">
                <div class="ecom-product-single__inventory-progress-bar--timer" />
            </div>
            <div v-if="!exporting" class="ecom-product-single_inventory-variant"
                v-html="liquid('product_json')"
            >
            </div>
        </div>
    </div>
</template>

<script>
    import { Element, Javascript, Liquid } from '@/mixins';
    export default {
        name: 'Product Inventory',

        mixins: [Element, Javascript, Liquid],
        props: {
            data: {
                type: Object,
                default () {
                    return {};
                }
            }
        },
        data(){
            return {
                jsreactives: ['inventory_text','unavailable_text','incoming_date_text','show_next_incoming_date','when_less_than']
            };
        },
        computed: {
            element_id() {
                return this.data.id;
            },
            liquids () {
                let tag = 'script';
                return {
                    inventory: {
                        code: `
                                {%-liquid
                                    if use_variant
                                        assign target = product.selected_or_first_available_variant
                                    else
                                        assign target = product
                                    endif

                                     assign stock_class = 'ecom-product-single-status--instock'
                                    if target.available == false
                                        assign stock_class = 'ecom-product-single-status--outstock'
                                    endif
                                -%}
                                <p
                                    class="ecom-product-single__inventory-status {{stock_class}}"
                                    data-product_id = "{{product.id}}"
                                    data-instock-text="${this.lang(this.data.settings.instock_text,'instock_text')}"
                                    data-outstock-text="${this.lang(this.data.settings.outstock_text,'outstock_text')}"
                                    data-inventory-text="${this.lang(this.data.settings.inventory_text,'inventory_text')}"
                                    data-show-less-than="${this.data.settings.when_less_than}"
                                    data-preorder-text="${this.lang(this.data.settings?.preorder_text,'preorder_text')}"
                                    data-show-next-incoming-date="${this.data.settings.show_next_incoming_date}"
                                    data-unavailable-text="${this.data.settings.unavailable_text}"
                                    data-incoming-date-text="${this.data.settings.incoming_date_text}"
                                >
                                    ${this.lang(this.data.settings.instock_text,'instock_text')}
                                </p>
                        `,
                        preview: `<div class="ecom-skeleton-item ecom__row">
                                <div class="ecom-skeleton-col-12 ecom-col-12">
                                    <div class="ecom-skeleton-row">
                                        <div class="ecom-skeleton-col-8 ecom-skeleton-big"></div>
                                    </div>
                                </div>
                            </div>`
                    },
                    product_json: {
                        code: `
                        {%- capture ecom_product_json -%}
                            {
                                "id":  {{ product.id }},
                                "title":  {{ product.title | json }},
                                "handle":  {{ product.handle | json }},
                                "price":  {{ product.price| json }},
                                "price_min":  {{ product.price_min| json }},
                                "price_max":  {{ product.price_max| json }},
                                "available":  {{ product.available | json }},
                                "price_varies":  {{ product.price_varies| json }},
                                "compare_at_price":  {{ product.compare_at_price| json }},
                                "compare_at_price_min":  {{ product.compare_at_price_min| json }},
                                "compare_at_price_max":  {{ product.compare_at_price_max| json }},
                                "compare_at_price_varies":  {{ product.compare_at_price_varies| json }},
                                "variants": [
                                    {%- for variant in product.variants -%}
                                    {
                                        "id":  {{ variant.id }},
                                        "title": " {{ variant.option1 | handleize }}{%- if product.options.size == 2 -%} / {%- endif -%} {{ variant.option2 | handleize }}{%- if product.options.size == 3 -%} / {%- endif -%} {{ variant.option3 | handleize }}",
                                        "option1":  {{ variant.option1  | json }},
                                        "option2": {%- if product.options.size == 1 -%}null{%-else-%} {{ variant.option2  | json }}{%-endif-%},
                                        "option3": {%- if product.options.size == 1 or product.options.size == 2 -%}null{%-else-%} {{ variant.option3  | json }}{%-endif-%},
                                        "sku":  {{ variant.sku | json }},
                                        "requires_shipping":  {{ variant.requires_shipping }},
                                        "taxable":  {{ variant.taxable |json }},
                                        "selling_plan_allocations":{%- if variant.selling_plan_allocations and variant.selling_plan_allocations.size > 0 -%}[{% for allocation in variant.selling_plan_allocations %}{"price":{{allocation.price}},"selling_plan":{"id":{{allocation.selling_plan.id}}, "name":{{allocation.selling_plan.name | json }} } }{%unless forloop.last%},{% endunless%}{%- endfor -%}]{%- else -%}null{%- endif -%},
                                        "featured_image": {%- if variant.image.id != blank -%}{
                                            "id":  {{ variant.image.id | json }},
                                            "product_id":  {{ product.id | json }},
                                            "width":  {{ variant.image.width | json }},
                                            "height":  {{ variant.image.height | json }},
                                            "src":  {{ variant.image.src | json }},
                                            "position": {{ variant.image.position }}
                                        }{%- else-%}null{%-endif-%},
                                        "available":  {{ variant.available }},
                                        "public_title": " {{ variant.option1 | handleize }}{%- if product.options.size == 2 -%} / {%- endif -%} {{ variant.option2 | handleize }}{%- if product.options.size == 3 -%} / {%- endif -%} {{ variant.option3 | handleize }}",
                                        "options":   {{ variant.options | json }},
                                        "price":  {{ variant.price| json }},
                                        "weight":  {{ variant.weight| json }},
                                        "unit_price": {{ variant.unit_price | json }},
                                        "unit_price_measurement": {
                                            "measured_type": {{ variant.unit_price_measurement.measured_type | json }},
                                            "quantity_value": {{ variant.unit_price_measurement.quantity_value | json }},
                                            "quantity_unit": {{ variant.unit_price_measurement.quantity_unit | json }},
                                            "reference_value": {{ variant.unit_price_measurement.reference_value | json }},
                                            "reference_unit": {{ variant.unit_price_measurement.reference_unit | json }}
                                        },
                                        "compare_at_price":  {{ variant.compare_at_price | json }},
                                        "inventory_quantity":  {{ variant.inventory_quantity | json }},
                                        "inventory_management":  {{ variant.inventory_management | json }},
                                        "inventory_policy":  {{ variant.inventory_policy | json }},
                                        "next_incoming_date": {% if variant.next_incoming_date %}{{variant.next_incoming_date | time_tag | json }}{% else%}null{% endif %},
                                        "barcode":  {{ variant.barcode | json }}
                                    }{%- if forloop.last == false -%},{%- endif -%}
                                    {%- endfor -%}
                                ],
                                "featured_image":  {{ product.featured_image | json }},
                                "options": [
                                    {%- for option in product.options -%}
                                    {{ option | json }}{%- if forloop.last == false -%},{%- endif -%}
                                    {%-endfor -%}
                                ]
                            }
                        {%- endcapture -%}
                        <${tag} type="application/json" id="inventory-product-json-{{product.id}}-${this.element_id}">
                            {{ ecom_product_json }}
                        </${tag}>
                        `,
                        preview: `<${tag} id="product-json-preview">{}</${tag}>`
                    }
                };
            },

            settings () {
                return [
                    {
                        params:[
                            {
                                type: 'text',
                                label: 'Instock label',
                                name: 'instock_text',
                                value: '',
                                placeholder: 'Instock'
                            },
                            {
                                type: 'text',
                                label: 'Outstock label',
                                name: 'outstock_text',
                                value: '',

                                placeholder: 'Outstock'

                            },
                            {
                                type: 'text',
                                name: 'unavailable_text',
                                label:'Unavailable text',
                            },
                            {
                                type: 'text',
                                name: 'preorder_text',
                                label:'PreOrder text',
                            },
                            {
                                type: 'text',
                                label: 'Inventory label',
                                name: 'inventory_text',
                                placeholder: '{quantity} in stock'
                            },
                            {
                                type: 'number',
                                label: 'Show product inventory when less than',
                                name: 'when_less_than',
                                description: 'Set 0 to disable',
                                options: {
                                    min: 0,
                                    max: 9999
                                }

                            },
                            {
                                type: 'toggle',
                                name: 'show_next_incoming_date',
                                label: 'Show next incoming date',
                                options: {
                                    values: {
                                        on: {
                                            label: 'Yes',
                                            value: true
                                        },
                                        off: {
                                            label: 'No',
                                            value: false
                                        }
                                    }
                                }
                            },
                            {
                                type: 'textarea',
                                label: 'Next incoming date text',
                                name:'incoming_date_text',
                                placeholder: 'Variant is out of stock. The product will available at: {next_incoming_date}',
                                options: {
                                    height: 80
                                }

                            },
                            {
                                name: 'enable_progress_bar',
                                label: 'Show progress bar',
                                value: true,
                                type: 'toggle',
                                options: {
                                    values: {
                                        on: {
                                            label: 'Yes',
                                            value: true
                                        },
                                        off: {
                                            label: 'No',
                                            value: false
                                        }
                                    }
                                }
                            },
                        ]
                    }
                ];
            },
            style () {
                let style = [
                    {
                        group_alias: 'box',
                        options: {
                            group_title: 'General',
                            group_name: 'genetal',
                            selector:' .ecom-product-single__inventory-container'
                        },
                        modify:{
                            params:{
                                alias:'justify-content',
                                options:{
                                    label:'Alignment'
                                }
                            },
                            remove:{
                                index:1,
                                length:1
                            }
                        }
                    },
                    {
                        group_alias: 'button:label',
                        options: {
                            group_title: 'Instock text',
                            group_name: 'instock',
                            selector: ' .ecom-product-single-status--instock'
                        }
                    },
                    {
                        group_alias: 'button:label',
                        options: {
                            group_title: 'Outstock text',
                            group_name: 'outstock',
                            selector: ' .ecom-product-single-status--outstock'
                        }
                    },
                    {
                        group_alias: 'button:label',
                        options: {
                            group_title: 'Unavailable text',
                            group_name: 'unavailable',
                            selector: ' .ecom-product-single-status--unavailable'
                        }
                    },
                    {
                        group_alias: 'button:label',
                        options: {
                            group_title: 'Preorder text',
                            group_name: 'preorder',
                            selector: ' .ecom-product-single-status--preorder'
                        }
                    }
                ];

                if (this.data.settings.enable_progress_bar) {
                    style.push({
                        group_name: 'progess_bar',
                        group_title: 'Progress bar',
                        selector: ' .ecom-product-single__inventory-progress-bar',
                        params: [
                            {
                                type: 'number',
                                name: 'maxWidth',
                                label: 'Width',
                                options: {
                                    responsive: true,
                                    reset: true,
                                    units: {
                                        '%': {
                                            min: 0,
                                            max: 100
                                        },
                                        px: {
                                            min: 0,
                                            max: 1000
                                        },
                                        vw: {
                                            min: 0,
                                            max: 100
                                        }
                                    }
                                },
                                css:{
                                    properties:{
                                        'max-width':''
                                    }
                                }
                            },
                            {
                                type: 'number',
                                name: 'height',
                                label: 'Height',
                                options: {
                                    responsive: true,
                                    reset: true,
                                    units: {
                                        px: {
                                            min: 0,
                                            max: 1000
                                        },
                                        vh: {
                                            min: 0,
                                            max: 100
                                        }
                                    }
                                },
                                css:{
                                    selector: ' .ecom-product-single__inventory-progress-bar--timer',
                                    properties:{
                                        'height':'',
                                        '--ecom-inventory-max-height':''
                                    }
                                }
                            },
                            {
                                name: 'tab',
                                type: 'tab',
                                value: 'normal',
                                options: {
                                    tabs: [{
                                            name: 'normal',
                                            title: 'Normal'
                                        },
                                        {
                                            name: 'active',
                                            title: 'Active'
                                        }
                                    ]
                                },
                                css: {
                                    isCss: false,
                                }
                            },
                            {
                                type: 'background',
                                label: 'Background',
                                name: 'background',
                                options: {
                                    oneline: true,
                                    visible: {
                                        keep_data: true,
                                        condition: (data) => {
                                            return data['tab'] === 'normal';
                                        }
                                    }
                                },
                                css:{
                                    properties:{
                                        'background':''
                                    }
                                }
                            },
                            {
                                type: 'dimension',
                                name: 'borderRadius',
                                label: 'Border radius',
                                options: {
                                    type: 'radius',
                                    units: 'default',
                                    visible: {
                                        keep_data: true,
                                        condition: (data) => {
                                            return data['tab'] === 'normal';
                                        }
                                    }
                                },
                                css:{
                                    properties:{
                                        'border-radius':''
                                    }
                                }
                            },
                            {
                                type: 'background',
                                label: 'Background',
                                name: 'backgroundActive',
                                options: {
                                    oneline: true,
                                    visible: {
                                        keep_data: true,
                                        condition: (data) => {
                                            return data['tab'] === 'active';
                                        }
                                    }
                                },
                                css:{
                                    selector: ' .ecom-product-single__inventory-progress-bar--timer',
                                    properties:{
                                        'background':''
                                    }
                                }
                            },

                            {
                                type: 'dimension',
                                name: 'borderRadiusActive',
                                label: 'Border radius',
                                options: {
                                    type: 'radius',
                                    units: 'default',
                                    visible: {
                                        keep_data: true,
                                        condition: (data) => {
                                            return data['tab'] === 'active';
                                        }
                                    }
                                },
                                css:{
                                    selector: ' .ecom-product-single__inventory-progress-bar--timer',
                                    properties:{
                                        'border-radius':''
                                    }
                                }
                            },
                            {
                                alias:'spacing'
                            }
                        ]
                    });
                }
                return style;
            },
            requestShopifyType () {
                return {
                    shopify_type: 'product'
                };
            },

            css(){
                return `
                    .ecom-product-single__inventory-status{
                        display:inline-block;
                        overflow:hidden;
                    }
                    .ecom-product-single__inventory-container{
                        display:flex;
                        flex-wrap:wrap;
                    }
                    .ecom-product-single__inventory-container .ecom-product-single__inventory-status {
                        margin:0;
                        padding:0;
                    }
                    .ecom-product-single__inventory-progress-bar {
                        display:none;
                        width:100%;
                    }
                     .ecom-builder .ecom-product-single__inventory-progress-bar{
                        display: block;
                    }
                    .ecom-product-single__inventory-progress-bar .ecom-product-single__inventory-progress-bar--timer{
                        width:30%;
                        display:block;
                    }
                    .ecom-product-single__inventory-progress-bar--timer {
                            background-image: -webkit-linear-gradient(45deg,rgba(255,255,255,.15) 25%,transparent 25%,transparent 50%,rgba(255,255,255,.15) 50%,rgba(255,255,255,.15) 75%,transparent 75%,transparent);
                            background-image: linear-gradient(45deg,rgba(255,255,255,.15) 25%,rgba(0,0,0,0) 25%,rgba(0,0,0,0) 50%,rgba(255,255,255,.15) 50%,rgba(255,255,255,.15) 75%,rgba(0,0,0,0) 75%,rgba(0,0,0,0));
                            -webkit-animation: 2s linear infinite ecom_progress_bar;
                            animation: 2s linear infinite ecom_progress_bar;
                            background-size: 60px 60px;
                            transition: width 1s;
                            -webkit-transition: width 1s;
                    }

                    @-webkit-keyframes ecom_progress_bar {
                        from {
                            background-position: 0 0
                        }

                        to {
                            background-position: 40px 0
                        }
                    }

                    @keyframes ecom_progress_bar {
                        from {
                            background-position: 0 0
                        }

                        to {
                            background-position: 40px 0
                        }
                    }
                `;
            },

            javascript(){
                if(this.exporting) {return function(){};}
                return function(){
                    const $el = this.$el;
                    if(!$el) return;

                    let $wrapper = $el.closest('.ecom-product-form--single'),
                        $progressbar = $el.querySelector('.ecom-product-single__inventory-progress-bar'),
                        $timer = $el.querySelector('.ecom-product-single__inventory-progress-bar--timer');

                    function progressbar(variant) {
                       if($progressbar) {
                           const $dom = $el.querySelector('.ecom-product-single__inventory-status');
                           if($dom) {
                               if ($dom.dataset.showLessThan && variant.inventory_management) {
                                    const less = parseInt($dom.dataset.showLessThan);
                                    if (variant.inventory_quantity > 0 && less && variant.inventory_quantity <= less) {
                                        $progressbar.style.display = "block";
                                        let result = Math.round((variant.inventory_quantity / less) * 100);
                                        $timer.style.width = result + '%';
                                    } else {
                                        $progressbar.style.display = "none";
                                    }
                                } else {
                                    $progressbar.style.display = "none";
                                }
                           }

                       }
                   }

                    function updateInventory(variant) {
                        const $dom = $el.querySelector('.ecom-product-single__inventory-status');
                        if ($dom) {
                            if (!variant) {
                                $dom.innerHTML = $dom.dataset.unavailableText;
                                $dom.classList.add('ecom-product-single-status--unavailable');
                                $dom.classList.remove('ecom-product-single-status--outstock', 'ecom-product-single-status--instock','ecom-product-single-status--preorder');
                            } else if (!variant.available) {
                                if (
                                    variant.next_incoming_date &&
                                    $dom.dataset.showNextIncomingDate &&
                                    $dom.dataset.showNextIncomingDate === 'true' &&
                                    $dom.dataset.incomingDateText
                                ) {
                                    $dom.innerHTML = $dom.dataset.incomingDateText.replace(/\{.*\}/g, variant.next_incoming_date);
                                } else {
                                    $dom.innerHTML = $dom.dataset.outstockText;
                                }
                                $dom.classList.add('ecom-product-single-status--outstock');
                                $dom.classList.remove('ecom-product-single-status--instock', 'ecom-product-single-status--unavailable','ecom-product-single-status--preorder');
                            } else {
                                if (variant.inventory_policy === 'continue' && variant.inventory_quantity < 1) {
                                    $dom.innerHTML = $dom.dataset.preorderText;
                                    $dom.classList.add('ecom-product-single-status--preorder');
                                    $dom.classList.remove('ecom-product-single-status--outstock', 'ecom-product-single-status--unavailable','ecom-product-single-status--instock');
                                    if (
                                        variant.next_incoming_date &&
                                        $dom.dataset.showNextIncomingDate &&
                                        $dom.dataset.showNextIncomingDate === 'true' &&
                                        $dom.dataset.incomingDateText
                                    ) {
                                        const p = document.createElement('p');
                                        const dateHtml = document.createElement('p');
                                        dateHtml.innerHTML = variant.next_incoming_date;

                                        const dateString = dateHtml.querySelector('time') ? dateHtml.querySelector('time').innerHTML : '';
                                        const date = dateString.split('at')[0];

                                        p.innerHTML = $dom.dataset.incomingDateText.replace(/\{.*\}/g, date);
                                        $dom.appendChild(p);
                                    }

                                } else {
                                    if ($dom.dataset.showLessThan && variant.inventory_management) {
                                        const less = parseInt($dom.dataset.showLessThan);
                                        if (less && variant.inventory_quantity <= less) {
                                            $dom.innerHTML = $dom.dataset.inventoryText.replace(
                                                /\{.*\}/g,
                                                variant.inventory_quantity
                                            );
                                        } else {
                                            $dom.innerHTML = $dom.dataset.instockText;
                                        }
                                    } else {
                                        $dom.innerHTML = $dom.dataset.instockText;
                                    }
                                    $dom.classList.add('ecom-product-single-status--instock');
                                    $dom.classList.remove('ecom-product-single-status--outstock', 'ecom-product-single-status--unavailable','ecom-product-single-status--preorder');
                                }

                            }
                        }
                    }
                    if(!$wrapper) {
                        return;
                    }
                    let selector = $wrapper.querySelector('select[name=id]');
                    const inventory = $el.querySelector('.ecom-product-single__inventory-status');
                    if(!inventory) return;
                    const product_id = inventory.dataset.product_id;
                    if(!product_id) return;

                    let product = null;
                    let json = $wrapper.querySelector('[id^=product-json-' + product_id + ']');
                    if(!json) json = $wrapper.querySelector('[id^=inventory-product-json-' + product_id + ']');
                    if (!json) {
                        return;
                    }
                    try {
                        product = JSON.parse(json.innerHTML);
                    } catch (e) {
                        return;
                    }
                    let variant = product.variants[0];
                    updateInventory(variant);
                    progressbar(variant);

                    if(selector) {
                        selector.addEventListener('change', function() {
                            let variant_id = this.value;
                            if(!variant_id) return;

                            let variant = product.variants.find( variant => variant.id == variant_id);
                            if(variant) {
                                updateInventory(variant);
                                progressbar(variant);
                            }
                        })
                    }
                };
            },

            default() {
                return {
                    "settings": {
                        "enable_progress_bar": true,
                        "instock_text": "Instock",
                        "outstock_text": "Outstock",
                        "inventory_text": "{quantity} in stock",
                        "when_less_than": 100,
                        "show_next_incoming_date": true,
                        "preorder_text": "Preorder text",
                        "unavailable_text": "Unavailable",
                        "incoming_date_text": "Variant is out of stock. The product will available at: {next_incoming_date}"
                    },
                    "style": {
                        "progess_bar": {
                            "tab": "normal",
                            "height": "15px",
                            "background": {
                                "classic": {
                                    "background-color": "#c7b1b1"
                                }
                            },
                            "backgroundActive": {
                                "classic": {
                                    "background-color": "#e34949"
                                }
                            }
                        },
                        "genetal": {
                            "justify-content": "flex-start"
                        }
                    }
                }
            }
        },
    };
</script>
