<template>
    <div class="ecom-element ecom-product-single ecom-product-single__buy_it_now_btn" :data-preview-mode="exporting.toString()">
        <div class="ecom-product-single__buy_it_now_btn-buttons-wrapper">
            <button
                type="button"
                class="ecom-product-single__buy_it_now_btn--checkout"
                :class="animatedClass"
                
            >
                <span v-html="lang(data.settings?.label, 'button_label')" />
                <span
                    v-if="data.settings?.icon"
                    :style="'order:' + data.settings?.icon_position"
                    class="ecom-button-icon ecom-product-single__buy_it_now-icon-link ecom-in_flex"
                    v-html="data.settings?.icon"
                >
                </span>
            </button>
        </div>
    </div>
</template>

<script>
import { Liquid, Javascript, Element } from '@/mixins';
export default {
    name: 'BuyNowButton',

    mixins: [Liquid, Javascript, Element],
    props: {

        data: {
            type: Object,
            default() {
                return {
                };
            }
        }
    },
    data(){
        return {
            jsreactives: [
                'animation_loop_time', 'animation'
            ]
        }
    },
    computed: {
        javascript() {
            return function () {
                if(!this.$el)
                {
                    return false;
                }
                const $el = this.$el;
                let $wrapper = $el.closest('.ecom-product-form--single');
                if(!$wrapper){
                    const section = $el.closest('.ecom-builder');
                    if(section){
                        $wrapper = section.querySelector('.ecom-product-form--single');
                    }
                }
                if (!$wrapper) {
                    return false;
                }
                if(this.isLive)
                {
                    let selector = $wrapper.querySelector('select[name=id]');
                    const buyNowBtn = $el.querySelector('.ecom-product-single__buy_it_now_btn--checkout');
                    const product_id = $wrapper.dataset.product_id;
                    if(!product_id) return;
                    let product = null;
                    const json = $wrapper.querySelector('[id^=product-json-' + product_id + ']');
                    if (!json) {
                        return;
                    }
                    try {
                        product = JSON.parse(json.innerHTML);
                    } catch (e) {
                        return;
                    }
                    if(buyNowBtn) {
                        buyNowBtn.addEventListener('click', function(e){
                            e.preventDefault();
                            let quantity = 1;
                            if($wrapper.querySelector('[name=quantity]')){
                                quantity = $wrapper.querySelector('[name=quantity]').value;
                            }
                            let id = $wrapper.querySelector('[name=id]').value;
                            let variant = product.variants.find(variant => variant.id == id);
                            if(!variant || variant.available === false){
                                return;
                            }
                            if(window.Shopify.analytics)
                            {
                                Shopify.analytics.publish("ec_buy_now",   {
                                    "cartLine": {
                                        "cost": {
                                            "totalAmount": {
                                                "amount": variant.price * quantity,
                                                "currencyCode": window.Shopify.currency.active
                                            }
                                        },
                                        "merchandise": {
                                            "id": variant.id,
                                            "image": variant.image,
                                            "price": {
                                                "amount": variant.price,
                                                "currencyCode": window.Shopify.currency.active
                                            },
                                            "product": {
                                                "id":  product.id,
                                                "title":  product.title,
                                                "vendor": product.vendor
                                            },
                                            "sku": variant.sku,
                                            "title": variant.title
                                        },
                                        "quantity": quantity
                                    }
                                })
                            }
                            const link = `${window.EComposer.routes.cart_url}/${id}:${quantity}`
                            location.href = link;
                        });
                    }
                    
                    if(!product.available){
                        buyNowBtn.classList.add('ecom-disable-buy');
                        buyNowBtn.disabled = true;
                    }
                    if(selector){
                        selector.addEventListener('change', function() {
                            let variant_id = this.value;
                            if(!variant_id) return;
                            let variant = product.variants.find(variant => variant.id == variant_id);
                            if(variant.available === false){
                                buyNowBtn.classList.add('ecom-disable-buy');
                                buyNowBtn.disabled = true;
                            }
                            else{
                                buyNowBtn.classList.remove('ecom-disable-buy');
                                buyNowBtn.disabled = false;
                            } 
                        });
                    }
                }

                const handleAnimation = function(_this) {
                  if (!$el) return;

                  const $btn_wrapper = $el.querySelector('.ecom-product-single__buy_it_now_btn--checkout');
                   if (!$btn_wrapper) return;

                  let intervalTime = parseInt(_this.settings.animation_loop_time) * 1000 || 6000,
                      animTime = 1000;

                  setInterval(function() {
                      $btn_wrapper.classList.add('animated');
                      setTimeout(function(){
                        $btn_wrapper.classList.remove('animated');
                      }, animTime);
                  }, intervalTime);
                }

                if (this.settings.animation) {
                  handleAnimation(this);
                }
            };
        },
        animatedClass() {
          return this.data?.settings?.animation ? 'animated' : '';
        },
        liquids() {
        },
        requestShopifyType() {
            return {
                shopify_type: 'product'
            };
        },
        css() {
          return `
            .ecom-product-single__buy_it_now_btn--checkout {
                display: flex;
                justify-content: center;
                cursor: pointer;
                align-items: center;
            }
            .ecom-product-single__buy_it_now_btn-buttons-wrapper{
                display: flex;
                width: 100%;
            }
            .ecom-product-single__buy_it_now-icon-link svg{
                width: 20px;
                height: 20px;
            }
            .ecom-product-single__buy_it_now_btn--checkout.ecom-disable-buy{
                opacity: 0.6;
            }
          `;
        },
        settings() {
            return [
                {
                    params: [
                        {
                            type: 'paragraph',
                            options: {
                                warnings: {
                                    content: 'Not working with Subscription Product'
                                }
                            }
                        },
                        {
                            type: 'text',
                            name: 'label',
                            label: 'Button label'
                        },
                        {
                            type: 'picker',
                            label: 'Icon',
                            name: 'icon',
                            options: {
                                type: 'icon',
                                layout: 'grid',
                                output: 'value',
                                multiple: false,
                                simple: false
                            }
                        },
                        {
                            type: 'choose',
                            label: 'Icon position',
                            name: 'icon_position',
                            options: {
                                type: 'align-x',
                                values: [-1, 1],
                                visible: function(data){
                                    return (
                                        data && 
                                        data['icon']
                                    )
                                }
                            },
                            css:{
                                selector:' .ecom-button-icon',
                                properties:{
                                    order:''
                                }
                            }
                        },
                        {
                            type: 'number',
                            label: 'Icon spacing',
                            name: 'spacing',
                            options: {
                                units: {
                                    px: {
                                        min: 0,
                                        max: 200
                                    }
                                },
                                visible: function(data){
                                    return (
                                        data &&
                                        data['icon']
                                    )
                                }
                            },
                            css:{
                                selector:' .ecom-product-single__buy_it_now_btn--checkout',
                                properties:{
                                    gap:''
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            name: 'animation',
                            label: 'Button animation',
                            options: {
                                values: {
                                    on: {
                                        label: 'Enable',
                                        value: true
                                    },
                                    off: {
                                        label: 'Disable',
                                        value: false
                                    }
                                }
                            },
                        },
                        {
                          type: "number",
                          label: 'Loop time <span class="lowercase">(s)</span>',
                          name: "animation_loop_time",
                          options: {
                              min: 2,
                              default: 6,
                              max: 10,
                              visible: function (data) {
                                  return data.animation == true;
                              }
                          }
                        },
                        {
                            type:'line'
                        },
                        {
                            type:'paragraph',
                            content: `Integrated with: [Omega Facebook Pixel](https://apps.shopify.com/facebook-multi-pixels?surface_detail=ECatc&surface_type=partner)`

                        }
                    ]
                }
            ];
        },
        default() {
            return {
                "settings": {
                    "animation": true,
                    "animation_loop_time": 3,
                    "label": "Buy it now",
                    "icon": "<svg version=\"1.1\" id=\"lni_lni-angle-double-right\" xmlns=\"http://www.w3.org/2000/svg\" xmlns:xlink=\"http://www.w3.org/1999/xlink\" x=\"0px\" y=\"0px\" viewBox=\"0 0 64 64\" style=\"enable-background:new 0 0 64 64;\" xml:space=\"preserve\" fill=\"currentColor\"><g><path d=\"M36.2,28.1L11.3,1.8c-0.7-0.7-1.8-0.7-2.5-0.1C8.1,2.4,8.1,3.5,8.8,4.2l24.9,26.3c0.7,0.7,0.7,1.9,0,2.6L8.8,59.7c-0.7,0.7-0.6,1.8,0.1,2.5c0.3,0.3,0.8,0.5,1.2,0.5c0.5,0,0.9-0.2,1.3-0.6l24.9-26.6C38.2,33.5,38.2,30.2,36.2,28.1z\"></path><path d=\"M54.3,28.2L29.3,1.9c-0.7-0.7-1.8-0.7-2.5-0.1c-0.7,0.7-0.7,1.8-0.1,2.5l24.9,26.3c0.7,0.7,0.7,1.9,0,2.6L26.8,59.8c-0.7,0.7-0.6,1.8,0.1,2.5c0.3,0.3,0.8,0.5,1.2,0.5c0.5,0,0.9-0.2,1.3-0.6l24.9-26.6C56.2,33.6,56.2,30.3,54.3,28.2z\"></path></g></svg>",
                    "icon_position": 1,
                    "spacing": "10px"
                },
                "style": {
                    "checkout_button": {
                        "tab": "normal",
                        "spacing": {
                            "padding": {
                            "top": "12.5px",
                            "left": "50px",
                            "bottom": "12.5px",
                            "right": "50px"
                            }
                        },
                        "buttonTypography": {
                            "text-transform": "uppercase",
                            "font-size": "16px"
                        },
                        "buttonColornormalmode": "#FFF",
                        "buttonBackgroundnormalmode": {
                            "classic": {
                            "background-color": "#0691b1"
                            }
                        },
                        "buttonBordernormalmode": {
                            "border-style": "none"
                        },
                        "buttonBackgroundhovermode": {
                            "classic": {
                            "background-color": "#12bbe0"
                            }
                        },
                        "transition": {
                            "transition": {
                            "duration": "300ms",
                            "timing": "ease"
                            }
                        },
                        "align-items": "center",
                        "buttonBorderRadiushovermode": {
                            "top": "3px",
                            "left": "3px",
                            "bottom": "3px",
                            "right": "3px"
                        },
                        "buttonBorderRadiusnormalmode": {
                            "right": "3px",
                            "top": "3px",
                            "left": "3px",
                            "bottom": "3px"
                        },
                        "buttonHeightnormalmode": "45px",
                        "width": "225px"
                    },
                    "button_animation": {
                        "animation": "shake"
                    },
                    "icon_button": {
                        "tab": "normal",
                        "iconFontSize": "20px"
                    }
                }
            }
        }
    },
    methods: {
        style() {
            const params = [
                {
                    group_alias: 'button',
                    options:{
                        group_title: 'Button',
                        group_name: 'checkout_button',
                        selector:' .ecom-product-single__buy_it_now_btn .ecom-product-single__buy_it_now_btn--checkout',
                    },
                    modify:{
                        remove: [
                            {index: 6, length: 1},
                            {index: 11, length: 1}
                        ],
                        params:[
                            {
                                position:0,
                                fields: [
                                    {
                                        alias: 'width',
                                        options: {
                                            css: {
                                                selector: 'root .ecom-product-single__buy_it_now_btn--checkout'
                                            }
                                        }
                                    },
                                    {
                                        alias:'align-items',
                                        options:{
                                            label:'Alignment',
                                            css:{
                                                selector:'root .ecom-product-single__buy_it_now_btn-buttons-wrapper',
                                                properties:{
                                                    'align-items':'',
                                                    'justify-content':''
                                                }
                                            }
                                        }
                                    }
                                ]
                            },
                        ]
                    }
                },
                {
                    group_alias: "icon:hover",
                    options: {
                        group_name: "icon_button",
                        selector: " .ecom-product-single__buy_it_now-icon-link"
                    }
                },
                {
                  group_name: 'button_animation',
                  group_title: 'Animation effect',
                  selector: ' .ecom-product-single__buy_it_now_btn--checkout',
                  params: [
                      {
                        name: "animation",
                        label: "Animation",
                        type: "popup",
                        options: {
                            type: "dropdown",
                            values: "entrance-animation",
                            size: "small",
                        },
                        css: {
                            selector: ".animated",
                            properties: {
                                'entrance-animation': ""
                            }
                        }
                    },
                  ]
              }
            ];

            return params;
        }

    }
};
</script>
