<template>
    <div class="ecom-element ecom-shopify-elements ecom-shopify__article-comments-count">
        <div class="ecom-shopify__article-comments-count--wrapper">
            <div
                class="ecom-shopify__article-comments-count--container"
                :class="!exporting ? 'ecom-placeholder-on-builder-mode' : ''"
                :data-ecom-placeholder="exporting ? '' : 'This article disabled comments'"
                v-html="liquid('comments_form')"
            />
        </div>
    </div>
</template>

<script>
import { Liquid, Element } from '@/mixins';

export default {
    name: 'ArticleCommentForm',

    mixins: [Liquid, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        css() {
            return `
                .ecom-shopify__article-comment-fields {
                    display: flex;
                    flex-wrap: wrap;
                }
                .ecom-shopify__article-comment-field.ecom-w-full {
                    width: 100%;
                    flex: none
                }
                .ecom-shopify__article-comments-fields, .ecom-shopify__article-comment-field {
                    display: flex;
                    flex-direction: column;
                }

                .ecom-shopify__article-comment-fields {
                    gap: inherit;
                }

                .ecom-shopify__article-comment-field {
                    flex: 1;
                }

                .ecom-shopify__article-comment-field__input.ecom-shopify__article-comment-field-text-area{
                    min-height:100px;
                    font-family:inherit;
                }

                .ecom-shopify__article-comment-field__input{
                    font-family:inherit;
                    min-height:30px;
                }
                .ecom-shopify__article-comment-horizontial .ecom-shopify__article-comment-field {
                    order: -1;
                }
                .ecom-shopify__article-comment-horizontial .ecom-shopify__article-comment-fields-horizontial {
                    gap: 15px;
                }
            `;
        },

        liquids() {
            return {
                comments_form: {
                    code: `
                        {%- if article != empty -%}
                                {% capture layout %}${this.data.settings?.layout || 'verticle'}{% endcapture %}
                             {%- if article.comments_enabled? -%}
                                    {% capture icon_success %}
                                        <svg aria-hidden="true" focusable="false" role="presentation" class="icon ecom-shopify__article--icon-success" viewBox="0 0 13 13">
                                            <path d="M6.5 12.35C9.73087 12.35 12.35 9.73086 12.35 6.5C12.35 3.26913 9.73087 0.65 6.5 0.65C3.26913 0.65 0.65 3.26913 0.65 6.5C0.65 9.73086 3.26913 12.35 6.5 12.35Z" fill="#428445" stroke="white" stroke-width="0.7"/>
                                            <path d="M5.53271 8.66357L9.25213 4.68197" stroke="white"/>
                                            <path d="M4.10645 6.7688L6.13766 8.62553" stroke="white">
                                        </svg>
                                    {% endcapture %}
                                    {% capture icon_error%}
                                        <svg aria-hidden="true" focusable="false" role="presentation" class="icon ecom-shopify__article--icon-error" viewBox="0 0 13 13">
                                            <circle cx="6.5" cy="6.50049" r="5.5" stroke="white" stroke-width="2"/>
                                            <circle cx="6.5" cy="6.5" r="5.5" fill="#EB001B" stroke="#EB001B" stroke-width="0.7"/>
                                            <path d="M5.87413 3.52832L5.97439 7.57216H7.02713L7.12739 3.52832H5.87413ZM6.50076 9.66091C6.88091 9.66091 7.18169 9.37267 7.18169 9.00504C7.18169 8.63742 6.88091 8.34917 6.50076 8.34917C6.12061 8.34917 5.81982 8.63742 5.81982 9.00504C5.81982 9.37267 6.12061 9.66091 6.50076 9.66091Z" fill="white"/>
                                            <path d="M5.87413 3.17832H5.51535L5.52424 3.537L5.6245 7.58083L5.63296 7.92216H5.97439H7.02713H7.36856L7.37702 7.58083L7.47728 3.537L7.48617 3.17832H7.12739H5.87413ZM6.50076 10.0109C7.06121 10.0109 7.5317 9.57872 7.5317 9.00504C7.5317 8.43137 7.06121 7.99918 6.50076 7.99918C5.94031 7.99918 5.46982 8.43137 5.46982 9.00504C5.46982 9.57872 5.94031 10.0109 6.50076 10.0109Z" fill="white" stroke="#EB001B" stroke-width="0.7">
                                        </svg>
                                    {% endcapture %}
                                {% if article != empty and article.id %}
                                <div>
                                    {% form 'new_comment', article %}
                                        {% capture post_message %}
                                            ${this.lang(this.data.settings?.success_message,'success_message')}
                                        {% endcapture %}
                                        {%- if article.moderated? and comment.status == 'unapproved' -%}
                                            {% capture post_message %}
                                                ${this.lang(this.data.settings?.success_moderated,'success_moderated')}
                                            {% endcapture %}
                                        {% endif %}

                                        {%- if form.errors or EComBuilderMode == true -%}
                                            <div class="ecom-shopify__article-form__message ecom-shopify__article-form__message--error" role="alert">
                                            <h3 class="ecom-shopify__article-form-status ecom-shopify__article-caption-large" tabindex="-1" autofocus>
                                                ${this.lang(this.data.settings?.error_heading,'error_heading')}
                                            </h3>

                                                <ul class="ecom-shopify__article-form-status-list ecom-shopify__article-caption-large">
                                                {%- for field in form.errors -%}
                                                    <li>
                                                    <a href="#CommentForm-{{ field }}" class="link">
                                                        {%- if form.errors.translated_fields[field] contains 'author' -%}
                                                        ${this.lang(this.data.settings?.name_label,'name_label')}
                                                        {%- elsif form.errors.translated_fields[field] contains 'body'-%}
                                                        ${this.lang(this.data.settings?.message_label,'message_label')}
                                                        {%- else -%}
                                                        {{ form.errors.translated_fields[field] }}
                                                        {%- endif -%}
                                                        {{ form.errors.messages[field] }}
                                                    </a>
                                                    </li>
                                                {%- endfor -%}
                                                </ul>
                                            </div>
                                        {%- endif -%}
                                        {%- if form.posted_successfully? or EComBuilderMode == true -%}
                                            <div class="ecom-shopify__article-form__message ecom-shopify__article-form__message--success" role="status">
                                                <h3 class="ecom-shopify__article-form-status" tabindex="-1" autofocus>{{ post_message }}</h3>
                                            </div>
                                        {%- endif -%}

                                        <div class="ecom-shopify__article-comment-fields ecom-shopify__article-comment-{{layout}} {% if article.moderated? == false %}ecom-shopify__article-comments-fields{% endif %}">
                                                {%- if layout contains 'horizontial' -%}
                                                    <div class="ecom-flex ecom-shopify__article-comment-fields-horizontial">
                                                {%- endif -%}
                                                <div class="ecom-shopify__article-comment-field ecom-shopify__article-field--with-error">
                                                    ${this.data.settings?.name_label
                                                        ? `
                                                            <label class="ecom-shopify__article-comment-field-label" for="CommentForm-author">${this.lang(this.data.settings?.name_label,'name_label')}<span aria-hidden="true">*</span></label>
                                                        `
                                                        : ''
                                                    }
                                                    <input
                                                    type="text"
                                                    name="comment[author]"
                                                    id="CommentForm-author"
                                                    class="ecom-shopify__article-comment-field__input"
                                                    autocomplete="name"
                                                    value="{{ form.author }}"
                                                    aria-required="true"
                                                    {% if form.errors contains 'author' %}
                                                        aria-invalid="true"
                                                        aria-describedby="CommentForm-author-error"
                                                    {% endif %}
                                                    placeholder="${this.lang(this.data.settings?.name_placeholder,'name_placeholder')}"
                                                    >
                                                    {%- if form.errors contains 'author' -%}
                                                    <small id="CommentForm-author-error">
                                                        <span class="ecom-shopify__article-comment-form-message">{{ icon_error}}${this.lang(this.data.settings?.name_label,'name_label')} {{ form.errors.messages['author'] }}.</span>
                                                    </small>
                                                    {%- endif -%}
                                                </div>
                                                <div class="ecom-shopify__article-comment-field ecom-shopify__article-comment-field--with-error">
                                                    ${this.data.settings?.email_label
                                                        ? `
                                                            <label class="ecom-shopify__article-comment-field-label" for="CommentForm-email">${this.lang(this.data.settings?.email_label,'email_label')} <span aria-hidden="true">*</span></label>
                                                        `
                                                        : ''
                                                    }
                                                    <input
                                                    type="email"
                                                    name="comment[email]"
                                                    id="CommentForm-email"
                                                    autocomplete="email"
                                                    class="ecom-shopify__article-comment-field__input"
                                                    value="{{ form.email }}"
                                                    autocorrect="off"
                                                    autocapitalize="off"
                                                    aria-required="true"
                                                    {% if form.errors contains 'email' %}
                                                        aria-invalid="true"
                                                        aria-describedby="CommentForm-email-error"
                                                    {% endif %}
                                                    placeholder="${this.lang(this.data.settings?.email_placeholder,'email_placeholder')}"
                                                    >
                                                    {%- if form.errors contains 'email' -%}
                                                    <small id="CommentForm-email-error">
                                                        <span class="ecom-shopify__article-comment-form-message">{{ icon_error}}${this.lang( this.data.settings?.email_label,'email_label')} {{ form.errors.messages['email'] }}.</span>
                                                    </small>
                                                    {%- endif -%}
                                                </div>
                                                {%- if layout contains 'horizontial' -%}
                                                    </div>
                                                {%- endif -%}
                                            <div class="ecom-w-full ecom-shopify__article-comment-field ecom-shopify__article-comment-field--with-error">
                                                ${this.data.settings?.message_label
                                                    ? `
                                                    <label class="ecom-shopify__article-comment-field-label" for="CommentForm-body">${this.lang(this.data.settings?.message_label,'message_label')} <span aria-hidden="true">*</span></label>
                                                    `
                                                    : ''
                                                }
                                                <textarea
                                                rows="5"
                                                name="comment[body]"
                                                id="CommentForm-body"
                                                class="ecom-shopify__article-comment-field-text-area ecom-shopify__article-comment-field__input"
                                                aria-required="true"
                                                {% if form.errors contains 'body' %}
                                                aria-invalid="true"
                                                aria-describedby="CommentForm-body-error"
                                                {% endif %}
                                                placeholder="${this.lang(this.data.settings?.message_placeholder,'message_placeholder')}"
                                            >{{ form.body }}</textarea>

                                            </div>
                                            <div class="element__button ecom-button-default">
                                                <button type="submit" class="ecom-shopify__article-comment-field-button">
                                                    ${this.lang(this.data.settings?.submit_label,'submit_label')}
                                                </button>
                                            </div>
                                            {%- if form.errors contains 'body' -%}
                                            <small id="CommentForm-body-error">
                                                <span class="ecom-shopify__article-comment-form--message">{{ icon_error}}${this.lang(this.data.settings?.message_label,'message_label')} {{ form.errors.messages['body'] }}.</span>
                                            </small>
                                            {%- endif -%}

                                        </div>
                                        {%- if article.moderated? -%}
                                            <p class="ecom-shopify__article-comment-warning ecom-shopify__article-comment-caption">${this.lang(this.data.settings?.moderated,'moderated')}</p>
                                        {%- endif -%}

                                        {% endform %}
                                    </div>
                                {% endif %}
                            {%- endif -%}
                        {% else %}
                            <div>Please select the article in settings</div>
                        {% endif %}
                    `,
                    preview: `
                        <div class="ecom-skeleton-item">
                            <div class="ecom-skeleton-col-12">
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-9 ecom-skeleton-big"></div>
                                </div>
                            </div>
                        </div>
                    `
                }
            };
        },

        requestShopifyType() {
            return {
                shopify_type: 'article'
            };
        },

        settings() {
            return [
                {
                    group_title: 'General',
                    params: [
                        {
                            type: 'popup',
                            label: 'Type',
                            name: 'layout',
                            options: {
                                type: 'dropdown',
                                default: false,
                                values: {
                                    verticle: 'Verticle',
                                    horizontial: 'Horizontial',
                                },
                                default: false
                            }
                        },
                        {
                            type: 'text',
                            label: 'Field name label',
                            name: 'name_label',
                            placeholder: 'Name'
                        },
                        {
                            type: 'text',
                            label: 'Field name placeholder',
                            name: 'name_placeholder',
                            placeholder: 'Name'
                        },
                        {
                            type: 'text',
                            label: 'Message field label',
                            name: 'message_label',
                            placeholder: 'Message'
                        },
                        {
                            type: 'text',
                            label: 'Message field placeholder',
                            name: 'message_placeholder',
                            placeholder: 'Message'
                        },
                        {
                            type: 'text',
                            label: 'Field Email label',
                            name: 'email_label',
                            placeholder: 'Email'
                        },
                        {
                            type: 'text',
                            label: 'Field Email placeholder',
                            name: 'email_placeholder',
                            placeholder: 'Email'
                        },
                        {
                            type: 'text',
                            label: 'Submit button label',
                            name: 'submit_label',
                            placeholder: 'Post comment'
                        },
                        {
                            name: 'column-gap',
                            type: 'number',
                            label: 'Column gap',
                            value: '12px',
                            options: {
                                units: {
                                    px: {
                                        min: 1,
                                        max: 64,
                                        step: 1
                                    }
                                },
                                responsive: true
                            },
                            css: {
                                selector: ' .ecom-shopify__article-comment-fields',
                            }
                        },
                        {
                            name: 'row-gap',
                            type: 'number',
                            label: 'Row gap',
                            value: '12px',
                            options: {
                                units: {
                                    px: {
                                        min: 1,
                                        max: 64,
                                        step: 1
                                    }
                                },
                                responsive: true
                            },
                            css: {
                                selector: ' .ecom-shopify__article-comment-fields',
                            }
                        }

                    ]
                },
                {
                    group_title: 'Notification',
                    params: [
                        {
                            type: 'text',
                            label: 'Success message',
                            name: 'success_message',
                            placeholder: 'Your comment was posted successfully! Thank you!'
                        },
                        {
                            type: 'text',
                            label: 'Success message approve by moderator',
                            name: 'success_moderated',
                            placeholder:
                                'Your comment was posted successfully. We will publish it in a little while, as our blog is moderated.'
                        },
                        {
                            type: 'textarea',
                            label: 'Moderated notice',
                            name: 'moderated',
                            options: {
                                placeholder:
                                'Please note, comments need to be approved before they are published.'
                            }
                        },
                        {
                            type: 'text',
                            label: 'Error message',
                            name: 'error_heading',
                            placeholder: 'Please adjust the following:'
                        },
                    ]
                }
            ];
        },

        style() {
            return [
                {
                    group_alias: 'box',
                    options: {
                        group_title: "General"
                    },
                    modify: {
                        params: {
                            alias: 'text-align',
                            options: {
                                label: 'Alignment'
                            }
                        }
                    }
                },
                {
                    group_alias: 'text',
                    options: {
                        group_title: 'Label',
                        group_name: 'label',
                        selector: ' .ecom-shopify__article-comment-field-label'
                    },
                    modify: {
                        params: {
                            position: 10,
                            fields: {alias: 'spacing'}
                        }
                    }
                },
                {
                    group_alias: 'input',
                    options: {
                        group_name: 'input_textarea',
                        group_title: 'Input',
                        selector: ' .ecom-shopify__article-comment-field input, .ecom-shopify__article-comment-field textarea'
                    }
                },
                {
                    group_alias: 'button',
                    options: {
                        group_name: 'button',
                        group_title: 'Button',
                        selector: ' .ecom-shopify__article-comment-field-button'
                    },
                    modify: {
                        params: [
                            {
                                position: 0,
                                fields: {
                                    alias: 'align-self',
                                    options: {
                                        label: 'Alignment'
                                    }
                                }
                            }
                        ]
                    }
                },
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_name: 'noticeSuccess',
                        group_title: 'Success message',
                        selector: ' .ecom-shopify__article-form__message--success .ecom-shopify__article-form-status'
                    }
                },
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_name: 'noticeError',
                        group_title: 'Error message',
                        selector: ' .ecom-shopify__article-form__message--error .ecom-shopify__article-form-status, .ecom-shopify__article-form__message--error .ecom-shopify__article-form-status-list'
                    }
                }
            ];
        },

        default() {
            return {
                "settings": {
                    "column-gap": "12px",
                    "row-gap": "12px",
                    "comment_form_title": "Leave a comment",
                    "success_message": "Your comment was posted successfully! Thank you!",
                    "success_moderated": "Your comment was posted successfully. We will publish it in a little while, as our blog is moderated.",
                    "error_heading": "Please adjust the following:",
                    "name_label": "Name",
                    "message_label": "Message",
                    "email_label": "Email",
                    "submit_label": "Post comment"
                },
                "style": {
                    "box": {
                        "text-align": "left"
                    },
                    "label": {
                        "textTextAlign": "left",
                        "spacing": {
                            "margin": {
                                "bottom": "4px"
                            }
                        }
                    },
                    "input_textarea": {
                        "text-align": "left",
                        "spacing": {
                            "padding": {
                                "left": "8px",
                                "top": "8px",
                                "bottom": "8px",
                                "right": "8px"
                            }
                        },
                        "borderRadius": {
                            "top": "0px",
                            "left": "0px",
                            "bottom": "0px",
                            "right": "0px"
                        },
                        "tab": "normal",
                        "border": {
                            "border-style": "solid",
                            "border-width": {
                                "top": "0.8px",
                                "left": "0.8px",
                                "bottom": "0.8px",
                                "right": "0.8px"
                            }
                        }
                    },
                    "button": {
                        "align-self": "flex-start",
                        "buttonColornormalmode": "#ffffff",
                        "buttonBackgroundnormalmode": {
                            "classic": {
                                "background-color": {
                                    "global-colors": "primary"
                                }
                            }
                        },
                        "buttonColorhovermode": "#ffffff",
                        "buttonBackgroundhovermode": {
                            "classic": {
                                "background-color": "#047857"
                            }
                        },
                        "buttonBordernormalmode": {
                            "border-style": "none"
                        },
                        "buttonBorderRadiusnormalmode": {
                            "top": "0px",
                            "left": "0px",
                            "bottom": "0px",
                            "right": "0px"
                        },
                        "spacing": {
                            "padding": {
                                "left": "20px",
                                "top": "12px",
                                "bottom": "12px",
                                "right": "20px"
                            }
                        }
                    },
                    "noticeSuccess": {
                        "textTextAlign": "left",
                        "spacing": {
                            "margin": {
                                "bottom": "24px"
                            }
                        }
                    },
                    "noticeError": {
                        "textTextAlign": "left",
                        "spacing": {
                            "margin": {
                                "bottom": "8px"
                            }
                        }
                    }
                }
            }
        }
    }
};
</script>