<template>
    <div class="ecom-element ecom-product-single ecom-product-single__wishlist">
        <div class="ecom-product-single__wishlist-wrapper">
            <div
                class="ecom-product-single__wishlist-container"
                :data-wishlist-app="liquid('wishlist_app')"
                :class="!exporting ? 'ecom-placeholder-on-builder-mode': ''"
                :data-ecom-placeholder="exporting? '': 'Please select wishlist app in settings'"
                v-html="liquid('wishlist')"
            />
        </div>
    </div>
</template>

<script>
    import { Liquid, Element, Javascript } from '@/mixins';
    export default {
        name: 'ProductWishlist',

        mixins: [Liquid, Element, Javascript],
        props: {
            data: {
                type: Object,
                default () {
                    return {};
                }
            }
        },
        computed: {

            classes () {
                return this.data && this.data.settings && this.data.settings.classes
                    ? this.data.settings.classes
                    : '';
            },

            heading () {
                return this.data && this.data.settings && this.data.settings.tag
                    ? this.data.settings.tag
                    : 'h3';
            },

            liquids () {
                return {
                    wishlist_app: {
                        code: `{%- assign wishlist_app = shop.metafields.ecomposer.app_wishlist.value -%}{{wishlist_app}}`,
                        preview: ''
                    },
                    wishlist: {
                        code: `{%- if wishlist_app -%}
                            {%- assign variant_selected = product.selected_or_first_available_variant -%}
                            {%- case wishlist_app -%}
                                {% when 'growave'%}
                                    {% capture the_snippet_fave %}{% render 'socialshopwave-widget-fave' with product:product %}{% endcapture %}
                                    {% unless the_snippet_fave contains 'Liquid error' %}
                                        {{ the_snippet_fave }}
                                    {% else %}
                                        {% capture the_snippet_fave_icon %}{% include 'ssw-widget-faveicon' with product.id, product:product%}{% endcapture %}
                                        {% unless the_snippet_fave_icon contains 'Liquid error' %}
                                            {{ the_snippet_fave_icon }}
                                        {% endunless %}
                                    {% endunless %}
                                {%- when 'swym-relay' -%}
                                    <div class="swym-wishlist-button-bar"></div>
                                    <button class="${!this.exporting ? '' : 'swym-loaded'} swym-button swym-add-to-wishlist-view-product product_{{product.id}}" data-swaction="addToWishlist" data-product-id="{{product.id | json}}" ></button>
                                {%- when 'wishlist-wishify' -%}
                                    {% if EComBuilderMode %}
                                        <div id="ZlistWishlist" data-product-id="{{product.id}}" data-handle = "{{product.handle}}"
                                        data-image="{{product.featured_image | img_url: "360x"}}"  data-variantname="{{product.selected_or_first_available_variant.title}}" data-title="{{product.title}}" data-variant="{{product.selected_or_first_available_variant.id}}" data-price="{{product.selected_or_first_available_variant.price | remove: "." | remove: "," | divided_by: 100.0 }}"></div>
                                        <${'script'} type="text/ecom-custom-js"  src="https://zooomyapps.com/wishlist/ListWishlistProduct.js" data-product-id="{{product.id}}" data-shop-id="{{shop.permanent_domain}}" data-customer-id="{{customer.id}}" ></${'script'}>
                                    {% else %}
                                        {% capture ZooomyListWishlistProduct%}
                                            {% render 'ZooomyListWishlistProduct', product: product %}
                                        {% endcapture %}
                                        {% unless ZooomyListWishlistProduct contains 'Liquid error' %}
                                            {{ ZooomyListWishlistProduct }}
                                        {% endunless %}
                                    {% endif %}
                                {%- when 'wishlist-hero' -%}
                                    {% capture wishlisthero %}
                                        {% render 'wishlisthero-custom-button' product: product %}
                                     {% endcapture %}
                                     {% unless wishlisthero contains 'Liquid error' %}
                                            {{ wishlisthero }}
                                    {% endunless %}
                                {%- else -%}
                            {%- endcase -%}
                        {%- endif -%}`,
                        preview: `
                            <div class="ecom-skeleton-item">
                                <div>
                                    <div class="ecom-skeleton-row">
                                        <div class="ecom-skeleton-col-4"></div>
                                        <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                        <div class="ecom-skeleton-col-6"></div>
                                        <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                        <div class="ecom-skeleton-col-2"></div>
                                        <div class="ecom-skeleton-col-10 ecom-skeleton-empty"></div>
                                    </div>
                                </div>
                            </div>
                        `
                    }
                };
            },

            settings () {
                return [{
                    params: [
                        {
                            type: 'paragraph',
                            content: 'Show add to wishlist button you must install the app first and after that select the wishlist app [here](#settings/app_settings/apps)'
                        }
                    ]
                }];
            },
            javascript () {
                if (this.exporting) {
                    return function () { };
                }
                return function () {
                    let $el = this.$el;
                    let $container = $el.querySelector('.ecom-product-single__wishlist-container');
                    if ($container) {
                        switch ($container.dataset.wishlistApp) {
                            case 'swym-relay':
                                if (window._swat) {
                                    window._swat.initializeActionButtons('.swym-add-to-wishlist-view-product');
                                }
                                break;
                            case 'wishlist-wishify':
                                // eslint-disable-next-line no-var
                                var dom = document.createElement('script');
                                dom.src = 'https://zooomyapps.com/wishlist/ListWishlistProduct.js';
                                window.document.head.appendChild(dom);
                                break;
                            case 'wishlist-hero':
                                var buttonDiv = $el.querySelector('.ecom-product-single-wishlist');
                                var ev = new CustomEvent('wishlist-hero-add-to-custom-element', {
                                    detail: buttonDiv,
                                });
                                document.dispatchEvent(ev);
                                break;
                            default:
                                break;
                        }
                    }

                };
            },
            requestShopifyType () {
                return {
                    shopify_type: 'product'
                };
            },

            css () {
                return ``;
            },
        },
    };
</script>
