<template>
    <div class="ecom-element ecom-shopify-elements ecom-shopify__article-author">
        <div class="ecom-shopify__article-author--wrapper">
            <div
                class="ecom-shopify__article-author--container ecom-flex"
            >
                <div
                    v-if="data.settings?.avatar_type === 'shopify' || data.settings?.avatar_type === 'gavatar'"
                    class="ecom-shopify__article-author--avatar"
                    v-html="liquid('articleAvatar')"
                />
                <div
                    v-if="data.settings?.icon_author"
                    class="ecom-shopify__article-author--icon ecom-flex ecom-al_center"
                    v-html="data.settings?.icon_author"
                />
                <div class="ecom-shopify__article-author-information ecom-flex ecom-column">
                    <div
                        class="ecom-shopify__article-author--text"
                        v-html="liquid('articleAuthor')"
                    />
                    <div
                        v-if="data.settings?.show_bio"
                        class="ecom-shopify__article-author--bjo"
                        v-html="liquid('articleAuthorBio')"
                    />
                </div>

            </div>
        </div>
    </div>
</template>

<script>
    import { Liquid, Element } from '@/mixins';

    export default {
        name: 'ArticleAuthor',

        mixins: [Liquid, Element],
        props: {
            data: {
                type: Object,
                default () {
                    return {};
                }
            }
        },
        computed: {
            css () {
                return `
                    .ecom-shopify__article-author--icon svg {
                        width: 16px;
                        height: 16px;
                    }
                    .ecom-shopify__article-author--avatar {
                        width: 50px;
                        height: 50px;
                    }
                `;
            },



            liquids () {
                return {
                    articleAuthor: {
                        code: `
                            {% if article != empty %}
                            {{article.author}}
                            {% else %}
                                <div>Please select the article in settings</div>
                            {% endif %}
                        `,
                        preview: `
                        <div class="ecom-skeleton-item">
                            <div class="ecom-skeleton-col-12">
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-9 ecom-skeleton-big"></div>
                                </div>
                            </div>
                        </div>
                    `
                    },
                    articleAuthorBio: {
                        code: `
                            {% if article.user.bio %}
                                {{ article.user.bio }}
                            {% endif %}
                        `,
                        preview: `
                        <div class="ecom-skeleton-item">
                            <div class="ecom-skeleton-col-12">
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-9 ecom-skeleton-big"></div>
                                </div>
                            </div>
                        </div>
                    `
                    },
                    articleAvatar: {
                        code: `
                            ${this.data.settings?.avatar_type == 'shopify'
                                ? `
                                    {%- if article.user.image -%}
                                        <div class="ecom-image-default">
                                            <img src="{{ article.user.image | img_url: 'master' }}" alt="{{ article.author }}">
                                        </div>
                                    {%- else -%}
                                        <div class="ecom-image-default">
                                            ${this.data.settings?.image_author?.url
                                                ? `
                                                    <img src="${this.data.settings?.image_author?.url}" alt="{{ article.author }}"/>
                                                `
                                                : ``
                                            }

                                        </div>
                                    {%- endif -%}
                                `
                                : `
                                    {%- if article.user.email -%}
                                        <div class="ecom-image-default">
                                            <img src="https://www.gravatar.com/avatar/{{ article.user.email | downcase | md5 }}" alt="{{ article.author }}">
                                        </div>
                                    {%- endif -%}
                                `
                            }
                        `,
                        preview: `
                        <div class="ecom-skeleton-item">
                            <div class="ecom-skeleton-col-12">
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-9 ecom-skeleton-big"></div>
                                </div>
                            </div>
                        </div>
                    `
                    }
                };
            },

            requestShopifyType () {
                return {
                    shopify_type: 'article'
                };
            },

            settings () {
                return [
                    {
                        group_title:'General',
                        params: [
                            {
                                type: 'picker',
                                label: 'Icon Author',
                                name: 'icon_author',
                                options: {
                                    type: 'icon',
                                    layout: 'grid',
                                    output: 'value',
                                    multiple: false,
                                    simple: false
                                }
                            },
                            {
                                type: 'choose',
                                label: 'Icon position',
                                name: 'icon_position',
                                options: {
                                    type: 'align-x',
                                    values: [-1, 1]
                                },
                                css:{
                                    selector:' .ecom-shopify__article-author--icon',
                                    properties:{
                                        order:''
                                    }
                                }
                            },
                            {
                                type: 'popup',
                                name: 'avatar_type',
                                label: 'Avatar Type',
                                options: {
                                    type: 'dropdown',
                                    default: false,
                                    preview: 'title',
                                    values: {
                                        off: 'Off',
                                        shopify: 'Shopify avatar',
                                        gavatar: 'G-Avatar',
                                    }
                                }
                            },
                            {
                                type: 'picker',
                                label: 'Avatar Author default',
                                name: 'image_author',
                                description: 'Image default will display if user not avatar',
                                options: {
                                    responsive: false,
                                    type: 'image',
                                    editAlt: false,
                                    visible: {
                                        keep_data: true,
                                        condition: (data) => {
                                            return data['avatar_type'] == 'shopify';
                                        }
                                    }
                                }
                            },
                            {
                                type: 'number',
                                label: 'Spacing',
                                name: 'spacing',
                                options: {
                                    units: {
                                        px: {
                                            min: 0,
                                            max: 200
                                        }
                                    }
                                },
                                css:{
                                    selector:' .ecom-shopify__article-author--container',
                                    properties:{
                                        gap:''
                                    }
                                }
                            },
                            {
                                type: 'toggle',
                                label: 'Show author bio',
                                name: 'show_bio',
                                options: {
                                    values:{
                                        on:{
                                            label: 'Yes',
                                            value: true
                                        },
                                        off: {
                                            label: 'No',
                                            value: false
                                        }
                                    }
                                }
                            },
                        ]
                    }
                ];
            },

            style () {
                return [
                    {
                        group_alias: 'text:spacing',
                        options: {
                            group_title: 'General',
                            group_name: 'author_text',
                            selector: ' .ecom-shopify__article-author--text'
                        }
                    },
                    {
                        group_alias:'icon',
                        options:{
                            group_name: 'icon',
                            group_title: 'Icon',
                            selector: ' .ecom-shopify__article-author--icon',
                        },
                        modify:{
                            params:[
                                {
                                    position:15,
                                    fields:[
                                        {
                                            type: 'line'
                                        },
                                        {
                                            alias:'spacing'
                                        }
                                    ]
                                }
                            ]
                        }
                    },
                    {
                        group_alias:'image',
                        options:{
                            selector: ' .ecom-shopify__article-author--avatar',
                            group_name: 'blog_meta_img',
                            group_title: 'Avatar'
                        },
                        modify: {
                            remove: {
                                label: 'Width',
                                length: 1
                            },
                            params: {
                                position: 1,
                                fields: [
                                {
                                    type: "number",
                                    name: "imageWidth",
                                    label: "Image Width",
                                    options: {
                                        responsive: true,
                                        reset: true,
                                        units: {
                                            "%": {
                                                min: 0,
                                                max: 100
                                            },
                                            px: {
                                                min: 0,
                                                max: 1000
                                            },
                                            vw: {
                                                min: 0,
                                                max: 100
                                            }
                                        }
                                    },
                                    css: {
                                        important: true,
                                        properties: {
                                            width: ""
                                        }
                                    }
                                },
                                ]
                            }
                        }
                    },
                    {
                        group_alias: 'text:spacing',
                        options: {
                            group_title: 'Author bio',
                            group_name: 'author_bio',
                            selector: ' .ecom-shopify__article-author--bjo'
                        }
                    },
                ];
            },

            default() {
                return {
                    style: {
                        author_text: {
                            textTextAlign: "left",
                            textColor: {
                                "global-colors": "primary"
                            }
                        }
                    }
                }
            }
        }
    };
</script>
