<template>
    <div
        v-bind="blockAttrs"
        @mouseenter="overGroup"
        @mouseleave="outGroup"
        @contextmenu="rightClickMenu"
    >
        <div
            class="core__group--wrapper"
            :data-r="refresh"
            :style="styleattr.wrapper"
            :class="gclass.wrapper"
        >
            <div
                v-if="isActive"
                class="core__section--settings core__group--settings"
                :style="styleattr.settings"
            >
                <span
                    class="section__settings--edit istext editicon"
                    :class="isEditing ? 'active' : ''"
                    :title="'Edit '+data.element"
                    @mousedown="editGroup"
                >
                    <text>{{ data?.title ?? data.name ?? data.element }}</text>
                     <svg xmlns="http://www.w3.org/2000/svg" class="ecom-icon" viewBox="0 0 512 512" fill="currentColor" ><path d="M373.2 16.97C395.1-4.901 430.5-4.901 452.4 16.97L495 59.6C516.9 81.47 516.9 116.9 495 138.8L182.3 451.6C170.9 462.9 156.9 471.2 141.5 475.8L20.52 511.3C14.9 512.1 8.827 511.5 4.687 507.3C.5466 503.2-1.002 497.1 .6506 491.5L36.23 370.5C40.76 355.1 49.09 341.1 60.44 329.7L373.2 16.97zM429.8 39.6C420.4 30.22 405.2 30.22 395.8 39.6L341 94.4L417.6 170.1L472.4 116.2C481.8 106.8 481.8 91.6 472.4 82.23L429.8 39.6zM109.6 402.4L173.4 415.2L394.1 193.6L318.4 117L96.84 338.6L109.6 402.4zM70.51 370.2C69.08 373.2 67.88 376.3 66.93 379.5L39.63 472.4L132.4 445.1C135.7 444.1 138.8 442.9 141.8 441.5L92.86 431.7C86.53 430.4 81.58 425.5 80.31 419.1L70.51 370.2z"/></svg>
                </span>
                <span
                    class="section__settings--delete"
                    :title="'Delete '+data.element"
                    @click="deleteGroup"
                >

                    <svg xmlns="http://www.w3.org/2000/svg" class="ecom-icon" viewBox="0 0 448 512" fill="currentColor"><path d="M144 400C144 408.8 136.8 416 128 416C119.2 416 112 408.8 112 400V176C112 167.2 119.2 160 128 160C136.8 160 144 167.2 144 176V400zM240 400C240 408.8 232.8 416 224 416C215.2 416 208 408.8 208 400V176C208 167.2 215.2 160 224 160C232.8 160 240 167.2 240 176V400zM336 400C336 408.8 328.8 416 320 416C311.2 416 304 408.8 304 400V176C304 167.2 311.2 160 320 160C328.8 160 336 167.2 336 176V400zM310.1 22.56L336.9 64H432C440.8 64 448 71.16 448 80C448 88.84 440.8 96 432 96H416V432C416 476.2 380.2 512 336 512H112C67.82 512 32 476.2 32 432V96H16C7.164 96 0 88.84 0 80C0 71.16 7.164 64 16 64H111.1L137 22.56C145.8 8.526 161.2 0 177.7 0H270.3C286.8 0 302.2 8.526 310.1 22.56V22.56zM148.9 64H299.1L283.8 39.52C280.9 34.84 275.8 32 270.3 32H177.7C172.2 32 167.1 34.84 164.2 39.52L148.9 64zM64 432C64 458.5 85.49 480 112 480H336C362.5 480 384 458.5 384 432V96H64V432z"/></svg>
                </span>
            </div>
            <template v-else />
            <slot
                name="before"
                :items="gitems"
                :haActived="haActived"
            />
            <slot
                name="items"
                :items="gitems"
                :haActived="haActived"
            >
                <div
                    class="core__group--items"
                    :class="gclass.items"
                    :style="gstyle.items"
                >
                    <div
                        v-for="(gitem, i) in gitems"
                        :id="gitem.id ? gitem.id : ''"
                        :key="i"
                        :class="{
                            'core__group--item': 1,
                            [gclass.item]: gclass.item,
                            'ecom-item-active': (!haActived && i === 0)
                        }"
                        :data-id="gitem.id"
                    >
                        <slot
                            name="title"
                            :item="gitem"
                            :haActived="haActived"
                            :index="i"
                        />
                        <div

                            :class="{
                                'core__group--body': 1,
                                [gclass.body]: gclass.body
                            }"
                        >
                            <!-- <Blocks
                                v-if="this.data.template"
                                :data="gblocks(gitem)"
                                :deep="deep"
                                :index="i"
                                :control="control"
                                :section_id="section_id"
                            /> -->
                            <Blocks
                                v-if="data.template"
                                :data="gblocks(gitem)"
                                :deep="deep"
                                :index="i"
                                :control="control"
                                :section_id="section_id"
                            />
                            <GroupLiquid
                                v-else
                                :deep="deep"
                                :index="i"
                                :control="control"
                                :data="gitem"
                                :section_id="section_id"
                            />
                        </div>
                    </div>
                </div>
            </slot>
            <slot
                name="after"
                :items="gitems"
            />
        </div>
        <template v-if="isDragging">
            <div
                class="placeholder__top"
                @mouseover="placehover"
            />
            <div
                class="placeholder__bottom"
                @mouseover="placehover"
            />
        </template>
        <template v-else />
    </div>
</template>

<script>
    import { mapGetters } from 'vuex';
    import { Core } from '@/mixins';
    import GroupLiquid from './GroupLiquid.vue';
    export default {
        name: 'Group',
        mixins: [ Core ],
        components: { GroupLiquid },
        props: {
            'data': {
                type: Object,
                default () {
                    return {};
                }
            },
            'index': {
                type: Number,
                default: 0
            },
            'deep': {
                type: Number,
                default: 0
            },
            'control': {
                type: Boolean,
                default: true
            },
            'gclasses': {
                type: Object,
                default () {
                    return {};
                }
            },
            'close_all': {
                type: Boolean,
                default: false
            },
            'gstyles': {
                type: Object,
                default () {
                    return {};
                }
            },
            'section_id': {
                type: String,
                default: ''
            }
        },
        data () {
            return {
                is: 'group',
                name: this.data.element,
                element: this.data.element,
                over: false,
                refresh: '',
                placeholder: '',
                validElements: ['heading', 'text', 'image', 'divi']
            };
        },
        computed: {
            css() {
                return `
                    .core__group--items {
                        flex: 1;
                    }
                `;
            },
            stack () {
                return this.$parent.$parent.$parent.blocks;
            },
            group () {
                return this.data;
            },
            column () {
                return this.block.$parent;
            },
            block () {
                return this.$parent.$parent;
            },
            blocks () {
                let re = this.gitems.filter((g) => { return g.id == this.data.actived; });
                if (re.length > 0) {
                    return re[0].blocks;
                } else {
                    return [];
                }
            },
            gitems () {
                return this?.group?.settings?.items ?? [];// &&  this.group.settings  && this.group.settings.items ? this.group.settings.items : [];
            },
            gclass () {
                return this.gclasses ? this.gclasses : {};
            },
            gstyle () {
                return this.gstyles ? this.gstyles : {};
            },
            styleattr () {
                return {
                    wrapper: `outline: ${this.isActive ? '1px solid rgba(var(--primary-color-light), .5)' : ''}`,
                    settings: `display: ${this.isActive ? 'flex' : 'none'}`,
                };
            },
            blockAttrs () {
                return {
                    ref: 'wrapper',
                    class: {
                        'ecom-core ecom-group core__block': true,
                        'ecom-combined-elements': !this.control,
                        ...this.classes
                    },
                    ...this.attributes
                };
            },
            haActived() {
                return this.close_all;
            },
            isActive () {
                return !this.isFullScreen &&
                (
                    this.over ||
                    this.activeElement?.data?.id === this.data.id ||
                    this.editingElement?.data?.id === this.data.id
                ) && (
                    !this.dragCurrent ||
                    this.dragCurrent.is != 'section'
                );
            },
            isEditing () {
                return this.editingElement?.data?.id === this.data.id;
            },
            isDragMe () {
                return this.dragCurrent === this.$parent.$parent;
            },
            isDropTo () {
                return this.dropTo === this.$parent.$parent;
            },
            sections() {
                return this.$store.getters['page/sections'];
            },
            ...mapGetters('builder', ['stacks'])
        },

        watch: {
            gitems: {
                immediate: true,
                deep: true,
                handler (val) {
                    if (val.length === 0) {
                        this.gitems.push({
                            'title': 'Untitled',
                            'blocks': []
                        });
                    }
                    this.gitems.forEach((gitem) => {
                        this.gindex(gitem);
                        this.gblocks(gitem);
                    });
                }
            }
        },

        created() {
            this.group.control = this.control;
        },

        methods: {

            gindex (group) {
                if (group.id === undefined) {
                    group.id = this.$helpers.randid();
                }
                return group.id;
            },

            gblocks (group) {
                if (!group.blocks || typeof group.blocks != 'object') {
                    group.blocks = [];
                }
                return group.blocks;
            },

            overGroup () {
                if (!this.isFullScreen) {
                    this.over = true;
                    clearTimeout(this.timmer);
                }
            },

            outGroup () {
                if (!this.isFullScreen) {
                    clearTimeout(this.timmer);
                    this.timmer = setTimeout(() => {
                        this.over = false;
                    }, 100);
                }
            },

            placehover (e) {

                if (
                    this.isFullScreen ||
                    ['section', 'column'].indexOf(this.dragCurrent.is) > -1
                ) {
                    return;
                }

                if (
                    this.dropTo !== this.$parent.$parent
                ) {
                    this.$store.commit('builder/setDropTo', this.$parent.$parent);
                }

                if (
                    this.dropTo === this.$parent.$parent
                ) {
                    let pos = e.target.classList.contains('placeholder__top') ? 'top' : 'bottom';
                    this.$parent.$parent.placeholder = pos;
                    this.dropPos = pos;
                    this.$store.commit('builder/setDropPos', pos);
                }
            },
            // Hold click on group to active --> conflict with element and column
            activeGroup(e) {
                let timmer = this.$store.getters['builder/dragging']?.timmer ?? null;
                if (
                    !this.isFullScreen &&
                    (
                        !timmer || timmer < new Date().getTime() - 250
                    ) &&
                    e.target.classList.contains('core__group--items')
                ) {
                    this.$store.commit('builder/setActiveElement', this);
                    this.$store.commit('builder/setEditingElement', this.$parent);
                    this.startDrag(e);
                }
            },

            editGroup (e) {
                this.$store.commit('builder/setActiveElement', this);
                this.$store.commit('builder/setEditingElement', this.$parent);
                this.startDrag(e);
            },

            deleteGroup () {
                if(this.section_id) {
                    const section = this.sections.find( (s) => s.id === this.section_id);
                    if(section) {
                        this.$store.dispatch('history/addStack', {
                            type: this.$options?.name ?? this?.name,
                            data: section,
                            section_id: this.section_id,
                            action: 'Deleted'
                        });
                    }
                }
                this.dropOut();
                this.$parent.$parent.$parent.refresh = Math.random().toString(36).substr(2, 5);
            },

            dropOut () {
                if (this.editingElement?.data?.id === this.$parent.data.id) {
                    this.$store.commit('builder/setActiveElement', null);
                    this.$store.commit('builder/setEditingElement', null);
                }
                return this.$parent.$parent.dropOut();
            },
            /*
            dropIn (current, pos) {
                this.stack.splice(
                    this.index + pos, 0, current.dropOut()
                );
                this.refresh = Math.random().toString(36).substr(2, 5);
            }*/

        }
    };
</script>
