<template>
    <div class="ecom__element ecom-element element__code exporting"  v-if="exporting" >
        <Liquid :data="data.settings.code" />
    </div>
    <div
    v-else
        class="ecom__element ecom-element element__code no-exporting"
        v-html="liquid('code')"
    />
    
</template>

<script>
import { mapGetters } from 'vuex';
import {Element, Liquid} from '@/mixins';
export default {
    name: 'Code',
    docs: 'https://help.ecomposer.io/docs/elements/shopify-elements/code/',
    mixins: [Element, Liquid],
    props: {
        'data': {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        settings() {
            return [{
                params: [{
                    label: 'Custom code',
                    type: 'textarea',
                    description: 'Add app snippets or other Liquid code to create advanced customizations. Javascript code only run when publish to your site',
                    name: 'code',
                    options: {
                        update: 'onchange',
                        height: 350
                    }
                }]
            }];
        },

        code() {
            return this.data;
        },
        codevalue() {
            return this.data.settings.code;
        },
        codePreiew(){
            if (this.codevalue.match(/{{[\s\S]*|{%[\s\S]*/g)) {
                return `
                    <div class="ecom-skeleton-item">
                        <div class="ecom-skeleton-col-12">
                            <div class="ecom-skeleton-row">
                                <div class="ecom-skeleton-col-9 ecom-skeleton-big"></div>
                            </div>
                        </div>
                    </div>
                `;
            } else {
                return this.codevalue;
            }
        },
        codeliquid() {
            if (this.codevalue?.match(/{{[\s\S]*|{%[\s\S]*/g) === null) {
                return '';
            } else {
                return this.codevalue;
            }
        },
        liquids() {
            return {
                code: {
                    code: this.codeliquid,
                    preview: this.codePreiew
                }
            };
        },
        style() {
            return [
                { group_alias: 'text' }
            ]
        },
        ...mapGetters('builder', ['getLiquids']),

        default() {
            return {
                settings: {
                    code: "<div>Your custom code is here (HTML, JavaScript, CSS, Shopify Liquid)</div>"
                },
                style: {
                    text: {
                        textTextAlign: "left"
                    }
                }
            }
        }
    },
    watch: {
        codevalue(val) {
            if(this.exporting) 
            {
                return false;
            }
            let myLiquid = this.getLiquids?.[this.data?.id] ?? {};
            if (
                this.data.id &&
                (myLiquid === undefined || myLiquid.content === undefined)
            ) {
                this.getLiquids[this.data.id] = { content: {} };
            }
            this.getLiquids[this.data.id].content.code = val;
        }
    }
};
</script>
