<template>
    <div class="ecom-element ecom-product-single ecom-product-single__buy_now_btn" :data-preview-mode="exporting.toString()">
        <div class="ecom-product-single__buy_now_btn-wrapper">
            <div class="ecom-product-single__buy_now_btn-container">
                <div class="ecom-product-single__buy_now_btn-buttons">
                    <div class="ecom-product-single__buy_now_btn-buttons-wrapper">
                    <div
                        class="ecom-product-single__buy_now_btn--dynamic-checkout"
                        :class="animatedClass"
                        v-html="liquid('dynamic_checkout')"
                    />
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import { Liquid, Javascript, Element } from '@/mixins';
export default {
    name: 'BuyNowButton',

    mixins: [Liquid, Javascript, Element],
    props: {

        data: {
            type: Object,
            default() {
                return {
                };
            }
        }
    },
    data(){
        return {
            jsreactives: [
                'animation_loop_time', 'animation'
            ]
        }
    },
    computed: {
        javascript() {
            return function () {
                if(!this.$el)
                {
                    return false;
                }

                const $el = this.$el;
                const $wrapper = $el.closest('.ecom-product-form--single');

                if (!$wrapper) {
                    return false;
                }

                if (!this.isLive ||  $wrapper.dataset.previewMode === 'preview' || window.EComposer.hasOwnProperty('DEMO')) {
                    const checkoutBtn = $el.querySelector(
                        '.shopify-payment-button__button'
                    );
                    if (checkoutBtn) {
                        checkoutBtn.innerText = 'Buy it now';
                        checkoutBtn.classList.remove('shopify-payment-button__button--hidden');
                        checkoutBtn.removeAttribute('disabled');
                    }

                }

                const product_id = $wrapper.dataset.product_id;
                    if(!product_id) return;
                    let product = null;
                    const json = $wrapper.querySelector('[id^=product-json-' + product_id + ']');
                    if (!json) {
                        return;
                    }
                    try {
                        product = JSON.parse(json.innerHTML);
                    } catch (e) {

                    }

                if(this.isLive)
                {
                    const buyNowBtn = $el.querySelector('.ecom-product-single__buy_now_btn--dynamic-checkout .shopify-payment-button');
                    if(buyNowBtn) {
                        buyNowBtn.addEventListener('click', function(){
                            let quantity = 1;
                            if($wrapper.querySelector('[name=quantity]')){
                                quantity = $wrapper.querySelector('[name=quantity]').value;
                            }
                            let id = $wrapper.querySelector('[name=id]').value;
                            let variant = product.variants.find(variant => variant.id == id);
                            if(!variant || variant.available === false){
                                return;
                            }
                            if(window.omegaCallBackCheckout && typeof window.omegaCallBackCheckout == "function")  { window.omegaCallBackCheckout(); }

                            if(window.Shopify.analytics)
                            {
                                Shopify.analytics.publish("ec_buy_now",   {
                                    "cartLine": {
                                        "cost": {
                                            "totalAmount": {
                                                "amount": variant.price * quantity,
                                                "currencyCode": window.Shopify.currency.active
                                            }
                                        },
                                        "merchandise": {
                                            "id": variant.id,
                                            "image": variant.image,
                                            "price": {
                                                "amount": variant.price,
                                                "currencyCode": window.Shopify.currency.active
                                            },
                                            "product": {
                                                "id":  product.id,
                                                "title":  product.title,
                                                "vendor": product.vendor
                                            },
                                            "sku": variant.sku,
                                            "title": variant.title
                                        },
                                        "quantity": quantity
                                    }
                                })
                            }
                        });


                    }
                }

                const handleAnimation = function(_this) {
                  if (!$el) return;

                  const $btn_wrapper = $el.querySelector('.ecom-product-single__buy_now_btn--dynamic-checkout');
                   if (!$btn_wrapper) return;

                  let intervalTime = parseInt(_this.settings.animation_loop_time) * 1000 || 6000,
                      animTime = 1000;

                  setInterval(function() {
                      $btn_wrapper.classList.add('animated');
                      setTimeout(function(){
                        $btn_wrapper.classList.remove('animated');
                      }, animTime);
                  }, intervalTime);
                }

                if (this.settings.animation) {
                  handleAnimation(this);
                }
            };
        },
        animatedClass() {
          return this.data?.settings?.animation ? 'animated' : '';
        },
        liquids() {
            return {
                dynamic_checkout: {
                    code: `
                            {{ form | payment_button }}
                        `,
                    preview: `
                        <div data-shopify="payment-button" class="shopify-payment-button">
                            <button class="shopify-payment-button__button shopify-payment-button__button--unbranded shopify-payment-button__button--hidden" aria-hidden="true">Buy it now</button>
                            <button class="shopify-payment-button__more-options shopify-payment-button__button--hidden" disabled="disabled" aria-hidden="true">&nbsp;</button>
                        </div>
                        `
                }
            };
        },
        requestShopifyType() {
            return {
                shopify_type: 'product'
            };
        },
        css() {
          return `
            .ecom-product-single__buy_now_btn .shopify-payment-button__button--unbranded:after,
            .shopify-payment-button__more-options.shopify-payment-button__button--hidden {
              display: none;
            }
            .shopify-payment-button > button {
                width: 100%;
            }
            .ecom-product-single__buy_now_btn--dynamic-checkout {
                display: flex;
            }
          `;
        },
        settings() {
            return [
                {
                    params: [
                        {
                            type: 'toggle',
                            name: 'animation',
                            label: 'Button animation',
                            options: {
                                values: {
                                    on: {
                                        label: 'Enable',
                                        value: true
                                    },
                                    off: {
                                        label: 'Disable',
                                        value: false
                                    }
                                }
                            },
                        },
                        {
                          type: "number",
                          label: 'Loop time <span class="lowercase">(s)</span>',
                          name: "animation_loop_time",
                          options: {
                              min: 2,
                              default: 6,
                              max: 10,
                              visible: function (data) {
                                  return data.animation == true;
                              }
                          }
                      },
                      {
                            type:'line'
                        },
                        {
                            type:'paragraph',
                            content: `Integrated with: [Omega Facebook Pixel](https://apps.shopify.com/facebook-multi-pixels?surface_detail=ECatc&surface_type=partner)`

                        }
                    ]
                }
            ];
        },
        default() {
            return {
                "settings": {
                    "animation": true,
                    "animation_loop_time": 3
                },
                "style": {
                    "dynamic_checkout_button": {
                        "tab": "normal",
                        "spacing": {
                            "padding": {
                                "top": "12.5px",
                                "left": "50px",
                                "bottom": "12.5px",
                                "right": "50px"
                            }
                        },
                        "buttonTypography": {
                            "text-transform": "uppercase"
                        },
                        "buttonColornormalmode": "#FFF",
                        "buttonBackgroundnormalmode": {
                            "classic": {
                                "background-color": "#0691b1"
                            }
                        },
                        "buttonBordernormalmode": {
                            "border-style": "none"
                        },
                        "buttonBackgroundhovermode": {
                            "classic": {
                                "background-color": "#12bbe0"
                            }
                        },
                        "transition": {
                            "transition": {
                                "duration": "300ms",
                                "timing": "ease"
                            }
                        },
                        "align-items": "center",
                        "buttonBorderRadiushovermode": {
                            "top": "3px",
                            "left": "3px",
                            "bottom": "3px",
                            "right": "3px"
                        },
                        "buttonBorderRadiusnormalmode": {
                            "right": "3px",
                            "top": "3px",
                            "left": "3px",
                            "bottom": "3px"
                        }
                    },
                    "button_animation": {
                        "animation": "shake"
                    }
                }
            }
        }
    },
    methods: {
        style() {
            const params = [
                {
                    group_alias: 'button',
                    options:{
                        group_title: 'Button',
                        group_name: 'dynamic_checkout_button',
                        selector:' .ecom-product-single__buy_now_btn .shopify-payment-button .shopify-payment-button__button, .ecom-product-single__buy_now_btn .shopify-payment-button .shopify-payment-button__button--unbranded',
                    },
                    modify:{
                        remove: [
                            {index: 6, length: 1},
                            {index: 11, length: 1}
                        ],
                        params:[
                            {
                                position:0,
                                fields: [
                                    {
                                        alias: 'width',
                                        options: {
                                            css: {
                                                selector: 'root .shopify-payment-button'
                                            }
                                        }
                                    },
                                    {
                                        alias:'align-items',
                                        options:{
                                            label:'Alignment',
                                            css:{
                                                selector:'root .ecom-product-single__buy_now_btn--dynamic-checkout',
                                                properties:{
                                                    'align-items':'',
                                                    'justify-content':''
                                                }
                                            }
                                        }
                                    }
                                ]
                            },
                        ]
                    }
                },
                {
                  group_name: 'button_animation',
                  group_title: 'Animation effect',
                  selector: ' .ecom-product-single__buy_now_btn--dynamic-checkout',
                  params: [
                      {
                        name: "animation",
                        label: "Animation",
                        type: "popup",
                        options: {
                            type: "dropdown",
                            values: "entrance-animation",
                            size: "small",
                        },
                        css: {
                            selector: ".animated",
                            properties: {
                                'entrance-animation': ""
                            }
                        }
                    },
                  ]
              }
            ];

            return params;
        }

    }
};
</script>
