<template>
    <div ref="wrapper" class="ecom__element ecom-element element__text" data-stopdrag>
        <Liquid :data="getMetaLiquid" />
        <div
            ref="content"
            class="text-content ecom-html"
            :style="[maxHeight ? `max-height: var(--ecom-text-height); --ecom-text-height: ${maxHeight}` : '']"
            :class="[
                `has-drop-cap-view-${dropCapView}`,
                data.settings?.dropcap ? 'ecom__text--dropcap' : '',
                contentType == 'short' ? 'ecom-text--is-mark' : ''
            ]"
            :contenteditable="isFullScreen ? null : true"
            spellcheck="false"
            @click="edit"
            @keyup="update"
            @blur="editing = false"
            v-html="editing ? editingValue : exporting ? lang(this.convertText, 'text-content', metaLang) : data.settings?.content"
        />

        <div class="ecom-text--more-actions" v-if="contentType === 'short' && view_more">
            <button
                type="button"
                class="ecom-text_view-more-btn"
                ref="showMore"
                @click="showMore"
            >
                <span class="ecom__element--button-icon" v-if="data.settings?.view_more_icon" v-html="data.settings.view_more_icon" />
                <span v-text="lang(data.settings.view_more_text, 'view_more_text')"></span>
            </button>
        </div>
        <div class="ecom-text--less-actions"  v-if="contentType === 'short' && view_more">
            <button
                type="button"
                ref="showLess"
                class="ecom-text_view-less-btn"
                style="display:none"
                @click="showLess"
            >
                <span class="ecom__element--button-icon" v-if="data.settings?.view_less_icon" v-html="data.settings.view_less_icon" />
                <span v-text="lang(data.settings.view_less_text, 'view_less_text')"></span>
            </button>

        </div>
    </div>
</template>

<script>
import { Element, Liquid, Javascript } from '@/mixins';
import { addSwitch } from "@/plugins/fields/utils";
export default {
    name: 'TextEditor',
    presets: true,
    docs: 'https://help.ecomposer.io/docs/elements/basic-elements/text/',
    mixins: [Element, Liquid, Javascript],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    data() {
        return {
            dropCapView: 'default',
            editing: false,
            timmer: null,
            editingValue: '',
            metaLang: {},
            metaLiquid: ''
        };
    },
    computed: {
        settings() {
            return [
                {
                    group_title:'General',
                    params: [
                        {
                            type: 'textarea',
                            label: 'Content',
                            name: 'content',
                            options: {
                                toolbar: 'full',
                                generator: true,
                                dynamic: true,
                                height: 250,
                                warnings: {
                                    content:
                                        'Notice: "Shopify Multiple Languages" will not support too long content. Please use the element "Page content" instead.'
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            label: 'Drop cap',
                            name: 'dropcap',
                            options: {
                                oneline: true,
                                values: {
                                    on: {
                                        label: 'YES',
                                        value: true
                                    },
                                    off: {
                                        label: 'NO',
                                        value: false
                                    }
                                }
                            }
                        },
                        {
                            type: 'popup',
                            label: 'Content type',
                            name: 'type',
                            value: 'full',
                            options: {
                                type: 'dropdown',
                                values: {
                                    short: 'Short',
                                    full: 'Full'
                                },
                                default: false
                            }
                        },
                        {
                            type: 'number',
                            label: 'Max height',
                            name: 'max-height',
                            placeholder: '200',
                            options: {
                                units: {
                                    px: {
                                        min: 0,
                                        max: 1000
                                    },
                                    'vh': {
                                        min: 0,
                                        max: 100
                                    },
                                    'vw': {
                                        min: 0,
                                        max: 100
                                    }
                                },
                                visible: function (data) {
                                    return data['type'] === 'short';
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            label: 'Show view more',
                            name: 'view_more',
                            options: {
                                oneline: true,
                                values: {
                                    on: {
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                },
                                visible: function (data) {
                                    return data['type'] === 'short';
                                }
                            }
                        },
                        {
                            type: 'picker',
                            label: 'View more icon',
                            name: 'view_more_icon',
                            options: {
                                type: 'icon',
                                layout: 'grid',
                                output: 'value',
                                multiple: false,
                                simple: false,
                                visible: function(data){return data.view_more && data.type == 'short'}
                            }
                        },
                        {
                            type: 'picker',
                            label: 'View less icon',
                            name: 'view_less_icon',
                            options: {
                                type: 'icon',
                                layout: 'grid',
                                output: 'value',
                                multiple: false,
                                simple: false,
                                visible:function(data){return data.view_more && data.type == 'short'}
                            }
                        },
                        {
                            type: 'choose',
                            label: 'Icon position',
                            name: 'icon_position',
                            options: {
                                type: 'align-x',
                                values: [-1, 1],
                                visible:function(data){return data.view_more && data.type == 'short'}
                            },
                            css:{
                                selector:' .ecom__element--button-icon',
                                properties:{
                                    order:''
                                }
                            }
                        },
                        {
                            type: "number",
                            name: "spacing",
                            label: "Spacing",
                            options: {
                                responsive: true,
                                units: {
                                    px: {
                                        min: 1,
                                        max: 50,
                                        step: 1
                                    }
                                },
                                visible:function(data){return data.view_more && data.type == 'short'},
                                reset:false
                            },
                            css: {
                                selector: 'root .ecom-text--more-actions button ,root .ecom-text--less-actions button',
                                properties: {
                                    gap: ""
                                }
                            }
                        },
                        {
                            label: 'View more text button',
                            name: 'view_more_text',
                            type: 'text',
                            options: {
                                visible: function (data) {
                                    return data['type'] === 'short' && data['view_more'];
                                }
                            }
                        } ,
                        {
                            label: 'View less text button',
                            name: 'view_less_text',
                            type: 'text',
                            options: {
                                visible: function (data) {
                                    return data['type'] === 'short' && data['view_more'];
                                }
                            }
                        }
                    ]
                }
            ];
        },

        css() {
            return `
                .ecom__text--dropcap:first-letter {
                    font-size: 40px;
                    color: #8A2BE2;
                    display:inline-flex;
                    border:1px solid transparent;
                    float:left;
                    line-height:1;
                }
                body[ecom-loaded] .ecom-sections .element__text a {
                    cursor: auto;
                }
                .text-content.ecom-html {
                    overflow: hidden;
                    position: relative;
                }

                .ecom-text--is-mark::after {
                    position: absolute;
                    content: '';
                    left: 0;
                    right: 0;
                    bottom: 0;
                    height: 150px;
                    background: linear-gradient(rgba(255, 255, 255, 0), rgb(255, 255, 255));
                    pointer-events: none;
                }

                .ecom-text_view-more-btn,
                .ecom-text_view-less-btn {
                    display: flex;
                    background: none;
                    outline: none;
                    border: none;
                    align-items: center;
                }

                .ecom__element--button-icon {
                    display: flex;
                    align-items: center;
                }
                
                .ecom__element--button-icon svg {
                    width: 16px;
                }
            `;
        },

        contentType() {
            return this.data.settings?.type;
        },

        maxHeight() {
            return this.data.settings.type === 'short'
                ? this.data.settings['max-height']
                : null;
        },

        view_more() {
            return this.data && this.data.settings && 'view_more' in this.data.settings
                ? this.data.settings.view_more
                : false;
        },

        text() {
            return this.data;
        },
        getMetaLiquid() {
            this.convertText;
            return this.metaLiquid;
        },
        convertText() {
            const _this = this;
            let text = this.data.settings?.content;
            if(text) {
                const dom = document.createElement('div');
                dom.innerHTML = text;
                const meta_nodes = dom.querySelectorAll('span[data-meta]');
                if(meta_nodes) {
                   meta_nodes.forEach((el, index) => {
                    let meta_key = el.dataset.metaKey.replaceAll(' ', '_'),
                        metafiled = el.dataset.meta,
                        meta = document.createTextNode(`{{${meta_key}}}`);;
                    if(metafiled.indexOf('|') == -1) {
                        _this.metaLang[meta_key] = metafiled
                    } else {
                        _this.metaLiquid += `{%- assign ${meta_key} = ${metafiled}-%}`
                        _this.metaLang[meta_key] = meta_key
                    }

                    el.replaceWith(meta);
                    //dom.replaceChild(meta, el)
                   })
                }

                return dom.innerHTML

            }
        },

        activeMe() {
            return (this.editingElement?.data?.id ?? 0) === this.text.id;
        },

        javascript() {
            return function() {
                if(!this.$el) { return; }

                const $el = this.$el;

                const read_more_btn = $el.querySelector('.ecom-text_view-more-btn');
                const read_less_btn = $el.querySelector('.ecom-text_view-less-btn');
                const el_content = $el.querySelector('.text-content.ecom-html');
                if(!el_content) { return; }

                if(read_more_btn) {
                    read_more_btn.addEventListener('click', () => {
                        el_content.classList.remove('ecom-text--is-mark');
                        el_content.style.maxHeight = "";
                        read_more_btn.style.display = "none";
                        read_less_btn.style.display = "";
                    })
                }

                if(read_less_btn) {
                    read_less_btn.addEventListener('click', () => {
                        el_content.classList.add('ecom-text--is-mark');
                        el_content.style.maxHeight = "var(--ecom-text-height)";
                        read_less_btn.style.display = "none";
                        read_more_btn.style.display = "";
                    })
                }
            }
        },

        default(){
            return {
                settings: {
            		type: "full",
                    content: "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.",
                    "max-height": "200px",
                    view_more: true,
                    view_less_text: "view less",
                    spacing: "6px",
                    view_more_text: "view more"
                },
                style: {
                    text: {
                        textTextAlign: "left"
                    },
                    text_dropcap: {
                        dropcapTypography: {
                            "font-size": "40px"
                        },
                        dropcapColor: "#f50c0c",
                        dropcapColorStackedView: "#5eff00",
                        dropcapBackgroundStackedView: "#4800ff",
                        dropcapColorFramedView: "#1aff00",
                        dropcapBackgroundFramedView: "#1100ff",
                        dropcapView: "framed"
                    },
                    collection_view_more: {
                        tab: "normal",
                        "align-items": "flex-start",
                        buttonTypography: {
                            "text-decoration": "underline",
                            "font-size": "none"
                        },
                        spacing: {
                            padding: {
                                top: "5px",
                                bottom: "5px"
                            }
                        }
                    },
                        collection_view_less: {
                            tab: "normal",
                            buttonTypography: {
                                "text-decoration": "underline"
                            },
                            spacing: {
                                padding: {
                                top: "5px",
                                bottom: "5px"
                                }
                            }
                        },
                        icon: {
                            tab: "normal",
                            iconFontSize: "16px",
                            iconPrimaryColornormalmode: "#000000"
                        }
                }
            }
        }
    },
    watch: {
        activeMe(val) {
            if (!val && this.editing) {
                this.editing = false;
                this.text.settings.content = this.$refs.content.innerHTML;
            }
        },

        'data.style': {
            handler() {
                this.dropCapView = this.data?.style?.text_dropcap?.dropcapView ?? 'default';
            },
            deep: true
        }
    },
    methods: {
        style() {
            let style = [
                {
                    group_alias:'text',
                    options:{
                        selector: ' .text-content,  .text-content a,  .text-content p,  .text-content span'
                    },
                }
            ];
            if (this.data.settings?.dropcap) {
                style.push({
                    group_alias: 'text:dropcap',
                    options:{
                        selector: ' .ecom__text--dropcap:first-letter'
                    }
                });
            }

            if (this.data.settings.view_more) {
                style.push({
                    group_alias: 'button',
                    options: {
                        group_title: 'View more',
                        group_name: 'collection_view_more',
                        selector: ' .ecom-text_view-more-btn'
                    },
                    modify: {
                        position: 0,
                        params:{
                            alias:'align-items',
                            options:{
                                label:'Alignment',
                                css:{
                                    properties:{
                                        'justify-content':''
                                    }
                                }
                            }
                        }
                    }
                });

                style.push({
                    group_alias: 'button',
                    options: {
                        group_title: 'View less',
                        group_name: 'collection_view_less',
                        selector: ' .ecom-text_view-less-btn'
                    },
                    modify: {
                        position: 0,
                        params:{
                            alias:'align-items',
                            options:{
                                label:'Alignment',
                                css:{
                                    properties:{
                                        'justify-content':''
                                    }
                                }
                            }
                        }
                    }
                });
                 const iconParamsDefault = [
                {
                    name: "iconPrimaryColor",
                    label: "Color",
                    type: "color",
                    options: {
                        oneline: true,
                        global: {
                            type: "colors"
                        },
                    },
                    css: {
                        properties: {
                            color: ""
                        }
                    }
                },
                {
                    name: "iconBackground",
                    label: "Background",
                    type: "background",
                    options: {
                        oneline: true,
                        responsive: true,
                    },
                    css: {
                        properties: {
                            background: ""
                        }
                    }
                },
                {
                    type: "popup",
                    label: "Border",
                    name: "iconBorder",
                    options: {
                        oneline: true,
                        type: "border",
                    },
                    css: {
                        properties: {
                            border: ""
                        }
                    }
                },
                {
                    name: "iconBoxShadow",
                    label: "Box Shadow",
                    type: "popup",
                    options: {
                        oneline: true,
                        type: "box-shadow",
                    },
                    css: {
                        properties: {
                            "box-shadow": ""
                        }
                    }
                },
                {
                    name: "iconBorderRadius",
                    label: "Border Radius",
                    type: "dimension",
                    options: {
                        responsive: true,
                        type: "radius",
                        units: {
                            "%": {
                                min: 0,
                                max: 100
                            },
                            px: {
                                min: 0,
                                max: 1000
                            }
                        }
                    },
                    css: {
                        properties: {
                            overflow:'hidden',
                            "border-radius": ""
                        }
                    }
                },
            ]

            if(this.data.settings?.view_more_icon || this.data.settings?.view_less_icon)
            {
                style.push({
                    group_title: 'Icon',
                    group_name: 'icon',
                    selector: ' .ecom-text_view-more-btn, .ecom-text_view-less-btn',
                    params:[
                        {
                            name: "iconFontSize",
                            label: "Size",
                            type: "number",
                            options: {
                                responsive: true,
                                units: {
                                    px: {
                                        min: 0,
                                        max: 300
                                    }
                                }
                            },
                            css: {
                                selector: " svg",
                                properties: {
                                    height: "",
                                    width: ""
                                }
                            }
                        },
                        {
                            name: "iconTransform",
                            label: "Rotate",
                            type: "number",
                            options: {
                                responsive: true,
                                min: 0,
                                max: 360
                            },
                            css: {
                                selector: " svg",
                                properties: {
                                    transform: "rotate(%value%deg)"
                                }
                            }
                        },
                        {
                            name: "tab",
                            type: "tab",
                            value: "normal",
                            options: {
                                tabs: [
                                    {
                                        name: "normal",
                                        title: "Normal"
                                    },
                                    {
                                        name: "hover",
                                        title: "Hover"
                                    }
                                ]
                            },
                            css: {
                                isCss: false
                            }
                        },
                        ...addSwitch(iconParamsDefault,'normal' , { selector: '  svg'}),
                        ...addSwitch(iconParamsDefault,'hover',{ selector: ':hover  svg'}),
                    ]
                });
            }
            }
            return style;
        },
        edit() {
            this.convertText;
            if (this.editing === false) {
                this.editing = true;
                this.editingValue = this.data.settings.content;
            }
        },
        showMore(){
            let showMore = this.$refs?.showMore;
            let showLess = this.$refs?.showLess;
            let content = this.$refs?.content;
            if(content && showMore){
                content.classList.remove('ecom-text--is-mark');
                content.style.maxHeight = "";
                showMore.style.display = "none";
                showLess.style.display = "";
            }
        },
        showLess(){
            let showMore = this.$refs?.showMore;
            let showLess = this.$refs?.showLess;
            let content = this.$refs?.content;
            if(content && showLess){
                content.classList.add('ecom-text--is-mark');
                content.style.maxHeight = "var(--ecom-text-height)";
                showLess.style.display = "none";
                showMore.style.display = "";
            }
        },
        update(e) {
            this.text.settings.content = e.target.innerHTML;
        }
    }
};
</script>
