<template>
    <div class="ecom-element ecom-shopify ecom-shopify__search">
        <div class="ecom-element ecom-shopify ecom-shopify__search-wrapper">
            <div
                class="ecom-element ecom-shopify ecom-shopify__search-container"
                v-html="liquid('search_form')"
            />
        </div>
    </div>
</template>

<script>
    import { Liquid, Element } from '@/mixins';
    export default {
        name: 'ShopifySearch',
        mixins: [Liquid, Element],
        props: {
            data: {
                type: Object,
                default () {
                    return {};
                }
            }
        },
        computed: {

            liquids () {
                return {
                    search_form: {
                        code: `
                            <form action="{{ routes.search_url }}" method="GET" class="ecom-shopify__search-form ecom-flex ecom-al_center">
                                <input type="text"
                                    class="ecom-shopify__search-input"
                                    placeholder="${this.lang(this.data.settings.placeholder,'search_placeholder' )}"
                                    name="q"
                                    value="{{ search.terms | escape }}"
                                />
                                <input type="hidden" name="type" value="${this.types}" />
                                <input type="hidden" name="options[unavailable_products]" value="${this.unavailable_products}" />
                                <input type="hidden" name="options[prefix]" value="${this.prefix}" />
                                <input type="hidden" name="options[sort_by]" value="${this.sort_by}" />
                                ${this.data.settings?.search_button_title || this.data.settings?.icon
                                 ? `
                                    <button type="submit" class="ecom-flex ecom-al_center ecom-fl_center ecom-search-btn-submit">
                                        ${this.lang(this.data.settings.search_button_title,'search_button_title')}
                                        ${this.data.settings?.icon ? `<span class="ecom__element--button-icon ecom-flex ecom-al_center ecom-fl_center">${this.data.settings?.icon}</span>` : ``}
                                    </button>
                                 `
                                 : ``
                                 }

                            </form>
                        `,
                        preview: `<div class="ecom-skeleton-item ecom__row">
                                <div class="ecom-skeleton-col-12 ecom-col-12">
                                    <div class="ecom-skeleton-row">
                                        <div class="ecom-skeleton-col-8 ecom-skeleton-big"></div>
                                    </div>
                                </div>
                            </div>`
                    }
                };
            },

            settings () {
                return [{
                    params: [

                        {
                            type: 'text',
                            name: 'placeholder',
                            label: 'Placeholder text',
                            options: {
                                toolbar: false
                            }
                        },
                        {
                            type: 'text',
                            name: 'search_button_title',
                            label: 'Button text',
                            options: {
                                toolbar: false
                            }
                        },
                        {
                            type: 'choose',
                            label: 'Button position',
                            name: 'btn_position',
                            options: {
                                type: 'align-x',
                                values: [-1, 1],
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['icon'];
                                    }
                                }
                            },
                            css:{
                                selector:' .ecom-search-btn-submit',
                                properties:{
                                    order:''
                                }
                            }
                        },
                        {
                            type: 'picker',
                            label: 'Icon',
                            name: 'icon',
                            options: {
                                type: 'icon',
                                layout: 'grid',
                                output: 'value',
                                multiple: false,
                                simple: false
                            }
                        },
                        {
                            type: 'choose',
                            label: 'Icon position',
                            name: 'icon_position',
                            options: {
                                type: 'align-x',
                                values: [-1, 1],
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['icon'];
                                    }
                                }
                            },
                            css:{
                                selector:' .ecom__element--button-icon',
                                properties:{
                                    order:''
                                }
                            }
                        },
                        {
                            type: 'number',
                            label: 'Icon spacing',
                            name: 'spacing',
                            options: {
                                units: {
                                    px: {
                                        min: 0,
                                        max: 200
                                    }
                                },
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['icon'];
                                    }
                                }
                            },
                            css:{
                                selector:' .ecom-search-btn-submit',
                                properties:{
                                    gap:''
                                }
                            }
                        },
                        {
                            type: 'line'
                        },
                        {
                            type: 'checkbox',
                            name: 'types',
                            label: 'Specifies the type of results requested',
                            options: {
                                values: {
                                    product: 'Product',
                                    page: 'Page',
                                    article: 'Article'
                                }
                            }
                        }
                        ,{
                            type: 'dropdown',
                            name: 'prefix',
                            label: 'Prefix',
                            options: {
                                preview: 'title',
                                default: false,
                                values: {
                                    none: 'None',
                                    show: 'Show',
                                }
                            },
                            description: 'For example, if "winter snow" is used as a search query, a search will find all applicable resources that contain both "winter" and any term that starts with "snow". This could be terms like "snow", "snowshoe", or "snowboard".'
                        },
                        {
                            type: 'dropdown',
                            name: 'unavailable_products',
                            label: 'Unavailable products display.',
                            options: {
                                preview: 'title',
                                default: false,
                                values: {
                                    last: 'Order last',
                                    hide: 'Hide',
                                    show: 'Show',
                                },
                                 visible: {
                                    condition: (data) => {
                                        return data['types'] && data['types'].includes('product');
                                    }
                                }
                            }
                        },
                        {
                            type: 'dropdown',
                            name: 'sort_by',
                            label: 'Sort by',
                            options: {
                                preview: 'title',
                                default: false,
                                values: {
                                    relevance: 'Relevance',
                                    'price-ascending': 'Price ascending',
                                    'price-descending': 'Price descending'
                                },
                                visible: {
                                    condition: (data) => {
                                        return data['types'] && data['types'].includes('product');
                                    }
                                }
                            },
                            description: 'Specifies the sort order of the <results className=""></results>'
                        },
                    ]
                }];
            },
            layout () {
                return this.data && this.data.settings && this.data.settings.layout ? this.data.settings.layout : 'horizontal';
            },
            types () {
                return this.data && this.data.settings && this.data.settings.types ? this.data.settings.types : 'all';
            },
            unavailable_products () {
                return this.data && this.data.settings && this.data.settings.unavailable_products ? this.data.settings.unavailable_products : 'hide';
            },
            prefix () {
                return this.data && this.data.settings && this.data.settings.prefix ? this.data.settings.prefix : 'none';
            },
            sort_by () {
                return this.data && this.data.settings && this.data.settings.sort_by ? this.data.settings.sort_by : 'relevance';
            },
            css(){
                return `
                .ecom-shopify__search-form input:focus-visible {
                    border : none;
                    outline : none;
                }
                .ecom-shopify__search-form input {
                    outline:none;
                }
                .ecom-shopify__search-form button {
                    cursor: pointer;
                }
                `;
            },

            default() {
                return {
                    "settings": {
                        "search_button_title": "Search",
                        "placeholder": "Type some thing to search on store...",
                        "prefix": "none",
                        "unavailable_products": "last",
                        "types": [
                            "page",
                            "article"
                        ],
                        "icon": "<svg version=\"1.1\" id=\"lni_lni-search-alt\" xmlns=\"http://www.w3.org/2000/svg\" xmlns:xlink=\"http://www.w3.org/1999/xlink\" x=\"0px\" y=\"0px\" viewBox=\"0 0 64 64\" style=\"enable-background:new 0 0 64 64;\" xml:space=\"preserve\" fill=\"currentColor\">\n<path d=\"M62.1,57L44.6,42.8c3.2-4.2,5-9.3,5-14.7c0-6.5-2.5-12.5-7.1-17.1v0c-9.4-9.4-24.7-9.4-34.2,0C3.8,15.5,1.3,21.6,1.3,28\n\tc0,6.5,2.5,12.5,7.1,17.1c4.7,4.7,10.9,7.1,17.1,7.1c6.1,0,12.1-2.3,16.8-6.8l17.7,14.3c0.3,0.3,0.7,0.4,1.1,0.4\n\tc0.5,0,1-0.2,1.4-0.6C63,58.7,62.9,57.6,62.1,57z M10.8,42.7C6.9,38.8,4.8,33.6,4.8,28s2.1-10.7,6.1-14.6c4-4,9.3-6,14.6-6\n\tc5.3,0,10.6,2,14.6,6c3.9,3.9,6.1,9.1,6.1,14.6S43.9,38.8,40,42.7C32,50.7,18.9,50.7,10.8,42.7z\"></path>\n</svg>",
                        "spacing": "10px"
                    },
                    "style": {
                        "input": {
                            "tab": "focus",
                            "text-align": "left",
                            "border": {
                                "border-style": "solid",
                                "border-width": {
                                    "top": "1px",
                                    "left": "1px",
                                    "bottom": "1px",
                                    "right": "1px"
                                },
                                "border-color": "#ccc"
                            },
                            "outline": {
                                "outline": {
                                    "outline-style": "none"
                                }
                            },
                            "borderRadius": {
                                "top": "0px",
                                "left": "0px",
                                "bottom": "0px",
                                "right": "0px"
                            },
                            "outline_focus": {
                                "outline": {
                                    "outline-style": "none"
                                }
                            }
                        },
                        "button": {
                            "tab": "normal",
                            "spacing": {
                                "margin": {
                                    "left": "13px"
                                }
                            },
                            "buttonBorderRadiusnormalmode": {
                                "top": "0px",
                                "left": "0px",
                                "bottom": "0px",
                                "right": "0px"
                            },
                            "buttonBordernormalmode": {
                                "border-style": "none"
                            },
                            "buttonBackgroundnormalmode": {
                                "classic": {
                                    "background-color": "#07a2bc"
                                }
                            },
                            "buttonColornormalmode": "#fff",
                            "buttonHeightnormalmode": "40px",
                            "buttonWidthnormalmode": "100px"
                        },
                        "icon": {
                            "iconFontSize": "19px"
                        }
                    }
                }
            }


        },
        methods: {
            style() {
                let styles = [
                    {
                        group_alias: 'box',
                        options: {
                            group_title: 'General',
                            selector: ' .ecom-shopify__search'
                        },
                    },
                    {
                        group_alias: 'input',
                        options: {
                            group_name: 'input',
                            group_title: 'Input',
                            selector: ' .ecom-shopify__search-input'
                        },
                        modify: {
                            params: [
                                {
                                    position: 6,
                                    fields: [
                                        {
                                            type: "number",
                                            name: 'height_input',
                                            label: "Height",
                                            options: {
                                                responsive: true,
                                                reset: true,
                                                units: {
                                                    px: {
                                                        min: 0,
                                                        max: 300
                                                    }
                                                }
                                            },
                                            css: {
                                                properties: {
                                                    height: ""
                                                }
                                            }
                                        }
                                    ]
                                }
                            ]
                        }
                    },
                    {
                        group_alias: 'button',
                        options:{
                            group_title: 'Submit button',
                            group_name: 'button',
                            selector: ' .ecom-search-btn-submit',
                        },
                        modify: {
                            remove: {
                                label: ['Width', 'Height']
                            },
                            params: [
                                {
                                    position: 6,
                                    fields: [
                                        {
                                            name: 'button_width',
                                            label: 'Set width',
                                            type: 'number',
                                            options: {
                                                visible: {
                                                    keep_data: false,
                                                    condition: data => data.tab == 'normal'
                                                },
                                                responsive: true,
                                                units: {
                                                    px: {
                                                        min: 0,
                                                        max: 500
                                                    },
                                                    '%': {
                                                        min: 0,
                                                        max: 100
                                                    }
                                                }
                                            },
                                            css:{
                                                properties:{
                                                    'width': ''
                                                }
                                            }
                                        },
                                        {
                                            name: 'button_height',
                                            label: 'Set height',
                                            type: 'number',
                                            options: {
                                                visible: {
                                                    keep_data: false,
                                                    condition: data => data.tab == 'normal'
                                                },
                                                responsive: true,
                                                units: {
                                                    px: {
                                                        min: 0,
                                                        max: 500
                                                    },
                                                    '%': {
                                                        min: 0,
                                                        max: 100
                                                    }
                                                }
                                            },
                                            css:{
                                                properties:{
                                                    'height': '',
                                                    'min-height': '',
                                                }
                                            }
                                        },
                                        {
                                            name: 'button_width_hover',
                                            label: 'Set width',
                                            type: 'number',
                                            options: {
                                                visible: {
                                                    keep_data: false,
                                                    condition: data => data.tab == 'hover'
                                                },
                                                responsive: true,
                                                units: {
                                                    px: {
                                                        min: 0,
                                                        max: 500
                                                    },
                                                    '%': {
                                                        min: 0,
                                                        max: 100
                                                    }
                                                }
                                            },
                                            css:{
                                                selector: 'root .ecom-search-btn-submit:hover',
                                                properties:{
                                                    'width': ''
                                                }
                                            }
                                        },
                                        {
                                            name: 'button_height_hover',
                                            label: 'Set height',
                                            type: 'number',
                                            options: {
                                                visible: {
                                                    keep_data: false,
                                                    condition: data => data.tab == 'hover'
                                                },
                                                responsive: true,
                                                units: {
                                                    px: {
                                                        min: 0,
                                                        max: 500
                                                    },
                                                    '%': {
                                                        min: 0,
                                                        max: 100
                                                    }
                                                }
                                            },
                                            css:{
                                                selector: 'root .ecom-search-btn-submit:hover',
                                                properties:{
                                                    'height': '',
                                                    'min-height': '',
                                                }
                                            }
                                        }
                                    ]
                                }
                            ]
                        }
                    },
                    {
                        group_alias:'icon',
                        options:{
                            group_name: 'icon',
                            group_title: 'Icon',
                            selector: ' .ecom__element--button-icon',
                        },
                        modify:{
                            params:[
                                {
                                    position:15,
                                    fields:[
                                        {
                                            type: 'line'
                                        },
                                        {
                                            alias:'spacing'
                                        }
                                    ]
                                }
                            ]
                        }
                    }
                ];

                return styles;
            }
        }

    };
</script>
