<template>
    <div class="ecom-element ecom-shopify-elements ecom-shopify__article-date">
        <div class="ecom-shopify__article-date--wrapper">
            <div
                class="ecom-shopify__article-date--container ecom-flex ecom-al_center"
            >
                <div
                    v-if="data.settings?.icon"
                    class="ecom-shopify__article-date-icon ecom-flex ecom-al_center"
                    v-html="data.settings?.icon"
                />
                <div
                    class="ecom-shopify__article-date--date"
                    v-html="liquid('date')"
                />
            </div>
        </div>
    </div>
</template>

<script>
    import { Liquid, Element } from '@/mixins';

    export default {
        name: 'ArticleAuthor',

        mixins: [Liquid, Element],
        props: {
            data: {
                type: Object,
                default () {
                    return {};
                }
            }
        },
        computed: {
            css () {
                return `
                    .ecom-shopify__article-date-icon svg {
                        width: 16px;
                        height: 16px;
                    }
                `;
            },
            liquids () {
                const format =this.data.settings?.date_format? this.data.settings.date_format: 'date';
                return {
                    date: {
                        code: `
                        {% if article != empty %}
                            <div class="ecom-shopify__article-date-text">
                                {{article.${this.type} | time_tag: format: '${format}'  }}
                            </div>
                        {% else %}
                            <div>Please select the article in settings</div>
                        {% endif %}
                    `,
                        preview: `
                        <div class="ecom-skeleton-item">
                            <div class="ecom-skeleton-col-12">
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-9 ecom-skeleton-big"></div>
                                </div>
                            </div>
                        </div>
                    `
                    }
                };
            },

            requestShopifyType () {
                return {
                    shopify_type: 'article'
                };
            },
            type(){
                return this.data?.settings?.type??'created_at';
            },
            settings () {
                return [
                    {
                        params: [
                            {
                                type: 'popup',
                                label: 'Date type',
                                name:'type',
                                options: {
                                    type: 'dropdown',
                                    default: false,
                                    values:{
                                        updated_at: 'Updated at',
                                        created_at: 'Created at',
                                        published_at: 'Published at'
                                    }
                                }
                            },
                            {
                                type: 'popup',
                                name: 'date_format',
                                label: 'Date format',
                                options: {
                                    type: 'dropdown',
                                    values: {
                                        default:
                                            'Monday, December 31, 2018 at 1:00 pm -0500',
                                        abbreviated_date: 'Aug 08, 1994',
                                        basic: '12/31/2021',
                                        date: 'December 31, 2018',
                                        date_at_time: 'December 31, 2018 at 1:00 pm',
                                        on_date: 'on Dec 31, 2018'
                                    }
                                }
                            },
                            {
                                type: 'picker',
                                label: 'Icon',
                                name: 'icon',
                                options: {
                                    type: 'icon',
                                    layout: 'grid',
                                    output: 'value',
                                    multiple: false,
                                    simple: false
                                }
                            },
                            {
                                type: 'choose',
                                label: 'Icon position',
                                name: 'icon_position',
                                options: {
                                    type: 'align-x',
                                    values: [-1, 1],
                                    visible: {
                                        keep_data: false,
                                        condition: (data) => {
                                            return data['icon'];
                                        }
                                    }
                                },
                                css:{
                                    selector:' .ecom-shopify__article-date-icon',
                                    properties:{
                                        order:''
                                    }
                                }
                            },
                            {
                                type: 'number',
                                label: 'Spacing',
                                name: 'spacing',
                                options: {
                                    units: {
                                        px: {
                                            min: 0,
                                            max: 200
                                        }
                                    },
                                    visible: {
                                        keep_data: false,
                                        condition: (data) => {
                                            return data['icon'];
                                        }
                                    }
                                },
                                css:{
                                    selector:' .ecom-shopify__article-date--container',
                                    properties:{
                                        gap:''
                                    }
                                }
                            },
                        ]
                    }
                ];
            },

            style () {
                return [
                    {
                        group_alias: 'text:spacing',
                        options: {
                            group_name: 'date_text',
                            group_title: 'General',
                            selector: ' .ecom-shopify__article-date-text'
                        }
                    },
                    {
                        group_alias:'icon',
                        options:{
                            group_name: 'icon',
                            group_title: 'Icon',
                            selector: ' .ecom-shopify__article-date-icon',
                        },
                        modify:{
                            params:[
                                {
                                    position:15,
                                    fields:[
                                        {
                                            type: 'line'
                                        },
                                        {
                                            alias:'spacing'
                                        }
                                    ]
                                }
                            ]
                        }
                    },
                ];
            },

            default() {
                return {
                    settings: {
                        type: "created_at",
                        date_format: "date"
                    },
                    style: {
                        date_text: {
                            textTextAlign: "left"
                        }
                    }
                }
            }
        }
    };
</script>
