        javascript() {
            return function () {
                let _this = this;
                let el_id = this.id;
                const $el = this.$el;

                if(! $el) return;
                
                var timelineCore = document.querySelector(`.${el_id}`);
                if(! timelineCore) return;
                var timelineBase = timelineCore.querySelector('.ecom-base-timeline');
                var timelineGrid = timelineBase.querySelector('.ecom-timeline_grid');

                var numberColumn = timelineBase.dataset.numberColumn || 3; // Desktop
                var numberColumnTablet = timelineBase.dataset.numberColumnTablet || 2; // Tablet
                var numberColumnMobile = timelineBase.dataset.numberColumnMobile || 1; // Mobile

                var itemLength = timelineBase.dataset.itemsLength;

                //init grid when in horizontal
                if(timelineBase && timelineBase.classList.contains('ecom-timeline--horizontal')) {
                    var prevBtn = timelineBase.querySelector('.ecom-timeline_prev');
                    var nextBtn = timelineBase.querySelector('.ecom-timeline_next');
                    var diff = 0;
                    var singleItemWidth = 0;
                    var stack = 0;
                    var limitStack = 0;


                    function initGrid() {
                        stack = 0;

                        if(!document.querySelector(`.${el_id}`)) {
                            window.removeEventListener('resize', initGrid);
                            return;
                        }


                        if(itemLength) {
                            // Init grid width
                            if(window.innerWidth <= 767) {
                                singleItemWidth = timelineCore.offsetWidth/numberColumnMobile;
                                diff = itemLength - numberColumnMobile;

                            } else if(window.innerWidth >= 768 && window.innerWidth <= 1024) {
                                singleItemWidth = timelineCore.offsetWidth/numberColumnTablet;
                                diff = itemLength - numberColumnTablet;

                            } else if(window.innerWidth >= 1025) {
                                singleItemWidth = timelineCore.offsetWidth/numberColumn; 
                                diff = itemLength - numberColumn;
                            }
                            limitStack = diff;

                            let initGridWidth = singleItemWidth*itemLength;
                            if(timelineGrid) {
                                timelineGrid.style.transform = `translateX(0px)`;
                                timelineGrid.style.width = initGridWidth + "px";
                                setTimeout(()=> {
                                    initHorizontalItemHeight();
                                }, 500)
                                // initSlider(singleItemWidth, diff);
                            }
                        }
                    }

                    function initHorizontalItemHeight() {
                        let itemsContent = null;
                        // Check if is horizontal alternating
                        let isHorizontalAlternating = timelineBase.classList.contains('ecom-timeline--alternating');
                        if(isHorizontalAlternating) {
                            //init item height
                            itemsContent = timelineBase.querySelectorAll('.timeline-item_content--top, .timeline-item_content');
                        } else {
                            //init item height
                            itemsContent = timelineBase.querySelectorAll('.timeline-item_content--top');
                        }

                        if(itemsContent) {
                            let maxHeight = 0;
                            itemsContent.forEach((itemContent) => {
                                itemContent.style.height = null;
                                maxHeight = (itemContent.offsetHeight > maxHeight) ? itemContent.offsetHeight : maxHeight;
                            })
                            if(maxHeight > 0) {
                                itemsContent.forEach((itemContent) => {
                                    itemContent.style.height = maxHeight + 'px';
                                })

                                if(!isHorizontalAlternating) {
                                    let lineHolders = timelineBase.querySelectorAll('.timeline-item_line-holder');
                                    if(lineHolders) {
                                        lineHolders.forEach((line) => {
                                            line.style.top = maxHeight + 'px';
                                        })
                                    }
                                    
                                    let pointHolders = timelineBase.querySelectorAll('.timeline-item_point-holder');
                                    if(pointHolders) {
                                        pointHolders.forEach((line) => {
                                            line.style.top = maxHeight + 'px';
                                        })
                                    }

                                    if(prevBtn) {
                                        prevBtn.style.top = maxHeight + 'px';
                                    }

                                    if(nextBtn) {
                                        nextBtn.style.top = maxHeight + 'px';
                                    }
                                }
                            }

                            
                        }
                    }

                    function initSlider() {
                        if(diff > 0) {
                            if(prevBtn) {
                                prevBtn.addEventListener('click', moveBack);
                            }
                            if(nextBtn) {
                                nextBtn.addEventListener('click', moveForward);
                            }

                            function moveBack() {
                                stack += singleItemWidth;
                                if(stack > 0) {
                                    stack = 0;
                                }
                                if(limitStack < diff) {
                                    limitStack += 1;
                                }
                                timelineGrid.style.transform = `translateX(${stack}px)`;
                            }

                            function moveForward() {
                                limitStack -= 1;
                                if(limitStack >= 0) {
                                    stack -= singleItemWidth;
                                    timelineGrid.style.transform = `translateX(${stack}px)`;
                                } else {
                                    limitStack = 0;
                                }
                            }
                        }
                    }


                    
                    initGrid();
                    initSlider();
                    window.addEventListener('resize', initGrid);
                    
                    //Rerender/init grid whenever an image is loaded
                    var images_list = timelineBase.querySelectorAll('img.ecom-image-default');
                    if(images_list) {
                        images_list.forEach((image) => {
                            image.onload = () => {
                                initGrid();
                            }
                        })
                    }
                }
                
                if(timelineBase) {
                    let activeAppearanceAnimation = timelineBase.dataset.appearanceAnimation || 'false';
                    let isLoopAnimation = timelineBase.dataset.loopAnimation || 'false';
                    
                    if(activeAppearanceAnimation == 'true') {
                        let options = {
                            threshold: 1
                        }
                        if(timelineBase.classList.contains('ecom-timeline--horizontal')) {
                            options = {
                                threshold: 0.5
                            }
                        }
                        // Handle add animation class on scroll
                        
                        var transDelay = 0;
                        function handleAddTransDelay(el, i) {
                            if(numberColumn && i < numberColumn) {
                                let contentTop = el.querySelector(".timeline-item_content--top");
                                let content = el.querySelector(".timeline-item_content");
                                transDelay = i * 250;
                                if(contentTop) {
                                    contentTop.style.transitionDelay = transDelay + "ms";
                                }
                                if(content) {
                                    content.style.transitionDelay = transDelay + "ms";
                                }
                            }
                        }
                        
                        function scrollTrigger(options = {}) {
                            // Timeline item handle
                            let els = timelineBase.querySelectorAll('.ecom-timeline_item');
                            if(els.length > 0 && timelineBase && timelineBase.classList.contains('ecom-timeline--horizontal')) {
                                els.forEach((el, i)=>{
                                    addObserver(el, options);
                                    handleAddTransDelay(el, i);
                                })
                            } else {
                                els.forEach((el)=>{
                                    addObserver(el, {rootMargin: "-25% 0% -25% 0%",threshold: 0});
                                })
                            }
                        }
                        
                        function addObserver(el, options) {
                            let observer = new IntersectionObserver((entries, observer) => {
                                entries.forEach(entry => {
                                    if(entry.boundingClientRect.top < 0) {
                                        entry.target.classList.add("ecom-line-animated", entry.isIntersecting);
                                    }
                                    if(isLoopAnimation == 'true') {
                                        if(entry.boundingClientRect.top > 0) {
                                            entry.target.classList.toggle("ecom-line-animated", entry.isIntersecting);
                                        }
                                    } else {
                                        if(entry.isIntersecting){
                                            entry.target.classList.add("ecom-line-animated");
                                            observer.unobserve(entry.target)
                                        }
                                    }
                                })
                            }, options)
                            observer.observe(el)
                        }

                        setTimeout(()=> {
                            scrollTrigger(options);
                            // Dynamic line vertical custom
                            if(timelineBase.classList.contains('ecom-timeline--vertical')) {
                                const element = timelineBase.querySelector(".ecom-timeline_dynamic-line");
                                let percent = 0;
                                
                                function initDynamicLineVertical() {
                                    let pageHeight = window.innerHeight;
                                    const rect = timelineBase.getBoundingClientRect();
                                    
                                    
                                    if(!element) return;
                                    if(rect.top < pageHeight && percent <= 130) {
                                        percent = Math.round((pageHeight - rect.top)* 100 / rect.height);
                                    }

                                    if(percent > 130) {
                                        percent = 130;
                                    }

                                    element.style.height = `calc(${percent}% - 100px)`;
                                }
    
                                window.addEventListener("scroll", initDynamicLineVertical);
                                window.addEventListener("resize", initDynamicLineVertical);

                            }
                        }, 700)
                    }
                    
                }            
            }
        }
    },
    methods: {
        style() {
            let style = [];
            if (this.data?.settings?.items) {
                // Check if there is any item
                if(this.data?.settings?.items.length > 0) {
