                                    }
                                }
                            },
                            {
                                alias:'padding',
                                options: {
                                    name: 'outer-padding',
                                    label: 'Outer padding',
                                    css: {
                                        important: true,
                                        selector: ' .timeline-item_content'
                                    }
                                }
                            }
                        ]
                    })
                }

                //Check if dynamic line available 
                if(this.isVertical && this.hasDynamicLine) {
                    style.push({
                        group_name: 'dynamic_line',
                        group_title: 'Dynamic line',
                        params: [
                            {
                                label: "Size",
                                type: "number",
                                name: "width",
                                options: {
                                    units: {
                                        px: { max: 20, min: 0, step: 1 }
                                    }
                                },
                                css: {
                                    selector: " .ecom-timeline_dynamic-line",
                                    properties: {
                                        width: ''
                                    }
                                }
                            },
                            {
                                label: "Color",
                                type: "color",
                                name: "color",
                                options: {
                                    oneline: true,
                                    global: {
                                        type: "colors"
                                    }
                                },
                                css: {
                                    selector: " .ecom-timeline_dynamic-line",
                                    properties: {
                                        'background-color': ""
                                    }
                                }
                            }
                        ]
                    })
                }

                //Check if there has any image
                if(this.items.some((item) => item.image?.value)) {
                    style.push({
                        group_alias:'image',
                        options:{
                            group_title: 'Thumbnail',
                            selector: ' .ecom-image-default'
                        },
                        modify:{
                            params:[
                                {
                                    position: 19,
                                    fields:[
                                        {
                                            type: "line"
                                        },
                                        {
                                            alias:'spacing',
                                            options: {
                                                css: {
                                                    selector: ' .ecom-image-default'
                                                }
                                            }
                                        }
                                    ]
                                }
                            ]
                        }
                    });
                }

                //Check if there has any date
                if(this.items.some((item) => item.date)) {
                    style.push({
                        group_alias:'text',
                        options: {
                            group_title: 'Date',
                            group_name: 'date',
                            selector: ' .timeline-item_date'
                        },
                        modify: {
                            params: [
                                {
                                    position: 10,
                                    fields: {alias: 'spacing'}
                                }
                            ]
                        }
                    });
                }

                //Check if there has any title
                if(this.items.some((item) => item.title)) {
                    style.push({
                        group_alias:'text',
                        options: {
                            group_title: 'Title',
                            group_name: 'title',
                            selector: ' .timeline-item_title'
                        },
                        modify: {
                            params: [
                                {
                                    position: 10,
                                    fields: {alias: 'spacing'}
                                }
                            ]
                        }
                    });
                }

                //Check if there has any content
                if(this.items.some((item) => item.content)) {
                    style.push({
                        group_alias:'text',
                        options: {
                            group_title: 'Content',
                            group_name: 'content',
                            selector: ' .timeline-item_text'
                        },
                        modify: {
                            params: [
                                {
                                    position: 10,
                                    fields: {alias: 'spacing'}
                                }
                            ]
                        }
                    });
                }

                let thickness_line_properties = null;
                let diamond_line_properties = null;
                let line_params = [];
                if(this.isHorizontal) {
                    thickness_line_properties = {
                        height: ''
                    }

                    diamond_line_properties = {
                        width: ''
                    }
                } else {
                    thickness_line_properties = {
                        width: ''
                    }

                    diamond_line_properties = {
                        height: ''
                    }

                    line_params.push({
                        label: 'Type',
                        type: 'popup',
                        name: 'type',
                        options: {
                            default: false,
                            type: 'dropdown',
                            values: {
                                inside: 'inside',
                                outside: 'outside'
                            }
                        }
                    })
                }
                
                line_params.push(
                    {
                        label: "Color nomal",
                        type: "color",
                        name: "color",
                        options: {
                            oneline: true,
                            global: {
                                type: "colors"
                            }
                        },
                        css: {
                            selector: " .timeline-item_line-holder .timeline-item_line",
                            properties: {
                                'background-color': ""
                            }
                        }
                    },
                    {
                        label: "Color active",
                        type: "color",
                        name: "color_active",
                        options: {
                            oneline: true,
                            global: {
                                type: "colors"
                            }
                        },
                        css: {
                            selector: " .ecom-timeline_item.line-active .timeline-item_line-holder .timeline-item_line",
                            properties: {
                                'background-color': ""
                            }
                        }
                    },
                    {
                        label: "Thickness",
                        type: "number",
                        name: "thickness",
                        options: {
                            responsive: true,
                            units: {
                                px: { max: 20, min: 0, step: 1 }
                            }
                        },
                        css: {
                            selector: " .timeline-item_line-holder, .timeline-item_point-holder.timeline-item_point-diamond::after, .timeline-item_point-holder.timeline-item_point-diamond::before",
                            properties: thickness_line_properties
                        }
                    }
                )

                if(this.isPointDiamond) {
                    line_params.push({
                        label: "Diamond line size",
                        type: "number",
                        name: "diamond_line_size",
                        options: {
                            responsive: true,
                            units: {
                                px: { max: 100, min: 0, step: 1 }
                            }
                        },
                        css: {
                            selector: " .timeline-item_point-holder.timeline-item_point-diamond::before, .timeline-item_point-holder.timeline-item_point-diamond::after",
                            properties: diamond_line_properties
                        }
                    })
                }
                //Line style

                //Lam den doan line style
                style.push({
                    group_name: 'line',
                    group_title: 'Line',
                    params: line_params
                });

                //Point style
                style.push({
                    group_name: 'point',
                    group_title: 'Point',
                    params: [
                        {
                            label: 'Type',
                            type: 'popup',
                            name: 'type',
                            options: {
                                default: false,
                                type: 'dropdown',
                                preview: 'title',
                                values: {
                                    standard: 'Standard',
                                    diamond: 'Diamond'
                                }
                            }
                        },

                        {
                            type: 'dimension',
                            label: 'Border radius',
                            name: 'border_radius',
                            options: {
                                type: 'radius',
                                units: 'default'
                            },
                            css: {
                                properties: {
                                    'border-radius': ''
                                },
                                selector: ' .timeline-item_point'
                            }
                        },
                        {
                            type: 'tab',
                            name: 'tab',
                            value: 'normal',
                            options: {
                                tabs: [
                                    {
                                        name: 'normal',
                                        title: 'Normal'
                                    },
                                    {
                                        name: 'hover',
                                        title: 'Hover'
                                    },
                                    {
                                        name: 'active',
                                        title: 'Active'
                                    }
                                ]
                            },
                            css: false
                        },
                        {
                            label: "Width",
                            type: "number",
                            name: "width",
                            options: {
                                visible: function(data) {
                                    return (data['tab'] == 'normal');
                                },
                                responsive: true,
                                units: {
                                    px: { max: 100, min: 0, step: 1 }
                                }
                            },
                            css: {
                                selector: " .timeline-item_point-holder",
                                properties: {
                                    width: ''
                                }
                            }
                        },
                        {
                            label: "Height",
                            type: "number",
                            name: "height",
                            options: {
                                visible: function(data) {
                                    return (data['tab'] == 'normal');
                                },
                                responsive: true,
                                units: {
                                    px: { max: 100, min: 0, step: 1 }
                                }
                            },
                            css: {
                                selector: " .timeline-item_point-holder",
                                properties: {
                                    height: ''
                                }
                            }
                        },
                        {
                            label: "Color",
                            type: "color",
                            name: "color",
                            options: {
                                visible: function(data) {
                                    return (data['tab'] == 'normal');
                                },
                                oneline: true,
                                global: {
                                    type: "colors"
                                }
                            },
                            css: {
                                selector: " .timeline-item_point",
                                properties: {
                                    'background-color': ""
                                }
                            }
                        },
                        {
                            label: "Size",
                            type: "number",
                            name: "size_hover",
                            options: {
                                visible: function(data) {
                                    return (data['tab'] == 'hover');
                                },
                                responsive: true,
                                units: {
                                    px: { max: 100, min: 0, step: 1 }
                                }
                            },
                            css: {
                                selector: " .ecom-timeline_item:hover .timeline-item_point-holder",
                                properties: {
                                    width: '',
                                    height: ''
                                }
                            }
                        },
                        {
                            label: "Width",
                            type: "number",
                            name: "width_hover",
                            options: {
                                visible: function(data) {
                                    return (data['tab'] == 'hover');
                                },
                                responsive: true,
                                units: {
                                    px: { max: 100, min: 0, step: 1 }
                                }
                            },
                            css: {
                                selector: " .ecom-timeline_item:hover .timeline-item_point-holder",
                                properties: {
                                    width: ''
                                }
                            }
                        },
                        {
                            label: "Height",
                            type: "number",
                            name: "height_hover",
                            options: {
                                visible: function(data) {
                                    return (data['tab'] == 'hover');
                                },
                                responsive: true,
                                units: {
                                    px: { max: 100, min: 0, step: 1 }
                                }
                            },
                            css: {
                                selector: " .ecom-timeline_item:hover .timeline-item_point-holder",
                                properties: {
                                    height: ''
                                }
                            }
                        },
                        {
                            label: "Color",
                            type: "color",
                            name: "color_hover",
                            options: {
                                visible: function(data) {
                                    return (data['tab'] == 'hover');
                                },
                                oneline: true,
                                global: {
                                    type: "colors"
                                }
                            },
                            css: {
                                selector: " .ecom-timeline_item:hover .timeline-item_point",
                                properties: {
                                    'background-color': ""
                                }
                            }
                        },
                        {
                            label: "Width",
                            type: "number",
                            name: "width_active",
                            options: {
                                visible: function(data) {
                                    return (data['tab'] == 'active');
                                },
                                responsive: true,
                                units: {
                                    px: { max: 100, min: 0, step: 1 }
                                }
                            },
                            css: {
                                selector: " .ecom-timeline_item.line-active .timeline-item_point-holder",
                                properties: {
                                    width: ''
                                }
                            }
                        },
                        {
                            label: "Height",
                            type: "number",
                            name: "height_active",
                            options: {
                                visible: function(data) {
                                    return (data['tab'] == 'active');
                                },
                                responsive: true,
                                units: {
                                    px: { max: 100, min: 0, step: 1 }
                                }
                            },
                            css: {
                                selector: " .ecom-timeline_item.line-active .timeline-item_point-holder",
                                properties: {
                                    height: ''
                                }
                            }
                        },
                        {
                            label: "Color",
                            type: "color",
                            name: "color_active",
                            options: {
                                visible: function(data) {
                                    return (data['tab'] == 'active');
                                },
                                oneline: true,
                                global: {
                                    type: "colors"
                                }
                            },
                            css: {
                                selector: " .ecom-timeline_item.line-active .timeline-item_point",
                                properties: {
                                    'background-color': ""
                                }
                            }
                        },

                        {
                            type: "popup",
                            label: "Hover Animation",
                            name: "animation",
                            options: {
                                visible: function(data) {
                                    return (data['tab'] === 'hover');
                                },
                                type: "dropdown",
                                values: "animation",
                                size: "small",
                            },
                            css: {
                                selector: "root .ecom-timeline_item:hover .timeline-item_point",
                                properties: {
                                    animation: ""
                                }
                            }
                        },
                        {
                            type: "popup",
                            label: "Transition",
                            name: "transition",
                            options: {
                                visible: function(data) {
                                    return (data['tab'] === 'hover');
                                },
                                type: "transitions",
                            },
                            css: {
                                selector: "root .timeline-item_point",
                            }
                        }
                    ]
                });

                if(this.items && this.items.some((item)=> item.icon)) {
                    //Icon style
                    style.push({
                        group_name: 'icon',
                        group_title: 'Icon',
                        params: [
                            {
                                name: "size",
                                label: "Size",
                                type: "number",
                                options: {
                                    responsive: true,
                                    units: {
                                        px: {
                                            min: 0,
                                            max: 100
                                        }
                                    }
                                },
                                css: {
                                    selector:' .timeline-item_icon-holder svg',
                                    properties: {
                                        width: ""
                                    }
                                }
                            },
                            {
                                name: "transform",
                                label: "Rotate",
                                type: "number",
                                options: {
                                    responsive: true,
                                    min: 0,
                                    max: 360
                                },
                                css: {
                                    selector:' .timeline-item_icon-holder svg',
                                    properties: {
                                        transform: "rotate(%value%deg)"
                                    }
                                }
                            },
                            {
                                name: "color",
                                label: "Color",
                                type: "color",
                                options: {
                                    oneline: true,
                                    global: {
                                        type: "colors"
                                    },
                                },
                                css: {
                                    selector:' .timeline-item_icon-holder svg',
                                    properties: {
                                        fill: ""
                                    }
                                }
                            }
                        ]
                    });
                }

                if(this.isHorizontal) {
                    //Prev and next button settings
                    if(this.prevBtn || this.nextBtn) {
                        style.push({
                            group_alias: 'icon:hover',
                            options: {
                                group_name: 'nav_icon',
                                group_title: 'Navigation icon',
                                selector: ' .ecom-timeline_prev, .ecom-timeline_next'
                            },
                            modify:{
                                params:[
                                    {
                                        position:30,
                                        fields:{
                                            alias:'spacing'
                                        }
                                    }
                                ]
                            }
                        })
                    }
                }

            }
            return style;
        }
    }
};
</script>


<!-- Add resize tinh toan lai dynamic line -->