<template>
    <div
        class="ecom-element ecom-product-single ecom-product-single__visitor"
    >
        <div class="ecom-product-single__visitor-wrapper">
            <div class="ecom-product-single__visitor-container">
                <div
                    v-if="data.settings.icon"
                    class="ecom-product-single__visitor-icon"
                    v-html="data.settings.icon"
                    />
                <div
                    class="ecom-product-single__visitor-content"
                    :data-text="lang(text,'visitor_text')"
                />
            </div>
        </div>
    </div>
</template>

<script>
import { Javascript, Element } from '@/mixins';
export default {
    name:'ProductVisitor',
    presets:true,
    mixins: [ Javascript, Element],
    props: {
        data: {
            type: Object,
            default () {
                return {
                    
                };
            }
        }
    },
    data() {
        return {
            jsreactives: ['text','interval','min','max']
        };
    },
    computed: {

        settings() {
            return [{
                params: [
                {
                    type: 'paragraph',
                    content: 'Display a fake the number of people viewing your product page.'
                },
                {
                    type: 'number',
                    label: 'Minimum number of visitors',
                    name: 'min',
                    value: 100,
                    placeholder: 100,
                    options: {
                        min: 1,
                        max: 200
                    },
                    description: 'Minimum visitors number must be less than maximum visitors'
                },
                {
                    type: 'number',
                    label: 'Maximum number of visitors',
                    name: 'max',
                    value: 100,
                    placeholder: 100,
                    options: {
                        min: 1,
                        max: 200
                    },
                    decription: 'Maximum visitors number must be greater than minimum visitors'
                },

                {
                    type: 'number',
                    label: 'Change count number every(second)',
                    name: 'interval',
                    value: 5,
                    options: {
                        min: 1,
                        max: 120
                    },
                    description: 'Set the time to set new visitor counter'
                },
                {
                    type: 'textarea',
                    label: 'Text',
                    name:'text',
                    value: '',
                    description: 'The text shows the number of visitors online. Example: {people} are viewing this right now',
                    options: {
                        toolbar: 'full',
                    }
                },
                {
                    type: 'picker',
                    name:'icon',
                    label: 'Icon',
                    options: {
                        oneline: true,
                        type: 'icon',
                        layout: 'grid',
                        output: 'value',
                        multiple: false
                    }
                },
                {
                    type: 'choose',
                    label: 'Alignment',
                    name: 'textTextAlign',
                    options: {
                        oneline: true,
                        responsive: true,
                        type: 'text-align',
                        values: ['flex-start', 'center', 'flex-end']
                    },
                    css: {
                        selector: ' .ecom-product-single__visitor-container',
                        properties: {
                            'justify-content': ''
                        },
                    }
                }
                ]
            }];
        },

        text(){
            return this.data && this.data.settings && this.data.settings.text ? this.data.settings.text : '';
        },
        interval(){
            return this.data && this.data.settings && this.data.settings.interval ? this.data.settings.interval : '';
        },
        javascript(){
            return function(){
                let $content = this.$el.querySelector('.ecom-product-single__visitor-content');
                console.log($content );
                function getRandomIntMinMax(min,max) {
                    return Math.floor(Math.random() *  (max - min)) + min ;
                }
                if($content && $content.dataset.text)
                {
                    let $max = parseInt(this.settings.max),
                        $min = parseInt(this.settings.min),
                        $interval = parseInt(this.settings.interval);
                    $max = isNaN($max) ? 120 : $max;
                    $min = isNaN($min) ? 2 : $min;
                    $min = $min > $max ? 0 : $min;
                    $interval = isNaN($interval) ? 5 : $interval;
                    $interval = $interval * 1000;
                    const fakeVisitor = function(){
                        let visitor = getRandomIntMinMax($min,$max);
                        let text = $content.dataset.text.replace(/\{.*\}/g,visitor);
                        $content.innerHTML = text;
                    };
                    fakeVisitor();
                    setInterval(fakeVisitor,$interval);

                }
            };
        },
        css(){
            return `
                .ecom-product-single__visitor-container {
                    display: flex;
                    align-items:center;
                    column-gap: 10px;

                }
                .ecom-product-single__visitor-icon{
                    display:inline-flex;
                }
                .ecom-product-single__visitor-icon svg{
                    width:28px;
                    height:auto
                }
            `;
        },
        default(){
            return {
                settings:{
                    "min": 76,
                    "max": 137,
                    "interval": 5,
                    "text": "{people} are viewing this right now",
                    "icon": "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 448 512\" fill=\"currentColor\"><path d=\"M272 304h-96C78.8 304 0 382.8 0 480c0 17.67 14.33 32 32 32h384c17.67 0 32-14.33 32-32C448 382.8 369.2 304 272 304zM48.99 464C56.89 400.9 110.8 352 176 352h96c65.16 0 119.1 48.95 127 112H48.99zM224 256c70.69 0 128-57.31 128-128c0-70.69-57.31-128-128-128S96 57.31 96 128C96 198.7 153.3 256 224 256zM224 48c44.11 0 80 35.89 80 80c0 44.11-35.89 80-80 80S144 172.1 144 128C144 83.89 179.9 48 224 48z\"></path></svg>"
                
                }
                
            }
        }
    },
    methods:{
         style(){
             let style = [
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_title: 'Text',
                        group_name:'text',
                    selector: ' .ecom-product-single__visitor-content'
                    }
                }
            ];
            if(this.data.settings.icon){
                style.unshift(
                     
                    {
                    group_alias: 'icon:hover',
                    options:{
                        group_name: 'iconGeneral',
                        group_title: 'Icon',
                        selector: ' .ecom-product-single__visitor-icon'
                    },
                    modify:{
                        params:[
                            {
                                position:0,
                                fields:{
                                    alias:'justify-content',
                                    options:{
                                        label:'Alignment',
                                        css:{
                                            selector:'root .ecom-icon'
                                        }
                                    }
                                }
                            },
                            {
                                position:30,
                                fields:{
                                    alias:'spacing'
                                }
                            }
                        ]
                    }
                }
                );
            }
            return style;
        }
    }
};
</script>
