<template>
    <div class="ecom-element ecom-base-image">
        <figure v-if="image">
            <div class="ecom-container-image ecom-image-align">
                <div
                    class="ecom-image-content-position ecom-image-default ecom-base-image-container-overlay"
                    :class="{
                        'ecom-replace-notice': $helpers.isPreviewImage(JSON.stringify(image))
                    }"
                >
                    <div v-if="data.settings.overlay_use === true && !link.href" class="ecom-base-image ecom-overlay">
                        <span class="ecom-overlay-text" v-if="data.settings.overlay_title === 'alt'" v-html="image.alt" />
                        <span
                            class="ecom-overlay-text"
                            v-else-if="data.settings.overlay_title === 'custom'"
                            v-html="data.settings.overlay_title_custom"
                        />
                    </div>
                    <picture
                        v-if="!link.href"
                        class="ecom-image-picture"
                        v-html="$helpers.renImageResponsive(image, screens, lazyload, focalPoint)"
                    />
                    <a
                        v-else
                        v-bind="link"
                        class="ecom-image-picture-link ecom-image-picture ecom-base-image-container-overlay ecom-flex"
                    >
                        <div v-if="data.settings.overlay_use === true" class="ecom-base-image ecom-overlay">
                            <span class="ecom-overlay-text" v-if="data.settings.overlay_title === 'alt'" v-html="image.alt" />
                            <span
                                class="ecom-overlay-text"
                                v-else-if="data.settings.overlay_title === 'custom'"
                                v-html="data.settings.overlay_title_custom"
                            />
                        </div>
                        <picture
                            v-html="$helpers.renImageResponsive(image, screens, lazyload, focalPoint)"
                        />
                    </a>
                </div>
                <figcaption
                    v-if="isCaption()"
                    class="ecom-image__caption"
                    v-html="data.settings.caption"
                />
            </div>
        </figure>
    </div>
</template>

<script lang="ts">
import { Element, Javascript } from '@/mixins';
import image from '@/plugins/fields/core/alias/image';
export default {
    name: 'Image',
    presets:false,
    docs: 'https://help.ecomposer.io/docs/elements/basic-elements/image/',
    mixins: [Element, Javascript],
    vendors: ['modal_js', 'modal_css'],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        focalPoint(){
            const style = {};
            if(this.image?.left && this.image?.top) {
                style['object-position'] = `${this.image?.left}% ${this.image?.top}%`;
            }
            return style;
        },
        imageComp() {
            return this.data;
        },
        screens() {
            return this.$store.getters['builder/screens'];
        },
        lazyload() {
            return this.data.settings?.lazyload ? this.data.settings?.lazyload === false : true
        },
        image() {
            if(!this.data.settings){
                return
            }
            let { image = {}, size, size_custom } = this.data.settings;
            if (Object.keys(image).length === 0) {
                return null;
            }
            if (size === 'custom') {
                size = size_custom;
            }
            else if(size) {
                size = {width: size.split('x')[0], height: size.split('x')[1]};
            }
            return {
                alt: image.name,
                top: image.top,
                left: image.left,
                ...Object.keys(image)
                    .filter((k) => k.startsWith('value'))
                    .reduce((acc, cur) => {
                        acc[cur] = this.$helpers.resizeImage(image[cur], size);
                        return acc;
                    }, {}),
            };
        },
        link() {
            let result = {};
            if (this.image?.value) {
                let { link, 'custom-link': customLink, lightbox } = this.data.settings;
                if (link === 'lightbox') {
                    if (lightbox === 'yes') {
                        result['ecom-modal'] = 'image';
                        result['ecom-modal-source'] = this.image.value;
                    }
                    result.href = this.image.value;
                } else if (link === 'custom' && customLink?.href) {
                    Object.assign(result, customLink);
                }
            }
            return result;
        },
        settings() {
            return [
                {
                    group_alias:'image:settings',
                    options:{
                        group_name:'',
                        group_title:''
                    }
                }
            ];
        },

        javascript() {
            return function () {
                if (
                    this.settings.link === 'lightbox' &&
                    this.settings.lightbox === 'yes'
                    &&  window.EComModal
                    && this.$el
                ) {
                    var popup_selector = this.$el.querySelector('[ecom-modal]');
                    new window.EComModal(popup_selector, {
                        cssClass: ['ecom-container-lightbox-' + this.id]
                    });
                }
            };
        },
        css() {
            return `
                    .ecom-base-image.ecom-overlay > .ecom-overlay-text{
                        text-align:center
                    }
                    .ecom-image-align{
                        display:flex;
                    }
                    .ecom-image-picture-link,
                    .ecom-image-picture{
                        overflow:hidden;
                        width:100%;
                        height:100%;
                    }
                    .ecom-base-image picture {
                        flex:1 1 auto;
                        min-height:1px;
                    }
                    .ecom-base-image picture img{
                        display: block;
                        max-width: 100%;
                        height: 100%;
                        width: 100%;
                    }
                    .ecom-base-image figure{
                        display: flex;
                    }
                    .ecom-container-image{
                        display: flex;
                        flex-direction: column;
                        width:100%;
                    }
                    .ecom-image-picture-link,
                    .ecom-image-content-position{
                        position: relative;
                    }
                    .ecom-base-image.ecom-overlay {
                        position: absolute;
                        z-index: 1;
                        opacity: 0.5;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        top: 0;
                        left: 0;
                        right: 0;
                        bottom: 0;
                    }
                    .ecom-image__caption{
                        width:100%;
                    }
                `;
        },
        default(){
            return {
                settings: {
                    image: {
                        value: "/images/placeholder.png"
                    },
                    link: "lightbox",
                    lightbox: "no",
                    use_caption: false,
                    caption: "Hello",
                    overlay_use: false
                },
                style: {
                    image: {
                        imageAlign: "center",
                        imageOpacitynormalmode: 1,
                        imageOpacityhovermode: 1
                    },
                    overlay: {
                        opacity: 0,
                        backgroundhovermode: {
                            classic: {
                                "background-color": "rgba(17, 24, 39, 0.3)"
                            }
                        },
                        opacityHover: 1
                    }
                }
            }
        }
    },
    watch: {
        image: {
            deep: true,
            handler: function () {
                this.refresh();
            }
        },
        link: {
            deep: true,
            handler: function () {
                this.refresh();
            }
        }
    },
    methods: {
        refresh() {
            this.imageComp.refresh = this.$helpers.randid();
        },
        isCaption() {
            return this.data.settings?.use_caption && this.data.settings.caption;
        },

        style() {
            let style = [];

            if (this.image) {
                style = [
                    {
                        group_alias:'image',
                        options:{
                            selector: ' .ecom-base-image .ecom-image-picture'
                        },
                        modify:{
                            params:[
                                {
                                    position:0,
                                    fields:[
                                        {
                                            type: 'choose',
                                            label: 'Alignment',
                                            name: 'imageAlign',
                                            options: {
                                                oneline: true,
                                                responsive: true,
                                                type: 'text-align',
                                                values: ['flex-start', 'center', 'flex-end'],
                                                visible: {
                                                    keep_data: false,
                                                    condition: data => data.imageWidth !== '100%'
                                                }
                                            },
                                            css: {
                                                selector:'root .ecom-image-align',
                                                properties: {
                                                    'align-items':''
                                                },
                                            }
                                        }
                                    ]
                                }
                            ]
                        }
                    },
                ];
            }
            if (this.isCaption()) {
                style.push({
                    group_alias:'text',
                    options:{
                        group_name:'caption',
                        group_title: 'Caption',
                        selector: ' .ecom-base-image .ecom-image__caption',
                    },
                    modify:{
                        params:{
                            position:20,
                            fields:{
                                alias:'spacing'
                            }
                        }
                    }
                });
            }
            let checkOverlayText = this.data.settings?.overlay_title && (this.data.settings.overlay_title == 'alt' || this.data.settings.overlay_title_custom);
            if (this.data.settings?.overlay_use) {
                style.push({
                    group_alias: !checkOverlayText ? 'background:overlay' : 'background:overlaytext',
                    options:{
                        group_name:'overlay',
                        group_title: 'Overlay',
                        selector: ' .ecom-base-image .ecom-base-image-container-overlay'
                    }
                });
            }

            return style;
        }
    }
};
</script>