        convertImageThumbnail(item) {
            if (!item) {
                return;
            }
            let { thumbnail = {} } = item;
            let { size, size_custom } = this.data.settings;
            if (Object.keys(thumbnail).length === 0) {
                return null;
            }
            if (size === 'custom') {
                size = size_custom;
            }
            else if(size) {
                size = {width: size.split('x')[0], height: size.split('x')[1]};
            }
            return {
                alt: thumbnail.name,
                src: this.$helpers.resizeImage(thumbnail.value, size)
            };
        },
