                                oneline: true,
                                type: "box-shadow",
                                visible: {
                                    condition: (data) => {
                                        return data["tab"] === "hover";
                                    }
                                }
                            },
                            css: {
                                selector: ":hover"
                            }
                        },
                        {
                            name: "borderRadiusHoverMode",
                            label: "Border Radius",
                            type: "dimension",
                            options: {
                                type: "radius",
                                responsive: true,
                                units: "default",
                                visible: {
                                    condition: (data) => {
                                        return data["tab"] === "hover";
                                    }
                                }
                            },
                            css: {
                                selector: ":hover",
                                properties: {
                                    "border-radius": ""
                                }
                            }
                        },
                        {
                            name: "transition",
                            type: "number",
                            label: 'Transition Duration <span class="lowercase">(ms)</span>',
                            options: {
                                min: 0,
                                max: 1500,
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data["tab"] === "hover";
                                    }
                                }
                            },
                            css: {
                                properties: {
                                    transition: "all %value%ms ease"
                                }
                            }
                        },
                        {
                            type: "line"
                        },
                        {
                            name: "spacing",
                            type: "dimension",
                            label: "Spacing",
                            options: {
                                responsive: true,
                                units: "default"
                            },
                            css: {
                                properties: {
