                                  },
                                    {
                                        position:3,
                                        fields:{
                                            alias:'justify-content',
                                            options:{
                                                label:'Alignment',
                                                css:{
                                                    selector:'root .ecom-image-align'
                                                }
                                            }
                                        }
                                    },
                                    {
                                        position: 20,
                                        fields: {
                                            alias: "spacing",
                                            modify: {
                                                css: {
                                                    selector: "root .ecom-image-align"
                                                }
                                            }
                                        }
                                    }
                              ]
                          }
                      }
                    : null,
                    this.check("box_icon")
                    ? {
                          group_alias: "icon",
                          options: {
                              group_name: "featured_icon",
                              group_title: "Featured icon",
                              selector: " .element__featured .element__featured--box-icon"
                          },
                          modify: {
                              params: [
                                  {
                                      position: 20,
                                      fields: {
                                            name: "icon_backgroundColor",
                                            label: "Background",
                                            type: "color",
                                            options: {
                                                global: {
                                                    type: "colors"
                                                },
                                                oneline: true,

                                            },
                                            css: {
                                                selector: 'root .element__featured--box-icon svg',
                                                properties: {
                                                    "background-color": ""
                                                }
                                            }
                                        },

                                  },
                                    {
                                        position: 20,
                                        fields:  {
                                            name: "icon_border_radius",
                                            label: "Border Radius",
                                            type: "dimension",
                                            options: {
                                                type: "radius",
                                                responsive: true,
                                                units: "default",

                                            },
                                            css: {
                                                selector: 'root .element__featured--box-icon svg',
                                                properties: {
                                                    "border-radius": ""
                                                }
                                            }
                                        },
                                    },
                                    {
                                        position:4,
                                        fields:{
                                            alias:'justify-content',
                                            options:{
                                                label:'Alignment',

                                            }
                                        }
                                    },
                                    {
                                        position: 20,
                                        fields: {
                                            alias: "spacing",
                                            options: {
                                                css:{
                                                    selector: 'root .element__featured--box-icon svg',
                                                }
                                            }

                                        }
                                    }
                              ]
                          }
                      }
                    : null,
                this.check("subTitle")
                    ? {
                          group_name: "subtitle",
                          group_title: "Subheading",
                          selector: " .element__featured",
                          params: [
                              {
                                  type: "popup",
                                  label: "Typography",
                                  name: "textTypography",
                                  options: {
                                      global: {
                                          type: "typography"
                                      },
                                      oneline: true,
                                      responsive: true,
                                      type: "typography"
                                  },
                                  css: {
                                      selector: " .element__featured--box-subtitle"
                                  }
                              },
                              {
                                  name: "tab",
                                  type: "tab",
                                  value: "normal",
                                  options: {
                                      tabs: [
                                          {
                                              name: "normal",
                                              title: "Normal"
                                          },
                                          {
                                              name: "hover",
                                              title: "Hover"
                                          }
                                      ]
                                  },
                                  css: {
                                      isCss: false
                                  }
                              },
                              ...addSwitch(textParamsDefaulttSubtitle, "normal"),
                              ...addSwitch(textParamsDefaulttSubtitle, "hover", {
                                  selector: "root .element__featured--box:hover",
                                  isBefore: true
                              }),
                              {
                                  name: "textTransition",
                                  type: "number",
                                  label: 'Transition Duration <span class="lowercase">(ms)</span>',
                                  options: {
                                      min: 0,
                                      max: 1500,
                                      visible: {
                                          keep_data: true,
                                          condition: (data) => {
                                              return data["tab"] === "hover";
                                          }
                                      }
                                  },
                                  css: {
                                      selector: " .element__featured--box-subtitle",
                                      properties: {
                                          transition: "all %value%ms ease"
                                      }
                                  }
                              },
                              {
                                  type: "line"
                              },
                              {
                                  alias: "spacing",
                                  options: {
                                      css: {
                                          selector: " .element__featured--box-subtitle"
                                      }
                                  }
                              }
                          ]
                      }
                    : null,
                this.check("title")
                    ? {
                          group_name: "title",
                          group_title: "Heading",
                          selector: " .element__featured",
                          params: [
                              {
                                  type: "popup",
                                  label: "Typography",
                                  name: "textTypography",
                                  options: {
                                      global: {
                                          type: "typography"
                                      },
                                      oneline: true,
                                      responsive: true,
                                      type: "typography"
                                  },
                                  css: {
                                      selector: " .element__featured--box-title"
                                  }
                              },
                              {
                                  name: "tab",
                                  type: "tab",
                                  value: "normal",
                                  options: {
                                      tabs: [
                                          {
                                              name: "normal",
                                              title: "Normal"
                                          },
                                          {
                                              name: "hover",
                                              title: "Hover"
                                          }
                                      ]
                                  },
                                  css: {
                                      isCss: false
                                  }
                              },
                              ...addSwitch(textParamsDefaulttTitle, "normal"),
                              ...addSwitch(textParamsDefaulttTitle, "hover", {
                                  selector: "root .element__featured--box:hover",
                                  isBefore: true
                              }),
                              {
                                  name: "textTransition",
                                  type: "number",
                                  label: 'Transition Duration <span class="lowercase">(ms)</span>',
                                  options: {
                                      min: 0,
                                      max: 1500,
                                      visible: {
                                          keep_data: true,
                                          condition: (data) => {
                                              return data["tab"] === "hover";
                                          }
                                      }
                                  },
                                  css: {
                                      selector: " .element__featured--box-title",
                                      properties: {
                                          transition: "all %value%ms ease"
                                      }
                                  }
                              },
                              {
                                  type: "line"
                              },
                              {
                                  alias: "spacing",
                                  options: {
                                      css: {
                                          selector: " .element__featured--box-title"
                                      }
                                  }
                              }
                          ]
                      }
                    : null,
                this.check("text")
                    ? {
                          group_name: "content",
                          group_title: "Content",
                          selector: " .element__featured",
                          params: [
                              {
                                type: "popup",
                                label: "Typography",
                                name: "textTypography",
                                options: {
                                    global: {
                                        type: "typography"
                                    },
                                    oneline: true,
                                    responsive: true,
                                    type: "typography"
                                },
                                css: {
                                    selector: " .element__featured--box-text, a"
                                }
                              },
                              {
                                  name: "tab",
                                  type: "tab",
                                  value: "normal",
                                  options: {
                                      tabs: [
                                          {
                                              name: "normal",
                                              title: "Normal"
                                          },
                                          {
                                              name: "hover",
                                              title: "Hover"
                                          }
                                      ]
                                  },
                                  css: {
                                      isCss: false
                                  }
                              },
                              ...addSwitch(textParamsDefaultContent, "normal"),
                              ...addSwitch(textParamsDefaultContent, "hover", {
                                  selector: "root .element__featured--box:hover",
                                  isBefore: true
                              }),
                              {
                                  name: "textTransition",
                                  type: "number",
                                  label: 'Transition Duration <span class="lowercase">(ms)</span>',
                                  options: {
                                      min: 0,
                                      max: 1500,
                                      visible: {
                                          keep_data: true,
                                          condition: (data) => {
                                              return data["tab"] === "hover";
                                          }
                                      }
                                  },
                                  css: {
                                      selector: " .element__featured--box-text",
                                      properties: {
                                          transition: "all %value%ms ease"
                                      }
                                  }
                              },
                              {
                                  type: "line"
                              },
                              {
                                  alias: "spacing",
                                  options: {
                                      css: {
                                          selector: " .element__featured--box-text"
                                      }
                                  }
                              }
                          ]
                      }
                    : null,
                this.check("content")
                    ? {
                          group_alias: "button",
                          options: {
                              group_name: "button",
                              group_title: "Button",
                              selector: " .element__featured .element__featured--box-btn"
                          },
                          modify: {
                              params: [
                                  {
                                      position: 0,
                                      fields: [
                                          {
                                            alias: "justify-content",
                                            options: {
                                                label: "Alignment",
                                                css: {
                                                    selector: "root .ecom-button-default"
                                                }
                                            }
                                          }
                                      ]
                                  }
                              ]
                          }
                      }
                    : null,
                this.check("icon")
                    ? {
                          group_alias: "icon:hover",
                          options: {
                              group_name: "icon_button",
                              selector: " .ecom-feature-box-icon-link"
                          },
                          modify: {
                            params: [
                                {
                                    position: 20,
                                    fields: {
                                        alias: 'spacing'
                                    }
                                }
                            ]
                          }
                      }
                    : null
            ].filter((x) => x);
            return style;
        },
        attrLink(item) {
            return item.link;
        }
    }
};
</script>
