                if (!dom) return;
                dom.addEventListener('itemChange', function () {
                    setTimeout(function () {
                        dom.masonry.layout();
                    }, 500);
                });
                var option = dom.getAttribute("data-ecom-packery-params") || '{}';
                let params = Object.assign({
                    itemSelector: ".ecom-masonry-layout__item",
                    layoutMode: 'masonry',
                    percentPosition: true,
                    resize: true
                }, JSON.parse(option));
                var masonry = new window.EComPackery(dom, params);
                dom.masonry = masonry;
                dom.querySelectorAll('img').forEach(function (img) {
                    img.addEventListener('load', function () {
                        masonry.layout();
                    });
                });



            }
        },
