            overflow: hidden;
            display:block;
        }
        .ecom-masonry-layout__button {
          text-decoration: none;
          pointer-events: auto;
        }
        .ecom-masonry__banner_hzoom {
          position: relative;
          overflow: hidden;
        }
        .ecom-masonry__nt_promotion_html {
          position: absolute;
          max-width: 100%;
          z-index: 10;
          pointer-events: none;
        }
        .ecom-masonry__banner_overlay {
          pointer-events: none;
          position: absolute;
          background-color: #000;
          z-index: 1;
          opacity: 0.3;
          display: flex;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
        }
        .ecom-masonry-layout__picture-link,
        .ecom-masonry-layout__picture,
        .ecom-masonry-layout__image-picture {
          width: 100%;
          object-fit: contain;
        }
        .ecom-content-hidden.ecom-masonry__nt_promotion .ecom-masonry__nt_promotion_html {
          opacity: 0;
          transition: all .3s ease;
        }
        .ecom-content-hidden.ecom-masonry__nt_promotion:hover .ecom-masonry__nt_promotion_html {
          opacity: 1;
        }
        .ecom-masonry-layout .ecom-image-default:hover {
            z-index: 10;
        }
        `;
        }
    },
    mounted() {
        if (!this.data?.settings) {
            this.data.settings = {
                button: {
                    titleAlignment: "center",
                    spacing: {
                        padding: {
                            top: "5px",
                            bottom: "5px",
                            left: "12px",
                            right: "12px"
                        }
                    }
                },
            }
        }
    },
    methods: {
        banner_width(item) {
            return [
                item.banner_width
                    ? item.banner_width == 2
                        ? 'ecom-col-lg-15'
                        : item.banner_width == 1
                            ? 'ecom-col-lg-2'
                            : 'ecom-col-lg-' + item.banner_width
                    : 'ecom-col-12',
                item.banner_width__tablet
                    ? item.banner_width__tablet == 2
                        ? 'ecom-col-md-15'
                        : item.banner_width__tablet == 1
                            ? 'ecom-col-md-2'
                            : 'ecom-col-md-' + item.banner_width__tablet
                    : '',
                item.banner_width__mobile
                    ? item.banner_width__mobile == 2
                        ? 'ecom-col-15'
                        : item.banner_width__mobile == 1
                            ? 'ecom-col-2'
                            : 'ecom-col-' + item.banner_width__mobile
                    : 'ecom-col-12'
            ];
        },
        attrLink(item) {
            return item.button?.link;
        },
        banner_style(item) {
            let style = {};
            if (item.horizontal) {
                let horizontal_val = '';
                switch (item.horizontal) {
                    case 'left':
                        horizontal_val = '0 auto 0 0';
                        break;
                    case 'center':
                        horizontal_val = '0 auto';
                        break;
                    case 'right':
                        horizontal_val = '0 0 0 auto';
                        break;
                }
                style.margin = horizontal_val
            }
            if (item.content_alignment) {
                style.textAlign = item.content_alignment
            }

            return style;
        },
        vertical_style(item) {
            let style = {};
            if (item.vertical) {
                style.alignItems = item.vertical;
            }
            return style;
        },
