<template>
    <div class="ecom-element ecom-shopify-elements ecom-shopify__article--excerpt">
        <div class="ecom-shopify__article__excerpt-wrapper">
            <div class="ecom-shopify__article__excerpt-container" :excerpt_type="excerptType">
                <template v-if="excerptType === 'text'">
                    <div class="ecom-shopify__article__excerpt--paragraph">
                        <span v-html="liquid(excerpt_short)" />
                        <span v-if="type === 'short' && viewMore">
                            <button
                                type="button"
                                class="ecom-shopify__article__excerpt-view-more-btn"
                                v-text="lang(viewMoreText, 'viewMoreText')"
                            />
                        </span>
                    </div>
                    <span
                        v-if="type === 'short' && viewMore"
                        style="display: none"
                        class="ecom-shopify__article__excerpt--full"
                        v-html="liquid('excerpt_text')"
                    />
                </template>
                <template v-else>
                    <div
                        class="ecom-shopify__article__excerpt--paragraph"
                        :style="{ 'max-height': maxHeight }"
                        v-html="liquid(excerpt_short)"
                    />
                    <span v-if="type === 'short' && viewMore">
                        <button
                            type="button"
                            class="ecom-shopify__article__excerpt-view-more-btn"
                            v-text="lang(viewMoreText, 'viewMoreText')"
                        />
                    </span>
                </template>
            </div>
        </div>
    </div>
</template>

<script>
import { Liquid, Element, Javascript } from '@/mixins';

export default {
    name: 'ArticleExcerpt',

    mixins: [Liquid, Element, Javascript],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        excerptType() {
            return this.data?.settings?.excerpt_type ?? 'text';
        },

        css() {
            return `
                .ecom-shopify__article__excerpt--paragraph {
                    position: relative;
                    overflow: hidden
                }
                .ecom-shopify__article__excerpt--paragraph[style*="max-height"]:after {
                    content: '';
                    position: absolute;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    height: 150px;
                    background: linear-gradient(rgba(255, 255, 255, 0), rgb(255, 255, 255))
                }
                .ecom-shopify__article__excerpt-view-more-btn{
                    border:none;
                    background:transparent;
                    cursor:pointer;
                }
            `;
        },

        excerpt_short() {
            return this.excerptType === 'text'
                ? this.type === 'short'
                    ? 'short_excerpt'
                    : 'excerpt_text'
                : 'excerpt';
        },

        javascript() {
            return function () {
                let $dom = this.$el.querySelector(
                    '.ecom-shopify__article__excerpt-view-more-btn'
                );
                let excerptType = this.settings['excerpt_type'];
                let $full = this.$el.querySelector('.ecom-shopify__article__excerpt--full');
                let $short = this.$el.querySelector(
                    '.ecom-shopify__article__excerpt--paragraph'
                );
                if ($dom) {
                    $dom.addEventListener('click', function () {
                        if (excerptType === 'text' && $full) {
                            $full.style.display = 'inherit';
                            $short.style.display = 'none';
                        } else {
                            $short.style.maxHeight = null;
                        }
                        this.style.display = 'none';
                    });
                }
            };
        },

        limit() {
            return this.data && this.data.settings && this.data.settings.limit && parseInt(this.data.settings.limit) > 0
                ? parseInt(this.data.settings.limit)
                : 50;
        },

        liquids() {
            return {
                excerpt: {
                    code: `
                        {%- if article.excerpt != empty -%}
                            {{article.excerpt}}
                        {%- endif -%}
                        {%- if EComBuilderMode and article.excerpt == empty -%}
                            <div>This Article don't have Excerpt</div>
                        {%- endif -%}
                    `,
                    preview: `
                        <div class="ecom-skeleton-item">
                            <div>
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-4"></div>
                                    <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                    <div class="ecom-skeleton-col-6"></div>
                                    <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                    <div class="ecom-skeleton-col-2"></div>
                                    <div class="ecom-skeleton-col-10 ecom-skeleton-empty"></div>
                                </div>
                            </div>
                        </div>
                    `
                },
                excerpt_text: {
                    code: `
                            {%- if article.excerpt != empty -%}
                                {{ article.excerpt | strip_html }}
                            {%- endif -%}
                            {%- if EComBuilderMode and article.excerpt == empty -%}
                                <div>This Article don't have Excerpt</div>
                            {%- endif -%}
                        `,
                    preview: `
                        <div class="ecom-skeleton-item">
                            <div>
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-4"></div>
                                    <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                    <div class="ecom-skeleton-col-6"></div>
                                    <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                    <div class="ecom-skeleton-col-2"></div>
                                    <div class="ecom-skeleton-col-10 ecom-skeleton-empty"></div>
                                </div>
                            </div>
                        </div>
                    `
                },
                short_excerpt: {
                    code: `
                        {%- if article.excerpt != empty -%}
                        {{ article.excerpt | strip_html | truncatewords: ${this.limit} }}
                        {%- endif -%}
                        {%- if EComBuilderMode and article.excerpt == empty -%}
                            <div>This Article don't have Excerpt</div>
                        {%- endif -%}
                    `,
                    preview: `
                        <div class="ecom-skeleton-item">
                            <div>
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-4"></div>
                                    <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                    <div class="ecom-skeleton-col-6"></div>
                                    <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                    <div class="ecom-skeleton-col-2"></div>
                                    <div class="ecom-skeleton-col-10 ecom-skeleton-empty"></div>
                                </div>
                            </div>
                        </div>

                    `
                }
            };
        },

        maxHeight() {
            return this.excerptType === 'html' && this.data.settings.type === 'short'
                ? this.data.settings['max-height']
                : null;
        },

        requestShopifyType() {
            return {
                shopify_type: 'article'
            };
        },

        settings() {
            return [
                {
                    params: [
                        {
                            type: 'popup',
                            label: 'Excerpt Type',
                            name: 'excerpt_type',
                            options: {
                                type: 'dropdown',
                                values: { text: 'Text', html: 'HTML' },
                                default: false
                            },
                            css:false
                        },
                        {
                            type: 'popup',
                            label: 'Display mode',
                            name: 'type',
                            options: {
                                type: 'dropdown',
                                values: {
                                    full: 'Full',
                                    short: 'Short'
                                },
                                default: false
                            },
                            css:false
                        },
                        {
                            type: 'number',
                            label: 'Maximum words to show',
                            name: 'limit',
                            value: '50',
                            placeholder: '50',
                            options: {
                                input:true,
                                slider:false,
                                min:0,
                                visible: function (data) {
                                    return (
                                        data &&
                                        data['type'] === 'short' &&
                                        data['excerpt_type'] === 'text'
                                    );
                                }
                            }
                        },
                        {
                            type: 'number',
                            label: 'Max height',
                            name: 'max-height',
                            placeholder: '200',
                            options: {
                                units: {
                                    px: {
                                        min: 0,
                                        max: 1000
                                    },
                                    'vh': {
                                        min: 0,
                                        max: 100
                                    },
                                    'vw': {
                                        min: 0,
                                        max: 100
                                    },
                                },
                                visible: function (data) {
                                    return (
                                        data &&
                                        data['type'] === 'short' &&
                                        data['excerpt_type'] == 'html'
                                    );
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            label: 'Show view more button?',
                            name: 'viewMore',
                            options: {
                                oneline: true,
                                values: {
                                    on: {
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                },
                                visible: function (data) {
                                    return data['type'] === 'short';
                                }
                            }
                        },
                        {
                            label: 'View more text button',
                            name: 'viewMoreText',
                            type: 'text',
                            options: {
                                visible: function (data) {
                                    return data['type'] === 'short' && data['viewMore'];
                                }
                            }
                        }
                    ]
                }
            ];
        },

        type() {
            return this.data.settings && this.data.settings.type ? this.data.settings.type : 'full';
        },

        viewMore() {
            return this.data && this.data.settings && 'viewMore' in this.data.settings
                ? this.data.settings.viewMore
                : false;
        },

        viewMoreText() {
            return this.data && this.data.settings && 'viewMoreText' in this.data.settings
                ? this.data.settings.viewMoreText
                : 'view more';
        },
        default() {
            return {
                settings: {
                    excerpt_type: "text",
                    type: "full",
                    limit: "50",
                    view_more: false,
                    view_more_text: "View more",
                    "max-height": "200px"
                },
                style: {
                    article_excerpt: {
                        textTextAlign: "left"
                    }
                }
            }
        },


    },
    methods:{
        style() {
            let style = [
                {
                    group_alias: 'text:spacing',
                    options:{
                        group_title: 'Excerpt',
                        group_name: 'article_excerpt',
                        selector: ' .ecom-shopify__article__excerpt-container',
                    }
                }
            ];

            if (this.data.settings.viewMore) {
                style.push({
                        group_alias: 'button',
                        options:{
                            group_title: 'Button',
                            group_name: 'article_button',
                            selector: ' .ecom-shopify__article__excerpt-view-more-btn',
                        }
                });
            }

            return style;
        }
    }
};
</script>