<template>
    <div class="ecom-element ecom-shopify-elements ecom-shopify__blog--count">
        <div class="ecom-shopify__blog--count-wrapper">
            <div class="ecom-shopify__blog--count-inner" v-html="liquid('blogArticlesCount')" />
        </div>
    </div>
</template>

<script>
import { Liquid, Element } from '@/mixins';

export default {
    name: 'BlogArticlesCount',
    mixins: [Liquid, Element],
    
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        liquids() {
            return {
                blogArticlesCount: {
                    code: `
                        {%- if  blog != blank -%}
                            {% if blog.articles_count == 1 %}
                            ${this.lang(this.data?.settings?.articleCountText ?? '','article_count_text',
                                    {
                                        article_count: 'blog.articles_count'
                                    }
                                )
                            }
                            {% else %}
                                ${this.lang(
                                this.data?.settings?.articlesCountText ??'','articles_count_text',
                                    {
                                        articles_count: 'blog.articles_count'
                                    }
                                )
                            }
                            {% endif %}
                        {%- else -%}
                            <div>Please select the blog in settings</div>
                        {%- endif -%}
                    `,
                    preview: `
                        <div class="ecom-skeleton-item">
                            <div class="ecom-skeleton-col-12">
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-9 ecom-skeleton-big"></div>
                                </div>
                            </div>
                        </div>
                    `
                }
            };
        },

        requestShopifyType() {
            return {
                shopify_type: 'blog'
            };
        },

        settings() {
            return [
                {
                    group_title: '',
                    params: [
                        {
                            type: 'text',
                            name: 'articleCountText',
                            label: 'Article text count',
                            placeholder: '{{article_count}}',
                            description:
                                'Use {{article_count}} to show article count',
                        },
                        {
                            type: 'text',
                            name: 'articlesCountText',
                            label: 'Articles text count',
                            placeholder: '{{articles_count}}',
                            description:
                                'Use {{articles_count}} to show articles count',
                        },
                    ]
                }
            ];
        },

        style() {
            return [
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_title: '',
                        selector: ' .ecom-shopify__blog--count-inner',
                        group_name: 'blog_artilces_count'
                    }
                }
            ];
        },

        default() {
            return {
                settings: {
                    articleCountText: "{{ article_count }} article",
                    articlesCountText: "{{ articles_count }} articles"
                },
                style: {
                    blog_artilces_count: {
                        textTextAlign: "left"
                    }
                }
            }
        }
    }
};
</script>
