<template>
    <div class="ecom-megamenu-wrapper">
        <nav
            class="ecom-megamenu-desktop"
            v-if="data.settings?.items?.length > 0"
        >
            <ul
                class="ecom-megamenu ecom-in_flex ecom-wrap ecom-al_center"
                :class="data?.settings?.menu_type"
            >
                <li
                    v-for="(item, index) in data.settings?.items"
                    :key="index"
                    class="ecom-megamenu__item ecom-megamenu__parent ecom-pr"
                    :class="['ecom-megamenu__item-'+index, item?.section?.value || item?.shopify_menu?.value ? 'ecom-has-submenu' : '']"
                >
                    <a
                    v-bind="item?.link"
                    class="ecom-megamenu__link ecom-flex ecom-al_center ecom-pr"
                    >
                        <span
                        v-if="item.icon?.value"
                        class="ecom-megamenu__item--icon ecom-items--icon ecom-flex"
                        :style="{
                            order: item.iconPosition
                        }"
                        v-html="item.icon?.value"
                        />
                        <span class="ecom-megamenu__link--title" v-html="item?.title" />
                        <span
                            class="ecom-megamenu__link--label"
                            :style="{backgroundColor: item?.label_background }"
                            v-html="item?.label"
                            v-if="item.label && item?.title"
                        />
                    </a>
                    <div
                        class="ecom-submenu"
                        :class="[item?.megamenu_type]"
                        :style="megamenu_width(item)"
                        v-if="item?.section?.value || item?.shopify_menu?.value"
                        v-html="liquid('item-' + index)"
                    />
                </li>
        </ul>
    </nav>
    <div
        class="ecom-mobile-menu__wrapper"
        v-if="data?.settings?.items_mobile?.length"
    >
        <div class="ecom-moblie-menu">
            <div
                v-for="(item, index) in data.settings?.items_mobile"
                :key="index"
                class="ecom-moblie-menu__item"
                :class="['ecom-megamenu__item-mobile-'+index]"
            >
                <a
                v-if="item?.menu_type === 'link'"
                v-bind="item?.link"
                class="ecom-moblie-menu-link"
                >
                    <span
                    v-if="item.icon?.value"
                    class="ecom-megamenu__item--icon ecom-items--icon ecom-flex"
                    :style="{
                        order: item.iconPosition
                    }"
                    v-html="item.icon?.value"
                    />
                    <span class="ecom-megamenu__link--title">
                        {{ item.title }}
                        <span
                            class="ecom-megamenu__link--label"
                            :style="{backgroundColor: item?.label_background }"
                            v-html="item.label"
                            v-if="item.label"
                        />
                    </span>
                </a>
                <div
                    v-else
                    v-html="liquid('mobile_menu')"
                />
            </div>
        </div>
    </div>
    <div id="MENUSETTING"></div>
</div>
</template>
<script>

import { Liquid, Javascript, Element } from "@/mixins";

export default {
    name: "Megamenu",
    presets:true,
    mixins: [Liquid, Javascript, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    methods: {
        megamenu_width(item) {
            return item?.megamenu_type == 'fullwidth' ? { width: '100vw' } : { width: item?.custom_width };
        }
    },
    watch: {
        'data.settings.items': {
            deep: true,
            handler: function () {
                this.data.refresh = this.$helpers.randid()
            }
        },
    },
    computed: {
        javascript() {
            return function () {
                if(!this.$el)
                {
                    return false;
                }

                if(!this.isLive) {
                    const initMegaMenu = function(li) {
                        if( window.innerWidth <= 767 ) return;

                        var html = document.querySelector('html'),
                            _rtl = html.getAttribute('dir') == 'rtl' ? true : false,
                            screenWidth = window.innerWidth,
                            viewportWidth =  screenWidth;

                        var nav_dropdown = li.querySelector('.ecom-submenu');
                        if (!nav_dropdown) return;

                        var rect = nav_dropdown.getBoundingClientRect();

                        var offset = {
                            top: rect.top + window.scrollY,
                            left: rect.left + window.scrollX,
                        };

                        var nav_dropdownWidth = nav_dropdown.offsetWidth,
                            nav_dropdownOffset = offset,
                            extraSpace = (nav_dropdown.classList.contains('fullwidth')) ? 0 : 10;

                        if( ! nav_dropdownWidth || ! nav_dropdownOffset ) return;
                        var dropdownOffsetRight = screenWidth - nav_dropdownOffset.left - nav_dropdownWidth;

                        if( nav_dropdownWidth >= 0 && nav_dropdown.classList.contains( 'ecom-position-center' ) ) {
                            var toLeft = (nav_dropdownOffset.left + (nav_dropdownWidth/2)) - screenWidth/2;
                            if (_rtl) {
                                nav_dropdown.style.right = toLeft + 'px';
                            } else {
                                nav_dropdown.style.left = - toLeft + 'px';
                            }
                        } else if ( _rtl && (dropdownOffsetRight + nav_dropdownWidth >= viewportWidth || nav_dropdown.classList.contains('fullwidth') ) ) {
                            // If right point is not in the viewport
                            var toLeft = dropdownOffsetRight + nav_dropdownWidth - viewportWidth;
                            nav_dropdown.style.right = - toLeft - extraSpace + 'px';

                        } else if ( (nav_dropdownOffset.left + nav_dropdownWidth >= viewportWidth || nav_dropdown.classList.contains('fullwidth') ) ) {
                            var toRight = nav_dropdownOffset.left + nav_dropdownWidth - viewportWidth;
                            nav_dropdown.style.left = - toRight - extraSpace + 'px';
                        }
                    };

                    document.querySelectorAll('.ecom-megamenu__item.ecom-has-submenu').length
                    && document.querySelectorAll('.ecom-megamenu__item.ecom-has-submenu').forEach(function(el) {
                        initMegaMenu(el)
                    });
                }

            };
        },
        default() {
            return {
                "settings": {
                    "items": [
                        {
                            "title": "Menu #1",
                            "custom_width": "700px",
                            "sub_type": "section",
                            "megamenu_type": "custom",
                            "link": {
                                "href": "#"
                            },
                            "label": "Hot",
                            "label_background": "#e81313"
                        },
                        {
                            "title": "Menu #2",
                            "custom_width": "700px",
                            "sub_type": "section",
                            "megamenu_type": "custom",
                            "link": {
                                "href": "#"
                            },
                            "label_background": "#e81313"
                        },
                        {
                            "title": "Menu #3",
                            "custom_width": "700px",
                            "sub_type": "section",
                            "megamenu_type": "custom",
                            "link": {
                                "href": "#"
                            },
                            "label_background": "#137de8",
                            "label": "Sale"
                        }
                    ],
                    "menu_type": "horizontal"
                },
                "style": {
                    "title": {
                        "titleAlignment": "center",
                        "textDecoration": "none",
                        "spacing": {
                            "padding": {
                                "top": "5px",
                                "bottom": "5px",
                                "left": "12px",
                                "right": "12px"
                            }
                        }
                    },
                    "submenu": {
                        "textDecoration": "none",
                        "spacing": {
                            "padding": {
                                "top": "5px",
                                "bottom": "5px",
                                "left": "12px",
                                "right": "12px"
                            }
                        }
                    },
                    "mobile_menu": {
                        "textDecoration": "none"
                    },
                    "main_menu_item": {
                        "tab": "normal",
                        "buttonTypography": {
                            "text-decoration": "none"
                        },
                        "spacing": {
                            "padding": {
                                "left": "12.5px",
                                "right": "12.5px"
                            }
                        }
                    }
                }
            }

        },
        settings() {
            return [
                {
                    group_title: 'Menu desktop',
                    params: [
                        {
                            name: 'menu_type',
                            type: 'dropdown',
                            label: 'Menu type',
                            options: {
                                oneline: false,
                                default: false,
                                values: {
                                    horizontal: 'Horizontal',
                                    vertical: 'Vertical'
                                }
                            },
                        },
                        {
                            name: "items",
                            type: "group",
                            label:'Menu items',
                            value: [],
                            options: {
                                add_text: "Add item",
                                is_clear_all: false
                            },
                            params: [
                                {
                                    type: "text",
                                    label: "Title",
                                    name: "title",
                                },
                                {
                                    name: 'link',
                                    type: 'link',
                                    label: 'Menu link',
                                    options: {

                                    }
                                },
                                {
                                    type: "picker",
                                    label: "Icon",
                                    name: "icon",
                                    options: {
                                        type: "icon",
                                        multiple: false
                                    },
                                    css: {
                                        isCss: false
                                    }
                                },
                                {
                                    name: "iconPosition",
                                    label: "Icon Position",
                                    type: "choose",
                                    value: "0",
                                    options: {
                                        oneline: true,
                                        type: "align-x",
                                        values: ["0", "1"],
                                        visible: (data) => data["icon"]
                                    }
                                },
                                {
                                    type: "text",
                                    label: "Label",
                                    name: "label",
                                },
                                {
                                  type: 'color',
                                  label: 'Label background',
                                  name: 'label_background',
                                  oneline: true,
                                  options: {
                                      global: {
                                          type: 'colors'
                                      }
                                  }
                                },
                                {
                                    type: "line"
                                },
                                {
                                    name: 'sub_type',
                                    type: 'dropdown',
                                    label: 'Megamenu type',
                                    options: {
                                        oneline: false,
                                        default: false,
                                        values: {
                                            section: 'Section',
                                            menu: 'Shopify menu'
                                        }
                                    },
                                },
                                {
                                  type: 'picker',
                                  label: 'Shopify menu',
                                  name: 'shopify_menu',
                                  options: {
                                      type: 'menu',
                                      visible: function (data) {
                                            return data["sub_type"] == "menu";
                                        },
                                      multiple: false
                                  }

                                },
                                {
                                  type: 'picker',
                                  label: 'Section',
                                  name: 'section',
                                  options: {
                                      type: 'section',
                                      layout:'list',
                                      visible: function (data) {
                                            return data["sub_type"] == "section";
                                        },
                                      multiple: false
                                  }

                                },
                                {
                                    name: 'megamenu_type',
                                    type: 'dropdown',
                                    label: 'Submenu Width',
                                    options: {
                                        oneline: false,
                                        default: false,
                                        visible: function (data) {
                                            return data["section"] != "";
                                        },
                                        values: {
                                            custom: 'Custom width',
                                            fullwidth: 'Full width'
                                        }
                                    },
                                },
                                {
                                    type: 'number',
                                    label: 'Custom width',
                                    name: 'custom_width',
                                    options: {
                                        visible: function (data) {
                                            return data["megamenu_type"] == "custom" && data["section"] != "";
                                        },
                                        units: {
                                            'px': {
                                                min: 250,
                                                max: 1200
                                            }
                                        },
                                        slider: true,
                                        input: true,
                                    }
                                },
                                // {
                                //     name: 'submenu_alignment',
                                //     type: 'dropdown',
                                //     label: 'Submenu position',
                                //     options: {
                                //         oneline: false,
                                //         default: false,
                                //         visible: function (data) {
                                //             return data["megamenu_type"] == "custom" && data["section"] != "";
                                //         },
                                //         values: {
                                //             default: 'Default',
                                //             center: 'Center'
                                //         }
                                //     },
                                // },
                                {
                                type: "line"
                                },
                            ]
                        },
                    ]
                },
                {
                    group_title: 'Menu Moblie',
                    params: [
                        // {
                        //     type: 'picker',
                        //     label: 'Select menu',
                        //     name: 'menu_mobile',
                        //     options: {
                        //         type: 'menu',
                        //         multiple: false,
                        //         layout: 'list',
                        //     }

                        // },
                        {
                            name: "items_mobile",
                            type: "group",
                            label:'Menu items',
                            value: [],
                            options: {
                                add_text: "Add item",
                                is_clear_all: false
                            },
                            params: [
                                {
                                    name: 'menu_type',
                                    type: 'dropdown',
                                    label: 'Menu type',
                                    value: 'link',
                                    options: {
                                        oneline: false,
                                        default: false,
                                        values: {
                                            link: 'Link',
                                            menu: 'Shopify menu'
                                        }
                                    },
                                },
                                {
                                  type: 'picker',
                                  label: 'Shopify menu',
                                  name: 'shopify_menu',
                                  options: {
                                      type: 'menu',
                                      visible: function (data) {
                                            return data["menu_type"] == "menu";
                                        },
                                      multiple: false,
                                      layout: 'list',
                                  }

                                },
                                {
                                    type: "text",
                                    label: "Title",
                                    name: "title",
                                    options: {
                                      visible: function (data) {
                                            return data["menu_type"] == "link";
                                        }
                                    }
                                },
                                {
                                    name: 'link',
                                    type: 'link',
                                    label: 'Link Menu',
                                    options: {
                                        visible: function (data) {
                                            return data["menu_type"] == "link";
                                        }
                                    }
                                },
                                {
                                    type: "picker",
                                    label: "Icon",
                                    name: "icon",
                                    options: {
                                        type: "icon",
                                        multiple: false,
                                        visible: function (data) {
                                            return data["menu_type"] == "link";
                                        }
                                    },
                                    css: {
                                        isCss: false
                                    }
                                },
                                {
                                    name: "iconPosition",
                                    label: "Icon Position",
                                    type: "choose",
                                    value: "0",
                                    options: {
                                        oneline: true,
                                        type: "align-x",
                                        values: ["0", "1"],
                                        visible: (data) => data["icon"] && data["menu_type"] == "link",
                                    }
                                },
                                {
                                    type: "text",
                                    label: "Label",
                                    name: "label",
                                    options: {
                                        visible: function (data) {
                                            return data["menu_type"] == "link";
                                        }
                                    }
                                },
                                {
                                  type: 'color',
                                  label: 'Label background',
                                  name: 'label_background',
                                  oneline: true,
                                  options: {
                                      global: {
                                          type: 'colors'
                                      },
                                      visible: function (data) {
                                        return data["menu_type"] == "link";
                                    }
                                  }
                                }
                            ]
                        },
                    ]
                }
            ];
        },

        style() {
            return [
                {
                    group_alias: 'box',
                    options:{
                        group_title: 'Main menu',
                        group_name: 'main_menu',
                        selector: ' .ecom-megamenu-desktop .ecom-megamenu',
                    },
                    modify: {
                        params: [
                            {
                                position: 0,
                                fields: [
                                    {
                                        alias: 'width'
                                    },
                                    {
                                        type: 'choose',
                                        label: 'Content alignment',
                                        name: 'content_alignment',
                                        options: {
                                            type: 'align-x-full',
                                            values: [
                                                "flex-start",
                                                "center",
                                                "flex-end"
                                            ]
                                        },
                                        css:{
                                            selector:'root .ecom-megamenu-wrapper',
                                            properties:{
                                                'justify-content':''
                                            }
                                        }
                                    },
                                ]
                            },
                            {
                                position: 5,
                                fields: [
                                    {
                                        alias: 'spacing'
                                    }
                                ]
                            }
                        ]
                    }
                },
                {
                    group_alias: 'button',
                    options:{
                        group_title: 'Main menu item',
                        group_name: 'main_menu_item',
                        selector: ' .ecom-megamenu__link',
                    },
                    modify: {
                        params: [
                            {
                                position: 3,
                                fields: [
                                    {
                                        type: "choose",
                                        label: "Alignment",
                                        name: "titleAlignment",
                                        options: {
                                            oneline: true,
                                            responsive: true,
                                            type: "text-align",
                                            values: ["flex-start", "center", "flex-end"]
                                        },
                                        css: {
                                            properties: {
                                                "justify-content": ""
                                            }
                                        }
                                    }
                                ]
                            }
                        ]
                    }
                },
                {
                    group_alias: 'button',
                    options:{
                        group_title: 'Sub Shopify menu',
                        group_name: 'submenu',
                        selector: ' .ecom-submenu__item-link',
                    },
                    modify: {
                        remove: [
                            {
                                index: 5,
                                length: 1,
                            },
                            {
                                index: 8,
                                length: 1,
                            },
                            {
                                label: 'Width',
                                length: 1,
                            },
                            {
                                label: 'Height',
                                length: 1,
                            }
                        ],
                    }
                },
                {
                    group_alias: 'box',
                    options: {
                        group_name: 'mobile_menu',
                        group_title: 'Mobile menu',
                        selector: ' .ecom-mobile-menu__wrapper',
                    },
                },
                {
                    group_alias: 'button',
                    options:{
                        group_title: 'Mobile menu item',
                        group_name: 'mobile_menu_item',
                        selector: ' .ecom-moblie-menu-link',
                    },
                    modify: {
                        remove: [
                            {
                                index: 5,
                                length: 1,
                            },
                            {
                                index: 7,
                                length: 1,
                            },
                            {
                                label: 'Width',
                                length: 1,
                            },
                            {
                                label: 'Height',
                                length: 1,
                            }
                        ],
                    }
                },
                {
                    group_alias: 'icon',
                    options:{
                        group_title: 'Icon',
                        group_name: 'icon',
                        selector: ' .ecom-megamenu__item--icon',
                    },
                    modify: {
                        params: [
                            {
                                position: 3,
                                fields: [
                                    {
                                        name: "iconSpacing",
                                        label: "Spacing",
                                        type: "number",
                                        options: {
                                            responsive: false,
                                            units: {
                                                px: {
                                                    min: 0,
                                                    max: 50
                                                }
                                            }
                                        },
                                        css: {
                                            properties: {
                                                'margin': "0 %value%",
                                            }
                                        }
                                    }
                                ]
                            }
                        ]
                    }
                },
            ];
        },
        liquids() {
            const submenu = {};
            if (this.data?.settings?.items) {
                let items = this.data?.settings?.items;
                items.forEach( (item, index) => {
                    if(item?.sub_type == 'section') {
                        if (item?.section?.value) {
                            submenu['item-' + index ] = {
                                code: `{%- capture ecomMegamenu -%}
                                            {%- section 'ecom-${item.section.value}-section' -%}
                                        {%- endcapture -%}
                                        {%- unless ecomMegamenu contains 'Liquid error' -%}
                                            {{ ecomMegamenu }}
                                        {%- endunless -%}`,
                                preview: ``
                            }
                        }
                    } else if(item?.sub_type == 'menu') {
                        if(item?.shopify_menu?.value) {
                            let menu_handle = item.shopify_menu.value;
                            submenu['item-' + index ] = {
                                code: `	<ul class="ecom-menu ecom-dropdown-menu">
                                        {%- for link in linklists['${menu_handle}'].links -%}
                                            <li class="ecom-submenu__item ecom-submenu__item--level_1 ecom-pr">
                                            <a class="ecom-submenu__item-link" href="{{ link.url }}" alt="{{ link.title }}">{{ link.title }}</a>

                                            {% if link.links.size > 0 %}
                                                <ul class="ecom-menu ecom-dropdown-child">
                                                {% for child_link in link.links %}
                                                    <li class="ecom-submenu__item ecom-submenu__item--level_2 ecom-pr">
                                                    <a class="ecom-submenu__item-link" href="{{ child_link.url }}" alt="{{ child_link.title }}">{{ child_link.title }}</a>

                                                    {% if child_link.links.size > 0 %}
                                                        <ul class="ecom-menu ecom-dropdown-grandchild">
                                                        {% for grandchild_link in child_link.links %}
                                                            <li class="ecom-submenu__item ecom-submenu__item--level_3 ecom-pr">
                                                            <a class="ecom-submenu__item-link" href="{{ grandchild_link.url }}" alt="{{ grandchild_link.title }}">{{ grandchild_link.title }}</a>
                                                            </li>
                                                        {% endfor %}
                                                        </ul>
                                                    {% endif %}
                                                    </li>
                                                {% endfor %}
                                                </ul>
                                            {% endif %}
                                            </li>
                                        {%- endfor -%}
                                        </ul>`,
                                preview: ``
                            }
                        }
                    }

                })
            }
            if(this.data?.settings?.menu_mobile) {
                let mobile_menu_handle = this.data.settings.menu_mobile.value;
                submenu.mobile_menu = {
                    code: `	<ul class="ecom-moblie-menu-shopify">
                                {%- for link in linklists['${mobile_menu_handle}'].links -%}
                                    {%- if link.links.size > 0 -%}
                                        <li class="ecom-moblie-menu__item ecom-mobile-menu__has-children ecom-moblie-menu--level_1 ecom-pr">
                                            <a class="ecom-moblie-menu-link" href="{{ link.url }}" alt="{{ link.title }}">
                                                {{ link.title }}
                                                <span class="ecom-nav_link_icon ecom-ml__5"></span>
                                            </a>
                                            <ul class="ecom-mobile-sub-menu ecom-moblie-dropdown-child">
                                            {% for child_link in link.links %}
                                                {% if child_link.links.size > 0 %}
                                                <li class="ecom-moblie-menu ecom-moblie-menu--level_2 ecom-mobile-menu__has-children ecom-pr">
                                                    <a class="ecom-moblie-menu-link" href="{{ child_link.url }}" alt="{{ child_link.title }}">
                                                        {{ child_link.title }}
                                                        <span class="ecom-nav_link_icon ecom-ml__5"></span>
                                                    </a>
                                                    <ul class="ecom-mobile-sub-menu ecom-moblie-dropdown-grandchild">
                                                    {% for grandchild_link in child_link.links %}
                                                        <li class="ecom-moblie-menu ecom-moblie-menu--level_3 ecom-pr">
                                                        <a class="ecom-moblie-menu-link" href="{{ grandchild_link.url }}" alt="{{ grandchild_link.title }}">{{ grandchild_link.title }}</a>
                                                        </li>
                                                    {% endfor %}
                                                    </ul>
                                                </li>
                                                {%- else -%}
                                                    <li class="ecom-moblie-menu ecom-moblie-menu--level_2 ecom-pr">
                                                        <a class="ecom-moblie-menu-link" href="{{ child_link.url }}" alt="{{ child_link.title }}">{{ child_link.title }}</a>
                                                    </li>
                                                {%- endif -%}
                                            {% endfor %}
                                            </ul>
                                        </li>
                                    {%- else -%}
                                        <li class="ecom-moblie-menu__item ecom-moblie-menu--level_1 ecom-pr">
                                            <a class="ecom-moblie-menu-link" href="{{ link.url }}" alt="{{ link.title }}">{{ link.title }}</a>
                                        </li>
                                    {%- endif -%}
                            {%- endfor -%}
                            </ul>`,
                    preview: ``
                }
                submenu.is_editor = {
                    code:`{%-if request.path contains 'ecomposer-visual-page-builder'-%}is_editor{%- endif -%}`,
                    preview: ``
                }
            }
            return submenu;
        },
        css() {
            return `
                @media(max-width: 1024px){
                    .ecom-megamenu-desktop {
                        display:none;
                    }
                }
                @media(min-width: 1025px){
                    .ecom-mobile-menu__wrapper {
                        display:none;
                    }
                }
                .ecom-builder .ecom-megamenu-wrapper {
                    display: flex;
