<template>
    <div class="ecom-element ecom-shopify-elements ecom-shopify__blog--categories">
        <div class="ecom-shopify__blog--categories-wrapper">
            <div class="ecom-shopify__blog--categories-inner" v-html="liquid('blogCategories')" />
        </div>
    </div>
</template>

<script>
import { Liquid, Element } from '@/mixins';

export default {
    name: 'BlogCategories',
    docs: 'https://help.ecomposer.io/docs/elements/shopify-elements/blog-categories/',
    mixins: [Liquid, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        articlesCountText() {
            return this.data && this.data.settings && this.data.settings.articlesCountText
                ? this.data.settings.articlesCountText
                : '';
        },



        isShowArticlesCount() {
            return this.data && this.data.settings && this.data.settings.isShowArticlesCount
                ? this.data.settings.isShowArticlesCount
                : false;
        },

        liquids() {
            return {
                blogCategories: {
                    code: `
                        {%- capture blogs_selected -%}
                            ${this.data.settings && this.data.settings.blogs ? 
                                this.data.settings.blogs.map((blog) => blog.value).join(',')
                                : 
                                `{{ blog.handle }}, `}
                            
                        {%-  endcapture-%}                        

                        {%- if blogs_selected -%}
                            {%- assign blogs_handle = blogs_selected | split: ',' -%}
                        {%- endif -%}

                        {% if blogs_handle != empty %}
                            <ul class="ecom-shopify__blog--categories-items">
                                
                                {% for blog_handle in blogs_handle %}
                                    {%- assign blog_item = blogs[blog_handle] -%}
                                    {%- if blog_item == blank -%}
                                    {%- continue -%}

                                    {%- endif -%}
                                    <li class="ecom-shopify__blog--categories-item">
                                        <a href="/blogs/{{ blog_item.handle }}" class="ecom-shopify__blog--categories-item-link">

                                            ${this.data.settings.show_thumbnail ? `
                                                {%- if blog_item.articles_count > 0 -%}
                                                    {% assign first_article = blog_item.articles.first %}
                                                    {% if first_article.image %}
                                                        <div class="ecom-shopify__blog--categories-item-thumbnail">
                                                            {{ first_article.image.src | image_url: width: 500 | img_tag: blog_item.title, 'ecom-image-default'  }}
                                                        </div>
                                                    {% endif %}
                                                {%- endif -%}
                                                <div class="ecom-shopify__blog--categories-item-information">
                                                    <span class="ecom-shopify__blog--categories-item-title">{{ blog_item.title }}</span>
                                                    ${this.lang(this.isShowArticlesCount && this.articlesCountText? `<span class="ecom-shopify__blog--categories-item-count">${this.articlesCountText}</span>`: '','articlesCountText',
                                                        {
                                                            articles_count: 'blog_item.articles_count'
                                                        }
                                                    )}
                                                </div>
                                            `:

                                                `
                                                 ${this.data.settings.icon ? `
                                                            <span class="ecom-shopify__blog--categories--icon">${this.data.settings.icon}</span>
                                                ` : '' }
                                                    <div class="ecom-shopify__blog--categories-item-information">

                                                        <span class="ecom-shopify__blog--categories-item-title">{{ blog_item.title }}</span>
                                                        ${this.lang(this.isShowArticlesCount && this.articlesCountText? `<span class="ecom-shopify__blog--categories-item-count">${this.articlesCountText}</span>`: '','articlesCountText',
                                                            {
                                                                articles_count: 'blog_item.articles_count'
                                                            }
                                                        )}
                                                    </div>
                                                `

                                            }

                                        </a>
                                    </li>
                                {% endfor %}
                            </ul>
                        {% else %}
                            ${this.exporting? '<div>No blogs yet!</div>': '<div>Please select the blogs in settings</div>'}
                        {% endif %}
                    `,
                    preview: `
                        <div class="ecom-skeleton-item">
                            <div class="ecom-skeleton-col-12">
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-9 ecom-skeleton-big"></div>
                                </div>
                            </div>
                        </div>
                    `
                }
            };
        },

        settings() {
            return [
                {
                    params: [
                        {
                            type: "paragraph",
                            content: "To apply the current template to some or all blogs you can configure it in [Page settings](#settings/page_settings/general)"
                        },
                        {
                            type: "line"
                        },
                        {
                            type: 'picker',
                            name: 'blogs',
                            label: 'Pick blogs to preview',
                            description:
                                'Leave this field blank to show the current category of article(available on live page)',
                            options: {
                                type: 'blog',
                                layout:'list',
                                multiple: true
                            }
                        },
                        {
                            type: 'popup',
                            label: 'Layout',
                            name: 'blogCategoriesDirection',
                            options: {
                                type: 'dropdown',
                                preview: 'title',
                                values: {
                                    column: 'Vertical',
                                    row: 'Horizontal'
                                },
                                default: false
                            },
                            css: {
                                selector: ' .ecom-shopify__blog--categories-items',
                                properties: {
                                    display: 'flex',
                                    'flex-direction': ''
                                }
                            }
                        },
                        {
                            name: 'column-gap',
                            type: 'number',
                            label: 'Column gap',
                            value: '12px',
                            options: {
                                units: {
                                    px: {
                                        min: 1,
                                        max: 120,
                                        step: 1
                                    }
                                },
                                responsive: true,
                                visible: {
                                    keep_data: false,
                                    condition: data => data['blogCategoriesDirection'] === 'row'
                                }
                            },
                            css: {
                                selector: ' .ecom-shopify__blog--categories-items'
                            }
                        },
                        {
                            name: 'row-gap',
                            type: 'number',
                            label: 'Row gap',
                            value: '12px',
                            options: {
                                units: {
                                    px: {
                                        min: 1,
                                        max: 120,
                                        step: 1
                                    }
                                },
                                responsive: true
                            },
                            css: {
                                selector: ' .ecom-shopify__blog--categories-items'
                            }
                        },

                        {
                            type: 'toggle',
                            name: 'isShowArticlesCount',
                            label: 'Show Articles Count',
                            options: {
                                values: {
                                    on: {
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                },
                                oneline: true
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            type: 'text',
                            name: 'articlesCountText',
                            label: 'Articles text count',
                            placeholder: '{{articles_count}}',
                            description: 'Use {{articles_count}} to show articles count',
                            options: {
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['isShowArticlesCount'];
                                    }
                                }
                            }
                        },
                        {
                            type:'toggle',
                            label: 'Show thumbnail',
                            name: 'show_thumbnail',
                            options:{
                                values: {
                                    on: {
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                },
                                oneline: true
                            },
                            description: 'Show the first article\'s thumbnail of blog'
                        },
                         {
                            type: 'picker',
                            label: 'Icon',
                            name: 'icon',
                            options: {
                                type: 'icon',
                                layout: 'grid',
                                output: 'value',
                                multiple: false,
                                simple: false,
                                visible: function(data){
                                    return !data.show_thumbnail;
                                }
                            }
                        }

                    ]
                }
            ];
        },

        css() {
            return `
                .ecom-shopify__blog--categories-items{
                    flex-wrap:wrap
                }

                li.ecom-shopify__blog--categories-item {
                    list-style: none;
                    // width: 100%;
                }
                .ecom-shopify__blog--categories-item-link {
                    width: 100%;
                }
                .ecom-shopify__blog--categories-item-information {
                    display: flex;
                    flex: 1;
                }
                .ecom-shopify__blog--categories-items .ecom-shopify__blog--categories-item-link{
                    text-decoration:none;
                    display: flex;
                    align-items: center;
                    gap: 5px;
                }
                .ecom-shopify__blog--categories--icon{
                    display: flex;
                    align-items: center;
                    gap: 5px;
                }
                `;
        },



        default() {
            return {
                "settings": {
                    "row-gap": "12px",
                    "blogCategoriesDirection": "column",
                    "blogs": [

                    ],
                    "isShowArticlesCount": true,
                    "articlesCountText": "({{ articles_count }})",
                    "show_thumbnail": true
                },
                "style": {
                    "blog_categories_title": {
                        "textTextAlign": "left",
                        "spacing": {
                            "margin": {
                                "bottom": "16px"
                            }
                        }
                    },
                    "general": {
                        "blogCategoriesAlignment": "flex-start"
                    },
                    "icon": {
                        "iconFontSize": "23px",
                        "iconTransform": 16,
                        "iconPrimaryColor": "#f73b3b"
                    },
                    "title": {
                        "tab": "normal",
                        "textColornormalmode": {
                            "global-colors": "primary"
                        },
                        "textTypography": {
                            "font-weight": "500"
                        }
                    },
                    "count_text": {
                        "tab": "normal",
                        "textColornormalmode": {
                            "global-colors": "primary"
                        },
                        "textTypography": {
                            "font-weight": "500"
                        }
                    },
                    "image": {
                        "tab": "normal",
                        "imageBorderRadiusnormalmode": {
                            "right": "5px",
                            "top": "5px",
                            "left": "5px",
                            "bottom": "5px"
                        }
                    }
                }
            }
        }
    },
    methods: {
        style() {
            let style = [

                {
                    group_alias: 'box',
                    options: {
                        group_title: 'General',
                        group_name: 'general'
                    },
                    modify: {
                        params: {
                            type: 'choose',
                            label: 'Alignment',
                            name: 'blogCategoriesAlignment',
                            options: {
                                oneline: true,
                                responsive: true,
                                type: 'text-align',
                                values: ['flex-start', 'center', 'flex-end']
                            },
                            css: {
                                selector: 'root .ecom-shopify__blog--categories-items',
                                properties: {
                                    'align-items': '',
                                    'justify-content': ''
                                }
                            }
                        },
                    }
                },
                {
                    group_alias: 'text:hover',
                    options:{
                        group_title:'Title',
                        selector: ' .ecom-shopify__blog--categories-item-title',
                        group_name: 'title'
                    }
                }

            ];
            if(this.data.settings?.blogs?.length > 0) {
                style.push({
                    group_alias: 'box',
                    options:{
                        group_title:'Category item',
                        selector: ' .ecom-shopify__blog--categories-item',
                        group_name: 'category_item'
                    },
                    modify: {
                        params: [
                            {
                                position: 0,
                                fields: [
                                    {
                                        name: 'cat_item_width',
                                        label: 'Width',
                                        type: 'number',
                                        options: {
                                            responsive: true,
                                            units: {
                                                px: {
                                                    min: 0,
                                                    max: 500
                                                },
                                                '%': {
                                                    min: 0,
                                                    max: 100
                                                }
                                            }
                                        },
                                        css:{
                                            properties:{
                                                'width': ''
                                            }
                                        }
                                    },
                                ]
                            },
                            {
                                position: 6,
                                fields: [
                                    {
                                        alias: 'spacing'
                                    }
                                ]
                            }
                        ]
                    }
                })
            }

            if(this.isShowArticlesCount)
            {
                style.push({
                    group_alias: 'text:hover',
                    options:{
                        group_title:'Count text',
                        selector: ' .ecom-shopify__blog--categories-item-count',
                        group_name: 'count_text'
                    }
                });
            }
            if(this.data.settings?.show_thumbnail)
            {
                style.push({
                    group_alias: 'image',
                    options:{
                        option_title: 'Thumnmail',
                        option_name:'thumbnail',
                        selector: ' .ecom-shopify__blog--categories-item-thumbnail'
                    }
                });
            }

            if (this.data.settings?.icon) {
                style.push({
                    group_alias:'icon',
                    options:{
                        group_name: 'icon',
                        group_title: 'Icon',
                        selector: ' .ecom-shopify__blog--categories--icon',
                    },
                    modify:{
                        params:[
                            {
                                position:15,
                                fields:[
                                    {
                                        type: 'line'
                                    },
                                    {
                                        alias:'spacing'
                                    }
                                ]
                            }
                        ]
                    }
                });
            }
            return style;
        }
    }
};
</script>
