<template>
    <div class="ecom-element ecom-collection ecom-collection__description">
        <div class="ecom-collection__description-wrapper">
            <div class="ecom-collection__description-container" :content_type="contentType">
                <template v-if="contentType === 'text'">
                    <div class="ecom-collection__description--paragraph">
                        <span v-html="liquid(description_short)" />
                        <span
                            v-if="type === 'short' && view_more"
                            class="ecom-collection__description--view-more ecom-w__full ecom-flex"
                        >
                            <button
                                type="button"
                                class="ecom-collection__description-view-more-btn"
                                v-text="lang(data.settings.view_more_text, 'view_more_text')"
                            />
                        </span>
                    </div>
                    <span
                        v-if="type === 'short' && view_more"
                        style="display: none"
                        class="ecom-collection__description--full"
                        v-html="liquid('description_text')"
                    />
                </template>
                <template v-else>
                    <div
                        class="ecom-collection__description--paragraph"
                        :style="{ 'max-height': maxHeight }"
                        v-html="liquid(description_short)"
                    />
                    <span v-if="type === 'short' && view_more">
                        <button
                            type="button"
                            class="ecom-collection__description-view-more-btn"
                            v-text="lang(data.settings.view_more_text, 'view_more_text')"
                        />
                    </span>
                </template>
            </div>
        </div>
    </div>
</template>

<script>
import { Liquid, Element, Javascript } from '@/mixins';
export default {
    name: 'CollectionDescription',
    docs: 'https://help.ecomposer.io/docs/elements/collection-elements/description/',
    mixins: [Liquid, Element, Javascript],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        contentType() {
            return this.data?.settings?.content_type ?? 'text';
        },

        css() {
            return `
                .ecom-collection__description--paragraph, .ecom-collection__description-view-more-btn {
                    overflow: hidden
                }
                .ecom-collection__description--paragraph[style*="max-height"] {
                    position: relative;
                }
                .ecom-collection__description--paragraph[style*="max-height"]:after {
                    content: '';
                    position: absolute;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    height: 150px;
                    background: linear-gradient(rgba(255, 255, 255, 0), rgb(255, 255, 255))
                }
                .ecom-collection__description-view-more-btn{
                    cursor:pointer;
                }
            `;
        },

        description_type() {
            return this.type === 'short' ? 'short_description' : 'description';
        },

        description_short() {
            return this.contentType === 'text'
                ? this.type === 'short'
                    ? 'short_description'
                    : 'description_text'
                : 'description';
        },

        javascript() {
            return function () {
                /**
                 * this.settings.view_more
                 * this.settings.view_more_text
                 */
                let $dom = this.$el.querySelector('.ecom-collection__description-view-more-btn');
                let contentType = this.settings['content_type'];
                let $full = this.$el.querySelector('.ecom-collection__description--full');
                let $short = this.$el.querySelector('.ecom-collection__description--paragraph');
                if ($dom) {
                    $dom.addEventListener('click', function () {
                        if (contentType === 'text' && $full) {
                            $full.style.display = 'inherit';
                            $short.style.display = 'none';
                        } else {
                            $short.style.maxHeight = null;
                        }
                        this.style.display = 'none';
                    });
                }
            };
        },

        limit() {
            return this.data &&
                this.data.settings &&
                this.data.settings.limit &&
                parseInt(this.data.settings.limit) > 0
                ? parseInt(this.data.settings.limit)
                : 50;
        },

        liquids() {
            return {
                description: {
                    code: `
                        {%- if collection != empty -%}
                            {%- if collection.description != empty -%}
                                {{collection.description}}
                            {%- else -%}
                                <div>Collection without description</div>
                            {%- endif -%}
                        {%- else -%}
                            <div>Please select the collection in settings</div>
                        {%- endif -%}
                    `,
                    preview: `
                        <div class="ecom-skeleton-item">
                            <div class="ecom-skeleton-row">
                                <div class="ecom-skeleton-col-4"></div>
                                <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-6"></div>
                                <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-2"></div>
                                <div class="ecom-skeleton-col-10 ecom-skeleton-empty"></div>
                            </div>
                            <div class="ecom-skeleton-row">
                                <div class="ecom-skeleton-col-10 big"></div>
                                <div class="ecom-skeleton-col-2 ecom-skeleton-empty big"></div>
                                <div class="ecom-skeleton-col-4"></div>
                                <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-6"></div>
                                <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-12"></div>
                            </div>
                        </div>
                    `
                },
                description_text: {
                    code: '{{collection.description | strip_html }}',
                    preview: `
                        <div class="ecom-skeleton-item">
                            <div class="ecom-skeleton-row">
                                <div class="ecom-skeleton-col-4"></div>
                                <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-6"></div>
                                <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-2"></div>
                                <div class="ecom-skeleton-col-10 ecom-skeleton-empty"></div>
                            </div>
                            <div class="ecom-skeleton-row">
                                <div class="ecom-skeleton-col-10 big"></div>
                                <div class="ecom-skeleton-col-2 ecom-skeleton-empty big"></div>
                                <div class="ecom-skeleton-col-4"></div>
                                <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-6"></div>
                                <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-12"></div>
                            </div>
                        </div>
                    `
                },
                short_description: {
                    code: `
                        {%- if collection != empty -%}
                            {%- if collection.description != empty -%}
                                {{ collection.description | strip_html | truncatewords: ${this.limit} }}
                            {%- elsif EComposerBuilderMode -%}
                                <div>Collection without description</div>
                            {%- endif -%}
                        {%- elsif EComposerBuilderMode -%}
                            <div>Please select the collection in settings</div>
                        {%- endif -%}
                    `,
                    preview: `
                        <div class="ecom-skeleton-item">
                            <div>
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-6"></div>
                                    <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                    <div class="ecom-skeleton-col-8"></div>
                                    <div class="ecom-skeleton-col-4 ecom-skeleton-empty"></div>
                                    <div class="ecom-skeleton-col-4"></div>
                                    <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                </div>
                            </div>
                        </div>

                    `
                }
            };
        },

        maxHeight() {
            return this.contentType === 'html' && this.data?.settings?.type === 'short'
                ? this.data.settings['max-height']
                : null;
        },

        requestShopifyType() {
            return {
                shopify_type: 'collection'
            };
        },

        settings() {
            return [
                {
                    params: [
                        {
                            type: 'popup',
                            label: 'Content Type',
                            name: 'content_type',
                            options: {
                                type: 'dropdown',
                                values: { text: 'Text', html: 'HTML' },
                                default: false
                            }
                        },
                        {
                            type: 'popup',
                            label: 'Description type',
                            name: 'type',
                            value: 'short',
                            options: {
                                type: 'dropdown',
                                values: {
                                    short: 'Short',
                                    full: 'Full'
                                },
                                default: false
                            }
                        },
                        {
                            type: 'number',
                            label: 'Maximum words to show',
                            name: 'limit',
                            value: '50',
                            placeholder: '50',
                            options: {
                                control_width: '40%',
                                input: true,
                                slider: false,
                                min: 0,
                                // oneline: true,
                                visible: function (data) {
                                    return (
                                        data &&
                                        data['type'] === 'short' &&
                                        data['content_type'] === 'text'
                                    );
                                }
                            }
                        },
                        {
                            type: 'number',
                            label: 'Max height',
                            name: 'max-height',
                            placeholder: '200',
                            options: {
                                units: {
                                    px: {
                                        min: 0,
                                        max: 1000
                                    },
                                    'vh': {
                                        min: 0,
                                        max: 100
                                    },
                                    'vw': {
                                        min: 0,
                                        max: 100
                                    },
                                },
                                visible: function (data) {
                                    return (
                                        data &&
                                        data['type'] === 'short' &&
                                        data['content_type'] == 'html'
                                    );
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            label: 'Show view more',
                            name: 'view_more',
                            options: {
                                oneline: true,
                                values: {
                                    on: {
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                },
                                visible: function (data) {
                                    return data['type'] === 'short';
                                }
                            }
                        },
                        {
                            label: 'View more text button',
                            name: 'view_more_text',
                            type: 'text',
                            options: {
                                visible: function (data) {
                                    return data['type'] === 'short' && data['view_more'];
                                }
                            }
                        }
                    ]
                }
            ];
        },

        type() {
            return this.data && this.data.settings && this.data.settings.type
                ? this.data.settings.type
                : 'short';
        },

        view_more() {
            return this.data && this.data.settings && this.data.settings.view_more
                ? this.data.settings.view_more
                : false;
        },

        default() {
            return {
                settings: {
                    content_type: "text",
                    type: "short",
                    limit: "50",
                    view_more: false,
                    view_more_text: "View more",
                    "max-height": "200px"
                },
                style: {
                    collection_description: {
                        textTextAlign: "left"
                    }
                }
            }
        }
    },

    methods: {
        style() {
            let style = [
                {
                    group_alias: 'text',
                    options: {
                        group_title: 'Description',
                        group_name: 'collection_description',
                        selector: ' .ecom-collection__description--paragraph, .ecom-collection__description--full',
                    }
                }
            ];

            if (this.data.settings.view_more) {
                style.push({
                    group_alias: 'button',
                    options: {
                        group_title: 'View more',
                        group_name: 'collection_view_more',
                        selector: ' .ecom-collection__description-view-more-btn'
                    },
                    modify: {
                        params: [
                            {
                                position: 0,
                                fields: [
                                    {
                                        type: 'choose',
                                        label: 'Alignment',
                                        name: 'alignment',
                                        options: {
                                            oneline: true,
                                            responsive: true,
                                            type: 'text-align',
                                            values: ['flex-start', 'center', 'flex-end']
                                        },
                                        css: {
                                            properties: {
                                                'justify-content':''
                                            },
                                            selector: 'root .ecom-collection__description--view-more'
                                        }
                                    }
                                ]
                            }
                        ]
                    }
                });
            }

            return style;
        }
    }
};
</script>
