<template>
    <div class="ecom-element ecom-product-single ecom-product-single__delivery-time">
        <div class="ecom-product-single__delivery-time-wrapper">
            <div class="ecom-delivery-time-wrapper">
                <div class="ecom-delivery-time">
                    <span
                        v-if="data.settings?.type_icon == 'icon' && data.settings?.icon_shipping && data.settings?.position_icon !== 'after'"
                        v-html="renderIcon"
                        class="ecom-delivery-icon"
                        :class="animatedClass"
                    />
                    <span v-if="data.settings.type_icon == 'img' && data.settings?.thumbnail_shipping && data.settings?.position_icon !== 'after'"
                        class="ecom-delivery-icon"
                        :class="animatedClass">
                        <img :src="renderIcon" alt="">
                    </span>
                    <div ref="textFilter"
                        v-html="lang(textFilter, 'delivery_text')"
                        class="ecom-delivery-time-text"
                        :data-ds="data.settings?.delivery_start_date"
                        :data-de="data.settings?.delivery_end_date"
                        :data-time-replace="lang('hour')"
                        :data-date-dow="data.settings?.delivery_custom_dow"
                        :data-date-month="data.settings?.delivery_custom_month"
                        :data-mode="data.settings?.exclude_days_from"
                        :data-format="data.settings?.delivery_format_date"
                        :data-cut="data.settings?.exclude_days"
                        :data-time="data.settings?.delivery_cut_off"
                        :data-text-min="lang(data.settings?.delivery_text_minutes, 'delivery_minute')"
                        :data-text-hour="lang(data.settings?.delivery_text_hours, 'delivery_hours')"
                        :data-content="lang(data.settings?.delivery_text, 'delivery_text')"
                        />
                    <span
                        v-if="data.settings?.type_icon == 'icon' && data.settings?.icon_shipping && data.settings?.position_icon == 'after'"
                        v-html="renderIcon"
                        class="ecom-delivery-icon"
                        :class="animatedClass"
                    />
                    <span v-if="data.settings.type_icon == 'img' && data.settings?.thumbnail_shipping && data.settings?.position_icon == 'after'"
                        class="ecom-delivery-icon"
                        :class="animatedClass">
                        <img :src="renderIcon" alt="">
                    </span>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import {Element, Javascript } from '@/mixins';
export default {
    name: 'ProductDeliveryTime',
    presets:true,
    mixins: [Element, Javascript],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    methods: {
        replaceDateTime(text_data){
                if(text_data){
                    let date = new Date(this.getTime());
                let hour = null,
                    min = null;
                if(date==='NaN'){
                    hour = 'NaN';
                    min = 'NaN';
                }else{
                    hour = date.getHours();
                    min = date.getMinutes();
                }
                let text_hour = this.data?.settings?.delivery_text_hours || '';
                let text_min = this.data?.settings?.delivery_text_minutes || '';
                text_data = text_data.replace('[hour]',`${hour} ${text_hour} ${min} ${text_min}`)
                text_data = text_data.replace("[date_start]", `<span class="ecom-delivery-time__date">${this.getDate[0]}</span>`);
                text_data = text_data.replace("[date_end]", `<span class="ecom-delivery-time__date">${this.getDate[1]}</span>`);
            }
            return text_data;
        },
        checkDayOfTheWeek(arr,day){
            if(arr.includes(day)) return false;
            return true;
        },
        convertFullDate(date,date_format){
            if(!date) return;
            let day_of_week = date.split(',')[0];
            let month = date.split(' ')[2];
            switch(day_of_week){
                case 'Mon':
                    date = date.replace('Mon', 'Monday');
                    break;
                case 'Tue':
                    date = date.replace('Tue', 'Tuesday');
                    break;
                case 'Wed':
                    date = date.replace('Wed', 'Wednesday');
                    break;
                case 'Thu':
                    date = date.replace('Thu', 'Thursday');
                    break;
                case 'Fri':
                    date = date.replace('Fri', 'Friday');
                    break;
                case 'Sat':
                    date = date.replace('Sat', 'Saturday');
                    break;
                case 'Sun':
                    date = date.replace('Sun', 'Sunday');
                    break;
            }
            if(date_format === 'DoW_dd_mm_yyyy_style_3' || date_format === 'DoW_mm_dd_yyyy_style_3' || date_format === 'DoW_yyyy_mm_dd') {
                switch(month){
                    case 'Jan':
                        date = date.replace('Jan', '01');
                        break;
                    case 'Feb':
                        date = date.replace('Feb', '02');
                        break;
                    case 'Mar':
                        date = date.replace('Mar', '03');
                        break;
                    case 'Apr':
                        date = date.replace('Apr', '04');
                        break;
                    case 'May':
                        date = date.replace('May', '05');
                        break;
                    case 'Jun':
                        date = date.replace('Jun', '06');
                        break;
                    case 'Jul':
                        date = date.replace('Jul', '07');
                        break;
                    case 'Aug':
                        date = date.replace('Aug', '08');
                        break;
                    case 'Sep':
                        date = date.replace('Sep', '09');
                        break;
                    case 'Oct':
                        date = date.replace('Oct', '10');
                        break;
                    case 'Nov':
                        date = date.replace('Nov', '11');
                        break;
                    case 'Dec':
                        date = date.replace('Dec', '12');
                        break;
                }
                return date;
            }
            switch(month){
                case 'Jan':
                    date = date.replace('Jan', 'January');
                    break;
                case 'Feb':
                    date = date.replace('Feb', 'February');
                    break;
                case 'Mar':
                    date = date.replace('Mar', 'March');
                    break;
                case 'Apr':
                    date = date.replace('Apr', 'April');
                    break;
                case 'Jun':
                    date = date.replace('Jun', 'June');
                    break;
                case 'Jul':
                    date = date.replace('Jul', 'July');
                    break;
                case 'Aug':
                    date = date.replace('Aug', 'August');
                    break;
                case 'Sep':
                    date = date.replace('Sep', 'September');
                    break;
                case 'Oct':
                    date = date.replace('Oct', 'October');
                    break;
                case 'Nov':
                    date = date.replace('Nov', 'November');
                    break;
                case 'Dec':
                    date = date.replace('Dec', 'December');
                    break;
            }
            return date;
        },
        formatDate(date){
            if(!date) return;
            let date_format = this.data?.settings?.delivery_format_date;
            let day_of_week = date.split(',')[0];
            let day = parseInt(date.split(' ')[1]);
            let month = date.split(' ')[2];
            let year = date.split(' ')[3];
            switch(date_format){
                case "DoW_dd_mm_style_1":
                    switch(day){
                        case 1:
                            return `${day_of_week}, ${day}st ${month}`;
                        case 21:
                            return `${day_of_week}, ${day}st ${month}`;
                        case 31:
                            return `${day_of_week}, ${day}st ${month}`;
                        case 2:
                            return `${day_of_week}, ${day}nd ${month}`;
                        case 22:
                            return `${day_of_week}, ${day}nd ${month}`;
                        case 3:
                            return `${day_of_week}, ${day}rd ${month}`;
                        case 23:
                            return `${day_of_week}, ${day}rd ${month}`;
                        default:
                            return `${day_of_week}, ${day}th ${month}`;
                    }

                case "DoW_dd_mm_style_2":
                    return `${day_of_week}, ${day} ${month}`;
                    break;
                case "DoW_mm_dd_style_1":
                    return `${day_of_week}, ${month} ${day}`;
                    break;
                case "DoW_mm_dd_style_2":
                    switch(day){
                        case 1:
                            return `${day_of_week}, ${month} ${day}st`;
                        case 21:
                            return `${day_of_week}, ${month} ${day}st`;
                        case 31:
                            return `${day_of_week}, ${month} ${day}st`;
                        case 2:
                            return `${day_of_week}, ${month} ${day}nd`;
                        case 22:
                            return `${day_of_week}, ${month} ${day}nd`;
                        case 3:
                            return `${day_of_week}, ${month} ${day}rd`;
                        case 23:
                            return `${day_of_week}, ${month} ${day}rd`;
                        default:
                            return `${day_of_week}, ${month} ${day}th`;
                    }

                case "DoW_dd_mm_yyyy_style_1":
                    return `${day_of_week}, ${day}th ${month} ${year}`;
                    break;
                case "DoW_dd_mm_yyyy_style_2":

                    switch(day){
                        case 1:
                            return `${day_of_week}, ${day}st ${month}, ${year}`;
                        case 21:
                            return `${day_of_week}, ${day}st ${month}, ${year}`;
                        case 31:
                            return `${day_of_week}, ${day}st ${month}, ${year}`;
                        case 2:
                            return `${day_of_week}, ${day}nd ${month}, ${year}`;
                        case 22:
                            return `${day_of_week}, ${day}nd ${month}, ${year}`;
                        case 3:
                            return `${day_of_week}, ${day}rd ${month}, ${year}`;
                        case 23:
                            return `${day_of_week}, ${day}rd ${month}, ${year}`;
                        default:
                            return `${day_of_week}, ${day}th ${month}, ${year}`;
                    }
                case "DoW_mm_dd_yyyy_style_1":
                    switch(day){
                        case 1:
                            return `${day_of_week}, ${month} ${day}st, ${year}`;
                        case 21:
                            return `${day_of_week}, ${month} ${day}st, ${year}`;
                        case 31:
                            return `${day_of_week}, ${month} ${day}st, ${year}`;
                        case 2:
                            return `${day_of_week}, ${month} ${day}nd, ${year}`;
                        case 22:
                            return `${day_of_week}, ${month} ${day}nd, ${year}`;
                        case 3:
                            return `${day_of_week}, ${month} ${day}rd, ${year}`;
                        case 23:
                            return `${day_of_week}, ${month} ${day}rd, ${year}`;
                        default:
                            return `${day_of_week}, ${month} ${day}th, ${year}`;
                    }
                case "DoW_mm_dd_yyyy_style_2":
                    return `${day_of_week}, ${month} ${day}, ${year}`;
                    break;
                case "DoW_dd_mm_yyyy_style_3":
                    return `${day_of_week}, ${day}/${month}/${year}`;
                    break;
                case "DoW_mm_dd_yyyy_style_3":
                    return `${day_of_week}, ${month}/${day}/${year}`;
                    break;
                case "DoW_yyyy_mm_dd":
                    return `${day_of_week}, ${year}/${month}/${day}`;
                    break;
                default:
                switch(day){
                        case 1:
                            return `${day_of_week}, ${day}st ${month}`;
                        case 21:
                            return `${day_of_week}, ${day}st ${month}`;
                        case 31:
                            return `${day_of_week}, ${day}st ${month}`;
                        case 2:
                            return `${day_of_week}, ${day}nd ${month}`;
                        case 22:
                            return `${day_of_week}, ${day}nd ${month}`;
                        case 3:
                            return `${day_of_week}, ${day}rd ${month}`;
                        case 23:
                            return `${day_of_week}, ${day}rd ${month}`;
                        default:
                            return `${day_of_week}, ${day}th ${month}`;
                    }
            }
        },
        getTime(){
            let cut_off_time = this.data?.settings?.delivery_cut_off || "16:00:00";
            let hour = null;
            let min = null;
            let sec = null;
            let date = new Date();
            if(cut_off_time === 'dontHave'){
                return 'NaN';
            }
            else{
                let pattern = /^([\d]{1,2})\:$/g;
                let pattern_2 = /^([\d]{1,2})\:([\d]{1,2})\:$/g;
                let pattern_3 = /^([\d]{1,2})\:([\d]{1,2})\:([\d]{1,2})$/g;
                if(cut_off_time.match(pattern) || cut_off_time.match(pattern_2) || cut_off_time.match(pattern_3)){
                    hour = parseInt(cut_off_time.split(':')[0]);
                    min = parseInt(cut_off_time.split(':')[1]);
                    sec = parseInt(cut_off_time.split(':')[2]);

                    if(!min){
                        min = 0;
                    }
                    if(!sec){
                        sec = 0;
                    }
                    if(hour>=24 || min >=60 || sec>=60) return 'NaN';
                }
                else{
                    return 'NaN';
                }
            }
            let time = new Date().toUTCString().split(' ')[4];
            let hour_c = parseInt(time.split(':')[0]);
            let min_c = parseInt(time.split(':')[1]);
            let sec_c = parseInt(time.split(':')[2]);
            let rs_hour = null;
            let rs_min = null;
            let rs_sec = null;
            if(sec_c>sec){
                rs_sec = sec + 60 - sec_c;
                min--;
            }
            else{
                rs_sec = sec-sec_c;
            }
            if(min_c>min){
                rs_min = min+60-min_c;
                hour--;
            }
            else{
                rs_min = min-min_c;
            }
            if(hour_c>hour){
                rs_hour = 24+hour-hour_c;
                date = new Date(date.setDate(date.getDate()+1));
            }
            else{
                rs_hour = hour-hour_c;
            }
            date.setSeconds(rs_sec);
            date.setMinutes(rs_min);
            date.setHours(rs_hour);
            return date;
        },
    },
    computed: {

        textFilter(){
            let delivery_text = this.data?.settings?.delivery_text;
            return this.replaceDateTime(delivery_text);
        },
        getDate(){
            let value_start = this.data?.settings?.delivery_start_date;
            let value_end = this.data?.settings?.delivery_end_date;
            let number_start = 0;
            let number_end = 0;
            let arr_day_of_week = ['mon','tue','wed','thu','fri','sat','sun'];
            if(value_start){
                number_start = parseInt(value_start.replace('days', ''));
            }
            if(value_end){
                number_end = parseInt(value_end.replace('days', ''));
            }
            let mode = this.data?.settings?.exclude_days_from;
            let exclude_days = this.data?.settings?.exclude_days || "";
            let date_format = this.data?.settings?.delivery_format_date;

            // handle date start and date end
            let date_start = new Date();
            let date_end = new Date();

            exclude_days = exclude_days.split(',');
            let arr_exclude_days = [];
            let arr = exclude_days.map(function(day){
                return day.toLowerCase();
            });
            for(let i = 0; i< arr.length; i++){
                if(arr_day_of_week.includes(arr[i]) && !arr_exclude_days.includes(arr[i])){
                    arr_exclude_days.push(arr[i]);
                }
            }
            if(arr_exclude_days.length>=7){
                return date_start = 'Invalid';
            };
            if(mode && mode==='only_delivery'){
                let current_date = this.getTime();
                date_start = new Date(date_start.setDate(current_date.getDate() + number_start));
                while(!this.checkDayOfTheWeek(arr_exclude_days, date_start.toUTCString().split(',')[0].toLowerCase()))
                {
                    date_start = new Date(date_start.setDate(date_start.getDate() + 1));
                }
                date_end = new Date(date_end.setDate(current_date.getDate()+number_end));
                while(!this.checkDayOfTheWeek(arr_exclude_days, date_end.toUTCString().split(',')[0].toLowerCase())){
                    date_end = new Date(date_end.setDate(date_end.getDate()+1));
                }
            }
            else if(mode && mode === 'delivery_shipping'){
                let current_date = this.getTime();
                for(let i = 0; i<= number_start; i++){
                    let new_date = new Date();
                    new_date = new Date(new_date.setDate(current_date.getDate() + i));
                    if(!this.checkDayOfTheWeek(arr_exclude_days, new_date.toUTCString().split(',')[0].toLowerCase())){
                        number_start +=1;
                    }
                }
                date_start = new Date(date_start.setDate(current_date.getDate() + number_start));
                while(!this.checkDayOfTheWeek(arr_exclude_days, date_start.toUTCString().split(',')[0].toLowerCase())){
                    date_start = new Date(date_start.setDate(date_start.getDate() + 1));
                }
                for(let i = 0; i<=number_end; i++){
                    let new_date = new Date();
                    new_date = new Date(new_date.setDate(current_date.getDate() + i));
                    if(!this.checkDayOfTheWeek(arr_exclude_days, new_date.toUTCString().split(',')[0].toLowerCase())){
                        number_end +=1;
                    }
                }
                date_end = new Date(date_end.setDate(current_date.getDate() + number_end));
                while(!this.checkDayOfTheWeek(arr_exclude_days, date_end.toUTCString().split(',')[0].toLowerCase())){
                    date_end = new Date(date_end.setDate(date_end.getDate() + 1));
                }
            }

            return [this.formatDate(this.convertFullDate(date_start.toUTCString(),date_format)),this.formatDate(this.convertFullDate(date_end.toUTCString(),date_format))];
        },
        renderIcon(){
            let type_icon = this.data?.settings.type_icon;
            if(type_icon === 'icon'){
                if(this.data?.settings?.icon_shipping){
                    return this.data?.settings?.icon_shipping.value;
                }
            }
            else if(type_icon === 'img'){
                if(this.data?.settings?.thumbnail_shipping){
                    return  this.data?.settings?.thumbnail_shipping.value;;
                }
            }
            return "";
        },
        requestShopifyType() {
            return {
                shopify_type: 'product'
            };
        },

        settings() {
            return [
                {
                    group_title: '',
                    params: [
                        {
                            type: 'popup',
                            label: 'Icon/Image',
                            name: 'type_icon',
                            options: {
                                preview: "title",
                                type: 'dropdown',
                                values: { icon: 'Icon', img: 'Image' },
                                default: false
                            }
                        },
                        {
                            type: "picker",
                            label: "Icon",
                            name: "icon_shipping",
                            options: {
                                type:'icon',
                                oneline: true,
                                reset: true,
                                visible: function (data) {
                                    return (
                                        data &&
                                        data['type_icon'] === 'icon'
                                    );
                                }
                            }
                        },
                        {
                            type: "picker",
                            label: "Choose Image",
                            name: "thumbnail_shipping",
                            options: {
                                type: "image",
                                output: ["value", "name"],
                                editAlt: true,
                                visible:function(data){
                                    return(
                                        data &&
                                        data['type_icon'] === 'img'
                                    )
                                }
                            }
                        },
                        {
                            type: 'popup',
                            label: 'Position',
                            name: 'position_icon',
                            options: {
                                type: 'dropdown',
                                default: false,
                                preview: 'title',
                                values: {
                                    after: 'After',
                                    before: 'Before'
                                }
                            }
                        },
                        {
                            name: "icon_animation",
                            label: "Animation",
                            type: "dropdown",
                            options: {
                                search: true,
                                type: "animation",
                                size: "small"
                            },
                            css: {
                                selector: ' .ecom-delivery-icon',
                                properties: {
                                    animation: ""
                                }
                            }
                        },
                        {
                            type: "textarea",
                            name: "delivery_text",
                            label: "Delivery text",
                            description: "Order in the next [hour] to get this to you between [date_start] and [date_end], Order in the next [hour] to get it by [date_end], Order in the next [hour] to get it soon"
                        },
                        {
                            type: 'number',
                            label: 'Delivery Start Date',
                            name: 'delivery_start_date',
                            options: {
                                units: {
                                    days: {
                                        min: 0,
                                        max: 99
                                    }
                                },
                                reset: false
                            },
                            css:{
                                selector:' .',
                            }
                        },
                        {
                            type: 'number',
                            label: 'Delivery End Date',
                            name: 'delivery_end_date',
                            options: {
                                units: {
                                    days: {
                                        min: 0,
                                        max: 99
                                    }
                                },
                                reset: false
                            }
                        },
                        {
                            type: 'popup',
                            label: 'Exclude Days From',
                            name: 'exclude_days_from',
                            options: {
                                type: 'dropdown',
                                values: { only_delivery: 'Only delivery', delivery_shipping: 'Delivery + shipping' },
                                default: false
                            }
                        },
                        {
                            type: "text",
                            name: "exclude_days",
                            label: "Exclude days",
                            description: "Use the 'mon','tue','wed','thu','fri','sat' and 'sun'. Separate exclude days with a comma (,)."
                        },
                        {
                            type: 'popup',
                            label: 'Date delivery format',
                            name: 'delivery_format_date',
                            options: {
                                preview: "title",
                                type: 'dropdown',
                                values: {
                                    DoW_dd_mm_style_1: 'Friday, 29th December',
                                    DoW_dd_mm_style_2: 'Friday, 29 December',
                                    DoW_mm_dd_style_1: 'Friday, December 29',
                                    DoW_mm_dd_style_2: 'Friday, December 29th',
                                    DoW_dd_mm_yyyy_style_1: 'Friday, 29th December 2022',
                                    DoW_dd_mm_yyyy_style_2: 'Friday, 29th December, 2022',
                                    DoW_mm_dd_yyyy_style_1: 'Friday, December 29th, 2022',
                                    DoW_mm_dd_yyyy_style_2: 'Friday, December 29, 2022',
                                    DoW_dd_mm_yyyy_style_3: 'Friday, 29/12/2022',
                                    DoW_mm_dd_yyyy_style_3: 'Friday, 12/29/2022',
                                    DoW_yyyy_mm_dd: 'Friday, 2022/12/29'
                                },
                                default: false,
                            }
                        },
                        {
                            type: "text",
                            name: "delivery_cut_off",
                            label: "Delivery cut off (UTC Time)",
                            description: "Number Only(24 Hours Format - 16:00:00 Means 4PM). [see local time](https://www.timeanddate.com/worldclock/)"
                        },
                        {
                            type: "text",
                            name: "delivery_text_hours",
                            label: "Text hour(s)"
                        },
                        {
                            type: "text",
                            name: "delivery_text_minutes",
                            label: "Text minute(s)"
                        },
                        {
                            type: "line"
                        },
                        {
                            type: "paragraph",
                            content: "### Translation"
                        },
                        {
                            type: "textarea",
                            name: "delivery_custom_dow",
                            label: "Days of Week",
                            description: "Ex: Monday,Tuesday,...,Sunday (order: Monday to Sunday in your language). Separate by (,)"
                        },
                        {
                            type: "textarea",
                            name: "delivery_custom_month",
                            label: "Months of Year",
                            description: "Ex: January,February,...,December (order: January to February in your language). Separate by (,)"
                        }
                    ]
                }
            ]
        },

        style() {
            let style = [
                {
                    group_alias: 'text',
                    options: {
                        group_name: "text_delivery",
                        group_title: 'General',
                        selector: ' .ecom-delivery-time-wrapper'
                    }
                },
                {
                    group_alias: "text",
                    options: {
                        group_name: "text_delivery_date",
                        group_title: "Date",
                        selector: ' .ecom-delivery-time__date'
                    },
                    modify: {
                        remove: {
                            index: 0, length: 1
                        }
                    }
                },
            ]
            let img = {
                group_title: "Image",
                params: [
                    {
                        type: "number",
                        name: "img_width",
                        label: "Width",
                        options: {
                            units: {
                                px: {
                                    min: 0,
                                    max: 200
                                }
                            }
                        },
                        css: {
                            selector: " .ecom-delivery-icon",
                            properties: {
                                width: ''
                            }
                        }
                    },
                    {
                        alias: 'spacing',
                        options: {
                            css: {
                                selector: " .ecom-delivery-icon",
                            }
                        }
                    }
                ]
            }
            let icon = {
                    group_title: "Icon",
                    params: [
                        {
                            name: 'icon_color',
                            label: 'Color',
                            type: 'color',
                            options: {
                                global: {
                                    type: 'colors'
                                },
                                oneline: true
                            },
                            css: {
                                selector: " .ecom-delivery-icon",
                                properties: {
                                    color: ''
                                }
                            }
                        },
                        {
                            type: "number",
                            name: "icon_size",
                            label: "Size",
                            options: {
                                units: {
                                    px: {
                                        min: 0,
                                        max: 200
                                    }
                                }
                            },
                            css: {
                                selector: " .ecom-delivery-icon",
                                properties: {
                                    width: ''
                                }
                            }
                        },
                        {
                            alias: 'spacing',
                            options: {
                                css: {
                                    selector: " .ecom-delivery-icon",
                                }
                            }
                        }
                    ]
                }
            if(this.data.settings?.type_icon === 'icon'){
                style.push(icon);
            }
            else{
                style.push(img);
            }
            return style;
        },
        css() {
            return `
                    .ecom-delivery-time-wrapper{
                        display: block;
                        font-size: 16px;
                    }
                    .ecom-delivery-time,
                    .ecom-delivery-time-text{
                        display: inline;
                    }
                    .ecom-delivery-icon{
                        display: inline-block;
                        margin-right: 5px;
                    }
                    .ecom-delivery-icon svg{
                        width: inherit;
                    }

                `;
        },
        default() {
            return {
                "settings": {
                    "delivery_end_date": "5days",
                    "delivery_start_date": "2days",
                    "exclude_days": "sat,sun",
                    "delivery_text": "Order in the next [hour] to get it between [date_start] and [date_end]",
                    "exclude_days_from": "only_delivery",
                    "delivery_cut_off": "16:00:00",
                    "delivery_text_hours": "hour(s)",
                    "delivery_text_minutes": "minute(s)",
                    "delivery_format_date": "DoW_dd_mm_style_1",
                    "type_icon": "icon",
                    "icon_animation": "pulse",
                    "icon_shipping": {
                        "value": "<svg xmlns=\"http:\/\/www.w3.org\/2000\/svg\" viewBox=\"0 0 640 512\" fill=\"currentColor\"><!--! Font Awesome Pro 6.1.1 by @fontawesome - https:\/\/fontawesome.com License - https:\/\/fontawesome.com\/license (Commercial License) Copyright 2022 Fonticons, Inc. --><path d=\"M64 416C28.65 416 0 387.3 0 352V64C0 28.65 28.65 0 64 0H352C387.3 0 416 28.65 416 64V96H481.3C495.3 96 508.6 102.1 517.7 112.8L596.4 204.6C603.9 213.3 608 224.4 608 235.8V384H624C632.8 384 640 391.2 640 400C640 408.8 632.8 416 624 416H576C576 469 533 512 480 512C426.1 512 384 469 384 416H255.1C255.1 469 213 512 159.1 512C106.1 512 63.1 469 63.1 416H64zM32 64V352C32 369.7 46.33 384 64 384H69.46C82.64 346.7 118.2 320 160 320C201.8 320 237.4 346.7 250.5 384H384V64C384 46.33 369.7 32 352 32H64C46.33 32 32 46.33 32 64zM570.9 224L493.4 133.6C490.4 130 485.1 128 481.3 128H416V224H570.9zM416 256V344.4C432.1 329.2 455.4 320 480 320C521.8 320 557.4 346.7 570.5 384H576V256H416zM160 352C124.7 352 96 380.7 96 416C96 451.3 124.7 480 160 480C195.3 480 224 451.3 224 416C224 380.7 195.3 352 160 352zM480 480C515.3 480 544 451.3 544 416C544 380.7 515.3 352 480 352C444.7 352 416 380.7 416 416C416 451.3 444.7 480 480 480z\"><\/path><\/svg>"
                    },
                    position_icon: 'before'
                },
                "style": {
                    "icon_size": "20px",
                    "img_width": "20px",
                    "text_delivery_date":{
                        "textTypography":{
                            "font-weight": "600",
                            "letter-spacing": "1.5px",
                            "font-style": "normal",
                            "text-decoration": "underline"
                        }
                    },
                    "text_delivery": {
                        "font-size": "16px",
                        "font-weight": "400"
                    }
                }
            }
        },
        animatedClass() {
          return this.data?.settings?.icon_animation ? 'animated' : '';
        },
        javascript(){
            return function(){
                if(!this.$el)
                {
                    return false;
                }
                const $el = this.$el;
                let element_delivery = $el.querySelector('.ecom-delivery-time-text');
                if(!element_delivery) return false;
                window.addEventListener("DOMContentLoaded", function(){
                    let data = element_delivery.dataset.content;
                    if(!data || data ==='') return false;
                    replaceTime();
                })
                function checkDayOfTheWeek(arr,day){
                    if(arr.includes(day)) return false;
                    return true;
                }
                function convertFullDate(date,date_format){
                    if(!date) return;
                    let format_dow_string = element_delivery.dataset.dateDow || "";
                    let format_month_string = element_delivery.dataset.dateMonth || "";
                    let format_dow_arr = format_dow_string.split(",");
                    let format_month_arr = format_month_string.split(",");
                    let day_of_week = date.split(',')[0];
                    let month = date.split(' ')[2];
                    switch(day_of_week){
                        case 'Mon':
                            date = format_dow_arr[0] ? date.replace('Mon', format_dow_arr[0]) : date.replace('Mon', 'Monday');
                            break;
                        case 'Tue':
                            date = format_dow_arr[1] ? date.replace('Tue', format_dow_arr[1]) : date = date.replace('Tue', 'Tuesday');
                            break;
                        case 'Wed':
                            date = format_dow_arr[2] ? date.replace('Wed', format_dow_arr[2]) : date = date.replace('Wed', 'Wednesday');
                            break;
                        case 'Thu':
                            date = format_dow_arr[3] ? date.replace('Thu', format_dow_arr[3]) :date = date.replace('Thu', 'Thursday');
                            break;
                        case 'Fri':
                            date = format_dow_arr[4] ? date.replace('Fri', format_dow_arr[4]) :date = date.replace('Fri', 'Friday');
                            break;
                        case 'Sat':
                            date = format_dow_arr[5] ? date.replace('Sat', format_dow_arr[5]) :date = date.replace('Sat', 'Saturday');
                            break;
                        case 'Sun':
                            date = format_dow_arr[6] ? date.replace('Sun', format_dow_arr[6]) :date = date.replace('Sun', 'Sunday');
                            break;
                    }
                    if(date_format === 'DoW_dd_mm_yyyy_style_3' || date_format === 'DoW_mm_dd_yyyy_style_3' || date_format === 'DoW_yyyy_mm_dd') {
                        switch(month){
                            case 'Jan':
                                date = date.replace('Jan', '01,');
                                break;
                            case 'Feb':
                                date = date.replace('Feb', '02,');
                                break;
                            case 'Mar':
                                date = date.replace('Mar', '03,');
                                break;
                            case 'Apr':
                                date = date.replace('Apr', '04,');
                                break;
                            case 'May':
                                date = date.replace('May', '05,');
                                break;
                            case 'Jun':
                                date = date.replace('Jun', '06,');
                                break;
                            case 'Jul':
                                date = date.replace('Jul', '07,');
                                break;
                            case 'Aug':
                                date = date.replace('Aug', '08,');
                                break;
                            case 'Sep':
                                date = date.replace('Sep', '09,');
                                break;
                            case 'Oct':
                                date = date.replace('Oct', '10,');
                                break;
                            case 'Nov':
                                date = date.replace('Nov', '11,');
                                break;
                            case 'Dec':
                                date = date.replace('Dec', '12,');
                                break;
                        }
                        return date;
                    }
                    switch(month){
                        case 'Jan':
                            date = format_month_arr[0] ? date.replace('Jan', `,${format_month_arr[0]},`) : date.replace('Jan', ',January,');
                            break;
                        case 'Feb':
                            date = format_month_arr[1] ? date.replace('Feb', `,${format_month_arr[1]},`) : date.replace('Feb', ',February,');
                            break;
                        case 'Mar':
                            date = format_month_arr[2] ? date.replace('Mar', `,${format_month_arr[2]},`) : date.replace('Mar', ',March,');
                            break;
                        case 'Apr':
                            date = format_month_arr[3] ? date.replace('Apr', `,${format_month_arr[3]},`) : date.replace('Apr', ',April,');
                            break;
                        case 'May':
                            date = format_month_arr[4] ? date.replace('May', `,${format_month_arr[4]},`) : date.replace('May', ',May,');
                            break;
                        case 'Jun':
                            date = format_month_arr[5] ? date.replace('Jun', `,${format_month_arr[5]},`) : date.replace('Jun', ',June,');
                            break;
                        case 'Jul':
                            date = format_month_arr[6] ? date.replace('Jul', `,${format_month_arr[6]},`) : date.replace('Jul', ',July,');
                            break;
                        case 'Aug':
                            date = format_month_arr[7] ? date.replace('Aug', `,${format_month_arr[7]},`) : date.replace('Aug', ',August,');
                            break;
                        case 'Sep':
                            date = format_month_arr[8] ? date.replace('Sep', `,${format_month_arr[8]},`) : date.replace('Sep', ',September,');
                            break;
                        case 'Oct':
                            date = format_month_arr[9] ? date.replace('Oct', `,${format_month_arr[9]},`) : date.replace('Oct', ',October,');
                            break;
                        case 'Nov':
                            date = format_month_arr[10] ? date.replace('Nov', `,${format_month_arr[10]},`) : date.replace('Nov', ',November,');
                            break;
                        case 'Dec':
                            date = format_month_arr[11] ? date.replace('Dec', `,${format_month_arr[11]},`) : date.replace('Dec', ',December,');
                            break;
                    }
                    return date;
                }
                function formatDate(date){
                    if(!date) return;
                    let date_format  = element_delivery.dataset.format || 'DoW_dd_mm_style_1';
                    let day_of_week = date.split(',')[0];
                    let day = parseInt(date.split(',')[1]);
                    let month = date.split(',')[2];
                    date = date.split(',')[3];
                    let year = date.split(' ')[1];
                    switch(date_format){
                        case "DoW_dd_mm_style_1":
                            switch(day){
                                case 1:
                                    return `${day_of_week}, ${day}st ${month}`;
                                case 21:
                                    return `${day_of_week}, ${day}st ${month}`;
                                case 31:
                                    return `${day_of_week}, ${day}st ${month}`;
                                case 2:
                                    return `${day_of_week}, ${day}nd ${month}`;
                                case 22:
                                    return `${day_of_week}, ${day}nd ${month}`;
                                case 3:
                                    return `${day_of_week}, ${day}rd ${month}`;
                                case 23:
                                    return `${day_of_week}, ${day}rd ${month}`;
                                default:
                                    return `${day_of_week}, ${day}th ${month}`;
                            }

                        case "DoW_dd_mm_style_2":
                            return `${day_of_week}, ${day} ${month}`;
                            break;
                        case "DoW_mm_dd_style_1":
                            return `${day_of_week}, ${month} ${day}`;
                            break;
                        case "DoW_mm_dd_style_2":
                            switch(day){
                                case 1:
                                    return `${day_of_week}, ${month} ${day}st`;
                                case 21:
                                    return `${day_of_week}, ${month} ${day}st`;
                                case 31:
                                    return `${day_of_week}, ${month} ${day}st`;
                                case 2:
                                    return `${day_of_week}, ${month} ${day}nd`;
                                case 22:
                                    return `${day_of_week}, ${month} ${day}nd`;
                                case 3:
                                    return `${day_of_week}, ${month} ${day}rd`;
                                case 23:
                                    return `${day_of_week}, ${month} ${day}rd`;
                                default:
                                    return `${day_of_week}, ${month} ${day}th`;
                            }

                        case "DoW_dd_mm_yyyy_style_1":
                            return `${day_of_week}, ${day}th ${month} ${year}`;
                            break;
                        case "DoW_dd_mm_yyyy_style_2":

                            switch(day){
                                case 1:
                                    return `${day_of_week}, ${day}st ${month}, ${year}`;
                                case 21:
                                    return `${day_of_week}, ${day}st ${month}, ${year}`;
                                case 31:
                                    return `${day_of_week}, ${day}st ${month}, ${year}`;
                                case 2:
                                    return `${day_of_week}, ${day}nd ${month}, ${year}`;
                                case 22:
                                    return `${day_of_week}, ${day}nd ${month}, ${year}`;
                                case 3:
                                    return `${day_of_week}, ${day}rd ${month}, ${year}`;
                                case 23:
                                    return `${day_of_week}, ${day}rd ${month}, ${year}`;
                                default:
                                    return `${day_of_week}, ${day}th ${month}, ${year}`;
                            }
                        case "DoW_mm_dd_yyyy_style_1":
                            switch(day){
                                case 1:
                                    return `${day_of_week}, ${month} ${day}st, ${year}`;
                                case 21:
                                    return `${day_of_week}, ${month} ${day}st, ${year}`;
                                case 31:
                                    return `${day_of_week}, ${month} ${day}st, ${year}`;
                                case 2:
                                    return `${day_of_week}, ${month} ${day}nd, ${year}`;
                                case 22:
                                    return `${day_of_week}, ${month} ${day}nd, ${year}`;
                                case 3:
                                    return `${day_of_week}, ${month} ${day}rd, ${year}`;
                                case 23:
                                    return `${day_of_week}, ${month} ${day}rd, ${year}`;
                                default:
                                    return `${day_of_week}, ${month} ${day}th, ${year}`;
                            }
                        case "DoW_mm_dd_yyyy_style_2":
                            return `${day_of_week}, ${month} ${day}, ${year}`;
                            break;
                        case "DoW_dd_mm_yyyy_style_3":
                            return `${day_of_week}, ${day}/${month}/${year}`;
                            break;
                        case "DoW_mm_dd_yyyy_style_3":
                            return `${day_of_week}, ${month}/${day}/${year}`;
                            break;
                        case "DoW_yyyy_mm_dd":
                            return `${day_of_week}, ${year}/${month}/${day}`;
                            break;
                        default:
                        switch(day){
                                case 1:
                                    return `${day_of_week}, ${day}st ${month}`;
                                case 21:
                                    return `${day_of_week}, ${day}st ${month}`;
                                case 31:
                                    return `${day_of_week}, ${day}st ${month}`;
                                case 2:
                                    return `${day_of_week}, ${day}nd ${month}`;
                                case 22:
                                    return `${day_of_week}, ${day}nd ${month}`;
                                case 3:
                                    return `${day_of_week}, ${day}rd ${month}`;
                                case 23:
                                    return `${day_of_week}, ${day}rd ${month}`;
                                default:
                                    return `${day_of_week}, ${day}th ${month}`;
                            }
                    }
                }
                function getTime(){
                    let cut_off_time = element_delivery.dataset.time || "16:00:00";
                    let hour = null;
                    let min = null;
                    let sec = null;
                    let date = new Date();
                    if(cut_off_time === 'dontHave'){
                        return 'NaN';
                    }
                    else{
                        let pattern = /^([\d]{1,2})\:$/g;
                        let pattern_2 = /^([\d]{1,2})\:([\d]{1,2})\:$/g;
                        let pattern_3 = /^([\d]{1,2})\:([\d]{1,2})\:([\d]{1,2})$/g;
                        if(cut_off_time.match(pattern) || cut_off_time.match(pattern_2) || cut_off_time.match(pattern_3)){
                            hour = parseInt(cut_off_time.split(':')[0]);
                            min = parseInt(cut_off_time.split(':')[1]);
                            sec = parseInt(cut_off_time.split(':')[2]);

                            if(!min){
                                min = 0;
                            }
                            if(!sec){
                                sec = 0;
                            }
                            if(hour>=24 || min >=60 || sec>=60) return 'NaN';
                        }
                        else{
                            return 'NaN';
                        }
                    }
                    let time = new Date().toUTCString().split(' ')[4];
                    let hour_c = parseInt(time.split(':')[0]);
                    let min_c = parseInt(time.split(':')[1]);
                    let sec_c = parseInt(time.split(':')[2]);
                    let rs_hour = null;
                    let rs_min = null;
                    let rs_sec = null;
                    if(sec_c>sec){
                        rs_sec = sec + 60 - sec_c;
                        min--;
                    }
                    else{
                        rs_sec = sec-sec_c;
                    }
                    if(min_c>min){
                        rs_min = min+60-min_c;
                        hour--;
                    }
                    else{
                        rs_min = min-min_c;
                    }
                    if(hour_c>hour){
                        rs_hour = 24+hour-hour_c;
                        date = new Date(date.setDate(date.getDate() + 1));
                    }
                    else{
                        rs_hour = hour-hour_c;
                    }
                    date.setSeconds(rs_sec);
                    date.setMinutes(rs_min);
                    date.setHours(rs_hour);
                    return date;
                }
                function getDate(){
                    let value_start = element_delivery.dataset.ds || "2days";
                    let value_end = element_delivery.dataset.de || "5days";
                    let number_start = 0;
                    let number_end = 0;
                    let arr_day_of_week = ['mon','tue','wed','thu','fri','sat','sun'];
                    if(value_start){
                        number_start = parseInt(value_start.replace('days', ''));
                    }
                    if(value_end){
                        number_end = parseInt(value_end.replace('days', ''));
                    }
                    let mode = element_delivery.dataset.mode || "only_delivery";
                    let exclude_days = element_delivery.dataset.cut || "";
                    let date_format = element_delivery.dataset.format || "DoW_dd_mm_style_1";

                    // handle date start and date end
                    let date_start = new Date();
                    let date_end = new Date();

                    exclude_days = exclude_days.split(',');
                    let arr_exclude_days = [];
                    let arr = exclude_days.map(function(day){
                        return day.toLowerCase();
                    });
                    for(let i = 0; i< arr.length; i++){
                        if(arr_day_of_week.includes(arr[i]) && !arr_exclude_days.includes(arr[i])){
                            arr_exclude_days.push(arr[i]);
                        }
                    }
                    if(arr_exclude_days.length>=7){
                        return date_start = 'Invalid';
                    };
                    if(mode && mode==='only_delivery'){
                        let current_date = getTime();
                        date_start = new Date(date_start.setDate(current_date.getDate() + number_start));
                        while(!checkDayOfTheWeek(arr_exclude_days, date_start.toUTCString().split(',')[0].toLowerCase()))
                        {
                            date_start = new Date(date_start.setDate(date_start.getDate() + 1));
                        }
                        date_end = new Date(date_end.setDate(current_date.getDate()+number_end));
                        while(!checkDayOfTheWeek(arr_exclude_days, date_end.toUTCString().split(',')[0].toLowerCase())){
                            date_end = new Date(date_end.setDate(date_end.getDate() + 1));
                        }
                    }
                    else if(mode && mode === 'delivery_shipping'){
                        let current_date = getTime();
                        for(let i = 0; i<= number_start; i++){
                            let new_date = new Date();
                            new_date = new Date(new_date.setDate(current_date.getDate() + i));
                            if(!checkDayOfTheWeek(arr_exclude_days, new_date.toUTCString().split(',')[0].toLowerCase())){
                                number_start +=1;
                            }
                        }
                        date_start = new Date(date_start.setDate(current_date.getDate() + number_start));
                        while(!checkDayOfTheWeek(arr_exclude_days, date_start.toUTCString().split(',')[0].toLowerCase())){
                            date_start = new Date(date_start.setDate(date_start.getDate() + 1));
                        }
                        for(let i = 0; i<=number_end; i++){
                            let new_date = new Date();
                            new_date = new Date(new_date.setDate(current_date.getDate() + i));
                            if(!checkDayOfTheWeek(arr_exclude_days, new_date.toUTCString().split(',')[0].toLowerCase())){
                                number_end +=1;
                            }
                        }
                        date_end = new Date(date_end.setDate(current_date.getDate() + number_end));
                        while(!checkDayOfTheWeek(arr_exclude_days, date_end.toUTCString().split(',')[0].toLowerCase())){
                            date_end = new Date(date_end.setDate(date_end.getDate() + 1));
                        }
                    }
                    return [formatDate(convertFullDate(date_start.toUTCString(),date_format)),formatDate(convertFullDate(date_end.toUTCString(),date_format))];
                }
                function replaceTime(){
                    let dataFilter = element_delivery.dataset.content;
                    if(!dataFilter || dataFilter==='') return false;
                    dataFilter = dataFilter.replace("[date_start]", `<span class="ecom-delivery-time__date">${getDate()[0]}</span>`);
                    dataFilter = dataFilter.replace("[date_end]", `<span class="ecom-delivery-time__date">${getDate()[1]}</span>`);
                    element_delivery.innerHTML = dataFilter;
                    let date = new Date(getTime());
                    let hour = null, min = null;
                    if(date==='NaN'){
                        hour = 'NaN';
                        min = 'NaN';
                    }else{
                        hour = date.getHours();
                        min = date.getMinutes();
                    }

                    let text_hour = element_delivery.dataset.textHour || 'hour(s)';
                    let text_min = element_delivery.dataset.textMin || 'minute(s)';
                    let text_replace = element_delivery.dataset.timeReplace;
                    if(!text_replace) {
                        dataFilter = dataFilter.replace(`[hour]`,``);
                        element_delivery.innerHTML = dataFilter;
                        return false;
                    }
                    dataFilter  = dataFilter.replace(`[${text_replace}]`,`${hour} ${text_hour} ${min} ${text_min}`);
                    element_delivery.innerHTML = dataFilter;
                    return true;
                }
                replaceTime();

                const handleReplaceDateTime = setInterval(function(){
                    let progress = replaceTime();
                    if(!progress) {
                        clearInterval(handleReplaceDateTime);
                    }
                },1000);
            }
        }
    },
    watch: {

    },
    mounted(){

    }
};
</script>
