<template>
    <div
        v-if="!exporting"
        class="drop__block--placeholder"
        :class="[
            'drop__block_' + index,
            {'ecom-current-block-drag': placeholderTop, 'ecom-drag-current': currentDrag}]"
        @mouseenter="overBlock"
    >
        <div class="drop__block--placeholder-body"></div>
    </div>
    <div
        v-bind="blockAttrs"
        @mouseenter="overBlock"
        @mouseleave="outBlock"
        @mousedown="dragBlock"
        @click="activeBlock"
        @contextmenu="rightClickMenu"
    >
        <Row
            v-if="block.row"
            :section_id="section_id"
            ref="blockElement"
            :data="block.row"
            :deep="deep+1"
            :index="index"
        />
        <component
            :is="block.group.element+'Element'"
            v-else-if="block.group"
            ref="blockElement"
            :data="block.group"
            :key="refreshKey"
            :deep="deep"
            :index="index"
        />
        <template v-else>
            <component
                :is="block.name+'Element'"
                ref="blockElement"
                :key="refreshKey"
                :data="block"
                :deep="deep"
            />
            <div v-if="!exporting" v-show="isActive && control">
                <div
                    class="core__block--settings"
                    :style="styleattr.settings"
                    :title="'Edit '+block.name"
                    @mousedown="editOrDrag"
                >
                    <span>
                        <svg xmlns="http://www.w3.org/2000/svg" class="ecom-icon" viewBox="0 0 512 512" fill="curerntColor" ><path d="M240 352V272H160C151.2 272 144 264.8 144 256C144 247.2 151.2 240 160 240H240V160C240 151.2 247.2 144 256 144C264.8 144 272 151.2 272 160V240H352C360.8 240 368 247.2 368 256C368 264.8 360.8 272 352 272H272V352C272 360.8 264.8 368 256 368C247.2 368 240 360.8 240 352zM512 256C512 397.4 397.4 512 256 512C114.6 512 0 397.4 0 256C0 114.6 114.6 0 256 0C397.4 0 512 114.6 512 256zM256 32C132.3 32 32 132.3 32 256C32 379.7 132.3 480 256 480C379.7 480 480 379.7 480 256C480 132.3 379.7 32 256 32z"/></svg>
                    </span>
                </div>
                <div
                    v-if="!exporting"
                    v-show="!isDragging"
                    class="core__block--settings"
                >
                    <div class="core__block--settings__left">
                        <span
                            class="core__block--settings__move"
                            @mousedown="editOrDrag"
                        >
                            <ecom-icon :icon="icons.faArrowsUpDownLeftRight" />
                        </span>
                        <span class="core__block--settings__title">{{ block?.title ?? block.name }}</span>
                    </div>
                    <div class="core__block--settings__action">
                        <span>
                            <ecom-icon :icon="icons.faPen" />
                        </span>
                        <span @click="duplicate()">
                            <ecom-icon :icon="icons.faClone" />
                        </span>
                        <span @click="deleteEl()">
                            <ecom-icon :icon="icons.faTrashCan" />
                        </span>
                    </div>
                </div>
            </div>
        </template>
        <template v-if="canDrop">
            <div
                class="placeholder__top"
                :class="{block__placeholder: !block.row && !block.group}"
                @mouseenter="placehover('top')"
            />
            <div
                class="placeholder__bottom"
                :class="{block__placeholder: !block.row && !block.group}"
                @mouseenter="placehover('bottom')"
            />
        </template>
        <template v-else />
        <div
            v-if="!block.row && !exporting"
            class="core__block--padding-bottom core__block--padding bottom-0"
            @mousedown.stop="startDragPadding($event, 'bottom')"
        >
           <span
                v-if="paddingPosition == 'bottom' && isDrag"
                class="core__block--padding-value"
                v-html="paddingCurrentVal + 'px'"
            />
            <span
                v-if="isDrag"
                class="core__block--padding-markup"
                :style="{height: paddingCurrentVal + 'px'}"
            />
        </div>
    </div>
    <div
        v-if="!exporting"
        :class="{'ecom-current-block-drag': placeholderBottom, 'ecom-drag-current': currentDrag}"
        class="drop__block--placeholder"
        @mouseenter="overBlock"
    >
        <div class="drop__block--placeholder-body"></div>
    </div>
</template>

<script lang="ts">
    import { Core } from '@/mixins';
    import { store } from '@/store';
    import { faClone, faCopy , faPen, faArrowsUpDownLeftRight, faTrashCan} from '@fortawesome/pro-light-svg-icons';
    import { componentName, loadComponent } from '@/utils/helpers';
    import * as AllCoreElements from '@/components/Builder/Components/Core/Elements/index.js';

    const components = function(){
        let extension_elements = store.getters['page/extension_elements'],
                ecosystem_apps = store.getters['page/ecosystem_apps'],
                coms = {...AllCoreElements};

            if (
                extension_elements &&
                extension_elements.elements &&
                extension_elements.path
            ) {
                let keys = Object.keys(extension_elements.elements);
                keys.forEach(key => {
                    let com_name = componentName(key) + 'Element';
                    coms[com_name] = loadComponent(
                       extension_elements.elements[key].file.indexOf('https://') === 0 ? extension_elements.elements[key].file:  `${extension_elements.path}${extension_elements.elements[key].file}?${extension_elements.query}`,
                       []
                    );
                });
            }

            if (ecosystem_apps) {
                ecosystem_apps.forEach((elm: object) => {
                    const com_name = componentName(elm.slug) + 'Element';
                    coms[com_name] = loadComponent(elm.data_url,[]);
                });
            }

            return coms;
    }
    export default {
        name: 'Block',
        mixins: [ Core ],
        props: {
            'data': {
                type: Object,
                default () {
                    return {
                        name: 'empty'
                    };
                }
            },
            'deep': {
                type: Number,
                default: 0
            },
            'index': {
                type: Number,
                default: 0
            },
            'control': {
                type: Boolean,
                default: true
            },
            'gindex': {
                type: Number,
                default: 0
            },
            'section_id': {
                type: String,
                default: ''
            },
        },

        components: components(),

        data (): object {
            return {
                is: 'block',
                name: this.data.row ? 'inner row' : (this.data.group ? this.data.group.element : 'block'),
                over: false,
                dragging: false,
                placeholder: 'top',
                refreshKey: '',
                isMounted: false,
                isDrag: false,
                space: 0,
                position: 0,
                paddingCurrentVal: 0,
                paddingPosition: ''
            };
        },

        computed: {
            icons(){
                return {faClone, faCopy , faPen, faArrowsUpDownLeftRight, faTrashCan};
            },
            stack () {
                return this.$parent.$parent.blocks;
            },

            block () {
                return this.data;
            },

            ingroup () {
                return this.$parent.$parent.group !== undefined;
            },
            editing () {
                return this.editingElement;
            },
            editingData(){
                return this.editing && this.editing.data ? this.editing.data : {};
            },
            styleattr () {
                return {
                    wrapper: {
                        outline: this.isActive && !this.isDragging && !this.block.group ? '1px solid rgb(var(--primary-color-light))' : '',
                        display: !this.isDragging || !(this.dropTo !== this && this.dragCurrent === this)
                    },
                    settings: `display: ${this.over && !this.isDragging ? 'inline-block' : 'none'
                        }`
                };
            },
            currentDrag() {
                return this.dropTo !== this && this.dragCurrent === this;
            },
            isFirst () {
                return this.index == 0;
            },

            isLast () {
                return this.index == this.$parent.$parent.blocks.length - 1;
            },

            type () {
                return this.data.row ? 'row' : (this.data.group ? 'group' : 'block');
            },

            canDrop () {
                return this.$helpers.canDrop(this.dragCurrent, this) && this.control;
            },

            blockAttrs () {
                //const spacingName = 'spacing' + (this.screen === 'desktop' ? '' : ('__' + this.screen));

                const attrs = {
                    ref: 'wrapper',
                    class: {
                        'ecom-animated': !this.exporting,
                        'ecom-animation': this.data?.advanced?.animation_type,
                        'ecom-block ecom-core core__block': true,
                        'ecom-block-active' : this.isActive,
                        'ecom-block-isDragPadding' : this.isActive && !this.isDragging && !this.block.group,
                        // 'elmspace': ['unset', 'custom'].indexOf(
                        //     this.data?.advanced?.['width-max'] ??
                        //     this.data?.group?.advanced?.['width-max']
                        // ) === -1,
                        'elmspace' : !this.data?.advanced?.spacing?.margin?.top,
                        ...this.classes
                    },
                    'data-core-is': this.block.row ? (this.deep === 2 ? 'block' : 'row') : (this.block.group ? 'group' : 'block'),
                    style: !this.block.row ? this.styleattr.wrapper : '',
                    ...this.attributes
                }

                if(this.scrollingAnimation) {
                    attrs['ecom-scrolling-animation'] = this.scrollingAnimation;
                }
                return attrs;
            },
            placeholderTop () {
                return this.control &&
                    this.placeholder != 'bottom' &&
                    this.dragCurrent &&
                    !this.placeholderBottom &&
                    (
                        (
                            this.dropTo === this &&
                            this.dragCurrent !== this &&
                            this.dragCurrent.$parent.$parent !== this
                        )
                        ||
                        (
                            !this.dropTo &&
                            (
                                this.dragCurrent === this ||
                                this.dragCurrent.$parent.$parent === this
                            )
                        )
                    );
            },

            placeholderBottom () {
                return this.control &&
                    this.placeholder == 'bottom' &&
                    this.dropTo === this &&
                    this.dragCurrent !== this &&
                    this.dragCurrent.$parent.$parent !== this;
            },

            refreshId() {
                return this.block.group ? this.block.group.refresh : this.block.refresh;
            },

            screen(){
                return this.$store.getters['builder/screen'];
            },
            sections() {
                return this.$store.getters['page/sections'];
            },
            ecosystem_apps(){
                return this.$store.getters['page/ecosystem_apps'];
            },
            appsLength(){
                return this.ecosystem_apps.length;
            }

        },

        //Added by daniel
        updated () {
            if (
                this.block.hasRequestLiquid !== true &&
                Object.keys(this.$refs?.blockElement?.requestShopifyType ?? {}).length > 0 &&
                Object.keys(this.$refs?.blockElement?.liquids ?? {}).length > 0
            ) {
                this.block.hasRequestLiquid = true;
            }
            if (
                this.data.editOnMounted &&
                this.$refs.blockElement
            ) {
                delete this.data.editOnMounted;
                // this.editBlock();
                this.openPresetLibrary();
            }
        },

        mounted () {
            if (
                this.data.editOnMounted &&
                this.$refs.blockElement
            ) {
                delete this.data.editOnMounted;
                // this.editBlock();
                this.openPresetLibrary();
            }
            //Modify by daniel
            if (
                Object.keys(this.$refs?.blockElement?.requestShopifyType ?? {}).length > 0 &&
                Object.keys(this.$refs?.blockElement?.liquids ?? {}).length > 0
            ) {
                this.block.hasRequestLiquid = true;
            }
            if (this.block.group && !this.block.id) {
                this.block.id = this.$helpers.randid();
            }
            this.stacks[this.data.id] = this;
            this.isMounted = true;

        },

        watch: {
            refreshId(val) {
                if (this.isMounted) {
                    this.refreshKey = val;
                }
            },
            appsLength(value){
                this.ecosystem_apps.forEach((elm: object) => {
                    const com_name = componentName(elm.slug) + 'Element';
                    if(!this.$options?.components[com_name]){
                        this.$options.components[com_name] = loadComponent(elm.data_url)
                    }
                });
            }
        },

        methods: {
            processMenu(e) {
                if (typeof this.ctx[e] == 'function') {
                    this.ctx[e]( clipboard );
                } else if (typeof this[e] == 'function') {
                    this[e](this.ctx);
                }

            },
            startDragPadding(e, name) {
                this.position = e['clientY'];
                this.paddingPosition = name;
                this.idoc().onmouseup = this.onMouseUp;
                this.idoc().onmousemove = this.onMouseMove;
                //this.$store.commit('builder/setActiveElement', this);
            },
            onMouseMove(e) {
                this.$store.commit('builder/setIsDragPadding', this.data.id);
                const element = this.$refs?.blockElement, $el = element?.$el?.parentElement,
                      advance = Object.assign({}, this.$refs?.blockElement?.data?.advanced);
                if(!$el) return false;
                let padding = this.paddingPosition == 'bottom' ? advance?.spacing?.padding?.bottom : advance?.spacing?.padding?.top;
                let space = 0, result = '';
                padding = padding ? parseInt(padding.replace(/[^0-9.]/g, "")) : 0;

                this.idoc().body.style.cursor = 'ns-resize';

                let newP = e['clientY'];
                space = newP - this.position;

                if(Math.abs(space) > 3 ) {
                    this.isDrag = true;
                    this.paddingCurrentVal = padding;
                    let padding_current = space + padding >= 0 ? space + padding : 0;
                    if(this.paddingPosition == 'bottom') {
                        $el.style.paddingBottom = (padding_current) + 'px';
                    } else {
                        $el.style.paddingTop = Math.abs(padding_current) + 'px';
                    }
                    this.paddingCurrentVal = result = Math.abs(padding_current);

                }

                this.space = result == 0 || result == '' ? 0 : result + 'px';
            },
            onMouseUp() {
                const currentScreen = this.currentScreen(), currentScreenName = currentScreen.name == 'desktop' ? '' :  currentScreen.name,
                        spacing_res = currentScreenName ? 'spacing__' + currentScreenName : 'spacing';

                const element = this.$refs?.blockElement, $el = element?.$el?.parentElement,
                      advance = Object.assign({}, this.$refs?.blockElement?.data?.advanced);

                if(!$el) return false;
                if(!advance.hasOwnProperty(spacing_res)) {
                    advance[spacing_res] = {
                        padding: {

                        }
                    }
                } else if(!advance[spacing_res].hasOwnProperty('padding')) {
                    advance[spacing_res].padding = {}
                }

                const space = this.space;
                this.space = 0;

                if(this.paddingPosition == 'bottom') {
                    advance[spacing_res].padding.bottom = space;
                } else {
                    advance[spacing_res].padding.top = space;
                }

                Object.assign(this.$refs?.blockElement.data.advanced, advance);

                setTimeout(() => {
                    $el.style.paddingBottom = null;
                    $el.style.paddingTop = null;
                }, 1000)
                this.paddingPosition = '';
                this.paddingCurrentVal = 0;
                this.isDrag = false;
                this.idoc().body.style.cursor = 'default';
                this.idoc().onmouseup = null;
                this.idoc().onmousemove = null;
                this.$store.commit('builder/setIsDragPadding', '');
            },
            currentScreen(){
                return this.$store.getters['builder/currentScreen'];
            },
            editOrDrag(e){
                if(this.$refs.blockElement?.$el)
                {
                    this.$refs.blockElement?.$el.dispatchEvent(new CustomEvent('setactive',{ detail: true}))
                }
            },
            overBlock () {
                if (!this.isFullScreen) {
                    this.over = true;
                    //this.parentsOut();
                    clearTimeout(this.timmer);
                }
            },

            outBlock () {
                if (!this.isFullScreen) {
                    if (this._group) {
                        this._group.over = true;
                    } else if (this._column) {
                        this._column.over = true;
                        if (this._row) {
                            this._row.over = true;
                        } else if (this._section) {
                            this._section.over = true;
                        }
                    }
                    clearTimeout(this.timmer);
                    this.timmer = setTimeout(() => {
                        this.over = false;
                    }, 100);
                }
            },

            placehover (pos) {
                if (
                    this.canDrop
                ) {
                    if(this.section_id) {
                        this.$store.commit('builder/sectionDrag', this.section_id);
                    }
                    this.$store.commit('builder/setDropTo', this);
                    this.$store.commit('builder/setDropPos', pos);
                    this.placeholder = pos;
                }
            },

            dragBlock (e) {

                if (
                    !this.isFullScreen &&
                    (this.type == 'block' || (this.deep == 4 && this.type == 'row')) &&
                    this.control
                ) {
                    this.startDrag(e);
                }
            },

            activeBlock () {
                if (
                    this.isFullScreen ||
                    this.type != 'block'
                ) {
                    return;
                }
                // For unbind select when click outside builder
                this.$store.commit('builder/setDragging', { timmer: new Date().getTime() });
                this.$store.commit('builder/setActiveElement', this);
                this.editBlock();
                // setTimeout(()=>{
                //     if( this.editingData.tab_active === 'presets')
                //         this.editingData.tab_active = 'content';
                // },100)
            },

            editBlock () {
                if (this.$refs?.blockElement?.settings || this.$refs?.blockElement?.style) {
                    this.$store.commit('builder/setEditingElement', this.$refs.blockElement);
                }
            },
            edit(){
                this.editBlock();
            },
            openPresetLibrary(){
                    // this.$store.commit('builder/setEditingElement', this.$refs.blockElement);
                     if (this.$refs?.blockElement?.settings || this.$refs?.blockElement?.style) {

                        this.$store.commit('builder/setEditingElement', this.$refs.blockElement);
                        // if( this.$refs.blockElement.$options?.presets)
                        // {
                        //     setTimeout(()=>{
                        //         this.editingData.tab_active = 'presets';
                        //         this.editingData.auto_apply = true;
                        //     },100)
                        // }
                    }
            },

            deleteEl(){
                if(this.section_id) {
                    const section = this.sections.find((s:any) => s.id == this.section_id);
                    if(section) {
                        this.$store.dispatch('history/addStack', {
                            type: this.$options?.name ?? this?.name,
                            data: section,
                            section_id: this.section_id,
                            action: 'Deleted'
                        });
                    }
                }

                this.dropOut();
            },

            duplicate () {
                let height = this.$refs?.wrapper?.offsetHeight ?? 0,
                    clone = this.cloneData();

                if (clone.title) {
                    clone.title = clone.title.replace(/\(([\d]*)\)|$/, (...m) => {
                        if (m[1]) {
                            return '(' + (parseInt(m[1]) + 1) + ')';
                        } else {
                            return '(1)';
                        }
                    });
                }
                this.stack.splice(this.index + 1, 0, clone);
                this.scroll(height);
            },

            paste (clipboard) {
                if(!clipboard) {
                    return;
                }
                let data = this.$helpers.copy(clipboard.data),
                    sect,
                    col;
                if(data?.group)
                {
                    data.group.id = this.$helpers.randid();
                    data.group.settings.items.map(blocks => {
                        blocks.blocks.map(block => {
                            block.id = this.$helpers.randid();
                        })
                    })
                }
                switch (clipboard.is) {
                    case 'section':
                        sect = this.parent('section');
                        sect.stack.splice(sect.index + 1, 0, data);
                        this.scroll(sect.$refs?.wrapper?.offsetHeight ?? 0);
                        break;
                    case 'column':
                        col = this.parent('section');
                        if (col.stack.length >= 10) {
                            this.notice('Can not insert more than 10 columns in a ' + col.$parent.is, 'error');
                        } else {
                            col.stack.splice(col.index + 1, 0, data);
                            col.$parent.refreshCols();
                        }
                        break;
                    case 'block':
                        data.id= this.$helpers.randid();// Keep current data
                        this.stack.splice(this.index + 1, 0, data);
                        this.scroll((this.$refs?.wrapper?.offsetHeight ?? 0) / 2);
                        break;
                }
            },

            insertCol () {

                if (this.columns.length >= 10) {
                    this.notice('Can not insert more than 10 columns in a row', 'error');
                } else {
                    this.columns.push({
                        width: Math.round(100 / (this.columns.length + 1)),
                        blocks: []
                    });
                    this.refreshCols();
                }
            },
            dropOut () {
                if (this.data.id == this.editingElement?.data?.id) {
                    setTimeout(() => {
                        this.$store.dispatch('builder/setEditingElement', null);
                    }, 50);
                }

                let currentID = this.data.id;
                let realIndex = this.stack.findIndex((item)=>{
                    return item.id  === currentID;
                });
                if(isNaN(realIndex))
                {
                    realIndex = this.index;
                }

                return this.stack && this.stack.splice(realIndex, 1)[0];
            },

            dropIn (target, pos) {
                if (
                    target._column === this._column &&
                    target.index < pos &&
                    target.index > 0
                ) {
                    pos--;
                }

                let currentID = this.data.id;
                let realIndex = this.stack.findIndex((item)=>{
                    return item.id  === currentID;
                });
                if(isNaN(realIndex))
                {
                    realIndex = this.index;
                }

                if(target.index < realIndex && target._column === this._column) {
                    realIndex -= 1;
                }

                if(this.section_id) {
                    const action = this.section_id == target.section_id ? 'add' : 'move',
                        section = this.sections.find((s:any) => s.id == this.section_id);
                    let payload = {
                        type: 'block',
                        data: section,
                        section_id: this.section_id,
                        action: action
                    }
                    if(action == 'move') {
                        payload.section_from = target.section_id;
                        payload.section_from_data = this.sections.find((s:any) => s.id == target.section_id);
                    }
                    this.$store.dispatch('history/addStack', payload);
                }

                this.stack?.splice( realIndex + pos, 0, target.dropOut());

                // this.$store.dispatch('history/addStack', {
                //     type: target.name,
                //     name: '',
                //     action: target.editOnMounted ? 'Droped' : 'Moved'
                // });
            }
        }

    };
</script>