<template>
    <div class="ecom-element ecom-product-single ecom-product-single__price">
        <div class="ecom-product-single__price-wrapper">
            <div class="ecom-product-single__price-container">
                <div class="ecom-product-single__price-container-grid">
                    <div
                        class="ecom-product-single ecom-product-single__price--prices"
                        v-html="liquid('prices')"
                    />
                    <div
                        v-if="show_badges" class="ecom-product-single__price--badges-wrapper"
                        v-html="liquid('badge')"
                    />
                    <div
                        v-if="show_sale_pecent" class="ecom-product-single__price--badges-pecent-wrapper"
                        v-html="liquid('badge_pecent')"
                    />
                </div>
                <div
                    v-if="show_groundprice"
                    class="ecom-product_ground-price" v-html="liquid('ground_price')"
                />
                <div class="ecom-product-single-afterpay" v-html="liquid('afterpay')" />
            </div>
        </div>
    </div>
</template>

<script>
import { Liquid, Element, Javascript } from '@/mixins';
export default {
    name: 'ProductPrice',

    presets:true,
    mixins: [Liquid, Element, Javascript],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    data() {
        return {
            jsreactives: ['sale_text', 'show_badges', 'sold_text']
        };
    },
    computed: {
        liquids() {
            return {
                prices: {
                    code: `
                    {%- liquid
                        if product.has_only_default_variant
                            assign target = product
                        else
                            assign target = product.selected_or_first_available_variant
                        endif
                    -%}

                    <div class="ecom-product-single__price--sale" data-price="{{target.price}}">{{target.price| money }}</div>
                    ${!this.data.settings?.show_total_price
                        ? `
                        <div class="ecom-product-single__price--regular" {%- if product.compare_at_price == nil or product.compare_at_price <  product.price -%}  style="display:none" {% endif %}>
                            {{target.compare_at_price | money }}
                        </div>
                        `
                        : ``
                    }
                    {%- capture taxes_text -%}${this.lang(this.data.settings.include_taxes,'include_taxes')}{%- endcapture -%}
                    {%- if cart.taxes_included or shop.shipping_policy.body != blank -%}
                        {% if taxes_text%}
                            <div class="ec-product__tax caption rte">
                                {%- if shop.taxes_included -%}
                                    ${this.lang(this.data.settings.include_taxes,'include_taxes')}
                                {%- endif -%}
                                {%- if shop.shipping_policy.body != blank -%}
                                    ${this.lang(this.data.settings.shipping_policy,'shipping_policy', {
                                        link: 'shop.shipping_policy.url'
                                    })}
                                {%- endif -%}
                            </div>
                            ${this.data.settings?.show_payment_terms ?
                                `
                                <div class="ec-form-payment_terms">
                                    {{ form | payment_terms}}
                                </div>
                                `
                                : ``
                            }
                        {%- endif -%}
                    {%- endif -%}

                `,
                    preview: `
                    <div class="ecom-skeleton-item" style="min-width: 180px">
                        <div class="ecom-skeleton-col-12">
                            <div class="ecom-skeleton-row">
                                <div class="ecom-skeleton-col-8"></div>
                            </div>
                        </div>
                        <div class="ecom-skeleton-col-12">
                        <div class="ecom-skeleton-row">
                            <div class="ecom-skeleton-col-10"></div>
                        </div>
                        </div>
                    </div>
                `
                },
                badge: {
                    code: `
                    <div class="ecom-product-single__price--badges">
                        {%- assign savings = product.compare_at_price | minus: product.price | times: 100.0 | divided_by: product.compare_at_price | round -%}
                        <span class="ecom-product-single__price--badges-sale" data-text=" ${this.lang(this.sale_text, 'product_single_sale_text')}" data-sale="{{savings}}" {%- if product.compare_at_price == nil or product.compare_at_price <=  product.price -%}  style="display:none" {% endif %}>
                           ${this.lang(this.sale_text,'product_single_sale_text')}
                        </span>
                        <span class="ecom-product-single__price--badges-sold-out" {% unless target.avaiable %} style="display:none" {% endunless %}>
                            ${this.lang(this.sold_text, 'product_single_sold_text')}
                        </span>
                    </div>
                    `,
                    preview: ``
                },
                badge_pecent: {
                    code: `
                    <div class="ecom-product-single__price--badges_pecent">
                        <span class="ecom-product-single__price--badges-sale-pecent" {%- if product.compare_at_price == nil or product.compare_at_price <=  product.price -%}  style="display:none" {% endif %}>
                            {%- assign savings = product.compare_at_price | minus: product.price | times: 100.0 | divided_by: product.compare_at_price | round | append: '%' | prepend: '-' -%}
                            {{ savings }}
                        </span>
                    </div>
                    `,
                    preview: ``
                },
                ground_price: {
                    code: `
                        <small class="ecom-unit-price" {% if product.selected_or_first_available_variant.unit_price_measurement == nil %}style="display: none;" {% endif %}">
                            <span class="visually-hidden">{{ 'products.product.price.unit_price' | t }}</span>
                            <span class="price-item price-item--last">
                                <span class="ecom-ground-price_unit-price">{{- product.selected_or_first_available_variant.unit_price | money -}}</span>
                                <span aria-hidden="true">/</span>
                                <span class="visually-hidden">&nbsp;{{ 'accessibility.unit_price_separator' | t }}&nbsp;</span>
                                <span class="ecom-ground-price_unit-price-measurement">
                                {%- if product.selected_or_first_available_variant.unit_price_measurement.reference_value != 1 -%}
                                    {{- product.selected_or_first_available_variant.unit_price_measurement.reference_value -}}
                                {%- endif -%}
                                {{ product.selected_or_first_available_variant.unit_price_measurement.reference_unit }}
                                </span>
                            </span>
                        </small>
                    `,
                    preview: `
                        <div class="ecom-skeleton-item" style="max-width: 100px; width: 100%">
                            <div class="ecom-skeleton-col-12">
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-12"></div>
                                </div>
                            </div>
                        </div>
                    `
                },

                afterpay: {
                    code: `
                    ${
                        this.enable_afterpay ? `
                         <afterpay-placement class="ecom-afterpay-integrate"
                            data-show-interest-free="true"
                            ${ this.afterpay_theme !== 'none' ? `data-badge-theme="${this.afterpay_theme}"` : '' }
                            data-modal-theme="${this.modal_theme}"
                            data-locale="{{country.iso_code}}"
                            data-currency="{{shop.currency}}"
                            data-prefix=""
                            data-logo-type="${this.afterpay_logo_type}"
                            data-lockup-theme="${this.afterpay_logo_color}"
                            data-show-lower-limit="${ this.data.settings?.show_lower_limit ?? 'false' }"
                            data-show-upper-limit="${ this.data.settings?.show_upper_limit ?? 'false' }"
                            data-amount="{{product.price | divided_by: 100}}">
                        </afterpay-placement>
                    `
                    : ''
                    }
                    `,
                    preview:''
                }
            };
        },

        settings() {
            return [
                {
                    group_title: 'General',
                    params: [
                        {
                            type: 'text',
                            label: 'Badge sale text',
                            placeholder: 'Sale {price}%',
                            value: 'Sale',
                            name: 'sale_text',
                            description: 'Badge sale off percent will replace in block {price}'
                        },
                        {
                            type: 'text',
                            label: 'Badge sold out text',
                            value: 'Sold out',
                            name: 'sold_text'
                        },
                        {
                            type: 'text',
                            label: 'Tax included text',
                            value: '',
                            name: 'include_taxes'
                        },
                        {
                            type:'text',
                            label: 'Shipping policy',
                            value: "",
                            name: 'shipping_policy'
                        },
                        {
                            type: 'toggle',
                            name: 'show_groundprice',
                            label: 'Enable ground price',
                            description: 'See detailed [guide](https://help.shopify.com/en/manual/intro-to-shopify/initial-setup/sell-in-germany/price-per-unit) to enable ground price.',
                            options: {
                                values: {
                                    on: {
                                        label: 'Show',
                                        value: true
                                    },
                                    off: {
                                        label: 'Hide',
                                        value: false
                                    }
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            name: 'show_total_price',
                            label: 'Enable total price',
                            description: 'Total price when customer change quantity (product price * quantity)',
                            options: {
                                values: {
                                    on: {
                                        label: 'Show',
                                        value: true
                                    },
                                    off: {
                                        label: 'Hide',
                                        value: false
                                    }
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            name: 'show_payment_terms',
                            label: 'Show payment terms',
                            options: {
                                values: {
                                    on: {
                                        label: 'Show',
                                        value: true
                                    },
                                    off: {
                                        label: 'Hide',
                                        value: false
                                    }
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            name: 'show_badges',
                            value: true,
                            label: 'Show sale/sold out badge',
                            options: {
                                oneline: true,
                                values: {
                                    on: {
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            name: 'show_sale_pecent',
                            value: true,
                            label: 'Show sale percent badge',
                            options: {
                                oneline: true,
                                values: {
                                    on: {
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                }
                            },
                        }
                    ]
                },
                {
                    group_title: 'AfterPay settings',
                    params: [
                        {
                            type: 'paragraph',
                            content:
                                'Afterpay is fully integrated with all of your favorite stores. Shop as usual, then choose Afterpay as your payment method at checkout. **To integrate Afterpay to your theme, you can follow these [instruction](https://developers.afterpay.com/afterpay-online/docs/shopify-getting-started)**'
                        },
                        {
                            type: 'toggle',
                            name: 'enable_afterpay',
                            label: 'Show afterpay?',
                            options: {
                                values: {
                                    on: {
                                        label: 'Show',
                                        value: true
                                    },
                                    off: {
                                        label: 'Hide',
                                        value: false
                                    }
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            label: 'Show minimum price',
                            name: 'show_lower_limit',
                            description: `Determines whether to show the minimum amount e.g) $1.00 – $1,000 for products outside of the provided minimum/maximum threshold. \n
                                Learn more [here](https://developers.afterpay.com/afterpay-online/docs/advanced-configuration#data-show-lower-limit)`,
                            options: {
                                visible(data) {
                                    return data['enable_afterpay'];
                                },
                                values: {
                                    on: {
                                        label: 'Yes',
                                        value: 'true'
                                    },
                                    off: {
                                        label: 'No',
                                        value: 'false'
                                    }
                                }
                            }
                        },
                        {
                            type: 'number',
                            name: 'lower_limit',
                            label: 'Minimum price',
                            options: {
                                min: 1,
                                slider: false,
                                visible(data) {
                                    return data['enable_afterpay'];
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            label: 'Show maximum price',
                            name: 'show_upper_limit',
                            description: `Determines whether to show the lower amount e.g) $1.00 – $1,000 for products outside of the provided minimum/maximum threshold. \n
                                 Learn more [here](https://developers.afterpay.com/afterpay-online/docs/advanced-configuration#data-show-lower-limit)`,
                            options: {
                                visible(data) {
                                    return data['enable_afterpay'];
                                },
                                values: {
                                    on: {
                                        label: 'Yes',
                                        value: 'true'
                                    },
                                    off: {
                                        label: 'No',
                                        value: 'false'
                                    }
                                }
                            }
                        },
                        {
                            type: 'number',
                            name: 'upper_limit',
                            label: 'Maximum price',
                            options: {
                                min: 1,
                                slider: false,
                                visible(data) {
                                    return data['enable_afterpay'];
                                }
                            }
                        },
                        {
                            type: 'popup',
                            label: 'Logo type',
                            name: 'afterpay_logo_type',
                            description:
                                'Learn more [here](https://developers.afterpay.com/afterpay-online/docs/afterpayjs-glossary#logo-type)',
                            options: {
                                type: 'dropdown',
                                values: {
                                    badge: 'Badge',
                                    lockup: 'Lockup',
                                    'compact-badge': 'Compact Badge'
                                },
                                visible(data) {
                                    return data['enable_afterpay'];
                                }
                            }
                        },

                        {
                            type: 'popup',
                            label: 'Lockup theme',
                            name: 'afterpay_logo_color',
                            options: {
                                type: 'dropdown',
                                visible(data) {
                                    return (
                                        data['enable_afterpay'] &&
                                        data['afterpay_logo_type'] === 'lockup'
                                    );
                                },
                                values: {
                                    black: 'Black',
                                    white: 'White',
                                    mint: 'Mint'
                                }
                            }
                        },
                        {
                            type: 'popup',
                            label: 'Afterpay badge theme',
                            description:
                                'Learn more about [badge theme](https://developers.afterpay.com/afterpay-online/docs/afterpayjs-glossary#badge-theme)',
                            name: 'afterpay_theme',
                            options: {
                                type: 'dropdown',
                                visible(data) {
                                    return (
                                        data['enable_afterpay'] &&
                                        data['afterpay_logo_type'] === 'badge'
                                    );
                                },
                                values: {
                                    none: 'None',
                                    'black-on-mint': 'Black on Mint',
                                    'mint-on-black': 'Mint on Black',
                                    'white-on-black': 'White on Black'
                                }
                            }
                        },
                        {
                            type: 'popup',
                            label: 'Modal Themes',
                            name: 'modal_theme',

                            options: {
                                type: 'dropdown',
                                visible(data) {
                                    return data['enable_afterpay'];
                                },
                                values: {
                                    mint: 'Mint',
                                    white: 'White'
                                }
                            }
                        }
                    ]
                }
            ];
        },

        show_badges() {
            return this.data && this.data.settings && 'show_badges' in this.data.settings
                ? this.data.settings.show_badges
                : false;
        },

        show_sale_pecent() {
            return this.data && this.data.settings && 'show_sale_pecent' in this.data.settings
                ? this.data.settings.show_sale_pecent
                : false;
        },

        show_groundprice() {
            return this.data && this.data.settings && 'show_groundprice' in this.data.settings
                ? this.data.settings.show_groundprice
                : false;
        },

        sale_text() {
            return this.data && this.data.settings && 'sale_text' in this.data.settings
                ? this.data.settings.sale_text
                : '';
        },
        sold_text() {
            return this.data && this.data.settings && 'sold_text' in this.data.settings
                ? this.data.settings.sold_text
                : '';
        },
        enable_afterpay() {
            return this.data?.settings?.enable_afterpay ?? false;
        },
        afterpay_theme() {
            return this.data?.settings?.afterpay_theme ?? 'none';
        },
        modal_theme() {
            return this.data?.settings?.modal_theme ?? 'white';
        },
        afterpay_logo_type() {
            return this.data?.settings?.afterpay_logo_type ?? 'badge';
        },
        afterpay_logo_color() {
            return this.data?.settings?.afterpay_logo_color ?? 'black';
        },
        css() {
            return `
                    .ecom-product-single__price-container-grid {
                        display: flex;
                    }
                    .ecom-product-single__price--sale{
                        font-size: 30px;
                        color: rgb(26, 27, 24);
                        display:inline-flex;
                    }
                    .ecom-product-single__price--regular{
                        text-decoration: line-through;
                        color: #6B7280;
                        display:inline-flex;
                    }
                    .ecom-product-single__price--badges-sale{
                        display: block;
                        color:red;
                    }
                    .ecom-product-single__price--badges-sale-pecent{
                        display: block;
                    }
                    .ecom-product-single__price-container .ecom-product-single__price--prices{
                        display:inline-flex;
                        align-items:center;
                        justify-content:center;
                        gap:10px;
                    }
                    .ecom-product-single__price-container {
                        display: flex;
                        flex-direction: column;
                        gap: 5px;
                    }
                    .ecom-product-single-afterpay afterpay-placement{
                        margin:0;
                        padding: 0;
                    }
                `;
        },

        javascript() {
            return function () {
                let $el = this.$el;

                if(!$el) {
                    return;
                }

                let $afterpay_element = $el.querySelector(
                        'afterpay-placement.ecom-afterpay-integrate'
                    );

                let $wrapper = this.$el.closest('.ecom-product-form--single');
                if ($wrapper) {
                    let selector = $wrapper.querySelector('[name="id"]');
                    if (selector) {
                        selector.dispatchEvent(new window.Event('ecomUpdate'));
                    }
                    if(this.settings.show_total_price) {
                        const quantity = $wrapper.querySelector('.ecom-product-single__quantity-input');
                        const priceWrapper = $el.querySelector('.ecom-product-single__price--sale');
                        const priceRegularWrapper = $el.querySelector('.ecom-product-single__price--regular');
                        if(!priceWrapper) return;

                        if(quantity) {
                            quantity.addEventListener('change', function() {
                                priceWrapper.innerHTML = window.EComposer.formatMoney(parseInt(priceWrapper.getAttribute('data-price')) * parseInt(this.value));
                            })
                        }

                        $wrapper.addEventListener('ecomVariantChange', function(event) {
                            if(event.detail.variant) {
                                const variant = event.detail.variant;
                                const qty = quantity ? parseInt(quantity.value) : 1;
                                priceWrapper.setAttribute('data-price', variant.price);

                                if(qty > 1) {
                                    priceWrapper.innerHTML = window.EComposer.formatMoney(variant.price * parseInt(qty));
                                }
                            }
                        })
                    }

                }
                if(this.settings.enable_afterpay) {
                    let $afterPayScript = window.document.querySelector('#ecom-after-pay-script');
                    window.afterpay_min = this.settings.lower_limit || 1;
                    if (!$afterPayScript) {
                        let script = window.document.createElement('script');
                        script.id = 'ecom-after-pay-script';
                        script.dataset.analyticsEnabled = true;

                        script.dataset.min = this.settings.lower_limit || 1;
                        script.dataset.max = this.settings.upper_limit || 1000;
                        script.src = 'https://js.afterpay.com/afterpay-1.x.js';
                        window.document.head.appendChild(script);
                    }
                    else
                    {
                        $afterPayScript.dataset.analyticsEnabled = true;

                        $afterPayScript.dataset.min = this.settings.lower_limit ?? 1;
                        $afterPayScript.dataset.max = this.settings.upper_limit ?? 1000;
                    }
                    if ($afterpay_element && this.isLive && window.jQuery) {
                        if (window.Afterpay) {
                            window.Afterpay.initProductPage(window.jQuery);
                        }
                    }
                }


                let sale_text = $el.querySelector('.ecom-product-single__price--badges-sale');
                if(sale_text){
                    let percent = sale_text.dataset.sale;
                    let text = sale_text.dataset.text;
                    text = text.replace(/\{.*\}/g, percent);
                    sale_text.innerHTML = text;
                }
            };
        },
        requestShopifyType() {
            return {
                shopify_type: 'product'
            };
        },
        default() {
            return {
                "settings": {
                    "sale_text": "Sale",
                    "sold_text": "Sold out",
                    "show_badges": true,
                    "shipping_policy":"<a href=\"{{ link }}\">Shipping<\/a> calculated at checkout.",
                    "include_taxes":"Tax included.",
                    "show_badges_pecent": true,
                    "enable_afterpay": false,
                    "show_lower_limit": "true",
                    "show_upper_limit": "true",
                    "upper_limit": "600",
                    "lower_limit": "100",
                    "show_sale_pecent": false
                },
                "style": {
                    "price_general": {
                        "text-align": "left"
                    },
                    "price_regular_price": {
                        "spacing": {
                            "margin": {
                                "right": "10px"
                            }
                        }
                    },
                    "price_badge": {
                        "textColor": "#ffffff",
                        "boxBackground": "#dc2626",
                        "spacing": {
                            "padding": {
                                "left": "8px",
                                "right": "8px"
                            },
                            "margin": {
                                "top": "12px"
                            }
                        }
                    },
                    "price_out_badge": {
                        "textColor": "#ffffff",
                        "boxBackground": "#111827",
                        "spacing": {
                            "padding": {
                                "left": "8px",
                                "right": "8px"
                            }
                        }
                    },
                    "price_pecent_badge": {
                        "boxBackground": "#ed1f1f",
                        "textColor": "#fff",
                        "spacing": {
                            "margin": {
                                "left": "10px",
                                "top": "12px",
                                "right": "10px"
                            },
                            "padding": {
                                "left": "8px",
                                "right": "8px"
                            }
                        }
                    }
                }
            }

        }
    },
    methods: {
        style() {
            let style = [
                {
                    group_alias: 'box',
                    options: {
                        group_title: 'General',
                        group_name: 'price_general',
                        selector: ' .ecom-product-single__price--prices'
                    },
                    modify: {
                        params: {
                            alias: 'justify-content',
                            options: {
                                label: 'Alignment',
                                css: {
                                    selector: 'root .ecom-product-single__price-container-grid'
                                }
                            }
                        }
                    }
                },
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_title: 'Price',
                        group_name: 'price_price',
                        selector: ' .ecom-product-single__price--sale'
                    },
                    modify: {
                        remove: {
                            index: 0, length: 1
                        }
                    }
                },
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_title: 'Compare at price',
                        group_name: 'price_regular_price',
                        selector: ' .ecom-product-single__price--regular'
                    },
                    modify: {
                        remove: {
                            index: 0, length: 1
                        }
                    }
                },
                {
                    group_alias: ['text:spacing', 'box'],
                    options: {
                        group_title: 'Sale badge',
                        group_name: 'price_badge',
                        selector: ' .ecom-product-single__price--badges-sale'
                    },
                    modify: {
                        remove: [
                            {
                                index: 0, length: 1
                            },
                            {
                                index: 3, length: 1
                            },
                        ],
                        // params : [
                        //     {
                        //     position: 30,
                        //         fields: {
                        //             alias: 'spacing'
                        //         }
                        //     }
                        // ]
                    }
                },
                {
                    group_alias: ['text:spacing', 'box'],
                    options: {
                        group_title: 'Sold out badge',
                        group_name: 'price_out_badge',
                        selector: ' .ecom-product-single__price--badges-sold-out'
                    },
                    modify: {
                        remove: [
                            {
                                index: 0, length: 1
                            },
                            {
                                index: 3, length: 1
                            },
                        ],
                        // params : {
                        //     position: 30,
                        //     fields: {
                        //         alias: 'spacing'
                        //     }
                        // }
                    }
                },
                {
                    group_alias: ['text:spacing', 'box'],
                    options: {
                        group_title: 'Sale percent badge',
                        group_name: 'price_pecent_badge',
                        selector: ' .ecom-product-single__price--badges-sale-pecent'
                    },
                    modify: {
                        remove: [
                            {
                                index: 0, length: 1
                            },
                            {
                                index: 3, length: 1
                            },
                        ],
                        // params : [
                        //     {
                        //     position: 30,
                        //         fields: {
                        //             alias: 'spacing'
                        //         }
                        //     }
                        // ]
                    }
                },
            ];

            if(this.data?.settings?.show_groundprice) {
                style.push({
                    group_alias: 'text',
                    options:{
                        group_title: '',
                        group_title: 'Ground price',
                        selector: ' .ecom-product_ground-price',
                    }
                });
            }

            return style;
        }
    }
};
</script>
