<template>
    <div
        class="ecom-element ecom-product-single ecom-product-single__pickup-availability"
    >
        <div class="ecom-product-single__pickup-availability-wrapper">
            <div class="ecom-product-single__pickup-availability-container" :data-section-id="exporting ? '{{section.id}}' : ''"
                :class="[!exporting ? 'ecom-placeholder-on-builder-mode': '', !exporting? liquid('product_available') : '']"
                :data-ecom-placeholder="exporting ? '': 'This feature will show when product available on one store'" v-html="liquid('pickup_availabilities')"
            />
        </div>
    </div>
</template>

<script>
import { Liquid,Javascript, Element } from '@/mixins';
export default {
    name:'ProductPickupAvailability',
    mixins: [ Liquid,Javascript, Element],
    props: {
        data: {
            type: Object,
            default () {
                return {};
            }
        }
    },
    computed: {
        liquids() {
            return {
            product_available: {
                code: `{% unless product.available%} ecom-force-show {% endunless%}`,
                preview: ''
            },
            pickup_availabilities: {
                code: `
                    {%- unless product_variant -%}
                        {%- assign product_variant = product.selected_or_first_available_variant -%}
                    {%- endunless -%}
                    {%- if product.available-%}
                    <!--EComposer-custom-liquid-pickup-->
                    {%- capture pickup_icon -%}<span class="ecom-icon-pickup-available">${this.data.settings.icon  ? this.data.settings.icon.value : ''}</span>{%- endcapture -%}
                    {%- capture unavailable_icon -%}<span class="ecom-icon-pickup-unavailable">${this.data.settings?.unavailable_icon ? this.data.settings.unavailable_icon.value: ''}</span>{%-endcapture -%}

                    <div class="ecom-product-single__pickup-availability-items">
                            {% if product_variant.available %}
                                {%- assign pick_up_availabilities = product_variant.store_availabilities | where: 'pick_up_enabled', true -%}
                                {%- if pick_up_availabilities.size > 0 -%}
                                    <div class="ecom-product-single__pickup-availability-item" data-variant_id="{{product_variant.id}}">
                                    <div class="ecom-product-single__pickup-availability-info">
                                        {%- assign closest_location = pick_up_availabilities.first -%}
                                        {%- if closest_location.available -%}
                                            <div class="ecom-product-single__pickup-availability-content">
                                                <div class="ecom-product-single__pickup-availability--icon">
                                                    {%- liquid
                                                        if closest_location.available
                                                                echo pickup_icon
                                                        else
                                                            echo unavailable_icon
                                                        endif
                                                    -%}
                                                </div>
                                                <p class="ecom-product-single__pickup-availability-info--caption-large">
                                                    ${this.lang(this.data.settings.pick_up_available_at_html,'pick_up_available_at_html',{
                                                        location_name: 'closest_location.location.name'
                                                    })}
                                                </p>
                                                <p class="ecom-product-single__pickup-availability-info--caption">{{ closest_location.pick_up_time }}</p>
                                            </div>

                                            <div class="ecom-product-single__pickup-availability-container-button ecom-button-default">
                                                <button type="button" class="ecom-product-single__pickup-availability--button">
                                                    {%- if pick_up_availabilities.size == 1 -%}
                                                        ${this.lang(this.data.settings.view_store_info,'view_store_info')}
                                                    {%- else -%}
                                                        ${this.lang(this.data.settings.check_other_stores,'check_other_stores')}
                                                    {%- endif -%}
                                                </button>
                                            </div>

                                        {%- else -%}
                                        <div class="ecom-product-single__pickup-availability-content">
                                                <div class="ecom-product-single__pickup-availability--icon">
                                                    {%- liquid
                                                        assign closest_location = pick_up_availabilities.first
                                                        if closest_location.available
                                                                echo pickup_icon
                                                        else
                                                            echo unavailable_icon
                                                        endif
                                                    -%}
                                                </div>
                                                <p class="ecom-product-single__pickup-availability-info--caption-large">
                                                    ${this.lang(this.data.settings.pick_up_unavailable_at_html,'pick_up_unavailable_at_html',{
                                                        location_name: 'closest_location.location.name'
                                                    })}
                                                </p>
                                        </div>

                                            <div class="ecom-product-single__pickup-availability-container-button ecom-button-default">
                                                <button type="button" class="ecom-product-single__pickup-availability--button" >
                                                    {%- if pick_up_availabilities.size == 1 -%}
                                                        ${this.lang(this.data.settings.view_store_info,'view_store_info')}
                                                    {%- else -%}
                                                        ${this.lang(this.data.settings.check_other_stores,'check_other_stores')}
                                                    {%- endif -%}
                                                </button>
                                            </div>
                                        {%- endif -%}
                                    </div>
                                    <div class="ecom-product-single__pickup-availability--detail">
                                        <div class="ecom-product-single__pickup-modal__content" style="${this.styleAnimation}">
                                            <div class="ecom-product-single__pickup-close-modal" title="Close">
                                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-x" viewBox="0 0 16 16">
                                                    <path d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z"/>
                                                </svg>
                                            </div>

                                                <div class="pickup-availability-header">
                                                    <h2 class="ecom-product-single__pickup-availability--title">{{ product_variant.product.title | escape }}</h2>
                                                    {%- unless product_variant.product.has_only_default_variant -%}
                                                        <p class="ecom-product-single__pickup-availability--variant">
                                                            {%- for product_option in product_variant.product.options_with_values -%}
                                                            {{ product_option.name | escape }}:&nbsp;
                                                            {%- for value in product_option.values -%}
                                                                {%- if product_option.selected_value == value -%}
                                                                <span>{{ value | escape }}</span>
                                                                {%- endif -%}
                                                            {%- endfor -%}
                                                            {%- unless forloop.last -%},&nbsp;{%- endunless forloop.last -%}
                                                            {%- endfor -%}
                                                        </p>
                                                        {%- endunless -%}
                                                </div>
                                                <div class="ecom-product-single__pickup-availability--content">
                                                        <ul class="ecom-product-single__pickup-availability--list" role="list">
                                                            {%- for availability in pick_up_availabilities -%}
                                                                <li class="ecom-product-single__pickup-availability--list-item">
                                                                <h3 class="ecom-product-single__pickup-availability--list-item-heading">{{ availability.location.name | escape }}</h3>

                                                                <p class="ecom-product-single__pickup-availability--list-item-caption-large">
                                                                    {%- if availability.available -%}
                                                                        {{ pickup_icon }} ${this.lang(this.data.settings.pick_up_available,'pick_up_available')}, {{ availability.pick_up_time | downcase }}
                                                                    {% else %}
                                                                        {{ unavailable_icon }} ${this.lang(this.data.settings.pick_up_unavailable_at_html,'pick_up_unavailable_at_html', {
                                                                            location_name: 'availability.location.name'
                                                                        })}
                                                                    {%- endif -%}
                                                                </p>

                                                                {%- assign address = availability.location.address -%}
                                                                <address class="ecom-product-single__pickup-availability--list-item-address">
                                                                    {{ address | format_address }}
                                                                    {%- if address.phone.size > 0 -%}
                                                                    <p>{{ address.phone }}</p>
                                                                    {%- endif -%}
                                                                </address>
                                                                </li>
                                                            {%- endfor -%}
                                                            </ul>
                                                    </div>
                                        </div>

                                        </div>
                                    </div>
                                {%- endif -%}
                            {% endif %}
                    </div>
                    <!--/ECom-custom-liquid-pickup-->
                    {%- endif -%}
                `,
                preview: `
                    <div class="ecom-skeleton-item">
                        <div>
                            <div class="ecom-skeleton-row">
                                <div class="ecom-skeleton-col-4"></div>
                                <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-6"></div>
                                <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-2"></div>
                                <div class="ecom-skeleton-col-10 ecom-skeleton-empty"></div>
                            </div>
                        </div>

                        <div class="ecom-skeleton-col-12">
                            <div class="ecom-skeleton-picture"></div>
                            <div class="ecom-skeleton-row">
                                <div class="ecom-skeleton-col-10 ecom-skeleton-big"></div>
                                <div class="ecom-skeleton-col-2 ecom-skeleton-empty ecom-skeleton-big"></div>
                                <div class="ecom-skeleton-col-4"></div>
                                <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-6"></div>
                                <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-12"></div>
                            </div>
                        </div>
                    </div>
                `
            }
            };
        },

        settings() {
            return [{
                group_title: 'General',
                params: [

                    {
                        type: 'paragraph',
                        content: `**Notice**: This feature only work properly when published`
                    },
                    {
                        type: 'picker',
                        name: 'icon',
                        label: 'Location available icon',
                        options: {
                            type: 'icon',
                            oneline: true,
                            multiple: false
                        }
                    },
                    {
                        type: 'picker',
                        name: 'unavailable_icon',
                        label: 'Location unavailable icon',
                        options: {
                            oneline: true,
                            type: 'icon',
                            multiple: false
                        }
                    },
                    {
                        name: 'position',
                        label: 'Popup position',
                        type: 'choose',
                        options: {
                            oneline: true,
                            type: 'align-x',
                            values: ['left', 'right']
                        }
                    },
                    {
                        type: 'paragraph',
                        content: 'You can set up the option for customers to pick up their online orders at your retail store, curbside, or any location that you choose.[Learn more](https://help.shopify.com/en/manual/shipping/setting-up-and-managing-your-shipping/local-methods/local-pickup)'
                    }
               ]
            },{
                group_title: 'Change text',
                params: [
                    {
                        type: 'text',
                        name: 'view_store_info',
                        label: 'View store information'
                    },
                    {
                        type: 'text',
                        name: 'check_other_stores',
                        label: 'Check availability at other stores'
                    },
                    {
                        type: 'text',
                        name: 'pick_up_available',
                        label: 'Pickup available'
                    },
                    {
                        type: 'text',
                        name: 'pick_up_available_at_html',
                        label: 'Pickup available at',
                        description: 'Pickup available at <span class="ecom-color-foreground">{{ location_name }}</span>'
                    },
                    {
                        type: 'text',
                        name: 'pick_up_unavailable_at_html',
                        label: 'Pickup unavailable at',
                        description: 'Pickup unavailable at <span class="ecom-color-foreground">{{ location_name }}</span>'
                    }
                ]
            }];
        },

        css(){
            return `
                .ecom-pickup-availability-popup {
                z-index: 999 !important;
            }
                .ecom-product-single__pickup-availability--detail{
                    position:fixed;
                    top:0px;
                    left:0px;
                    bottom:0px;
                    right:0px;
                    z-index:1000;
                }
                .ecom-product-single__pickup-availability-content{
                    display:flex;
                    align-items:center;
                    gap:10px;
                    margin-bottom:10px;
                }
                .ecom-product-single__pickup-modal__content{
                    max-width:450px;
                    position:absolute;
                    width:100%;
                    top:0;
                    bottom:0;
                    padding:35px 25px;
                    transition:all linear .3s;
                    overflow:auto;
                }
                .ecom-product-single__pickup-close-modal{
                    display:flex;
                    position:absolute;
                    top:0px;
                    right:0;
                    cursor:pointer;

                    align-items: center;
                    justify-content: center;
                }
                .ecom-product-single__pickup-close-modal svg{
                    width:100%;
                    height:100%;
                }

                .ecom-product-single__pickup-availability--icon> span,
                .ecom-product-single__pickup-availability--icon{
                    display:inline-flex;
                }
                .ecom-icon-pickup-available{
                    display:inline-flex;
                }
                .ecom-icon-pickup-unavailable{
                    display:inline-flex;
                }
                .ecom-product-single__pickup-availability-item,
                .ecom-product-single__pickup-availability-container-button{
                    display:flex;
                }



                .ecom-product-single__pickup-availability--detail{
                    display:none;
                }
                .ecom-product-single__pickup-availability--detail.show-modal{
                    display:block;
                }

                @keyframes leftright {
                    from  {opacity: 0; transform:translateX(-100%)}
                    to  {opacity: 1; transform:translateX(0)}
                }
                @keyframes rightleft {
                    from  {opacity: 0; transform:translateX(100%)}
                    to  {opacity: 1; transform:translateX(0)}
                }
                .ecom-pickup-availability-popup >div{
                    display:none
                }

                .ecom-product-single__pickup-availability--list-item-caption-large {
                    display: flex;
                    align-items: center;
                    align-self: center;
                    gap:5px;
                }
            `;
        },
        styleAnimation(){
            return`
                left: ${this.data.settings.position ? (this.data.settings.position == 'left' ? '0' : 'auto')  : '0'};
                right:${this.data.settings.position ? (this.data.settings.position == 'left' ? 'auto' : '0')  : 'auto'};
                animation: linear .3s ${this.data.settings.position ? (this.data.settings.position == 'left' ? 'leftright' : 'rightleft')  : 'leftright'};
            `;
        },
        requestShopifyType() {
            return {
                shopify_type: 'product'
            };
        },
        javascript(){
            return function(){
                let $el = this.$el,isLive = this.isLive,id = this.id;
                if(!$el) return;

                let $wrapper = $el.closest('.ecom-product-form--single');
                if ($wrapper&& !isLive) {
                    let selector = $wrapper.querySelector('[name="id"]');
                    if (selector) {
                        selector.dispatchEvent(new window.Event('ecomUpdate'));
                    }
                }

                function handlePopup(){
                    let $dom = $el.querySelector('.ecom-product-single__pickup-availability--detail');
                    if($dom)
                    {
                        document.querySelectorAll('.ecom-pickup-availability-popup').forEach(function($popup){
                            $popup.remove();
                        });
                        let dom  = document.createElement('div');
                        dom.classList.add('ecom-pickup-availability-popup');
                        dom.classList.add(id);
                        dom.classList.add('ecom-core');
                        dom.innerHTML = $dom.outerHTML;
                        document.body.appendChild(dom);
                        const modalContainer = dom.querySelector('.ecom-product-single__pickup-availability--detail');
                        modalContainer.classList.add('show-modal');

                        /*=============== CLOSE MODAL ===============*/
                        const closeBtn = dom.querySelector('.ecom-product-single__pickup-close-modal');


                        closeBtn.addEventListener('click', function(){
                            modalContainer.classList.remove('show-modal');
                        });

                        window.addEventListener('click', (ev) => {
                            if (ev.target == modalContainer) {
                                modalContainer.classList.remove('show-modal');
                            }
                        });
                    }
                }
                $el.addEventListener('click', function(e){
                    if(e.target.classList.contains('ecom-product-single__pickup-availability--button') || e.target.closest('.ecom-product-single__pickup-availability--button'))
                    {
                        handlePopup();
                    }
                });
                // $el.querySelector('.ecom-product-single__pickup-availability-container') &&
                // $el.querySelector('.ecom-product-single__pickup-availability-container').addEventListener('ecomVariantChange',function(e){
                //   if(e&&e.detail && isLive)
                //     {
                //         var $target =e.target
                //         $target.innerHTML = '';
                //         console.log((window?.EComposer?.routes?.root_url??'/')+ 'variants/'+ e.detail.variant_id + '/?section_id=' + e.target.dataset.sectionId)
                //         window.fetch((window?.EComposer?.routes?.root_url??'/')+ 'variants/'+ e.detail.variant_id + '/?section_id=' + e.target.dataset.sectionId,{
                //             method: 'GET',
                //             headers: {
                //                 'Content-Type':'text/html'
                //             },

                //         })
                //         .then(response=>response.text())
                //         .then(text=>{
                //             console.log(text)
                //             if(text.indexOf('<!--EComposer-custom-liquid-pickup-->') > 0 && text.indexOf('<!--/EComposer-custom-liquid-pickup-->') > 0)
                //             {
                //                 let html = text.split('<!--EComposer-custom-liquid-pickup-->').pop().split('<!--/EComposer-custom-liquid-pickup-->')[0];

                //                 let scriptEl = document.createRange().createContextualFragment(html);

                //                     $target.append(scriptEl);

                //             }
                //         })
                //         .catch(err=>{
                //             console.warn(err.message);
                //         });
                //     }
                // });

                function updatePickupAvailability(variant) {
                    if(!$el) return;
                    let availability_item = $el.querySelector('.ecom-product-single__pickup-availability-item');
                    if(availability_item) {
                        availability_item.style.display = 'none';
                    }
                    const $container = $el.querySelector('.ecom-product-single__pickup-availability-container');
                    if ($container) {
                        $container.dispatchEvent(
                            new window.CustomEvent('ecomVariantChange', {
                                detail: {
                                    variant_id: variant?.id
                                }
                            })
                        );
                    }

                    if (variant && variant.available) {
                        const $dom = $el.querySelector(`.ecom-product-single__pickup-availability-item[data-variant_id="${variant.id}"]`);
                        if ($dom) {
                            $dom.style.display = 'flex';
                        }
                        if (!isLive) {
                            $el
                                .querySelector('.ecom-product-single__pickup-availability .ecom-placeholder-on-builder-mode')
                                .classList.add('ecom-force-show');
                        }
                    } else if (!isLive) {
                        $el.querySelector.classList('.ecom-product-single__pickup-availability .ecom-placeholder-on-builder-mode').add('ecom-force-show');
                    }
                }

                if(this.isLive) {
                    var $form_wrapper = $el.closest('.ecom-product-form--single');
                    if(!$wrapper && !$el) {
                        return;
                    }
                    var selector = $form_wrapper.querySelector('select[name=id]');

                    var product_id = $form_wrapper.dataset.product_id;

                    if(!product_id) return;

                    var product = null, json = $form_wrapper.querySelector('[id^=product-json-' + product_id + ']');

                    if (!json) {
                        return;
                    }
                    try {
                        product = JSON.parse(json.innerHTML);
                    } catch (e) {
                        return;
                    }

                    if(!selector) {
                        let variant = product.variants[0];
                        updatePickupAvailability(variant);
                    } else {
                        selector.addEventListener('change', function() {
                            let variant_id = this.value;
                            if(!variant_id) return;

                            let variant = product.variants.find( variant => variant.id == variant_id);
                            if(variant) {
                                updatePickupAvailability(variant);
                            }
                        })
                    }
                }


            };
        },

        default() {
            return {
                "settings": {
                    "view_store_info": "View store information",
                    "check_other_stores": "Check availability at other stores",
                    "pick_up_available": "Pickup available",
                    "pick_up_available_at_html": "Pickup available at <span class=\"ecom-color-foreground\">{{ location_name }}</span>",
                    "pick_up_unavailable_at_html": "Pickup currently unavailable at <span class=\"ecom-color-foreground\">{{ location_name }}</span>",
                    "icon": {
                        "name": "check",
                        "cate": "Duotone",
                        "value": "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 448 512\" fill=\"currentColor\"><!--! Font Awesome Pro 6.1.1 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2022 Fonticons, Inc. --><defs><style>.fa-secondary{opacity:.4}</style></defs><path class=\"fa-primary\" d=\"M438.6 105.4C451.1 117.9 451.1 138.1 438.6 150.6L182.6 406.6C170.1 419.1 149.9 419.1 137.4 406.6L9.372 278.6C-3.124 266.1-3.124 245.9 9.372 233.4C21.87 220.9 42.13 220.9 54.63 233.4L159.1 338.7L393.4 105.4C405.9 92.88 426.1 92.88 438.6 105.4H438.6z\"></path></svg>",
                        "thumbnail": "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 448 512\" fill=\"currentColor\"><!--! Font Awesome Pro 6.1.1 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2022 Fonticons, Inc. --><defs><style>.fa-secondary{opacity:.4}</style></defs><path class=\"fa-primary\" d=\"M438.6 105.4C451.1 117.9 451.1 138.1 438.6 150.6L182.6 406.6C170.1 419.1 149.9 419.1 137.4 406.6L9.372 278.6C-3.124 266.1-3.124 245.9 9.372 233.4C21.87 220.9 42.13 220.9 54.63 233.4L159.1 338.7L393.4 105.4C405.9 92.88 426.1 92.88 438.6 105.4H438.6z\"></path></svg>"
                    },
                    "unavailable_icon": {
                        "name": "close",
                        "value": "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 512 512\" fill=\"currentColor\"><path d=\"M464 32H48C21.5 32 0 53.5 0 80v352c0 26.5 21.5 48 48 48h416c26.5 0 48-21.5 48-48V80c0-26.5-21.5-48-48-48zm0 394c0 3.3-2.7 6-6 6H54c-3.3 0-6-2.7-6-6V86c0-3.3 2.7-6 6-6h404c3.3 0 6 2.7 6 6v340zM356.5 194.6L295.1 256l61.4 61.4c4.6 4.6 4.6 12.1 0 16.8l-22.3 22.3c-4.6 4.6-12.1 4.6-16.8 0L256 295.1l-61.4 61.4c-4.6 4.6-12.1 4.6-16.8 0l-22.3-22.3c-4.6-4.6-4.6-12.1 0-16.8l61.4-61.4-61.4-61.4c-4.6-4.6-4.6-12.1 0-16.8l22.3-22.3c4.6-4.6 12.1-4.6 16.8 0l61.4 61.4 61.4-61.4c4.6-4.6 12.1-4.6 16.8 0l22.3 22.3c4.7 4.6 4.7 12.1 0 16.8z\"></path></svg>",
                        "thumbnail": "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 512 512\" fill=\"currentColor\"><path d=\"M464 32H48C21.5 32 0 53.5 0 80v352c0 26.5 21.5 48 48 48h416c26.5 0 48-21.5 48-48V80c0-26.5-21.5-48-48-48zm0 394c0 3.3-2.7 6-6 6H54c-3.3 0-6-2.7-6-6V86c0-3.3 2.7-6 6-6h404c3.3 0 6 2.7 6 6v340zM356.5 194.6L295.1 256l61.4 61.4c4.6 4.6 4.6 12.1 0 16.8l-22.3 22.3c-4.6 4.6-12.1 4.6-16.8 0L256 295.1l-61.4 61.4c-4.6 4.6-12.1 4.6-16.8 0l-22.3-22.3c-4.6-4.6-4.6-12.1 0-16.8l61.4-61.4-61.4-61.4c-4.6-4.6-4.6-12.1 0-16.8l22.3-22.3c4.6-4.6 12.1-4.6 16.8 0l61.4 61.4 61.4-61.4c4.6-4.6 12.1-4.6 16.8 0l22.3 22.3c4.7 4.6 4.7 12.1 0 16.8z\"></path></svg>"
                    }
                },
                "style": {
                    "general": {
                        "justify-content": "flex-start"
                    },
                    "pickup_icon": {
                        "iconFontSize": "20px"
                    },
                    "pickup_button": {
                        "spacing": {
