<template>
    <div class="ecom-element ecom-shopify-elements ecom-shopify__page">
        <div class="ecom-shopify__page-wrapper">
            <div class="ecom-shopify__page-container ecom-html">
                <Liquid :data="liquids.define_page.code" />
                <div v-for="(attribute, index) in data.settings.attributes" :key="index">
                    <template v-if="attribute.type === 'title'">
                        <a
                            v-if="attribute.settings.show_as_link"
                            :href="liquid('url')"
                            :title="liquid('title')"
                            class="ecom-shopify__page--headline-link"
                        >
                            <component
                                :is="attribute.settings.tag ? attribute.settings.tag : 'h3'"
                                class="ecom-shopify__page--headline"
                                v-html="liquid('title')"
                            />
                        </a>
                        <component
                            :is="attribute.settings.tag ? attribute.settings.tag : 'h3'"
                            v-else
                            class="ecom-shopify__page--headline"
                            v-html="liquid('title')"
                        />
                    </template>
                    <template v-else-if="attribute.type === 'content'">
                        <div class="ecom-shopify__page-content" v-html="liquid('content')" />
                    </template>
                    <template v-else-if="attribute.type === 'author'">
                        <div class="ecom-shopify__page-author" v-html="liquid('author')" />
                    </template>
                    <template v-else-if="attribute.type === 'published_at'">
                        <div
                            class="ecom-shopify__page-published_at"
                            v-html="liquid('published_at')"
                        />
                    </template>
                    <template v-else-if="attribute.type === 'description'">
                        <div
                            class="ecom-shopify__page-description"
                            v-html="liquid('description')"
                        />
                    </template>
                    <template v-else-if="attribute.type === 'liquid'">
                        <div class="ecom-shopify__page-description" v-html="liquid('liquid')" />
                    </template>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Liquid, Element } from '@/mixins';
export default {
    name: 'ProductTitle',

    mixins: [Liquid, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        liquids() {
            return {
                define_page: {
                    code: `
                    {%- capture page_handle -%}${this.selected_page}{%- endcapture -%}
                    {%- if page_handle != empty -%}
                        {% assign page = pages[page_handle] %}
                    {%- endif -%}
                    `,
                    preview: ''
                },
                title: {
                    code: `
                        {%- if page != empty %}{{page.title}}{%- endif -%}
                    `,
                    preview: `The preview title`
                },
                url: {
                    code: '{{page.url}}',
                    preview: '#'
                },
                content: {
                    code: `
                        {% if page %}
                            {{ page.content }}
                        {% elsif page_handle == empty %}
                            Please select the page to show content
                        {% endif %}
                    `,
                    preview: 'The preview content'
                },
                description: {
                    code: '{{page.metafields.global.description_tag}}',
                    preview: 'The preview page description'
                },
                published_at: {
                    code: '{{ page.published_at | time_tag }}',
                    preview: ''
                },
                author: {
                    code: '{{ page.author }}',
                    preview: 'Admin'
                },
                liquid: {
                    code: `
                            ${
                                this.data.settings && this.data.settings.attributes
                                    ? this.data.settings.attributes
                                          .map((el) => {
                                              if (el.type === 'liquid') {
                                                  return el.settings.code;
                                              }
                                              return '';
                                          })
                                          .join('')
                                    : ''
                            }
                        `,
                    preview: ''
                }
            };
        },

        settings() {
            return [
                {
                    params: [
                        {
                            type: 'picker',
                            label: 'Select a page',
                            name: 'page',
                            options: {
                                type: 'page',
                                layout:'list',
                                multiple: false
                            }
                        },
                        {
                            type: 'group',
                            name: 'attributes',
                            label: 'Select attributes to show',
                            params: [
                                {
                                    type: 'title',
                                    name: 'Page title',
                                    max: 1,
                                    settings: [
                                        {
                                            type: 'paragraph',
                                            content: 'The title of selected page'
                                        },
                                        {
                                            type: 'choose',
                                            label: 'Heading Tag',
                                            name: 'tag',
                                            options: {
                                                type:'heading'
                                            }
                                        },
                                        {
                                            type: 'toggle',
                                            name: 'show_as_link',
                                            label: 'Show title as link',
                                            value: false,
                                            options: {
                                                values: {
                                                    on: {
                                                        label: 'Yes',
                                                        value: true
                                                    },
                                                    off: {
                                                        label: 'No',
                                                        value: false
                                                    }
                                                }
                                            }
                                        }
                                    ]
                                },
                                {
                                    type: 'content',
                                    name: 'Page content',
                                    max: 1,
                                    settings: [
                                        {
                                            type: 'paragraph',
                                            content: 'The content of selected page'
                                        }
                                    ]
                                },
                                {
                                    type: 'author',
                                    name: 'Page author',
                                    max: 1,
                                    settings: [
                                        {
                                            type: 'paragraph',
                                            content: 'The author of selected page'
                                        }
                                    ]
                                },
                                {
                                    type: 'published_at',
                                    name: 'Page published at',
                                    max: 1,
                                    settings: [
                                        {
                                            type: 'paragraph',
                                            content: 'The published date of selected page'
                                        }
                                    ]
                                },
                                {
                                    type: 'description',
                                    name: 'Page description',
                                    max: 1,
                                    settings: [
                                        {
                                            type: 'paragraph',
                                            content: 'The description of page'
                                        }
                                    ]
                                },
                                {
                                    type: 'liquid',
                                    name: 'Custom liquid code',
                                    max: 1,
                                    settings: [
                                        {
                                            type: 'textarea',
                                            label: 'Custom liquid code',
                                            name: 'code',
                                            placeholder: '{{page.metafields.ecomposer.description_tag}}',
                                            description: 'Learn more about [Liquid](https://shopify.dev/api/liquid/objects/page)',
                                            options: {
                                                toolbar: true
                                            }
                                        }
                                    ]
                                }
                            ]
                        }
                    ]
                }
            ];
        },

        selected_page() {
            return this.data && this.data.settings && this.data.settings.page
                ? this.data.settings.page.value
                : '';
        },
        show_page_title() {
            return this.data && this.data.settings && 'show_page_title' in this.data.settings
                ? this.data.settings.show_page_title
                : false;
        },

        css(){
            return `
                .ecom-shopify__page-container{
                    display:flex;
                    flex-direction:column
                }
                .ecom-shopify__page-container > div > *{
                    width:100%
                }
                .ecom-shopify__page--headline-link{
                    text-decoration:none;
                }
            `;
        },

        default() {
            return {
                "settings": {
                    "attributes": [
                        {
                            "type": "title",
                            "settings": {
                                "show_as_link": true,
                                "tag": "h2"
                            }
                        },
                        {
                            "type": "content"
                        },
                        {
                            "type": "author"
                        },
                        {
                            "type": "published_at"
                        },
                        {
                            "type": "description"
                        }
                    ]
                },
                "style": {
                    "page_title": {
                        "textTextAlign": "left",
                        "spacing": {
                            "margin": {
                                "bottom": "10px"
                            }
                        }
                    },
                    "page_content": {
                        "textTextAlign": "left",
                        "spacing": {
                            "margin": {
                                "bottom": "20px"
                            }
                        }
                    },
                    "page_author": {
                        "textTextAlign": "left",
                        "textColor": "#000"
                    },
                    "page_publish_at": {
                        "textTextAlign": "left"
                    },
                    "page_description": {
                        "textTextAlign": "left"
                    }
                }
            }
        }
    },
    methods: {
        check(val){
            return this.data.settings.attributes && this.data.settings.attributes.some(item => item.type == val);
        },
        style() {
            return [
                {
                    group_alias: 'box',
                    options:{
                        selector: ' .ecom-shopify__page-container',
                        group_name: 'general',
                        group_title: 'General'
                    },
                    modify:{
                        params:{
                            position:20,
                            fields:{
                                alias:'spacing'
                            }
                        }

                    }
                },
                (this.check('title')) ? {
                    group_alias:'text',
                    options:{
                        group_name: 'page_title',
                        group_title: 'Page title',
                        selector: ' .ecom-shopify__page--headline',
                    },
                    modify:{
                        params:{
                            fields:{
                                alias:'spacing'
                            },
                            position:20
                        }
                    }
                } : null,
                (this.check('content')) ? {
                    group_alias:'text',
                    options:{
                        group_name: 'page_content',
                        group_title: 'Page content',
                        selector: ' .ecom-shopify__page-content',
                    },
                    modify:{
                        params:{
                            fields:{
                                alias:'spacing'
                            },
                            position:20
                        }
                    }
                } : null,
                (this.check('author')) ? {
                    group_alias:'text',
                    options:{
                        group_name: 'page_author',
                        group_title: 'Page author',
                        selector:' .ecom-shopify__page-author'
                    },
                    modify:{
                        params:{
                            fields:{
                                alias:'spacing'
                            },
                            position:20
                        }
                    }
                } : null,
                (this.check('published_at')) ? {
                    group_alias:'text',
                    options:{
                        group_name: 'page_publish_at',
                        group_title: 'Page publish at',
                        selector:' .ecom-shopify__page-published_at'
                    },
                    modify:{
                        params:{
                            fields:{
                                alias:'spacing'
                            },
                            position:20
                        }
                    }
                } : null,
                (this.check('description')) ? {
                    group_alias:'text',
                    options:{
                        group_name: 'page_description',
                        group_title: 'Page description',
                        selector:' .ecom-shopify__page-description'
                    },
                    modify:{
                        params:{
                            fields:{
                                alias:'spacing'
                            },
                            position:20
                        }
                    }
                } : null,

            ].filter(x => x);
        }
    }
};
</script>
