<template>
    <div ref="wrapper" class="ecom__element ecom-element-table ecom-flex" :data-stopdrag="editing ? 'true' : null" data-disable-inline-editor>
      <table
        v-if="table.rows && table.columns"
      >
        <thead v-if="table.layout == 'horizontal'">
            <tr>
                <th
                  v-for="(column, index) in table.columns"
                  :key="index"
                  :data-index="'column-' + index"
                  contenteditable="true"
                  @blur="changeHeading"
                  v-html="printContent('0', index, 'heading')"
                />
            </tr>
        </thead>
        <tbody>
          <tr
            v-for="(row, row_index) in table.rows"
            :key="'row'+row_index"
            :data-row="'row-' + row_index"
          >
            <component
              :is="table.layout == 'horizontal' || column_index != 0 ? 'td' : 'th'"
              v-for="(column, column_index) in table_columns"
              :key="'col-'+column_index"
              contenteditable="true"
              :data-col="column_index"
              :data-row="row_index"
              :data-index="'column-' + row_index + column_index"
              @blur="changeContent"
              v-html="printContent(row_index, column_index, 'data')"
            />
          </tr>
        </tbody>
      </table>
    </div>
</template>

<script>
import { Element } from '@/mixins';
export default {
    name: 'Table',
    presets: true,
    docs: 'https://help.ecomposer.io/docs/elements/basic-elements/table/',
    mixins: [Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    data() {
      return {
        editing: false
      }
    },
    computed: {
        table() {
          return this.data.settings;
        },
        table_columns() {
          return this.data.settings.layout == 'horizontal' ? this.data.settings.columns : this.data.settings.columns + 1;
        },
        settings() {
            return [
                {
                    group_title:'General',
                    params: [
                      {
                          name: 'layout',
                          type: 'dropdown',
                          label: 'Layout',
                          options: {
                              default: false,
                              values: {
                                  'horizontal': 'Horizontal',
                                  'vertical': 'Vertical',
                              }
                          },
                      },
                      {
                        name: 'rows',
                        type: 'number',
                        label: 'Row',
                        options: {
                            min: 1,
                            max: 50
                        }
                      },
                      {
                        name: 'columns',
                        type: 'number',
                        label: 'Column',
                        options: {
                            min: 1,
                            max: 20
                        }
                      },
                      {
                        type: 'line'
                      },
                      {
                        type: 'popup',
                        name: 'border_collapse',
                        label: 'Border collapse',
                        options: {
                            type: 'dropdown',
                            default: false,
                            values: {
                              'collapse': 'Collapse',
                              'separate': 'Separate',
                              'inherit': 'Inherit'
                            }
                        },
                        css: {
                            properties: {
                                'border-collapse': ''
                            },
                            selector: ' .ecom-element-table table',
                        }
                      },
                      {
                        type: 'number',
                        name: 'border_spacing',
                        label: 'Border spacing',
                        options: {
                            visible: (data) => {
                                return data['border_collapse'] == 'separate' || data['border_collapse'] == 'inherit';
                            },
                            units: {
                                px: {
                                    min: 0,
                                    max: 30
                                },
                            }
                        },
                        css: {
                            properties: {
                                'border-spacing': ''
                            },
                            selector: ' .ecom-element-table table',
                        }
                      },
                      {
                        name: 'table_data',
                        type: 'textarea',
                        options: {
                          visible: (data) => {
                              return false;
                          }
                        },
                      }
                    ]
                }
            ];
        },

        css() {
            return `
              .ecom-element-table {
                overflow-x: auto;
              }
              .ecom-element-table td {
                font-weight: inherit;
              }
              .ecom-element-table table {
                overflow: hidden;
              }
              .ecom-element-table td, .ecom-element-table tr, .ecom-element-table th {
                white-space: pre;
                overflow: hidden;
                text-overflow: ellipsis;
              }
            `;
        },
        default(){
          return {
              "settings": {
                "table_data": {
                  "column-00": "Content",
                  "column-01": "Content",
                  "column-02": "Content",
                  "column-03": "Content",
                  "column-04": "Content",
                  "column-05": "Content"
                },
                "table_heading": {
                  "column-0": "Heading 1",
                  "column-1": "HEADING 2",
                  "column-2": "HEADING 3",
                  "column-3": "HEADING 4",
                  "column-4": "HEADING 5",
                  "column-5": "HEADING 6"
                },
                "rows": 6,
                "columns": 6,
                "layout": "horizontal"
              },
              "style": {
                "heading": {
                  "background-color": "#0691b1",
                  "textColor": "#fff",
                  "border": {
                    "border-style": "none"
                  },
                  "textTypography": {
                    "font-weight": "400",
                    "text-transform": "uppercase"
                  },
                  "textTextAlign": "center",
                  "col_vertical": "middle",
                  "th_height": "50px"
                },
                "gerenal": {
                  "boxBorder": {
                    "border-style": "solid",
                    "border-width": {
                      "bottom": "0.8px",
                      "left": "0.8px",
                      "right": "0.8px",
                      "top": "0.8px"
                    },
                    "border-color": "#0691b1"
                  },
                  "spacing": {
                    "margin": {
                      "top": "30px",
                      "left": "0px",
                      "bottom": "30px",
                      "right": "0px"
                    }
                  },
                  "boxShadow": {
                    "box-shadow": {
                      "color": "#ffffff",
                      "vertical": "0px"
                    }
                  }
                },
                "table_row": {
                  "row_odd_bg": "rgba(255, 255, 255, 0.16)",
                  "background-color": "rgba(6, 145, 177, 0.16)",
                  "border": {
                    "border-style": "none"
                  },
                  "row_height": "50px",
                  "color": "#000000",
                  "row_odd_color": "#0a0a0a"
                },
                "table_col": {
                  "border": {
                    "border-style": "dotted",
                    "border-width": {
                      "top": "0.8px",
                      "left": "0.8px",
                      "bottom": "0.8px",
                      "right": "0.8px"
                    },
                    "border-color": "rgba(15, 105, 125, 0.16)"
                  },
                  "textTextAlign": "center",
                  "col_vertical": "middle"
                }
              }
            }
        }
    },
    watch: {

    },
    methods: {
        changeContent(e) {
          this.editing = true;
          let content = e.target.innerText ? e.target.innerText : '',
              col = e.target.dataset.col,
              row = e.target.dataset.row;

          if(col && row) {
            if(this.table?.layout == 'vertical') {
              if(col == 0) {
                let index = 'column-' + row;
                this.table.table_heading[index] = content;
              } else {
                col -= 1;
                let index = 'column-' + row + col;
                this.table.table_data[index] = content;
              }
            } else {
              let index = 'column-' + row + col;
              this.table.table_data[index] = content;
            }

          }
        },
        changeHeading(e) {
          this.editing = true;
          let content = e.target.innerText,
              index = e.target.dataset.index;
          index = index < 10 ? '0' + index : index;

          if(content && index) {
            if(this.table.table_heading) {
              this.table.table_heading[index] = content;
            }else {
              this.table.table_heading = Object;
              this.table.table_heading[index] = content;
            }

          }
        },
        printContent(row, col, type) {
          if( type == 'data') {
            if(this.table?.layout == 'vertical') {
              if(col == 0) {
                let index = 'column-' + row;
                return this.table?.table_heading[index] ? this.table.table_heading[index] : '';
              } else {
                col -= 1;
                let index = 'column-' + row + col;
                return this.table?.table_data[index] ? this.table.table_data[index] : '';
              }
            } else {
              let index = 'column-' + row + col;
              return this.table?.table_data[index] ? this.table.table_data[index] : '';
            }
          } else {
            let index = 'column-' + col;
            return this.table?.table_heading[index] ? this.table.table_heading[index] : '';
          }
        },
        style() {
          return [
            {
                group_alias: 'box',
                options: {
                    group_title: 'Gerenal',
                    group_name: 'gerenal',
                    selector: ' .ecom-element-table table'
                },
                modify: {
                  params: [
                    {
                      position: 0,
                      fields: [
                        {
                            name: 'Table Width',
                            label: 'Width',
                            type: 'number',
                            options: {
                                responsive: true,
                                units: {
                                    px: {
                                        min: 0,
                                        max: 1600
                                    },
                                    '%': {
                                        min: 0,
                                        max: 100
                                    }
                                }
                            },
                            css:{
                                properties:{
                                    'width': ''
                                }
                            }
                        },
                        {
                          alias: 'justify-content',
                          options: {
                            label: 'Alignment',
                            css: {
                                selector: 'root .ecom-element-table'
                            }
                          }
                        }
                      ]
                    },
                    {
                      position: 6,
                      fields: [
                        {
                          alias: 'spacing'
                        }
                      ]
                    }
                  ]
                }
            },
            {
                group_alias:'text',
                options: {
                    group_title: 'Heading',
                    group_name: 'heading',
                    selector: ' .ecom-element-table th'
                },
                modify: {
                  params: [
                    {
                      position: 1,
                      fields: [
                        {
                          type: 'choose',
                          label: 'Vertical align',
                          name: 'col_vertical',
                          options: {
                              oneline: true,
                              responsive: true,
                              type: 'align-y-full',
                              values: ['top', 'middle', 'bottom']
                          },
                          css: {
                              properties: {
                                  'vertical-align':''
                              },
                          }
                      },
                      ]
                    },
                    {
                      position: 4,
                      fields: [
                        {
                          alias: 'background-color'
                        },
                        {
                          alias: 'border'
                        },
                        {
                            name: 'th_min_width',
                            label: 'Width',
                            type: 'number',
                            options: {
                                responsive: true,
                                units: {
                                    px: {
                                        min: 0,
                                        max: 1000
                                    },
                                }
                            },
                            css:{
                                properties:{
                                    'min-width': ''
                                }
                            }
                        },
                        {
                            name: 'th_height',
                            label: 'Min height',
                            type: 'number',
                            options: {
                                responsive: true,
                                units: {
                                    px: {
                                        min: 0,
                                        max: 300
                                    },
                                }
                            },
                            css:{
                                properties:{
                                    'height': ''
                                }
                            }
                        },
                        {
                          alias: 'padding'
                        }
                      ]
                    }
                  ]
                }
            },
            {
                group_title: 'Table row',
                group_name: 'table_row',
                selector: ' .ecom-element-table tbody tr',
                params: [
                  {
                    alias: 'border'
                  },
                  {
                      name: 'row_height',
                      label: 'Min height',
                      type: 'number',
                      options: {
                          responsive: true,
                          units: {
                              px: {
                                  min: 0,
                                  max: 300
                              },
                          }
                      },
                      css:{
                          properties:{
                              'height': ''
                          }
                      }
                  },
                  {
                    type: 'color',
                    label: 'Odd Row background',
                    name: 'row_odd_bg',
                    selector: 'root .ecom-element-table tbody tr:nth-child(odd)',
                    options: {
                      global: {
                          type: 'colors'
                      },
                      oneline: true
                    },
                    css: {
                        properties: {
                            'background-color': ''
                        },
                    }
                  },
                  {
                    type: 'color',
                    label: 'Odd Row color',
                    name: 'row_odd_color',
                    selector: 'root .ecom-element-table tbody tr:nth-child(odd)',
                    options: {
                      global: {
                          type: 'colors'
                      },
                      oneline: true
                    },
                    css: {
                        properties: {
                            'color': ''
                        },
                    }
                  },
                  {
                      type: 'popup',
                      label: 'Odd Row Typography',
                      name: 'row_odd_typography',
                      options: {
                          global: {
                              type: 'typography'
                          },
                          oneline: true,
                          type: 'typography'
                      },
                      css: {
                          selector: 'root .ecom-element-table tbody tr:nth-child(odd)'
                      }
                  },
                  {
                    type: 'line'
                  },
                  {
                    alias: 'background-color',
                    group_name: 'row_even_bg',
                    options: {
                      label: 'Even Row background',
                      css: {
                          selector: 'root .ecom-element-table tbody tr:nth-child(even)'
                      }
                    }
                  },
                  {
                    alias: 'color',
                    group_name: 'row_even_color',
                    options: {
                      label: 'Even Row Color',
                      css: {
                          selector: 'root .ecom-element-table tbody tr:nth-child(even)'
                      }
                    }
                  },
                  {
                      type: 'popup',
                      label: 'Even Row Typography',
                      name: 'row_even_typography',
                      options: {
                          global: {
                              type: 'typography'
                          },
                          oneline: true,
                          type: 'typography'
                      },
                      css: {
                          selector: 'root .ecom-element-table tbody tr:nth-child(even)'
                      }
                  },
                ]
            },
            {
                group_alias:'text',
                options: {
                    group_title: 'Table col',
                    group_name: 'table_col',
                    selector: ' .ecom-element-table tbody td'
                },
                modify: {
                  params: [
                    {
                      position: 1,
                      fields: [
                        {
                          type: 'choose',
                          label: 'Vertical align',
                          name: 'col_vertical',
                          options: {
                              oneline: true,
                              responsive: true,
                              type: 'align-y-full',
                              values: ['top', 'middle', 'bottom']
                          },
                          css: {
                              properties: {
                                  'vertical-align':''
                              },
                          }
                      },
                      ]
                    },
                    {
                      position: 4,
                      fields: [
                        {
                          alias: 'background-color'
                        },
                        {
                          alias: 'border'
                        },
                        {
                          alias: 'padding'
                        }
                      ]
                    }
                  ]
                }
            }
          ]
        }

    }
};
</script>
