<template>
    <div class="ecom-element ecom-product-single ecom-product-single__selling-plan">
        <div class="ecom-product-single__selling-plan-wrapper">
            <div class="ecom-product-single__selling-plan-container" :class="!exporting ? 'ecom-placeholder-on-builder-mode': ''" :data-ecom-placeholder="exporting? '': 'This product don\'t have selling plans'"
                v-html="liquid('selling_plans')"
            />
        </div>
    </div>
</template>

<script>
import { Liquid, Element,Javascript } from '@/mixins';
export default {
    name: 'ProductSellingPlan',
    presets:true,
    mixins: [Liquid, Element, Javascript],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {


        css() {
            return `
                .ecom-product-single__selling-plan-fieldset{
                    display: block;
                    margin-inline-start: 2px;
                    margin-inline-end: 2px;
                    padding-block-start: 0.35em;
                    padding-inline-start: 0.75em;
                    padding-inline-end: 0.75em;
                    padding-block-end: 0.625em;
                    min-inline-size: min-content;
                    border-width: 2px;
                    border-style: groove;
                    border-color: threedface;
                    border-image: initial;
                }
                .ec-purchase-options{
                    margin-left:15px;
                }
                .ecom-product-single__selling-plan-fieldset--input {
                    display: block
                }
                .ecom-product-single__selling-plan-fieldset--input input[type="radio"]{
                        -webkit-appearance: none;
                        -moz-appearance: none;
                        appearance: none;
                        padding: 0;
                        -webkit-print-color-adjust: exact;
                        color-adjust: exact;
                        display: inline-block;
                        vertical-align: middle;
                        background-origin: border-box;
                        -webkit-user-select: none;
                        -moz-user-select: none;
                        -ms-user-select: none;
                        user-select: none;
                        flex-shrink: 0;
                        position:relative;
                    }
                .ecom-product-single__selling-plan-fieldset--input input[type="radio"]:checked::after{
                        position:absolute;
                        content:'';
                        left:50%;
                        top:50%;
                        transform:translate(-50%,-50%)
                    }
            `;
        },

        heading() {
            return this.data && this.data.settings && this.data.settings.tag
                ? this.data.settings.tag
                : 'h3';
        },

        liquids() {
            return {
                'selling_plans': {
                    code: `
                        {%- liquid
                            assign current_variant = product.selected_or_first_available_variant | default: product.variants.first
                            assign current_selling_plan_allocation = current_variant.selected_selling_plan_allocation
                            if current_selling_plan_allocation == nil and current_variant.requires_selling_plan
                            assign current_selling_plan_allocation = current_variant.selling_plan_allocations | first
                            endif
                            assign offer = current_selling_plan_allocation | default: current_variant
                        -%}
                         {% comment %}
                            {{ offer.price | money }}
                            {% if offer.compare_at_price > offer.price %}
                            <s>{{ offer.compare_at_price | money }}</s>
                            <span>{% if offer.selling_plan %}Subscription{% else %}Sale{% endif %}</span>
                        {% endif %}
                        {% endcomment %}
                        <fieldset class="ecom-product-single__selling-plan-fieldset">
                            <legend class="ecom-product-single__selling-plan-fieldset-header">${this.lang(this.data.settings?.title??'','selling_plan_title')}</legend>
                            <input type="hidden" name="selling_plan" value="{{ current_selling_plan_allocation.selling_plan.id | default: '' }}">
                            {% unless product.requires_selling_plan %}
                                <label class="ecom-product-single__selling-plan-fieldset--input">
                                    <input type="radio" name="purchase_option" value="onetime"> 
                                    ${this.lang(this.data.settings?.one_time_text,'one_time_text')}
                                </label>
                            {% endunless %}
                            {% for group in product.selling_plan_groups %}
                                <div  class="ecom-product-single__selling-plan-fieldset--input">
                                    <label class="ecom-product-single__selling-plan-fieldset-frequency-header" for="purchase_option">
                                    <input type="radio"  {% if product.requires_selling_plan %} required {% endif %} name="purchase_option" value="{{group.id}}">  
                                        {%- capture ec_text -%}${this.lang(this.data.settings?.subscription_text,'subscription_text')}{% endcapture%}
                                        {%- if ec_text != "" -%}{{- ec_text -}}{%- else -%}{{ group.name}}{%- endif -%}
                                    </label>
                                </div>
                                <div class="ec-purchase-options">
                                    {% for option in group.options %}
                                        <label class="ec-selling-plan-frequency-label">
                                            {%- capture ec_text -%}${this.lang(this.data.settings?.frequency_header,'frequency_header')}{% endcapture%}
                                            {%- if ec_text != "" -%}{{- ec_text -}}{%- else -%}{{ option.name  }}{%- endif -%}
                                        </label>
                                        <select class="ecom-product-single__selling-plan-select" name="group-option-{{group.id}}" data-group-id="{{group.id}}"  data-position="{{ option.position }}">
                                        {% for value in option.values %}
                                            <option value="{{value}}">{{ value }}</option>
                                        {% endfor %}
                                        </select>
                                    {% endfor %}
                                </div>
                               
                            {% endfor %}
                                
                        </fieldset>
                    `,
                    preview: ''
                }
            };
        },
        javascript(){
            return function(){
                let $el = this.$el;
                var sellingPlanContainer = $el.querySelector('.ecom-product-single__selling-plan-fieldset');
                var productForm =$el.closest('form.ecom-product-form--single');
                if(!productForm || !sellingPlanContainer)
                {
                    return ;
                }
                let selector = productForm.querySelector('[name="id"]');
                if (!selector) { return; }
                let json = productForm.querySelector('#' + selector.dataset.jsonProduct);
                if (!json) { return; }
                let product = null;
                try {
                    product = JSON.parse(json.innerHTML);
                } catch (e) {
                    console.warn(e.message);
                    return;
                }

                var findSelectedVariant = function() {
                    var selectedVariantId = parseInt(selector.value);
                    var selectedVariant;

                    for (var i = 0; i < product.variants.length; i++) {
                        if (product.variants[i].id === selectedVariantId) {
                        selectedVariant = product.variants[i];
                        break;
                        }
                    }

                    return selectedVariant;
                };

                // Watch for variant selection to update selling plan option selectors
                productForm.addEventListener('change', function() {
                var selectedVariant = findSelectedVariant();
                var availableSellingPlanAllocations = selectedVariant.selling_plan_allocations;
                // Update the selling plan option selectors based on the available selling plan allocations
                // for the selected variant
                });

                // Watch for selling plan option change to update the selected selling plan
                sellingPlanContainer.querySelectorAll('select').forEach(function(optionSelector) {
                optionSelector.addEventListener('change', function(e) {
                    var selectedVariant = findSelectedVariant();
                    var availableSellingPlanAllocations = selectedVariant.selling_plan_allocations;
                    var selectedSellingPlanId;
                    for (var i = 0; i < availableSellingPlanAllocations.length; i++) {
                    var sellingPlan = availableSellingPlanAllocations[i].selling_plan;
                    var matchingSellingPlan = e.target.value === sellingPlan.name;

                    // Logic to find matching selling plan

                        if (matchingSellingPlan) {
                            selectedSellingPlanId = sellingPlan.id;
                        }
                    }

                    selectedSellingPlanId = (typeof selectedSellingPlanId === 'undefined') ? '' : selectedSellingPlanId;
                    productForm.querySelector('[name="selling_plan"]').value = selectedSellingPlanId;
                    let id = e.target.dataset.groupId;
                    productForm.querySelector('[name="purchase_option"][value="'+id+'"]').checked = true;

                });
                });
                sellingPlanContainer.querySelectorAll('[name="purchase_option"]').forEach(function(optionSelector) {
                    optionSelector.addEventListener('change', function(e) {
                        if(e.target.value == 'onetime')
                        {
                            productForm.querySelector('[name="selling_plan"]').value = '';
                        }
                        else 
                        {
                            let defaultOption = productForm.querySelector('[name="group-option-'+e.target.value+'"]');
                            if(defaultOption)
                            {
                                defaultOption.dispatchEvent(new Event('change'))
                            }
                            
                        }
                    });
                });

            };
        },
        requestShopifyType() {
            return {
                shopify_type: 'product'
            };
        },

        settings() {
            return [{
                params: [
                

                    {
                        type: 'text',
                        name: 'title',
                        label: 'Title',
                        options: {
                            placeholder: 'Purchase options'
                        }
                    },
                    {
                        type: 'text',
                        name: 'one_time_text',
                        label: 'One time title',
                        options: {
                            placeholder: 'One-time purchase'
                        }
                    },
                    {
                        type: 'text',
                        name: 'subscription_text',
                        label: 'Subscription Text',
                        options: {
                            placeholder: 'Subscribe and Save'
                        }
                    },
                    {
                        type: 'text',
                        name: 'frequency_header',
                        label: 'Frequency Header',
                        options: {
                            placeholder: 'Delivery Frequency'
                        }
                    },

                    {
                        type: 'paragraph',
                        content: 'To see information about your subscriptions, you must be using a [subscription app from the Shopify App Store](https://apps.shopify.com/collections/checkout-subscription).'
                    },
                ]
            }];
        },

        style() {
            return [
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_title: 'Header text',
                        group_name: 'title',
                        selector: ' .ecom-product-single__selling-plan-fieldset-header'
                    },
                    modify: {
                        params: {
                            position: 4,
                            fields: {
                                alias: 'border',
                                options: {
                                    css: {
                                        selector: 'root .ecom-product-single__selling-plan-fieldset'
                                    }
                                }
                            }
                        }
                    }
                },
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_title: 'Frequency header',
                        group_name: 'frequency_header',
                        selector: ' .ecom-product-single__selling-plan-fieldset-frequency-header'
                    },
                
                },
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_title: 'Frequency text',
                        group_name: 'frequency_text',
                        selector: ' .ec-selling-plan-frequency-label'
                    },
                   
                },
                {
                    group_title: 'Radio',
                    group_name: 'radio',
                    selector: ' .ecom-product-single__selling-plan-fieldset--input input[type="radio"]',
                    params: [
                          { 
                                    alias: 'spacing',
                                    options: {
                                        name: 'spacingLabel'
                                    }
                                },
                                {
                                        type: 'tab',
                                        name: 'tab',
                                        value: 'normal',
                                        options: {
                                            tabs: [
                                                {
                                                    name: 'normal',
                                                    title: 'Normal'
                                                },
                                                {
                                                    name: 'hover',
                                                    title: 'Hover'
                                                },
                                                {
                                                    name: 'active',
                                                    title: 'Active'
                                                }
                                            ]
                                        },
                                        css: false
                                    },
                                     { 
                                        alias: 'background', 
                                        options: {
                                            css: {
                                            },
                                            options: {
                                                visible: data => data['tab'] === 'normal'
                                            }
                                        }
                                    },
                                    { 
                                        alias: 'background', 
                                        options: {
                                            name: 'backgroundHover',
                                            css: {
                                                selector:':hover',
                                                properties: {
                                                    background: ''
                                                },
                                            },
                                            options: {
                                                visible: data => data['tab'] === 'hover'
                                            }
                                        }
                                    },
                                    { 
                                        alias: 'background', 
                                        options: {
                                            name: 'backgroundActive',
                                            css: {
                                                properties: {
                                                    background: ''
                                                },
                                                selector:':checked',
                                                important: true,
                                            },
                                            options: {
                                                visible: data => data['tab'] === 'active'
                                            }
                                        }
                                    },
                                    {
                                        type: 'popup',
                                        label: 'Border',
                                        name: 'popup',
                                        options: {
                                            type: 'border',
                                            visible: data => data['tab'] === 'normal'
                                        },
                                        css: {
                                        }
                                    },
                                    {
                                        type: 'popup',
                                        label: 'Border',
                                        name: 'popupHover',
                                        options: {
                                            type: 'border',
                                            visible: data => data['tab'] === 'hover'
                                        },
                                        css: {
                                            selector:':hover',
                                            properties: {
                                                border: ''
                                            },
                                        }
                                    },
                                    {
                                        type: 'popup',
                                        label: 'Border',
                                        name: 'popupActive',
                                        options: {
                                            type: 'border',
                                            visible: data => data['tab'] === 'active'
                                        },
                                        css: {
                                            properties: {
                                                border: ''
                                            },
                                            selector:':checked',
                                            important: true,
                                        }
                                    },
                                    {
                                        type: 'dimension',
                                        label: 'Border radius',
                                        name: 'border-radius',
                                        options: {
                                            units: 'default',
                                            type: 'radius',
                                            visible: data => data['tab'] === 'normal'
                                        },
                                        css: {
                                            important:true,
                                        }
                                    },
                                    {
                                        type: 'dimension',
                                        label: 'Border radius',
                                        name: 'border-radiusHover',
                                        options: {
                                            units: 'default',
                                            type: 'radius',
                                            visible: data => data['tab'] === 'hover'
                                        },
                                        css: {
                                            important:true,
                                            selector:':hover',
                                            properties: {
                                                'border-radius': ''
                                            },
                                        }
                                    },
                                    {
                                        type: 'dimension',
                                        label: 'Border radius',
                                        name: 'border-radiusActive',
                                        options: {
                                            units: 'default',
                                            type: 'radius',
                                            visible: data => data['tab'] === 'active'
                                        },
                                        css: {
                                            selector:':checked',
                                            properties: {
                                                'border-radius': ''
                                            },
                                            important: true,
                                        }
                                    },
                                    {
                                        name: "transition",
                                        type: "number",
                                        label: 'Transition Duration <span class="lowercase">(ms)</span>',
                                        options: {
                                            min: 0,
                                            max: 5000,
                                            visible: {
                                                keep_data: true,
                                                condition: (data) => {
                                                    return data["tab"] === "hover";
                                                }
                                            }
                                        },
                                        css: {
                                            properties: {
                                                transition: "all %value%ms ease"
                                            }
                                        }
                                    },
                                    {
                                        type: 'line'
                                    },
                                    { alias: 'spacing', options: {css: { }}}
                             
                        ],
                    
                }
                
            ];
        },

        default() {
            return  {
                "settings": {
                    "title": "Purchase options",
                    "one_time_text": "One-time purchase"
                },
                "style": {
                    "radio": {
                        "tab": "hover",
                        "popup": {
                            "border-style": "solid",
                            "border-width": {
                                "top": "2px",
                                "left": "2px",
                                "bottom": "2px",
                                "right": "2px"
                            },
                            "border-color": "#8c8c8c"
                        },
                        "border-radius": {
                            "right": "50%",
                            "top": "50%",
                            "left": "50%",
                            "bottom": "50%"
                        },
                        "spacing": {
                            "margin": {
                                "right": "5px",
                                "top": "5px",
                                "left": "5px",
                                "bottom": "5px"
                            },
                            "padding": {
                                "top": "6px",
                                "left": "6px",
                                "bottom": "6px",
                                "right": "6px"
                            }
                        },
                        "popupHover": {
                            "border-style": "solid",
                            "border-width": {
                                "right": "2px",
                                "top": "2px",
                                "left": "2px",
                                "bottom": "2px"
                            },
                            "border-color": "#0e7490"
                        },
                        "backgroundActive": {
                            "classic": {
                                "background-color": "#0e7490"
                            }
                        },
                        "transition": 300
                    },
                    "title": {
                        "textTextAlign": "left"
                    },
                    "Radio": {
                        "textTextAlign": "left"
                    }
                }
            }
        }
    },
};
</script>
