                                        properties: {
                                            'color': ''
                                        },
                                        selector: 'root  .el__location-name'
                                    }
                                },
                            },
                            {
                                position: 10,
                                fields: {alias: 'spacing'}
                            }
                        ]
                    }
                },
                {
                    group_alias:'text',
                    options: {
                        group_name: 'loaction_address',
                        group_title: 'Address',
                        selector: ' .el__location-address'
                    },
                    modify: {
                        remove: {
                            index: 2, length: 1
                        },
                        params: [
                            {
                                position: 3,
                                fields: {
                                    name: 'textColor',
                                    label: 'Text Color',
                                    type: 'color',
                                    options: {
                                        oneline: true,
                                        global: {
                                            type: 'colors'
                                        }
                                    },
                                    css: {
                                        properties: {
                                            'color': ''
                                        },
                                        selector: 'root  .el__location-address'
                                    }
                                },
                            },
                            {
                                position: 10,
                                fields: {alias: 'spacing'}
                            }
                        ]
                    }
                },
                {
                    group_alias:'text',
                    options: {
                        group_name: 'loaction_detail',
                        group_title: 'Details',
                        selector: ' .el__location-detail'
                    },
                    modify: {
                        remove: {
                            index: 2, length: 1
                        },
                        params: [
                            {
                                position: 3,
                                fields: {
                                    name: 'textColor',
                                    label: 'Text Color',
                                    type: 'color',
                                    options: {
                                        oneline: true,
                                        global: {
                                            type: 'colors'
                                        }
                                    },
                                    css: {
                                        properties: {
                                            'color': ''
                                        },
                                        selector: 'root  .el__location-detail'
                                    }
                                },
                            },
                            {
                                position: 10,
                                fields: {alias: 'spacing'}
                            }
                        ]
                    }
                },
                {

                    group_name: 'box_item',
                    group_title: 'Location box',
                    selector: 'root .element__address-location-item',

                    params: [
                        {
                            alias: 'spacing'
                        },
                        {
                            alias: 'border'
                        }
                    ]
                },
            ];
        },
        javascript(){
            return function(){
                if(!this.$el) return false;
                const $el = this.$el;
                let api_key = this.settings.api_key || null;
                let zoom = this.settings.zoom || 15;
                let script = document.createElement('script');
                let itemLocations = $el.querySelectorAll('.element__address-location-item');
                let map = $el.querySelector(".ecom__element-location");
                let element = $el.querySelector('.element__address-location-item.active');
                let address = "";
                if(element){
                    address = element.dataset.location;
                }
                else{
                    address = 'Ha Noi';
                }
                let imageMarker = null;
                let animation = null;
                if(!element) return;
                element.dataset.image ? imageMarker = element.dataset.image : null;
                element.dataset.animation ? animation = element.dataset.animation : null;
                
                itemLocations.forEach(function(item, index){
                    item.addEventListener('click', function(){
                        itemLocations.forEach(function(item){
                            item.classList.remove('active');
                        });
                        let animation = item.dataset.animation
                        item.classList.add('active');
                        window.initialize = initialize(item.dataset.location,item.dataset.image,animation);
                        map.scrollIntoView();
                    });
                });
                function initialize(address, image_marker, animation){
                    let geocoder = new google.maps.Geocoder();
                    
                    geocoder.geocode( {'address' : address}, function(results, status) {
                        if (status == google.maps.GeocoderStatus.OK) {
                            let latitude = results[0].geometry.location.lat();
                            let longitude = results[0].geometry.location.lng();
                            let latlng = new google.maps.LatLng(latitude, longitude);
                            let mapOptions = {
                                zoom: zoom,
                                center: latlng
                            }
    
                            const map = new google.maps.Map($el.querySelector(".ecom__element-location"), mapOptions);
                            //IMG
                            let icon = null;
                            if(image_marker){
                                 icon = {
                                    url: image_marker, // url
                                    scaledSize: new google.maps.Size(50, 50), // scaled size
                                };
                            }
                            const marker = new google.maps.Marker({
                                position: results[0].geometry.location,
                                map: map,
                                icon: icon
                            });
                            if(animation=='true'){
                                marker.setAnimation(google.maps.Animation.BOUNCE);
                            }
                            let infowindow = new google.maps.InfoWindow({
                                content:address,
                                maxWidth: 200,
                            });
                            marker.setMap(map);
                            marker.addListener('click', ()=>{
                                infowindow.open({
                                anchor: marker,
                                map,
                            });
                            })
                            map.addListener('zoom_changed', ()=>{
                                map.setCenter(latlng);
                                map.panTo(latlng);
                            });
                        }
                    });
                }
                setTimeout(()=>window.initialize = initialize(address,imageMarker, animation),1000);
                
                if(api_key){
                    script.src = `https://maps.googleapis.com/maps/api/js?key=${api_key}&callback=initialize`;
                    script.id = 'ecom__location-map--api_key';
                    const _script = document.getElementById('ecom__location-map--api_key');
                    if(_script){
                        document.head.removeChild(_script);
                    }
                    document.head.appendChild(script);
                }
                
            }
        },
        css() {
            return `
                .ecom__element-location-wrapper{
                    display:flex;
                }
                .element__address-location{
                    height: auto;
                }
                .ecom__element-location{
