<template>
    <div
      class="ecom-element ecom-collection ecom-collection__filter-values"
      v-html="liquid('filter_values')"
    >
    </div>
</template>

<script>
import { Liquid, Element } from '@/mixins';
export default {
    name: 'CollectionFilterValue',
    docs: 'https://help.ecomposer.io/docs/elements/collection-elements/applied-filters/',
    presets:true,
    mixins: [Liquid, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        liquids() {
            return {
                'filter_values': {
                    code: `
                    <!--EComposer-custom-liquid-filters-${this.data.id.split('-').pop()}-->
                      {% capture close_icon %}
                      ${this.data.settings?.general?.icon ?
                        `<span class="ecom-colletion-filter-value--close-icon ecom-flex">
                            ${this.data.settings?.general?.icon}
                          </span>
                        `
                        : ``
                      }
                       {% endcapture %}

                      <ul class="ecom-collection-filter-values-list" role="list">
                        {%- if EComBuilderMode -%}
                          {% for filter in collection.filters %}
                              {% if filter.type == 'list' %}
                                  {% for value in filter.values  %}
                                      <li class="ecom-collection-filter-values-list__item ecom-al_center">
                                          <a
                                            href="{{value.url_to_remove}}" title="{{value.label}}"
                                            class="ecom-w__full ecom-flex ecom-al_center"
                                          >
                                              {{ value.label | escape }} {{close_icon}}
                                          </a>
                                      </li>
                                  {% endfor %}
                                  {% break %}
                              {% endif %}
                          {% endfor %}
                        {%- else -%}
                          {%- for filter in collection.filters -%}
                              {%- if filter.active_values.size > 0 -%}
                                  {% for value in filter.active_values  %}
                                      <li class="ecom-collection-filter-values-list__item ecom-al_center">
                                          <a
                                            href="{{value.url_to_remove}}" title="{{value.label}}"
                                            class="ecom-w__full ecom-flex ecom-al_center"
                                          >
                                              {{ value.label | escape }} {{close_icon}}
                                          </a>
                                      </li>
                                  {% endfor %}
                              {% endif %}
                          {% endfor %}
                        {%- endif -%}
                      </ul>
                    <!--/EComposer-custom-liquid-filters-${this.data.id.split('-').pop()}-->
                    `,
                    preview:
                      `<div class="ecom-skeleton-item">
                          <div>
                              <div class="ecom-skeleton-row">
                                  <div class="ecom-skeleton-col-4"></div>
                                  <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                  <div class="ecom-skeleton-col-6"></div>
                                  <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                  <div class="ecom-skeleton-col-2"></div>
                                  <div class="ecom-skeleton-col-10 ecom-skeleton-empty"></div>
                              </div>
                          </div>
                      </div>`
                }
            };
        },

        settings() {
            return [
              {
                group_name: 'general',
                group_title: 'General',
                params: [
                    {
                        type: 'popup',
                        label: 'Layout',
                        values: '',
                        name: 'layout_type',
                        options: {
                            type: 'dropdown',
                            default: false,
                            icon_type: 'alignment',
                            preview: 'title',
                            values: {
                                'inline-flex': 'Horizontal',
                                'block': 'Vertical'
                            }
                        },
                        css:{
                            selector:' .ecom-collection-filter-values-list',
                            properties:{
                                'display':''
                            }
                        }
                    },
                    {
                      type: 'picker',
                      label: 'Icon',
                      name: 'icon',
                      options: {
                          type: 'icon',
                          layout: 'grid',
                          output: 'value',
                          multiple: false,
                          simple: false
                      }
                    },
                ]
              }
            ];
        },
        requestShopifyType() {
            return {
                shopify_type: 'collection'
            };
        },

        default() {
          return {
            "settings": {
              "general": {
                "layout_type": "inline-flex",
                "icon": "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 320 512\" fill=\"currentColor\"><!--! Font Awesome Pro 6.1.1 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2022 Fonticons, Inc. --><defs><style>.fa-secondary{opacity:.4}</style></defs><path class=\"fa-secondary\" d=\"M310.6 361.4c12.5 12.5 12.5 32.75 0 45.25C304.4 412.9 296.2 416 288 416s-16.38-3.125-22.62-9.375L160 301.3L54.63 406.6C48.38 412.9 40.19 416 32 416S15.63 412.9 9.375 406.6c-12.5-12.5-12.5-32.75 0-45.25l105.4-105.4L9.375 150.6c-12.5-12.5-12.5-32.75 0-45.25s32.75-12.5 45.25 0L160 210.8l105.4-105.4c12.5-12.5 32.75-12.5 45.25 0s12.5 32.75 0 45.25l-105.4 105.4L310.6 361.4z\"></path></svg>"
              }
            },
            "style": {
              "filter_value": {
                "tab": "hover",
                "buttonColornormalmode": "#737070",
                "buttonTypography": {
                  "text-transform": "uppercase",
                  "text-decoration": "none",
                  "font-size": "14px"
                },
                "spacing": {
                  "margin": {
                    "right": "20px"
                  }
                },
                "buttonBackgroundnormalmode": {
                  "classic": {
                    "background-color": "#cfcccc"
                  }
                },
                "margin_item": {
                  "top": "0px",
                  "left": "0px",
                  "bottom": "12.5px",
                  "right": "12.5px"
                },
                "padding": {
                  "right": "5px",
                  "top": "5px",
                  "left": "5px",
                  "bottom": "5px"
                }
              },
              "filter_icon": {
                "iconFontSize": "12px",
                "iconPrimaryColor": "#858383",
                "spacing": {
                  "margin": {
                    "right": "0px",
                    "left": "6px"
                  }
                }
              }
            }
          }
        },
        css() {
          return `
            .ecom-collection-filter-values-list,
            .ecom-collection-filter-values-list li {
              list-style: none;
            }
            .ecom-collection-filter-values-list {
              flex-wrap: wrap;
            }
          `;
        }
    },
    methods: {
        style() {
            return [
              {
                group_alias: 'box',
                options: {
                    group_title: 'Gerenal',
                    group_name: 'gerenal',
                    selector: ' .ecom-collection-filter-values-list'
                },
              },
              {
                group_alias: 'button',
                options: {
                    group_title: 'Filter value',
                    group_name: 'filter_value',
                    selector: ' .ecom-collection-filter-values-list__item a'
                },
                modify: {
                  remove: {
                      index: 18, length: 1
                  },
                  params: [
                    {
                      position: 19,
                      fields: [
                        {
                          alias: 'padding'
                        },
                        {
                          alias: 'margin',
                          options: {
                            name: 'margin_item',
                            css: {
                              selector: 'root .ecom-collection-filter-values-list__item'
                            }
                          }
                        }
                      ]
                    }
                  ]
                }
              },
              {
                group_alias: 'icon',
                options: {
                    group_title: 'Icon',
                    group_name: 'filter_icon',
                    selector: 'root .ecom-colletion-filter-value--close-icon',
                },
                modify:{
                    params:[
                        {
                            position:15,
                            fields:[
                                {
                                    type:'line'
                                },
                                {
                                    alias:'spacing'
                                }
                            ]
                        }
                    ]
                  }
              }
            ];
        },
    }
};
</script>
