<template>
    <div class="ecom__element ecom-element element__divi" @click="test">
        <div class="ecom__element-divi">
            <div class="divi-line divi-style" :style="styleDivider" />
            <div class="divi-cont" :style="styleDivider2">
                <div
                    v-if="divi.icon || divi.text"
                    class="divi-cont-before divi-style"
                    :style="diviLeft"
                />
                <span v-if="divi.icon && divi.type == 'icon'" v-html="divi.icon" />
                <span v-if="divi.text && divi.type == 'text'" class="divi-text">
                    {{ lang(divi.text) }}
                </span>
                <div
                    v-if="divi.icon || divi.text"
                    class="divi-cont-after divi-style"
                    :style="diviRight"
                />
            </div>
        </div>
    </div>
</template>

<script>
import { Element } from '@/mixins';
export default {
    name: 'Divider',
    docs: 'https://help.ecomposer.io/docs/elements/basic-elements/divider/',
    mixins: [Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        settings() {
            return [
                {
                    group_title: 'Divider',
                    params: [
                        {
                            type: 'popup',
                            label: 'Style',
                            name: 'borderStyle',
                            options: {
                                type: 'dropdown',
                                default:false,
                                values: {
                                    solid: 'Solid',
                                    dotted: 'Dotted',
                                    dashed: 'Dashed',
                                    double: 'Double'
                                }
                            },
                            css: {
                                selector: ' div.divi-style',
                                properties: {
                                    'border-style': ''
                                }
                            }
                        },
                        {
                            type: 'popup',
                            label: 'Select type',
                            name: 'type',
                            options: {
                                type: 'dropdown',
                                default:false,
                                values: { none: 'None', text: 'Text', icon: 'Icon' }
                            },
                            css: {
                                isCss: false
                            }
                        },
                        
                        {
                            type: 'picker',
                            label: 'Icon',
                            name: 'icon',
                            options: {
                                type: 'icon',
                                layout: 'grid',
                                output: 'value',
                                multiple: false,
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data['type'] == 'icon';
                                    }
                                }
                            }
                        },
                        
                        {
                            name: 'position',
                            label: 'Icon position',
                            type: 'choose',
                            options: {
                                type: 'vertical-align',
                                values: ['left', 'center', 'right'],
                                oneline: true,
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data['type'] && data['type'] !== 'none';
                                    }
                                }
                            }
                        },
                        {
                            alias:'justify-content',
                            options:{
                                name: 'justifyContent',
                                label: 'Alignment',
                                css:{
                                    selector: ' .ecom__element-divi, .divi-cont',
                                }
                            }
                        },
                        {
                            type: 'text',
                            label: 'Text',
                            name: 'text',
                            options: {
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data['type'] == 'text';
                                    }
                                }
                            }
                        },
                        {
                            name: 'gridColumnGap',
                            label: 'Spacing',
                            type: 'number',
                            options: {
                                units: {
                                    px: {
                                        min: 0,
                                        max: 50,
                                        step: 1
                                    }
                                },
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data['type'] && data['type'] !== 'none';
                                    }
                                }
                            },
                            css: {
                                selector: ' .divi-cont',
                                properties: {
                                    'grid-column-gap': ''
                                }
                            }
                        },
                        {
                            type: 'number',
                            label: 'Width',
                            name: 'width',
                            options: {
                                responsive: true,
                                units: {
                                    '%': {
                                        min: 0,
                                        max: 100,
                                        step: 1
                                    },
                                    px: {
                                        min: 0,
                                        max: 1000,
                                        step: 1
                                    }
                                }
                            },
                            css: {
                                selector: ' .divi-line.divi-style',
                                properties: {
                                    width: ''
                                }
                            }
                        },
                    ]
                }
            ];
        },
        divi() {
            return this.data.settings;
        },
        unit() {
            return this.divi.width ? this.divi.width.replace(/[0-9]/g, '') : 'px';
        },
        styleDivider() {
            return {
                // width: this.divi.width,
                display:
                    (this.divi.icon && this.divi.type == 'icon') ||
                    (this.divi.text && this.divi.type == 'text')
                        ? 'none'
                        : 'block'
            };
        },
        styleDivider2() {
            return {
                '--divider-width': this.divi.width,
                '--divider-width-tablet': this.divi?.width__tablet ?? this.divi.width,
                '--divider-width-mobile': this.divi?.width__mobile ?? this.divi.width__tablet ?? this.divi.width__mobile,
                display:
                    (this.divi.icon && this.divi.type == 'icon') ||
                    (this.divi.text && this.divi.type == 'text')
                        ? 'flex'
                        : 'none'
            };
        },
        diviLeft() {
            return {
                display: this.divi.position == 'left' ? 'none' : 'block'
            };
        },
        diviRight() {
            return {
                display: this.divi.position == 'right' ? 'none' : 'block'
            };
        },
        css() {
            return `
                .element__divi{
                    padding:10px 0;
                    overflow:hidden;
                }
                .ecom__element-divi{
                    display:flex;
                }
                .divi-line{
                    margin:10px 0;
                    border:0;
                    border-bottom: 1px solid #D1D5DB;
                }
                .divi-cont{
                    position:relative;
                    z-index:1;
                    display:flex;
                    align-items:center;
                    transition: all .3s;
                    color: #818a91;
                    width:100%;
                }
                .divi-cont span{
                    display:flex;
                    white-space:nowrap;
                }
                .divi-cont-before{
                    display:block;
                    border:0;
                }
                .divi-cont-after{
                    display:block;
                    border:0;
                }
                .divi-style{
                    padding:0 !important;
                    border-bottom: 2px solid #D1D5DB;
                }
                .divi-cont>*{
                    margin:0;
                }
                .divi-cont svg{
                    width:36px;
                    height:auto;
                }
                .divi-cont-before.divi-style, .divi-cont-after.divi-style {
                    width: calc(var(--divider-width) / 2);
                }
                @media(max-width: 1024px) {
                    .divi-cont-before.divi-style, .divi-cont-after.divi-style {
                        width: calc(var(--divider-width-tablet) / 2);
                    }
                }
                @media(max-width: 767px) {
                    .divi-cont-before.divi-style, .divi-cont-after.divi-style {
                        width: calc(var(--divider-width-mobile) / 2);
                    }
                }
            `;
        },

        default() {
            return {
                settings: {
                        add_element: 'text',
                        gridColumnGap: '7px',
                        width: '100%',
                        text: '+',
                        tag: 'h2',
                        position: 'center',
                        icon: "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 496 512\" fill=\"currentColor\"><!-- Font Awesome Free 5.15.3 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license/free (Icons: CC BY 4.0, Fonts: SIL OFL 1.1, Code: MIT License) --><path d=\"M248 167.5l64.9 98.8H183.1l64.9-98.8zM496 256c0 136.9-111.1 248-248 248S0 392.9 0 256 111.1 8 248 8s248 111.1 248 248zm-99.8 82.7L248 115.5 99.8 338.7h30.4l33.6-51.7h168.6l33.6 51.7h30.2z\"></path></svg>",
                        justifyContent:'center'
                    },
                style:{
                    divider:{
                        borderBottomWidth:'1px'
                    }
                }
            }
        }
    },
    methods: {
        style() {
            let style = [
                {
                    group_name: 'divider',
                    group_title: 'Divider',
                    selector: ' .ecom__element-divi .divi-style',
                    params: [
                        {
                            name: 'borderColor',
                            label: 'Color',
                            type: 'color',
                            options: {
                                global: {
                                    type: 'colors'
                                },
                                oneline: true
                            },
                            css: {
                                properties: {
                                    'border-color': ''
                                }
                            }
                        },
                        {
                            name: 'borderBottomWidth',
                            label: 'Size',
                            type: 'number',
                            options: {
                                units: {
                                    px: {
                                        min: 0,
                                        max: 100,
                                        step: 0.1
                                    }
                                }
                            },
                            css: {
                                properties: {
                                    'border-bottom-width': ''
                                }
                            }
                        }
                    ]
                },
                {
                    group_name: 'dividerGap',
                    group_title: 'Gap',
                    selector: ' .ecom__element-divi',
                    params: [
                        {
                            name: 'dividerPadding',
                            type: 'dimension',
                            label: 'Gap',
                            options: {
                                simple: true,
                                responsive: true,
                                units: 'default'
                            },
                            css: {
                                properties: {
                                    padding: ''
                                }
                            }
                        }
                    ]
                }
            ];
            if (this.divi.icon && this.divi.type == 'icon') {
                style.unshift({
                    group_name: 'divider_icon',
                    group_title: 'Icon',
                    selector: ' .ecom__element-divi .divi-cont svg',
                    params: [
                        {
                            type: 'number',
                            label: 'Size',
                            name: 'dividerIconWidth',
                            options: {
                                units: {
                                    px: {
                                        min: 6,
                                        max: 300
                                    }
                                }
                            },
                            css: {
                                properties: {
                                    width: ''
                                }
                            }
                        },
                        {
                            name: 'dividerIconFill',
                            label: 'Color',
                            type: 'color',
                            options: {
                                global: {
                                    type: 'colors'
                                },
                                oneline: true
                            },
                            css: {
                                properties: {
                                    color: ''
                                }
                            }
                        },
                        {
                            name: 'dividerIconTransform',
                            label: 'Rotate',
                            type: 'number',
                            options: {
                                min: 0,
                                max: 360,
                                step: 1
                            },
                            css: {
                                properties: {
                                    transform: 'rotate(%value%deg)'
                                }
                            }
                        }
                    ]
                });
            }
            if (this.divi.text && this.divi.type == 'text') {
                style.unshift({
                    group_name: 'divider_text',
                    group_title: 'Text',
                    selector: ' .ecom__element-divi .divi-cont .divi-text',
                    params: [
                        {
                            type: 'color',
                            label: 'Color',
                            name: 'color',
                            options: {
                                global: {
                                    type: 'colors'
                                },
                                oneline: true
                            },
                            css: {
                                properties: {
                                    color: ''
                                }
                            }
                        },
                        {
                            name: 'dividerTypography',
                            label: 'Typography',
                            type: 'popup',
                            options: {
                                oneline: true,
                                global: {
                                    type: 'typography'
                                },
                                type: 'typography'
                            },
                            css: {
                                properties: {
                                    typography: ''
                                }
                            }
                        }
                    ]
                });
            }

            return style;
        }
    }
};
</script>
