<template>
    <div
        ref="wrapper"
        class="ecom__element element__heading ecom-element ecom-html"
        :data-stopdrag="true"
    >
        <Liquid :data="getMetaLiquid" />
        <component
            :is="data?.settings?.tag ?? 'h2'"
            v-if="data.settings?.link && data.settings?.link.href"
            class="ecom__heading"
        >
            <a
                v-bind="data.settings?.link"
                ref="content"
                :contenteditable="isFullScreen ? null : true"
                spellcheck="false"
                @click="edit"
                @keyup="update"
                @blur="editing=false"
                v-html="editing ? editingValue : exporting 
                ? 
                ( data.settings.enable_tooltip 
                    ? 
                    (data.settings.position_icon == 'before' ? tooltip + lang(this.convertText, 'heading-text', metaLang) : lang(this.convertText, 'heading-text', metaLang) + tooltip)
                    :
                    lang(this.convertText, 'heading-text', metaLang)

                ) 
                : content"
            />
        </component>
        <component
            :is="data?.settings?.tag ?? 'h2'"
            v-else
            ref="content"
            class="ecom__heading"
            :contenteditable="isFullScreen ? null : true"
            spellcheck="false"
            @click="edit"
            @keyup="update"
            @blur="editing=false"
            v-html="editing ? editingValue : exporting 
            ? 
            ( data.settings.enable_tooltip 
                ? 
                (data.settings.position_icon == 'before' ? tooltip + lang(this.convertText, 'heading-text', metaLang) : lang(this.convertText, 'heading-text', metaLang) + tooltip)
                :
                lang(this.convertText, 'heading-text', metaLang)

            ) 
            : content"
        />
    </div>
</template>

<script lang="ts">
    import { Element, Javascript, Liquid } from '@/mixins';
    export default {
        name: 'Heading',
        presets:true,
        docs: 'https://help.ecomposer.io/docs/elements/basic-elements/heading/',
        mixins: [Element, Javascript, Liquid],
        props: {
            data: {
                type: Object,
                default () {
                    return {};
                }
            }
        },
        data () {
            return {
                editing: false,
                is: 'heading',
                editingValue: '',
                metaLang: {},
                metaLiquid: ''
            };
        },
        computed: {
            tooltip(){
                let icon_tooltip = this.data.settings?.icon_tooltip,
                    content_tooltip = this.data.settings?.content_tooltip,
                    enable_arrow = this.data.settings?.enable_arrow,
                    position_tooltip = this.data.settings?.position_tooltip,
                    position_tooltip__tablet = this.data.settings?.position_tooltip__tablet,
                    position_tooltip__mobile = this.data.settings?.position_tooltip__mobile
                    ;
                return `<div class="el__heading--tooltip-wrapper">
                                        ${icon_tooltip.value}
                                        <div class="el__heading--tooltip-content ${enable_arrow==true ? 'tooltip__arrow' : ''} el__tooltip-desktop el__heading--tooltip-${position_tooltip ? position_tooltip : 'bottom'}">
                                            ${content_tooltip}
                                        </div>
                                        <div class="el__heading--tooltip-content ${enable_arrow==true ? 'tooltip__arrow' : ''} el__tooltip-tablet el__heading--tooltip-${position_tooltip__tablet ? position_tooltip__tablet : 'bottom'}">
                                            ${content_tooltip}
                                        </div>
                                        <div class="el__heading--tooltip-content ${enable_arrow==true ? 'tooltip__arrow' : ''} el__tooltip-mobile el__heading--tooltip-${position_tooltip__mobile ? position_tooltip__mobile : 'bottom'}">
                                            ${content_tooltip}
                                        </div>
                                    </div>`
            },
            content(){
                let contentHtml = this.data.settings?.content;
                let enable_tooltip = this.data.settings?.enable_tooltip,
                    icon_tooltip = this.data.settings?.icon_tooltip,
                    content_tooltip = this.data.settings?.content_tooltip,
                    position_tooltip = this.data.settings?.position_tooltip,
                    position_tooltip__tablet = this.data.settings?.position_tooltip__tablet,
                    position_tooltip__mobile = this.data.settings?.position_tooltip__mobile,
                    enable_arrow = this.data.settings?.enable_arrow,
                    position_icon = this.data.settings?.position_icon;
                if(enable_tooltip==true && icon_tooltip && content_tooltip){
                    if(position_icon == 'before'){
                        contentHtml = `<div class="el__heading--tooltip-wrapper">
                                        ${icon_tooltip.value}
                                        <div class="el__heading--tooltip-content ${enable_arrow==true ? 'tooltip__arrow' : ''} el__tooltip-desktop el__heading--tooltip-${position_tooltip ? position_tooltip : 'bottom'}">
                                            ${content_tooltip}
                                        </div>
                                        <div class="el__heading--tooltip-content ${enable_arrow==true ? 'tooltip__arrow' : ''} el__tooltip-tablet el__heading--tooltip-${position_tooltip__tablet ? position_tooltip__tablet : 'bottom'}">
                                            ${content_tooltip}
                                        </div>
                                        <div class="el__heading--tooltip-content ${enable_arrow==true ? 'tooltip__arrow' : ''} el__tooltip-mobile el__heading--tooltip-${position_tooltip__mobile ? position_tooltip__mobile : 'bottom'}">
                                            ${content_tooltip}
                                        </div>
                                    </div>` + contentHtml;
                    }
                    else{
                        contentHtml += `<div class="el__heading--tooltip-wrapper">
                                        ${icon_tooltip.value}
                                        <div class="el__heading--tooltip-content ${enable_arrow==true ? 'tooltip__arrow' : ''} el__tooltip-desktop el__heading--tooltip-${position_tooltip ? position_tooltip : 'bottom'}">
                                            ${content_tooltip}
                                        </div>
                                        <div class="el__heading--tooltip-content ${enable_arrow==true ? 'tooltip__arrow' : ''} el__tooltip-tablet el__heading--tooltip-${position_tooltip__tablet ? position_tooltip__tablet : 'bottom'}">
                                            ${content_tooltip}
                                        </div>
                                        <div class="el__heading--tooltip-content ${enable_arrow==true ? 'tooltip__arrow' : ''} el__tooltip-mobile el__heading--tooltip-${position_tooltip__mobile ? position_tooltip__mobile : 'bottom'}">
                                            ${content_tooltip}
                                        </div>
                                    </div>`
                    }
                }
                return contentHtml;
            },
            settings () {
                return [
                    {
                        params: [
                            {
                                type: 'textarea',
                                label: 'Heading Text',
                                name: 'content',
                                options: {
                                    toolbar: 'short',
                                    dynamic: true,
                                    height: 80
                                }
                            },

                            {
                                type: 'link',
                                label: 'Insert Link',
                                name: 'link'
                            },
                            {
                                type: 'choose',
                                label: '<span class="uppercase">HTML</span> Tag',
                                name: 'tag',
                                options: {
                                    type: 'heading'
                                }
                            },
                            {
                                type: 'switch',
                                name: 'enable_tooltip',
                                label: 'Use Tooltip',
                                value: false,
                                options: {
                                    values: {
                                        on: {
                                            label: 'Yes',
                                            value: true
                                        },
                                        off: {
                                            label: 'No',
                                            value: false
                                        }
                                    }
                                }
                            },
                            {
                                type: 'textarea',
                                name: 'content_tooltip',
                                label: 'Tooltip content',
                                options: {
                                    visible: (data) => {
                                            return data['enable_tooltip'] === true;
                                    },
                                    toolbar: 'short',
                                    dynamic: true,
                                    height: 80

                                }
                            },
                            {
                                type: 'switch',
                                name: 'enable_arrow',
                                label: 'Use Arrow',
                                value: false,
                                options: {
                                    values: {
                                        on: {
                                            label: 'Yes',
                                            value: true
                                        },
                                        off: {
                                            label: 'No',
                                            value: false
                                        }
                                    },
                                    visible: (data) => {
                                            return data['enable_tooltip'] === true;
                                    }
                                }
                            },
                            {
                                type: 'picker',
                                label: 'Tooltip Icon',
                                name: 'icon_tooltip',
                                options: {
                                    type: 'icon',
                                    multiple: false,
                                    oneline: true,
                                    visible: (data) => {
                                            return data['enable_tooltip'] === true;
                                    }
                                }
                            },
                            {
                                type: 'popup',
                                name: 'position_icon',
                                label: 'Icon Position',
                                options: {
                                    visible: (data) => {
                                            return data['enable_tooltip'] === true;
                                    },
                                    type: 'dropdown',
                                    default: false,
                                    preview: 'title',
                                    values: {
                                        before: 'Before the heading',
                                        after: 'After the heading',
                                    }
                                }
                            },
                            {
                                type: 'popup',
                                name: 'position_tooltip',
                                label: 'Tooltip Position',
                                options: {
                                    visible: (data) => {
                                            return data['enable_tooltip'] === true;
                                    },
                                    type: 'dropdown',
                                    default: false,
                                    preview: 'title',
                                    responsive: true,
                                    values: {
                                        top: 'Top',
                                        bottom: 'Bottom',
                                        left: 'Left',
                                        right: 'Right'
                                    }
                                }
                            }
                        ]
                    }
                ];
            },

            style () {
                let style = [
                    {
                        group_alias:'text',
                        options: {
                            selector: ' .element__heading .ecom__heading'
                        },
                        modify: {
                            remove: {
                                index: 2, length: 1
                            },
                            params: [
                                {
                                    position: 3,
                                    fields: {
                                    name: 'textColor',
                                    label: 'Text Color',
                                    type: 'color',
                                    options: {
                                        oneline: true,
                                        global: {
                                            type: 'colors'
                                        }
                                    },
                                    css: {
                                        properties: {
                                            'color': ''
                                        },
                                        selector: 'root  .element__heading .ecom__heading,root  .element__heading .ecom__heading a'
                                    }
                                },
                                },
                                {
                                    position: 5,
                                    fields: {alias: 'border'}
                                },
                                {
                                    position: 10,
                                    fields: {alias: 'spacing'}
                                }
                            ]
                        }
                    },
                ];
                let styleTooltip = [
                {
                        group_alias:'text',
                        options: {
                            group_title: 'Tooltip',
                            group_name: 'tooltip',
                            selector: ' .el__heading--tooltip-content'
                        },
                        modify: {
                            remove:
                                {
                                    index: [0,3], length: 1
                                },
                            params: [
                                {
                                    position: 3,
                                    fields: {
                                        name: 'background_color',
                                        label: 'Background Color',
                                        type: 'color',
                                        options: {
                                            oneline: true,
                                            global: {
                                                type: 'colors'
                                            }
                                        },
                                        css: {
                                            properties: {
                                                'background-color': ''
                                            },
                                            selector: 'root  .el__heading--tooltip-content'
                                        }
                                    },
                                },
                                {
                                    position: 8,
                                    fields: {
                                        type: 'dimension',
                                        name: 'borderRadius',
                                        label: 'Border radius',
                                        options: {
                                            type: 'radius',
                                            units: 'default',
                                        },
                                        css:{
                                            properties:{
                                                'border-radius':''
                                            }
                                        }
                                    }
                                },
                                {
                                    position: 10,
                                    fields: {alias: 'spacing'}
                                }
                            ]
                        }
                    },
                    {
                        group_alias:'icon',
                        options: {
                            group_title: 'Tooltip Icon',
                            group_name: 'icon_tooltip',
                            selector: ' .el__heading--tooltip-wrapper'
                        },
                        modify: {
                            
                        }
                    }
                ];
                if(this.data.settings?.enable_tooltip){
                    style = style.concat(styleTooltip);
                }
                return style;
            },

            css(){
                return `
                    
                    .el__heading--tooltip-wrapper{
                        position: relative;
                        display: inline-block;
                    }
                    .el__heading--tooltip-wrapper svg{
                        width: 18px;
                        cursor: pointer;
                    }
                    .el__heading--tooltip-content{
                        max-width: 200px;
                        position: absolute;
                        display: block;
                        width: max-content;
                        opacity: 0;
                        padding: 5px;
                        visibility: hidden;
                        font-size: 14px;
                        background-color: #545454;
                        color: #ffffff;
                        border-radius: 5px;
                        transition: .5s ease all;
                        z-index: 999;
                    }
                    .el__heading--tooltip-content.el__tooltip-tablet, .el__heading--tooltip-content.el__tooltip-mobile {
                        display: none;
                    }
                    .el__heading--tooltip-bottom{
                        left: 50%;
                        transform: translate(-50%, 10px);
                    }
                    
                    .el__heading--tooltip-left{
                        transform: translate(-20px, -50%);
                        right: 100%;
                        top: 50%;
                    }
                    .el__heading--tooltip-top{
                        left: 50%;
                        bottom: 100%;
                        transform: translate(-50%, -10px);
                    }
                    .el__heading--tooltip-right {
                        top: 50%;
                        left: 100%;
                        transform: translate(20px, -50%);
                    }
                    .tooltip__arrow:before{
                        position: absolute;
                        content: '';
                        width: 8px;
                        height: 8px;
                        background: inherit;
                        z-index: -1;
                    }
                    .tooltip__arrow.el__heading--tooltip-bottom:before{
                        top: 0;
                        transform: translate(-50%, -50%) rotate(45deg);
                        left: 50%;
                    }
                    .tooltip__arrow.el__heading--tooltip-top:before{
                        transform: translate(-50%,50%) rotate(45deg);
                        bottom: 0%;
                        left: 50%;
                    }
                    .tooltip__arrow.el__heading--tooltip-right:before{
                        transform: translate(-50%,50%) rotate(45deg);
                        bottom: 50%;
                        left: 0%;
                    }
                    .tooltip__arrow.el__heading--tooltip-left:before{
                        transform: translate(50%,50%) rotate(45deg);
                        bottom: 50%;
                        right: 0%;
                    }
                    .el__heading--tooltip-wrapper svg:hover~.el__heading--tooltip-bottom, .el__heading--tooltip-wrapper svg:hover~.el__heading--tooltip-top {
                        opacity: 1;
                        visibility: visible;
                        transform: translate(-50%, 0);
                    }
                    .el__heading--tooltip-wrapper svg:hover~.el__heading--tooltip-left {
                        opacity: 1;
                        visibility: visible;
                        transform: translate(-15px, -50%);
                    }
                    .el__heading--tooltip-wrapper svg:hover~.el__heading--tooltip-right {
                        opacity: 1;
                        visibility: visible;
                        transform: translate(15px, -50%);
                    }
                    @media screen and (max-width: 1024px) {
                        .el__heading--tooltip-content.el__tooltip-desktop {
                            display: none;
                        }
                        .el__heading--tooltip-content.el__tooltip-tablet {
                            display: block;
                        }
                    }
                    @media screen and (max-width: 768px) {
                        .el__heading--tooltip-content.el__tooltip-tablet {
                            display: none;
                        }
                        .el__heading--tooltip-content.el__tooltip-mobile {
                            display: block;
                        }
                    }
                `;
            },

            heading () {
                return this.data;
            },

            activeMe () {
                return (this.editingElement?.data?.id ?? 0) === this.heading.id;
            },
            getMetaLiquid() {
                this.convertText;
                return this.metaLiquid;
            },
            convertText() {
                const _this = this;
                let text = this.data.settings?.content;
                if(text) {
                    const dom = document.createElement('div');
                    dom.innerHTML = text;
                    const meta_nodes = dom.querySelectorAll('span[data-meta]');
                    if(meta_nodes) {
                    meta_nodes.forEach(el => {
                        let meta_key = el.dataset.metaKey.replaceAll(' ', '_'),
                        metafiled = el.dataset.meta,
                        meta = document.createTextNode(`{{${meta_key}}}`);;
                        if(metafiled.indexOf('|') == -1) {
                            _this.metaLang[meta_key] = metafiled
                        } else {
                            _this.metaLiquid += `{%- assign ${meta_key} = ${metafiled}-%}`
                            _this.metaLang[meta_key] = meta_key
                        }

                        el.replaceWith(meta);
                    })
                    }
                    return dom.innerHTML;
                }
            },
            default() {
                return  {
                    "settings": {
                        "enable_tooltip": false,
                        "content": "Enter your heading",
                        "tag": "h3",
                        "icon_width": "30px",
                        "content_tooltip": "Tooltip",
                        "icon_tooltip": {
                            "id": "qSmBN5zG",
                            "name": "circle-question",
                            "cate": "Duotone",
                            "url": "https://local.ecomposer.app/storage/icons/font-awesome/duotone/circle-question.svg",
                            "value": "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 512 512\" fill=\"currentColor\"><!--! Font Awesome Pro 6.1.1 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2022 Fonticons, Inc. --><defs><style>.fa-secondary{opacity:.4}</style></defs><path class=\"fa-primary\" d=\"M256 336c-18 0-32 14-32 32c0 18 13.1 32 32 32c17.1 0 32-13.1 32-32C288 350 273.1 336 256 336zM289.1 128h-51.1C198.1 128 168 158.1 168 198c0 12.1 11 24 24 24s24-11 24-24C216 186 225.1 176 237.1 176h51.1C301.1 176 312 186 312 198c0 8-4 14.1-11 18.1L244 251C236 256 232 264 232 272V288c0 13 11 24 24 24S280 301 280 288V286l45.1-28c21-13 34-36 34-60C360 158.1 328.1 128 289.1 128z\"></path><path class=\"fa-secondary\" d=\"M256 0C114.6 0 0 114.6 0 256s114.6 256 256 256s256-114.6 256-256S397.4 0 256 0zM256 400c-18 0-32-14-32-32s13.1-32 32-32c17.1 0 32 14 32 32S273.1 400 256 400zM325.1 258L280 286V288c0 13-11 24-24 24S232 301 232 288V272c0-8 4-16 12-21l57-34C308 213 312 206 312 198C312 186 301.1 176 289.1 176h-51.1C225.1 176 216 186 216 198c0 13-11 24-24 24s-24-11-24-24C168 159 199 128 237.1 128h51.1C329 128 360 159 360 198C360 222 347 245 325.1 258z\"></path></svg>",
                            "thumbnail": "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 512 512\" fill=\"currentColor\"><!--! Font Awesome Pro 6.1.1 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2022 Fonticons, Inc. --><defs><style>.fa-secondary{opacity:.4}</style></defs><path class=\"fa-primary\" d=\"M256 336c-18 0-32 14-32 32c0 18 13.1 32 32 32c17.1 0 32-13.1 32-32C288 350 273.1 336 256 336zM289.1 128h-51.1C198.1 128 168 158.1 168 198c0 12.1 11 24 24 24s24-11 24-24C216 186 225.1 176 237.1 176h51.1C301.1 176 312 186 312 198c0 8-4 14.1-11 18.1L244 251C236 256 232 264 232 272V288c0 13 11 24 24 24S280 301 280 288V286l45.1-28c21-13 34-36 34-60C360 158.1 328.1 128 289.1 128z\"></path><path class=\"fa-secondary\" d=\"M256 0C114.6 0 0 114.6 0 256s114.6 256 256 256s256-114.6 256-256S397.4 0 256 0zM256 400c-18 0-32-14-32-32s13.1-32 32-32c17.1 0 32 14 32 32S273.1 400 256 400zM325.1 258L280 286V288c0 13-11 24-24 24S232 301 232 288V272c0-8 4-16 12-21l57-34C308 213 312 206 312 198C312 186 301.1 176 289.1 176h-51.1C225.1 176 216 186 216 198c0 13-11 24-24 24s-24-11-24-24C168 159 199 128 237.1 128h51.1C329 128 360 159 360 198C360 222 347 245 325.1 258z\"></path></svg>"
                        },
                        "position_icon": "after",
                        "position_tooltip": "bottom",
                        "position_tooltip__tablet": "bottom",
                        "position_tooltip__mobile": "bottom"
                    },
                    "style": {
                        "text": {
                            "textTextAlign": "left",
                            "textColor": "#000000",
                            "textTypography": {
                                "font-weight": "500",
                                "font-size": "30px"
                            },
                        },
                        "tooltip": {
                            "textColor": "#ffffff",
                            "background_color": "#545454",
                            "textTypography": {
                                "font-size": "14px"
                            },
                            "borderRadius": {
                                "right": "5px",
                                "top": "5px",
                                "left": "5px",
                                "bottom": "5px"
                            }
                        },
                        "heading": {
                            "textTypography": {
                                "font-size": "24px"
                            }
                        },
                        "icon_tooltip": {
                            "iconFontSize": "18px"
                        }
                    }
                }
            }
        },
        watch: {
            activeMe (val) {
                if (!val && this.editing === true) {
                    this.editing = false;
                    this.heading.settings.content = this.$refs.content.innerHTML;
                }
            }
        },
        methods: {
            edit() {
                this.convertText;
                if (this.editing === false) {
                    this.editing = true;
                    this.editingValue = this.data.settings.content;
                }
            },
            update(e) {
                this.heading.settings.content = e.target.innerHTML;
            }
        },

    };
</script>