<template>
    <div class="ecom__element ecom-element element__text-marquee">
        <input type="hidden" name="speed_animation" :value="animSpeed">
        <div class="ecom-marquee" :class="isReverse ? 'animation-reverse' : ''">
            <div class="ecom-flex ecom-marquee_items">
                <template v-for="(item, index) in items" :key="index">
                    <div class="ecom-marquee_item">
                        <h3 
                        v-html="lang(item.text, 'marquee_text' + index)"
                        />
                        <template v-if="item?.type_icon !== 'img' && item?.icon">
                            <span class="ecom-icon-separator" v-html="item.icon.value"></span>
                        </template>
                        <template v-if="item?.type_icon === 'img' && item?.image?.value">
                            <span  class="ecom-marquee-image">
                                <img :src="item?.image?.value" alt="" style="max-width: unset; width: 40px;">
                            </span>
                        </template>
                    </div>
                </template>
            </div>
        </div>
    </div>
</template>

<script lang="ts">
import { Element, Javascript } from '@/mixins';
export default {
    name: 'TextMarqueeElement',
    presets: true,
    docs: 'https://help.ecomposer.io/docs/elements/basic-elements/text/',
    mixins: [Element, Javascript],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    data() {
        return {
            jsreactives: [
                'items'
            ]
        };
    },
    computed: {
        textMarqueeData() {
            return this.data;
        },
        items() {
            return this.data?.settings?.items;
        },
        animSpeed() {
            return this.data?.settings?.animation_speed;
        },
        isReverse() {
            return this.data?.settings?.reverse_direction;
        },
        iconSeparator() {
            return this.data?.settings?.icon;
        },
        javascript() {
            return function () {
                let checkLang = document.querySelector('html[dir="rtl"]');
                let _this = this;
                let el_id = this.id;
                let $el = this.$el;
                let speed_input = $el.querySelector("input[name='speed_animation']");
                let speed_val =null, anim_duration = null;
                if(speed_input) {
                    speed_val = parseFloat(speed_input.value);
                    anim_duration = Math.round(10/speed_val);
                }
                let is_reverse = $el.querySelector('.animation-reverse') ? true : false;
                function Marquee(selector, speed) {
                    const parentSelector = $el.querySelector(selector);
                    const firstElement = parentSelector.children[0];
                    setTimeout(() => {
                        if(firstElement.clientWidth <= parentSelector.clientWidth) {
                            if(checkLang){
                                parentSelector.style.flexDirection = "row-reverse";
                            }
                            $el.addEventListener('mouseover', function() {
                                firstElement.style.animationPlayState = "paused"
                            });
                            $el.addEventListener('mouseout', function() {
                                firstElement.style.animationPlayState = "running"
                            });

                            firstElement.classList.add('ecom-flex_full');
                            const clone = parentSelector.innerHTML;
                            parentSelector.insertAdjacentHTML('beforeend', clone);
                            if(anim_duration && anim_duration > 0) {
                                if(!is_reverse) {
                                    firstElement.style.animation =  `marquee ${anim_duration}s linear infinite`;
                                } else {
                                    firstElement.style.animation =  `marqueeReverse ${anim_duration}s linear infinite`;
                                }
                            } else {
                                firstElement.classList.add('ecom-anim_marquee');
                            }
                        } else if(firstElement.clientWidth > parentSelector.clientWidth) {
                            if(checkLang){
                                parentSelector.style.flexDirection = "row-reverse";
                            }
                            const clone = parentSelector.innerHTML;
                            parentSelector.insertAdjacentHTML('beforeend', clone);
                            let i = is_reverse ? firstElement.clientWidth : 0;
                                let flag = true;
                                let intervalFunc = setInterval(function () {
                                    if(!flag) return;

                                    if(!document.querySelector(`.${el_id}`)) {
                                        clearInterval(intervalFunc);
                                    }
                                    firstElement.style.marginLeft = `-${i}px`;
                                    if(!is_reverse) {
                                        if (i > firstElement.clientWidth) {
                                            i = 0;
                                        }
                                        i = i + speed;
                                    } else {
                                        if (i < 0) {
                                            i = firstElement.clientWidth;
                                        }
                                        i = i - speed;
                                    }
                                }, 0);

                                $el.addEventListener('mouseover', function() {
                                    flag = false;
                                });
                                $el.addEventListener('mouseout', function() {
                                    flag = true;
                                });

                        }
                    }, 200)
                }
                window.addEventListener('load', Marquee('.ecom-marquee', speed_val??0));
            }
        },
        settings() {
            return [
                {
                    group_title:'General',
                    params: [
                        {
                            name: "items",
                            type: "group",
                            label: "Group items",
                            value: [],
                            options: {
                                add_text: "Add item"
                            },
                            params: [
                                {
                                    name: "text",
                                    type: "textarea",
                                    label: "Text",
                                    options: {
                                        toolbar: true
                                    }
                                },
                                {
                                    type: 'popup',
                                    label: 'Icon/Image',
                                    name: 'type_icon',
                                    options: {
                                        preview: "title",
                                        type: 'dropdown',
                                        values: { icon: 'Icon', img: 'Image' },
                                        default: false
                                    }
                                },
                                {
                                    type: "picker",
                                    label: "Choose icon",
                                    name: "icon",
                                    options: {
                                        type: "icon",
                                        multiple: false,
                                        visible: {
                                            name: 'type_icon',
                                            value: 'icon'
                                        }
                                    }
                                },
                                {
                                    type: 'picker',
                                    label: 'Choose Image',
                                    name: 'image',
                                    options: {
                                        responsive: false,
                                        type: 'image',
                                        editAlt: true,
                                        visible: {
                                            name: 'type_icon',
                                            value: 'img'
                                        }
                                    }
                                },
                            ]
                        },
                        {
                            type: 'choose',
                            label: 'Icon/Image position',
                            name: 'icon_position',
                            options: {
                                type: 'align-x',
                                values: [-1, 1],
                                visible: function(data){
                                    return (
                                        data &&
                                        (data['items'].some((item) => item?.icon) || data['items'].some((item) => item?.image?.url))
                                    )
                                }
                            },
                            css:{
                                selector:' .ecom-icon-separator, .ecom-marquee-image',
                                properties:{
                                    order:''
                                }
                            }
                        },
                        {
                            name: 'animation_speed',
                            label: 'Animation speed',
                            type: 'number',
                            options: {
                                min: 0,
                                max: 3,
                                step: 0.1
                            }
                        },
                        {
                            type: 'toggle',
                            label: 'Reverse direction',
                            name: 'reverse_direction',
                            options: {
                                oneline: true,
                                values: {
                                    on: {
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                }
                            }
                        },
                    ]
                }
            ];
        },

        css() {
            return `
                .ecom-marquee {
                    overflow: hidden;
                    display: flex;
                }

                .ecom-marquee .ecom-marquee_items {
                    font-size: 1.8em;
                    width: fit-content;
                    white-space: nowrap;
                }

                .ecom-marquee.animation-reverse .ecom-marquee_items {
                    justify-content: end;
                }

                .ecom-marquee_items.ecom-anim_marquee {
                    animation: marquee 10s linear infinite;
                }

                .ecom-marquee.animation-reverse .ecom-marquee_items.ecom-anim_marquee {
                    animation: marqueeReverse 10s linear infinite;
                }

                .element__text-marquee:hover .ecom-marquee_items {
                    animation-play-state: paused;
                }
                .ecom-marquee_items .ecom-marquee_item {
                    display: flex;
                }
                @keyframes marquee {
                    0% { margin-left: 0; }
                    100% { margin-left: -100%; }
                }

                @keyframes marqueeReverse {
                    0% { margin-left: -100%; }
                    100% { margin-left: 0; }
                }

                .ecom-marquee_items h3 {
                    display: flex;
                    align-items: center;
                }

                .ecom-flex_full {
                    flex: 0 0 100%;
                    width: 100%;
                }

                .ecom-icon-separator svg {
                    width: 20px;
                    height: 20px;
                }

                .ecom-icon-separator, .ecom-marquee-image {
                    padding-left: 15px;
                    padding-right: 15px;
                    line-height: normal;
                    align-self: center;
                }
            `;
        },

        text() {
            return this.data;
        },

        default(){
            return {
                settings: {
                    items: [
                        {
                            text: "Text content 1",
                            "type_icon": "icon",
                            icon: {
                                "id": "4hQcXzCH",
                                "name": "bolt",
                                "cate": "Solid",
                                "url": "https://tiep.ecomposer.app/storage/icons/font-awesome/solid/bolt.svg",
                                "value": "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 384 512\" fill=\"currentColor\"><path d=\"M240.5 224H352C365.3 224 377.3 232.3 381.1 244.7C386.6 257.2 383.1 271.3 373.1 280.1L117.1 504.1C105.8 513.9 89.27 514.7 77.19 505.9C65.1 497.1 60.7 481.1 66.59 467.4L143.5 288H31.1C18.67 288 6.733 279.7 2.044 267.3C-2.645 254.8 .8944 240.7 10.93 231.9L266.9 7.918C278.2-1.92 294.7-2.669 306.8 6.114C318.9 14.9 323.3 30.87 317.4 44.61L240.5 224z\"></path></svg>",
                                "thumbnail": "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 384 512\" fill=\"currentColor\"><path d=\"M240.5 224H352C365.3 224 377.3 232.3 381.1 244.7C386.6 257.2 383.1 271.3 373.1 280.1L117.1 504.1C105.8 513.9 89.27 514.7 77.19 505.9C65.1 497.1 60.7 481.1 66.59 467.4L143.5 288H31.1C18.67 288 6.733 279.7 2.044 267.3C-2.645 254.8 .8944 240.7 10.93 231.9L266.9 7.918C278.2-1.92 294.7-2.669 306.8 6.114C318.9 14.9 323.3 30.87 317.4 44.61L240.5 224z\"></path></svg>"
                            }
                        },
                        {
                            text: "Text content 2",
                            "type_icon": "icon",
                            icon: {
                                "id": "4hQcXzCH",
                                "name": "bolt",
                                "cate": "Solid",
                                "url": "https://tiep.ecomposer.app/storage/icons/font-awesome/solid/bolt.svg",
                                "value": "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 384 512\" fill=\"currentColor\"><path d=\"M240.5 224H352C365.3 224 377.3 232.3 381.1 244.7C386.6 257.2 383.1 271.3 373.1 280.1L117.1 504.1C105.8 513.9 89.27 514.7 77.19 505.9C65.1 497.1 60.7 481.1 66.59 467.4L143.5 288H31.1C18.67 288 6.733 279.7 2.044 267.3C-2.645 254.8 .8944 240.7 10.93 231.9L266.9 7.918C278.2-1.92 294.7-2.669 306.8 6.114C318.9 14.9 323.3 30.87 317.4 44.61L240.5 224z\"></path></svg>",
                                "thumbnail": "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 384 512\" fill=\"currentColor\"><path d=\"M240.5 224H352C365.3 224 377.3 232.3 381.1 244.7C386.6 257.2 383.1 271.3 373.1 280.1L117.1 504.1C105.8 513.9 89.27 514.7 77.19 505.9C65.1 497.1 60.7 481.1 66.59 467.4L143.5 288H31.1C18.67 288 6.733 279.7 2.044 267.3C-2.645 254.8 .8944 240.7 10.93 231.9L266.9 7.918C278.2-1.92 294.7-2.669 306.8 6.114C318.9 14.9 323.3 30.87 317.4 44.61L240.5 224z\"></path></svg>"
                            }
                        },
                        {
                            text: "Text content 3",
                            "type_icon": "icon",
                            icon: {
                                "id": "4hQcXzCH",
                                "name": "bolt",
                                "cate": "Solid",
                                "url": "https://tiep.ecomposer.app/storage/icons/font-awesome/solid/bolt.svg",
                                "value": "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 384 512\" fill=\"currentColor\"><path d=\"M240.5 224H352C365.3 224 377.3 232.3 381.1 244.7C386.6 257.2 383.1 271.3 373.1 280.1L117.1 504.1C105.8 513.9 89.27 514.7 77.19 505.9C65.1 497.1 60.7 481.1 66.59 467.4L143.5 288H31.1C18.67 288 6.733 279.7 2.044 267.3C-2.645 254.8 .8944 240.7 10.93 231.9L266.9 7.918C278.2-1.92 294.7-2.669 306.8 6.114C318.9 14.9 323.3 30.87 317.4 44.61L240.5 224z\"></path></svg>",
                                "thumbnail": "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 384 512\" fill=\"currentColor\"><path d=\"M240.5 224H352C365.3 224 377.3 232.3 381.1 244.7C386.6 257.2 383.1 271.3 373.1 280.1L117.1 504.1C105.8 513.9 89.27 514.7 77.19 505.9C65.1 497.1 60.7 481.1 66.59 467.4L143.5 288H31.1C18.67 288 6.733 279.7 2.044 267.3C-2.645 254.8 .8944 240.7 10.93 231.9L266.9 7.918C278.2-1.92 294.7-2.669 306.8 6.114C318.9 14.9 323.3 30.87 317.4 44.61L240.5 224z\"></path></svg>"
                            }
                        }
                    ],
                    icon_position: 1,
                    animation_speed: 0.3,
                    reverse_direction: false
                },
                style: {
                    text: {
                        textTextAlign: "left"
                    },
                    text_dropcap: {
                        dropcapTypography: {
                            "font-size": "40px"
                        },
                        dropcapColor: "#f50c0c",
                        dropcapColorStackedView: "#5eff00",
                        dropcapBackgroundStackedView: "#4800ff",
                        dropcapColorFramedView: "#1aff00",
                        dropcapBackgroundFramedView: "#1100ff",
                        dropcapView: "framed"
                    },
                    icon: {
                        iconFontSize: "20px",
                        spacing: {
                            padding: {
                                left: "15px",
                                right: "15px"
                            }
                        }
                    },
                    text_item: {
                        textTypography: {
                            "font-size": "35px"
                        },
                        spacing: {
                            padding: {
                                left: "15px",
                                right: "15px"
                            }
                        }
                    }
                }
            }
        }
    },
    unmounted() {

    },
    watch: {
        items: {
            deep: true,
            handler: function(newVal) {
                this.refresh();
            }
        },
        animSpeed: {
            deep: true,
            handler: function() {
                this.refresh();
            }
        },
        iconSeparator: {
            deep: true,
            handler: function() {
                this.refresh();
            }
        },
        isReverse: {
            deep: true,
            handler: function(newVal) {
                this.refresh();
            }
        }
    },
    methods: {
        refresh() {
            this.textMarqueeData.refresh = this.$helpers.randid();
        },

        style() {
            let style = [];
            if(this.data.settings.items.length > 0) {
                style.push(
                {
                    group_alias: 'text',
                    options: {
                        group_title: 'Text',
                        group_name: 'text_item',
                        selector: ' .ecom-marquee_items h3'
                    },
                    modify: {
                        remove: [
                            { index: 0, length: 1 }
                        ],
                        params: {
                            position: 10,
                            fields: {alias: 'spacing'}
                        }
                    }
                })
            }

            if(this.data.settings?.items?.some((item) => item.icon)) {
                style.push({
                    group_alias: 'icon',
                    options: {
                        group_title: 'Icon',
                        group_name: 'icon',
                        selector: ' .ecom-icon-separator',
                    },
                    modify:{
                        params:[
                            {
                                position:15,
                                fields:[
                                    {
                                        type:'line'
                                    },
                                    {
                                        alias:'spacing'
                                    }
                                ]
                            }
                        ]
                    }
                })
            }
            if(this.data.settings.items.some((item) => item.image?.url)) {
                style.push({
                    group_alias:'image',
                    options:{
                        selector: ' .ecom-marquee-image'
                    },
                    modify:{
                        remove: {
                            index: 0,
                            length: 2
                        },
                        params:[
                            {
                                position: 2,
                                fields: [
                                    {
                                        type: 'number',
                                        label: 'Width',
                                        name: 'width',
                                        options: {
                                            responsive: true,
                                            units: {
                                                px: {
                                                    min: 0,
                                                    max: 200
                                                },
                                                'vw': {
                                                    min: 0,
                                                    max: 100
                                                }
                                            }
                                        },
                                        css:{
                                            important: true,
                                            selector: ' img',
                                            properties:{
                                                width:''
                                            }
                                        }
                                    },
                                ]
                            },
                            {
                                position:20,
                                fields:{
                                    alias:'spacing'
                                }
                            }
                        ]
                        
                    }
                })
            }

            return style;
        }
    }
};
</script>
