<template>
    <div class="ecom-element ecom-collection ecom-collection__banner">
        <div class="ecom-collection__banner-wrapper" :style="styleWraper">
            <h1
                v-if="show_title"
                class="ecom-collection__banner-heading"
                v-html="liquid('title')"
            />
            <div
                v-if="show_description"
                class="ecom-collection__banner-paragraph"
                v-html="liquid('description')"
            />
            <div v-if="data.settings.overlay" class="ecom-overlay" />
        </div>
    </div>
</template>

<script>
import { Liquid, Element } from '@/mixins';
export default {
    name: 'CollectionBanner',
    presets:true,
    docs: 'https://help.ecomposer.io/docs/elements/collection-elements/banner/',
    mixins: [Liquid, Element],
    props: {
        data: {
            type: Object,
            default() {}
        }
    },
    computed: {
        liquids() {
            return {
                image: {
                    code: `
                        ${this.data.settings?.default_banner
                            ? `{%- capture default_img -%}${this.data.settings?.default_banner?.value}{%- endcapture -%}`
                            : ''
                        }
                    {%- assign banner = 'https://cdn2.shopify.com/s/files/1/0121/5945/1236/files/backpack.svg?7273' -%}
                    {%- if collection.featured_image -%}
                        {%- assign banner = collection.featured_image | img_url: 'master' -%}
                    {%- elsif default_img -%}
                    {%- assign banner = default_img -%}
                    {%- endif -%}
                    {{ banner }}`,
                    preview: `https://cdn2.shopify.com/s/files/1/0121/5945/1236/files/backpack.svg?7273`
                },
                title: {
                    code: '{{ collection.title }}',
                    preview: 'The sample collection title'
                },
                description: {
                    code: '{{ collection.description }}',
                    preview: 'The sample collection description'
                }
            };
        },

        settings() {
            return [
                {
                    params: [
                        {
                            type: 'popup',
                            label: 'Image source',
                            name: 'source',
                            value: 'collection',
                            options: {
                                type: 'dropdown',
                                default: false,
                                values: {
                                    collection: 'Collection',
                                    file: 'File upload'
                                }
                            }
                        },
                        {
                            type: 'picker',
                            label: 'Custom banner',
                            name: 'banner',
                            options: {
                                type: 'image',
                                multiple: false,
                                visible: function (data) {
                                    return data['source'] === 'file';
                                }
                            },
                            value: {
                                value: 'https://cdn2.shopify.com/s/files/1/0121/5945/1236/files/backpack.svg?7273'
                            }
                        },
                        {
                            type: 'picker',
                            label: 'Default banner',
                            name: 'default_banner',
                            description: 'Default image will display if collection not have image',
                            options: {
                                type: 'image',
                                multiple: false,
                                visible: function (data) {
                                    return data['source'] === 'collection';
                                }
                            },
                            value: {
                                value: 'https://cdn2.shopify.com/s/files/1/0121/5945/1236/files/backpack.svg?7273'
                            }
                        },
                        {
                            type: 'toggle',
                            label: 'Show title',
                            value: true,
                            name: 'show_title',
                            options: {
                                values: {
                                    on: {
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                },
                                oneline: true
                            }
                        },
                        {
                            type: 'toggle',
                            label: 'Show description',
                            value: true,
                            name: 'show_description',
                            options: {
                                values: {
                                    on: {
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                },
                                oneline:true
                            },

                        },
                        {
                            type: 'toggle',
                            label: 'Enable Parallax background',
                            name: 'background_parallax',
                            options: {
                                values: {
                                    on: {
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                },
                                oneline: true
                            }
                        },
                        {
                            type: 'toggle',
                            label: 'Enable overlay',
                            name: 'overlay',
                            options: {
                                values: {
                                    on: {
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                },
                                oneline: true
                            }
                        },
                        {
                            name: "position",
                            label: "Content position",
                            type: "popup",
                            value: "center center",
                            options: {
                                default: false,
                                values: {
                                    "center center": "Center center",
                                    "center right": "Center right",
                                    "center left": "Center left",
                                    "top left": "Top left",
                                    "top center": "Top center",
                                    "top right": "Top right",
                                    "bottom right": "Bottom right",
                                    "bottom left": "Bottom left",
                                    "bottom center": "Bottom center"
                                },
                                type: "dropdown",
                                oneline: true,
                            },
                            css: {
                                selector: ' .ecom-collection__banner-wrapper',
                                properties: {
                                    'align-items': `
                                        switch(value) {
                                            case 'center center':
                                            case 'top center':
                                            case 'bottom center':
                                                return 'center';
                                            case 'top left':
                                            case 'bottom left':
                                            case 'center left':
                                                return 'flex-start';
                                            case 'top right':
                                            case 'bottom right':
                                            case 'center right':
                                                return 'flex-end';
                                        }
                                    `,
                                    'justify-content': `
                                        switch(value) {
                                            case 'center center':
                                            case 'center right':
                                            case 'center left':
                                                return 'center';
                                            case 'top center':
                                            case 'top left':
                                            case 'top right':
                                                return 'flex-start';
                                            case 'bottom center':
                                            case 'bottom left':
                                            case 'bottom right':
                                                return 'flex-end';
                                        }
                                    `
                                }
                            }
                        },
                        {
                            label: 'Custom Height',
                            name: 'min-height',
                            type: 'number',
                            options: {
                                responsive: true,
                                slider: true,
                                units: {
                                    px: {
                                        min: 100,
                                        max: 1000
                                    }
                                }
                            },
                            css: {
                                selector: ' .ecom-collection__banner-wrapper'
                            }
                        }
                    ]
                }
            ];
        },

        source() {
            return this.data && this.data.settings && 'source' in this.data.settings
                ? this.data.settings.source
                : 'collection';
        },
        banner() {
            return this.data && this.data.settings && 'banner' in this.data.settings
                ? this.data.settings.banner
                : 'https://cdn2.shopify.com/s/files/1/0121/5945/1236/files/backpack.svg?7273';
        },
        show_title() {
            return this.data && this.data.settings && 'show_title' in this.data.settings
                ? this.data.settings.show_title
                : false;
        },
        show_description() {
            return this.data && this.data.settings && 'show_description' in this.data.settings
                ? this.data.settings.show_description
                : false;
        },
        styleWraper() {
            let s = {
                'backgroundImage':
                    'url("' +
                    (this.source === 'collection' ? this.liquid('image') : this.banner.value) +
                    '")'
            };
            if (this.data.settings.background_parallax) {
                s['backgroundAttachment'] = 'fixed';
            }
            return s
        },

        requestShopifyType() {
            return {
                shopify_type: 'collection'
            };
        },

        css() {
            return `
                .ecom-collection__banner-wrapper{
                    display:flex;
                    flex-direction: column;
                    justify-content:center;
                    background-size: cover;
                    background-repeat: no-repeat;
                    background-position: center;
                    position:relative;
                }
                .ecom-collection__banner .ecom-collection__banner-wrapper {
                    display: block !important;
                }
                .ecom-collection__banner-wrapper .ecom-collection__banner-heading{
                    position:relative;
                    z-index:1;
                }
                .ecom-collection__banner-wrapper .ecom-collection__banner-paragraph{
                    position:relative;
                    z-index:1;

                }
                .ecom-collection__banner-wrapper .ecom-overlay{
                    position:absolute;
                    top:0;
                    left:0;
                    right:0;
                    bottom:0;
                    display:block;
                }
            `;
        },

        default() {
            return {
                "settings": {
                    "source": "collection",
                    "show_title": true,
                    "show_description": true,
                    "position": "center center",
                    "overlay": false,
                    "banner": {
                        "value": "/images/placeholder.png"
                    },
                    "min-height": "300px",
                    "justify-content": "center"
                },
                "style": {
                    "overlay": {
                        "tab": "normal",
                        "imageOpacity": 0.35,
                        "backgroundnormalmode": {
                            "classic": {
                                "background-color": "#f78b8b"
                            }
                        }
                    }
                }
            }
        }
    },
    methods: {
        style() {
            let style = [
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_title: 'Title',
                        group_name: 'title',
                        selector: ' .ecom-collection__banner-heading'
                    },
                    modify: {
                        remove: {
                            index:0 ,length: 1
                        }
                    }
                },
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_title: 'Description',
                        group_name: 'description',
                        selector: ' .ecom-collection__banner-paragraph'
                    },
                    modify: {
                        remove: {
                            index:0 ,length: 1
                        }
                    }
                },
                {
                    group_title: 'Background',
                    group_name: 'background',
                    selector: ' .ecom-collection__banner-wrapper',
                    params: [
                        {
                            type: 'background',
                            label: 'Style',
                            name: 'background',
                            options: {
                                responsive: true,
                                types: ['classic'],
                                image: false,
                                color: false
                            },
                            css: {
                                properties: {
                                    background: ''
                                }
                            }
                        }
                    ]
                }
            ];
            if ('overlay' in this.data.settings) {
                style.push({
                    group_alias: 'background:overlay',
                    options: {
                        group_title: 'Overlay',
                        group_name: 'overlay',
                        selector: ' .ecom-collection__banner-wrapper',
                    }
                });
            }
            return style;
        }
    }
};
</script>
