<template>
    <div class="ecom-element ecom-shopify-elements ecom-shopify__article-comments-count">
        <div class="ecom-shopify__article-comments-count--wrapper">
            <div
                class="ecom-shopify__article-comments-count--container ecom-flex ecom-al_center"
            >
                <div
                    v-if="data.settings?.icon"
                    class="ecom-shopify__article-comments-count-icon ecom-flex ecom-al_center"
                    v-html="data.settings?.icon"
                />
                <div
                    class="ecom-shopify__article-comments-count--count"
                    v-html="liquid('comments_count')"
                />
            </div>
        </div>
    </div>
</template>

<script>
import { Liquid, Element } from '@/mixins';

export default {
    name: 'ArticleTitle',

    mixins: [Liquid, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {

        liquids() {
            return {
                comments_count: {
                    code: `
                        {% if article != empty %}
                             {% if article.comments_enabled? %}
                                {% if article.comments_count == 1%}
                                    <p class="ecom-shopify__article-comments-count--container-counter">
                                        ${this.lang(this.data?.settings?.comment_count_text ? this.data.settings.comment_count_text : '','comment_count_text',
                                            {
                                                comment_count: 'article.comments_count'
                                            }
                                        )}
                                        </p>
                                    {% else %}
                                        <p class="ecom-shopify__article-comments-count--container-counter">
                                            ${this.lang(this.data?.settings?.comments_count_text ? this.data.settings.comments_count_text : '',
                                                'comments_count_text',
                                                {
                                                    comments_count: 'article.comments_count'
                                                }
                                            )}
                                    </p>
                                {% endif %}
                            {% endif %}
                        {% else %}
                            <div>Please select the article in settings</div>
                        {% endif %}
                    `,
                    preview: `
                        <div class="ecom-skeleton-item">
                            <div class="ecom-skeleton-col-12">
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-9 ecom-skeleton-big"></div>
                                </div>
                            </div>
                        </div>
                    `
                }
            };
        },

        requestShopifyType () {
            return {
                shopify_type: 'article'
            };
        },
        css () {
            return `
                .ecom-shopify__article-comments-count-icon svg {
                    width: 16px;
                    height: 16px;
                }
            `;
        },
        settings() {
            return [
                {
                    params: [
                        {
                            type: 'paragraph',
                            content: 'Show the number of published comments for the article.'
                        },
                        {
                            type: 'text',
                            name: 'comment_count_text',
                            label: 'Comment text count',
                            placeholder: '{{comment_count}} comment',
                            description: 'Use {{comment_count}} to show comment count'
                        },
                        {
                            type: 'text',
                            name: 'comments_count_text',
                            label: 'Many comments text count',
                            placeholder: '{{comments_count}} comments',
                            description: 'Use {{comments_count}} to show comments count'
                        },
                        {
                            type: 'picker',
                            label: 'Icon',
                            name: 'icon',
                            options: {
                                type: 'icon',
                                layout: 'grid',
                                output: 'value',
                                multiple: false,
                                simple: false
                            }
                        },
                        {
                            type: 'choose',
                            label: 'Icon position',
                            name: 'icon_position',
                            options: {
                                type: 'align-x',
                                values: [-1, 1],
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['icon'];
                                    }
                                }
                            },
                            css:{
                                selector:' .ecom-shopify__article-comments-count-icon',
                                properties:{
                                    order:''
                                }
                            }
                        },
                        {
                            type: 'number',
                            label: 'Spacing',
                            name: 'spacing',
                            options: {
                                units: {
                                    px: {
                                        min: 0,
                                        max: 200
                                    }
                                },
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['icon'];
                                    }
                                }
                            },
                            css:{
                                selector:' .ecom-shopify__article-comments-count--container',
                                properties:{
                                    gap:''
                                }
                            }
                        },
                    ]
                }
            ];
        },

        style() {
            return [
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_name: 'comments_count_text',
                        group_title: 'General',
                        selector:
                            ' .ecom-shopify__article-comments-count--container .ecom-shopify__article-comments-count--container-counter'
                    }
                },
                {
                    group_alias:'icon',
                    options:{
                        group_name: 'icon',
                        group_title: 'Icon',
                        selector: ' .ecom-shopify__article-comments-count-icon',
                    },
                    modify:{
                        params:[
                            {
                                position:15,
                                fields:[
                                    {
                                        type: 'line'
                                    },
                                    {
                                        alias:'spacing'
                                    }
                                ]
                            }
                        ]
                    }
                },
            ];
        },

        default() {
            return {
                settings: {
                    comment_count_text: "{{comment_count}} comment",
                    comments_count_text: "{{comments_count}} comments"
                },
                style: {
                    comments_count_text: {
                        textTextAlign: "left"
                    }
                }
            }
        }
    }
};
</script>
