<template>
    <div class="ecom-element ecom-product-single ecom-product-single__title">
        <div class="ecom-product-single__title-wrapper">
            <div class="ecom-product-single__title-container">
                <component
                    :is="tag"
                    class="ecom-product__heading"
                    :class="classes"
                    :href="liquid('url')"
                    :title="liquid('title')"
                    v-html="liquid('title')"
                />
            </div>
        </div>
    </div>
</template>

<script>
import { Liquid, Element } from '@/mixins';
export default {
    name: 'ProductTitle',

    presets:true,
    mixins: [Liquid, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {

        classes() {
            return this.data && this.data.settings && this.data.settings.classes
                ? this.data.settings.classes
                : '';
        },

        tag() {
            return this.data && this.data.settings && this.data.settings.tag
                ? this.data.settings.tag
                : 'h3';
        },

        liquids() {
            return {
                title: {
                    code: `{{ product.title }}`,
                    preview: `Loading ....`
                },
                url: {
                    code: '{{ product.url }}',
                    preview: '#'
                }
            };
        },

        requestShopifyType() {
            return {
                shopify_type: 'product'
            };
        },

        settings() {
            return [{
                    params: [
                        {
                            type: "choose",
                            label: "<span class='uppercase'>HTML</span> Tag",
                            name: "tag",
                            options: {
                                type:'heading',

                            }
                        },
                        {
                            type: "text",
                            label: "Custom classes",
                            name: "classes",
                            options: {
                                placeholder: "Your Classes"
                            }
                        }
                    ]
                }]
        },

        style() {
            return [
                {
                    group_alias: 'text',
                    options:{
                        group_title: '',
                        group_title: 'Text',
                        selector: ' .ecom-product__heading',
                    }
                }
            ];
        },

        default() {
            return {
                settings: {
                    tag: "h2",
                    classes: ""
                },
                style: {
                    textSpacing: {
                        textTextAlign: "left"
                    }
                }
            }
        },
        css () {
                return `

                a.ecom-product__heading {
                    display: block;
                }
            `;
            },
    },
};
</script>
