<template>
    <div class="ecom-element ecom-instagram">
        <div
            v-if="instagram.settings.layout !== 'slider'"
            class="ecom-instagram--container ecom-instagram-wrapper ecom-base-instagram-grid"
        />
        <div v-else class="ecom-instagram--container">
            <div
                class="ecom-instagram--slider ecom-swiper-container"
                :data-option-swiper="optionSwiper"
            >
                <div class="ecom-instagram-wrapper ecom-swiper-wrapper" />
            </div>
            <div
                v-if="isNavigation && isCombined() == 'combine'"
                class="ecom-swiper-navigation"
                :data-navigator-type="isCombined() == 'combine'"
            >
                <div class="ecom-flex-center">
                    <button
                        type="button"
                        v-show="isArrow()"
                        class="ecom-swiper-button ecom-swiper-button-prev"
                        v-html="instagram.settings.slider_prev_icon"
                    />
                    <div v-show="isPagination()" class="ecom-swiper-pagination" />
                    <button
                        type="button"
                        v-show="isArrow()"
                        class="ecom-swiper-button ecom-swiper-button-next"
                        v-html="instagram.settings.slider_next_icon"
                    />
                </div>
            </div>
            <div v-if="isNavigation && !(isCombined() == 'combine')" v-show="isArrow()" class="ecom-swiper-navigation-position">
                <button
                    :style="sliderNav"
                    class="ecom-swiper-button ecom-swiper-button-prev"
                    v-html="instagram.settings?.slider_prev_icon"
                />
                <button
                    :style="sliderNav"
                    class="ecom-swiper-button ecom-swiper-button-next"
                    v-html="instagram.settings?.slider_next_icon"
                />
            </div>
            <div v-if="isNavigation && !(isCombined() == 'combine')" v-show="isPagination()" class="ecom-swiper-pagination-position ecom-swiper-pagination" />
        </div>
    </div>
</template>

<script>
import { Element, Javascript } from "@/mixins";
export default {
    name: "Instagram",
    docs: 'https://help.ecomposer.io/docs/elements/basic-elements/instagram/',
    presets:true,
    mixins: [Element, Javascript],
    vendors: ["slider_js", "slider_css", "modal_js", "modal_css"],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        },
        index: {
            type: Number,
            default: 0
        },
        deep: {
            type: Number,
            default: 0
        }
    },
    data() {
        return {
            is: "instagram",
            name: "Instagram",
            jsreactives: ["items", "spacing", "group", "navigator","lightbox", "layout", "itemsCount", "overlay_use", "overlay_title", "link"]
        };
    },
    computed: {

        instagram() {
            return this.data;
        },
        screens() {
            let screens = this.$store.getters["builder/screens"];
            if (!screens) {
                return [];
            }
            return Object.entries(screens).sort(([, b1], [, b2]) => b1.width - b2.width);
        },
        screenMinW() {
            let rs = [];
            for (let i in this.screens) {
                let screen = this.$helpers.copy(this.screens[i]);
                screen[1].width = i === "0" ? 0 : parseInt(this.screens[i - 1][1].width) + 1;
                rs.push(screen);
            }
            return rs;
        },
        isNavigation() {
            return this.instagram.settings.slider_navigation_layout;
        },
        optionSwiper() {
            return this.$helpers.optionSwiper(this.instagram.settings);
        },
        sliderNav(){
            return {
                position: this.instagram.settings.navigation_position !== 'center' ? 'unset' : 'absolute'
            }
        },
        settings() {
            return [
                {
                    group_title: "General",
                    params: [
                        {
                            type: "paragraph",
                            content:
                                "Connect your Instagram account to get photos. [Connect here](#settings/app_settings/apps)"
                        },
                        {
                            type: "popup",
                            name: "layout",
                            label: "Layout",
                            options: {
                                type: "dropdown",
                                default: false,
                                values: {
                                    grid: "Grid",
                                    slider: "Slider"
                                }
                            }
                        },
                        {
                            type: "popup",
                            name: "link",
                            label: "Image Link",
                            options: {
                                type: "dropdown",
                                default: false,
                                values: {
                                    "": "None",
                                    media: "Media Link",
                                    instagram: "Instagram Post"
                                }
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            type: "popup",
                            name: "lightbox",
                            label: "Lightbox",
                            value: false,
                            options: {
                                type: "dropdown",
                                default:false,
                                values: {
                                    true: "Yes",
                                    false: "No"
                                },
                                visible: (data) => {
                                    return data["link"] === "media";
                                }
                            }
                        },
                        {
                            type: "number",
                            label: "Maximum items to show",
                            name: "itemsCount",
                            options: {
                                min: 1,
                                max: 30
                            }
                        },
                        {
                            type: "paragraph",
                            content: "** Overlay **"
                        },
                        {
                            type: "toggle",
                            name: "overlay_use",
                            label: "Enable Overlay",
                            value: true,
                            options: {
                                values: {
                                    off: {
                                        label: "NO",
                                        value: false
                                    },
                                    on: {
                                        label: "YES",
                                        value: true
                                    }
                                }
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            type: "popup",
                            label: "Title source",
                            name: "overlay_title",
                            options: {
                                type: "dropdown",
                                values: {
                                    "": "None",
                                    username: "Username",
                                    caption: "Caption"
                                },
                                visible: (data) => {
                                    return data["overlay_use"] === true;
                                }
                            }
                        }
                    ]
                },
                {
                    group_alias: "swiper",
                    options: {
                        options: {
                            keep_data: false,
                            visible: (data) => {
                                return data["layout"] == "slider";
                            }
                        }
                    },
                    modify: {
                        remove: {
                            name: ['line_pagination','title_pagination','slider_pagination_style', 'slider_spacing_row']
                        }
                    }
                },
                {
                    group_title: "Grid settings",
                    options: {
                        visible: {
                            keep_data: false,
                            condition: (data) => data.layout !== "slider"
                        }
                    },
                    params: [
                        {
                            type: "number",
                            label: "Items per row",
                            name: "slider_items",
                            options: {
                                responsive: true,
                                min: 1,
                                max: 12
                            },
                            css: {
                                selector: " .ecom-instagram--container.ecom-base-instagram-grid",
                                properties: {
                                    "grid-template-columns": "repeat(%value%, 1fr)"
                                }
                            }
                        }
                    ]
                }
            ];
        },
        css() {
            return `
                    /**
                     * EComposer Element Instagram
                     */
                    .ecom-base-instagram-grid {
                        display: grid;
                        gap: 30px 30px;
                        grid-template-columns: repeat(4, 1fr)
                    }
                    .ecom-flex-center, .ecom-swiper-navigation {
                        display: flex;
                        align-items: center;
                    }
                    .ecom-swiper-navigation{
                        justify-content: center
                    }
                    .ecom-swiper-navigation[data-navigator-type="combine"]{
                        justify-content: center
                    }
                    .ecom-instagram--container:not(.ecom-base-instagram-grid) {
                        position: relative;
                        display: flex;
                        flex-direction: column;
                    }
                    .ecom-instagram--item figure{
                        position: relative;
                        overflow: hidden;
                    }
                    .ecom-instagram--container .ecom-instagram--item,
                    .ecom-instagram--container .ecom-instagram--item a{
                        display: block
                    }
                    .ecom-instagram--container .ecom-instagram--item img{
                        position: relative;
                        object-fit: cover;
                    }
                    .ecom-instagram .ecom-swiper-button-next:after,
                    .ecom-instagram .ecom-swiper-button-prev:after {
                        content: none;
                    }
                    .ecom-instagram .ecom-swiper-button-next,
                    .ecom-instagram .ecom-swiper-button-prev {
                        border: 0;
                        background: transparent;
                        width: auto;
                        height: auto;
                        padding: 5px;
                        color: #444;
                        margin-top: 0;
                        transform: translateY(-50%);
                    }
                    .ecom-instagram--container .ecom-swiper-navigation[data-navigator-type="combine"] .ecom-swiper-button-next,
                    .ecom-instagram--container .ecom-swiper-navigation[data-navigator-type="combine"] .ecom-swiper-button-prev {
                        position: static;
                        margin: 0;
                        transform: none;
                    }
                    .ecom-instagram--slider,
                    .ecom-instagram--container .ecom-swiper-navigation[data-navigator-type="classic"] .ecom-swiper-pagination,
                    .ecom-instagram--container .ecom-swiper-navigation:not([data-navigator-type]) .ecom-swiper-pagination {
                        width:100%
                    }
                    .ecom-swiper-pagination-bullet:only-child {
                        opacity: none
                    }
                    .ecom-instagram .ecom-swiper-pagination {
                        position: static;
                        color: rgb(5, 150, 105);
                        display:flex;
                        flex-wrap:wrap;
                        justify-content:center;
                        padding:10px;
                    }
                    .ecom-swiper-navigation-position {
                        display: flex;
                    }
                    .ecom-instagram .ecom-swiper-pagination-bullet{
                       border-radius:unset
                    }
                    .ecom-instagram .ecom-swiper-pagination-bullet,
                    .ecom-instagram .ecom-swiper-pagination-bullet-active{
                        background: currentColor;
                    }
                    .ecom-base-instagram-overlay.ecom-overlay {
                        position: absolute;
                        inset: 0;
                        z-index: 1;
                        color: #ffffff;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        pointer-events: none;
                        background: rgba(0,0,0,0.3);
                    }
                    .ecom-base-instagram-overlay.ecom-overlay>span {
                        padding: 0 10%;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                    }
                    .ecom-instagram--item{
                        text-align:center;
                    }
                `;
        },
        javascript() {
            return function () {
                let $el = this.$el;
                let limit = this.settings.itemsCount ? this.settings.itemsCount : 8;
                function getToken() {
                    if (window.EComposer && window.EComposer.configs && window.EComposer.configs.instagram) {
                        return window.EComposer.configs.instagram;
                    } else {
                        let token = null;
                        try{
                            window.localStorage.getItem("ecom-instagram");
                        }
                        catch(e){
                            console.log(e)
                        }
                        return token ? token : null;
                    }
                }
                const appendHTML = (medias) => {
                    let html = "";
                    let link = this.settings.link,
                        lightbox = link === "media" ? this.settings.lightbox : false;

                    html += medias
                        .map((m) => {
                            return `<div class="ecom-instagram--item ${
                                this.settings.layout === "slider" ? " ecom-swiper-slide" : ""
                            }">
                                ${ link ? `<a target="_blank" href="${link === "media" ? m.media : m.url}" ${ lightbox == 'true' ? `ecom-modal="image"` : ""}>`
                                        : ""
                                }
                                    <figure class="ecom-image-default">
                                        <img src="${m.thumb}" alt="${m.username}">
                                        ${
                                            this.settings.overlay_use
                                                ? `<div class="ecom-base-instagram-overlay ecom-overlay">
                                                ${
                                                    this.settings.overlay_title
                                                        ? `<span class="ecom-overlay-text">${
                                                              this.settings.overlay_title ===
                                                              "username"
                                                                  ? m.username
                                                                  : this.settings.overlay_title === "caption"
                                                                  ? m.caption ?? ""
                                                                  : ""
                                                          }</span>`
                                                        : ""
                                                }
                                            </div>`
                                                : ""
                                        }
                                    </figure>
                                ${link ? "</a>" : ""}
                            </div>`;
                        })
                        .join("");
                    $el.querySelector(".ecom-instagram-wrapper").innerHTML = html;
                };

                const getMedias = (token) => {
                    let kStore = "ecom-instagram-" + token;
                    return new Promise((resolve, reject) => {
                        let dataStore = window.localStorage.getItem(kStore);
                        if (dataStore) {
                            let jData = JSON.parse(dataStore);
                            if (jData.exp >= new Date().getTime() && jData.data.length >= limit) {
                                resolve(jData.data.slice(0, limit));
                                return false;
                            } else {
                                window.localStorage.removeItem(kStore);
                                getMedias(token).then((response) => resolve(response));
                            }
                        } else {
                            window
                                .fetch(
                                    "https://graph.instagram.com/me/media?" +
                                        new URLSearchParams({
                                            fields: "id,caption,media_url,thumbnail_url,media_type,permalink,username",
                                            access_token: token,
                                            limit
                                        }).toString()
                                )
                                .then((res) => res.json())
                                .then((response) => {
                                    if (response && response.data) {
                                        const getKey = (type, key) => {
                                            let ks = {
                                                image: {
                                                    thumb: "media_url",
                                                    media: "media_url",
                                                    url: "permalink"
                                                },
                                                video: {
                                                    thumb: "thumbnail_url",
                                                    media: "media_url",
                                                    url: "permalink"
                                                },
                                                carousel_album: {
                                                    thumb: "media_url",
                                                    media: "media_url",
                                                    url: "permalink"
                                                }
                                            };
                                            return ks?.[type]?.[key];
                                        };
                                        let datas = response.data.map((item) => {
                                            let type = item.media_type.toLowerCase();
                                            return {
                                                type,
                                                thumb: item[getKey(type, "thumb")],
                                                media: item[getKey(type, "media")],
                                                url: item[getKey(type, "url")],
                                                username: item.username,
                                                caption: item.caption
                                            };
                                        });
                                        window.localStorage.setItem(
                                            kStore,
                                            JSON.stringify({
                                                exp: new Date().setDate(new Date().getDate() + 7),
                                                data: datas
                                            })
                                        );
                                        resolve(datas);
                                    }
                                })
                                .catch((e) => {
                                    reject(e);
                                });
                        }
                    });
                };
                const initSlider = () => {
                    if(!$el) return;
                    let $container = $el.querySelector(".ecom-swiper-container");
                    let options = JSON.parse($container.dataset.optionSwiper);
                    options.pagination.el = $el.querySelector('.ecom-swiper-pagination');
                    options.navigation.nextEl = $el.querySelector('.ecom-swiper-button-next');
                    options.navigation.prevEl = $el.querySelector('.ecom-swiper-button-prev');
                    if(window.EComSwiper) {
                        new window.EComSwiper(
                            $container,
                            Object.assign(options, { allowTouchMove: this.isLive })
                        );
                    }
                };
                let token = getToken();
                if (!token) {
                    return false;
                }
                getMedias(token)
                    .then((res) => {
                        appendHTML(res);
                        if (this.settings.layout === "slider") {
                            initSlider();
                        }
                        let arr =  $el.querySelectorAll('[ecom-modal]');

                        if(arr.length) {
                            new window.EComModal(arr, {
                                gallery: true,
                                cssClass: ['ecom-container-lightbox-' + this.id]
                            });
                        }
                    })
                    .catch((e) => {
                        console.info(e.message);
                    });
            };
        },
        default() {
            return {
                    "settings": {
                        "overlay_use": true,
                        "slider_spacing": "30",
                        "itemsCount": 8,
                        "layout": "slider",
                        "slider_items": 4,
                        "slider_items__tablet": 3,
                        "slider_items__mobile": 2,
                        "slider_loop": true,
                        "slider_autoplay": true,
                        "slider_autoplay_speed": 2000,
                        "slider_pause": true,
                        "slider_navigation_layout": "classic_full",
                        "slider_prev_icon": "<svg xmlns=\"http: //www.w3.org/2000/svg\" width=\"52\" height=\"52\" viewBox=\"0 0 24 24\" fill=\"none\" stroke=\"currentColor\" stroke-width=\"2\" stroke-linecap=\"round\" stroke-linejoin=\"round\" class=\"feather feather-chevron-left\"><polyline points=\"15 18 9 12 15 6\"></polyline></svg>",
                        "slider_next_icon": "<svg xmlns=\"http://www.w3.org/2000/svg\" width=\"52\" height=\"52\" viewBox=\"0 0 24 24\" fill=\"none\" stroke=\"currentColor\" stroke-width=\"2\" stroke-linecap=\"round\" stroke-linejoin=\"round\" class=\"feather feather-chevron-right\"><polyline points=\"9 18 15 12 9 6\"></polyline></svg>",
                        "slider_pagination_position": "static",
                        "slider_effect": "slide",
                        "slider_speed": 200,
                        "overlay_title": "username",
                        "slider_group": 1,
                        "slider_spacing_row": 10,
                        "slider_navigation_position": "bottom_center"
                    },
                    "style": {
                        "instagramImage": {
                            "imageOpacitynormalmode": 1,
                            "imageOpacityhovermode": 1,
                            "tab": "normal"
                        },
                        "swiper_nav": {
                            "navigatorFontSize": "36px",
                            "navigatorPrimaryColornormalmode": "rgba(59, 50, 50, 0.98)",
                            "navigatorPrimaryColorhovermode": "#000000",
                            "navtab": "normal",
                            "tab": "normal"
                        },
                        "instagramOverlay": {
                            "opacity": 0.28,
                            "opacityHover": 1,
                            "backgroundhovermode": {
                                "classic": {
                                    "background-color": "rgba(17, 24, 39, 0.3)"
                                }
                            },
                            "tab": "hover"
                        }
                    }

            };
        }
    },
    watch: {
        optionSwiper: {
            deep: true,
            handler: function () {
                this.instagram.refresh = this.$helpers.randid();
            }
        },
        isCombined: function() {
            this.instagram.refresh = this.$helpers.randid();
        }
    },
    methods: {
        isCombined() {
            let navigator = this.data?.settings?.slider_navigation_layout;
            return navigator === 'neo_full' ? 'combine' : 'classic';
        },
        refresh() {
            this.instagram.refresh = this.$helpers.randid();
        },
        isArrow() {
            return this.data.settings?.slider_navigation_layout === 'neo_full' ||
                    this.data.settings?.slider_navigation_layout === 'neo_navigator' ||
                    this.data.settings?.slider_navigation_layout === 'classic_full' ||
                    this.data.settings?.slider_navigation_layout === 'navigation';
        },
        isPagination() {
            return this.data.settings?.slider_navigation_layout === 'neo_full' ||
                    this.data.settings?.slider_navigation_layout === 'classic_full' ||
                    this.data.settings?.slider_navigation_layout === 'pagination';
        },
        style() {
            let checkNavPagination = [];
            if (this.isArrow()) {
                checkNavPagination.push({
                    title: "Navigator",
                    type: "swiper:nav"
                });
            }
            if (this.isPagination()) {
                checkNavPagination.push({
                    title: "Pagination",
                    type: "swiper:pagination"
                });
            }
            let modifyNavigation = {};
            if(this.isCombined === 'combine') {
                modifyNavigation = {
                    params: [
                        {
                            alias: 'spacing',
                            options: {
                                name: 'spacingNavigation',
                                css: {
                                    selector: ' .ecom-swiper-navigation'
                                }
                            }
                        },
                        {
                            type: 'line'
                        }
                    ],
                    remove: {
                        name: 'justify-content'
                    }
                };
            }
            let rs = [
                {
                    group_alias: "image",
                    options: {
                        group_name: "instagramImage",
                        group_title: "Image",
                        selector:
                            " .ecom-instagram .ecom-instagram--item figure,.ecom-instagram .ecom-instagram--item a"
                    }
                }
            ];

            if (this.instagram.settings.layout == 'grid') {
                rs.push({
                    group_name: "instagramGridStyle",
                    group_title: "Grid",
                    selector: " .ecom-instagram--container.ecom-base-instagram-grid",
                    params: [
                        {
                            type: "number",
                            name: "slider_spacing",
                            label: "Spacing",
                            options: {
                                responsive: true,
                                units: {
                                    'px': {
                                        min: 0,
                                        max: 100
                                    }
                                }
                            },
                            css: {
                                properties: {
                                    gap: "%value%"
                                },
                            }
                        }
                    ]
                });
            }

            if (this.instagram.settings.overlay_use) {
                rs.push({
                    group_alias: "background:overlaytext",
                    options: {
                        group_name: "instagramOverlay",
                        group_title: "Overlay",
                        selector: " .ecom-instagram .ecom-image-default"
                    }
                });
            }
            //console.log(checkNavPagination.length , 'check',this.isArrow(),this.isPagination());
            if(checkNavPagination.length) {
                rs.push({
                        group_alias:checkNavPagination,
                        options:{
                            group_title: 'Navigation',
                            selector:' .ecom-instagram--container'
                        },
                        modify: modifyNavigation

                });
            }
            return rs;
        }
    }
};
</script>
