<template>
    <div class="ecom-element ecom-product-single ecom-product-single__payment-method">
        <div class="ecom-product-single__playment-method-wrapper">
            <div class="ecom-product-single-payment-method-container">
                <div>
                    <ul
                        class="ecom-product-single__payment-method-list"
                        role="list"
                        :class="!exporting ? 'ecom-placeholder-on-builder-mode' : ''"
                        :data-ecom-placeholder="exporting ? '' : 'Your site don\'t have any payment methods'"
                        v-html="liquid('payment_methods')"
                    />
                </div>
                <p
                    class="ecom-product-single-payment-method-content"
                    v-html="lang(message, 'payment_methods_message')"
                />
            </div>
        </div>
    </div>
</template>

<script>
import { Liquid, Element } from '@/mixins';
export default {
    name: 'ProductPaymentMethod',
    
    mixins: [Liquid, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        liquids() {
            return {
                payment_methods: {
                    code: `
                        {%- for type in shop.enabled_payment_types -%}
                            <li class="ecom-product-single__payment-method-item">
                            {{ type | payment_type_svg_tag: class: 'ecom-product-single__payment-method-svg' }}
                            </li>
                        {%- endfor -%}
                    `,
                    preview: `
                    <div class="ecom-skeleton-item">
                        <div>
                            <div class="ecom-skeleton-row">
                                <div class="ecom-skeleton-col-4"></div>
                                <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-6"></div>
                                <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-2"></div>
                                <div class="ecom-skeleton-col-10 ecom-skeleton-empty"></div>
                            </div>
                        </div>
                    </div>
                `
                }
            };
        },

        settings() {
            return [
                {
                    params: [
                         {
                            type: 'number',
                            name:'spacing',
                            label:'Spacing',
                            options:{
                                responsive: true,
                                units:{
                                    px:{
                                        min:0,
                                        max:50
                                    }
                                }
                            },
                            css:{
                                selector: ' .ecom-product-single__payment-method-list',
                                properties: {
                                    gap: ''
                                }
                            }
                        },
                        {
                            type: 'textarea',
                            name: 'message',
                            label: 'Message',
                            value: 'Guarantee safe & secure checkout',
                            options: {
                                height: 80,
                                toolbar: 'short'
                            }
                        }
                    ]
                }
            ];
        },
        message() {
            return this.data && this.data.settings && this.data.settings.message
                ? this.data.settings.message
                : '';
        },

        style() {
            return [
                {
                    group_title: 'Payment',
                    group_name: 'payment_group',
                    selector:
                        ' .ecom-product-single__playment-method-wrapper .ecom-product-single__payment-method-list',
                    params: [
                        {
                            type: 'choose',
                            label: 'Alignment',
                            name: 'justify-content',
                            value: 'left',
                            options: {
                                oneline: true,
                                responsive: true,
                                type: 'text-align',
                                values: ['flex-start', 'center', 'flex-end']
                            }
                        },
                        {
                            name: 'spacing',
                            type: 'dimension',
                            label: 'Spacing',
                            options: {
                                responsive: true,
                                units: 'default'
                            }
                        },
                    ]
                },
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_title: 'Label',
                        group_name: 'payment_text',
                        selector:
                            ' .ecom-product-single__playment-method-wrapper .ecom-product-single-payment-method-content'
                    }
                }
            ];
        },

        css() {
            return `
                .ecom-product-single__payment-method-list{
                    padding:0;
                    display:flex;
                    flex-wrap:wrap;
                }
                .ecom-product-single__payment-method-list li{
                    display:inline-flex;
                }
                 
            `;
        },

        default() {
            return {
                settings: {
                    message: "Guarantee safe & secure checkout"
                },
                style: {
                    payment_group: {
                        "justify-content": "flex-start"
                    }
                }
            }
        }
    },
    methods: {}
};
</script>
