<template>
    <div class="ecom-element ecom-product-single ecom-product-single__metafield">
        <div class="ecom-product-single__metafield-wrapper">
            <div
                class="ecom-product-single__metafield-container"
                v-html="liquid('metafields')"
            />
        </div>
    </div>
</template>

<script>
    import { Liquid, Element } from '@/mixins';
    export default {
        name: 'ProductMetafield',
        
        mixins: [Liquid, Element],
        props: {
            data: {
                type: Object,
                default () {
                    return {};
                }
            }
        },
        computed: {

            liquids () {
                return {
                    metafields: {
                        code: `${this.code}`,
                        preview: `
                            <div class="ecom-skeleton-item">
                                <div>
                                    <div class="ecom-skeleton-row">
                                        <div class="ecom-skeleton-col-4"></div>
                                        <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                        <div class="ecom-skeleton-col-6"></div>
                                        <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                        <div class="ecom-skeleton-col-2"></div>
                                        <div class="ecom-skeleton-col-10 ecom-skeleton-empty"></div>
                                    </div>
                                </div>
                            </div>
                        `
                    }
                };
            },

            settings () {
                return [{
                    params: [
                        {
                            type: 'textarea',
                            label: 'Metafield namespace and key',
                            name: 'code',
                            value: '{{ product.metafields.ecomposer.countdown }}',
                            options: {
                                toolbar: false
                            },
                            placeholder: '{{ product.metafields.ecomposer.countdown }}',
                        }]
                }];
            },

            code () {
                return this.data && this.data.settings && this.data.settings.code ? this.data.settings.code : '';
            },

            requestShopifyType () {
                return {
                    shopify_type: 'product'
                };
            },

            style() {
                return [
                    {
                        group_alias: 'text:spacing',
                        options: {
                            group_title: '',
                            selector: ' .ecom-product-single__metafield-container'
                        },
                        modify: {
                            params: {
                                type: 'paragraph',
                                content: '** Text **'
                            }
                        }
                    }
                ];
            },

            default() {
                return {
                    settings: {
                        code: "{{ product.metafields.ecomposer.countdown }}"
                    },
                    style: {
                        textSpacing: {
                            textTextAlign: "left"
                        }
                    }
                }
            }
        },
        methods: {

        }

    };
</script>
