<template>
    <div class="ecom-element ecom-shopify-elements ecom-shopify__article--image ">
        <div class="ecom-shopify__article--image-wrapper" v-html="liquid('articleImage')" />
        <figcaption
            v-if="isHasCaption"
            class="ecom-image__caption" 
            v-html="captionText"
        />
    </div>
</template>

<script>
import { Liquid, Element } from '@/mixins';
 
export default {
    name: 'ArticleImage',
    
    mixins: [Liquid, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        captionText() {
            return this.data && this.data.settings && this.data.settings.caption ? this.data.settings.caption : '';
        },

        css() {
            return `
                .ecom-shopify__article--image-wrapper .ecom-image-default{
                    position:relative;
                }
                .ecom-shopify__article--image-wrapper .ecom-shopify__article-hero-portrait img,
                .ecom-shopify__article--image-wrapper .ecom-shopify__article-hero-square img {
                    height: 100%;
                    object-fit: cover;
                }
            `;  
        },
        isHasCaption () {
            return this.data && this.data.settings && this.data.settings.useCaption && this.data.settings.caption;
        },

        liquids() {
            return {
                articleImage: {
                    code: `
                        {% if article != empty %}
                            {% if article.image %}
                                <div class="ecom-shopify__article-hero-container ecom-image-align">
                                    <div class="ecom-shopify__article-hero-image ecom-image-default"
                                    itemprop="image"
                                    >
                                    <img srcset="
                                        {% if article.image.width >= 350 %}{{ article.image | img_url: '350x' }} 350w,{% endif %}
                                        {% if article.image.width >= 700 %}{{ article.image | img_url: '700x' }} 700w,{% endif %}
                                        {% if article.image.width >= 749 %}{{ article.image | img_url: '749x' }} 749w,{% endif %}
                                        {% if article.image.width >= 1498 %}{{ article.image | img_url: '1498x' }} 1498w,{% endif %}
                                        {% if article.image.width >= 1100 %}{{ article.image | img_url: '1100x' }} 1100w,{% endif %}
                                        {% if article.image.width >= 2200 %}{{ article.image | img_url: '2200x' }} 2200w,{% endif %}"
                                        sizes="(min-width: 1200px) 1100px, (min-width: 750px) calc(100vw - 10rem), 100vw"
                                        src="{{ article.image | img_url: '1100x' }}"
                                        loading="lazy"
                                        width="{{ article.image.width }}"
                                        height="{{ article.image.height }}"
                                        alt="{{ article.image.alt | escape }}">
                                    </div>
                                </div>
                            {% endif %}
                        {% else %}
                            <div>Please select the article in settings</div>
                        {% endif %}
                    `,
                    preview: `
                        <div class="ecom-skeleton-item">
                            <div>
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-4"></div>
                                    <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                    <div class="ecom-skeleton-col-6"></div>
                                    <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                    <div class="ecom-skeleton-col-2"></div>
                                    <div class="ecom-skeleton-col-10 ecom-skeleton-empty"></div>
                                </div>
                            </div>
                        </div>
                    `
                }
            };
        },

        requestShopifyType() {
            return {
                shopify_type: 'article'
            };
        },

        settings() {
            return [
                {
                    params: [
                        {
                            type: 'toggle',
                            name: 'useCaption',
                            label: 'Caption',
                            options: {
                                oneline: true,
                                values: {
                                    on: {
                                    label: 'YES',
                                    value: true
                                    },
                                    off: {
                                        label: 'NO',
                                        value: false
                                    }
                                }
                            },
                            css:false
                        },
                        {
                            type: 'text',
                            name: 'caption',
                            options: {
                                placeholder: 'Enter your caption',
                                visible: (data) => {
                                    return data['useCaption'];
                                }
                            }
                        },
                    ]
                }
            ];
        },

        default() {
            return {}
        }

    },
    methods:{
        style() {
            let style = [
                {
                    group_alias: 'image', 
                    options: {
                        group_title: 'Image',
                        group_name: 'Image',
                        selector: ' .ecom-shopify__article-hero-container'
                    }
                }
            ];
            if((this.data.settings.useCaption && this.data.settings.caption) ? true : false){
                style.push(
                    {
                        group_alias: 'text:spacing',
                        options: {
                            group_title: 'Caption',
                            group_name:'caption',
                            selector: ' .ecom-image__caption'
                        }
                    }
                );
            }
            return style;
        },
    }
};
</script>