<template>
    <div
        data-stopdrag
        :data-auto-slide-hover="autoSlideHover ? 'true' : 'false'"
        class="ecom-element ecom-base_image-comparison">
        <div class="ecom-image_compare-wrapper">
            <picture
                v-if="imgAfter"
                class="ecom-image_compare"
                v-html="$helpers.renImageResponsive(imgAfter ? imgAfter : '', screens)">
            </picture>


            <div
                v-if="hasOverlay"
                class="ecom-image_comp-overlay">
                <div v-if="data.settings?.beforeText" class="ecom-image_comp-before">
                    <span class="ecom-image_comp-text" v-html="lang(data.settings?.beforeText)"></span>
                </div>
                <div v-if="data.settings?.afterText" class="ecom-image_comp-after">
                    <span class="ecom-image_comp-text" v-html="lang(data.settings?.afterText)"></span>
                </div>
            </div>

           <div v-if="autoSlideHover" class="ecom-overlay_autoslide-fake"></div>

            <div class="ecom-image_ratio-wrapper">
                <div
                    v-if="imgBefore && imgAfter"
                    class="ecom-img_comp-slider pulse"
                    :style="{ 'left': ratioDefault }"
                    ref="ratioPoint">
                    <template v-if="iconSlide">
                        <span class="ecom-slide_icon" v-html="iconSlide"></span>
                    </template>
                    <div
                        v-else
                        class="ecom-slide_button">
                        <svg width="28" height="28" viewBox="0 0 44 44" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                            <path d="M16 14L7 22L16 30L16 14Z"/>
                            <path d="M28 30L37 22L28 14V30Z"/>
                        </svg>
                    </div>
                </div>
            </div>

            <picture
                v-if="imgBefore"
                ref="imgCompare"
                class="ecom-image_compare ecom-image_compare-overlay"
                v-html="$helpers.renImageResponsive(imgBefore ? imgBefore : '', screens)">
            </picture>
        </div>
    </div>
</template>

<script lang="ts">
import { Element, Javascript } from '@/mixins';
import { hover } from "@/plugins/fields/core/alias/icon";
import image from '@/plugins/fields/core/alias/image';

export default {
    name: 'Image',
    presets:false,
    docs: 'https://help.ecomposer.io/docs/elements/basic-elements/image/',
    mixins: [Element, Javascript],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        imageComp() {
            return this.data;
        },

        screens() {
            return this.$store.getters['builder/screens'];
        },
        // Get images
        imgBefore() {
            if(! this.data.settings) return;
            let image = this.data.settings?.beforeImage;
            return image;
        },
        imgAfter() {
            if(! this.data.settings) return;
            let image = this.data.settings?.afterImage;
            return image;
        },
        // Others
        ratioDefault() {
            return this.data.settings?.ratio;
        },
        hasOverlay() {
            return this.data.settings?.overlay;
        },
        iconSlide() {
            return this.data.settings?.icon;
        },
        autoSlideHover() {
            return this.data.settings?.autoSlideHover;
        },


        settings() {
            let settings =
            [
                {
                    group_title: 'General',
                    params: [
                        {
                            type: 'tab',
                            name: 'tab',
                            options: {
                                tabs: [
                                    {
                                        name: 'normal',
                                        title: 'Before'
                                    },
                                    {
                                        name: 'hover',
                                        title: 'After'
                                    }
                                ]
                            },
                            css: {
                                isCss: false
                            }
                        },
                        // Before tab content
                        {
                            type: 'picker',
                            name: 'beforeImage',
                            label: 'Choose Image',
                            options: {
                                responsive: true,
                                type: 'image',
                                editAlt: true,
                                visible: function(data : any) {
                                    return data['tab'] === 'normal'
                                }
                            }
                        },
                        // After tab content
                        {
                            type: 'picker',
                            name: 'afterImage',
                            label: 'Choose Image',
                            options: {
                                responsive: true,
                                type: 'image',
                                editAlt: true,
                                visible: function(data : any) {
                                    return data['tab'] === 'hover'
                                }
                            }
                        },
                        //
                        {
                            type:'number',
                            name:'ratio',
                            label:'Default Ratio',
                            options:{
                                units:{
                                    '%': {
                                        min:0,
                                        max:100
                                    }
                                },
                                reset:false,
                                responsive:true
                            },
                            css:
                                {
                                    selector: ' .ecom-image_compare.ecom-image_compare-overlay img',
                                    properties: {
                                        'clip-path':'polygon(%value% 0%, %value% 100%, 0% 100%, 0% 0%)',
                                    }
                                }
                        },
                        {
                            type: 'toggle',
                            name: 'autoSlideHover',
                            label: 'Auto slide on hover',
                            options: {
                                online: true,
                                values: {
                                    off: {
                                        label: 'NO',
                                        value: false
                                    },
                                    on: {
                                        label: 'YES',
                                        value: true
                                    }
                                }
                            }
                        },
                        {
                            type:'popup',
                            name:'orientation',
                            label:'Orientation',
                            options:{
                                visible: function(data : any) {
                                    return false;
                                },
                                default: false,
                                type: 'dropdown',
                                values: {
                                    horizontal: 'Horizontal',
                                    vertical: 'Vertical'
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            name: 'overlay',
                            label: 'Overlay',
                            options: {
                                online: true,
                                values: {
                                    off: {
                                        label: 'NO',
                                        value: false
                                    },
                                    on: {
                                        label: 'YES',
                                        value: true
                                    }
                                }
                            }
                        },
                        {
                            type: 'picker',
                            label: 'Icon',
                            name: 'icon',
                            options: {
                                type: 'icon',
                                layout: 'grid',
                                output: 'value',
                                multiple: false,
                                simple: false
                            }
                        },
                    ]
                }
            ];

            if(this.data.settings.overlay === true) {
                settings.push({
                    group_title: 'Overlay text',
                    params: [
                        {
                            type: 'text',
                            name: 'beforeText',
                            label: 'Before'
                        },
                        {
                            type: 'text',
                            name: 'afterText',
                            label: 'After'
                        },
                    ]
                    }
                );
            };

            return settings;

        },
        hasIcon() {
            return this.data.settings?.icon;
        },

        javascript() {

            return function () {
                const $el = this.$el;
                const _this = this;
                const isAutoSlide = this.settings.autoSlideHover;
                let w = '';
                var canHiddenOverlay = true;
                if(! $el) return;
                // Constants
                const btnSlide = $el.querySelector('.ecom-img_comp-slider');
                if(! btnSlide) return;
                const compImage = $el.querySelector('.ecom-image_compare-overlay img');
                const overlay = $el.querySelector('.ecom-image_comp-overlay');
                // Check if auto slide on hover mode is on

                // Overlay hanlde
                btnSlide.onmouseover = function() {
                    hiddenOverlay();
                }
                btnSlide.onmouseout = function() {
                    if(canHiddenOverlay) {
                        removeHiddenOverlay();
                    }
                }

                if(btnSlide && compImage) {
                    compareImages();
                }

                window.addEventListener('resize', function() {
                    if(btnSlide && compImage) {
                        w = compImage.offsetWidth;
                        compareImages();
                    }
                });

                // Overlay handle
                function hiddenOverlay() {
                    if(overlay) {
                        overlay.classList.add("overlay-hidden");
                    }
                }
                function removeHiddenOverlay() {
                    if(overlay) {
                        overlay.classList.remove("overlay-hidden");
                    }
                }
                // Functions handle for dragging
                function compareImages() {
                    var clicked = 0, touchPoint, initRatio, pos;
                    initSlide();

                    if(! isAutoSlide) {
                        /* Execute a function when the mouse button is pressed: */
                        btnSlide.onmousedown = function(e) {
                            slideReady(e);
                        }
                        /* And another function when the mouse button is released: */
                        $el.onmouseup = function() {
                            slideFinish();
                        }
                        /* Or touched (for touch screens: */
                        btnSlide.ontouchstart = function(e) {
                            slideReady(e);
                        };
                        /* And released (for touch screens: */
                        $el.ontouchend = function() {
                            slideFinish()
                        };
                    } else {
                        $el.onmouseover = function(e) {
                            slideReady(e);
                        }
                    }

                    function initSlide() {
                        if(btnSlide.style.left) {
                            w = compImage.offsetWidth;
                            initRatio = parseInt(btnSlide.style.left);
                        }
                    }

                    function slideReady(e) {
                        canHiddenOverlay = false;
                        /* Prevent any other actions that may occur when moving over the image: */
                        e.preventDefault();
                        if(e.clientX) {
                            touchPoint = e.clientX;
                        } else {
                            touchPoint = e.touches[0].clientX;
                        }
                        /* The slider is now clicked and ready to move: */
                        clicked = 1;
                        /* Execute a function when the slider is moved: */
                        $el.onmousemove = function(e) {
                            slideMove(e);
                        }
                        $el.ontouchmove = function(e) {
                            slideMove(e);
                        }

                    }
                    function slideFinish() {

                        /* The slider is no longer clicked: */
                        clicked = 0;
                        if(! isNaN(pos)) {
                            initRatio = pos;
                        }
                        canHiddenOverlay = true;
                        removeHiddenOverlay();
                    }
                    function slideMove(e) {
                        /* If the slider is no longer clicked, exit this function: */
                        if (clicked == 0) {
                            return false;
                        } else if (clicked == 1) {
                            hiddenOverlay();
                        }
                        /* Get the cursor's x position by percentage: */
                        pos = getCursorPos(e);

                        /* Prevent the slider from being positioned outside the image: */
                        if (pos < 0) pos = 0;
                        if (pos > 100) pos = 100;
                        /* Execute a function that will resize the overlay image according to the cursor: */
                        slide(pos);
                    }
                    function getCursorPos(e) {
                        e = (e.changedTouches) ? e.changedTouches[0] : e;
                        var cal;
                        if(! isAutoSlide) {
                            cal = Math.round(initRatio - ((touchPoint - e.pageX)*100/w));
                        } else {
                            cal = Math.round(e.offsetX*100/w);
                        }
                        return cal;
                    }
                    function slide(x) {
                        btnSlide.style.left = x + '%';
                        compImage.style.clipPath = `polygon(${x}% 0%, ${x}% 100%, 0% 100%, 0% 0%)`;
                    }
                }
            };
        },

        css() {
            return `
                .ecom-base_image-comparison {
                    overflow: hidden;
                }
                .ecom-image_compare-wrapper {
                    position: relative;
                }

                .ecom-overlay_autoslide-fake {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    z-index: 15;
                    cursor: ew-resize;
                }

                .ecom-image_compare {
                    height: 100%;
                    width: 100%;
                }

                .ecom-image_compare.ecom-image_compare-overlay {
                    position: absolute;
                    width: auto;
                    height: 100%;
                    overflow: hidden;
                    width: 100%;
                    inset: 0;
                }

                .ecom-image_compare picture {
                    width: 100%;
                }

                .ecom-base_image-comparison .ecom-image_comp-overlay {
                    position: absolute;
                    inset: 0;
                    z-index: 8;
                    opacity: 0;
                    visibility: hidden;
                    transition: .35s all ease-in-out;
                }
                .ecom-base_image-comparison:hover .ecom-image_comp-overlay {
                    opacity: 1;
                    visibility: visible;
                }

                .ecom-base_image-comparison .ecom-image_comp-overlay.overlay-hidden {
                    opacity: 0;
                    visibility: hidden;
                }

                .ecom-image_comp-before,
                .ecom-image_comp-after {
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    z-index: 9;
                    min-height: 38px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background: rgba(255,255,255,.2);
                    padding: 0 20px;
                    -webkit-border-radius: 2px;
                    -moz-border-radius: 2px;
                    border-radius: 2px;
                    color: #fff;
                }

                .ecom-image_comp-before {
                    left: 10px;
                }

                .ecom-image_comp-after {
                    right: 10px;
                }

                .ecom-img_comp-slider {
                    position: absolute;
                    min-width: 45px;
                    min-height: 45px;
                    cursor: ew-resize;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    transform: translate(-50%,-50%);
                    background: #fff;
                    border-radius: 100%;
                }

                .pulse {
                   animation: pulse-animation 1s ease-in-out 0s infinite alternate;
                }

                @keyframes pulse-animation {
                    0% {
                        box-shadow: 0px 0px 2px rgba(61, 61, 61, 0.8), 0 0 0 4px rgba(255, 255, 255, 0.15)
                    }
                    100% {
                        box-shadow: 0px 0px 2px rgba(61, 61, 61, 0.8), 0 0 0 12px rgba(255, 255, 255, 0.3)
                    }
                }

                .ecom-img_comp-slider .ecom-slide_icon {
                    display: flex;
                    z-index: 9;
                    padding: 12px;
                }

                .ecom-img_comp-slider::before {
                    content: '';
                    position: absolute;
                    display: block;
                    height: 9999px;
                    width: 4px;
                    left: 50%;
                    top: 100%;
                    background: #fff;
                    transform: translateX(-50%);
                    z-index: 8;
                }

                .ecom-img_comp-slider::after {
                    content: '';
                    position: absolute;
                    display: block;
                    height: 9999px;
                    width: 4px;
                    left: 50%;
                    bottom: 100%;
                    background: #fff;
                    transform: translateX(-50%);
                    transform-origin: top center;
                    z-index: 8;
                }

                .ecom-slide_button {
                    padding: 8px;
                    display: inline-flex;
                    width: 100%;
                    align-items: center;
                    justify-content: center;
                }

                .ecom-slide_button > * + * {
                    margin-left: 8px;
                }

                span.ecom-slide_arrow {
                    width: min-content;
                    height: min-content;
                    display: block;
                }

                .ecom-slide_arrow--left,
                .ecom-slide_arrow--right {
                    border: 6px solid transparent;
                    border-right: 0;
                    border-left-color: currentColor;
                }

                .ecom-slide_arrow--left {
                    transform: rotate(180deg);
                }

                .ecom-image_compare-wrapper .ecom-image_compare img {
                    width: 100%;
                    height: 100%;
                    display: block;
                    vertical-align: middle;
                    object-fit: cover;
                    object-position: center center;
                }

                .ecom-image_ratio-wrapper {
                    width: 100%;
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    z-index: 9;
                }

            `;
        },
        default(){
            return {
                settings: {
                    tab: "normal",
                    beforeImage: {
                        value: "https://cdn.shopify.com/s/files/1/0629/7318/2186/files/com1com1.jpg?v=1661481717",
			            thumbnail: "https://cdn.shopify.com/s/files/1/0629/7318/2186/files/com1com1.jpg?v=1661481717",
                    },
                    afterImage: {
                        value: "https://cdn.shopify.com/s/files/1/0629/7318/2186/files/com2com2.jpg?v=1661481843",
			            thumbnail: "https://cdn.shopify.com/s/files/1/0629/7318/2186/files/com2com2.jpg?v=1661481843"
                    },
                    ratio: "50%",
                    overlay: true,
                    beforeText: "Before",
                    afterText: "After",
                },
                style: {
                    general: {
                        totalHeight: "520px"
                    },
                    icon: {
                        iconFontSize: "22px",
                        color: "#4b00e7"
                    },
                    overlay: {
                        normalBackground: {
                            classic: {
                                "background-color": "rgba(0, 0, 0, 0.35)"
                            }
                        }
                    }
                }
            }
        }
    },
    watch: {
        imgBefore: {
            deep: true,
            handler: function (newVal) {
                this.refresh();
            }
        },
        imgAfter: {
            deep: true,
            handler: function (newVal) {
                if(this.$refs.imgCompare) {
                    var img = this.$refs.imgCompare.querySelector("img");
                }

                if(! newVal) {
                    if(img) {
                        img.style.clipPath = "unset";
                    }
                } else {
                    this.refresh();
                }
            }
        },
        'data.settings.overlay': {
            deep: true,
            handler: function() {
                this.refresh();
            }
        },
        'data.settings.ratio': {
            deep: true,
            handler: function(newVal) {
                if(this.$refs.ratioPoint) {
                    this.$refs.ratioPoint.style.left = newVal;
                    if(this.$refs.imgCompare) {
                        var img = this.$refs.imgCompare.querySelector("img");
                        if(img) {
                            img.style.clipPath = `polygon(${newVal} 0%, ${newVal} 100%, 0% 100%, 0% 0%)`;
                        }
                    }
                }
            }
        },
        'data.settings.icon': {
            deep: true,
            handler: function() {
                this.refresh();
            }
        },
        'data.settings.autoSlideHover': {
            deep: true,
            handler: function() {
                this.refresh();
            }
        }
    },
    methods: {
        refresh() {
            this.imageComp.refresh = this.$helpers.randid();
        },

        style() {
            let style = [];

            style.push({
                group_title: 'General',
                group_name: 'general',
                params: [
                    // {
                    //     type: 'number',
                    //     label: 'Element height',
                    //     name: 'totalHeight',
                    //     options: {
                    //         responsive: true,
                    //         units: {
                    //             px: {
                    //                 min: 0,
                    //                 max: 2000,
                    //                 step: 10
                    //             }
                    //         }
                    //     },
                    //     css: {
                    //         selector: " .ecom-image_compare-wrapper",
                    //         properties: {
                    //             height: ""
                    //         }
                    //     }
                    // },
                    {
                        type: "popup",
                        name: "iamgeBorder",
                        label: "Border",
                        options: {
                            oneline: true,
                            type: "border",
                            size: "small"
                        },
                        css: {
                            selector: ""
                        }
                    },
                    {
                        type: "dimension",
                        name: "imageBorderRadius",
                        label: "Border Radius",
                        options: {
                            type: "radius",
                            responsive: true,
                            units: "default"
                        },
                        css: {
                            selector: "",
                            properties: {
                                "border-radius": "",
                                overflow:'hidden',

                            }
                        }
                    },
                    {
                        type: "popup",
                        name: "imageBoxShadow",
                        label: "Box Shadow",
                        options: {
                            oneline: true,
                            type: "box-shadow"
                        },
                        css: {
                            selector: "",
                        }
                    },
                    {
                        name: 'spacing',
                        type: 'dimension',
                        label: 'Spacing',
                        options: {
                            responsive: true,
                            units: {
                                margin: {
                                    px: {
                                        min: -1000,
                                        max: 1000
                                    },
                                    '%': {
                                        min: -100,
                                        max: 100
                                    },
                                    em: {
                                        min: -100,
                                        max: 100
                                    },
                                    rem: {
                                        min: -100,
                                        max: 100
                                    }
                                },
                                padding: {
                                    px: {
                                        min: 0,
                                        max: 1000
                                    },
                                    '%': {
                                        min: 0,
                                        max: 100
                                    },
                                    em: {
                                        min: 0,
                                        max: 100
                                    },
                                    rem: {
                                        min: 0,
                                        max: 100
                                    }
                                }

                            }
                        },
                        css: {
                            selector: ""
                        }
                    }
                ]
            });

            if(this.hasOverlay) {
                style.push(
                {
                    group_title: "Overlay",
                    group_name: "overlay",
                    params: [
                        {
                            type: "background",
                            label: "Background",
                            name: "normalBackground",
                            options: {
                                oneline: true,
                                responsive: true
                            },
                            css: {
                                selector: " .ecom-image_comp-overlay",
                                properties: {
                                    background: ""
                                }
                            }
                        }
                    ]
                },
                {
                    group_title: 'Overlay Text',
                    group_name: 'overlayText',
                    selector: ' .ecom-image_comp-text',
                    params: [
                        {
                            type: 'popup',
                            label: 'Typography',
                            name: 'textTypography',
                            options: {
                                global: {
                                    type: 'typography'
                                },
                                oneline: true,
                                responsive: true,
                                type: 'typography'
                            }
                        },
                        {
                            name: 'textColor',
                            label: 'Text Color',
                            type: 'color',
                            options: {
                                oneline: true,
                                global: {
                                    type: 'colors'
                                }
                            },
                            css: {
                                properties: {
                                    'color': ''
                                },
                            }
                        },
                        {
                            name: 'textTextShadow',
                            label: 'Text Shadow',
                            type: 'popup',
                            options: {
                                oneline: true,
                                type: 'text-shadow'
                            }
                        },
                    ]
                }
                );
            }

            style.push({
                group_title: 'Spacing bar',
                group_name: 'spacingBar',
                params: [
                    {
                        name: "color",
                        label: "Color",
                        type: "color",
                        options: {
                            oneline: true,
                            global: {
                                type: "colors"
                            }
                        },
                        css: {
                            selector: " .ecom-img_comp-slider, root .ecom-img_comp-slider::before, root .ecom-img_comp-slider::after",
                            properties: {
                                "background-color": ""
                            }
                        }
                    },
                    {
                        type: "popup",
                        name: "boxShadow",
                        label: "Box Shadow",
                        options: {
                            oneline: true,
                            type: "box-shadow"
                        },
                        css: {
                            selector: " .ecom-img_comp-slider, root .ecom-img_comp-slider::before, root .ecom-img_comp-slider::after",
                        }
                    },
                    {
                        type: 'number',
                        label: 'Size',
                        name: 'size',
                        options: {
                            units: {
                                px: {
                                    min: 0,
                                    max: 20,
                                    step: 1
                                }
                            }
                        },
                        css: {
                            selector: " .ecom-img_comp-slider::before, root .ecom-img_comp-slider::after",
                            properties: {
                                width: ""
                            }
                        }
                    },
                ]
            })
            let icon_settings = [ ...hover('root .ecom-img_comp-slider:hover').params ].splice(0, 13);
            if(this.hasIcon) {
                style.push({
                    group_title:'Spacing Icon',
                    group_name:'icon',
                    selector:' .ecom-img_comp-slider',
                    params:[
                        ...icon_settings
                    ],
                    modify: {
                        remove: {index: 1, length: 4}
                    }
                })
            } else {
                style.push({
                    group_title:'Spacing Icon',
                    group_name:'icon',
                    selector: ' .ecom-slide_button svg',
                    params:[
                        {
                            name: "color",
                            label: "Color",
                            type: "color",
                            options: {
                                oneline: true,
                                global: {
                                    type: "colors"
                                }
                            },
                            css: {
                                properties: {
                                    fill: ""
                                }
                            }
                        },
                        {
                            type: 'number',
                            label: 'Size',
                            name: 'size',
                            options: {
                                units: {
                                    px: {
                                        min: 0,
                                        max: 150,
                                        step: 1
                                    }
                                }
                            },
                            css: {
                                properties: {
                                    width: '',
                                    height: ''
                                }
                            }
                        },
                    ]
                })
            }

            return style;
        },

    }
};
</script>
