        :deep="deep"
        :index="index"
        :section_id="section_id"
        :gclasses="{
            wrapper: `tabs__wrapper ecom__element ecom__tabs tabs__wrapper--horizontal ${
                tabs.settings &&
                tabs.settings['position'] &&
                tabs.settings['position'] === 'vertical'
                    ? `tabs__wrapper--${tabs.settings['position']}`
                    : ''
            }`,
            body: 'tabs__body',
            item: 'tab__item'
        }"
        :gstyles="{
            items:styleVertical?.tabItem
        }"
    >
        <template #before="payload">
            <div
                class="tabs__navs"
                :class="
                    tabs.settings && tabs.settings['position']
                        ? `tabs__navs--${tabs.settings['position']}`
                        : ''
                "
                :style="styleVertical?.tabNav"
            >
              <div class="tabs__navs--items" :class="{'ecom-position-sticky': tabs.settings.tabs_sticky}">
                <div
                    v-for="(item, i) in payload.items"
                    :key="item.id"
                    :id="item.tab_id"
                    class="ecom-items tabs__nav"
                    :class="{
                        'ecom-item-active': (i === 0)
                    }"
                    :data-target="item.tab_id ? '#' + item.tab_id : ''"
                    @click="setEditingElement(i)"
                    @contextmenu="tabs.actived = item.id"
                >
                    <div
                        v-if="item.picker && item.picker.value"
                        class="tabs_nav--icon ecom-items--icon"
                        :style="{
                            order: item.iconPosition
                        }"
                        v-html="item.picker && item.picker.value"
                    />
                    <div class="tabs_nav--content_right">
                        <div class="tabs_nav--content">
                            <component
                                :is="item.tag ? item.tag : 'div'"
                                class="tabs_nav--text ecom-items--text"
                                v-html="lang(item.title, 'tab-title-' + i)"
