                    width:24px;
                    height:auto;
                }
                .tabs__navs--horizontal, .tabs__navs--items{
                    display:flex;
                    overflow-x:auto;
                    overflow: -moz-scrollbars-none;
                    -ms-overflow-style: none;
                }
                .tabs__navs--horizontal::-webkit-scrollbar {
                    width: 0 !important;
                    display: none;
