                      "border-style": "solid",
                      "border-width": {
                        "bottom": "2px"
                      },
                      "border-color": "#333"
                    },
                    "boxBorderactivemode": {
                      "border-style": "solid",
                      "border-width": {
                        "bottom": "2px"
                      },
                      "border-color": "#333"
                    }
                  },
                  "texticon": {
                    "texttab": "active",
                    "textTypography": {
                      "font-family": {
                        "name": "Jost",
                        "value": "https://fonts.googleapis.com/css?family=Jost:100,200,300,400,500,600,700,800,900",
                        "thumbnail": "Jost"
                      },
                      "font-size": "20px",
                      "font-weight": "500"
                    },
                    "textColornormalmode": "#b8b8b8",
                    "textColorhovermode": "#111",
                    "textColoractivemode": "#111"
                  },
                  "tabs_content": {
                    "spacing": {
                      "padding": {
                        "left": "15px",
                        "top": "10px",
                        "bottom": "10px",
                        "right": "15px"
                      }
                    },
                    "boxBorder": {
                      "border-style": "solid",
                      "border-color": "rgba(5, 150, 105, 0.1)"
                    },
                    "spacing__mobile": {
                      "padding": {
                        "left": "20px",
                        "top": "20px",
                        "bottom": "20px",
                        "right": "20px"
                      },
                      "margin": {}
                    }
                  }
                },
            };
        }
    },
    methods: {
        setEditingElement(i) {
            this.$store.commit('builder/setEditingElement', this);
            if( !'tabOpened' in this.data.settings.items)
                {
                    Object.defineProperty( this.data.settings.items, "tabOpened", { enumerable: false, writable: true } );

                }
                this.data.settings.items.tabOpened = i;
        },
        check(val) {
            return this.data.settings?.items?.filter((x) => x[val]).length;
        },
        style() {
            let check = [];
            let title = "";
            let items_alignemt_type = "align-full";

            if (this.check("title") && this.check("picker")) {
                title = "Title & Icon";
                check.push(
                    {
                        title: "Title",
                        type: "items:text"
                    },
                    {
                        title: "Icon",
                        type: "items:icon"
                    }
                );
            } else {
                if (this.check("title")) {
                    title = "Title";
                    check.push({
                        title: "Title",
                        type: "items:text"
                    });
                }
                if (this.check("picker")) {
                    title = "Icon";
                    check.push({
                        title: "Icon",
                        type: "items:icon",
                    });
                }
            }
            let modify = "";
            if (this.data.settings?.position == "vertical") {
                modify = {
                    params: [
                        {
                            position: 1,
                            fields: {
                                alias: "text-align",
                                options: {
                                    label: "Alignment"
                                }
                            }
                        }
                    ]
                };

                items_alignemt_type = "align-y-full";
            }

            let style = [
                {
                    group_alias: "items",
                    options: {
                        group_name: "general",
                        selector: " .tabs__wrapper.ecom__element .tabs__nav"
                    },
                    modify: {
                        params: [
                            {
                                position: 3,
                                fields: {
                                    name: 'box-shadow',
                                    label: 'Box Shadow',
                                    type: 'popup',
                                    options: {
                                        oneline: true,
                                        type: 'box-shadow',
                                        visible: {
                                            keep_data: true,
                                            condition: (data) => {
                                                return data['itemtab'] === 'normal';
                                            }
                                        }
                                    },
                                    css: {
                                        selector: "root .tabs__nav"
                                    }
                                }
                            },
                            {
                                position: 8,
                                fields: {
                                    name: 'box-shadow-hover',
                                    label: 'Box Shadow',
                                    type: 'popup',
                                    options: {
                                        oneline: true,
                                        type: 'box-shadow',
                                        visible: {
                                            keep_data: true,
                                            condition: (data) => {
                                                return data['itemtab'] === 'hover';
                                            }
                                        }
                                    },
                                    css: {
                                        selector: "root .tabs__nav:hover"
                                    }
                                }
                            },
                            {
                                position: 13,
                                fields: {
                                    name: 'box-shadow-active',
                                    label: 'Box Shadow',
                                    type: 'popup',
                                    options: {
                                        oneline: true,
                                        type: 'box-shadow',
                                        visible: {
                                            keep_data: true,
                                            condition: (data) => {
                                                return data['itemtab'] === 'active';
                                            }
                                        }
                                    },
                                    css: {
                                        selector: "root .tabs__nav.ecom-item-active"
                                    }
                                }
                            },
                            {
                                fields: {
                                    name: "justifyContent",
                                    label: "Alignment",
                                    type: "choose",
                                    options: {
                                        responsive: true,
                                        type: items_alignemt_type,
                                        values: [
                                            "flex-start",
                                            "center",
                                            "flex-end",
                                            "space-between"
                                        ]
                                    },
                                    css: {
                                        selector: "root .tabs__wrapper.ecom__element .tabs__navs",
                                        properties: {
                                            "justify-content": ""
                                        }
                                    }
                                }
                            },
                            {
                                position: 20,
                                fields: [
                                    {
                                        type: "line"
                                    },
                                    {
                                        alias: "spacing"
                                    }
                                ]
                            }
                        ]
                    }
                },
                {
                    group_alias: check,
                    options: {
                        group_name: "texticon",
                        group_title: title,
                        selector: " .tabs__wrapper.ecom__element"
                    },
                    modify: {
                        params: [
                            {
                                position: 1,
                                fields: {
                                    alias: "align-items",
                                    options: {
                                        label: "Alignment",
                                        css: {
                                            selector:' .tabs__nav'
                                        }
                                    },
                                }
                            },
                            {
                                position: 6,
                                fields:
                                {
                                    type: 'dimension',
                                    label: 'Spacing',
                                    name: 'spacing_title',
                                    options: {
                                        visible: {
                                            keep_data: true,
                                            condition: (data) => {
                                                return data["texttab"] === "normal";
                                            }
                                        },
                                        responsive: true,
                                        units: 'default'
                                    },
                                    css: {
                                        selector: ' .tabs_nav--text',
                                        properties: {
                                            spacing: ''
                                        }
                                    }
                                }
                            },
                            {
                                position: 11,
                                fields:
                                {
                                    type: 'dimension',
                                    label: 'Spacing',
                                    name: 'spacing_title_hover',
                                    options: {
                                        visible: {
                                            keep_data: true,
                                            condition: (data) => {
                                                return data["texttab"] === "hover";
                                            }
                                        },
                                        responsive: true,
                                        units: 'default'
                                    },
                                    css: {
                                        selector: ' .ecom-items:hover .tabs_nav--text',
                                        properties: {
                                            spacing: ''
                                        }
                                    }
                                }
                            },
                            {
                                position: 20,
                                fields: {
                                    type: "popup",
                                    label: "Transition",
                                    name: "transition",
                                    options: {
                                        visible: {
                                            keep_data: true,
                                            condition: (data) => {
                                                return data["texttab"] === "hover";
                                            }
                                        },
                                        type: "transitions",
                                    },
                                    css: {
                                        selector: "root .tabs_nav--text.ecom-items--text",
                                    }
                                },
                            },
                            {
                                position: 12,
                                fields:
                                {
                                    type: 'dimension',
                                    label: 'Spacing',
                                    name: 'spacing_title_active',
                                    options: {
                                        visible: {
                                            keep_data: true,
                                            condition: (data) => {
                                                return data["texttab"] === "active";
                                            }
                                        },
                                        responsive: true,
                                        units: 'default'
                                    },
                                    css: {
                                        selector: ' .ecom-item-active .tabs_nav--text',
                                        properties: {
                                            spacing: ''
                                        }
                                    }
                                }
                            },
                            {
                                position: 20,
                                fields: {
                                    alias: "align-items",
                                    options: {
                                        label: "Icon alignment",
                                        name: 'icon_alignment',
                                        css: {
                                            selector:' .tabs_nav--icon',
                                            properties: {
                                                'align-self': ''
                                            }
                                        }
                                    }
                                }
                            },
                            {
                                position: 21,
                                fields: [
                                    {
                                      name: 'margin',
                                      type: 'dimension',
                                      label: 'Margin',
                                      options: {
                                          responsive: true,
                                          simple: true,
                                          units: 'default',
                                          visible: {
                                            keep_data: true,
                                            condition: (data) => {
                                                return data["tab"] === "normal";
                                            }
                                        }
                                      },
                                      css: {
                                          selector:' .tabs_nav--icon',
                                          properties: {
                                              margin: ''
                                          }
                                      }
                                  }
                                ]
                            }
                        ]
                    }
                },
                {
                    group_alias: 'items:sub',
                    options: {
                        group_name: 'sub_title',
                        group_title: 'Sub title',
                        selector: ' .tabs_nav--sub-text'
                    },
                    modify: {
                        params: [
                            {
                                position: 1,
                                fields: {
                                    alias: "justify-content",
                                    options: {
                                        label: "Alignment"
                                    },
                                }
                            },
                            {
                                position: 2,
                                fields: [
                                    {
                                        type: 'background',
                                        label: 'Text Gradient',
                                        name: 'sub_gradient',
                                        options: {
                                            oneline: true,
                                            reset: true,
                                            types: ['gradient'],
                                        },
                                        css:{
                                            properties:{
                                                'background':'',
                                                ' -webkit-background-clip': 'text',
                                                '-webkit-text-fill-color': 'transparent'
                                            }
                                        }
                                    }
                                ]
                            },
                            {
                                position: 6,
                                fields:
                                {
                                    type: 'dimension',
                                    label: 'Spacing',
                                    name: 'spacing_sub',
                                    options: {
                                        visible: {
                                            keep_data: true,
                                            condition: (data) => {
                                                return data["subTitleTab"] === "normal";
                                            }
                                        },
                                        responsive: true,
                                        units: 'default'
                                    },
                                    css: {
                                        selector: 'root .ecom-items--sub-text',
                                        properties: {
                                            spacing: ''
                                        }
                                    }
                                }
                            },
                            {
                                position: 11,
                                fields:
                                {
                                    type: 'dimension',
                                    label: 'Spacing',
                                    name: 'spacing_sub_hover',
                                    options: {
                                        visible: {
                                            keep_data: true,
                                            condition: (data) => {
                                                return data["subTitleTab"] === "hover";
                                            }
                                        },
                                        responsive: true,
                                        units: 'default'
                                    },
                                    css: {
                                        selector: 'root .ecom-items.tabs__nav:hover .ecom-items--sub-text',
                                        properties: {
                                            spacing: ''
                                        }
                                    }
                                }
                            },

                            {
                                position: 12,
                                fields:
                                {
                                    type: 'dimension',
                                    label: 'Spacing',
                                    name: 'spacing_sub_active',
                                    options: {
                                        visible: {
                                            keep_data: true,
                                            condition: (data) => {
                                                return data["subTitleTab"] === "active";
                                            }
                                        },
                                        responsive: true,
                                        units: 'default'
                                    },
                                    css: {
                                        selector: 'root .ecom-item-active .ecom-items--sub-text',
                                        properties: {
                                            spacing: ''
                                        }
                                    }
                                }
                            }
                        ]
                    }
                },
                {
                    group_alias: "box",
                    options: {
                        group_name: "tabs_content",
                        group_title: "Content",
                        selector: " .tabs__body"
                    },
                    modify: {
                        params: [
                            {
                                position: 15,
                                fields: [
                                    {
                                        alias: "spacing"
                                    }
                                ]
                            }
                        ]
                    }
                }
            ];

            return style;
        }
    },
    watch: {
        'data.settings.items.tabOpened': function(index){
            if(index >= 0)
            {
                setTimeout(()=>{
                    let dom = this?.$el?.querySelector('.tabs__navs > div:nth-of-type('+(index+1)+')');
                    if(dom && !dom.classList.contains('ecom-item-active'))
                    {
                        dom.click();
                    }
                },200)
            }
        },
    },
};
</script>