<template>
    <div class="ecom-element ecom-collection ecom-collection__sorting">
        <div class="ecom-collection__sorting-wrapper">
            <div class="ecom-collection__sorting-container">
                <h3
                    v-if="data.settings.title"
                    class="ecom-collection__filters-heading"
                    v-html="lang(data.settings.title, 'sortting_title')"
                />
                <select
                    name="sort_by"
                    class="ecom-collection__sorting-select"
                    aria-describedby="a11y-refresh-page-message"
                    v-html="liquid('sorting')"
                />
            </div>
        </div>
    </div>
</template>

<script>
import { Liquid, Javascript, Element } from '@/mixins';
export default {
    name: 'CollectionSort',
    docs: 'https://help.ecomposer.io/docs/elements/collection-elements/sorting/',
    presets:true,
    mixins: [Liquid, Javascript, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        liquids() {
            return {
                sorting: {
                    code: `
                        {%- assign sort_by = collection.sort_by | default: collection.default_sort_by -%}
                        {%- for option in collection.sort_options -%}
                            <option value="{{ option.value | escape }}"{% if option.value == sort_by %} selected="selected"{% endif %}>{{ option.name | escape }}</option>
                        {%- endfor -%}
                    `,
                    preview: ''
                }
            };
        },

        settings() {
            return [
                {
                    params: [
                        {
                            type: 'text',
                            name: 'title',
                            label: 'Label',
                            value: 'Sort by',
                            options: {
                                placeholder: 'Sort by'
                            }
                        },
                        {
                            name: 'sortStyle',
                            label: 'Layout',
                            type: 'popup',
                            options: {
                                type: 'dropdown',
                                values: {
                                    column: 'Vertical',
                                    row: 'Horizontal'
                                }
                            },
                            css: {
                                selector: ' .ecom-collection__sorting-container',
                                properties: {
                                    'flex-direction': ''
                                }
                            }
                        }
                    ]
                }
            ];
        },

        javascript() {
            return function () {
                if (this.isLive) {
                    this.$el
                        .querySelector('[name="sort_by"]')
                        .addEventListener('change', function (e) {
                            window.EComposer.queryParams.sort_by = e.target.value;
                            window.location.search = new URLSearchParams(
                                window.EComposer.queryParams
                            ).toString();
                        });
                }
            };
        },
        requestShopifyType() {
            return {
                shopify_type: 'collection'
            };
        },
        css() {
            return `
            .ecom-collection__sorting-wrapper{
                display:flex
            }
                .ecom-collection__filters-heading {
                    display: block;
                    white-space:nowrap;
                }
                .ecom-collection__sorting-select {
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    outline:none;
                }
                .ecom-collection__sorting-container{
                    display:inline-flex;
                    flex-direction:column
                }
            `;
        },
        default() {
            return {
                settings: {
                    title: "Sort by",
                    sortStyle: "column"
                }
            }
        }
    },
    methods: {
        style() {
            let modify = {}
            if(this.data.settings.sortStyle == 'row'){
                modify = {
                    remove:{
                        index:0,
                        length:1
                    },
                    params:[
                        {
                            position:1,
                            fields:{
                                alias:'align-items',
                                options:{
                                    label:'Alignment',
                                    options:{
                                        values:['start','center','end']
                                    },
                                    css:{
                                        properties:{
                                            display:'flex',
                                            'align-items':''
                                        }
                                    }
                                }
                            }
                        }
                    ]
                }
            }
            let style = [
                {
                    group_alias: 'box',
                    options: {
                        group_name: 'general',
                        group_title: 'General',
                        selector: ' .ecom-collection__sorting-wrapper',
                    },
                    modify: {
                        params: [
                            {
                                fields: {
                                    name: 'alignment',
                                    label: 'Alignment',
                                    type: 'choose',
                                    options: {
                                        type: 'text-align',
                                        values: ['flex-start', 'center', 'flex-end']
                                    },
                                    css: {
                                        properties: {
                                            display: 'flex',
                                            'justify-content': ''
                                        }
                                    }
                                }
                            },
                            {
                                position: 20,
                                fields: {
                                    alias: 'spacing'
                                }
                            }
                        ]

                    }
                },
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_name: 'label',
                        group_title: 'Label',
                        selector: ' .ecom-collection__filters-heading'
                    },
                    modify
                },
                {
                    group_alias: 'input',
                    options: {
                        group_name: 'sorting',
                        group_title: 'Sorting',
                        selector: ' .ecom-collection__sorting-select'
                    },
                    modify: {
                        remove: {
                            index: 3, length: 1
                        }
                    }
                }
            ]
            return style;
        }
    }
};
</script>
