<template>
    <div class="ecom-element ecom-product-single ecom-product-single__add-to-cart">
        <div class="ecom-product-single__add-to-cart-wrapper">
            <div class="ecom-product-single__add-to-cart-container">
                <div v-if="data.settings?.payment_terms" class="ecom-product-single__add-to-cart-payment-terms" v-html="liquid('payment_terms')" />
                <div class="ecom-product-single__add-to-cart-buttons">
                    <div class="ecom-product-single__add-to-cart-buttons-wrapper">
                        <button type="submit" class="ecom-product-single__add-to-cart--submit ecom-flex"
                            :class="animatedClass"
                            :data-text-add-cart="lang(data.settings?.add_to_cart, 'add_to_cart')"
                            :data-text-pre-order="lang(data.settings?.pre_order, 'pre_order')"
                            :data-text-unavailable="lang(data.settings?.product_unavailable, 'product_unavailable')"
                            :data-text-outstock="lang(data.settings?.product_outstock, 'out_stock')"
                            :data-text-added-cart="lang(data.settings?.added_cart_text, 'added_cart_text')"
                            :data-message-added-cart="lang(data.settings?.added_cart_message, 'added_cart_message')"
                            v-bind="attrLink"
                            :data-action="data?.settings?.action ?? 'popup'"
                            :data-variant-id="liquid('variant_id')"
                        >
                            <span
                                class="ecom-add-to-cart-text"
                                v-html="lang(data.settings.add_to_cart, 'add_to_cart')"
                            />
                            <span
                                class="ecom__element--button-icon"
                                v-html="data.settings?.icon"
                            />
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Liquid, Javascript, Element } from '@/mixins';
export default {
    name: 'AddToCart',

    presets:true,
    mixins: [Liquid, Javascript, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {

                };
            }
        }
    },
    data(){
        return {
            jsreactives: [
                'add_to_cart', 'product_unavailable','product_outstock','animation', 'animation_loop_time'
            ]
        }
    },
    computed: {
        javascript() {
            return function () {
                if(!this.$el)
                {
                    return false;
                }
                const $el = this.$el;
                const $wrapper = $el.closest('.ecom-product-form--single');
                if (!$wrapper) {
                    return false;
                }
                    const selector = $wrapper.querySelector('[name="id"]');
                if (!this.isLive) {
                    if ($wrapper) {
                        if (selector) {
                            selector.dispatchEvent(new window.Event('ecomUpdate'));
                        }
                    }
                } else {
                    const $addCartButton = $el.querySelector('.ecom-product-single__add-to-cart--submit'),
                          form = $el.closest('form');
                    if(!form) return;

                    if(!form.querySelector('select[name=id]')) {
                        let product = null;
                        const product_id = form.dataset.product_id;

                        if(!product_id) return;
                        const json = form.querySelector('[id^=product-json-' + product_id + ']');

                        if (!json) {
                            return;
                        }
                        try {
                            product = JSON.parse(json.innerHTML);
                        } catch (e) {
                            return;
                        }

                        let variant = product.variants[0];

                        if(variant) {

                            const AtcText = $addCartButton.querySelector('.ecom-add-to-cart-text');
                            if(!AtcText) return;

                            if(variant.available == false) {
                                $addCartButton.setAttribute('disabled', 'disabled');
                                AtcText.innerText = $addCartButton.dataset.textOutstock;
                            } else if(variant.inventory_quantity <= 0 && variant.inventory_policy == 'continue') {
                                AtcText.innerText = $addCartButton.dataset.textPreOrder;
                            }

                        }
                    }

                    if(!selector && $addCartButton && $addCartButton.dataset.variant_id)
                    {
                        const dom = document.createElement('input');
                        dom.type ='hidden';
                        dom.value = $addCartButton.dataset.variant_id;
                        $el.appendChild(dom);
                    }
                }

                const handleAnimation = function(_this) {
                  if (!$el) return;
                  const $btn_wrapper = $el.querySelector('.ecom-product-single__add-to-cart--submit');
                  if (!$btn_wrapper) return;

                  let intervalTime = parseInt(_this.settings.animation_loop_time) * 1000 || 6000,
                      animTime = 1000;
                      
                  setInterval(function() {
                      $btn_wrapper.classList.add('animated');
                      setTimeout(function(){
                        $btn_wrapper.classList.remove('animated');
                      }, animTime);
                  }, intervalTime);
                }

                if (this.settings.animation) {
                  handleAnimation(this);
                }
            };
        },

        liquids() {
            return {
                variant_id: {
                    code: '{{product.first_available_variant.id}}',
                    preview: '0'
                },
                payment_terms: {
                    code: `
                        {% comment %}
                            this.data.settings.payment_terms
                        {% endcomment %}
                        {{ form | payment_terms }}

                        `,
                    preview: ``
                }
            };
        },
        attrLink() {
            const link = this.data?.settings.link;
            return {
                'data-href': link?.href ?? '#',
                'data-target': link?.target ?? ''
            };
        },
        settings() {
            return [
                {
                    params: [
                        {
                            type: 'text',
                            label: 'Add to cart text',
                            name: 'add_to_cart'
                        },
                        {
                            type: 'text',
                            label: 'Pre order text',
                            name: 'pre_order',
                            value: 'Pre Order'
                        },
                        {
                            type: 'text',
                            name: 'product_unavailable',
                            label: 'Product is not available text',
                        },
                        {
                            type: 'text',
                            name: 'product_outstock',
                            label: 'Product is out of stock text'
                        },
                        {
                            type:'line'
                        },
                        {
                            type: 'toggle',
                            name: 'payment_terms',
                            label: 'Show payment terms',
                            options: {
                                values: {
                                    on: {
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                }
                            },
                        },
                        {
                            type: 'popup',
                            name: 'action',
                            label: 'Added to cart then',
                            options: {
                                type: 'dropdown',
                                default:false,
                                values: {
                                    popup: 'Show cart popup',
                                    reload: 'Reload page',
                                    message: 'Show a message',
                                    cart: 'Redirect to cart page',
                                    checkout: 'Go to checkout page',
                                    link: 'Go to Special url'
                                }
                            },
                            description: 'To enable this feature you must go to the **Extensions** > ** Ajax cart** > ** Settings ** > ** Enable ajax cart ** [Ajax cart](#extensions/1)'
                        },
                        {
                            type: 'link',
                            label: 'Target url',
                            name: 'link',
                            options: {
                                visible: function (data) {
                                    return data.action === 'link';
                                }
                            }
                        },
                        {
                            type: 'text',
                            label: 'Added to cart message',
                            name: 'added_cart_message',
                            placeholder: 'Added to to your cart',
                            options: {
                                visible: function (data) {
                                    return data.action === 'message';
                                }
                            }
                        },
                        {
                            type: 'text',
                            label: 'Added to cart text',
                            name: 'added_cart_text',
                            options: {
                                placeholder: 'Added to cart'
                            }
                        },
                        {
                            type: 'picker',
                            label: 'Icon',
                            name: 'icon',
                            options: {
                                type: 'icon',
                                layout: 'grid',
                                output: 'value',
                                multiple: false,
                                simple: false
                            }
                        },
                        {
                            type: 'choose',
                            label: 'Icon position',
                            name: 'icon_position',
                            options: {
                                type: 'align-x',
                                values: [-1, 1],
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['icon'];
                                    }
                                }
                            },
                            css:{
                                selector:' .ecom__element--button-icon',
                                properties:{
                                    order:''
                                }
                            }
                        },
                        {
                            type: 'number',
                            label: 'Icon spacing',
                            name: 'spacing',
                            options: {
                                units: {
                                    px: {
                                        min: 0,
                                        max: 200
                                    }
                                },
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['icon'];
                                    }
                                }
                            },
                            css:{
                                selector:' .ecom-product-single__add-to-cart--submit',
                                properties:{
                                    gap:''
                                }
                            }
                        },
                        {
                            type:'line'
                        },
                        {
                            type: 'toggle',
                            name: 'animation',
                            label: 'Button animation',
                            options: {
                                values: {
                                    on: {
                                        label: 'Enable',
                                        value: true
                                    },
                                    off: {
                                        label: 'Disable',
                                        value: false
                                    }
                                }
                            },
                        },
                        {
                          type: "number",
                          label: 'Loop time <span class="lowercase">(s)</span>',
                          name: "animation_loop_time",
                          options: {
                              min: 2,
                              default: 6,
                              max: 10,
                              visible: function (data) {
                                  return data.animation == true;
                              }
                          }
                        },
                        {
                            type:'line'
                        },
                        {
                            type:'paragraph',
                            content: `Integrated with: [Omega Facebook Pixel](https://apps.shopify.com/facebook-multi-pixels?surface_detail=ECatc&surface_type=partner)`

                        }
                    ]
                }
            ];
        },
        css() {
            return `
                    .ecom-builder .shopify-payment-button__button--hidden{
                        visibility: visible
                    }
                    .ecom-product-single__add-to-cart-payment-terms:empty{
                        display:none;
                    }

                    .ecom-product-single__add-to-cart--submit {
                        display: flex;
                        align-items: center;
                    }
                    .ecom-product-single__add-to-cart--submit[disable] {
                        cursor: not-allowed;
                    }
                    .ecom-product-single__add-to-cart-buttons-wrapper {display: flex;align-items: center;}
                `;
        },

        requestShopifyType() {
            return {
                shopify_type: 'product'
            };
        },

        default() {
            return {
                "settings": {
                    "add_to_cart": "Add to cart",
                    "product_unavailable": "Unavailable",
                    "product_outstock": "Out stock",
                    "show_dynamic_checkout": true,
                    "added_cart_text": "Added to cart",
                    "added_cart_message": "Added to cart",
                    "action": "popup",
                    "payment_terms": false,
                    "icon": "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 640 512\" fill=\"currentColor\"><!--! Font Awesome Pro 6.1.1 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2022 Fonticons, Inc. --><path d=\"M423.3 440.7c0 25.3-20.3 45.6-45.6 45.6s-45.8-20.3-45.8-45.6 20.6-45.8 45.8-45.8c25.4 0 45.6 20.5 45.6 45.8zm-253.9-45.8c-25.3 0-45.6 20.6-45.6 45.8s20.3 45.6 45.6 45.6 45.8-20.3 45.8-45.6-20.5-45.8-45.8-45.8zm291.7-270C158.9 124.9 81.9 112.1 0 25.7c34.4 51.7 53.3 148.9 373.1 144.2 333.3-5 130 86.1 70.8 188.9 186.7-166.7 319.4-233.9 17.2-233.9z\"></path></svg>",
                    "icon_position": -1,
                    "spacing": "10px",
                    "animation": true,
                    "animation_loop_time": 3
                },
                "style": {
                    "add_to_cart_button": {
                        "tab": "normal",
                        "spacing": {
                            "padding": {
                                "top": "12.5px",
                                "left": "50px",
                                "bottom": "12.5px",
                                "right": "50px"
                            }
                        },
                        "buttonColorhovermode": "#fff",
                        "buttonBackgroundhovermode": {
                            "classic": {
                                "background-color": "#16b9de"
                            }
                        },
                        "buttonBackgroundnormalmode": {
                            "classic": {
                                "background-color": "#0691b1"
                            }
                        },
                        "buttonColornormalmode": "#fff",
                        "buttonBordernormalmode": {
                            "border-style": "none"
                        },
                        "buttonTypography": {
                            "font-weight": "500",
                            "text-transform": "uppercase",
                            "letter-spacing": "1.5px",
                            "font-style": "normal"
                        },
                        "transition": {
                            "transition": {
                                "duration": "300ms",
                                "timing": "ease"
                            }
                        },
                        "buttonBorderRadiusnormalmode": {
                            "right": "5px",
                            "top": "5px",
                            "left": "5px",
                            "bottom": "5px"
                        },
                        "atc_animation": "shake"
                    },
                    "sold_out_or_unavailable_button": {
                        "tab": "normal",
                        "buttonColornormalmode": "#fff",
                        "buttonBackgroundnormalmode": {
                            "classic": {
                                "background-color": "#bdb9b9"
                            }
                        },
                        "buttonBordernormalmode": {
                            "border-style": "none"
                        },
                        "spacing": {
                            "padding": {
                                "right": "50px",
                                "left": "50px",
                                "top": "12.5px",
                                "bottom": "12.5px"
                            }
                        }
                    },
                    "icon": {
                        "iconFontSize": "20px"
                    }
                }
            }
        },
        animatedClass() {
          return this.data?.settings?.animation ? 'animated' : '';
        },
    },
    methods: {
        style() {
            const params = [
                {
                    group_alias: 'button',
                    options:{
                        group_title: 'Add to cart',
                        group_name: 'add_to_cart_button',
                        selector: ' .ecom-product-single__add-to-cart--submit',
                    },
                    modify:{
                        params:[
                            {
                                position:0,
                                fields: [
                                    {
                                        alias:'align-items',
                                        options:{
                                            label:'Alignment',
                                            name: 'button_alignment',
                                            css:{
                                                selector:'root .ecom-product-single__add-to-cart-buttons-wrapper',
                                                properties:{
                                                    'align-items':'',
                                                    'justify-content':''
                                                }
                                            }
                                        }
                                    },
                                    {
                                        alias:'align-items',
                                        options:{
                                            label:'Text Alignment',
                                            name: 'text_alignment',
                                            css:{
                                                selector:'root .ecom-product-single__add-to-cart--submit',
                                                properties:{
                                                    'justify-content':''
                                                }
                                            }
                                        }
                                    }
                                ]
                            },
                            {
                                position:30,
                                fields:[
                                    {
                                        type: 'line'
                                    },
                                    {
                                        name: "atc_animation",
                                        label: "Animation effect",

                                        type: "popup",
                                        options: {
                                            type: "dropdown",
                                            values: "entrance-animation",
                                            size: "small",
                                        },
                                        css: {
                                            selector: ".animated",
                                            properties: {
                                                'entrance-animation': ""
                                            }
                                        }
                                    },
                                ]
                            }
                        ]
                    }
                },
                {
                    group_alias: 'button',
                    options:{
                        group_title: 'Sold out or Unavailable',
                        group_name: 'sold_out_or_unavailable_button',
                        selector: ' .ecom-product-single__add-to-cart--submit:disabled',
                    }
                }
            ];
            if (this.data.settings?.icon) {
                params.push({
                    group_alias:'icon',
                    options:{
                        group_name: 'icon',
                        group_title: 'Icon',
                        selector: ' .ecom__element--button-icon',
                    },
                    modify:{
                        params:[
                            {
                                position:15,
                                fields:[
                                    {
                                        type: 'line'
                                    },
                                    {
                                        alias:'spacing'
                                    }
                                ]
                            }
                        ]
                    }
                });
            }
            return params;
        }
    }
};
</script>
