<template>
    <div class="ecom-element ecom-shopify ecom-shopify__global-block">
        <div
            class="ecom-element ecom-shopify ecom-shopify__global-block__wrapper"
            v-html="liquid('block')"
        />
    </div>
</template>

<script>
    import { Liquid, Element, Javascript } from '@/mixins';
    export default {
        name: 'ShopifyGlobalBlock',
        mixins: [Liquid, Element, Javascript],
        docs: "https://help.ecomposer.io/docs/pages-templates/extra-builder/global-block/",
        props: {
            data: {
                type: Object,
                default () {
                    return {};
                }
            }
        },
        computed: {

            liquids () {
                return {
                    block: {
                        code: `
                        ${this.data.settings?.block?.value
                            ? `
                                {%- render 'ecom-${this.data.settings?.block.value}-block' -%}
                            `
                            :`
                            {%- if EComBuilderMode -%}
                                <p>Please select block in Settings, if you don't have block, create a block first</p>
                            {%- endif -%}
                            `
                        }
                        `,
                        preview: `
                            <div class="ecom-skeleton-item">
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-12 ecom-skeleton-big"></div>
                                </div>
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-12 ecom-skeleton-big"></div>
                                </div>
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-12 ecom-skeleton-big"></div>
                                </div>
                            </div>
                        `
                    }
                };
            },

            javascript() {
                return function() {
                    if (!this.settings || !this.$el || this.isLive) {
                        return;
                    }
                    const $el = this.$el;
                    let scripts = $el.querySelectorAll('script:not([type="application/json"])');
                    if(scripts.length > 0) {
                        scripts.forEach( function(script) {
                            let tmp = document.createElement('script');
                            tmp.type = 'text/javascript';
                            tmp.innerText = script.innerText;
                            if(script.src) {
                                tmp.src = script.src
                            }

                            $el.appendChild(tmp);
                            script.remove();
                        })
                    }
                }
            },

            settings () {
                return [{
                    params: [
                        {
                            type: 'picker',
                            name: 'block',
                            label: 'Picker a block',
                            options: {
                                type: 'block',
                                layout:'list',
                                multiple: false
                            },
                            description: 'Select a block to render or create a [new block](' +route("builder",{page:'block', layout: 'ecom'})+')'
                        },

                    ]
                }];
            },

            style(){
                return [
                    {
                        group_alias: 'box',
                        options: {
                            group_title: "General"
                        },

                    }
                ];
            },

            default() {
                return {}
            }


        },
        methods: {

        }

    };
</script>
