<template>
    <div class="ecom-element ecom-shopify-elements ecom-shopify__article--tags">
        <div class="ecom-shopify__article--tags-wrapper ecom-flex ecom-column">
            <div class="ecom-shopify__article--tags-inner" v-html="liquid('articleTags')" />
        </div>
    </div>
</template>

<script>
import { Liquid, Element } from '@/mixins';

export default {
    name: 'ArticleTags',
    
    mixins: [Liquid, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        css() {
            return `
                .ecom-shopify__article--tags-item {
                    display: inline-block;
                    list-style: none;
                }
            `;
        },

        liquids() {
            return {
                articleTags: {
                    code: `
                        {%- capture limit -%}${this.data.settings?.limit? this.data.settings.limit: 3}{%-endcapture-%}
                        {% assign limit = limit | plus: 0 %}
                        {% assign tmpl =  '' %}
                        {% if  template.suffix  %}
                            {% assign tmpl = '?view=' | append: template.suffix %}
                        {% endif %}

                        {% if article  and article.tags  %}
                            <ul class="ecom-shopify__article--tags-items ecom-flex ecom-wrap">
                                {% for tag in article.tags limit: limit %}
                                    <li class="ecom-shopify__article--tags-item">
                                        <a href="/blogs/{{blog.handle}}/tagged/{{tag | handleize }}{{tmpl}}">{{ tag }}</a>
                                    </li>
                                {% endfor %}
                            </ul>
                        {% else %}
                            <div>Please select the article in settings</div>
                        {% endif %}
                    `,
                    preview: `
                        <div>Loading...</div>
                    `
                }
            };
        },

        requestShopifyType() {
            return {
                shopify_type: 'article'
            };
        },

        settings() {
            return [
                {
                    params: [
                        {
                            type: 'popup',
                            label: 'Layout',
                            name: 'flexDirection',
                            options: {
                                type: 'dropdown',
                                default: false,
                                values: {
                                    column: 'Vertical',
                                    row: 'Horizontal',
                                }
                            },
                            css:{
                                selector: ' .ecom-shopify__article--tags-items',
                                properties:{
                                    'flex-direction':''
                                }
                            }
                        },
                        {
                            type: 'number',
                            name: 'limit',
                            label: 'Maximum tags to show',
                            options: {
                                min: 1,
                                max: 20
                            }
                        },

                        {
                            name: 'column-gap',
                            type: 'number',
                            label: 'Column gap',
                            value: '12px',
                            options: {
                                units: {
                                    px: {
                                        min: 1,
                                        max: 64,
                                        step: 1
                                    }
                                },
                                responsive: true,
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['flexDirection'] === 'row';
                                    }
                                }
                            },
                            css: {
                                selector: ' .ecom-shopify__article--tags-items',
                            }
                        },
                        {
                            name: 'row-gap',
                            type: 'number',
                            label: 'Row gap',
                            value: '12px',
                            options: {
                                units: {
                                    px: {
                                        min: 1,
                                        max: 64,
                                        step: 1
                                    }
                                },
                                responsive: true
                            },
                            css: {
                                selector: ' .ecom-shopify__article--tags-items',
                            }
                        }
                    ]
                }
            ];
        },

        style() {
            return [
                {
                    group_alias: 'box',
                    options: {
                        group_title: 'General',
                        group_name:'general',
                    },
                    modify: {
                        params: [
                            {
                                type: 'choose',
                                label: 'Alignment',
                                name: 'alignItems',
                                value: 'left',
                                options: {
                                    oneline: true,
                                    responsive: true,
                                    type: 'text-align',
                                    values: ['flex-start', 'center', 'flex-end']
                                },
                                css:{
                                    selector: ' .ecom-shopify__article--tags-wrapper',
                                    properties:{
                                        'align-items':''
                                    }
                                }
                            }
                        ]
                    }
                },
                {
                    group_alias: 'button',
                    options: {
                        group_title: 'Tag',
                        group_name:'tag',
                        selector: ' .ecom-shopify__article--tags-item a'
                    }
                }
            ];
        },

        default() {
            return {
                "settings": {
                    "flexDirection": "row",
                    "row-gap": "12px",
                    "limit": 10,
                    "isShowTitle": true,
                    "textTitle": "Article Tags",
                    "column-gap": "15px"
                },
                "style": {
                    "general": {
                        "alignItems": "flex-start"
                    },
                    "tag": {
                        "tab": "normal",
                        "buttonBorderRadiusnormalmode": {
                            "top": "0px",
                            "left": "0px",
                            "bottom": "0px",
                            "right": "0px"
                        },
                        "buttonBackgroundnormalmode": {
                            "classic": {
                                "background-color": "rgba(17, 24, 39, 0.05)"
                            }
                        },
                        "buttonBordernormalmode": {
                            "border-style": "none"
                        },
                        "buttonBackgroundhovermode": {
                            "classic": {
                                "background-color": "rgba(17, 24, 39, 0.1)"
                            }
                        },
                        "spacing": {
                            "padding": {
                                "left": "16px",
                                "top": "8px",
                                "bottom": "8px",
                                "right": "16px"
                            }
                        },
                        "buttonTypography": {
                            "text-decoration": "none"
                        },
                        "buttonColornormalmode": "#000000"
                    }
                }
            }
        }
    }
};
</script>
