<template>
    <div class="ecom-element ecom-shopify-elements ecom-shopify__blog--tags">
        <div class="ecom-shopify__blog--tags-wrapper">
            <div class="ecom-shopify__blog--tags-inner" v-html="liquid('blogTags')" />
        </div>
    </div>
</template>

<script>
import { Liquid, Element } from '@/mixins';

export default {
    name: 'BlogTags',

    mixins: [Liquid, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        liquids() {
            return {
                blogTags: {
                    code: `
                        {%- capture limit -%}${this.data.settings?.limit? this.data.settings.limit: 3}{%-endcapture-%}
                        {% assign limit = limit | plus: 0 %}

                        {% if blog != empty %}
                            {% assign tmpl =  '' %}
                            {% if  template.suffix %}
                                {% assign tmpl = '?view=' | append: template.suffix %}
                            {% endif %}
                            <ul class="ecom-shopify__blog--tags-items">
                                {% for tag in blog.all_tags limit: limit %}
                                    <li class="ecom-shopify__blog--tags-item">
                                    <a href="/blogs/{{blog.handle}}/tagged/{{tag | handleize }}{{tmpl}}">{{ tag }}</a>
                                    </li>
                                {% endfor %}
                            </ul>
                        {% else %}
                            ${!this.exporting? '<div>Please select the blog in settings</div>' : '<div>No tags yet!</div>'}
                        {% endif %}
                    `,
                    preview: `
                        <div class="ecom-skeleton-item">
                            <div class="ecom-skeleton-col-12">
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-9 ecom-skeleton-big"></div>
                                </div>
                            </div>
                        </div>
                    `
                }
            };
        },

        requestShopifyType() {
            return {
                shopify_type: 'blog'
            };
        },

        settings() {
            return [
                {
                    params: [
                        {
                            type: 'popup',
                            label: 'Layout',
                            name: 'flexDirection',
                            value: 'column',
                            options: {
                                type: 'dropdown',
                                default: false,
                                values: {
                                    column: 'Vertical',
                                    row: 'Horizontal',
                                }
                            },
                            css:{
                                selector: ' .ecom-shopify__blog--tags-items',
                                properties:{
                                    'flex-direction':''
                                }
                            }
                        },
                        {
                            type: 'number',
                            name: 'limit',
                            label: 'Maximum tags to show',
                            options: {
                                min: 1,
                                max: 20
                            }
                        },

                        {
                            name: 'column-gap',
                            type: 'number',
                            label: 'Column gap',
                            value: '12px',
                            options: {
                                units: {
                                    px: {
                                        min: 1,
                                        max: 64,
                                        step: 1
                                    }
                                },
                                responsive: true,
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['flexDirection'] === 'row';
                                    }
                                }
                            },
                            css: {
                                selector: ' .ecom-shopify__blog--tags-items',
                            }
                        },
                        {
                            name: 'row-gap',
                            type: 'number',
                            label: 'Row gap',
                            value: '12px',
                            options: {
                                units: {
                                    px: {
                                        min: 1,
                                        max: 64,
                                        step: 1
                                    }
                                },
                                responsive: true
                            },
                            css: {
                                selector: ' .ecom-shopify__blog--tags-items',
                            }
                        }
                    ]
                }
            ];
        },

        style() {
            return [
                {
                    group_alias: 'box',
                    options: {
                        group_title: 'General',
                        group_name: 'blog_tags_general',
                        selector: ' .ecom-shopify__blog--tags-wrapper',
                    },
                    modify: {
                    params: [
                            {
                                position:0,
                                fields:{
                                    type: 'choose',
                                    label: 'Alignment',
                                    name: 'blogTagsAlignment',
                                    options: {
                                        oneline: true,
                                        responsive: true,
                                        type: 'text-align',
                                        values: ['flex-start', 'center', 'flex-end']
                                    },
                                    css: {
                                        selector: ' .ecom-shopify__blog--tags-inner',
                                        properties: {
                                            display: 'flex',
                                            'justify-content': ''
                                        }
                                    }
                                }
                            },
                            {
                                position:20,
                                fields:{
                                    alias:'spacing'
                                }
                            }
                        ]
                    }
                },
                {
                    group_alias: 'button',
                    options: {
                        group_title: 'Tag',
                        selector: ' .ecom-shopify__blog--tags-item a',
                        group_name: 'blog_tags_item'
                    }
                }
            ];
        },
        css() {
            return `
            .ecom-shopify__blog--tags-items {
                display: flex;
                flex-wrap: wrap;
                list-style: none;
            }
            .ecom-shopify__blog--tags-items li {
                list-style: none;
            }
            .ecom-shopify__blog--tags-item a {
                display: block;
            }
            `;
        },

        default() {
            return {
                settings: {
                    limit: 10,
                    isShowTitle: true,
                    textTitle: "Blog Tags",
                    flexDirection: "row",
                    "column-gap": "10px",
                    "row-gap": "10px"
                },
                style: {
                    blog_tags_general: {
                        blogTagsAlignment: "flex-start"
                    },
                    blog_tags_item: {
                        spacing: {
                            padding: {
                                left: "16px",
                                top: "8px",
                                bottom: "8px",
                                right: "16px"
                            }
                        },
                        buttonColornormalmode: "#111827",
                        buttonBackgroundnormalmode: {
                            classic: {
                                "background-color": "rgba(17, 24, 39, 0.05)"
                            }
                        },
                        buttonColorhovermode: "#111827"
                    }
                }
            }
        }
    }
};
</script>
