<template>
    <div class="ecom-element ecom-product-single ecom-product-single__variant-attributes">
        <div class="ecom-product-single__variant-attributes-wrapper">
            <div class="ecom-product-single__variant-attributes-container">
                <div
                    class="ecom-product-single__variant-attributes--sku"
                    :data-text="lang(data.settings.sku_text, 'sku_text')"
                >
                    <span class="ecom-product-single__variant-attributes--label" v-html="lang(data.settings.sku_text, 'sku_text')" />

                    <span
                        class="ecom-product-single__variant-attributes--text"
                        v-html="liquid('sku')"
                    />
                </div>
                <div
                    v-if="data.settings?.barcode_text"
                    class="ecom-product-single__variant-attributes--barcode"
                    :data-text="lang(data.settings.barcode_text, 'barcode_text')"
                >
                    <span class="ecom-product-single__variant-attributes--label" v-html="lang(data.settings.barcode_text, 'barcode_text')" />
                    <span
                        class="ecom-product-single__variant-attributes--text"
                        v-html="liquid('barcode')"
                    />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { Liquid, Element, Javascript} from '@/mixins';
    export default {
        name: 'ProductVariantAttribute',

        mixins: [Liquid, Element, Javascript],
        props: {
            data: {
                type: Object,
                default () {
                    return {};
                }
            }
        },
        data(){
            return {
                jsreactives: ['sku_text','barcode_text']
            };
        },
        computed: {
            css () {
                return `

                .ecom-product-single__variant-attributes-container span {
                    display: inline-block
                }
            `;
            },

            liquids () {
                return {
                    sku: {
                        code: `{% if variant_selected.sku %}{{variant_selected.sku}}{% else %}N/A{%endif%}`,
                        preview: `SKU-123`
                    },
                    barcode: {
                        code: '{% if variant_selected.barcode %}{{variant_selected.barcode}}{% else %}N/A{%endif%}',
                        preview: '123-456-789'
                    }
                };
            },

            requestShopifyType () {
                return {
                    shopify_type: 'product'
                };
            },
            javascript(){
                return function(){
                    if(!this.isLive)
                    {
                        let $wrapper = this.$el.closest('.ecom-product-form--single');
                        if ($wrapper) {
                            let selector = $wrapper.querySelector('[name="id"]');
                            if (selector) {
                                selector.dispatchEvent(new window.Event('ecomUpdate'));
                            }
                        }
                    }
                };
            },

            settings () {
                return [
                    {
                        params: [
                            {
                                type: 'text',
                                label: 'Sku text',
                                name: 'sku_text',
                            },
                            {
                                type: 'text',
                                label: 'Barcode text',
                                name: 'barcode_text',
                            }
                        ]
                    }
                ];
            },

            style () {
                return [
                    {
                        group_alias: 'box',
                        options: {
                            group_title: 'General',
                            group_name: 'general'
                        },
                        modify: {
                            params: {
                                type: 'choose',
                                label: 'Alignment',
                                name: 'text-align',
                                options: {
                                    oneline: true,
                                    responsive: true,
                                    type: 'text-align',
                                    values: ['left', 'center', 'right']
                                },
                                css: {
                                    selector: ' .ecom-product-single__variant-attributes-container',
                                }
                            }
                        }
                    },
                    {
                        group_alias: 'text:spacing',
                        options: {
                            group_title: 'Label',
                            group_name: 'Label',
                            selector: ' .ecom-product-single__variant-attributes--label'
                        },
                        modify: {
                            remove: {
                                index: 0, length: 1
                            }
                        }
                    },
                    {
                        group_alias: 'text:spacing',
                        options: {
                            group_title: 'Text',
                            group_name: 'Text',
                            selector: ' .ecom-product-single__variant-attributes--text'
                        },
                        modify: {
                            remove: {
                                index: 0, length: 1
                            }
                        }
                    }
                ];
            },

            default() {
                return {
                    settings: {
                        sku_text: "Sku:",
                        barcode_text: "Barcode:"
                    },
                    style: {
                        general: {
                            "text-align": "left"
                        },
                        Label: {
                            spacing: {
                                margin: {
                                    right: "10px",
                                    bottom: "4px"
                                }
                            }
                        },
                        Text: {
                            spacing: {
                                margin: {
                                    bottom: "4px"
                                }
                            }
                        }
                    }
                }
            }
        },
        methods: {},

    };
</script>
