<template>
    <div class="ecom-element ecom-base-gallery">
        <div class="ecom-gallery--container ecom-base-gallery-grid">
            <div
                v-for="image in images()"
                :key="image.id"
                class="ecom-gallery--item"
                :class="{
                    'ecom-replace-notice': $helpers.isPreviewImage(image.value)
                }"
            >
                <figure class="ecom-image-align">
                    <a
                        v-if="image.link"
                        v-bind="handleLink(image)"
                        class="ecom-image-default"
                        style="position:relative"
                    >
                        <img :src="image.value" :alt="image.name" class="ecom-base-gallery-block" loading="lazy">
                        <div v-if="data.settings.overlay_use === true" class="ecom-base-gallery-overlay ecom-overlay">
                            <span
                                v-if="data.settings.overlay_title === 'alt'"
                                class="ecom-overlay-text"
                                v-html="image.name"
                            />
                            <span
                                v-if="data.settings.overlay_title === 'caption'"
                                class="ecom-overlay-text"
                                v-html="image.caption"
                            />
                            <span
                                v-if="data.settings.overlay_title === 'custom'"
                                class="ecom-overlay-text"
                                v-html="data.settings.overlay_title_custom"
                            />
                        </div>
                    </a>
                    <div v-else class="ecom-relative ecom-image-default" style="position:relative">
                        <img :src="image.value" :alt="image.name" class="ecom-base-gallery-block" loading="lazy">
                        <div v-if="data.settings.overlay_use === true" class="ecom-base-gallery-overlay ecom-overlay">
                            <span
                                v-if="data.settings.overlay_title === 'alt'"
                                class="ecom-overlay-text"
                                v-html="image.name"
                            />
                            <span
                                v-if="data.settings.overlay_title === 'caption'"
                                class="ecom-overlay-text"
                                v-html="image.caption"
                            />
                            <span
                                v-if="data.settings.overlay_title === 'custom'"
                                class="ecom-overlay-text"
                                v-html="data.settings.overlay_title_custom"
                            />
                        </div>
                    </div>
                    <figcaption
                        v-if="data.settings?.show_caption && image.caption"
                        class="ecom-gallery--caption"
                        v-html="image.caption"
                    />
                </figure>
            </div>
        </div>
    </div>
</template>

<script lang="ts">
import { Element, Javascript } from '@/mixins';

export default {
    name: 'Gallery',
    docs: 'https://help.ecomposer.io/docs/elements/basic-elements/gallery/',
    mixins: [Element, Javascript],
    vendors: ['slider_js', 'slider_css','modal_js', 'modal_css'],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    data(){
        return {
            jsreactives:[
                'images'
            ]
        }
    },
    computed: {


        size() {
            let { size, size_custom } = this.data.settings;
            if (size === 'custom') {
                size = size_custom;
            }
            else if(size) {
                size = {width: size.split('x')[0], height: size.split('x')[1]};
            }
            return size;
        },
        settings() {
            return [
                {
                    group_title: 'Images',
                    params: [
                        {
                            type: 'picker',
                            label: 'Choose Images',
                            name: 'images',
                            options: {
                                type: 'image',
                                multiple: true,
                                modify: [
                                    {
                                        name: 'name',
                                        label: 'Alt',
                                        type: 'text'
                                    },
                                    {
                                        name: 'caption',
                                        label: 'Caption',
                                        type: 'text'
                                    },
                                    {
                                        type: 'popup',
                                        name: 'link_type',
                                        label: 'Link',
                                        options: {
                                            default: false,
                                            values: {
                                                'none': 'None',
                                                media_file: 'Media File',
                                                custom: 'Custom URL'
                                            },
                                            type: 'dropdown'
                                        },
                                        css: {
                                            isCss: false
                                        }
                                    },
                                    {
                                        type: 'link',
                                        name: 'custom-link',
                                        options: {
                                            position: 'fixed',
                                            visible: (data) => {
                                                return data['link_type'] === 'custom';
                                            }
                                        }
                                    },
                                    {
                                        type: 'popup',
                                        name: 'lightbox',
                                        label: 'Click open in lightbox',
                                        options: {
                                            type: 'dropdown',
                                            default: false,
                                            values: {
                                                yes: 'Yes',
                                                no: 'No'
                                            },
                                            visible: {
                                                keep_data: true,
                                                condition: (data) => {
                                                    return data['link_type'] === 'media_file';
                                                }
                                            }
                                        }
                                    },
                                ]
                            }
                        },

                        {
                            type: 'popup',
                            label: 'Crop size',
                            name: 'size',
                            value: '',
                            options: {
                                type: 'dropdown',
                                default: false,
                                values: {
                                    '': 'Original',
                                    '400x300': '400 x 300px',
                                    '800x450': '800 x 450px',
                                    '500x500': '500 x 500px',
                                    custom: 'Custom'
                                }
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            type: 'size',
                            name: 'size_custom',
                            options: {
                                visible: (data : any) => {
                                    return data['size'] === 'custom';
                                },
                                warnings: {
                                    content:
                                        'You can crop the original image size to any custom size. You can also set a single value for height or width in order to keep the original size ratio.'
                                }
                            }
                        },
                        {
                            name: 'gridTemplateColumns',
                            label: 'Images per row',
                            type: 'number',
                            options: {
                                min: 1,
                                max: 10,
                                responsive: true,
                                slider: true,

                            },
                            css: {
                                selector: ' .ecom-base-gallery .ecom-gallery--container',
                                properties: {
                                    'grid-template-columns': 'repeat(%value%, 1fr)'
                                }
                            }
                        },

                        {
                            type: 'number',
                            name: 'ecomGalleryGap',
                            label: 'Spacing between',
                            options: {
                                responsive: true,
                                units: {
                                    px: { min: 0, max: 100 }
                                }
                            },
                            css: {
                                selector: ' .ecom-base-gallery .ecom-gallery--container',
                                properties: {
                                    '--ecom-gallery-gap': ''
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            name: 'show_caption',
                            label: 'Show caption',
                            options: {
                                oneline: true,
                                values: {
                                    off: {
                                        label: 'NO',
                                        value: false
                                    },
                                    on: {
                                        label: 'YES',
                                        value: true
                                    }
                                }
                            },
                            css: {
                                isCss: false
                            }
                        },
                    ]
                },
                {
                    group_title: 'Overlay',
                    params: [
                        {
                            type: 'toggle',
                            name: 'overlay_use',
                            label: 'Enable Overlay',
                            value: true,
                            options: {
                                oneline: true,
                                values: {
                                    off: {
                                        label: 'NO',
                                        value: false
                                    },
                                    on: {
                                        label: 'YES',
                                        value: true
                                    }
                                }
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            type: 'popup',
                            label: 'Title source',
                            name: 'overlay_title',
                            options: {
                                type: 'dropdown',
                                default:false,
                                values: {
                                    'none': 'None',
                                    alt: 'Alt',
                                    caption: 'Caption',
                                    custom: 'Custom'
                                },
                                visible: (data) => {
                                    return data['overlay_use'] === true;
                                }
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            name: 'overlay_title_custom',
                            label: 'Custom title',
                            type: 'text',
                            options: {
                                visible: (data) => {
                                    return (
                                        data['overlay_title'] == 'custom' &&
                                        data['overlay_use'] === true
                                    );
                                }
                            }
                        }
                    ]
                }
            ];
        },
        javascript(){
              return function () {
                  if(!this.$el || !window.EComModal) return;

                  let arr =  this.$el.querySelectorAll('[ecom-modal]');
                  if(arr.length) {
                    new window.EComModal(arr, {
                        gallery: true,
                        cssClass: ['ecom-container-lightbox-' + this.id]
                    });
                  }
            };
        },
        css() {
            return `
                .ecom-base-gallery-grid {
                    display: grid
                }
                .ecom-base-gallery-block {
                    display: block
                }
                .ecom-gallery--container {
                    grid-gap: var(--ecom-gallery-gap, 30px) var(--ecom-gallery-gap, 30px);
                }
                .ecom-gallery--container{
                    grid-template-columns: repeat(4, 1fr);
                }

                .ecom-gallery--item figure {
                    display: flex;
                    flex-direction:column;
                }
                .ecom-gallery--item a{
                    position: relative;
                }
                .ecom-gallery--item img {
                    width: 100%;
                }
                .ecom-base-gallery-overlay.ecom-overlay {
                    position: absolute;
                    inset: 0;
                    z-index: 1;
                    align-items: center;
                    justify-content: center;
                    pointer-events: none;
                    display:flex;
                    text-align:center;
                }
                .ecom-gallery--caption, .ecom-image-default{
                    width:100%
                }
                `;
        },
        default(){
            return {
                settings: {
                    images: [
                        {
                            id: 1,
                            value: "/images/placeholder.png",
                            name: "Placeholder #1",
                            lightbox: "yes",
                            link_type: "media_file"
                        },
                        {
                            id: 2,
                            value: "/images/placeholder.png",
                            name: "Placeholder #2",
                            lightbox: "yes",
                            link_type: "media_file"
                        },
                        {
                            id: 3,
                            value: "/images/placeholder.png",
                            name: "Placeholder #3",
                            lightbox: "yes",
                            link_type: "media_file"
                        },
                        {
                            id: 4,
                            value: "/images/placeholder.png",
                            name: "Placeholder #4",
                            lightbox: "yes",
                            link_type: "media_file"
                        }
                    ],
                    " .ecom-base-gallery .ecom-gallery--container-child": {
                        "--ecom-gallery-gap": "30px",
                        "column-count": 4,
                        "grid-template-columns": "repeat(4, 1fr)"
                    },
                    size: "",
                    overlay_use: false,
                    overlay_title: "alt",
                    ecomGalleryGap: "10px",
                    gridTemplateColumns: 4
                },
                style: {
                    gallery_image: {
                        "align-items": "center",
                        imageOpacitynormalmode: 1,
                        imageOpacityhovermode: 1
                    },
                    overlay: {
                        opacity: 0,
                        opacityHover: 1,
                        backgroundhovermode: {
                            classic: {
                                "background-color": "rgba(17, 24, 39, 0.3)"
                            }
                        }
                    }
                }
            }
        }
    },
    methods: {
        images() {
            let images = this.$helpers.copy(this.data.settings?.images);
            if (images) {
                let id = this.$helpers.randid() + '-gallery';
                images.forEach((image) => {
                    image.value = this.$helpers.resizeImage(image.value, this.size);
                    let link = null;
                    if (image.link_type === 'custom') {
                        link = image['custom-link'] ?? {};
                    } else if (image.link_type === 'media_file') {
                        link = {
                            'data-ecom-modal-src':image.value,
                            'ecom-modal': 'image',
                            href: image.value
                        };
                        if (image.lightbox === 'yes') {
                            link['ecom-modal-source'] = image.value;
                        }
                    }
                    image.link = link;
                });
                return images;
            }
            return [];
        },
        isCaption() {
            return this.data.settings?.show_caption && this.images().some((x) => x.caption);
        },
        style() {
            let style = [];
            if (this.images().length) {
                style.push({
                    group_alias: 'image',
                    options:{
                        group_name: 'gallery_image',
                        group_title: 'Image',
                        selector: ' .ecom-base-gallery .ecom-gallery--item .ecom-image-default',
                    },
                    modify:{
                        params:{
                            alias:'align-items',
                            options:{
                                css: {
                                    selector:'root .ecom-image-align',
                                }
                            }
                        }
                    }
                });
            }
            if (this.isCaption()) {
                style.push({
                    group_alias:'text',
                    options:{
                        group_name: 'gallery_caption',
                        group_title: 'Caption',
                        selector: ' .ecom-base-gallery .ecom-gallery--caption'
                    },
                    modify:{
                        params:
                        {
                            position:15,
                            fields:[
                                {
                                    alias:'spacing'
                                }
                            ]
                        }
                    }
                });
            }
            // let checkOverlayText = this.data.settings?.overlay_title && (this.data.settings.overlay_title == 'alt' || this.data.settings.overlay_title_custom);
            if (this.data.settings?.overlay_use) {
                style.push({
                    group_alias:  'background:overlaytext',
                    options:{
                        group_name:'overlay',
                        group_title: 'Overlay',
                        selector: ' .ecom-base-gallery .ecom-image-default'
                    }
                });
            }
            return style;
        },
        handleLink(val){
            if(val.lightbox == 'yes'){
                return val.link;
            }
            else{
                return {
                    href:val.link.href
                }
            }
        }
    }
};
</script>
