<template>
    <div class="ecom__element ecom-element element__counter">
        <div class="ecom__element-counter--align">
            <div
                v-if="counter?.icon"
                class="element__counter--icon"
                data-ecom-setting-key="counter-icon"
                v-html="counter?.icon.value"
            />
            <div class="element__counter--default">
                <div class="element__counter--wrapper">
                    <span class="element__counter--prefix">{{ counter?.numberPrefix }}</span>
                    <span
                        class="element__counter--number"
                        :data-duration="
                            counter?.animationDuration ? counter?.animationDuration : 1000
                        "
                        :data-to-value="counter?.endingNumber ? counter?.endingNumber : -1"
                        :data-from-value="counter?.startingNumber ? counter?.startingNumber : 0"
                        :data-step="counter?.step ? counter?.step : 1"
                        data-delimiter=","
                        >{{ counter?.thousandSeparator ? displayedCount : numCounter }}</span
                    >
                    <span class="element__counter--suffix">{{ counter?.numberSuffix }}</span>
                </div>
                <div class="element__counter--title" data-ecom-setting-key="counter-title">
                    {{ counter?.title }}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Element, Javascript } from "@/mixins";

export default {
    name: "Counter",
    presets:true,
    mixins: [Element, Javascript],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    data() {
        return {
            counterAlreadyFired: false,
            numCounter: 0,
            timer: null,
            jsreactives: ['startingNumber','step','animationDuration', 'endingNumber']
        };
    },
    computed: {
        css() {
            return `
                .ecom-element.element__counter {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
                .element__counter--wrapper {
                    display: flex;
                }
                .element__counter--default {
                    display: flex;
                    flex-direction: column;
                    align-items: inherit;
                }
                .element__counter .element__counter--prefix,
                .element__counter .element__counter--suffix {
                    flex-grow: 1;
                    white-space: pre-wrap;
                }
                .element__counter .element__counter--prefix {
                    text-align: right;
                }
                .element__counter .element__counter--suffix {
                    text-align: left;
                }
                .ecom__element-counter--align{
                    display: inline-flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                    gap:12px 20px;
                }
                .element__counter--icon{
                    display:inline-flex;
                }
                .element__counter--icon svg{
                    fill:currentColor;
                    width:24px;
                    height:auto
                }

            `;
        },
        counter() {
            this.updateCounter(
                this.data.settings?.startingNumber,
                this.data.settings?.endingNumber,
                this.data.settings?.animationDuration,
                this.data.settings?.step,
            );
            return this.data.settings;
        },
        displayedCount() {
            return this.numCounter.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        },
        javascript() {
            return function () {
                const $el = this.$el;
                     if(!$el) return;

                const elements = $el.querySelectorAll(".element__counter--number");

                elements.forEach(function (item) {
                    item.counterAlreadyFired = false;
                    item.counterDuration = +item.getAttribute("data-duration");
                    item.counterInitVal = +item.getAttribute("data-from-value");
                    item.counterLastVal = +item.getAttribute("data-to-value");
                    item.counterStep = +item.getAttribute("data-step");
                    item.updateCounter = function () {
                        if (item.counterInitVal === item.counterLastVal) {
                            return false;
                        }
                        let range = item.counterLastVal - item.counterInitVal;
                        let current = item.counterInitVal;
                        let increment =  item.counterStep;
                        let stepTime = Math.abs(Math.floor(item.counterDuration / range));

                        let timer = setInterval(function () {
                            current += increment;

                            if (current >= item.counterLastVal) {
                                clearInterval(timer);
                                item.innerText = item.counterLastVal;
                            }
                            else
                            {
                                item.innerText = current;
                            }
                        }, stepTime);
                    };
                });

                function isElementVisible(el) {
                    if(!el) return false;
                    let scroll = window.scrollY || window.pageYOffset;
                    let boundsTop = el.getBoundingClientRect().top + scroll;
                    let viewport = {
                        top: scroll,
                        bottom: scroll + window.innerHeight
                    };

                    let bounds = {
                        top: boundsTop,
                        bottom: boundsTop + el.clientHeight
                    };

                    return (
                        (bounds.bottom >= viewport.top && bounds.bottom <= viewport.bottom) ||
                        (bounds.top <= viewport.bottom && bounds.top >= viewport.top)
                    );
                }

                function handleScroll() {
                    elements.forEach(function (item) {
                        if (true === item.counterAlreadyFired) {
                            return false;
                        }
                        if (!isElementVisible(item)) {
                            return false;
                        }

                        item.updateCounter();
                        item.counterAlreadyFired = true;
                        window.removeEventListener("scroll", handleScroll);
                    });
                }

                window.addEventListener("scroll", handleScroll);
                handleScroll();
            };
        },
        settings() {
            return [
                {
                    params: [
                        {
                            type: "text",
                            label: "Title",
                            name: "title",
                            options: {
                                toolbar: false
                            }
                        },
                        {
                            name: "counterOrder",
                            label: "Title Position",
                            type: "choose",
                            options: {
                                oneline: true,
                                type: "align-y",
                                values: ["-1", "0"]
                            },
                            css: {
                                selector: " .element__counter--title",
                                properties: {
                                    order: ""
                                }
                            }
                        },
                        {
                            type: "picker",
                            label: "Icon",
                            name: "icon",
                            options: {
                                type: "icon"
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            name: "flexDirection",
                            label: "Icon position",
                            type: "choose",
                            options: {
                                oneline: true,
                                type: "align-position",
                                values: ["row", "column", "column-reverse", "row-reverse"]
                            },
                            css: {
                                selector: " .ecom__element-counter--align",
                                properties: {
                                    "flex-direction": ""
                                }
                            }
                        },
                        {
                            name: "startingNumber",
                            type: "number",
                            label: "Start",
                            value: 1,
                            options: {
                                min: 0,
                                slider: false,
                                half: true
                            }
                        },
                        {
                            name: "endingNumber",
                            type: "number",
                            label: "End",
                            value: 100,
                            options: {
                                min: 1,
                                slider: false,
                                half: true
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            name: "step",
                            type: "number",
                            label: "Step",
                            value: 10,
                            options: {
                                min: 1,
                                slider: false,

                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            type: "toggle",
                            label: "Thousand Separator",
                            name: "thousandSeparator",
                            value: false,
                            options: {
                                oneline: true,
                                values: {
                                    on: {
                                        label: "Yes",
                                        value: true
                                    },
                                    off: {
                                        label: "No",
                                        value: false
                                    }
                                },
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data["endingNumber"] > 999;
                                    }
                                }
                            },
                            description: `When ending number is greater than 999 it works <br>
                            e.g:1000 ==> 1,000`
                        },
                        {
                            name: "numberPrefix",
                            type: "text",
                            label: "Number Prefix",
                            options: {
                                half: true
                            }
                        },
                        {
                            name: "numberSuffix",
                            type: "text",
                            label: "Number Suffix",
                            options: {
                                half: true
                            }
                        },
                        {
                            name: "animationDuration",
                            type: "number",
                            label: "Duration(ms)",
                            value: 2000,
                            options: {
                                min: 1,
                                max: 10000
                            }
                        }
                    ]
                }
            ];
        },
        default() {
            return {
                "settings": {
                    "startingNumber": 1,
                    "endingNumber": 9500,
                    "animationDuration": 2000,
                    "title": "Happy Customers",
                    "icon": {
                        "name": "happy",
                        "cate": "Emoji",
                        "value": "<svg version=\"1.1\" id=\"lni_lni-happy\" xmlns=\"http://www.w3.org/2000/svg\" xmlns:xlink=\"http://www.w3.org/1999/xlink\" x=\"0px\" y=\"0px\" viewBox=\"0 0 64 64\" style=\"enable-background:new 0 0 64 64;\" xml:space=\"preserve\" fill=\"currentColor\">\n<g>\n\t<path d=\"M32,1.3C15,1.3,1.3,15,1.3,32C1.3,49,15,62.8,32,62.8C49,62.8,62.8,49,62.8,32C62.8,15,49,1.3,32,1.3z M32,59.3\n\t\tC17,59.3,4.8,47,4.8,32C4.8,17,17,4.8,32,4.8C47,4.8,59.3,17,59.3,32C59.3,47,47,59.3,32,59.3z\"></path>\n\t<path d=\"M45.6,35.5H18.4c-0.9,0-1.7,0.4-2.3,1.1c-0.5,0.7-0.8,1.5-0.6,2.4C17,46.8,23.9,52.6,32,52.6c8.1,0,15-5.7,16.5-13.6\n\t\tc0.2-0.8-0.1-1.7-0.6-2.4C47.4,35.9,46.5,35.5,45.6,35.5z M32,49.1c-6.1,0-11.4-4.2-12.9-10.1h25.9C43.4,44.9,38.1,49.1,32,49.1z\"></path>\n\t<path d=\"M20.1,26.7c2.2,0,4-1.8,4-4c0-2.2-1.8-4-4-4c-2.2,0-4,1.8-4,4C16.2,25,18,26.7,20.1,26.7z\"></path>\n\t<circle cx=\"43.9\" cy=\"22.8\" r=\"4\"></circle>\n</g>\n</svg>",
                        "thumbnail": "<svg version=\"1.1\" id=\"lni_lni-happy\" xmlns=\"http://www.w3.org/2000/svg\" xmlns:xlink=\"http://www.w3.org/1999/xlink\" x=\"0px\" y=\"0px\" viewBox=\"0 0 64 64\" style=\"enable-background:new 0 0 64 64;\" xml:space=\"preserve\" fill=\"currentColor\">\n<g>\n\t<path d=\"M32,1.3C15,1.3,1.3,15,1.3,32C1.3,49,15,62.8,32,62.8C49,62.8,62.8,49,62.8,32C62.8,15,49,1.3,32,1.3z M32,59.3\n\t\tC17,59.3,4.8,47,4.8,32C4.8,17,17,4.8,32,4.8C47,4.8,59.3,17,59.3,32C59.3,47,47,59.3,32,59.3z\"></path>\n\t<path d=\"M45.6,35.5H18.4c-0.9,0-1.7,0.4-2.3,1.1c-0.5,0.7-0.8,1.5-0.6,2.4C17,46.8,23.9,52.6,32,52.6c8.1,0,15-5.7,16.5-13.6\n\t\tc0.2-0.8-0.1-1.7-0.6-2.4C47.4,35.9,46.5,35.5,45.6,35.5z M32,49.1c-6.1,0-11.4-4.2-12.9-10.1h25.9C43.4,44.9,38.1,49.1,32,49.1z\"></path>\n\t<path d=\"M20.1,26.7c2.2,0,4-1.8,4-4c0-2.2-1.8-4-4-4c-2.2,0-4,1.8-4,4C16.2,25,18,26.7,20.1,26.7z\"></path>\n\t<circle cx=\"43.9\" cy=\"22.8\" r=\"4\"></circle>\n</g>\n</svg>"
                    },
                    "thousandSeparator": true,
                    "numberSuffix": "+",
                    "flexDirection": "column"
                },
                "style": {
                    "counter_general": {
                        "alignItems": "flex-start",
                        "justify-content": "center"
                    },
                    "counter_icon": {
                        "iconFontSize": "54px",
                        "iconPrimaryColornormalmode": "#333",
                        "iconPrimaryColorhovermode": "#047857",
                        "tab": "normal"
                    },
                    "counter_number": {
                        "textTypography": {
                            "font-family": {
                                "name": "Jost",
                                "value": "https://fonts.googleapis.com/css?family=Jost:100,200,300,400,500,600,700,800,900",
                                "thumbnail": "Jost"
                            },
                            "font-size": "38px",
                            "font-weight": "600",
                            "line-height": "42px"
                        },
                        "textColor": "#333"
                    },
                    "counter_number_surfix": {
                        "textTypography": {
                            "font-family": {
                                "name": "Jost",
                                "value": "https://fonts.googleapis.com/css?family=Jost:100,200,300,400,500,600,700,800,900",
                                "thumbnail": "Jost"
                            },
                            "font-size": "38px",
                            "font-weight": "500",
                            "line-height": "42px"
                        },
                        "textColor": "#333"
                    },
                    "counter_title": {
                        "textTypography": {
                            "global-typography": "AbgUXj49"
                        }
                    }
                }
            }
        }
    },
    mounted() {
        window.addEventListener("scroll", this.handleScroll);
    },
    beforeUnmount() {
        window.removeEventListener("scroll", this.handleScroll);
    },
    methods: {
        handleScroll() {
            if (true === this.counterAlreadyFired) {
                return false;
            }
            if (!this.isElementVisible(document.querySelector(".element__counter--number"))) {
                return false;
            }
            this.updateCounter(
                this.data.settings?.startingNumber,
                this.data.settings?.endingNumber,
                this.data.settings?.animationDuration,
                this.data.settings?.step
            );
            this.counterAlreadyFired = true;
        },
        isElementVisible(el) {
            if(!el) return false;
            let scroll = window.scrollY || window.pageYOffset;
            let boundsTop = el.getBoundingClientRect().top + scroll;
            let viewport = {
                top: scroll,
                bottom: scroll + window.innerHeight
            };

            let bounds = {
                top: boundsTop,
                bottom: boundsTop + el.clientHeight
            };

            return (
                (bounds.bottom >= viewport.top && bounds.bottom <= viewport.bottom) ||
                (bounds.top <= viewport.bottom && bounds.top >= viewport.top)
            );
        },
        style() {
            let style = [
                {
                    group_alias: "box",
                    options: {
                        group_name: "counter_general",
                        group_title: "General",
                        selector: " .element__counter"
                    },
                    modify: {
                        params: [
                            {
                            position : 0,
                            fields: [
                                {
                                    alias:'justify-content',
                                    options:{
                                        label:'Alignment'
                                    }
                                },
                                {
                                    alias:'align-items',
                                    options:{
                                        label:'Content alignment',
                                        name: 'content_alignment',
                                        css:{
                                            selector: 'root .element__counter--title, root .element__counter--wrapper, root .element__counter--icon',
                                            properties:{
                                                'align-self':''
                                            }
                                        }
                                    }
                                },
                            ]
                        },
                        {
                            position : 4,
                            fields : {
                                alias: "spacing",
                                options: {
                                css: {
                                    selector: " .ecom__element-counter--align"
                                }
                            }
                            }

                        }
                        ]
                    }
                },
                {
                    group_alias: "text:spacing",
                    options: {
                        group_name: "counter_number",
                        group_title: "Number",
                        selector: " .element__counter .element__counter--number"
                    },
                    modify: {
                        remove: {
                            index: 0,
                            length: 1
                        }
                    }
                },
                {
                    group_alias: "text:spacing",
                    options: {
                        group_name: "counter_title",
                        group_title: "Title",
                        selector: " .element__counter .element__counter--title"
                    },
                    modify: {
                        remove: {
                            index: 0,
                            length: 1
                        },

                    }
                }
            ];
            if (this.counter?.numberSuffix) {
                style.push({
                    group_alias: "text:spacing",
                    options: {
                        group_name: "counter_number_surfix",
                        group_title: "Number surfix",
                        selector: " .element__counter .element__counter--suffix"
                    },
                    modify: {
                        remove: { index:0, length: 1}
                    }
                });
            }
            if (this.counter?.numberPrefix) {
                style.push({
                    group_alias: "text:spacing",
                    options: {
                        group_name: "counter_number_prefix",
                        group_title: "Number prefix",
                        selector: " .element__counter .element__counter--prefix"
                    }
                });
            }
            if (this.counter?.icon) {
                style.splice(2, 0,{
                    group_alias: "icon:hover",
                    options: {
                        group_name: "counter_icon",
                        group_title: "Icon",
                        selector: " .element__counter .element__counter--icon"
                    },
                    modify:{
                        params: [
                            {
                                position:2,
                                fields: [
                                    { alias: 'spacing'}
                                ]
                            }
                        ]
                    }

                });
            }

            return style;
        },
        updateCounter(initVal, lastVal, duration, step) {
            if (this.timer) {
                clearInterval(this.timer);
            }
            if (initVal === lastVal) {
                return false;
            }
            if (isNaN(initVal)) {
                initVal = 0;
            }
            if (isNaN(lastVal)) {
                lastVal = -1;
            }
            if (isNaN(duration)) {
                duration = 1000;
            }
            let range = lastVal - initVal;
            let current = initVal;
            let increment = step;
            let stepTime = Math.abs(Math.floor(duration / range));

            this.timer = setInterval(() => {
                current += increment;

                if (current >= lastVal) {
                     this.numCounter = lastVal;
                    clearInterval(this.timer);
                }
                else
                {
                     this.numCounter = current;
                }
            }, stepTime);
        }
    }
};
</script>
