<template>
    <div class="ecom-element ecom-base-image">
        <figure v-if="image">
            <div class="ecom-container-image ecom-image-align">
                <div
                    class="ecom-image-content-position ecom-image-default ecom-base-image-container-overlay"
                    :class="{
                        'ecom-replace-notice': $helpers.isPreviewImage(JSON.stringify(image))
                    }"
                >
                    <div v-if="data.settings.overlay_use === true && !link.href" class="ecom-base-image ecom-overlay">
                        <span
                            v-if="data.settings.overlay_title === 'alt'"
                            class="ecom-overlay-text" v-html="image.alt" />
                        <span
                            v-else-if="data.settings.overlay_title === 'custom'"
                            class="ecom-overlay-text"
                            v-html="data.settings.overlay_title_custom"
                        />
                    </div>
                    <div
                        v-if="!link.href"
                        class="ecom-image-pictures-wrapper">
                        <picture
                            class="ecom-image-picture"
                            :class="imageHover && data.settings.hover_image.enable ? 'ecom-image-has-hover' : ''"
                            v-html="$helpers.renImageResponsive(image, screens, exporting)"
                        />
                        <picture
                            v-if="imageHover && data.settings.hover_image.enable"
                            class="ecom-image-picture-hover"
                            v-html="$helpers.renImageResponsive(imageHover, screens, exporting)"
                        />
                    </div>


                    <component
                        v-else
                        :is="hasButtonOverlay ? 'div' : 'a'"
                        v-bind="hasButtonOverlay ? '' : link"
                        class="ecom-image-picture-link ecom-element-overlay ecom-base-image-container-overlay ecom-flex"
                        :class=" imageHover ? '' : 'ecom-image-picture'"
                    >
                        <div v-if="data.settings.overlay_use === true" class="ecom-base-image ecom-overlay">
                            <span
                                v-if="data.settings.overlay_title === 'alt'" v-html="image.alt"
                                class="ecom-overlay-text"
                                />
                            <span
                                class="ecom-overlay-text"
                                v-else-if="data.settings.overlay_title === 'custom' && hasButtonOverlay == false"
                                v-html="data.settings.overlay_title_custom"
                                />
                            <a
                                v-else-if="hasButtonOverlay == true"
                                class="ecom-button-overlay"
                                v-bind="link"
                                :style="[horizontal_style, vertical_style]"
                                v-text="data?.settings?.overlay_title_custom">
                            </a>
                        </div>
                        <div class="ecom-image-pictures-wrapper">
                            <picture
                                class="ecom-image-picture"
                                :class="imageHover && data.settings.hover_image.enable ? 'ecom-image-has-hover' : ''"
                                v-html="$helpers.renImageResponsive(image, screens, exporting)"
                            />
                            <picture
                                v-if="imageHover && data.settings.hover_image.enable"
                                class="ecom-image-picture-hover"
                                v-html="$helpers.renImageResponsive(imageHover, screens, exporting)"
                            />
                        </div>
                    </component>
                </div>
                <figcaption
                    v-if="isCaption()"
                    class="ecom-image__caption"
                    v-html="data.settings.caption"
                />
            </div>
        </figure>
    </div>
</template>

<script lang="ts">
import { Element, Javascript } from '@/mixins';
import exporting from '@/utils/exporting';
export default {
    name: 'AdvanceImage',
    presets:false,
    docs: 'https://help.ecomposer.io/docs/elements/basic-elements/image/',
    mixins: [Element, Javascript],
    vendors: ['modal_js', 'modal_css'],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },

    data() {
        return {
            jsreactives: ["overlay_title_custom"]
        };
    },
    computed: {

        imageComp() {
            return this.data;
        },
        screens() {
            return this.$store.getters['builder/screens'];
        },
        useOverlay() {
            return this.data?.settings?.overlay_use;
        },
        hasButtonOverlay() {
            return this.data?.settings?.link == 'custom' && this.data?.settings?.use_button;
        },
        enableLightBox() {
            return this.data?.settings?.lightbox;
        },
        horizontal_style() {
            let style = {};
            if(this.data?.settings?.horizontal_btn) {
                switch( this.data.settings.horizontal_btn ) {
                case 'left':
                    style.left = 0;
                    break;
                case 'center':
                    style.left = '50%';
                    if(this.data?.settings?.vertical_btn == 'center') {
                        style.transform = 'translate(-50%, -50%)';
                    } else {
                        style.transform = 'translateX(-50%)';
                    }
                    style.margin = 'unset';
                    break;
                case 'right':
                    style.right = 0;
                    break;
                }
            }
            return style;
        },
        vertical_style() {
            let style = {};
            if(this.data?.settings?.vertical_btn) {
                style.alignItems = this.data?.settings?.vertical;

                switch( this.data.settings.vertical_btn ) {
                case 'top':
                    style.top = 0;
                    break;
                case 'center':
                    style.top = '50%';
                    if(this.data?.settings?.horizontal_btn == 'center') {
                        style.transform = 'translate(-50%, -50%)';
                    } else {
                        style.transform = 'translateY(-50%)';
                    }
                    style.margin = 'unset';
                    break;
                case 'bottom':
                    style.bottom = 0;
                    break;
                }
            }
            return style;
        },
        image() {

            if(!this.data.settings){
                return
            }
            let { image = {}, size, size_custom } = this.data.settings;
            if (Object.keys(image).length === 0) {
                return null;
            }
            if (size === 'custom') {
                size = size_custom;
            }
            else if(size) {
                size = {width: size.split('x')[0], height: size.split('x')[1]};
            }
            return {
                alt: image.name,
                ...Object.keys(image)
                    .filter((k) => k.startsWith('value'))
                    .reduce((acc, cur) => {
                        acc[cur] = this.$helpers.resizeImage(image[cur], size);
                        return acc;
                    }, {})
            };
        },
        imageHover() {
            if(!this.data.settings.hover_image){
                return
            }

            let {enable, image = {}, size, size_custom } = this.data.settings.hover_image;

            if (Object.keys(image).length === 0) {
                return null;
            }

            if (size === 'custom') {
                size = size_custom;
            }
            else if(size) {
                size = {width: size.split('x')[0], height: size.split('x')[1]};
            }

            return {
                alt: image.name,
                ...Object.keys(image)
                    .filter((k) => k.startsWith('value'))
                    .reduce((acc, cur) => {
                        acc[cur] = this.$helpers.resizeImage(image[cur], size);
                        return acc;
                    }, {})
            };
        },
        link() {
            let result = {};
            if (this.image?.value) {
                let { link, 'custom_link': customLink, lightbox } = this.data.settings;
                if (link === 'lightbox') {
                    if (lightbox === 'yes') {
                        result['ecom-modal'] = 'image';
                        result['ecom-modal-source'] = this.image.value;
                    }
                    result.href = this.image.value;
                } else if (link === 'custom' && customLink?.href) {
                    Object.assign(result, customLink);
                }
            }
            return result;
        },
        settings() {
            return [
                {
                    group_title: "General",
                    params:[
                        {
                            type: 'popup',
                            name: 'link',
                            label: 'Link',
                            options: {
                                type: 'dropdown',
                                default:false,
                                values: {
                                    '': 'None',
                                    lightbox: 'Media File',
                                    custom: 'Custom URL'
                                }
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            type: 'size',
                            name: 'size_custom',
                            options: {
                                visible: (data : any) => {
                                    return data['size'] === 'custom';
                                },
                                warnings: {
                                    content:
                                        'You can crop the original image size to any custom size. You can also set a single value for height or width in order to keep the original size ratio.'
                                }
                            }
                        },
                        {
                            type: 'link',
                            name: 'custom_link',
                            options: {
                                visible: (data : any) => {
                                    return data['link'] === 'custom';
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            name: 'use_button',
                            label: 'Show as button',
                            description: 'Must turn on overlay required to use this option.',
                            options: {
                                visible: (data : any) => {
                                    return data['link'] === 'custom';
                                },
                                oneline: true,
                                values: {
                                    off: {
                                        label: 'NO',
                                        value: false
                                    },
                                    on: {
                                        label: 'YES',
                                        value: true
                                    }
                                }
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            type: 'toggle',
                            name: 'overlay_use',
                            label: 'Enable Overlay',
                            options: {
                                oneline: true,
                                values: {
                                    off: {
                                        label: 'NO',
                                        value: false
                                    },
                                    on: {
                                        label: 'YES',
                                        value: true
                                    }
                                }
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            type: 'popup',
                            label: 'Title source',
                            name: 'overlay_title',
                            options: {
                                default:false,
                                type: 'dropdown',
                                values: {
                                    alt: 'Alt',
                                    custom: 'Custom'
                                },
                                visible: (data : any) => {
                                    return data['overlay_use'] === true;
                                }
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            type: 'toggle',
                            name: 'use_caption',
                            label: 'Use image caption',
                            options: {
                                oneline: true,
                                values: {
                                    on: {
                                        label: 'YES',
                                        value: true
                                    },
                                    off: {
                                        label: 'NO',
                                        value: false
                                    }
                                }
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            name: "line_extra",
                            type: "line",
                            options: {
                                visible: (data : any) => {
                                    return (
                                        data['use_button'] === true
                                        && data['overlay_use'] == true 
                                        && data['link'] == 'custom'
                                    );
                                }
                            }
                        },
                        {
                            name: "paragraph_extra",
                            type:'paragraph',
                            content:'<strong>Button</strong>',
                            options: {
                                visible: (data : any) => {
                                    return (
                                        data['use_button'] === true
                                        && data['overlay_use'] == true 
                                        && data['link'] == 'custom'
                                    );
                                }
                            }
                        },
                        {
                            name: 'overlay_title_custom',
                            label: 'Custom title',
                            type: 'text',
                            options: {
                                visible: (data : any) => {
                                    return (
                                        data['overlay_use'] === true &&
                                        data['overlay_title'] == 'custom' 
                                        || data['use_button'] === true
                                        && data['overlay_use'] == true 
                                        && data['link'] == 'custom'
                                    );
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            name: 'lightbox',
                            label: 'Open in lightbox',
                            value: 'yes',
                            options: {
                                values: {
                                    on: {
                                        label: 'YES',
                                        value: 'yes'
                                    },
                                    off: {
                                        label: 'NO',
                                        value: 'no'
                                    }
                                },
                                visible: (data : any) => {
                                    return data['link'] === 'lightbox';
                                },
                                oneline: true
                            }
                        },
                        {
                            type: 'text',
                            name: 'caption',
                            options: {
                                placeholder: 'Enter your caption',
                                visible: (data : any) => {
                                    return data['use_caption'];
                                }
                            }
                        },
                        {
                            type: 'choose',
                            label: 'Content Aligntment',
                            name: 'content_alignment',
                            value: '',
                            options: {
                                visible: (data : any) => {
                                    return (
                                        data['use_button'] === true
                                        && data['overlay_use'] == true
                                        && data['link'] == 'custom'
                                    );
                                },
                                type: 'text-align',
                                values: [
                                    "left",
                                    "center",
                                    "right"
                                ]
                            },
                            css:{
                                selector:' .ecom-button-overlay',
                                properties:{
                                    'text-align':''
                                }
                            }
                        },
                        {
                            type: 'choose',
                            label: 'Horizontal Position',
                            name: 'horizontal_btn',
                            value: '',
                            options: {
                                visible: (data : any) => {
                                    return (
                                        data['use_button'] === true
                                        && data['overlay_use'] == true
                                        && data['link'] == 'custom'
                                    );
                                },
                                type: 'align-x-full',
                                values: [
                                    "left",
                                    "center",
                                    "right"
                                ]
                            },
                            css:{
                                isCss: false
                            }
                        },
                        {
                            type: 'choose',
                            label: 'Vertical Position',
                            name: 'vertical_btn',
                            value: '',
                            options: {
                                visible: (data : any) => {
                                    return (
                                        data['use_button'] === true
                                        && data['overlay_use'] == true
                                        && data['link'] == 'custom'
                                    );
                                },
                                type: 'align-y-full',
                                values: [
                                    "top",
                                    "center",
                                    "bottom"
                                ]
                            },
                            css:{
                                isCss: false
                            }
                        }
                    ]
                },
                {
                    group_title:'Image',
                    params: [
                        {
                            type: 'picker',
                            label: 'Choose Image',
                            name: 'image',
                            options: {
                                responsive: true,
                                type: 'image',
                                editAlt: true
                            }
                        },
                        {
                            type: 'popup',
                            label: 'Crop size',
                            name: 'size',
                            value: '',
                            options: {
                                type: 'dropdown',
                                default: false,
                                values: {
                                    '': 'Original',
                                    '400x300': '400 x 300px',
                                    '800x450': '800 x 450px',
                                    '500x500': '500 x 500px',
                                    custom: 'Custom'
                                }
                            },
                            css: {
                                isCss: false
                            }
                        }
                    ]
                },
                {
                    group_name: 'hover_image',
                    group_title: 'Hover Image',
                    params: [
                        {
                            type: 'toggle',
                            name: 'enable',
                            label: 'Display Hover Image',
                            options: {
                                oneline: true,
                                values: {
                                    off: {
                                        label: 'NO',
                                        value: false
                                    },
                                    on: {
                                        label: 'YES',
                                        value: true
                                    }
                                }
                            }
                        },
                        {
                            type: 'picker',
                            label: 'Choose Image',
                            name: 'image',
                            options: {
                                visible: (data : any) => {
                                    return data['enable'] === true;
                                },
                                responsive: true,
                                type: 'image',
                                editAlt: true
                            }
                        },
                        {
                            type: 'popup',
                            label: 'Crop size',
                            name: 'size',
                            value: '',
                            options: {
                                type: 'dropdown',
                                default: false,
                                visible: (data : any) => {
                                    return data['enable'] === true;
                                },
                                values: {
                                    '': 'Original',
                                    '400x300': '400 x 300px',
                                    '800x450': '800 x 450px',
                                    '500x500': '500 x 500px',
                                    custom: 'Custom'
                                }
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            type: 'size',
                            name: 'size_custom',
                            options: {
                                visible: (data : any) => {
                                    return data['size'] === 'custom';
                                },
                                warnings: {
                                    content:
                                        'You can crop the original image size to any custom size. You can also set a single value for height or width in order to keep the original size ratio.'
                                }
                            }
                        },
                    ]
                }
            ];
        },

        javascript() {
            return function () {
                if(!this.$el) return;
                const $el = this.$el;
                if (
                    this.settings.link === 'lightbox'
                    && this.settings.lightbox === 'yes'
                    &&  window.EComModal
                    && this.$el
                ) {
                    var arr =  this.$el.querySelectorAll('[ecom-modal]');

                        if(arr.length) {
                            new window.EComModal(arr, {
                                cssClass: ['ecom-container-lightbox-' + this.id]
                            });
                        }
                }
                if(this.settings.link !== 'lightbox' && this.settings.link !== 'custom'){
                    if( /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ) {
                    // some code..
                        let imageHover = $el.querySelector('.ecom-image-picture-hover');
                        let image = $el.querySelector('.ecom-image-picture.ecom-image-has-hover')
                        if(!imageHover || !image) return;
                        imageHover.addEventListener('click', function(){
                            image.classList.add('image-active');
                            imageHover.classList.add('image-hidden');
                        });
                        image.addEventListener('click', function(){
                            image.classList.remove('image-active');
                            imageHover.classList.remove('image-hidden');
                        });
                    }
                }
            };
        },
        css() {
            return `
                .ecom-base-image.ecom-overlay > .ecom-overlay-text{
                    text-align:center
                }
                .ecom-image-align{
                    display:flex;
                }
                .ecom-image-picture-link,
                .ecom-image-picture{
                    overflow:hidden;
                    width:100%;
                    height:100%;
                }

                .ecom-image-pictures-wrapper {
                    position: relative;
                    width: 100%;
                }


                .ecom-base-image picture {
                    flex:1 1 auto;
                    min-height:1px;
                }

                .ecom-image-picture.ecom-image-has-hover,
                .ecom-overlay ~ .ecom-image-pictures-wrapper .ecom-image-picture.ecom-image-has-hover {
                    visibility: visible;
                    opacity: 1;
                    transition: .4s ease-in-out;
                }

                .ecom-image-pictures-wrapper:hover .ecom-image-picture.ecom-image-has-hover,
                .ecom-overlay:hover ~ .ecom-image-pictures-wrapper .ecom-image-picture.ecom-image-has-hover {
                    visibility: hidden;
                    opacity: 0;
                }

                .ecom-image-picture-hover {
                    position: absolute;
                    inset: 0;
                    visibility: hidden;
                    opacity: 0;
                    transition: .4s ease-in-out;
                }
                .ecom-image-picture-hover img {
                    height: 100%;
                }

                .ecom-image-pictures-wrapper:hover .ecom-image-picture-hover,
                .ecom-overlay:hover ~ .ecom-image-pictures-wrapper .ecom-image-picture-hover {
                    visibility: visible;
                    opacity: 1;
                }

                .ecom-base-image picture img{
                    display: block;
                    width:100%;
                }
                .ecom-base-image figure{
                    display: flex;
                }
                .ecom-container-image{
                    display: flex;
                    flex-direction: column;
                    width:100%;
                }
                .ecom-image-picture-link,
                .ecom-image-content-position{
                    position: relative;
                }
                .ecom-base-image.ecom-overlay {
                    position: absolute;
                    z-index: 1;
                    opacity: 0.5;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                }

                .ecom-button-overlay {
                    position: absolute;
                    opacity: 0;
                    visibility: hidden;
                    transition: .2s ease-in-out;
                }

                .ecom-base-image.ecom-overlay:hover .ecom-button-overlay {
                    opacity: 1;
                    visibility: visible;
                }

                .ecom-image__caption{
                    width:100%;
                }
                @media screen and (max-width: 768px) {
                    .ecom-image-picture-hover.image-hidden { 
                        visibility: hidden !important;
                        opacity: 0 !important;
                    }
                    .ecom-image-picture.ecom-image-has-hover.image-active {
                        visibility: visible !important;
                        opacity: 1 !important;
                    }
                }
            `;
        },
        default(){
            return {
                settings: {
                    image: {
                        value: "/images/placeholder.png"
                    },
                    link: "lightbox",
                    lightbox: "no",
                    use_caption: false,
                    caption: "Hello",
                    overlay_use: false,
                    hover_image: {
                        image: {
                            value: "/images/placeholder.png"
                        }
                    },
                    custom_link: {
                        href: "#"
                    },
                    overlay_title_custom: "Content"
                },
                style: {
                    image: {
                        imageOpacitynormalmode: 1,
                        imageOpacityhovermode: 1
                    },

                    imageAlign: "center",

                    overlay: {
                        opacity: 0,
                        backgroundhovermode: {
                            classic: {
                                "background-color": "rgba(17, 24, 39, 0.3)"
                            }
                        },
                        opacityHover: 1,
            			tab: "normal",
            			imageOpacity: 1

                    }
                }
            }
        }
    },
    watch: {
        // image: {
        //     deep: true,
        //     handler: function () {
        //         this.refresh();
        //     }
        // },
        // link: {
        //     deep: true,
        //     handler: function () {
        //         this.refresh();
        //     }
        // },
        imageHover: {
            deep: true,
            handler: function () {
                this.refresh();
            }
        },
        enableLightBox: {
            deep: true,
            handler: function() {
                this.refresh();
            }
        },
        useOverlay: {
            deep: true,
            handler: function() {
                this.refresh();
            }
        }
    },
    methods: {
        refresh() {
            this.imageComp.refresh = this.$helpers.randid();
        },
        isCaption() {
            return this.data.settings?.use_caption && this.data.settings.caption;
        },

        style() {
            let style = [];
            if (this.image) {
                let delTransition, delFilter = 0;
                if(this.data.settings.hover_image.enable && this.imageHover) {
                    delTransition = 2;
                    delFilter = 1;
                }
                style = [
                    {
                        group_alias:'image',
                        options:{
                            selector: ' .ecom-base-image .ecom-image-default'
                        },
                        modify:{
                            remove: [
                                {
                                    index: 7,
                                    length: delFilter
                                },
                                {
                                    index: 16,
                                    length: delTransition
                                }
                            ],
                            params:[
                                {
                                    position:0,
                                    fields:[
                                        {
                                            type: 'choose',
                                            label: 'Alignment',
                                            name: 'imageAlign',
                                            options: {
                                                oneline: true,
                                                responsive: true,
                                                type: 'text-align',
                                                values: ['flex-start', 'center', 'flex-end'],
                                                visible: {
                                                    keep_data: false,
                                                    condition: data => data.imageWidth !== '100%'
                                                }
                                            },
                                            css: {
                                                selector:'root .ecom-image-align',
                                                properties: {
                                                    'align-items':''
                                                },
                                            }
                                        }
                                    ]
                                },
                                {
                                    position: 8,
                                    fields:[
                                    {
                                        name: "normal_image_filter",
                                        label: "CSS Filters",
                                        type: "popup",
                                        options: {
                                            visible: (data : any) => {
                                                return data['tab'] == 'normal'
                                            },
                                            oneline: true,
                                            type: "filter"
                                        },
                                        css: {
                                            selector: "root .ecom-image-has-hover img",
                                        }
                                    },
                                    ]
                                }
                            ]
                        }
                    },
                ];
            }

            if(this.imageHover && this.data.settings.hover_image.enable) {
                style.push(
                    {
                        group_name: 'hover_image',
                        group_title: 'Hover Image',
                        selector: ' .ecom-base-image .ecom-image-picture-hover img',
                        params: [
                            {
                                name: "imageObjectFit",
                                label: "Image fit",
                                type: "popup",
                                options: {
                                    type: "dropdown",
                                    default: false,
                                    values: {
                                        none: "None",
                                        fill: "Fill",
                                        contain: "Contain",
                                        cover: "Cover",
                                        "scale-down": "Scale down"
                                    }
                                },
                                css: {
                                    properties: {
                                        "object-fit": ""
                                    }
                                }
                            },
                            {
                                name: "imageOpacity",
                                type: "number",
                                label: "Opacity",
                                options: {
                                    step: 0.01,
                                    min: 0.1,
                                    max: 1
                                },
                                css: {
                                    properties: {
                                        opacity: ""
                                    }
                                }
                            },
                            {
                                name: "imageAnimation",
                                label: "Animation",
                                type: "dropdown",
                                options: {
                                    search: true,
                                    type: "animation",
                                    size: "small"
                                },
                                css: {
                                    selector: "root .ecom-image-pictures-wrapper:hover .ecom-image-picture-hover, root .ecom-overlay:hover + .ecom-image-pictures-wrapper .ecom-image-picture-hover",
                                    properties: {
                                        animation: ""
                                    }
                                }
                            },
                            {
                                name: "imageTransition",
                                type: "number",
                                label: 'Transition Duration <span class="lowercase">(ms)</span>',
                                options: {
                                    min: 0,
                                    max: 1500
                                },
                                css: {
                                    properties: {
                                        transition: "all %value%ms ease"
                                    }
                                }
                            }
                        ]
                    }
                )
            }

            if (this.isCaption()) {
                style.push({
                    group_alias:'text',
                    options:{
                        group_name:'caption',
                        group_title: 'Caption',
                        selector: ' .ecom-base-image .ecom-image__caption',
                    },
                    modify:{
                        params:{
                            position:20,
                            fields:{
                                alias:'spacing'
                            }
                        }
                    }
                });
            }
            let checkOverlayText = this.data.settings?.overlay_title && (this.data.settings.overlay_title == 'alt' || this.data.settings.overlay_title_custom);
            if (this.data.settings?.overlay_use) {
                style.push({
                    group_alias: !checkOverlayText ? 'background:overlay' : 'background:overlaytext',
                    options:{
                        group_name:'overlay',
                        group_title: 'Overlay',
                        selector: ' .ecom-base-image .ecom-base-image-container-overlay'
                    }
                });
            }

            if (this.data?.settings?.use_button) {
                style.push({
                    group_alias:'button',
                    options:{
                        group_name:'overlay_button',
                        group_title: 'Overlay Button',
                        selector: ' .ecom-button-overlay',
                    },
                    modify: [
                        {
                            position:20,
                            fields:{
                                alias:'spacing',
                            }
                        }
                    ]
                });
            }

            return style;
        }
    }
};
</script>
