<template>
    <div class="ecom-element ecom-collection ecom-collection__tags">
        <div class="ecom-collection__tags-wrapper">
            <div class="ecom-collection__tags-container" v-html="liquid('tags')" />
        </div>
    </div>
</template>

<script>
import { Liquid, Element } from '@/mixins';
export default {
    name: 'CollectionBanner',
    docs: 'https://help.ecomposer.io/docs/elements/collection-elements/collection-tags/',
    mixins: [Liquid, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        liquids() {
            return {
                tags: {
                    code: `
                        {% capture limit %}${this.data.settings?.type === 'current' ? this.limit: 100}{% endcapture %}
                        {% assign limit  = limit | plus: 0 %}
                        {% if collection.all_tags %}

                            <ul class="ecom-collection__tags-list" role="list">
                                ${this.type === 'current'? `{% for tag in collection.all_tags limit: limit %}`
                                :
                                `
                                    {% capture custom_tags %}${this.tags.join(',')}{% endcapture %}
                                    {% assign custom_tags = custom_tags | split: ','%}
                                    {% for tag in custom_tags  limit: limit %}
                                    ${this.hide_on_not_exists? '{% unless collection.all_tags contains tag %}{% continue %}{% endunless%}' : ''}
                                `
                                }

                                       <li class="ecom-collection__tags-list-item
                                            {% if current_tags contains tag %}
                                                ecom-collection__tags-list-item-to-remove
                                                ">
                                                {{ tag | link_to_remove_tag: tag }}
                                            {% else %}
                                                 ecom-collection__tags-list-item-to-add
                                                 ">
                                                 {{ tag | link_to_tag: tag }}
                                            {% endif %}

                                       </li>
                                {% endfor %}
                            </li>
                        {% endif %}
                        `,
                    preview: `
                            <div class="ecom-skeleton-item">
                                <div>
                                    <div class="ecom-skeleton-row">
                                        <div class="ecom-skeleton-col-4"></div>
                                        <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                        <div class="ecom-skeleton-col-6"></div>
                                        <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                        <div class="ecom-skeleton-col-2"></div>
                                        <div class="ecom-skeleton-col-10 ecom-skeleton-empty"></div>
                                    </div>
                                </div>
                            </div>
                        `
                }
            };
        },
        limit() {
            return this.data?.settings?.limit ?? 12;
        },
        settings() {
            return [
                {
                    params: [
                        {
                            type: 'popup',
                            label: 'Layout',
                            name: 'flexDirection',
                            value: 'column',
                            options: {
                                type: 'dropdown',
                                default: false,
                                values: {
                                    column: 'Vertical',
                                    row: 'Horizontal',
                                }
                            },
                            css:{
                                selector: ' .ecom-collection__tags-list',
                                properties:{
                                    'flex-direction':''
                                }
                            }
                        },
                        {
                            type: 'popup',
                            name: 'type',
                            label: 'Tags type',
                            options: {
                                type: 'dropdown',
                                values: {
                                    current: 'Tags of current collection',
                                    custom: 'Manual fill tags'
                                }
                            },
                            css: false
                        },
                        {
                            type: 'number',
                            label: 'Maximum tags to show',
                            name: 'limit',
                            options: {
                                min: 1,
                                max: 100,
                                visible: function (data) {
                                    return !data['type'] || data['type'] === 'current';
                                }
                            }
                        },
                        {
                            type: 'textarea',
                            name: 'tags',
                            label: 'Enter the tags to filter in the collection',
                            description:
                                'Enter tags for each line (Note: divide value with linebreaks (Enter).',
                            placeholder: 'Summer,Winter',

                            options: {
                                visible: function (data) {
                                    return data['type'] && data['type'] === 'custom';
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            label: 'Hide tags that don\'t exist',
                            name: 'hide_on_not_exists',
                            options: {
                                visible: function (data) {
                                    return data['type'] && data['type'] === 'custom';
                                },
                                values: {
                                    on: {
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'no',
                                        value: false
                                    }
                                }
                            }
                        },
                        {
                            name: 'column-gap',
                            type: 'number',
                            label: 'Column gap',
                            value: '12px',
                            options: {
                                units: {
                                    px: {
                                        min: 1,
                                        max: 64,
                                        step: 1
                                    }
                                },
                                responsive: true,
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['flexDirection'] === 'row';
                                    }
                                }
                            },
                            css: {
                                selector: ' .ecom-collection__tags-list',
                            }
                        },
                        {
                            name: 'row-gap',
                            type: 'number',
                            label: 'Row gap',
                            value: '12px',
                            options: {
                                units: {
                                    px: {
                                        min: 1,
                                        max: 64,
                                        step: 1
                                    }
                                },
                                responsive: true
                            },
                            css: {
                                selector: ' .ecom-collection__tags-list',
                            }
                        }
                    ]
                }
            ];
        },
        hide_on_not_exists() {
            return this.data?.settings?.hide_on_not_exists ?? false;
        },
        type() {
            return this.data?.settings?.type ?? 'current';
        },

        tags() {
            let tags = this.data?.settings?.tags ?? '';
            return tags.split('\n');
        },

        requestShopifyType() {
            return {
                shopify_type: 'collection'
            };
        },

        css() {
            return `
                .ecom-collection__tags-container,
                .ecom-collection__tags-list{
                    list-style: none;
                    padding: 0;
                    display:flex;
                    flex-wrap:wrap;
                }
                .ecom-collection__tags-list-item a{
                    text-decoration:none;
                    display:inline-flex;
                }
            `;
        },

        default() {
            return {
                settings: {
                    limit: 10,
                    type: "current",
                    tags: "Shoes\nBag",
                    hide_on_not_exists: true,
                    "row-gap": "10px",
                    flexDirection: "row",
                    "column-gap": "10px"
                },
                style: {
                    general: {
                        blogTagsAlignment: "flex-start"
                    },
                    tag: {
                        spacing: {
                            padding: {
                                left: "16px",
                                top: "8px",
                                bottom: "8px",
                                right: "16px"
                            }
                        },
                        buttonColornormalmode: "#111827",
                        buttonBackgroundnormalmode: {
                            classic: {
                                "background-color": "rgba(17, 24, 39, 0.05)"
                            }
                        },
                        buttonColorhovermode: "#111827",
                        buttonBackgroundhovermode: {
                            classic: {
                                "background-color": "rgba(17, 24, 39, 0.1)"
                            }
                        }
                    }
                }
            }
        }
    },
    methods: {
        style() {
            return [
                {
                    group_alias: 'box',
                    options: {
                        group_title: 'General',
                        group_name: 'general',
                        selector: ' .ecom-collection__tags-list',
                    },
                    modify: {
                        params: [
                            {
                                type: 'choose',
                                label: 'Alignment',
                                name: 'blogTagsAlignment',
                                options: {
                                    oneline: true,
                                    responsive: true,
                                    type: 'text-align',
                                    values: ['flex-start', 'center', 'flex-end']
                                },
                                css: {
                                    selector: 'root .ecom-collection__tags-container',
                                    properties: {
                                        display: 'flex',
                                        'justify-content': ''
                                    }
                                }
                            }
                        ]
                    }
                },
                {
                    group_alias: 'button',
                    options: {
                        group_title: 'Tag',
                        group_name: 'tag',
                        selector: ' .ecom-collection__tags-list-item a'
                    }
                }
            ];
        }
    }
};
</script>
