<template>
    <div class="ecom-element ecom-product-single ecom-product-single__quantity">
        <div class="ecom-product-single__quantity-wrapper">
            <div class="ecom-product-single__quantity-container">
                <label class="ecom-product-single__quantity-container-label" for="quantity" v-text="lang(title, 'quanity_label')" />
                <div class="ecom-product-single__quantity-controls">
                    <button
                        type="button"
                        class=" ecom-product-single__quantity-controls-button ecom-product-single__quantity-controls-minus"
                    >
                        <ecom-icon :icon="icons.faMinus" class="ecom-product-single__quantity-controls-icon" />
                    </button>
                    <input
                        name="quantity"
                        type="number"
                        value="1"
                        max="9999"
                        min="1"
                        class="ecom-product-single__quantity-input"
                        required
                    >

                    <button
                        type="button"
                        class=" ecom-product-single__quantity-controls-button ecom-product-single__quantity-controls-plus"
                    >
                        <ecom-icon :icon="icons.faPlus" class="ecom-product-single__quantity-controls-icon" />
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {faPlus,faMinus} from '@fortawesome/pro-light-svg-icons';
import { Element, Javascript } from '@/mixins';

export default {
    name: 'ProductQuantity',

    presets:true,

    mixins: [Element, Javascript],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        icons(){
            return {faPlus,faMinus};
        },
        css() {
            return `
              .ecom-product-single__quantity-wrapper{
                display:flex;
            }
            .ecom-product-single__quantity-container{
                display:flex;

            }
            .ecom-product-single__quantity-controls {
                display: flex;
                align-items: center;
            }
            .ecom-product-single__quantity-controls-button {
                outline: none;
                cursor: pointer;
                border: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                margin: 0;
                transition: background-color .1s cubic-bezier(.4,0,.6,1);
            }
            .ecom-product-single__quantity-input:focus-visible {
                box-shadow: none;
            }
            .ecom-product-single__quantity-input {
                cursor: text;
                display: flex;
                align-items: center;
                justify-content: center;
                transition: background-color .1s cubic-bezier(.4,0,.6,1);
            }
            .ecom-product-single__quantity-input::-webkit-outer-spin-button,
                input::-webkit-inner-spin-button {
                -webkit-appearance: none;
                margin: 0;
            }
            .ecom-product-single__quantity-container {
                width: 100%
            }

                `;
        },
        settings() {
            return [
                {
                    params: [
                        {
                            name: 'title',
                            label: 'Title',
                            type: 'text'
                        },
                        {
                            type: 'popup',
                            label: 'Layout',
                            name: 'layout',
                            options: {
                                oneline: true,
                                responsive: true,
                                preview: 'title',
                                default: false,
                                type: 'dropdown',
                                values: {
                                    column: 'Vertical',
                                    row: 'Horizontal',
                                },
                                visible: function (data) {
                                    return data.title;
                                }
                            },
                            css: {
                                properties: {
                                    'flex-direction': ''
                                },
                                selector: ' .ecom-product-single__quantity-container',
                            }
                        },
                        {
                            type: 'number',
                            name:'spacing',
                            label:'Spacing',
                            options:{
                                responsive: true,
                                units:{
                                    px:{
                                        min:0,
                                        max:50
                                    }
                                }
                            },
                            css:{
                                selector: ' .ecom-product-single__quantity-container',
                                properties: {
                                    gap: ''
                                }
                            }
                        },
                        {
                            type: 'choose',
                            label: 'Alignment',
                            name: 'justify-content',
                            options: {
                                oneline: true,
                                responsive: true,
                                type: 'text-align',
                                values: ['flex-start', 'center', 'flex-end']
                            },
                            css: {
                                selector: ' .ecom-product-single__quantity-wrapper',
                            }
                        }
                    ]
                }
            ];
        },
        javascript() {
            return function () {
                const $el = this.$el;
                    if(!$el) {
                        return;
                    }

                    const $plus = $el.querySelector('.ecom-product-single__quantity-controls-plus');
                    const $minus = $el.querySelector('.ecom-product-single__quantity-controls-minus');
                    const $input = $el.querySelector('.ecom-product-single__quantity-input');
                if ($input.value === '') {
                    $input.value = '1';
                }
                $plus.addEventListener('click', function () {
                    $input.stepUp();
                    $input.dispatchEvent(new Event('change'));
                });
                $minus.addEventListener('click', function () {
                    $input.stepDown();
                    $input.dispatchEvent(new Event('change'));
                });
                $input.addEventListener('change', function (e) {
                    if (e.target.max && parseInt(e.target.max) < e.target.value) {
                        e.target.value = e.target.max;
                    }
                });
            };
        },
        requestShopifyType() {
            return {
                shopify_type: 'product'
            };
        },
        title() {
            return this.data && this.data.settings && this.data.settings.title
                ? this.data.settings.title
                : '';
        },
        default() {
            return {
                "settings": {
                    "justify-content": "flex-start",
                    "spacing": "0px",
                    "layout": "column"
                },
                "style": {
                    "quanity_title": {
                        "textTextAlign": "left",
                        "spacing": {
                            "margin": {
                                "bottom": "0px"
                            }
                        },
                        "textColor": "#2e2c2c",
                        "textTypography": {
                            "font-weight": "500"
                        }
                    },
                    "quanity_quanity": {
                        "border": {
                            "border-style": "solid",
                            "border-width": {
                                "top": "1px",
                                "left": "1px",
                                "bottom": "1px",
                                "right": "1px"
                            },
                            "border-color": "rgba(17, 24, 39, 0.2)"
                        },
                        "tab": "normal",
                        "borderRadius": {
                            "right": "0px",
                            "top": "0px",
                            "left": "0px",
                            "bottom": "0px"
                        },
                        "outline": {
                            "outline": {
                                "outline-style": "none"
                            }
                        },
                        "text-align": "center",
                        "color": "#404040",
                        "spacing": {
                            "margin": {
                                "top": "0px",
                                "left": "0px",
                                "bottom": "0px",
                                "right": "0px"
                            },
                            "padding": {
                                "top": "0px",
                                "left": "0px",
                                "bottom": "0px",
                                "right": "0px"
                            }
                        }
                    },
                    "quanity_plus": {
                        "iconBordernormalmode": {
                            "border-style": "solid",
                            "border-width": {
                                "top": "1px",
                                "left": "1px",
                                "bottom": "1px",
                                "right": "0px"
                            },
                            "border-color": "rgba(17, 24, 39, 0.2)"
                        },
                        "tab": "normal",
                        "iconFontSize": "12px",
                        "iconBackgroundnormalmode": {
                            "classic": {
                                "background-color": "#f5f5f5"
                            }
                        },
                        "iconPrimaryColornormalmode": "#2b2b2b",
                        "iconBackgroundhovermode": {
                            "classic": {
                                "background-color": "#d1d1d1"
                            }
                        },
                        "padding": {
                            "top": "0px",
                            "left": "15px",
                            "bottom": "0px",
                            "right": "15px"
                        }
                    },
                    "quanity_minus": {
                        "iconBordernormalmode": {
                            "border-style": "solid",
                            "border-width": {
                                "top": "1px",
                                "left": "0px",
                                "bottom": "1px",
                                "right": "1px"
                            },
                            "border-color": "rgba(17, 24, 39, 0.2)"
                        },
                        "tab": "normal",
                        "iconFontSize": "12px",
                        "iconPrimaryColornormalmode": "#000000",
                        "iconBackgroundnormalmode": {
                            "classic": {
                                "background-color": "#f5f5f5"
                            }
                        },
                        "iconBackgroundhovermode": {
                            "classic": {
                                "background-color": "#cfcfcf"
                            }
                        },
                        "padding": {
                            "top": "0px",
                            "left": "15px",
                            "bottom": "0px",
                            "right": "15px"
                        }
                    },
                    "general": {
                        "height": "40px"
                    }
                },
                "advanced": {
                    "width-max": "unset",
                    "spacing": {
                        "margin": {
                            "top": "15px"
                        }
                    }
                }
            }
        }
    },
    methods: {
        style() {
            return [
                {
                    group_name: 'general',
                    group_title: 'General',
                    selector: ' .ecom-product-single__quantity-container .ecom-product-single__quantity-controls input, .ecom-product-single__quantity-controls-button',
                    params: [
                        {
                            type: "number",
                            label: 'Height',
                            name: 'height',
                            options: {
                                responsive: true,
                                reset: true,
                                units: {
                                    px: {
                                        min: 0,
                                        max: 100
                                    },
                                }
                            },
                            css: {
                                properties: {
                                    height: ""
                                }
                            }
                        }
                    ]
                },
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_title: 'Title',
                        group_name: 'quanity_title',
                        selector: ' .ecom-product-single__quantity-container label'
                    }
                },
                {
                    group_alias: 'input',
                    options: {
                        group_title: 'Quantity input',
                        group_name: 'quanity_quanity',
                        selector:
                            ' .ecom-product-single__quantity-container .ecom-product-single__quantity-controls input'
                    },

                },
                {
                    group_alias: 'icon:hover',
                    options: {
                        group_title: 'Minus',
                        group_name: 'quanity_plus',
                        selector:
                            ' .ecom-product-single__quantity-container .ecom-product-single__quantity-controls-minus'
                    },
                    modify:{
                        params:[
                            {
                            position:10,
                            fields: {
                                type: 'dimension',
                                label: 'Padding',
                                name: 'padding',
                                options: {
                                    units: 'default',
                                    simple: true
                                }
                            }
                        }

                        ]
                    }
                },
                {
                    group_alias: 'icon:hover',
                    options: {
                        group_title: 'Plus',
                        group_name: 'quanity_minus',
                        selector:
                            ' .ecom-product-single__quantity-container .ecom-product-single__quantity-controls-plus'
                    },
                    modify:{
                        params:[
                            {
                                position:10,
                                fields: {
                                    type: 'dimension',
                                    label: 'Padding',
                                    name: 'padding',
                                    options: {
                                        units: 'default',
                                        simple: true
                                    }
                                }
                            }

                        ]
                    }
                }
            ];
        }
    }
};
</script>
