<template>
    <div class="ecom-element ecom-shopify-elements ecom-shopify__newsletter-form">
        <div class="ecom-shopify__newsletter-form-wrapper ecom-w__full">
            <div class="ecom-shopify__newsletter-form-container">
                <div v-html="liquid('newsletter')" />
            </div>
        </div>
    </div>
</template>

<script>
import { Liquid, Element, Javascript } from '@/mixins';
export default {
    name: 'NewsletterForm',
    presets:true,

    mixins: [Liquid, Element, Javascript],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        liquids() {
            return {
                newsletter: {
                    code: `
                    {% form 'customer', action:'/contact',  class: 'ecom-shopify__newsletter-form' ${this.data.settings?.tracking_id ? `,data-ec-tracking-id:'${this.data.settings?.tracking_id}'`:''},id:'newsletter-form-${
                        this.data.id
                    }' %}
                        <input type="hidden" name="contact[tags]" value="${this.data.settings?.customer_tag || 'newsletter'}" />
                        <div class="ecom-newsletter-form-content">
                            <input
                            id="ECom-NewsletterForm-${this.data.id}"
                            type="email"
                            name="contact[email]"
                            class="ecom-shopify__newsletter-form-field--input"
                            value="{{ form.email }}"
                            aria-required="true"
                            autocorrect="off"
                            autocapitalize="off"
                            autocomplete="email"
                            {% if form.errors  %}
                                autofocus
                                aria-invalid="true"
                            {% endif %}
                            placeholder="${this.data.settings?.email_placeholder}"
                            required
                            >
                        <button type="submit" class="ecome-shopify__newsletter__button" name="commit">
                        <span> ${this.lang(this.data.settings?.button_title, 'submit_title')} </span>
                        <span class="ecom-shopify__newsletter__button-icon">${this.lang(this.data.settings?.icon)} </span>
                        </button>
                        </div>



                        {%- if form.posted_successfully? or EComBuilderMode == true ${!this.data.settings.show_preview ? ' and false ' :''} -%}
                        <div class="ecom-shopify__newsletter-form-success" {% unless EComBuilderMode %}tabindex="-1"  autofocus{% endunless %}>
                            ${this.lang(this.data.settings?.success_message, 'success_message')}
                        </div>
                        {%- endif -%}
                        {%- if form.errors or EComBuilderMode == true  ${!this.data.settings.show_preview ? ' and false ' :''}  -%}
                        <div class="ecom-shopify__newsletter-form-message">
                            <h2 class="ecom-shopify__newsletter-form-status-error" role="alert" {% unless EComBuilderMode %}tabindex="-1"  autofocus{% endunless %}>
                              ${this.lang(this.data.settings?.error_title, 'error_title')}
                            </h2>
                            <p class="ecom-shopify__newsletter-form-detail-error">
                                {% if EComBuilderMode %}
                                    This is example error text when missing email field
                                {% endif %}
                                {{ form.errors.translated_fields['email'] | capitalize }} {{ form.errors.messages['email'] }}
                            </p>
                        </div>
                        {%- endif -%}
                        <div class="ecom-shopify__newsletter-form-message ecom-dn">
                            <p class="ecom-shopify__newsletter-form-detail-error">
                                ${this.lang('Email have already subscribed!', 'error_already')}
                            </p>
                        </div>
                    {% endform %}

                    `,
                    preview: ''
                }
            };
        },
        javascript() {
            return function () {
                const $el = this.$el;
                const $this = this;
                if(!$el) return;
                const form = $el.querySelector('form');
                let link = this.settings.link_redirect;
                if(this.isLive) {
                    function ecomNewsletterRedirect(){
                        if(!link) return false;
                        let href = link.href;
                        if(href=='') return false;
                        let target = link.target;
                        let url = window.location.href;
                        if(url.includes('customer_posted=true')){
                            if(target === '_blank'){
                                window.open(href);
                            }
                            else{
                                window.location.href = href;
                            }
                        }
                    }
                    ecomNewsletterRedirect();
                    if(window.location.pathname.includes('challenge') && this.settings.scroll_in_view) {
                        setTimeout(function() {
                            var element = document.querySelector(".shopify-challenge__container");
                            element.scrollIntoView();
                        }, 100)
                    }
                    if (location.search === '?contact%5Btags%5D=newsletter&form_type=customer') {
                        
                        if(form) {
                            form.querySelector('.ecom-shopify__newsletter-form-message.ecom-dn')
                            && form.querySelector('.ecom-shopify__newsletter-form-message.ecom-dn').classList.remove('ecom-dn');
                        }
                    };
                    if(form.dataset.ecTrackingId)
                    {
                        form.addEventListener('submit', function(){
                            if(window.Shopify && window.Shopify.analytics)
                            {
                                Shopify.analytics.publish("ec_custom_events",{
                                    "button_id": form.id,
                                    "tracking_id": form.dataset.ecTrackingId
                                })
                            }
                        },{once: true});
                    }
                    

                }
            }
        },
        settings() {
            return [
                {
                    group_title: 'General',
                    params: [
                        {
                            type: 'text',
                            label: 'Placeholder text',
                            name: 'email_placeholder',
                            value: 'example@gmail.com',
                            placeholder: 'example@gmail.com'
                        },
                        {
                            type: 'text',
                            label: 'Submit button label',
                            name: 'button_title',
                            value: 'Subscribe',
                            placeholder: 'Subscribe'
                        },
                        {
                            type: 'picker',
                            label: 'Icon',
                            name: 'icon',
                            options: {
                                type: 'icon',
                                layout: 'grid',
                                output: 'value',
                                multiple: false,
                                simple: false
                            }
                        },
                        {
                            type: 'choose',
                            label: 'Icon position',
                            name: 'icon_position',
                            options: {
                                type: 'align-x',
                                values: [-1, 1],
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['icon'];
                                    }
                                }
                            },
                            css:{
                                selector:' .ecom-shopify__newsletter__button-icon',
                                properties:{
                                    order:''
                                }
                            }
                        },
                        {
                            type: 'number',
                            label: 'Icon spacing',
                            name: 'spacing',
                            options: {
                                units: {
                                    px: {
                                        min: 0,
                                        max: 200
                                    }
                                },
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['icon'];
                                    }
                                }
                            },
                            css:{
                                selector:' .ecome-shopify__newsletter__button',
                                properties:{

                                    gap:''
                                }
                            }
                        },
                        {
                            type: "line"
                        },
                        {
                            type: 'text',
                            label: 'Customer tag',
                            name: 'customer_tag',
                            value: 'newsletter',
                            placeholder: 'newsletter'
                        },
                        {
                            type: "line"
                        },
                        {
                            type: 'popup',
                            label: 'Layout',
                            name: 'layout',
                            options: {
                                type: 'dropdown',
                                default: false,
                                values: {
                                    row: 'Horizontal',
                                    column: 'Vertical'
                                },
                                preview: 'title'
                            },
                            css:{
                                selector:' .ecom-newsletter-form-content',
                                properties:{
                                    'flex-direction':'',
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            name: 'show_preview',
                            label:'Show preview messages',
                            options:{
                                    values: {
                                    on: {
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            name: 'scroll_in_view',
                            value: true,
                            label:'Scroll to form after submit',
                            options:{
                                    values: {
                                    on: {
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                }
                            }
                        },
                        {
                            type: 'text',
                            label: 'Success message',
                            name: 'success_message',
                            value: 'Subscribed successfully!',
                            placeholder: 'Subscribed successfully!'
                        },
                        {
                            type: 'text',
                            label: 'Error message',
                            name: 'error_title',
                            placeholder: 'Oops! An error has occured'
                        },
                        {
                            type: 'line'
                        },
                        {
                            type: 'link',
                            name: 'link_redirect',
                            label: 'Link redirect after successful form submit'
                        }
                    ]
                },
                {
                    
                    group_title: 'Tracking',
                    params: [
                     
                        {
                            type: 'text',
                            name: 'tracking_id',
                            label: 'Tracking ID',
                            options: {
                                placeholder: 'newsletter_form'
                            }
                        },
                        {
                            type: 'paragraph',
                            content: 'You can use this field to add a tracking ID to your form. This ID will be sent to your EC Analytics when the form is submited. \n Example: *newsletter_form*'
                        },
                    ]
                }
            ];
        },

        fields() {
            return this.data && this.data.settings && 'fields' in this.data.settings
                ? this.data.settings?.fields
                : [];
        },
        style() {
            const params = [
                {
                    group_alias: 'box',
                    options: {
                        group_title: 'General',
                        group_name: 'general'
                    },
                    modify: {
                        params: [
                            {
                                alias: 'width',
                                options: {
                                    css: {
                                        selector: 'root .ecom-newsletter-form-content'
                                    }
                                }
                            },
                            {
                                alias:'align-items',
                                options:{
                                    label: 'Alignment',
                                    css:{
                                        selector: ' .ecom-shopify__newsletter-form-container form',
                                        properties:{
                                            'justify-content':'',
                                            'text-align': `if(value === 'flex-start') return 'left';
                                                            else if(value === 'flex-end') return 'right';
                                                            return 'center';`
                                        }
                                    }
                                }
                            },
                            {
                                alias:'align-items',
                                options:{
                                    label:'Content alignment',
                                    name: 'content_alignment',
                                    css:{
                                        selector:'root .ecom-newsletter-form-content',
                                        properties:{
                                            'justify-content':'',
                                            'align-items': ''
                                        }
                                    }
                                }
                            }
                        ]
                    }
                },
                {
                    group_alias: 'input',
                    options: {
                        group_name: 'input',
                        group_title: 'Input',
                        selector: ' .ecom-shopify__newsletter-form-field--input'
                    },
                    modify: {
                        params: [
                            {
                                position: 5,
                                fields: [
                                    {
                                        alias: 'height'
                                    }
                                ]
                            }
                        ]
                    }
                },
                {
                    group_alias: 'button',
                    options: {
                        group_title: 'Button',
                        group_name: 'button',
                        selector: ' .ecome-shopify__newsletter__button'
                    },
                    modify: {
                        params: [
                            {
                                position: 7,
                                fields: [
                                    {
                                        alias:'align-items',
                                        options:{
                                            label:'Content alignment',
                                            name: 'btn_text_alignment',
                                            css:{
                                                properties:{
                                                    'justify-content':'',
                                                    'align-items': ''
                                                }
                                            }
                                        }
                                    }
                                ]
                            }
                        ]
                    }
                },
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_name: 'noticeSuccess',
                        group_title: 'Success message',
                        selector: ' .ecom-shopify__newsletter-form-success'
                    }
                },
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_name: 'noticeError',
                        group_title: 'Error message',
                        selector: ' .ecom-shopify__newsletter-form-status-error'
                    },
                    modify:{
                        params: [
                                {
                                    position:20,
                                    fields: [
                                        {
                                            type: 'line'
                                        },
                                        {
                                            type: 'paragraph',
                                            content: '### Error detail text'
                                        },
                                                                                {
                                            type: 'choose',
                                            label: 'Alignment',
                                            name: 'error_text_align',
                                            options: {
                                                oneline: true,
                                                responsive: true,
                                                type: 'text-align',
                                                values: ['left', 'center', 'right']
                                            },
                                            css: {
                                                selector: 'root .ecom-shopify__newsletter-form-detail-error:not(empty)',
                                                properties: {
                                                    'text-align': ''
                                                },
                                            }
                                        },
                                        {
                                            type: 'popup',
                                            label: 'Typography',
                                            name: 'error_typo',
                                            options: {
                                                global: {
                                                    type: 'typography'
                                                },
                                                oneline: true,
                                                responsive: true,
                                                type: 'typography'
                                            },
                                            css: {
                                                selector: 'root  .ecom-shopify__newsletter-form-detail-error:not(empty)',
                                            }
                                        },
                                        {
                                            name: 'error_color',
                                            label: 'Text Color',
                                            type: 'color',
                                            options: {
                                                oneline: true,
                                                global: {
                                                    type: 'colors'
                                                }
                                            },
                                            css: {
                                                selector: 'root .ecom-shopify__newsletter-form-detail-error:not(empty)',
                                                properties: {
                                                    'color': ''
                                                },
                                            }
                                        },

                                        {
                                            name: 'error_text_shadow',
                                            label: 'Text Shadow',
                                            type: 'popup',
                                            options: {
                                                oneline: true,
                                                type: 'text-shadow'
                                            },
                                            css: {
                                                selector: 'root .ecom-shopify__newsletter-form-detail-error:not(empty)',
                                                properties: {
                                                    'text-shadow': ''
                                                },
                                            }
                                        },
                                        {
                                             alias: 'spacing',
                                             options: {
                                                 name: 'error_line_spacing',
                                                 css: {
                                                        selector: 'root .ecom-shopify__newsletter-form-detail-error:not(empty)',
                                                    }
                                             }
                                        }


                                    ]
                                }
                            ]
                    }
                }
            ];
             if (this.data.settings?.icon) {
                params.push({
                    group_alias:'icon',
                    options:{
                        group_name: 'icon',
                        group_title: 'Icon',
                        selector: ' .ecom-shopify__newsletter__button-icon',
                    },
                    modify:{
                        params:[
                            {
                                position:15,
                                fields:[
                                    {
                                        type: 'line'
                                    },
                                    {
                                        alias:'spacing'
                                    }
                                ]
                            }
                        ]
                    }
                });
            }
            return params;
        },
        css() {
            return `
                .ecom-shopify__newsletter-form-heading,.ecom-shopify__newsletter-form-paragraph
                {
                    text-align:center;
                }

                .ecom-shopify__newsletter-form {
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: center;
                    align-items: center;
                    margin: 0px auto;
                    gap: 5px;
                }
                .ecom-shopify__newsletter-form-field label {
                    display: none;
                }
                .ecom-shopify__newsletter-form-field--input {
                    background: #FFFFFF;
                    border: 1px solid #D1D5DB;
                    outline:none;
                    padding: 9px 13px;
                    border-radius:0;
                }
                .ecom-shopify__newsletter-form-field--input:focus{
                    border-color:rgba(5, 150, 105,1);
                    // box-shadow:rgb(255, 255, 255) 0px 0px 0px 0px, rgba(5, 150, 105,1) 0px 0px 0px 1px, rgba(0, 0, 0, 0.05) 0px 1px 2px 0px;
                }
                .ecom-shopify__newsletter-form-field--input:focus-visible {
                    // border : none;
                    outline : none;
                    box-shadow: none
                }
                    .ecom-shopify__newsletter-form-heading {
                        margin: 0;
                    }
                    .ecom-shopify__newsletter-form-paragraph {
                        margin-top : 12px;
                        font-size: 1.6rem;
                        line-height: 24px;
                    }
                     .ecome-shopify__newsletter__button {
                        border-radius:0;
                        background : rgba(5, 150, 105,1);
                        padding : 9px 17px;
                        color: #FFFFFF;
                        border: none;
                        cursor: pointer;
                        flex-direction: row;
                        display: flex;
                        align-items: center;
                    }
                    .ecome-shopify__newsletter__button:hover {
                       background-color: rgb(97 188 160);
                    }
                    .ecom-newsletter-form-content{
                        display:flex;
                        align-items:center;
                        justify-content: space-between;
                    }
                    .ecom-shopify__newsletter-form-success,
                    .ecom-shopify__newsletter-form-message{
                        width: 100%
                    }
                    .ecom-shopify__newsletter__button-icon svg {
                        width: 24px;
                        height: 24px;
                    }
                    @media screen and (max-width : 640px) {
                        .ecom-newsletter-form-content{
                            flex-wrap:wrap;
                        }
                    }
            `;
        },
        default(){
            return  {
                "settings": {
                    "email_placeholder": "example@gmail.com",
                    "button_title": "Subscribe",
                    "success_message": "Subscribed successfully!",
                    "error_title": "Opps!. An error has occurred",
                    "link_redirect": {
                        "href": "",
                        "target": ""
                    },
                },
                "style": {
                    "general": {
                        "alignItems": "center",
                        "align-items": "center"
                    },
                    "input": {
                        "text-align": "left",
                        "spacing": {
                            "margin": {
                                "right": "10px",
                                "top": "0px",
                                "left": "0px",
                                "bottom": "0px"
                            },
                            "padding": {
                                "top": "10px",
                                "left": "10px",
                                "bottom": "10px",
                                "right": "10px"
                            }
                        },
                        "borderRadius": {
                            "top": "0px",
                            "left": "0px",
                            "bottom": "0px",
                            "right": "0px"
                        },
                        "tab": "normal"
                    },
                    "button": {
                        "buttonColornormalmode": "#ffffff",
                        "buttonBackgroundnormalmode": {
                            "classic": {
                                "background-color": "#047857"
                            }
                        },
                        "buttonColorhovermode": "#ffffff",
                        "buttonBackgroundhovermode": {
                            "classic": {
                                "background-color": "#047857"
                            }
                        },
                        "spacing": {
                            "padding": {
                                "top": "10px",
                                "left": "10px",
                                "bottom": "10px",
                                "right": "10px"
                            }
                        },
                        "tab": "hover",
                        "transition": {
                            "transition": {
                                "duration": "300ms",
                                "delay": "200ms",
                                "timing": "ease-in"
                            }
                        }
                    },
                    "noticeSuccess": {
                        "textTextAlign": "center",
                        "spacing": {
                            "margin": {
                                "top": "0px",
                                "bottom": "0px",
                                "right": "0px"
                            }
                        }
                    },
                    "noticeError": {
                        "textTextAlign": "center",
                        "spacing": {
                            "padding": {
                                "top": "0px",
                                "left": "0px",
                                "bottom": "0px",
                                "right": "0px"
                            },
                            "margin": {
                                "top": "0px",
                                "left": "0px",
                                "bottom": "0px",
                                "right": "0px"
                            }
                        }
                    }
                },
                "advanced": {
                    "spacing": {
                        "margin": {
                            "top": "0px"
                        },
                        "padding": {
                            "top": "20px"
                        }
                    }
                }
            }
        }
    },
    methods: {}
};
</script>