<template>
    <div
        ref="wrapper"
        class="ecom__element element__heading ecom-element ecom-html"
        :data-stopdrag="true"
    >
        <Liquid :data="getMetaLiquid" />
        <component
            :is="data?.settings?.tag ?? 'h2'"
            v-if="data.settings?.link && data.settings?.link.href"
            class="ecom__heading"
        >
            <a
                v-bind="data.settings?.link"
                ref="content"
                :contenteditable="isFullScreen ? null : true"
                spellcheck="false"
                @click="edit"
                @keyup="update"
                @blur="editing=false"
                v-html="editing ? editingValue : exporting ? lang(this.convertText, 'heading-text', metaLang) : content"
            />
        </component>
        <component
            :is="data?.settings?.tag ?? 'h2'"
            v-else
            ref="content"
            :contenteditable="isFullScreen ? null : true"
            class="ecom__heading"
            spellcheck="false"
            @click="edit"
            @keyup="update"
            @blur="editing=false"
            v-html="editing ? editingValue : exporting ? lang(this.convertText, 'heading-text', metaLang) : content"
        />
    </div>
</template>

<script lang="ts">
    import { Element, Javascript, Liquid } from '@/mixins';
    export default {
        name: 'Heading',
        presets:true,
        docs: 'https://help.ecomposer.io/',
        mixins: [Element, Javascript, Liquid],
        props: {
            data: {
                type: Object,
                default () {
                    return {};
                }
            }
        },
        data () {
            return {
                editing: false,
                is: 'heading',
                editingValue: '',
                metaLang: {},
                metaLiquid: '',
                jsreactives:[
                'animation', 'style', 'heading_highlight','link', "line"
                ]
            };
        },
        computed: {
            heading () {
                return this.data;
            },
            content(){
                let content_heading = this.data.settings?.content;
                const $this = this;
                let highlight_text = this.headingHighlight || '';
                let line = this.data.settings?.line || '30%';
                let highlight_array='';
                highlight_array = highlight_text.split('|');
                if(content_heading && highlight_text){
                    content_heading = this.data.settings?.content.replace('[highlight heading]',`
                            <span class="ecom__heading-highlight" style="background-size: 100% ${line};${this.backgroundHighlight}">${highlight_text}</span>`);
                    if(this.data.settings?.animation && this.data.settings?.animation === 'typing'){
                        if(!this.data.settings?.style){
                            content_heading = this.data.settings?.content.replace('[highlight heading]',`
                                <span class="ecom__heading-highlight highlight-animation--typing" style="background-size: 100% ${line};${this.backgroundHighlight}" data-heading="${highlight_text}">${highlight_array[0] ? highlight_array[0] : ''}</span>`);
                        }
                        else{
                            content_heading = this.data.settings?.content.replace('[highlight heading]',`
                                <span class="ecom__heading-highlight highlight-animation--typing ecom__heaading-linear-gradient" style="background-size: 100% ${line};${this.backgroundHighlight}" data-heading="${highlight_text}">${highlight_array[0] ? highlight_array[0] : ''}</span>`);
                        }
                    }
                    else if(this.data.settings?.animation && this.data.settings?.animation === 'smooth'){
                        let item = '';
                        highlight_array.forEach(function(element,index) {
                            if(!$this.data.settings?.style){
                                item += `<span class="heading-animation--smooth-item ${index==0 ? 'is-active' : ''}">${element}</span>`
                            }
                            else{
                                item += `<span class="heading-animation--smooth-item ${index==0 ? 'is-active' : ''}" style="background-size: 100% ${line};${$this.backgroundHighlight}">${element}</span>`
                            }
                        });
                        if(!this.data.settings?.style){
                            content_heading = this.data.settings?.content.replace('[highlight heading]',`
                                <span class="ecom__heading-highlight highlight-animation--smooth" style="display: inline-flex;background-size: 100% ${line};${this.backgroundHighlight}">${item}</span>`);
                        }
                        else{
                            content_heading = this.data.settings?.content.replace('[highlight heading]',`
                                <span class="ecom__heading-highlight highlight-animation--smooth ecom__heaading-linear-gradient" style="display: inline-flex;">${item}</span>`);
                        }
                    }
                }
                return content_heading;
            },
            headingHighlight(){
                return this.data.settings?.heading_highlight; 
            },
            settings () {
                return [
                    {
                        params: [
                            {
                                type: 'textarea',
                                label: 'Heading text',
                                description: 'Highlight heading text you enter bellow will replace the text "[highlight heading]"',
                                name: 'content',
                                options: {
                                    toolbar: 'short',
                                    dynamic: true,
                                    height: 80
                                }
                            },
                            {
                                type: 'link',
                                label: 'Insert Link',
                                name: 'link'
                            },
                            {
                                type: 'choose',
                                label: '<span class="uppercase">HTML</span> Tag',
                                name: 'tag',
                                options: {
                                    type: 'heading'
                                }
                            },
                            {
                                type: 'line'
                            },
                            {
                                type: 'text',
                                name: 'heading_highlight',
                                label: 'Highlight heading',
                                description: 'Multiple text: highlight1|highlight2|...|highlight(n). Separate by "|" ([Mutiple texts] only works when the Animation option is turned on)'
                            },
                            {
                                type: 'toggle',
                                name: 'style',
                                label: 'Use text gradient',
                                value: false,
                                options: {
                                    values: {
                                        on: {
                                            label: 'Yes',
                                            value: true
                                        },
                                        off: {
                                            label: 'No',
                                            value: false
                                        }
                                    }
                                }
                            },
                            {
                                type: 'popup',
                                name: 'animation',
                                label: 'Animation',
                                options: {
                                    type: 'dropdown',
                                    default: false,
                                    preview: 'title',
                                    values: {
                                        off: 'Off',
                                        typing: 'Typing',
                                        smooth: 'Smooth',
                                    }
                                }
                            },
                            {
                                type: "number",
                                name: "line",
                                label: "Height highlight",
                                "options": {
                                    "responsive": false,
                                    "units": {
                                        "%": {
                                            "min": 0,
                                            "max": 100
                                        }
                                    },
                                    visible: {
                                        name: 'style',
                                        value: false
                                    }
                                },
                                "slider": true,
                                "input": true,
                                "min": 0,
                                "max": 100
                            }
                        ]
                    }
                ];
            },
            style () {
                let style = [
                    {
                        group_alias:'text',
                        options: {
                            group_name: 'heading',
                            group_title: 'Heading',
                            selector: ' .element__heading .ecom__heading'
                        },
                        modify: {
                            remove: {
                                index: 2, length: 1
                            },
                            params: [
                                {
                                    position: 3,
                                    fields: {
                                        name: 'textColor',
                                        label: 'Text Color',
                                        type: 'color',
                                        options: {
                                            oneline: true,
                                            global: {
                                                type: 'colors'
                                            }
                                        },
                                        css: {
                                            properties: {
                                                'color': ''
                                            },
                                            selector: 'root  .element__heading .ecom__heading,root  .element__heading .ecom__heading a'
                                        }
                                    },
                                },
                                {
                                    position: 6,
                                    fields: {alias: 'border'}
                                },
                                {
                                    position: 10,
                                    fields: {alias: 'spacing'}
                                }
                            ]
                        }
                    },
                    {
                        group_alias:'text',
                        options: {
                            group_name: 'heading_highlight',
                            group_title: 'Highlight heading',
                            selector: ' .ecom__heading-highlight'
                        },
                        modify: {
                            remove: [
                                
                                {
                                    index: 2, length: 1
                                }
                            ],
                            params: [
                                {
                                    position: 3,
                                    fields: {
                                        name: 'textColor',
                                        label: 'Text Color',
                                        type: 'color',
                                        options: {
                                            oneline: true,
                                            global: {
                                                type: 'colors'
                                            }
                                        },
                                        css: {
                                            properties: {
                                                '-webkit-text-fill-color': ''
                                            },
                                            selector: 'root .ecom__heading-highlight'
                                        }
                                    },
                                },
                                
                                {
                                    position: 6,
                                    fields: {alias: 'border'}
                                },
                                {
                                    position: 10,
                                    fields: {alias: 'spacing'}
                                },
                                {   position:4,
                                    fields: {
                                        type: 'background',
                                        label: 'Background',
                                        name: 'sub_gradient',
                                        options: {
                                            oneline: true,
                                            reset: true,
                                        }
                                    }
                                }
                            ]
                        }
                    }
                ]

                //check 
                if(this.data.settings.style) {
                    style[1].modify.params.pop();
                    style[1].modify.remove.push({index: 0, length: 1});
                }
                else{
                    style[1].modify.remove.push({index: 4, length: 1});
                    style[1].modify.remove.push({index: 0, length: 1});
                }
                return style;
            },
            backgroundHighlight(){
                if(this.data.settings.style==true) {
                    if(!this.data.style?.heading_highlight?.text_gradient){
                        if(this.data.style?.heading_highlight?.textColor){
                            return `--background-image: linear-gradient(90deg, ${this.data.style?.heading_highlight?.textColor} 0%, ${this.data.style?.heading_highlight?.textColor} 100%)`;
                        }
                        return `--background-image: linear-gradient(90deg, #000000 0%,#000000 100%)`;
                    }
                    let background_gradient = JSON.parse(JSON.stringify(this.data.style?.heading_highlight?.text_gradient)) || "";
                    let background_linear = background_gradient.gradient.positions||[];
                    let background_css = '';
                    let color = '';
                    background_linear.forEach(function(value,index){
                        if(index<background_linear.length-1){
                            color += `${value.color} ${value.index}%,`;
                        }
                        else{
                            color += `${value.color} ${value.index}%`;
                        }
                    });
                    let background_type = background_gradient.gradient.type || 'linear';
                    let background_position = background_gradient.gradient.position || 'center center';
                    let angle = background_gradient.gradient.angle || '180';
                    if(background_type === 'linear'){
                        background_css = `--background-image: linear-gradient(${angle}deg, ${color});`;
                        return background_css;
                    }
                    else if(background_type === 'radial'){
                        background_css = `--background-image: radial-gradient(at ${background_position}, ${color});`;
                        return background_css;
                    }
                    return '';
                }
                let background = this.data.style?.heading_highlight?.sub_gradient;
                if(!background) return '';
                if(background.classic){
                    let background_classic = background.classic || "" ;
                    let background_classic_json = JSON.stringify(background_classic);
                    if(!background_classic_json.includes('background-image')){
                        let color = background_classic['background-color'];
                        return `background-image: linear-gradient(${color}, ${color});`;
                    }
                    let background_image = background_classic['background-image'];
                    let background_attachment = background_classic['background-attachment'];
                    let background_position = background_classic['background-position'];
                    let background_repeat = background_classic['background-repeat'];
                    let background_size = background_classic['background-size'];
                    if(background_classic['image-size']){
                        let background_format = background_classic['image-size'].format||'';
                        if(background_format && background_format!=''){
                            return `background-image: url(${background_image});
                            background-attachment: ${background_attachment};
                            background-position: ${background_position};
                            background-repeat: ${background_repeat};
                            background-size: ${background_size};
                            `;
                        }
                        else{
                            let width = background_classic['image-size'].width||false;
                            let height = background_classic['image-size'].height || false;
                            if(width && height){
                                let background_position = background_classic['image-size'].crop || 'center';
                                return `background-image: url(${background_image});
                                        background-size: ${width}px ${height}px;
                                        background-position: ${background_position};
                                        `;
                            }
                        }
                    }
                    // 
                    return `background-image: url(${background_image});
                            background-attachment: ${background_attachment};
                            background-position: ${background_position};
                            background-repeat: ${background_repeat};
                            background-size: ${background_size};
                            `;
                }
                if(background.gradient){
                    let background_gradient = JSON.parse(JSON.stringify(background.gradient)) || "";
                    let background_linear = background_gradient.positions||[];
                    let background_css = '';
                    let color = '';
                    background_linear.forEach(function(value,index){
                        if(index<background_linear.length-1){
                            color += `${value.color} ${value.index}%,`;
                        }
                        else{
                            color += `${value.color} ${value.index}%`;
                        }
                    });
                    let background_type = background_gradient.type || 'linear';
                    let background_position = background_gradient.position || 'center center';
                    let angle = background_gradient.angle || '180';
                    if(background_type === 'linear'){
                        background_css = `background-image: linear-gradient(${angle}deg, ${color});`;
                        return background_css;
                    }
                    else if(background_type === 'radial'){
                        background_css = `background-image: radial-gradient(at ${background_position}, ${color});`;
                        return background_css;
                    }
                }
                return '';
            },
            css(){
                return `
                    .ecom__heading-highlight{
                        -webkit-text-fill-color: #000;
                        background-position: 100% 100%;
                        background-repeat: no-repeat;
                        position: relative;
                        max-width: 100%;
                    }
                    .ecom__heading-highlight.highlight-animation--typing::after {
                        content: "|";
                        position: absolute;
                        font-weight: 100;
                        color: #000;
                        animation:  blink 1s step-end infinite;
                    }
                    .ecom__heading-highlight.highlight-animation--typing.ecom__heaading-linear-gradient::after{
                       
                    }

                    @keyframes blink {
                        0%, 100% {opacity: 1;}
                        50% {opacity: 0;}
                    }
                    
                    .ecom__heading-highlight.highlight-animation--smooth::after{
                        position: absolute;
                        content: '';
                        width: 2px;
                        height: 100%;
                        background-color: #000;
                        top: 0;
                        right: -5px;
                    }
                    .heading-animation--smooth-item{
                        display: inline-block;
                        position: absolute;
                        top: 0;
                        left: 0;
                        white-space: nowrap;
                        margin: 0;
                        visibility: hidden;
                        opacity:0;
                    }
                    .ecom__heaading-linear-gradient>.heading-animation--smooth-item{
                        background-image: var(--background-image);
                        -webkit-background-clip: text;
                        -webkit-text-fill-color: transparent;
                    }
                    .heading-animation--smooth-item.is-active{
                        position: relative;
                        visibility: visible;
                        opacity:1;
                        overflow: hidden;
                        -webkit-animation: animateSmooth 3s ease-in-out infinite;
                    }
                    @-webkit-keyframes animateSmooth {
                        0% {
                            max-width: 0;
                        }
                        50% {
                            max-width: 1000px;

                        }
                        100% {
                            max-width: 0;
                        }
                    }
                `
            },
            

            activeMe () {
                return (this.editingElement?.data?.id ?? 0) === this.heading.id;
            },
            getMetaLiquid() {
                this.convertText;
                return this.metaLiquid;
            },
            convertText() {
                const _this = this;
                let text = this.data.settings?.content;
                if(text) {
                    const dom = document.createElement('div');
                    dom.innerHTML = text;
                    const meta_nodes = dom.querySelectorAll('span[data-meta]');
                    if(meta_nodes) {
                        meta_nodes.forEach(el => {
                            let meta_key = el.dataset.metaKey.replaceAll(' ', '_'),
                            metafiled = el.dataset.meta,
                            meta = document.createTextNode(`{{${meta_key}}}`);;
                            if(metafiled.indexOf('|') == -1) {
                                _this.metaLang[meta_key] = metafiled
                            } else {
                                _this.metaLiquid += `{%- assign ${meta_key} = ${metafiled}-%}`
                                _this.metaLang[meta_key] = meta_key
                            }

                            el.replaceWith(meta);
                        });
                    }
                    let HTML = dom.innerHTML;
                    HTML = this.content;
                    return HTML;
                }
            },
            javascript() {
                return function() {
                    if(!this.$el)
                    {
                        return false;
                    }
                    const $el = this.$el;
                    const $this = this;
                    const $animation = this.settings.animation;
                    const eventRunAnimation = new CustomEvent("animation", function(){
                    });
                    let $elementEdit = $el.querySelector('.ecom__heading');
                    if($elementEdit.querySelector('a')){
                        $elementEdit = $elementEdit.querySelector('a');
                        // console.log($elementEdit);
                    }
                    $elementEdit.addEventListener('animation',()=>{
                            headingAnimation();
                        });
                    $elementEdit.addEventListener('blur',function(e){
                        e.target.dispatchEvent(eventRunAnimation);
                    });
                    function headingAnimation(){
                        if($animation==='typing'){
                            let element = $el.querySelector('.highlight-animation--typing');
                            if(!element) return false;
                            let arr = element.dataset.heading.split('|') || '';
                            let index = 0, isAdding = true, textToBeTypedIndex = 0;
                            // Typing text
                            const _this = $this;
                            function playAnim() {
                                setTimeout(function () {
                                    let animation = _this.settings?.animation
                                    // set the text of typeText to a substring of the text to be typed using index.
                                    element.innerText = arr[textToBeTypedIndex].slice(0, index)
                                    if (isAdding) {
                                    // adding text
                                        if (index > arr[textToBeTypedIndex].length) {
                                            
                                            // no more text to add
                                            isAdding = false;
                                            //break: wait 2s before playing again
                                            if(animation=='typing'){
                                                setTimeout(function () {
                                                    playAnim();
                                                }, 2000);
                                            }
                                            return;
                                        } 
                                        else {
                                            // increment index by 1
                                            index++;
                                        }
                                    } 
                                    else {
                                    // removing text
                                        if (index === 0) {
                                            // no more text to remove
                                            isAdding = true;
                                            //switch to next text in text array
                                            textToBeTypedIndex = (textToBeTypedIndex + 1) % arr.length;
                                        } else {
                                            // decrement index by 1
                                            index--;
                                        }
                                    }
                                    // call itself
                                    playAnim();
                                }, isAdding ? 120 : 60);
                            }
                            // start animations
                            playAnim();
                        }
                        if($animation==='smooth'){
                            let elements = $el.querySelectorAll('.heading-animation--smooth-item');
                            if(!elements) return false;
                            function playAnim(){
                                let count = 0;
                                let animationSmooth = setInterval(function (){
                                    if($animation!='smooth'){
                                        clearInterval(animationSmooth);
                                    }
                                    count++;
                                    elements.forEach((item, index)=> {
                                        if(count == elements.length) {
                                            count = 0;
                                        }
                                        if(index == count) {
                                            item.classList.add('is-active');
                                        } else {
                                            item.classList.remove('is-active');
                                        }
                                    });
                                },3000);
                                
                            }
                            playAnim();
                            
                        }
                    }
                    headingAnimation();
                    
                }
            },
            default() {
                return  {
                    "settings": {
                        "style": false,
                        "animation": 'smooth',
                        "content": "Ecomposer is [highlight heading]",
                        "tag": "h3",
                        "heading_highlight": "Creative|Awesome|Beautiful",
                        "line": "30%"
                    },
                    "style": {
                        "text": {
                            "textTextAlign": "left",
                            "textColor": "#000000",
                            "textTypography": {
                                "font-weight": "500",
                                "font-size": "30px"
                            }
                        },
                        "heading_highlight": {
                        "sub_gradient": {
                            "classic": {
                            "background-size": "cover",
                            "background-repeat": "no-repeat",
                            "background-position": "center left",
                            "background-attachment": "scroll",
                            "background-color": "#f27e7e"
                            }
                        },
                        "text_gradient": {
                            "gradient": {
                            "positions": [
                                {
                                "index": 0,
                                "color": "#e34646"
                                },
                                {
                                "index": 100,
                                "color": "#0033ff"
                                }
                            ],
                            "type": "linear",
                            "angle": 90,
                            "position": "bottom center"
                            }
                        },
                        "textColor": "#000000",
                        "textTypography": {
                            "font-weight": "600"
                        }
                        },
                        "heading": {
                            "textTypography": {
                                "font-size": "50px"
                            },
                        "textColor": "#595757"
                        }
                    }
                }
            }
            
        },
        watch: {
            activeMe (val) {
                if (!val && this.editing === true) {
                    this.editing = false;
                    this.heading.settings.content = this.$refs.content.innerHTML;
                }
            }
        },
        methods: {
            edit() {
                this.convertText;
                if (this.editing === false) {
                    this.editing = true;
                    this.editingValue = this.data.settings.content;
                }
            },
            update(e) {
                this.heading.settings.content = e.target.innerHTML;
            }
        },

    };
</script>
