<template>
    <div ref="wrapper" class="ecom__element element__spacer ecom-element">
        <div class="ecom__element-spacer">
            <div class="ecom__element-spacing" />
        </div>
    </div>
</template>

<script>
import { Element, Javascript, Liquid } from '@/mixins';
export default {
    name: 'Spacer',
    docs: 'https://help.ecomposer.io/docs/elements/basic-elements/spacer/',
    presets: false,
    mixins: [Element, Javascript, Liquid],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    data() {
        return {};
    },
    computed: {
        settings() {
            return [
                {
                    params: [
                        {
                            type: 'number',
                            label: 'Spacer',
                            name: 'height',
                            options: {
                                units: {
                                    px: {
                                        min: 10,
                                        max: 600,
                                        step: 1
                                    },
                                    vh: {
                                        min: 0,
                                        max: 100,
                                        step: 1
                                    },
                                    em: {
                                        min: 0.1,
                                        max: 20,
                                        step: 0.1
                                    }
                                },
                                responsive: true
                            },
                            css:{
                                selector: ' .ecom__element-spacing'
                            }
                        }
                    ]
                }
            ];
        },
        css() {
            return `
                .ecom__element-spacer{
                    display:flex;
                }
                .ecom__element-spacer div{
                    display: block; /* dont delete me */
                    width:100%;
                }
                `;
        },
        default() {
            return {
                settings:{
                    height:'50px'
                }
            }
        }
    }
};
</script>
