<template>
    <div
      class="ecom-element ecom-base ecom-base__breadcrumbs--wrapper"
      v-html="liquid('breadcrumbs')"
    >
    </div>
</template>

<script>
import { Liquid, Element } from '@/mixins';

export default {
    name: 'BaseBreadcrumbs',
    docs: 'https://help.ecomposer.io/docs/elements/basic-elements/breadcrumbs/',
    presets:true,
    mixins: [Liquid, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        liquids() {
            return {
                'breadcrumbs': {
                    code: `
                      {%- unless EComBuilderMode -%}
                        {%- assign page_type = request.page_type %}
                      {%- endunless %}
                      {%- capture icon_parent -%}
                      ${this.data.settings?.general?.icon_parent ?
                        `<span class="ecom-base__breadcrumbs--icon">
                            ${this.data.settings?.general?.icon_parent}
                          </span>
                        `
                        : ``
                      }
                      {%- endcapture -%}
                      {%- capture icon_breadcrumbs -%}
                      ${this.data.settings?.general?.icon_breadcrumbs ?
                        `<span class="ecom-base__breadcrumbs--icon">
                            ${this.data.settings?.general?.icon_breadcrumbs}
                          </span>
                        `
                        : ``
                      }
                      {%- endcapture -%}

                      {%- unless page_type == 'index' or page_type == 'cart' or page_type == 'list-collections' or page_type == '404' -%}

                      <nav class="ecom-base__breadcrumbs ecom-flex" role="navigation" aria-label="breadcrumbs">
                        <div class="ecom-base__breadcrumbs__list">
                            {{ icon_parent }}<a class="ecom-base__breadcrumbs__link" href="/">${this.lang(this.data.settings?.general?.home_text ? this.data.settings?.general?.home_text : 'Home','breadcrumbs_home' )}</a>
                          {%- case page_type -%}
                            {%- when 'page' -%}
                                {{ icon_breadcrumbs }}
                                {%- if EComBuilderMode -%}
                                  <a class="ecom-base__breadcrumbs__link" href="#" aria-current="page">Untitled</a>
                                {%- else -%}
                                  <a class="ecom-base__breadcrumbs__link" href="{{ page.url }}" aria-current="page">{{ page.title }}</a>
                                {%- endif -%}
                            {%- when 'product' -%}
                              {%- if collection.url -%}
                                  {{ icon_breadcrumbs }}{{ collection.title | link_to: collection.url }}
                              {%- endif -%}
                                {{ icon_breadcrumbs }}<a class="ecom-base__breadcrumbs__link" href="{{ product.url }}" aria-current="page">{{ product.title }}</a>
                            {%- when 'collection' and collection.handle -%}
                              {%- if current_tags -%}
                                  {{ icon_breadcrumbs }}{{ collection.title | link_to: collection.url }}
                                  {%- capture tag_url -%}{{ collection.url }}/{{ current_tags | join: "+"}}{%- endcapture -%}
                                  {{ icon_breadcrumbs }}<a class="ecom-base__breadcrumbs__link" href="{{ tag_url }}" aria-current="page">{{ current_tags | join: " + "}}</a>
                              {%- else -%}
                                  {{ icon_breadcrumbs }}<a class="ecom-base__breadcrumbs__link" href="{{ collection.url }}" aria-current="page">{{ collection.title }}</a>
                              {%- endif -%}
                            {%- when 'blog' -%}
                              {%- if current_tags -%}
                                  {{ icon_breadcrumbs }}{{ blog.title | link_to: blog.url }}
                                  {%- capture tag_url -%}{{blog.url}}/tagged/{{ current_tags | join: "+" }}{%- endcapture -%}
                                  {{ icon_breadcrumbs }}<a class="ecom-base__breadcrumbs__link" href="{{ tag_url }}" aria-current="page">{{ current_tags | join: " + " }}</a>
                              {%- else -%}
                                  {{ icon_breadcrumbs }}<a class="ecom-base__breadcrumbs__link" href="{{ blog.url }}" aria-current="page">{{ blog.title }}</a>
                              {%- endif -%}
                            {%- when 'article' -%}
                                {{ icon_breadcrumbs }}{{ blog.title | link_to: blog.url }}
                                {{ icon_breadcrumbs }}<a class="ecom-base__breadcrumbs__link" href="{{ article.url }}" aria-current="page">{{ article.title }}</a>
                            {%- else -%}
                                {{ icon_breadcrumbs }}<a class="ecom-base__breadcrumbs__link" href="{{ request.path }}" aria-current="page">{{ page_title }}</a>
                          {%- endcase -%}
                        </div>
                      </nav>
                      {%- endunless -%}{%- assign page_type = request.page_type %}
                    `,
                    preview:
                      `<div class="ecom-skeleton-item">
                          <div>
                              <div class="ecom-skeleton-row">
                                  <div class="ecom-skeleton-col-4"></div>
                                  <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                  <div class="ecom-skeleton-col-6"></div>
                                  <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                  <div class="ecom-skeleton-col-2"></div>
                                  <div class="ecom-skeleton-col-10 ecom-skeleton-empty"></div>
                              </div>
                          </div>
                      </div>`
                }
            };
        },
        params(){
          return this.$store.getters['page/params'];
        },

        settings() {
            return [
              {
                group_name: 'general',
                group_title: 'General',
                params: [
                    {
                      type: 'text',
                      label: 'Home text',
                      name: 'home_text',
                      value: 'Home'
                    },
                    {
                      type: 'picker',
                      label: 'First Parent Icon',
                      name: 'icon_parent',
                      options: {
                          type: 'icon',
                          layout: 'grid',
                          output: 'value',
                          multiple: false,
                          simple: false
                      }
                    },
                    {
                      type: 'picker',
                      label: 'Breadcrumbs Icon',
                      name: 'icon_breadcrumbs',
                      options: {
                          type: 'icon',
                          layout: 'grid',
                          output: 'value',
                          multiple: false,
                          simple: false
                      }
                    },
                ]
              }
            ];
        },
        css() {
          return `

          `;
        },
        default() {
          return {
            "settings": {
              "general": {
                "home_text": "Home",
                "icon_parent": "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 448 512\" fill=\"currentColor\"><!--! Font Awesome Pro 6.1.1 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2022 Fonticons, Inc. --><defs><style>.fa-secondary{opacity:.4}</style></defs><path class=\"fa-primary\" d=\"M256 448c-8.188 0-16.38-3.125-22.62-9.375c-12.5-12.5-12.5-32.75 0-45.25L370.8 256l-137.4-137.4c-12.5-12.5-12.5-32.75 0-45.25s32.75-12.5 45.25 0l160 160c12.5 12.5 12.5 32.75 0 45.25l-160 160C272.4 444.9 264.2 448 256 448z\"></path><path class=\"fa-secondary\" d=\"M64 448c-8.188 0-16.38-3.125-22.62-9.375c-12.5-12.5-12.5-32.75 0-45.25L178.8 256L41.38 118.6c-12.5-12.5-12.5-32.75 0-45.25s32.75-12.5 45.25 0l160 160c12.5 12.5 12.5 32.75 0 45.25l-160 160C80.38 444.9 72.19 448 64 448z\"></path></svg>",
                "icon_breadcrumbs": "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 256 512\" fill=\"currentColor\"><!--! Font Awesome Pro 6.1.1 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2022 Fonticons, Inc. --><defs><style>.fa-secondary{opacity:.4}</style></defs><path class=\"fa-secondary\" d=\"M64 448c-8.188 0-16.38-3.125-22.62-9.375c-12.5-12.5-12.5-32.75 0-45.25L178.8 256L41.38 118.6c-12.5-12.5-12.5-32.75 0-45.25s32.75-12.5 45.25 0l160 160c12.5 12.5 12.5 32.75 0 45.25l-160 160C80.38 444.9 72.19 448 64 448z\"></path></svg>"
              }
            },
            "style": {
              "icon": {
                "iconFontSize": "13px",
                "spacing": {
                  "margin": {
                    "right": "2px"
                  }
                }
              },
              "breadcrumbs_link": {
                "textTextAlign": "right",
                "textTypography": {
                  "font-size": "15px",
                  "text-decoration": "none"
                },
                "textColor": "#1f1b1b"
              }
            }
          }
        },
        requestShopifyType(){
          const page = this.params?.page;
          const type = ['collection', 'product', 'blog', 'article', 'page']
          return (type.includes(page) ? page : null);
        }
    },
    methods: {
        style() {
            return [
              {
                group_alias: 'box',
                options: {
                    group_title: 'Gerenal',
                    group_name: 'gerenal',
                    selector: ' .ecom-base__breadcrumbs'
                },
                modify: {
                  params: [
                    {
                      position: 0,
                      fields: [
                        {
                          alias: 'justify-content',
                          options: {
                            label: 'Alignment'
                          }
                        }
                      ]
                    },
                    {
                      position: 5,
                      fields: [
                        {
                          alias: 'spacing'
                        }
                      ]
                    }
                  ]
                }
              },
              {
                group_alias: 'text',
                options: {
                    group_title: 'Breadcrumbs Link',
                    group_name: 'breadcrumbs_link',
                    selector: ' .ecom-base__breadcrumbs__list a'
                },
                modify: {
                  remove: {
                    index: 0,
                    length: 1
                  }
                }
              },
              {
                group_alias: 'icon',
                options: {
                    group_title: 'Icon',
                    group_name: 'icon',
                    selector: ' .ecom-base__breadcrumbs--icon',
                },
                modify:{
                    params:[
                        {
                            position:15,
                            fields:[
                                {
                                    type:'line'
                                },
                                {
                                    alias:'spacing'
                                }
                            ]
                        }
                    ]
                  }
              }
            ];
        },
    }
};
</script>
