<template>
    <div class="ecom-element ecom-product-single ecom-product-single__description">
        <div class="ecom-product-single__description-wrapper">
            <div class="ecom-product-single__description-container" :data-show-type="type">
                <template v-if="contentType === 'text'">
                    <div class="ecom-product-single__description--paragraph">
                        <div v-html="liquid(description_short)" />
                        <div class="ecom-product-single__description--more-actions" v-if="type === 'short' && view_more">
                            <button
                                type="button"
                                class="ecom-product-single__description-view-more-btn"

                            >
                                <span class="ecom__element--button-icon" v-if="data.settings?.view_more_icon" v-html="data.settings.view_more_icon" />
                                <span v-text="lang(data.settings.view_more_text, 'view_more_text')"></span>
                            </button>
                        </div>
                    </div>
                    <div
                        v-if="type === 'short' && view_more"
                        style="display: none"
                        class="ecom-product-single__description--full"

                    >
                        <div class="ecom-product-single__description--full-content" v-html="liquid('description_text')" />
                        <div class="ecom-product-single__description--less-actions"  v-if="type === 'short' && view_more">
                            <button
                                type="button"
                                class="ecom-product-single__description-view-less-btn"
                            >
                                <span class="ecom__element--button-icon" v-if="data.settings?.view_less_icon" v-html="data.settings.view_less_icon" />
                                <span v-text="lang(data.settings.view_less_text, 'view_less_text')"></span>
                            </button>

                        </div>
                    </div>
                </template>
                <template v-else>
                    <div
                        class="ecom-product-single__description--paragraph"
                        :style="{ 'max-height': 'var(--ecom-description-height)' ,'--ecom-description-height': maxHeight}"
                        v-html="liquid(description_short)"
                    />
                    <div class="ecom-product-single__description--more-actions" v-if="type === 'short' && view_more">
                        <button
                            type="button"
                            class="ecom-product-single__description-view-more-btn"
                        >
                            <span class="ecom__element--button-icon" v-if="data.settings?.view_more_icon" v-html="data.settings.view_more_icon" />
                            <span v-text="lang(data.settings.view_more_text, 'view_more_text')"></span>
                        </button>
                    </div>
                    <div class="ecom-product-single__description--less-actions"  v-if="type === 'short' && view_more">
                            <button
                                type="button"
                                class="ecom-product-single__description-view-less-btn"
                                style="display:none"
                            >
                                <span class="ecom__element--button-icon" v-if="data.settings?.view_less_icon" v-html="data.settings.view_less_icon" />
                                <span v-text="lang(data.settings.view_less_text, 'view_less_text')"></span>
                            </button>

                        </div>
                </template>
            </div>
        </div>
    </div>
</template>

<script>
import { Liquid, Element, Javascript } from '@/mixins';
import { addSwitch } from "@/plugins/fields/utils";
export default {
    name: 'Product Description',

    mixins: [Liquid, Element, Javascript],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    data(){
        return {
            jsreactives: ['view_more_text','view_more','type','content_type']
        }
    },
    computed: {
        css() {
            return `
                .ecom-product-single__description--paragraph, .ecom-product-single__description-view-more-btn {
                    overflow: hidden;
                }
                .ecom-product-single__description--paragraph[style*="max-height"] {
                    position: relative;
                }
                .ecom-product-single__description-container[data-show-type="short"] .ecom-product-single__description--paragraph[style*="max-height"]:after {
                    content: '';
                    position: absolute;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    height: 150px;
                    background: linear-gradient(rgba(255, 255, 255, 0), rgb(255, 255, 255))
                }
                .ecom-product-single__description-view-more-btn{
                    cursor:pointer;
                    display:block;
                }
                .ecom-product-single__description-container span {
                    display: inline;
                }
                .ecom-product-single__description--more-actions, .ecom-product-single__description--less-actions{
                    display: flex;
                    position: relative;
                    width: 100%;
                }
                .ecom-product-single__description--more-actions button, .ecom-product-single__description--less-actions button{
                    display: flex;
                    align-items: center;
                    align-content: center;
                }
            `;
        },

        description_short() {
            return this.contentType === 'text'
                ? this.type === 'short'
                    ? 'short_description'
                    : 'description_text'
                : 'description';
        },
        maxHeight() {
            return this.contentType === 'html' && this.data?.settings?.type === 'short'
                ? this.data.settings['max-height']
                : null;
        },
        javascript() {
            return function () {
                /**
                 * this.settings.view_more
                 * this.settings.view_more_text
                 */
                if(!this.$el) {
                    return;
                }
                const $dom = this.$el.querySelector(
                    '.ecom-product-single__description-view-more-btn'
                );
                const contentType = this.settings.content_type;
                const $full = this.$el.querySelector('.ecom-product-single__description--full');
                const $less = this.$el.querySelector('.ecom-product-single__description-view-less-btn');
                const $short = this.$el.querySelector('.ecom-product-single__description--paragraph');
                if ($dom) {
                    $dom && $dom.addEventListener('click', function () {
                        if (contentType === 'text' && $full) {
                            $full.style.display = 'block';
                            $short.style.display = 'none';
                        } else {
                            $short.style.maxHeight = null;
                        }
                        this.style.display = 'none';
                        if($less)
                            $less.style.display = 'flex';
                    });
                    $less && $less.addEventListener('click', function(){
                        $dom.style.display = 'flex';
                        this.style.display = 'none';
                         if (contentType === 'text' && $full) {
                            if($full)
                                $full.style.display = 'none';

                            $short.style.display = 'block';
                        } else {
                            $short.style.maxHeight = 'var(--ecom-description-height)';
                        }
                    });
                }
            };
        },

        limit() {
            return  this.data?.settings?.limit && parseInt(this.data.settings.limit) > 0? parseInt(this.data.settings.limit): 50;
        },

        liquids() {
            return {
                description: {
                    code: '{{product.description}}',
                    preview: `
                        <div class="ecom-skeleton-item">
                            <div class="ecom-skeleton-row">
                                <div class="ecom-skeleton-col-4"></div>
                                <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-6"></div>
                                <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-2"></div>
                                <div class="ecom-skeleton-col-10 ecom-skeleton-empty"></div>
                            </div>
                            <div class="ecom-skeleton-row">
                                <div class="ecom-skeleton-col-10 big"></div>
                                <div class="ecom-skeleton-col-2 ecom-skeleton-empty big"></div>
                                <div class="ecom-skeleton-col-4"></div>
                                <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-6"></div>
                                <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-12"></div>
                            </div>
                        </div>
                    `
                },
                description_text: {
                    code: '{{product.description | strip_html }}',
                    preview: `
                        <div class="ecom-skeleton-item">
                            <div class="ecom-skeleton-row">
                                <div class="ecom-skeleton-col-4"></div>
                                <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-6"></div>
                                <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-2"></div>
                                <div class="ecom-skeleton-col-10 ecom-skeleton-empty"></div>
                            </div>
                            <div class="ecom-skeleton-row">
                                <div class="ecom-skeleton-col-10 big"></div>
                                <div class="ecom-skeleton-col-2 ecom-skeleton-empty big"></div>
                                <div class="ecom-skeleton-col-4"></div>
                                <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-6"></div>
                                <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                <div class="ecom-skeleton-col-12"></div>
                            </div>
                        </div>
                    `
                },
                short_description: {
                    code: `
                        <div>{{ product.description | strip_html | truncatewords: ${this.limit} }}</div>
                    `,
                    preview: `
                        <div class="ecom-skeleton-item">
                            <div>
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-4"></div>
                                    <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                    <div class="ecom-skeleton-col-6"></div>
                                    <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                    <div class="ecom-skeleton-col-2"></div>
                                    <div class="ecom-skeleton-col-10 ecom-skeleton-empty"></div>
                                </div>
                            </div>
                        </div>

                    `
                }
            };
        },

        settings() {
            return [
                {
                    group_title: 'Description',
                    params: [
                        {
                            type: 'popup',
                            label: 'Content Type',
                            name: 'content_type',
                            options: {
                                type: 'dropdown',
                                values: { text: 'Text', html: 'HTML' },
                                default: false
                            }
                        },
                        {
                            type: 'popup',
                            label: 'Description type',
                            name: 'type',
                            value: 'short',
                            options: {
                                type: 'dropdown',
                                values: {
                                    short: 'Short',
                                    full: 'Full'
                                },
                                default: false
                            }
                        },
                        {
                            type: 'number',
                            label: 'Maximum words to show',
                            name: 'limit',
                            value: '50',
                            placeholder: '50',
                            options: {
                                control_width: '40%',
                                input: true,
                                slider: false,
                                min: 0,
                                // oneline: true,
                                visible: function (data) {
                                    return (
                                        data &&
                                        data['type'] === 'short' &&
                                        data['content_type'] === 'text'
                                    );
                                }
                            }
                        },
                        {
                            type: 'number',
                            label: 'Max height',
                            name: 'max-height',
                            placeholder: '200',
                            options: {
                                units: {
                                    px: {
                                        min: 0,
                                        max: 1000
                                    },
                                    'vh': {
                                        min: 0,
                                        max: 100
                                    },
                                    'vw': {
                                        min: 0,
                                        max: 100
                                    }
                                },
                                visible: function (data) {
                                    return (
                                        data &&
                                        data['type'] === 'short' &&
                                        data['content_type'] == 'html'
                                    );
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            label: 'Show view more',
                            name: 'view_more',
                            options: {
                                oneline: true,
                                values: {
                                    on: {
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                },
                                visible: function (data) {
                                    return data['type'] === 'short';
                                }
                            }
                        },
                        {
                            type: 'picker',
                            label: 'View more icon',
                            name: 'view_more_icon',
                            options: {
                                type: 'icon',
                                layout: 'grid',
                                output: 'value',
                                multiple: false,
                                simple: false,
                                visible:function(data){return data.view_more}
                            }
                        },
                        {
                            type: 'picker',
                            label: 'View less icon',
                            name: 'view_less_icon',
                            options: {
                                type: 'icon',
                                layout: 'grid',
                                output: 'value',
                                multiple: false,
                                simple: false,
                                visible:function(data){return data.view_more}
                            }
                        },
                        {
                            type: 'choose',
                            label: 'Icon position',
                            name: 'icon_position',
                            options: {
                                type: 'align-x',
                                values: [-1, 1],
                                visible:function(data){return data.view_more}
                            },
                            css:{
                                selector:' .ecom__element--button-icon',
                                properties:{
                                    order:''
                                }
                            }
                        },
                        {
                            type: "number",
                            name: "spacing",
                            label: "Spacing",
                            options: {
                                responsive: true,
                                units: {
                                    px: {
                                        min: 1,
                                        max: 50,
                                        step: 1
                                    }
                                },
                                visible:function(data){return data.view_more},
                                reset:false
                            },
                            css: {
                                selector: 'root .ecom-product-single__description--less-actions button ,root .ecom-product-single__description--more-actions button',
                                properties: {
                                    gap: ""
                                }
                            }
                        },
                        {
                            label: 'View more text button',
                            name: 'view_more_text',
                            type: 'text',
                            options: {
                                visible: function (data) {
                                    return data['type'] === 'short' && data['view_more'];
                                }
                            }
                        } ,
                        {
                            label: 'View less text button',
                            name: 'view_less_text',
                            type: 'text',
                            options: {
                                visible: function (data) {
                                    return data['type'] === 'short' && data['view_more'];
                                }
                            }
                        }
                    ]
                }
            ];
        },

        requestShopifyType() {
            return {
                shopify_type: 'product'
            };
        },

        type() {
            return this.data?.settings?.type ?? 'short';
        },

        contentType() {
            return this.data?.settings?.content_type ?? 'text';
        },

        view_more() {
            return this.data && this.data.settings && 'view_more' in this.data.settings
                ? this.data.settings.view_more
                : false;
        },

        default() {
            return  {
                "settings": {
                    "type": "short",
                    "content_type": "html",
                    "limit": "50",
                    "view_more": true,
                    "view_more_text": "View more",
                    "view_less_text": "View less",
                    "max-height": "137px",
                    "spacing": "5px",
                    "icon_position": 1
                },
                "style": {
                    "icon": {
                        "tab": "normal",
                        "iconFontSize": "20px"
                    },
                    "collection_description": {
                        "textTextAlign": "left",
                        "textColor": "#6D7175",
                        "textTypography": {
                            "font-family": {
                                "name": "Jost",
                                "value": "https://fonts.googleapis.com/css?family=Jost:100,200,300,400,500,600,700,800,900",
                                "thumbnail": "Jost"
                            },
                            "font-size": "14px",
                            "font-weight": "300"
                        }
                    },
                    "collection_view_more": {
                        "tab": "normal",
                        "align-items": "flex-start",
                        "buttonBoxShadow": {
                            "box-shadow": {
                                "color": "rgba(255, 255, 255, 0)"
                            }
                        },
                        "buttonBordernormalmode": {
                            "border-style": "none"
                        },
                        "buttonBackgroundnormalmode": {
                            "classic": {
                                "background-color": "rgba(255, 255, 255, 0)"
                            }
                        },
                        "buttonColornormalmode": "#333",
                        "buttonBackgroundhovermode": {
                            "classic": {
                                "background-color": "rgba(255, 255, 255, 0)"
                            }
                        },
                        "buttonTypography": {
                            "font-family": {
                                "name": "Jost",
                                "value": "https://fonts.googleapis.com/css?family=Jost:100,200,300,400,500,600,700,800,900",
                                "thumbnail": "Jost"
                            },
                            "font-size": "14px",
                            "text-decoration": "underline"
                        },
                        "spacing": {
                            "margin": {
                                "top": "0px",
                                "left": "0px",
                                "bottom": "0px",
                                "right": "0px"
                            },
                            "padding": {
                                "top": "0px",
                                "left": "0px",
                                "bottom": "0px",
                                "right": "0px"
                            }
                        },
                        "buttonColorhovermode": "#333",
                        "transitions": {
                            "transitions": {
                                "duration": "300ms"
                            }
                        }
                    },
                    "collection_view_less": {
                        "tab": "normal",
                        "align-items": "flex-start",
                        "buttonColornormalmode": "#0a0808",
                        "buttonBackgroundnormalmode": {
                            "classic": {
                                "background-color": "rgba(250, 250, 250, 0)"
                            }
                        },
                        "spacing": {
                            "margin": {
                                "top": "0px",
                                "left": "0px",
                                "bottom": "0px",
                                "right": "0px"
                            },
                            "padding": {
                                "top": "0px",
                                "left": "0px",
                                "bottom": "0px",
                                "right": "0px"
                            }
                        },
                        "buttonBordernormalmode": {
                            "border-style": "none"
                        },
                        "buttonTypography": {
                            "font-family": {
                                "name": "Jost",
                                "value": "https://fonts.googleapis.com/css?family=Jost:100,200,300,400,500,600,700,800,900",
                                "thumbnail": "Jost"
                            },
                            "text-decoration": "underline"
                        }
                    }
                },
                "advanced": {
                    "spacing": {
                        "margin": {
                            "top": "50px"
                        },
                        "padding": {
                            "bottom": 0
                        }
                    }
                },
                "advanced": {
                    "custom-css": "li {\nmargin-left: 20px; list-style-type: disc;\n}"
                }
            }
        }
    },
    methods: {
        style() {
            var style = [
                {
                    group_alias: 'text',
                    options: {
                        group_title: 'Description',
                        group_name: 'collection_description',
                        selector: ' .ecom-product-single__description--paragraph, .ecom-product-single__description--full',
                    }
                }
            ];

            if (this.data.settings.view_more) {
                style.push({
                    group_alias: 'button',
                    options: {
                        group_title: 'View more',
                        group_name: 'collection_view_more',
                        selector: ' .ecom-product-single__description-view-more-btn'
                    },
                    modify: {
                        position: 0,
                        params:{
                            alias:'align-items',
                            options:{
                                label:'Alignment',
                                css:{
                                    selector:'root .ecom-product-single__description--more-actions',
                                    properties:{
                                        'align-items':'',
                                        'justify-content':''
                                    }
                                }
                            }
                        }
                    }
                });

                style.push({
                    group_alias: 'button',
                    options: {
                        group_title: 'View less',
                        group_name: 'collection_view_less',
                        selector: ' .ecom-product-single__description-view-less-btn'
                    },
                    modify: {
                        position: 0,
                        params:{
                            alias:'align-items',
                            options:{
                                label:'Alignment',
                                css:{
                                    selector:'root .ecom-product-single__description--less-actions',
                                    properties:{
                                        'align-items':'',
                                        'justify-content':''
                                    }
                                }
                            }
                        }
                    }
                });
                 const iconParamsDefault = [
                {
                    name: "iconPrimaryColor",
                    label: "Color",
                    type: "color",
                    options: {
                        oneline: true,
                        global: {
                            type: "colors"
                        },
                    },
                    css: {
                        properties: {
                            color: ""
                        }
                    }
                },
                {
                    name: "iconBackground",
                    label: "Background",
                    type: "background",
                    options: {
                        oneline: true,
                        responsive: true,
                    },
                    css: {
                        properties: {
                            background: ""
                        }
                    }
                },
                {
                    type: "popup",
                    label: "Border",
                    name: "iconBorder",
                    options: {
                        oneline: true,
                        type: "border",
                    },
                    css: {
                        properties: {
                            border: ""
                        }
                    }
                },
                {
                    name: "iconBoxShadow",
                    label: "Box Shadow",
                    type: "popup",
                    options: {
                        oneline: true,
                        type: "box-shadow",
                    },
                    css: {
                        properties: {
                            "box-shadow": ""
                        }
                    }
                },
                {
                    name: "iconBorderRadius",
                    label: "Border Radius",
                    type: "dimension",
                    options: {
                        responsive: true,
                        type: "radius",
                        units: {
                            "%": {
                                min: 0,
                                max: 100
                            },
                            px: {
                                min: 0,
                                max: 1000
                            }
                        }
                    },
                    css: {
                        properties: {
                            overflow:'hidden',
                            "border-radius": ""
                        }
                    }
                },
            ]

            if(this.data.settings?.view_more_icon || this.data.settings?.view_less_icon)
            {
                style.push({
                    group_title: 'Icon',
                    group_name: 'icon',
                    selector: ' .ecom-product-single__description-view-less-btn, .ecom-product-single__description-view-more-btn ',
                    params:[
                        {
                                        name: "iconFontSize",
                                        label: "Size",
                                        type: "number",
                                        options: {
                                            responsive: true,
                                            units: {
                                                px: {
                                                    min: 0,
                                                    max: 300
                                                }
                                            }
                                        },
                                        css: {
                                            selector: " svg",
                                            properties: {
                                                height: "",
                                                width: ""
                                            }
                                        }
                                    },
                                    {
                                        name: "iconTransform",
                                        label: "Rotate",
                                        type: "number",
                                        options: {
                                            responsive: true,
                                            min: 0,
                                            max: 360
                                        },
                                        css: {
                                            selector: " svg",
                                            properties: {
                                                transform: "rotate(%value%deg)"
                                            }
                                        }
                                    },
                                    {
                                        name: "tab",
                                        type: "tab",
                                        value: "normal",
                                        options: {
                                            tabs: [
                                                {
                                                    name: "normal",
                                                    title: "Normal"
                                                },
                                                {
                                                    name: "hover",
                                                    title: "Hover"
                                                }
                                            ]
                                        },
                                        css: {
                                            isCss: false
                                        }
                                    },
                                    ...addSwitch(iconParamsDefault,'normal' , { selector: '  svg'}),
                                    ...addSwitch(iconParamsDefault,'hover',{ selector: ':hover  svg'}),
                    ]
                });
            }
            }

            return style;
        }
    }
};
</script>