<template>
    <div ref="wrapper" class="ecom__element ecom-element-button ecom-button-default" >
        <a
            :id="button?.button_id"
            :class="animatedClass"
            :href="button?.link ? button.link.href : '#'"
            v-bind="attrLink"
            :data-ec-tracking-id="button?.tracking_id"

            class="ecom__element--button ecom-flex ecom-fl_center ecom-al_center"
        >
            <span class="ecom__element--button-icon-text" v-html="lang(button?.content, 'button-content')" />
            <span
                class="ecom-button-icon ecom__element--button-icon"
                v-html="button?.icon"
            />
        </a>
    </div>
</template>

<script>
import { Element, Javascript } from '@/mixins';
import {hover} from "@/plugins/fields/core/alias/icon";
export default {
    name: 'Button',
    presets:true,
    docs: 'https://help.ecomposer.io/docs/elements/basic-elements/button/',
    mixins: [Element, Javascript],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    data() {
        return {
            editing: false,
            jsreactives: ['animation_loop_time','animation']
        };
    },
    computed: {
        javascript() {
            return function () {
                if(!this.$el)
                {
                    return false;
                }
                const $el = this.$el;



                if (this.settings.animation) {
                    (function(_this) {
                        if (!$el) return;

                        const $btn_wrapper = $el.querySelector('.ecom__element--button');
                        if (!$btn_wrapper) return;

                        let intervalTime = parseInt(_this.settings.animation_loop_time) * 1000 || 6000,
                            animTime = 1000;

                        setInterval(function() {
                            $btn_wrapper.classList.add('animated');
                            setTimeout(function(){
                                $btn_wrapper.classList.remove('animated');
                            }, animTime);
                        }, intervalTime);

                        })(this)
                }
                var $button = $el.querySelector('.ecom__element--button');
                if(this.isLive && $button && $button.dataset.ecTrackingId)
                {
                    $button.addEventListener('click', function(e){
                        if(window.Shopify.analytics)
                        {
                            e.preventDefault();
                            let dom = document.createElement('div');
                            document.body.appendChild(dom)
                            dom.click();
                            // document.click();
                            Shopify.analytics.publish("ec_custom_events",{
                                "button_id": $button.id,
                                "tracking_id": $button.dataset.ecTrackingId
                            })
                            let clone = $button.cloneNode(true);

                            clone.click();
                        }
                    },{once:true});
                }
            };
        },
        animatedClass() {
          return this.data?.settings?.animation ? 'animated' : '';
        },
        settings() {
            return [
                {
                    group_alias:'button:settings',
                    options:{
                        group_name:'',
                        group_title:'Button'
                    },
                    modify: {
                        params: {
                            position: 10,
                            fields: [
                                {
                                    type: 'line'
                                },
                                {
                                    type: 'toggle',
                                    name: 'animation',
                                    label: 'Button animation',
                                    options: {
                                        values: {
                                            on: {
                                                label: 'Enable',
                                                value: true
                                            },
                                            off: {
                                                label: 'Disable',
                                                value: false
                                            }
                                        }
                                    },
                                },
                                {
                                    type: "number",
                                    label: 'Loop time <span class="lowercase">(s)</span>',
                                    name: "animation_loop_time",
                                    options: {
                                        min: 2,
                                        default: 6,
                                        max: 10,
                                        visible: function (data) {
                                            return data.animation == true;
                                        }
                                    }
                                },
                            ]
                        }
                    }
                },
                {

                    group_title: 'Tracking',
                    params: [

                        {
                            type: 'text',
                            name: 'tracking_id',
                            label: 'Tracking ID',
                            options: {
                                placeholder: 'your-cta-name'
                            }
                        },
                        {
                            type: 'paragraph',
                            content: 'You can use this field to add a tracking ID to your button. This ID will be sent to your EC Analytics when the button is clicked.\n Example: *your-cta-name*'
                        },
                    ]
                }
            ];
        },
        button() {
            return this.data.settings;
        },
        attrLink() {
            return this.button?.link;
        },
        css() {
            return `
                    .ecom-element-button.ecom-button-default{
                        display:flex;
                    }
                    a.ecom__element--button{
                        display: inline-flex;
                        align-items: center;
                        text-decoration: none;
                        text-align:center
                    }
                    .ecom__element--button-icon{
                        display:inline-flex;
                        color:inherit;
                    }
                    .ecom__element--button-icon svg{
                        width:24px;
                        height:auto
                    }
                `;
        },
        default(){
            return  {
                "settings": {
                    "content": "Shop Now",
                    "icon": "<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 32 32\" fill=\"currentColor\"><path d=\"M 12.96875 4.28125 L 11.53125 5.71875 L 21.8125 16 L 11.53125 26.28125 L 12.96875 27.71875 L 23.96875 16.71875 L 24.65625 16 L 23.96875 15.28125 Z\"></path></svg>",
                    "spacing": "6px"
                },
                "style": {
                    "button": {
                        "justify-content": "flex-start",
                        "buttonColornormalmode": "#ffffff",
                        "buttonBackgroundnormalmode": {
                            "classic": {
                                "background-color": "#333"
                            }
                        },
                        "buttonColorhovermode": "#ffffff",
                        "buttonBackgroundhovermode": {
                            "classic": {
                                "background-color": "rgba(51, 51, 51, 0.84)"
                            }
                        },
                        "spacing": {
                            "padding": {
                                "bottom": "8px",
                                "top": "8px",
                                "left": "30px",
                                "right": "30px"
                            },
                            "margin": {}
                        },
                        "tab": "hover",
                        "buttonTypography": {
                            "global-typography": "m5lJMKLv"
                        },
                        "buttonBorderRadiusnormalmode": {
                            "top": "3px",
                            "left": "3px",
                            "bottom": "3px",
                            "right": "3px"
                        },
                        "buttonBoxShadow": {
                            "box-shadow": {
                                "horizontal": "1px",
                                "vertical": "1px",
                                "blur": "5px",
                                "color": "rgba(51, 51, 51, 0.49)"
                            }
                        },
                        "buttonTransition": 500
                    },
                    "icon": {
                        "tab": "normal",
                        "iconFontSize": "14px"
                    }
                }
            }
        }
    },
    methods: {
        style() {
            let style = [
                {
                    group_alias: 'button',
                    options:{
                        selector: ' .ecom__element--button'
                    },
                    modify:{
                        params:[
                            {
                                position:0,
                                fields:[
                                    {
                                        alias:'justify-content',
                                        options:{
                                            label:'Alignment',
                                            css:{
                                                selector:'root .ecom-button-default'
                                            }
                                        }
                                    },
                                    {
                                        alias:'text-align',
                                        options:{
                                            label:'Content alignment',
                                            name: 'content_alignment',
                                            css:{
                                                selector:'root .ecom__element--button, root .ecom__element--button span',
                                                properties:{
                                                    'justify-content':'',
                                                    'text-align':''
                                                }
                                            }
                                        }
                                    }
                                ]
                            },

                        ]
                    }
                },
                {
                  group_name: 'button_animation',
                  group_title: 'Animation effect',
                  selector: ' .ecom__element--button',
                  params: [
                      {
                        name: "animation",
                        label: "Animation",
                        type: "popup",
                        value: "shake",
                        options: {
                            type: "dropdown",
                            values: "entrance-animation",
                            size: "small",
                        },
                        css: {
                            selector: ".animated",
                            properties: {
                                'entrance-animation': ""
                            }
                        }
                    },
                  ]
              }
            ];
            if (this.button?.icon) {
                let icon_params = [...hover('root .ecom__element--button:hover .ecom-button-icon').params];
                icon_params.push(
                    {
                        type: "line"
                    },
                    {
                        alias: "spacing"
                    }
                )
                style.push({
                    group_title:'Icon',
                    group_name:'icon',
                    selector:' .ecom-button-icon',
                    params: icon_params
                });
            }

            return style;
        }
    }
};
</script>
