<template>
    <div
        class="ecom-element ecom-product-single ecom-product-single__countdown"
        :data-show-on-sale="show_countdown_on_sale"
    >
        <div class="ecom-product-single__countdown-wrapper">
            <div
                v-if="title"
                class="ecom-product-single__countdown-title ecom-html"
                v-html="lang(title, 'countdown_title')"
            />
            <div
                class="ecom-product-single__countdown-container"
                :class="
                    data.settings.styleCountdown == 'column'
                        ? 'ecom-product-single__countdown-block'
                        : ''
                "
                :data-week="lang(data.settings.text_week, 'text_week')"
                :data-day="lang(data.settings.text_day, 'text_day')"
                :data-hour="lang(data.settings.text_hour, 'text_hour')"
                :data-minute="lang(data.settings.text_minute, 'text_minute')"
                :data-second="lang(data.settings.text_second, 'text_second')"
                :data-shows="shows"
                v-html="liquid('countdown')"
            />
            <div v-if="enable_progress_bar" class="ecom-product-single__countdown-progress-bar">
                <div class="ecom-product-single__countdown-progress-bar--timer" />
            </div>
        </div>
    </div>
</template>

<script>
import { Liquid, Element, Javascript } from '@/mixins';
export default {
    name: 'ProductCountdown',
    docs: 'https://help.ecomposer.io/docs/elements/product-elements/countdown/',
    presets:true,
    mixins: [Liquid, Element, Javascript],
    vendors: ['countdown_js'],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    data() {
        return {
            jsreactives: [
                'text_week',
                'text_day',
                'text_hour',
                'text_minute',
                'text_second',
                'enable_progress_bar',
                'show_countdown_on_sale',
                'show_week',
                'show_day',
                'show_hour',
                'show_minute',
                'show_second',
            ]
        };
    },
    computed: {

        _data() {
            return this.data;
        },
        liquids() {
            let finalDate = new Date();
            finalDate.setDate(new Date().getDate() + 3);
            finalDate = `${finalDate.getFullYear()}/${
                finalDate.getMonth() + 1
            }/${finalDate.getDate()}`;
            return {
                countdown: {
                    code: `
                    {% if product != empty %}
                        ${  this.type === 'metafields' || this.type === 'metafield_or_custom'  ?
                        `
                        {%- assign countdown = product.metafields.ecomposer.countdown -%}
                        {%- assign countdown_from = product.metafields.ecomposer.countdown_from -%}
                        ${this.type === 'metafield_or_custom'?
                                `
                                    {%- unless countdown -%}
                                        {% capture countdown%}${this.countdown}{% endcapture %}
                                    {%- endunless -%}
                                    {%- unless countdown_from -%}
                                        {% capture countdown_from%}${this.countdown_from}{% endcapture %}
                                    {%- endunless -%}
                                `
                                : ''
                             }
                            `
                            : `
                            {%- assign countdown = '${this.countdown}' -%}
                            {%- assign countdown_from = '${this.countdown_from}' -%}
                            `
                        }
                        <div data-product-id="{{product.id}}" class="ecom-product-single__countdown-time"  data-ecom-countdown="{{countdown}}"  data-ecom-countdown-from="{{countdown_from}}" data-countdown-restart="${this.data.settings?.countdown_restart ? this.data.settings?.countdown_restart : 'false'}"></div>
                    {% endif%}
                `,
                preview: `<div data-product-id="0" class="ecom-product-single__countdown-time" data-ecom-countdown="${finalDate}"></div>`
                }
            };
        },
        settings() {
            return [
                {
                    group_title: 'General',
                    params: [
                        {
                            type: 'text',
                            label: 'Title',
                            name: 'title',
                            value: 'Hurry up! Sale ends in',
                            options: {
                                toolbar: true
                            },
                            placeholder: 'Hurry up! Sale ends in'
                        },
                        {
                            type: 'popup',
                            label: 'Type',
                            name: 'type',
                            options: {
                                type: 'dropdown',
                                default: false,
                                values: {
                                    metafields: 'Metafields',
                                    custom: 'Custom',
                                    metafield_or_custom: 'Metafield & custom'
                                },
                                preview: 'title'
                            }
                        },
                        {
                            name: 'styleCountdown',
                            label: 'Layout',
                            type: 'popup',
                            options: {
                                type: 'dropdown',
                                preview: 'title',
                                default: false,
                                values: {
                                    column: 'Vertical',
                                    row: 'Horizontal'
                                }
                            },
                            css: {
                                selector: ' [class*="ecom-product-single__countdown-time--"]',
                                properties: {
                                    'flex-direction': ''
                                }
                            }
                        },

                        {
                            name: 'countdown_from',
                            type: 'datetime',
                            placeholder: 'YYYY/MM/DD hh:mm',
                            label: 'Countdown start from',
                            options: {
                                type: 'datetime',
                                format: 'YYYY/MM/DD hh:mmZ',
                                visible: function (data) {
                                    return data['type'] !== 'metafields'
                                }
                            }
                        },
                        {
                            name: 'countdown',
                            options: {
                                toolbar: false,
                                type: 'datetime',
                                format: 'YYYY/MM/DD hh:mmZ',
                                visible: function (data) {
                                    return (
                                        (data && data['type'] === 'custom') ||
                                        data['type'] === 'metafield_or_custom'
                                    );
                                }
                            },
                            type: 'datetime',
                            placeholder: 'YYYY/MM/DD hh:mmZ',
                            label: 'Countdown end at',
                        },
                        {
                            type: 'toggle',
                            name: 'show_countdown_on_sale',
                            label: 'Only show when on Sale',
                            value: false,
                            options: {
                                values: {
                                    on: {
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                }
                            }
                        },
                        {
                            name: "countdown_restart",
                            label: "Restart on finish",
                            type: "toggle",
                            options: {
                                values: {
                                    on: {
                                        label: "Yes",
                                        value: true
                                    },
                                    off: {
                                        label: "No",
                                        value: false
                                    }
                                }
                            }
                        },
                        {
                            name: 'enable_progress_bar',
                            label: 'Show progress bar',
                            value: true,
                            type: 'toggle',
                            options: {
                                values: {
                                    on: {
                                        label: 'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                }
                            }
                        },
                        ...['week','day','hour','minute','second'].map(value => {
                            return {
                                name: 'show_' + value,
                                label: 'Show ' + value,
                                value: true,
                                type: 'toggle',
                                options: {
                                    values: {
                                        on: {
                                            value: true
                                        },
                                        off: {
                                            value: false
                                        }
                                    }
                                }
                            }
                        })
                    ]
                },
                {
                    group_title: 'Change text',
                    options: {
                        visible: (data) => {
                            return data.show_week ||
                                data.show_day ||
                                data.show_hour ||
                                data.show_minute ||
                                data.show_second
                        }
                    },
                    params: [
                        {
                            type: 'text',
                            name: 'text_week',
                            value: '[%-W] week%!W',
                            description: 'Example: [%-W] week%!W',
                            label: 'Week',
                            options: {
                                visible: (data) =>
                                    data.show_week
                            }
                        },
                        {
                            type: 'text',
                            name: 'text_day',
                            value: '[%d] day%!d',
                            description: 'Example: [%-d] day%!d',
                            label: 'Days',
                            options: {
                                visible: (data) =>
                                    data.show_day
                            }
                        },
                        {
                            type: 'text',
                            name: 'text_hour',
                            value: '[%-H] hour%!H',
                            description: 'Example:  [%-H] hour%!H',
                            label: 'Hours',
                            options: {
                                visible: (data) =>
                                    data.show_hour
                            }
                        },
                        {
                            type: 'text',
                            name: 'text_minute',
                            value: '[%-M] minute%!M',
                            description: 'Example: [%-M] minute%!M',
                            label: 'Minutes',
                            options: {
                                visible: (data) =>
                                    data.show_minute
                            }
                        },
                        {
                            type: 'text',
                            name: 'text_second',
                            value: '[%-S] second%!S',
                            description: 'Example: [%-S] second%!S',
                            label: 'Seconds',
                            options: {
                                visible: (data) =>
                                    data.show_second
                            }
                        }
                    ]
                }
            ];
        },
        title() {
            return this.data && this.data.settings && this.data.settings.title
                ? this.data.settings.title
                : '';
        },
        countdown() {
            return this.data && this.data.settings && 'countdown' in this.data.settings
                ? this.data.settings.countdown
                : '';
        },
        countdown_from() {
            return this.data && this.data.settings && 'countdown_from' in this.data.settings
                ? this.data.settings.countdown_from
                : '';
        },
        show_countdown_on_sale() {
            return this.data && this.data.settings && 'show_countdown_on_sale' in this.data.settings
                ? this.data.settings.show_countdown_on_sale
                : false;
        },
        enable_progress_bar() {
            return this.data && this.data.settings && 'enable_progress_bar' in this.data.settings
                ? this.data.settings.enable_progress_bar
                : false;
        },

        type() {
            return this.data?.settings?.type ?? '';
        },
        shows() {
            let shows = ['week','day','hour','minute','second'];
            return shows.filter(show => this.data?.settings?.['show_' + show]);
        },
        javascript() {
            return function () {
                var $el = this.$el,
                    isLive = this.isLive,
                    $countdown = $el.querySelector('.ecom-product-single__countdown-time'),
                    $progressbar = $el.querySelector('.ecom-product-single__countdown-progress-bar'),
                    $timer = $el.querySelector('.ecom-product-single__countdown-progress-bar--timer'),
                    from = $countdown ? $countdown.getAttribute('data-ecom-countdown-from') : 0,
                    restart = $countdown ? ($countdown.dataset.countdownRestart ? $countdown.dataset.countdownRestart : false) : false,
                    $container = $el.querySelector('.ecom-product-single__countdown-container');
                let trans = $countdown
                    ? $container.dataset
                    : {};
                let shows = $container.dataset.shows.split(',');


                const regex = /\[([^\]]+)\]/gm;
                var format = "";
                if(shows.indexOf('week')  >=0 && trans.week)
                {
                    let number = '';
                    let text = trans.week.replace(regex,(...m)=>{
                            number =  m[1];
                            return '';
                    });
                    format += `<div class="ecom-product-single__countdown-time--week"><span class="ecom-product-single__countdown-number">
                            ${ number }
                        </span>
                        <span class="ecom-product-single__countdown-text">
                            ${ text}
                        </span>
                    </div>`;
                }
                if(shows.indexOf('day')  >=0 && trans.day)
                {
                    let number = '';
                    let text = trans.day.replace(regex,(...m)=>{
                            number =  m[1];
                            return '';
                    });
                    format += `<div class="ecom-product-single__countdown-time--day">
                                    <span class="ecom-product-single__countdown-number">${number}</span>
                                    <span class="ecom-product-single__countdown-text">
                                        ${text}
                                    </span>
                                </div> `;
                }
                if(shows.indexOf('hour')  >=0 && trans.hour)
                {
                    let number = '';
                    let text = trans.hour.replace(regex,(...m)=>{
                            number =  m[1];
                            return '';
                    });
                    format +=`<div class="ecom-product-single__countdown-time--hour">
                                    <span class="ecom-product-single__countdown-number">
                                        ${ number}
                                    </span>
                                    <span class="ecom-product-single__countdown-text">
                                        ${text}
                                    </span>
                            </div>  `
                }
                if(shows.indexOf('minute')  >=0 && trans.minute)
                {
                    let number = '';
                    let text = trans.minute.replace(regex,(...m)=>{
                            number =  m[1];
                            return '';
                    });
                    format +=  `<div class="ecom-product-single__countdown-time--minute">
                                    <span class="ecom-product-single__countdown-number">
                                        ${ number }
                                    </span>
                                    <span class="ecom-product-single__countdown-text">
                                        ${text}
                                    </span>
                                </div>`
                }
                if(shows.indexOf('second')  >=0 && trans.second)
                {
                    let number = '';
                    let text = trans.second.replace(regex,(...m)=>{
                            number =  m[1];
                            return '';
                    });
                    format +=  `<div class="ecom-product-single__countdown-time--second">
                                    <span class="ecom-product-single__countdown-number">
                                        ${ number }
                                    </span>
                                    <span class="ecom-product-single__countdown-text">
                                        ${text}
                                    </span>
                                </div>`
                }

                function countdownCallback(event) {
                    this.innerHTML = event.strftime(format);
                    if ($progressbar && from) {
                        let now_time = new Date().getTime(),
                            start = new Date(from),
                            start_time = start.getTime(),
                            end_time = event.finalDate.getTime();
                        if (start_time < now_time && end_time > start_time) {
                            $progressbar.style.display = 'inherit';
                            let total = end_time - start_time;
                            let elapse = end_time - now_time;
                            let result = Math.round((elapse * 100) / total);
                            $timer.style.width = result + '%' ;
                        } else {
                            $progressbar.style.display = 'none';
                        }
                    }
                    if (event.type === 'finish' && restart != 'true') {
                        onFinish();
                    }
                }
                function onFinish() {
                    if ($countdown && isLive) {
                        $countdown.closest('.ecom-block.ecom-core').style.display = 'none';
                    }
                }
                if ($countdown) {
                    if ($countdown.getAttribute('data-ecom-countdown')) {
                        let from = $countdown.getAttribute('data-ecom-countdown-from');
                        if (from && new Date().getTime() < new Date(parseInt(from)).getTime()) {
                            // console.info('Countdown will run on: ',new Date(parseInt(from)));
                        } else {
                            let date = new Date($countdown.getAttribute('data-ecom-countdown'));
                            if(window.EComCountdown )
                            {
                                let date_diff =  new Date($countdown.dataset.ecomCountdown).getTime() - new Date($countdown.dataset.ecomCountdownFrom).getTime(),
                                    new_date = new Date($countdown.dataset.ecomCountdown).getTime() + date_diff;
                                if(date_diff == 0 ) return;
                                
                                window.EComCountdown($countdown,date, {});
                                $countdown.addEventListener('update.ecom.countdown', countdownCallback);
                                $countdown.addEventListener('finish.ecom.countdown', function(event) {
                                    if(restart == 'true') {
                                        while(new_date < new Date().getTime()){
                                            new_date += date_diff;
                                        }
                                        window.EComCountdown($countdown,new Date(new_date), countdownCallback);
                                    }
                                });
                            }
                        }
                    }
                }
                let $wrapper = this.$el.closest('.ecom-product-form--single');
                if ($wrapper && !isLive) {
                    let selector = $wrapper.querySelector('[name="id"]');
                    if (selector) {
                        selector.dispatchEvent(new window.Event('ecomUpdate'));
                    }
                }
            };
        },
        css() {
            return `
                    .ecom-product-single__countdown-container{
                        display:flex;
                    }
                    .ecom-product-single__countdown-progress-bar{
                        display:block;
                        width:100%;
                    }
                    .ecom-product-single__countdown-progress-bar .ecom-product-single__countdown-progress-bar--timer{
                        width:100%;
                        display:block;
                    }
                    .ecom-product-single__countdown-time{
                        display: inline-flex;
                        flex-wrap: wrap;
                        justify-content:inherit;
                    }
                    .ecom-product-single__countdown-time > div{
                        display:inline-flex;
                        gap:6px;
                        padding:10px;
                        border:1px solid #eee;
                        justify-content: center;
                        align-items: center;
                    }
                    .ecom-product-single__countdown-block .ecom-product-single__countdown-time > div span{
                        width:100%;
                    }

                    .ecom-product-single__countdown-progress-bar--timer {
                            background-image: -webkit-linear-gradient(45deg,rgba(255,255,255,.15) 25%,transparent 25%,transparent 50%,rgba(255,255,255,.15) 50%,rgba(255,255,255,.15) 75%,transparent 75%,transparent);
                            background-image: linear-gradient(45deg,rgba(255,255,255,.15) 25%,rgba(0,0,0,0) 25%,rgba(0,0,0,0) 50%,rgba(255,255,255,.15) 50%,rgba(255,255,255,.15) 75%,rgba(0,0,0,0) 75%,rgba(0,0,0,0));
                            -webkit-animation: 2s linear infinite ecom_progress_bar;
                            animation: 2s linear infinite ecom_progress_bar;
                            background-size: 60px 60px;
                            transition: width 1s;
                            -webkit-transition: width 1s;
                    }

                    @-webkit-keyframes ecom_progress_bar {
                        from {
                            background-position: 0 0
                        }

                        to {
                            background-position: 40px 0
                        }
                    }

                    @keyframes ecom_progress_bar {
                        from {
                            background-position: 0 0
                        }

                        to {
                            background-position: 40px 0
                        }
                    }

                `;
        },

        requestShopifyType() {
            return {
                shopify_type: 'product'
            };
        },

        default() {
            let date = new Date();
            date.setDate(date.getDate()+ 6)
            let cur_month = date.getMonth();

            let cur_year = date.getFullYear();

            let current_date = date.getDate();

            return {
                "settings": {
                    "title": "Hurry up! Sale ends in",
                    "show_countdown_on_sale": false,
                    "enable_progress_bar": true,
                    "show_week": true,
                    "show_day": true,
                    "show_hour": true,
                    "show_minute": true,
                    "show_second": true,
                    "text_week": "[%-W] week%!W",
                    "text_day": "[%d] day%!d",
                    "text_hour": "[%-H] hour%!H",
                    "text_minute": "[%-M] minute%!M",
                    "text_second": "[%-S] second%!S",
                    "type": "custom",
                    countdown_from: `${cur_year}/${cur_month}/01 12:30+0700`,
                    countdown: `${cur_year}/${cur_month+1}/${(current_date)} 12:30+0700`,
                    "styleCountdown": "column"
                },
                "style":   {
                "progess_bar": {
                    "background": {
                        "classic": {
                            "background-color": "rgba(17, 24, 39, 0.1)"
                        }
                    },
                    "backgroundActive": {
                        "classic": {
                            "background-color": "rgba(166, 0, 11, 0.97)"
                        }
                    },
                    "tab": "active",
                    "maxWidth": "100%",
                    "height": "20px",
                    "borderRadius": {
                        "top": "5px",
                        "left": "5px",
                        "bottom": "5px",
                        "right": "5px"
                    },
                    "spacing": {
                        "margin": {
                            "right": "2px",
                            "top": "2px",
                            "left": "2px",
                            "bottom": "2px"
                        },
                        "padding": {}
                    },
                    "borderRadiusActive": {
                        "right": "0px",
                        "top": "5px",
                        "left": "5px",
                        "bottom": "0px"
                    }
                },
                "title": {
                    "textTextAlign": "center",
                    "spacing": {
                        "margin": {
                            "bottom": "8px"
                        }
                    }
                },
                "countdown_number": {
                    "buttonColor": "#dc2626",
                    "text-align": "center"
                },
                "items": {
                    "spacing": {
                        "margin": {
                            "right": "8px",
                            "bottom": "8px"
                        }
                    },
                    "width": "95px",
                    "justify-content": "center"
                },
                "countdown_label": {
                    "text-align": "center"
                }
            }
            }



        }
    },
    watch: {
        shows: {
            deep: true,
            handler(){
                this._data.refresh = this.$helpers.randid();
            }
        }
    },
    methods: {
        style() {
            let style = [
                {
                    group_alias: 'text:spacing',
                    options: {
                        group_name: 'title',
                        group_title: 'Title',
                        selector: ' .ecom-product-single__countdown-title'
                    }
                },
                {
                    group_alias: 'box',
                    options: {
                        group_title: 'Items',
                        group_name: 'items',
                        selector: ' [class*="ecom-product-single__countdown-time--"]'
                    },
                    modify: {
                        params: [
                            {
                                position:0,
                                fields:[
                                    {
                                        type:'number',
                                        label:'Width',
                                        name:'width',
                                        options:{
                                            units:{
                                                px:{
                                                    min:20,
                                                    max:200
                                                }
                                            },
                                            reset:false,
                                            responsive:true
                                        },
                                        css:{
                                            properties:{
                                                width:''
                                            }
                                        }
                                    },
                                    {
                                        type:'number',
                                        label:'Height',
                                        name:'height',
                                        options:{
                                            units:{
                                                px:{
                                                    min:20,
                                                    max:200
                                                }
                                            },
                                            reset:false,
                                            responsive:true
                                        },
                                        css:{
                                            properties:{
                                                height:''
                                            }
                                        }
                                    }
                                ]
                            },
                            {
                                position:2,
                                fields:{
                                    alias:'justify-content',
                                    options:{
                                        label:'Alignment',
                                        css:{
                                            selector:'root .ecom-product-single__countdown-container'
                                        }
                                    }
                                }
                            },
                            {
                                position: 10,
                                fields: {
                                    alias: 'spacing'
                                }
                            }
                        ]
                    }
                },
                {
                    group_alias: 'button:label',
                    options: {
                        group_name: 'countdown_number',
                        group_title: 'Number',
                        selector: ' .ecom-product-single__countdown-number'
                    },
                    modify:this.data.settings.styleCountdown == 'column' ? {
                        params:{
                            alias:'text-align',
                            options:{
                                label:'Alignment'
                            }
                        }
                    } : {}
                },
                {
                    group_alias: 'button:label',
                    options: {
                        group_name: 'countdown_label',
                        group_title: 'Label',
                        selector: ' .ecom-product-single__countdown-text'
                    },
                    modify:this.data.settings.styleCountdown == 'column' ? {
                        params:{
                            alias:'text-align',
                            options:{
                                label:'Alignment'
                            }
                        }
                    } : {}
                }
            ];
            if (this.data.settings.enable_progress_bar) {
                style.push({
                    group_name: 'progess_bar',
                    group_title: 'Progress bar',
                    selector: ' .ecom-product-single__countdown-progress-bar',
                    params: [
                        {
                            type: 'number',
                            name: 'maxWidth',
                            label: 'Width',
                            options: {
                                responsive: true,
                                reset: true,
                                units: {
                                    '%': {
                                        min: 0,
                                        max: 100
                                    },
                                    px: {
                                        min: 0,
                                        max: 1000
                                    },
                                    vw: {
                                        min: 0,
                                        max: 100
                                    }
                                }
                            },
                            css:{
                                properties:{
                                    'max-width':''
                                }
                            }
                        },
                        {
                            type: 'number',
                            name: 'height',
                            label: 'Height',
                            options: {
                                responsive: true,
                                reset: true,
                                units: {
                                    px: {
                                        min: 0,
                                        max: 1000
                                    },
                                    vh: {
                                        min: 0,
                                        max: 100
                                    }
                                }
                            },
                            css:{
                                selector: ' .ecom-product-single__countdown-progress-bar--timer',
                                properties:{
                                    'height':'',
                                    '--ecom-countdown-max-height':''
                                }
                            }
                        },
                        {
                            name: 'tab',
                            type: 'tab',
                            value: 'normal',
                            options: {
                                tabs: [{
                                        name: 'normal',
                                        title: 'Normal'
                                    },
                                    {
                                        name: 'active',
                                        title: 'Active'
                                    }
                                ]
                            },
                            css: {
                                isCss: false,
                            }
                        },
                        {
                            type: 'background',
                            label: 'Background',
                            name: 'background',
                            options: {
                                oneline: true,
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data['tab'] === 'normal';
                                    }
                                }
                            },
                            css:{
                                properties:{
                                    'background':''
                                }
                            }
                        },
                        {
                            type: 'dimension',
                            name: 'borderRadius',
                            label: 'Border radius',
                            options: {
                                type: 'radius',
                                units: 'default',
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data['tab'] === 'normal';
                                    }
                                }
                            },
                            css:{
                                properties:{
                                    'border-radius':''
                                }
                            }
                        },
                        {
                            type: 'background',
                            label: 'Background',
                            name: 'backgroundActive',
                            options: {
                                oneline: true,
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data['tab'] === 'active';
                                    }
                                }
                            },
                            css:{
                                selector: ' .ecom-product-single__countdown-progress-bar--timer',
                                properties:{
                                    'background':''
                                }
                            }
                        },

                        {
                            type: 'dimension',
                            name: 'borderRadiusActive',
                            label: 'Border radius',
                            options: {
                                type: 'radius',
                                units: 'default',
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data['tab'] === 'active';
                                    }
                                }
                            },
                            css:{
                                selector: ' .ecom-product-single__countdown-progress-bar--timer',
                                properties:{
                                    'border-radius':''
                                }
                            }
                        },
                        {
                            alias:'spacing'
                        }
                    ]
                });
            }
            return style;
        }
    }
};
</script>
