<template>
    <div class="ecom-element ecom-shopify-elements ecom-shopify__article-title">
        <div :class="classes" class="ecom-shopify__article--title-wrapper">
            <div
                :class="classes"
                class="ecom-shopify__article--title-container"
                v-html="liquid('articleTitle')"
            />
        </div>
    </div>
</template>

<script>
import { Liquid, Element } from '@/mixins';

export default {
    name: 'ArticleTitle',
    mixins: [Liquid, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        classes() {
            return this.data && this.data.settings && this.data.settings.classes
                ? this.data.settings.classes
                : '';
        },

        tag() {
            return this.data && this.data.settings && this.data.settings.tag
                ? this.data.settings.tag
                : 'h3';
        },

        liquids() {
            return {
                articleTitle: {
                    code: `
                        {% if article != empty %}
                            <${this.tag} itemprop="headline" class="ecom-shopify__article--title-text" href="{{article.url}}" title="{{article.title | escape }}">{{article.title  | escape }}</${this.tag}>
                        {% else %}
                            <div>Please select the article in settings</div>
                        {% endif %}
                    `,
                    preview: `
                        <div class="ecom-skeleton-item">
                            <div class="ecom-skeleton-col-12">
                                <div class="ecom-skeleton-row">
                                    <div class="ecom-skeleton-col-9 ecom-skeleton-big"></div>
                                </div>
                            </div>
                        </div>
                    `
                }
            };
        },

        requestShopifyType() {
            return {
                shopify_type: 'article'
            };
        },

        settings() {
            return [
                {
                    group_alias:'title:settings',
                    options:{
                        group_name:'',
                        group_title:''
                    }
                }
            ];
        },

        style() {
            return [
                {
                    group_alias:'text:spacing',
                    options:{
                        group_title: '',
                        selector: ' .ecom-shopify__article--title-text',
                    }
                }
            ];
        },

        default() {
            return{
                settings: {
                    tag: "h2"
                },
                style: {
                    textSpacing: {
                        textTextAlign: "left"
                    }
                }
            }
        }
    }
};
</script>
