<template>
  <div class="ecom-element ecom-shopify-elements ecom-shopify__article-prev_next">
      <div class="ecom-shopify__article--prev_next-wrapper">
          <div
            class="ecom-shopify__article--prev_next-container"
            :class="{'ecom-flex ecom-fl_between': data.settings?.type == 'all'}"
          >
            <div
              v-if="data.settings?.type == 'prev' || data.settings?.type == 'all'"
              class="ecom-shopify__article--prev"
              v-html="liquid('prevArticle')"
            />
            <div
              v-if="data.settings?.type == 'next' || data.settings?.type == 'all'"
              class="ecom-shopify__article--next"
              v-html="liquid('nextArticle')"
            />
          </div>
      </div>
  </div>
</template>

<script>
import { Liquid, Element } from '@/mixins';

export default {
  name: 'ArticlePrevNext',
  mixins: [Liquid, Element],
  props: {
      data: {
          type: Object,
          default() {
              return {};
          }
      }
  },
  computed: {
      liquids() {
          return {
            prevArticle: {
                  code: `
                    {%- liquid
                        assign prev_article = blog.previous_article
                      if EComBuilderMode
                        assign prev_article = blog.articles.first
                      endif
                    -%}
                    {%- if prev_article -%}
                      <div class="ecom-shopify__article--prev-content ecom-flex ecom-column">
                        ${this.data.settings?.prev_title
                          ?
                            `
                              <div class="ecom-shopify__article--item-heading ecom-shopify__article--item-heading-prev ecom-flex ecom-al_center">
                                ${this.data.settings?.icon_prev
                                    ? `
                                        <span class="ecom-shopify__article--item-icon ecom-flex ecom-al_center">${this.data.settings?.icon_prev}</span>
                                    `
                                    : ''
                                }
                                <a href="{{prev_article.url}}" alt="{{prev_article.title}}">${this.lang(this.data.settings.prev_title, 'prev_title')}</a>
                              </div>
                            `
                          : ``
                        }
                        <div class="ecom-shopify__article--item-body ecom-flex">
                          ${this.data.settings?.show_featured_image
                            ?
                              `
                                {%- if prev_article.image -%}
                                  <div class="ecom-shopify__article--item-image ecom-col-auto">
                                    <div class="ecom-image-default">
                                      <img src="{{ prev_article.image | img_url: '360x'}}" alt="{{ prev_article.image.src.alt | escape }}" />
                                    </div>
                                  </div>
                                {%- endif -%}
                              `
                            : ``
                          }
                          ${this.data.settings?.show_title || this.data.settings?.show_time
                            ?
                              `
                                <div class="ecom-shopify__article--item-content ecom-flex ecom-column ecom-col">
                                  ${this.data.settings?.show_title
                                    ?
                                      `
                                      <div class="ecom-shopify__article--item-title ecom-shopify__article--item-title-prev">
                                        <a href="{{prev_article.url}}" alt="{{prev_article.title}}">{{ prev_article.title }}</a>
                                      </div>
                                      `
                                    : ``
                                  }
                                  ${this.data.settings?.show_time
                                    ?
                                      `
                                      <div class="ecom-shopify__article--item-time ecom-flex ecom-al_center">
                                          ${this.data.settings?.icon_time
                                              ? `
                                                  <span class="ecom-shopify__article--item-icon ecom-flex ecom-al_center">${this.data.settings?.icon_time}</span>
                                              `
                                              : ''
                                          }
                                          <span>{{ prev_article.published_at | time_tag: format: '${this.data.settings?.date_format || 'basic'}' }}</span>
                                      </div>
                                      `
                                    : ``
                                  }
                                </div>

                              `
                            : ``
                          }
                        </div>
                      </div>
                    {%- endif -%}
                  `,
                  preview: `
                    <div class="ecom-skeleton-item">
                          <div>
                              <div class="ecom-skeleton-row">
                                  <div class="ecom-skeleton-col-4"></div>
                                  <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                  <div class="ecom-skeleton-col-6"></div>
                                  <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                  <div class="ecom-skeleton-col-2"></div>
                                  <div class="ecom-skeleton-col-10 ecom-skeleton-empty"></div>
                              </div>
                          </div>
                      </div>
                  `
              },
              nextArticle: {
                  code: `
                    {%- liquid
                        assign next_article = blog.next_article
                      if EComBuilderMode
                        assign next_article = blog.articles.last
                      endif
                    -%}
                    {%- if next_article -%}
                      <div class="ecom-shopify__article--next-content ecom-flex ecom-column">
                        ${this.data.settings?.next_title
                          ?
                            `
                              <div class="ecom-shopify__article--item-heading ecom-shopify__article--item-heading-next ecom-flex ecom-al_center ecom-fl_right">
                                ${this.data.settings?.icon_next
                                    ? `
                                        <span class="ecom-shopify__article--item-icon ecom-flex ecom-al_center">${this.data.settings?.icon_next}</span>
                                    `
                                    : ''
                                }
                                <a href="{{next_article.url}}" alt="{{next_article.title}}">${this.lang(this.data.settings.next_title, 'next_title')}</a>
                              </div>
                            `
                          : ``
                        }
                        <div class="ecom-shopify__article--item-body ecom-flex">
                          ${this.data.settings?.show_featured_image
                            ?
                              `
                                {%- if next_article.image -%}
                                  <div class="ecom-image-default">
                                    <img src="{{ next_article.image | img_url: '360x'}}" alt="{{ next_article.image.src.alt | escape }}" />
                                  </div>
                                {%- endif -%}
                              `
                            : ``
                          }
                          ${this.data.settings?.show_title || this.data.settings?.show_time
                            ?
                              `
                                <div class="ecom-shopify__article--item-content ecom-flex ecom-column ecom-col">
                                  ${this.data.settings?.show_title
                                    ?
                                      `
                                      <div class="ecom-shopify__article--item-title ecom-shopify__article--item-title-next">
                                        <a href="{{next_article.url}}" alt="{{next_article.title}}">{{ next_article.title }}</a>
                                      </div>
                                      `
                                    : ``
                                  }
                                  ${this.data.settings?.show_time
                                    ?
                                      `
                                      <div class="ecom-shopify__article--item-time ecom-flex ecom-al_center ecom-fl_right">
                                          ${this.data.settings?.icon_time
                                              ? `
                                                  <span class="ecom-shopify__article--item-icon ecom-flex ecom-al_center">${this.data.settings?.icon_time}</span>
                                              `
                                              : ''
                                          }
                                          <span>{{ next_article.published_at | time_tag: format: '${this.data.settings?.date_format || 'basic'}' }}</span>
                                      </div>
                                      `
                                    : ``
                                  }
                                </div>

                              `
                            : ``
                          }
                        </div>
                      </div>
                    {%- endif -%}
                  `,
                  preview: `
                    <div class="ecom-skeleton-item">
                          <div>
                              <div class="ecom-skeleton-row">
                                  <div class="ecom-skeleton-col-4"></div>
                                  <div class="ecom-skeleton-col-8 ecom-skeleton-empty"></div>
                                  <div class="ecom-skeleton-col-6"></div>
                                  <div class="ecom-skeleton-col-6 ecom-skeleton-empty"></div>
                                  <div class="ecom-skeleton-col-2"></div>
                                  <div class="ecom-skeleton-col-10 ecom-skeleton-empty"></div>
                              </div>
                          </div>
                      </div>
                  `
              }
          };
      },

      requestShopifyType() {
          return {
              shopify_type: 'article'
          };
      },

      settings() {
          return [
              {
                group_title: 'General',
                params: [
                  {
                    type: 'paragraph',
                    options: {
                        warnings: {
                            content:
                                'Note: Posts in Editor are for preview only !!!'
                        }
                    }
                  },
                  {
                    type: 'line'
                  },
                  {
                      type: 'popup',
                      label: 'Type',
                      name: 'type',
                      options: {
                          type: 'dropdown',
                          default: false,
                          values: {
                              all: 'Prev/Next article',
                              prev: 'Prev article',
                              next: 'Next article'
                          },
                          default: false
                      }
                  },
                  {
                      type: 'text',
                      label: 'Prev Heading',
                      name: 'prev_title',
                      value: 'Previous Post'
                  },
                  {
                      type: 'picker',
                      label: 'Icon',
                      name: 'icon_prev',
                      options: {
                          type: 'icon',
                          layout: 'grid',
                          output: 'value',
                          multiple: false,
                          simple: false,
                          visible: {
                              keep_data: false,
                          }
                      }
                  },
                  {
                      type: 'choose',
                      label: 'Icon position',
                      name: 'icon_prev_position',
                      options: {
                          type: 'align-x',
                          values: [-1, 1],
                          visible: {
                              keep_data: false,
                              condition: (data) => {
                                  return data['icon_prev'];
                              }
                          }
                      },
                      css:{
                          selector:' .ecom-shopify__article--item-heading-prev .ecom-shopify__article--item-icon',
                          properties:{
                              order:''
                          }
                      }
                  },
                  {
                      type: 'number',
                      label: 'Icon spacing',
                      name: 'icon_prev_spacing',
                      options: {
                          units: {
                              px: {
                                  min: 0,
                                  max: 200
                              }
                          },
                          visible: {
                              keep_data: false,
                              condition: (data) => {
                                  return data['icon_prev'];
                              }
                          }
                      },
                      css:{
                          selector:' .ecom-shopify__article--item-heading-prev',
                          properties:{
                              gap:''
                          }
                      }
                  },
                  {
                      type: 'text',
                      label: 'Next Heading',
                      name: 'next_title',
                      value: 'Next Post'
                  },
                  {
                      type: 'picker',
                      label: 'Icon',
                      name: 'icon_next',
                      options: {
                          type: 'icon',
                          layout: 'grid',
                          output: 'value',
                          multiple: false,
                          simple: false,
                          visible: {
                              keep_data: false,
                          }
                      }
                  },
                  {
                      type: 'choose',
                      label: 'Icon position',
                      name: 'icon_next_position',
                      options: {
                          type: 'align-x',
                          values: [-1, 1],
                          visible: {
                              keep_data: false,
                              condition: (data) => {
                                  return data['icon_next'];
                              }
                          }
                      },
                      css:{
                          selector:' .ecom-shopify__article--item-heading-next .ecom-shopify__article--item-icon',
                          properties:{
                              order:''
                          }
                      }
                  },
                  {
                      type: 'number',
                      label: 'Icon spacing',
                      name: 'icon_next_spacing',
                      options: {
                          units: {
                              px: {
                                  min: 0,
                                  max: 200
                              }
                          },
                          visible: {
                              keep_data: false,
                              condition: (data) => {
                                  return data['icon_next'];
                              }
                          }
                      },
                      css:{
                          selector:' .ecom-shopify__article--item-heading-next',
                          properties:{
                              gap:''
                          }
                      }
                  },
                  {
                    type: 'toggle',
                    label: 'Show article featured image',
                    name: 'show_featured_image',
                    value: true,
                    options: {
                        values:{
                            on:{
                                label: 'Yes',
                                value: true
                            },
                            off: {
                                label: 'No',
                                value: false
                            }
                        }
                    }
                  },
                  {
                    type: 'toggle',
                    label: 'Show article title',
                    name: 'show_title',
                    value: true,
                    options: {
                        values:{
                            on:{
                                label: 'Yes',
                                value: true
                            },
                            off: {
                                label: 'No',
                                value: false
                            }
                        }
                    }
                  },
                  {
                    type: 'toggle',
                    label: 'Show article date',
                    name: 'show_time',
                    value: true,
                    options: {
                        values:{
                            on:{
                                label: 'Yes',
                                value: true
                            },
                            off: {
                                label: 'No',
                                value: false
                            }
                        }
                    }
                  },
                  {
                      type: 'popup',
                      name: 'date_format',
                      label: 'Date format?',
                      options: {
                          type: 'dropdown',
                          default: false,
                          values: {
                              default:
                                  'Monday, December 31, 2018 at 1:00 pm -0500',
                              abbreviated_date: 'Aug 08, 1994',
                              basic: '12/31/2021',
                              date: 'December 31, 2018',
                              date_at_time: 'December 31, 2018 at 1:00 pm',
                              on_date: 'on Dec 31, 2018'
                          }
                      }
                  },
                  {
                      type: 'picker',
                      label: 'Icon Time',
                      name: 'icon_time',
                      options: {
                          type: 'icon',
                          layout: 'grid',
                          output: 'value',
                          multiple: false,
                          simple: false,
                          visible: {
                              keep_data: false,
                              condition: (data) => {
                                  return data['show_time'];
                              }
                          }
                      }
                  },
                  {
                      type: 'choose',
                      label: 'Icon position',
                      name: 'icon_position',
                      options: {
                          type: 'align-x',
                          values: [-1, 1],
                          visible: {
                              keep_data: false,
                              condition: (data) => {
                                  return data['icon_time'];
                              }
                          }
                      },
                      css:{
                          selector:' .ecom-shopify__article--item-time .ecom-shopify__article--item-icon',
                          properties:{
                              order:''
                          }
                      }
                  },
                  {
                      type: 'number',
                      label: 'Icon spacing',
                      name: 'spacing',
                      options: {
                          units: {
                              px: {
                                  min: 0,
                                  max: 200
                              }
                          },
                          visible: {
                              keep_data: false,
                              condition: (data) => {
                                  return data['icon_time'];
                              }
                          }
                      },
                      css:{
                          selector:' .ecom-shopify__article--item-time',
                          properties:{
                              gap:''
                          }
                      }
                  },
                ]
              },
          ];
      },

      style() {
          return [
              {
                  group_alias: 'box',
                  options: {
                      group_name: 'general',
                      group_title: 'General'
                  },
              },
              {
                  group_alias: 'box',
                  options: {
                      group_name: 'prev_article',
                      group_title: 'Prev Article',
                      selector: ' .ecom-shopify__article--prev'
                  },
                  modify: {
                    params: {
                      position: 0,
                      fields: {
                          name: 'boxWidth',
                          label: 'Width',
                          type: 'number',
                          options: {
                              responsive: true,
                              units: {
                                  px: {
                                      min: 0,
                                      max: 1000
                                  },
                                  '%': {
                                      min: 0,
                                      max: 100
                                  }
                              }
                          },
                          css:{
                              properties:{
                                  'width': ''
                              }
                          }
                      },
                    }
                  }
              },
              {
                  group_alias: 'box',
                  options: {
                      group_name: 'next_article',
                      group_title: 'Next Article',
                      selector: ' .ecom-shopify__article--next'
                  },
                  modify: {
                    params: {
                      position: 0,
                      fields: {
                          name: 'boxWidth',
                          label: 'Width',
                          type: 'number',
                          options: {
                              responsive: true,
                              units: {
                                  px: {
                                      min: 0,
                                      max: 1000
                                  },
                                  '%': {
                                      min: 0,
                                      max: 100
                                  }
                              }
                          },
                          css:{
                              properties:{
                                  'width': ''
                              }
                          }
                      },
                    }
                  }
              },
              {
                group_alias:'text:spacing',
                options: {
                    group_title: 'Heading',
                    group_name: 'heading',
                    selector: ' .ecom-shopify__article--item-heading a'
                },
                modify: {
                  remove: {
                    index: 0, length: 1
                  }
                }
              },
              {
                group_alias:'text',
                options: {
                    group_title: 'Article Title',
                    group_name: 'title',
                    selector: ' .ecom-shopify__article--item-title a'
                },
                modify: {
                  remove: {
                    index: 0, length: 1
                  }
                }
              },
              {
                group_alias:'text',
                options: {
                    group_title: 'Article Date',
                    group_name: 'date',
                    selector: ' .ecom-shopify__article--item-time time'
                },
                modify: {
                  remove: {
                    index: 0, length: 1
                  }
                }
              },

            {
                group_alias: 'image',
                options: {
                    group_title: 'Image',
                    group_name: 'Image',
                    selector: ' .ecom-shopify__article--item-image'
                }
            },
            {
                group_alias:'icon',
                options:{
                    group_name: 'icon_heading',
                    group_title: 'Icon Heading',
                    selector: ' .ecom-shopify__article--item-heading .ecom-shopify__article--item-icon',
                },
                modify:{
                    params:[
                        {
                            position:15,
                            fields:[
                                {
                                    type: 'line'
                                },
                                {
                                    alias:'spacing'
                                }
                            ]
                        }
                    ]
                }
            },
            {
                group_alias:'icon',
                options:{
                    group_name: 'icon_time',
                    group_title: 'Icon Time',
                    selector: ' .ecom-shopify__article--item-time .ecom-shopify__article--item-icon',
                },
                modify:{
                    params:[
                        {
                            position:15,
                            fields:[
                                {
                                    type: 'line'
                                },
                                {
                                    alias:'spacing'
                                }
                            ]
                        }
                    ]
                }
            }
          ];
      },
      css() {
        return `
          .ecom-shopify__article--item-icon svg {
            width: 16px;
            height: 16px;
          }
          .ecom-shopify__article--next .ecom-shopify__article--item-body {
            flex-direction: row-reverse;
          }
          .ecom-shopify__article--item-title-next {
            text-align: right;
          }
          .ecom-shopify__article--prev_next-container {
            flex-wrap: wrap;
          }
        `
      },
      default() {
          return {
            "settings": {
              "prev_title": "Previous Post",
              "next_title": "Next Post",
              "show_featured_image": true,
              "show_title": true,
              "show_time": true,
              "type": "all"
            },
            "style": {
              "Image": {
                "tab": "normal",
                "imageWidth": "100px"
              },
              "icon_heading": {
                "iconFontSize": "16px"
              },
              "icon_time": {
                "iconFontSize": "16px"
              }
            }
          }
      }
  }
};
</script>
