<template>
    <div ref="wrapper" class="ecom__element ecom-element element__map">
        <div class="ecom__element-map">
            <iframe
                frameborder="0"
                scrolling="no"
                marginheight="0"
                marginwidth="0"
                :src="
                    'https://maps.google.com/maps?q=' +
                    location +
                    '&amp;t=m&amp;z=' +
                    map?.zoom +
                    '&amp;output=embed&amp;iwloc=near'
                "
                title="%3$s"
                aria-label="%3$s"
            />
        </div>
    </div>
</template>

<script>
import { Element, Javascript, Liquid } from '@/mixins';
export default {
    name: 'Map',
    docs: 'https://help.ecomposer.io/docs/elements/basic-elements/google-map/',
    mixins: [Element, Javascript, Liquid],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    data() {
        return {};
    },
    computed: {
        settings() {
            return [
                {
                    params: [
                        {
                            type: 'textarea',
                            label: 'Location',
                            name: 'location'
                        },
                        {
                            name: 'zoom',
                            label: 'Zoom',
                            type: 'number',
                            options: {
                                min: 1,
                                max: 20,
                                step: 1
                            }
                        },
                         {
                            name: 'gmap_height',
                            label: 'Height',
                            type: 'number',
                            options: {
                                units:{
                                    
                                    'px':{
                                        min:0,
                                        max: 2000
                                    },
                                    vh:{
                                        min:0,
                                        max: 100
                                    }
                                },
                                responsive: true
                            },
                            css: {
                                selector: ' .ecom__element-map iframe',
                                properties: {
                                    'height': ''
                                }
                            }
                        },
                        {
                            name: 'generalHeight',
                            label: 'Map Aspect Ratio',
                            type: 'popup',
                            options: {
                                type: 'dropdown',
                                values: {
                                    '16/9': '16:9 Optimize for desktop',
                                    '4/3': '4:3 Optimize for mobile',
                                    '1/1': '1:1',
                                    '3/2': '3:2 Optiomize for tablet'
                                },
                                responsive: true
                            },
                            css: {
                                selector: ' .ecom__element-map iframe',
                                properties: {
                                    'aspect-ratio': ''
                                }
                            }
                        },
                       
                    ]
                }
            ];
        },
        style() {
            return [
                {
                    group_name: 'gmap_general',
                    selector: ' .ecom__element-map iframe',
                    params: [
                        {
                            type: 'tab',
                            name: 'tab',
                            options: {
                                tabs: [
                                    {
                                        name: 'normal',
                                        title: 'Normal'
                                    },
                                    {
                                        name: 'hover',
                                        title: 'Hover'
                                    }
                                ]
                            },
                            css: {
                                isCss: false
                            }
                        },
                        {
                            name: 'gmapFilter',
                            label: 'CSS Filters',
                            type: 'popup',
                            options: {
                                oneline: true,
                                type: 'filter',
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data['tab'] == 'normal';
                                    }
                                }
                            },
                            css: {}
                        },
                        {
                            name: 'gmapFilterHoverMode',
                            label: 'CSS Filters',
                            type: 'popup',
                            options: {
                                oneline: true,
                                type: 'filter',
                                visible: {
                                    keep_data: true,
                                    condition: (data) => {
                                        return data['tab'] == 'hover';
                                    }
                                }
                            },
                            css: {
                                selector: ':hover'
                            }
                        }
                    ]
                }
            ];
        },
        map() {
            return this.data.settings;
        },
        location() {
            let location =this.map?.location ? this.removeVietnameseTones(this.map.location).toLowerCase() : '';
            return location.replace(/\s/g, '');
        },
        css() {
            return `
                .ecom__element-map{
                    display:flex;
                }
                .ecom__element-map iframe{
                    width:100%;
                }
                `;
        },
        default(){
            return {
                settings: {
                    location: '150 Elgin Street 8th Floor Ottawa, ON K2P 1L4 Canada',
                    zoom: 12,
                    generalHeight: '16/9',
                }
            }
        }
    },
    methods: {
        removeVietnameseTones(str) {
            if (str) {
                str = str.replace(/à|á|ạ|ả|ã|â|ầ|ấ|ậ|ẩ|ẫ|ă|ằ|ắ|ặ|ẳ|ẵ/g, 'a');
                str = str.replace(/è|é|ẹ|ẻ|ẽ|ê|ề|ế|ệ|ể|ễ/g, 'e');
                str = str.replace(/ì|í|ị|ỉ|ĩ/g, 'i');
                str = str.replace(/ò|ó|ọ|ỏ|õ|ô|ồ|ố|ộ|ổ|ỗ|ơ|ờ|ớ|ợ|ở|ỡ/g, 'o');
                str = str.replace(/ù|ú|ụ|ủ|ũ|ư|ừ|ứ|ự|ử|ữ/g, 'u');
                str = str.replace(/ỳ|ý|ỵ|ỷ|ỹ/g, 'y');
                str = str.replace(/đ/g, 'd');
                str = str.replace(/À|Á|Ạ|Ả|Ã|Â|Ầ|Ấ|Ậ|Ẩ|Ẫ|Ă|Ằ|Ắ|Ặ|Ẳ|Ẵ/g, 'A');
                str = str.replace(/È|É|Ẹ|Ẻ|Ẽ|Ê|Ề|Ế|Ệ|Ể|Ễ/g, 'E');
                str = str.replace(/Ì|Í|Ị|Ỉ|Ĩ/g, 'I');
                str = str.replace(/Ò|Ó|Ọ|Ỏ|Õ|Ô|Ồ|Ố|Ộ|Ổ|Ỗ|Ơ|Ờ|Ớ|Ợ|Ở|Ỡ/g, 'O');
                str = str.replace(/Ù|Ú|Ụ|Ủ|Ũ|Ư|Ừ|Ứ|Ự|Ử|Ữ/g, 'U');
                str = str.replace(/Ỳ|Ý|Ỵ|Ỷ|Ỹ/g, 'Y');
                str = str.replace(/Đ/g, 'D');
                str = str.replace(/\u0300|\u0301|\u0303|\u0309|\u0323/g, '');
                str = str.replace(/\u02C6|\u0306|\u031B/g, '');
                str = str.replace(/ + /g, ' ');
                str = str.trim();
                str = str.replace(
                    /!|@|%|\^|\*|\(|\)|\+|=|\\<|\\>|\?|\/|,|\.|\\:|\\;|\\'|\\"|\\&|\\#|\[|\]|~|\$|_|`|-|{|}|\||\\/g,
                    ' '
                );
            }
            return str;
        }
    }
};
</script>
