<template>

    <div class="ecom-elements ecom-base-element ecom-elements-slider">
        <GroupElement v-if="true" :data="data" :deep="deep" :index="index" :section_id="section_id">
            <template #items="payload">
                <div class="ecom-slider--container">
                    <div
                        class="ecom-element ecom-slider slider__body ecom-swiper-container"
                        :class="data.settings?.slider_effect === 'cube' ? 'ecom-swiper-effect-cube' : ''"
                        :data-option-swiper="optionSwiper"
                    >
                        <div class="ecom-swiper-wrapper">
                            <div
                                v-for="(item, i) in payload.items"
                                :key="item.id"
                                class="ecom-swiper-slide"
                            >
                                <div class="ecom-content-item">
                                    <div
                                        v-if="item.image"
                                        class="ecom-slider__image--wrapper"
                                        :class="{'ecom-slider__has-link' : item?.link?.href && item?.link?.href != ''}"
                                        :data-id="item.id"
                                        @click="editSlider(i)"
                                    >
                                    <template v-if="item?.link?.href && item?.link?.href != ''">
                                        <a
                                        :href="item.link.href"
                                        v-bind="item.link"
                                        class="ecom-slider__image--link"
                                        >
                                            <picture
                                                class="ecom-image-background"

                                                v-html="$helpers.renImageResponsive(item.image, storeScreens, i == 0, img_style(item))"
                                            />
                                            <div v-if="item.overlay" class="ecom-overlay"><span>overlay</span></div>
                                        </a>
                                    </template>
                                    <template v-else>
                                        <picture
                                            class="ecom-image-background"
                                            v-html="$helpers.renImageResponsive(item.image, storeScreens, i == 0, img_style(item))"
                                        />
                                        <div v-if="item.overlay" class="ecom-overlay"><span>overlay</span></div>
                                    </template>
                                    </div>
                                    <div
                                        class="ecom-content-item__container"
                                        :class="['ecom-transition-' + slider?.settings?.content_animation]"
                                    >
                                        <Blocks :data="item.blocks" :deep="deep" :index="i" :group="item" :section_id="section_id" />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div
                        v-if="isNavigation && isCombined() == 'combine'"
                        class="ecom-swiper-navigation"
                        :data-navigator-type="isCombined() == 'combine'"
                    >
                        <div class="ecom-flex-center">
                            <button
                                v-show="isArrow()"
                                class="ecom-swiper-button ecom-swiper-button-prev"
                                v-html="slider.settings.slider_prev_icon"
                            />
                            <div v-show="isPagination()" class="ecom-swiper-pagination" />
                            <button
                                v-show="isArrow()"
                                class="ecom-swiper-button ecom-swiper-button-next"
                                v-html="slider.settings.slider_next_icon"
                            />
                        </div>
                    </div>
                    <div v-if="isNavigation && !(isCombined() == 'combine')" v-show="isArrow()" class="ecom-swiper-navigation-position">
                        <button
                            class="ecom-swiper-button ecom-swiper-button-prev"
                            :style="sliderNav"
                            v-html="slider.settings.slider_prev_icon"
                        />
                        <button
                            class="ecom-swiper-button ecom-swiper-button-next"
                            :style="sliderNav"
                            v-html="slider.settings.slider_next_icon"
                        />
                    </div>
                    <div v-if="isNavigation && !(isCombined() == 'combine')" v-show="isPagination()" class="ecom-swiper-pagination-position ecom-swiper-pagination" />
                    <div v-if="data.settings?.autoplay_progress"
                      class="ecom-autoplay-progress">
                      <svg viewBox="0 0 48 48">
                        <circle cx="24" cy="24" r="20"></circle>
                      </svg>
                      <span></span>
                    </div>
                </div>
            </template>
        </GroupElement>
    </div>
</template>

<script>
import GroupElement from '../../Layout/Group.vue';
import { Element, Javascript } from '@/mixins';
export default {
    name: 'slider',
    presets:true,
    docs: 'https://help.ecomposer.io/docs/elements/basic-elements/slider/',
    vendors: ['slider_js', 'slider_css'],
    components: { GroupElement },
    mixins: [Element, Javascript],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        },
        index: {
            type: Number,
            default: 0
        },
        deep: {
            type: Number,
            default: 0
        }
    },
    data() {
        return {
            is: 'slider',
            name: 'Slider',
            watchHeight: {},
            jsreactives:['slider_navigation_layout','items', 'autoplay_progress']
        };
    },
    computed: {
        section_id() {
          return this?.$parent.section_id || '';
        },
        slider() {
            return this.data;
        },
        storeScreens() {
            return this.$store.getters['builder/screens'];
        },
        items() {
            return this.data?.settings?.items ?? [];
        },
        isNavigation() {
            return this.slider.settings?.slider_navigation_layout;
        },
        optionSwiper() {
            return this.$helpers.optionSwiper(this.slider.settings);
        },
        paginationImages() {
            return this.items.map(item => item.slider_pagination_image);
        },
        sliderNav(){
            if(!this.slider.settings.navigation_position__tablet) {
              this.slider.settings.navigation_position__tablet = this.slider.settings.navigation_position;
            }
            if(!this.slider.settings.navigation_position__mobile) {
              this.slider.settings.navigation_position__mobile = this.slider.settings.navigation_position;
            }

            return {
                '--ecom-position': this.slider.settings.navigation_position !== 'center' ? 'unset' : 'absolute',
                '--ecom-position__tablet': this.slider.settings.navigation_position__tablet !== 'center' ? 'unset' : 'absolute',
                '--ecom-position__mobile': this.slider.settings.navigation_position__mobile !== 'center' ? 'unset' : 'absolute',
            }
        },

        settings() {
            return [
                {
                    group_title: 'Slides',
                    params: [
                        {
                            name: 'items',
                            type: 'group',
                            options: {
                                add_text: 'Add new slide',
                                is_clear_all:false
                            },
                            params: [
                                {
                                    type: 'text',
                                    label: 'Heading',
                                    name: 'title'
                                },
                                {
                                    type: 'picker',
                                    label: 'Choose background',
                                    name: 'image',
                                    options: {
                                        responsive: true,
                                        type: 'image',
                                        editAlt: true
                                    }
                                },
                                {
                                    type: 'popup',
                                    name: 'img_position',
                                    label: 'Image position',
                                    options: {
                                        type: 'dropdown',
                                        default:false,
                                        values: {
                                            'center-center': 'Center - Center',
                                            'center-left': 'Center - Left',
                                            'center-right': 'Center - Right',
                                            'top-center': 'Top - Center',
                                            'top-left': 'Top - Left',
                                            'top-right': 'Top - Rigth',
                                            'bottom-center': 'Bottom - Center',
                                            'bottom-left': 'Bottom - Left',
                                            'bottom-right': 'Bottom - Rigth',
                                        }
                                    },
                                    css: {
                                        isCss: false
                                    }
                                },
                                {
                                    type: 'popup',
                                    name: 'img_size',
                                    label: 'Image size',
                                    value: 'cover',
                                    options: {
                                        type: 'dropdown',
                                        default:false,
                                        values: {
                                            'cover': 'Cover',
                                            'contain': 'Contain',
                                            'inherit': 'Inherit',
                                            'revert': 'Revert',
                                            'fill': 'Fill',
                                            'none': 'None',
                                            'unset': 'Unset',
                                        }
                                    },
                                    css: {
                                        isCss: false
                                    }
                                },
                                {
                                    type: 'picker',
                                    label: 'Pagination image',
                                    name: 'slider_pagination_image',
                                    options: {
                                        type: 'image',
                                        output: 'value',
                                        visible: {
                                            keep_data: false,
                                            condition: (data, datas, values = this.data.settings) => {
                                                return (
                                                        values["slider_navigation_layout"] === "classic_full" ||
                                                        values["slider_navigation_layout"] === "neo_full" ||
                                                        values["slider_navigation_layout"] === "pagination"
                                                    ) && values['slider_pagination_style'] === 'images';
                                            }
                                        }
                                    }
                                },
                                {
                                    type: 'link',
                                    label: 'Image link',
                                    name: 'link'
                                },
                                {
                                    type: 'toggle',
                                    name: 'overlay',
                                    label: 'Enable overlay',
                                    options: {
                                        oneline: true,
                                        values: {
                                            on: {
                                                label: 'YES',
                                                value: true
                                            },
                                            off: {
                                                label: 'NO',
                                                value: false
                                            }
                                        }
                                    }
                                },
                            ]
                        },
                        {
                            type: 'line'
                        },
                        {
                            name: 'contentWidth',
                            label: 'Content Width',
                            type: 'number',
                            options: {
                                units: {
                                    '%': {
                                        min: 10,
                                        max: 100,
                                        step: 1
                                    }
                                },
                                responsive: true
                            },
                            css: {
                                selector: ' .ecom-slider--container',
                                properties: {
                                    'padding-left': 'calc((100% - %value%) / 2)',
                                    'padding-right': 'calc((100% - %value%) / 2)',
                                }
                            }
                        },
                        {
                            type: 'toggle',
                            name: 'isCustomHeight',
                            label: 'Custom Height',
                            options: {
                                oneline: true,
                                values: {
                                    on: {
                                        label: 'YES',
                                        value: true
                                    },
                                    off: {
                                        label: 'NO',
                                        value: false
                                    }
                                }
                            }
                        },
                        {
                            type: 'number',
                            label: 'Height',
                            name: 'height',
                            placeholder: '300',
                            options: {
                                responsive:true,
                                units: {
                                    px: {
                                        min: 100,
                                        max: 1500
                                    }
                                },
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['isCustomHeight'];
                                    }
                                }
                            },
                            css: {
                                selector: ' .ecom-slider .ecom-swiper-slide'
                            }
                        },
                        {
                            type: 'popup',
                            label: 'Vertical align',
                            name: 'verticalAlign',
                            options: {
                                preview: 'title',
                                type: 'dropdown',
                                values: {
                                    'flex-start': 'Top',
                                    'center': 'Middle',
                                    'flex-end': 'Bottom',
                                },
                                visible: function (data) {
                                    return data['isCustomHeight'];
                                }
                            },
                            css: {
                                selector: ' .ecom-content-item',
                                properties: {
                                    'align-content': '',
                                    'align-items': '',
                                    'display': 'return value ? "flex" : ""'
                                }
                            },
                        }
                    ]
                },
                {
                    group_alias: 'swiper',
                    modify: {
                        params: [
                            {
                              position: 0,
                              fields: [
                                {
                                  type: 'popup',
                                  name: 'slider_effect',
                                  label: 'Transition effect',
                                  options: {
                                    type: 'dropdown',
                                    preview: 'title',
                                    default: false,
                                    values: {
                                      fade: 'Fade',
                                      slide: 'Slide',
                                      // cube: 'Cube',
                                      coverflow: 'Coverflow',
                                      flip: 'Flip',
                                      cards: 'Cards',
                                      creative: 'Creative',
                                      creative2: 'Creative style 2',
                                      creative3: 'Creative style 3',
                                      creative4: 'Creative style 4',
                                      creative5: 'Creative style 5',
                                      creative6: 'Creative style 6' 
                                    }
                                  }
                                }
                              ]
                            },
                            {
                              position: 4,
                              fields: [
                                {
                                  name: 'autoplay_progress',
                                  label: 'Autoplay progress',
                                  type: 'toggle',
                                  options: {
                                    visible: function(data) {
                                      return data &&
                                            data['slider_autoplay'] === true
                                    },
                                    online: true,
                                    values: {
                                      on: {
                                        label: 'yes',
                                        value: true
                                      },
                                      off: {
                                        label: 'no',
                                        value: false
                                      }
                                    }
                                  }
                                }
                              ]
                            },
                            {
                                position: 20,
                                fields: [
                                    {
                                        type: 'toggle',
                                        name: 'paginitionAnimation',
                                        label: 'Pagination Animation',
                                        options: {
                                            oneline: true,
                                            values: {
                                                on: {
                                                    label: 'YES',
                                                    value: true
                                                },
                                                off: {
                                                    label: 'NO',
                                                    value: false
                                                }
                                            }
                                        },
                                        css: {
                                            selector: ' .ecom-swiper-pagination-bullet.ecom-swiper-pagination-bullet-active:after',
                                            properties: {
                                                'height': 'return value ? "100%" : "0"'
                                            }
                                        },
                                    },
                                ]
                            }
                        ]
                    }
                },
                {
                    group_title: 'Animation',
                    params: [
                        {
                            type: 'popup',
                            name: 'content_animation',
                            label: 'Content Animation',
                            options: {
                                type: 'dropdown',
                                default: false,
                                value: 'none',
                                preview: 'title',
                                values: {
                                    none: 'None',
                                    Bottom2Top: 'Bottom to top',
                                    Top2Bottom: 'Top to Bottom',
                                    Left2Right: 'Left to Right',
                                    Right2Left: 'Right to Left'
                                }
                            }
                        },
                    ]
                }
            ];
        },

        javascript() {

            return function () {
                const  $el = this.$el;
                if(!$el) return;
                const autoplayProgress = this.settings.autoplay_progress ?? false;
                const progressCircle = $el.querySelector(".ecom-autoplay-progress svg");
                const progressContent = $el.querySelector(".ecom-autoplay-progress span");
                let on = {}
                if(progressCircle && progressContent) {
                    on = {
                    autoplayTimeLeft(s, time, progress) {
                        progressCircle.style.setProperty("--progress", 1 - progress);
                        progressContent.textContent = `${Math.ceil(time / 1000)}s`;
                        }
                    }
                }
                const $container = $el.querySelector('.ecom-swiper-container');
                const optionSwiper = JSON.parse($container.dataset.optionSwiper);
                optionSwiper.pagination.el = $el.querySelector('.ecom-swiper-pagination');
                optionSwiper.navigation.nextEl = $el.querySelector('.ecom-swiper-button-next');
                optionSwiper.navigation.prevEl = $el.querySelector('.ecom-swiper-button-prev');
                optionSwiper.pagination.renderBullet = (index, className) => {
                    return `<span class="${className}">
                            ${this.settings.items[index] && this.settings.items[index].slider_pagination_image ?
                                `<img src="${this.settings.items[index].slider_pagination_image}">` :
                                ''
                            }</span>`;
                }
                const params = {
                    allowTouchMove: this.isLive,
                    on: on
                };
                if(!this.isLive){
                  params.loop = false;
                }

                if(window.EComSwiper) {
                  function initSwiper() {
                    const swiper = new window.EComSwiper($container, Object.assign(optionSwiper, params));

                    swiper.on('slideChange', function() {
                      //Fix entrance with slider
                        const preSlideIndex = swiper.activeIndex == 0 ? swiper.slides.length - 1 : swiper.activeIndex - 1,
                              preSlide = swiper.slides[preSlideIndex];
                        if(preSlide) {
                          const preAnimations = preSlide.querySelectorAll('.ecom-animation.ecom-animated');
                          if(preAnimations.length) {
                            preAnimations.forEach(function(ani) {
                              ani.classList.remove('ecom-animated', 'animated');
                              ani.classList.add('ecom-animated-slider');
                            })
                          }
                        }
                        if(swiper.el) {
                          const currentSlide = swiper.slides[swiper.activeIndex];
                          let currentAnimations = null;
                          if(currentSlide){
                            currentAnimations = currentSlide.querySelectorAll('.ecom-animation.ecom-animated-slider');
                          }
                          if(currentAnimations && currentAnimations.length) {
                            currentAnimations.forEach(function(ani) {
                              ani.classList.add('ecom-animated');
                            })
                          }
                        }
                        //Fix pagination not update when touch
                        setTimeout(function(){
                          swiper.pagination.render();
                          swiper.pagination.update();
                        }, 500)
                    });
                  };

                  var timer;

                  window.addEventListener('resize', function () {
                    if (timer) {
                      clearTimeout(timer);
                    }

                    timer = setTimeout(initSwiper, 200);
                  });

                  initSwiper();
                }


                let itv = null;
                $container.querySelectorAll('img').length
                && $container.querySelectorAll('img').forEach(function(img, i){
                        if(i != 0) {
                          img.removeAttribute('loading');
                        }
                        img.addEventListener('load', function(){
                            clearTimeout(itv);
                            itv =  setTimeout(()=>window.dispatchEvent(new window.Event('resize')),500);
                        });
                    })

            };
        },
        css() {
            return `
                    /**
                     * EComposer Element Slider
                     */
                    .ecom-elements-slider {
                      overflow: hidden;
                    }
                    body[ecom-loaded] .ecom-elements-slider {
                      overflow: unset;
                    }
                    .ecom-slider__image--link {
                      position: absolute;
                      inset: 0;
                    }
                    .ecom-slider__has-link .ecom-content-item__container {
                      pointer-events: none;
                    }
                    .ecom-swiper-navigation-position{
                        display:flex;
                    }
                    .ecom-swiper-navigation-position button{
                        margin:0
                    }
                    .ecom-swiper-navigation button{
                        position:unset;
                        margin:0
                    }
                    .ecom-slider--container {
                        position: relative;
                        display: flex;
                        flex-direction: column;
                    }
                    .ecom-swiper-pagination-bullet:only-child {
                        opacity: none
                    }
                    .ecom-swiper-button svg{
                        width:24px;
                        height:24px;
                    }
                    .ecom-flex-center, .ecom-swiper-navigation {
                        display: flex;
                        align-items: center;
                    }
                    .ecom-swiper-navigation{
                        justify-content: center
                    }
                    .ecom-slider img {
                        display: block
                    }
                    .ecom-slider--container .ecom-swiper-pagination-bullet img{
                        display: block;
                        width: 100%;
                        height: 100%;
                        object-fit: cover
                    }
                    .ecom-slider--container .ecom-swiper-button-next:after,
                    .ecom-slider--container .ecom-swiper-button-prev:after {
                        content: none;
                    }
                    .ecom-slider--container .ecom-swiper-navigation[data-navigator-type="combine"] .ecom-swiper-button-next,
                    .ecom-slider--container .ecom-swiper-navigation[data-navigator-type="combine"] .ecom-swiper-button-prev {
                        position: static;
                        margin: 0;
                    }
                    .ecom-slider--container .ecom-swiper-button-next,
                    .ecom-slider--container .ecom-swiper-button-prev {
                        border: 0;
                        background: transparent;
                        width: auto;
                        height: auto;
                        padding: 5px;
                        color: #444;
                        transform: translateY(-50%);
                    }
                    .ecom-slider--container .ecom-swiper-pagination {
                        position: relative;
                        display: flex;
                        flex-wrap: wrap;
                        align-items: center
                    }
                    .ecom-slider--container .ecom-swiper-pagination-bullet {
                        width: 15px;
                        height: 15px;
                        opacity: 1;
                        overflow: hidden;
                    }
                    .ecom-slider--container .ecom-swiper-pagination-bullet,
                    .ecom-slider--container .ecom-swiper-pagination-bullet-active{
                        background-clip: content-box;
                        padding: 1px;
                        box-sizing: content-box !important;
                        background-color: currentColor;
                    }
                     .ecom-swiper-container-horizontal>.ecom-swiper-pagination-bullets{
                        width:auto;
                    }
                    .ecom-slider--container .ecom-swiper-slide{
                        position: relative;
                    }
                    .ecom-swiper-slide div.core__blocks>div.core__blocks--body{
                        display:flex
                    }
                    .ecom-content-item__container {
                        width: 100%;
                        z-index: 99;
                    }
                    .ecom-swiper-slide .ecom-content-item {
                        display: flex;
                        justify-content: center;
                    }
                    .ecom-image-background{
                        position:absolute;
                        top:0;
                        left:0;
                        height: 100% !important;
                        width: 100%;
                    }
                    .ecom-image-background img{
                        position:absolute;
                        top:0;
                        left:0;
                        height: 100% !important;
                        width: 100%;
                        object-fit: cover;
                        object-position: center center;
                    }

                    .ecom-overlay {
                        display: block;
                        position: absolute;
                        inset: 0;
                    }

                    .ecom-overlay span {
                        opacity: 0;
                        visibility: hidden;
                    }

                    .ecom-slider--container .ecom-swiper-pagination-bullets, .ecom-swiper-pagination-custom, .ecom-swiper-pagination-fraction {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        bottom: 0;
                    }

                    .ecom-slider--container .ecom-swiper-pagination-bullets, .ecom-swiper-pagination-custom, .ecom-swiper-pagination-fraction {
                        bottom: 0;
                    }
                    .ecom-content-item {
                        height: 100%;
                    }
                    .ecom-slider--container .ecom-swiper-navigation[data-navigator-type="classic"] .ecom-swiper-pagination,
                    .ecom-slider--container .ecom-swiper-navigation:not([data-navigator-type]) .ecom-swiper-pagination {
                        width:100%
                    }
                    .ecom-transition-Bottom2Top, .ecom-transition-Top2Bottom, .ecom-transition-Left2Right, .ecom-transition-Right2Left {
                        transform: translateY(40px);
                        -webkit-transform: translateY(40px);
                        -moz-transform: translateY(40px);
                        transition: .4s cubic-bezier(.44, .13, .48, .87) .3s;
                        -webkit-transition: .4s cubic-bezier(.44, .13, .48, .87) .3s;
                        opacity: 0;
                    }
                    .ecom-transition-Top2Bottom {
                        transform: translateY(-40px);
                        -webkit-transform: translateY(-40px);
                        -moz-transform: translateY(-40px);
                    }
                    .ecom-transition-Left2Right {
                        transform: translateX(-40px);
                        -webkit-transform: translateX(-40px);
                        -moz-transform: translateX(-40px);
                    }
                    .ecom-transition-Right2Left {
                        transform: translateX(40px);
                        -webkit-transform: translateX(40px);
                        -moz-transform: translateX(40px);
                    }
                    .ecom-swiper-slide.ecom-box-active .ecom-transition-Bottom2Top, .ecom-swiper-slide.ecom-box-active .ecom-transition-Top2Bottom, .ecom-swiper-slide.ecom-box-active .ecom-transition-Left2Right, .ecom-swiper-slide.ecom-box-active .ecom-transition-Right2Left {
                        transform: translateY(0);
                        -webkit-transform: translateY(0);
                        -moz-transform: translateY(0);
                        opacity: 1;
                    }
                    .ecom-swiper-pagination-bullet {
                        position: relative;
                    }
                    .ecom-swiper-pagination-bullet:after {
                        content: '';
                        position: absolute;
                        bottom: 0;
                        height: 0;
                        left: 0;
                        width: 100%;
                        box-shadow: 0 0 1px #fff;
                        -webkit-transition: height .3s;
                        transition: height .5s;
                    }
                    .ecom-swiper-button {
                      position: var(--ecom-position);
                    }
                    .ecom-swiper-effect-cube .ecom-image-background img {
                      // visibility: visible;
                    }
                    .ecom-autoplay-progress {
                      position: absolute;
                      right: 16px;
                      bottom: 16px;
                      z-index: 10;
                      width: 48px;
                      height: 48px;
                      display: flex;
                      align-items: center;
                      justify-content: center;
                      font-weight: bold;
                      color: #000;
                    }

                    .ecom-autoplay-progress svg {
                      --progress: 0;
                      position: absolute;
