    watch: {
        'data.settings.items.tabOpened': function(index){
            if(index >= 0)
            {
                this?.$el && this?.$el?.querySelector('.ecom-swiper-container')?.swiper?.slideTo(index);
            }
        },
        optionSwiper: {
            deep: true,
            handler: function () {
                this.slider.refresh = this.$helpers.randid();
            }
        },
        items: {
            deep: true,
            handler: function () {
                setTimeout(() => {
                    this?.$el && this?.$el?.querySelector('.ecom-swiper-container')?.swiper?.update();
                }, 100);
            }
        },
        storeScreens: {
            deep: true,
            immediate: true,
            handler(val) {
                let screens = Object.keys(val);
                screens.forEach(screen => {
                    if(!(screen in this.watchHeight)) {
                        let watch = this.$watch('data.settings.height' +(screen === 'desktop' ? '' : ('__' + screen)) , function(){
                            let itv = null;
                            clearTimeout(itv);

                            itv = setTimeout(() => {
                              this?.$el?.querySelector('.ecom-swiper-container')?.swiper?.update();
                            }, 100)

                        });
                        this.watchHeight[screen] = watch
                    }
                });
            }
        },
        paginationImages: {
            deep: true,
            handler() {
                this.slider.refresh = this.$helpers.randid();
            }
        },
    },
    unmounted () {
        for(let k in this.watchHeight) {
            this.watchHeight[k]();
        }
    },
    methods: {
        isCombined() {
            let navigator = this.data?.settings?.slider_navigation_layout;
            return navigator === 'neo_full' ? 'combine' : 'classic';
        },
        isArrow() {
            return this.data.settings?.slider_navigation_layout === 'neo_full' ||
                    this.data.settings?.slider_navigation_layout === 'navigation' ||
                    this.data.settings?.slider_navigation_layout === 'classic_full'
        },
        isPagination() {
            return this.data.settings?.slider_navigation_layout === 'neo_full' ||
                    this.data.settings?.slider_navigation_layout === 'classic_full' ||
                    this.data.settings?.slider_navigation_layout === 'pagination';
        },
        editSlider (i) {
                this.$store.commit('builder/setActiveElement', this);
                this.$store.commit('builder/setEditingElement', this);
                if( !'tabOpened' in this.data.settings.items)
                {
                    Object.defineProperty( this.data.settings.items, "tabOpened", { enumerable: false, writable: true } );

                }
                this.data.settings.items.tabOpened = i;

        },
        img_style(item) {
          const style = {};
          if(item?.img_position) {
            style['object-position'] = item?.img_position;
          }

          if(item?.img_size) {
            style['object-fit'] = item?.img_size;
          }

          return style;
        },

        style() {

            let checkNavPagination = [];
            if(this.isArrow()){
                checkNavPagination.push({
                    title:'Navigator',
                    type:'swiper:nav'
                });
            }
            if(this.isPagination()){
                checkNavPagination.push({
                    title:'Pagination',
                    type:'swiper:pagination'
                });
            }
            const style = [
                {
                    group_alias: 'box:active',
                    options: {
                        group_title: 'Slide',
                        selector: ' .ecom-swiper-slide'
                    },
                    modify: {
                        params: [
                            {
                                position: 50,
                                fields: [{ type: 'line' },{ alias: 'spacing' }]
                            },
                            {
                                position: 0,
                                fields: [
                                    {
                                        type: "popup",
                                        label: "Slider Animation",
                                        name: "sliderAnimation",
                                        selector: ' .ecom-swiper-slide',
                                        options: {
                                            type: "dropdown",
                                            values: "animation",

                                            size: "small",
                                        },
                                        css: {
                                            selector: ".ecom-box-active",
                                            properties: {
                                                animation: ""
                                            }
                                        }
                                    },
                                    {
                                        type: "number",
                                        label: 'Slider Transition Duration <span class="lowercase">(ms)</span>',
                                        name: "sliderTransition",
                                        selector: ' .ecom-swiper-slide',
                                        options: {
                                            min: 0,
                                            max: 5000,
                                            step: 100
                                        },
                                        css: {
                                            properties: {
                                                'animation-duration': "%value%ms !important"
                                            }
                                        }
                                    },
                                ]
                            }
                        ],
                        remove: {
                            label: 'Box Shadow'
                        }
                    }
                }
            ];
            let modifyNavigation = {};
            if(this.isCombined() === 'combine') {
                modifyNavigation = {
                    params: [
                        {
                            alias: 'spacing',
                            options: {
                                name: 'spacingNavigation',
                                css: {
                                    selector: ' .ecom-swiper-navigation'
                                }
                            }
                        },
                        {
                            type: 'line'
                        }
                    ],
                    remove: {
                        name: 'paginationAlignment'
                    }
                };
            }
            if(checkNavPagination.length) {
                style.push({
                    group_alias:checkNavPagination,
                    options:{
                        group_title: 'Navigation',
                        selector:' .ecom-slider--container'
                    },
                    modify: modifyNavigation
                });
            }

            if(this.items) {
                let hasOverLay = this.items.some((record) => record.overlay == true);

                if(hasOverLay) {
                    style.push({
                        group_alias: 'background:overlay',
                        options: {
                            group_name:'overlay',
                            group_title: 'Overlay',
                            selector: ' .ecom-slider__image--wrapper, .ecom-slider__image--wrapper .ecom-slider__image--link'
                        }
                    });
                }
            }
            let autoplay_progress = {
              group_title: 'Autoplay progress',
              params: [
                {
                  type: 'number',
                  name: 'size_progress',
                  label: 'Size',
                  options: {
                    units: {
                        px: {
                            min: 0,
                            max: 200
                        }
                    },
                  },
                  css: {
                    properties: {
                      width: '%value%',
                      height: '%value%'
                    },
                    selector: 'root .ecom-autoplay-progress'
                  }
                },
                {
                  name: 'text_progress_color',
                  label: 'Text Color',
                  type: 'color',
                  options: {
                      oneline: true,
                      global: {
                          type: 'colors'
                      }
                  },
                  css: {
                      properties: {
                          'color': ''
                      },
                      selector: 'root  .ecom-autoplay-progress'
                  }
                },
                {
                  type: 'number',
                  name: 'size_progress_stroke',
                  label: 'Stroke width',
                  options: {
                    units: {
                        px: {
                            min: 0,
                            max: 50
                        }
                    },
                  },
                  css: {
                    properties: {
                      'stroke-width':''
                    },
                    selector: 'root .ecom-autoplay-progress svg'
                  }
                },
                {
                  name: 'stroke_progress_color',
                  label: 'Stroke color',
                  type: 'color',
                  options: {
                      oneline: true,
                      global: {
                          type: 'colors'
                      }
                  },
                  css: {
                      properties: {
                          'stroke': ''
                      },
                      selector: 'root  .ecom-autoplay-progress svg'
                  }
                },
                {
                  type: 'line'
                },
                {
                  type: 'paragraph',
                  content: 'Position'
                },
                {
                  type: 'number',
                  name: 'position_right',
                  label: 'Right',
                  options: {
                    units: {
                        '%': {
                            min: 0,
                            max: 100
                        },
                        px: {
                          min: 0,
                          max: 1000
                        }
                    },
                  },
                  css: {
                    properties: {
                      right: ''
                    },
                    selector: 'root .ecom-autoplay-progress'
                  }
                },
                {
                  type: 'number',
                  name: 'position_bottom',
                  label: 'Bottom',
                  options: {
                    units: {
                        '%': {
                            min: 0,
                            max: 100
                        },
                        px: {
                          min: 0,
                          max: 1000
                        }
                    },
                  },
                  css: {
                    properties: {
                      bottom: ''
                    },
                    selector: 'root .ecom-autoplay-progress'
                  }
                }
              ]
            }
            if(this.data.settings?.autoplay_progress) {
              style.push(autoplay_progress)
            }

            return style;
        },
    }
};
</script>