<template>
    <div class="ecom-element ecom-collection ecom-collection__title">
        <div class="ecom-element ecom-collection ecom-collection__title-wrapper">
            <div class="ecom-element ecom-collection ecom-collection__title-container">
                <component
                    :is="tag"
                    class="ecom-collection__title--headline ecom__heading"
                    :class="classes"
                    :href="tag === 'a' ? liquid('url') : null"
                    v-html="liquid('title')"
                />
            </div>
        </div>
    </div>
</template>

<script>
import { Liquid, Element } from '@/mixins';
export default {
    name: 'CollectionTitle',
    docs: 'https://help.ecomposer.io/docs/elements/collection-elements/title/',
    presets:true,
    mixins: [Liquid, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    computed: {
        liquids() {
            return {
                title: {
                    code: `{{collection.title}}`,
                    preview: `The preview title`
                },
                url: {
                    code: '{{ collection.url }}',
                    preview: '#'
                }
            };
        },

        settings() {
            return [
                {
                    group_alias:'title:settings',
                    options:{
                        group_name:'',
                        group_title:''
                    }
                }
            ];
        },
        classes(){
            return this.data?.settings?.classes??'';
        },
        tag() {
            return this.data && this.data.settings && this.data.settings.tag
                ? this.data.settings.tag
                : 'h3';
        },

        

        requestShopifyType() {
            return {
                shopify_type: 'collection'
            };
        },

        default() {
            return {
                settings: {
                    tag: "h2"
                },
                style: {
                    textSpacing: {
                        textTextAlign: "left"
                    }
                }
            }
        }
    },
    methods: {
        style() {
            
            let style = [ 
                {
                    group_alias: 'text:spacing',
                    options:{
                        group_title: '',
                        selector: ' .ecom-collection__title--headline'
                    }
                }
            ];
            return style;
        },
    }
};
</script>
